import AppService from '../../../services/AppService';
import utils from '../../../utils';
import { IContainerProps as IProps } from "./types";

export const onClickTitle = (props: IProps) => () => {
    const appService = AppService.getService(props.instanceId);

    if (appService.onClickProfile) {
        appService.onClickProfile();
    }
};

export const hasProfileClickHover = (props:IProps) =>
    utils.hasHookConfigured(AppService.getService(props.instanceId).onClickProfile);

