import { isMobile } from 'react-device-detect';
import styled from 'styled-components';
import withIdentifier, { DomIdentifier } from "../../hoc/withIdentifier";
import MessageSectionPopupWrapper from '../../common/MessageSectionPopupWrapper';
import utils from '../../../utils';

interface IMessageSectionContainerWrapperProps {
    minMessageSectionHeight: number;
}

 export interface IMessengerBaseButtonProps extends React.HTMLAttributes<HTMLDivElement> {
    isInverted?: boolean;
    isActive?: boolean;
    isDropDownOpen?: boolean;
    isResponsive?: boolean;
    hasOnlySendButton?: boolean;
}

interface IMessengerButtonsWrapper {
	hasButtonsMargin?: boolean;
}

interface IMessengerSectionWrapperProps {
    isFullscreen: boolean;
}

interface IMessengerBoxProps {
    className?: string;
    hasText: boolean;
    hasEditor: boolean;
    isInteractive: boolean;
	shouldIncreaseMessageFontSize: boolean;
}

interface IDropDownContentProps {
    isAnySectionOpen?: boolean;
    isOpen: boolean;
	shouldUseDropdownAnimation: boolean;
}

interface IDropDownButtonProps {
    dropDownOpen?: boolean;
    isInverted?: boolean;
    isActive?: boolean;
    hasLeftSeparator?: boolean;
}

interface IMessengerBaseButtonIconProps {
    hasLeftSeparator?: boolean;
    isInverted?: boolean;
}

interface IMessageSectionPopupsProps {
    isResponsive?: boolean;
}

export const MessageSectionContainerWrapper =
    styled.div.attrs({ className: 'MessageSectionContainerWrapper' })<IMessageSectionContainerWrapperProps>`
    min-height: ${props => props.minMessageSectionHeight}px;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    -webkit-tap-highlight-color: ${(props) => props.theme.colors.selectedButton.clickHighlight};
    
    .-icon-send-chat-full {
        line-height: 2;
    }
`;

export const MessageSectionWrapper = styled.div.attrs({ className: 'message-section-wrapper' })`
    position: relative;
    width: 100%;
    bottom: 0px;
`;

export const MessengerBox = styled.div.attrs({ className: "messenger-box" })<IMessengerBoxProps>`
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-weight: ${(props) => props.theme.typography.weight.medium};
    background-color: ${(props) => props.theme.colors.messageEditorBackgroundColor};
    color: ${props => props.hasText ? "#888" : props.theme.colors.messageEditorTextColor};
    
    ${(props) => !props.isInteractive && `cursor: not-allowed;`}

    ${(props) => props.hasEditor && `margin: 5px;`}
    
    font-size: ${({ shouldIncreaseMessageFontSize }) => 
		`${utils.computeMessageFontSize(shouldIncreaseMessageFontSize, 15)} !important`};
    box-sizing: border-box;
    flex: 1 0;
    font-size: 1em;
    min-height: 45px;
    max-height: 100px;
    padding-left: 3px;
    padding-top: 13px;
    word-break: break-all;
    overflow-y: auto;
`;

export const MessengerSectionWrapper = withIdentifier<IMessengerSectionWrapperProps>(styled.div<IMessengerSectionWrapperProps>`
    background-color: ${(props) => props.theme.colors.messageEditorBackgroundColor};
    ${(props: IMessengerSectionWrapperProps) => props.isFullscreen && `
        margin: 5px 0 10px;            
    `}

    display: flex;
    min-height: 50px;
    align-items: center;
`, DomIdentifier.MessengerSection);

export const MessengerButtonsWrapper = styled.div<IMessengerButtonsWrapper>`
    display: flex;
	align-content: center;
	${({ hasButtonsMargin }) => hasButtonsMargin && `margin-right: 15px;`}
`;

export const MessengerBaseButton = styled.div<IMessengerBaseButtonProps>`
    ${props => props.isResponsive
        ?`
            width: 100%;
            border-bottom: 0.5px solid rgba(184, 184, 184, 0.6);
            justify-content: space-between;
                
            &:hover {
                color: ${props.theme.colors.messengerButtonInvertedText};
                background-color: ${props.theme.colors.selectedButton.blueBackgroundHover};
            }
                
            &:hover .-icon-single-arrow-right-line {
                display: flex;
            }
        `:`
            width: 35px;
            justify-content: center;

            &:hover {
                color: ${props.theme.colors.messengerButtonHover};
            }
        `
    }
    
    ${props => props.isActive && `
        color: ${props.theme.colors.selectedButton.blueBackgroundHover}!important;
    `}

    ${props => props.hasOnlySendButton && 'margin-right: 10px;'}
    padding: ${props => props.isResponsive ? '15px 0' : '4px 0px'};
    
    color: ${props => props.isInverted 
		? props.theme.colors.selectedButton.blueBackgroundHover
		: props.theme.colors.selectedButton.greyBackgroundHover
	};

    height: 28px;
    box-sizing: content-box;
    cursor: pointer;
    text-align: center;
    display: inline-flex;
    align-items: center;
    position: relative;
    
    & .-icon-send-msg-single-full {
        margin-top: 3px;
    }
`;

export const MessengerBaseButtonIcon = styled.i<IMessengerBaseButtonIconProps>`
    ${props => props.isInverted && 'transform: rotate(135deg);'}

    ${props => props.hasLeftSeparator && `
        border-left: 1px solid #ccc;
        padding-left: 7px;
        line-height: 2;
    `}

    position: relative;
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 2;
    z-index: 0;
    transition: transform .5s ease-in-out;
    
    width: 50px;
    align-content: center;
    justify-content: center;
`;

export const ToyControlBaseButtonIcon = styled(MessengerBaseButtonIcon)`
    margin-left: 20px;
`;

export const ToyControlIcon = styled.i`
    color: ${props => props.theme.colors.toySection.primaryColor};
    font-size: 40px;
    margin: 0px 10px 0px 5px;
`;

export const DropDownButton = styled.div.attrs({ className: 'DropDownButton' })<IDropDownButtonProps>`
	${props => props.hasLeftSeparator && 'border-left: 1px solid #DBDDDE;'}

	color: ${props => {
		const { blueBackgroundHover, defaultDropDownBackground } = props.theme.colors.selectedButton;
		return props.isInverted ? blueBackgroundHover : defaultDropDownBackground;
	}};

	${props => !isMobile &&
		`&:hover {
			color: ${props.theme.colors.selectedButton.blueBackgroundHover};
		}`
	}}
    
    position: relative;
    width: 35px;
    height: 28px;
    padding: 4px 0px;
    cursor: pointer;
    text-align: center;
    display: inline-flex;
    align-items: center;
	justify-content: center;
	box-sizing: content-box;
`;

export const DropDownContent = styled(MessageSectionPopupWrapper).attrs({ className: 'DropDownContent' })<IDropDownContentProps>`
    ${props => `
        left: ${(props.isAnySectionOpen) ? '-100%' : '0'};
        height: ${props.isOpen ? '100%' : '0'};
        background-color: ${props.theme.colors.dropDownContentBgColor};
    `}
    
    transition: ${props => props.shouldUseDropdownAnimation ? 'height' : 'all'} 0.5s ease-in-out;

	font-size: 15px;
    border: 0;
    bottom: 0;
    z-index: 4;
    width: 100%;
    display: flex;
    min-width: auto;
    position: absolute;
    flex-direction: column;
    align-items: flex-end;
`;

export const MessageSectionPopups = styled.div.attrs({ className: 'MessageSectionPopups' })<IMessageSectionPopupsProps>`
    overflow: hidden;
    position: relative;
    height: auto;
    width: 100%;
    z-index: 2;
`;
