import { isEmpty, get } from 'lodash';
import { Action } from 'redux-actions';
import { put, select, takeEvery } from "redux-saga/effects";
import { ACTION, CMDP_LOGIN_RESPONSE, ICOMMAND, SupportedLanguage } from 'vxcontrol-client-lib';

import types from "./types";
import config from '../../static/config';
import { ActionIds } from '../../redux/interfaces';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import ApiService from '../../services/ApiService';
import { setIsVip } from "../../redux/modules/user";
import {
    initializeQuantitiesToInventoryGifts,
    setAutoRecharge,
    setChatFeatures,
    setFreePlatformMessages,
    setOneClickRecharge,
    setSession
} from '../../redux/modules/session';

const createLoginActionType = createActionType(ActionIds.LOGIN, { isSagaAction: true });

// Actions
const REQUEST_LOGIN = createLoginActionType("REQUEST_LOGIN");
export const RECEIVE_LOGIN = createLoginActionType("RECEIVE_LOGIN");

// Action Creators
export const requestLogin = createAction<string>(REQUEST_LOGIN);
export const onReceiveLogin = createAction<CMDP_LOGIN_RESPONSE>(RECEIVE_LOGIN);

/** Receives user's webtoken through which the login session will be created */
export const requestLoginSaga = function* ({ payload }: Action<IPayload<string>>) {
    const { instanceId, value: webtoken } = payload;

    const language: SupportedLanguage = yield select(selectors.getLanguage, instanceId);
    const usrKey: string = yield select(selectors.getUserKey, instanceId) || config.defaultUserKey;

    const service = ApiService.getService(instanceId);
    service.closeConnection();
    service.connect(webtoken, language);

    /** Send the login request afterwards */
    const command: ICOMMAND = {
        action: ACTION.CMDP_LOGIN,
        params: {
            webtoken,
            usrKey
        }
    };

    service.sendCommand(command);
};

/**
 * If login is successful, it saves the sessionID nad inventory gifts in the Redux store.
 * Afterwards, it starts the NOOP heartbeat communication.
 */
export const receiveLoginSaga = function* ({ payload }: Action<IPayload<CMDP_LOGIN_RESPONSE>>) {
    const { instanceId, value } = payload;

    if (!value || !value.payload) {
        return;
	}

    if (!isEmpty(get(value, "values.sessionID"))) {
        yield put(setSession(instanceId, value.values.sessionID));
    }

    const guest = value.payload.guest as types.ILoginGuestResponse;
    if (isEmpty(guest)) {
        return;
    }

    yield put(setIsVip(instanceId, guest.vip || false));
    yield put(setFreePlatformMessages(instanceId, guest.messages.freeCount || 0));

    if (!isEmpty(guest.boughtMessengerGifts)) {
        yield put(initializeQuantitiesToInventoryGifts(instanceId, guest.boughtMessengerGifts));
    }

    const { autoRecharge, chatFeatures, oneClickRecharge} = guest;

    if (!isEmpty(autoRecharge)) {
        yield put(setAutoRecharge(instanceId, autoRecharge));
    }

    if (!isEmpty(chatFeatures)) {
        yield put(setChatFeatures(instanceId, chatFeatures));
    }

    if (!isEmpty(oneClickRecharge)) {
        yield put(setOneClickRecharge(instanceId, oneClickRecharge));
    }
};

const watchSagas = function* () {
    yield takeEvery(REQUEST_LOGIN, requestLoginSaga);
    yield takeEvery(RECEIVE_LOGIN, receiveLoginSaga);
};

export default watchSagas;
