import * as cuid from 'cuid';
import * as moment from 'moment';
import { Action } from 'redux-actions';
import { includes, isEmpty } from 'lodash';
import request from 'superagent';
import { ACTION, IRESPONSE, ResultCode } from "vxcontrol-client-lib";
import { call, put, select, takeEvery } from "redux-saga/effects";

import types from './types';
import loggers from "../../loggers";
import config from '../../static/config';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import { onReceiveError } from './handleError';
import selectors from '../../redux/selectors';
import ChatService from "../../services/ChatService";
import { PromiseResult } from "../../services/types";
import { switchVoyeur2LiveChat } from '../common/videochat';
import { ActionIds } from '../../redux/interfaces';
import { getChatInstanceSaga, IGetChatInstanceSagaResult } from "../common/chat";
import { addMessage, closeDropDown, removeMessage, setActionResponse } from '../../redux/modules/messenger';
import {
    ActionResponseType,
	IErrorTranslation,
    IMessage,
    ISession,
    ITranslation,
    MessageContentType,
    MessageDirection
} from '../../redux/store';
import { VXControlCodes } from '../../static/constants/errorCodes';

interface IErrorData {
	response: IRESPONSE;
	messageKey: string;
}

const createImageActionType = createActionType(ActionIds.IMAGE, { isSagaAction: true });

// Actions
const REQUEST_SEND_IMAGE = createImageActionType("REQUEST_SEND_IMAGE");
const RECEIVE_SEND_IMAGE_ERROR = createImageActionType("RECEIVE_SEND_IMAGE_ERROR");

// Action Creators
export const requestSendImage = createAction<File>(REQUEST_SEND_IMAGE);
export const receiveSendImageError = createAction<IErrorData>(RECEIVE_SEND_IMAGE_ERROR);

/** Saga for sending an image */
const requestSendImageSaga = function* ({ payload }: Action<IPayload<File>>) {
    const { instanceId, value: file } = payload;

    const session: ISession = yield select(selectors.getSession, instanceId);
    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);
    const webtoken: string = yield select(selectors.getWebtoken, instanceId);
    const chat = yield select(selectors.getChat, instanceId);

    /** The first step of this process is validating the file upload */
    if (!includes(config.limitations.fileUpload.acceptedFileFormats, file.type)) {
        yield put(setActionResponse(instanceId, {
            text: translation.common.imageUploadFail,
            type: ActionResponseType.ERROR
        }));

        return;
    }

    /** If file upload is validated we are closing the dropDown menu */
    yield put(closeDropDown(instanceId));

    /** The data should be a FormData with file's binary value and file's name */
    const data = new FormData();
    data.append("file", file, file.name);

    const messageKey = cuid.slug();

    const message: IMessage = {
        id: cuid.slug(),
        content: URL.createObjectURL(file),
        dateCreated: moment().toString(),
        direction: MessageDirection.out,
        contentType: MessageContentType.IMAGE,
        messageKey,
        isNew: true,
        isOnLoading: true,
        isSystem: false
    };

    if (chat) {
        message.chatId = chat.chatId;
    }

    const isPlaying = yield select(selectors.isPlaying, instanceId);
    const isChatExternallyProvided = yield select(selectors.isChatExternallyProvided, instanceId);

    if (isPlaying || isChatExternallyProvided) {
        // send through vchat-core
        const chatResult: IGetChatInstanceSagaResult = yield call(getChatInstanceSaga, false, instanceId);

        if (chatResult.chatConf.error || chatResult.chatInstance?.error) {
            loggers.saga.debug('IMAGE - could not get chat to upload an image; aborting');
            return;
        }

        yield put(addMessage(instanceId, message));

	    const isVideoChatInVoyeurMode: boolean = yield select(selectors.isVideoChatInVoyeurMode, instanceId);
	    if (isVideoChatInVoyeurMode) {
		    yield put(switchVoyeur2LiveChat(instanceId));
	    }
	    
        /** Send the request */
        const sendImageResult: PromiseResult<any, any> =
            yield call(ChatService.getService(instanceId).sendMediaFile, file, messageKey);

        if (!sendImageResult.error) {
            const imageMessage: IMessage = yield select(selectors.findMessage(
                (messageItem: IMessage) => messageItem.messageKey === messageKey
            ), instanceId);

            if (imageMessage) {
                /** Add the successful image upload message in the Redux store */
                yield put(setActionResponse(instanceId, {
                    text: translation.common.imageUploadOk,
                    type: ActionResponseType.SUCCESSFUL
                }));
            }
        }
    } else {
        // send it through VXControls API
        const queryParams: types.ISuploadPicRequest = {
            action: ACTION.CMDP_SUPLOADPIC,
            sessionID: session.sessionID,
            channelId: session.channelId,
            messageKey,
            format: "json",
            webtoken
        } as types.ISuploadPicRequest;

        /** Send the request */
        const imageRequest = request
            .post(config.endpoints.imageUpload)
            .query(queryParams)
            .send(data)
			.set("Accept", "application/json");
			
        /**
         * Add the message with the loading animation and when the data will come from the server in the NOOP request,
         * we will remove the animation and render the image
         */
        yield put(addMessage(instanceId, message));

        /**
         * Async waiting for request's response and based on its content,
         * we may apply an error handling on image upload.
         */
		let requestResponse: any;

		try {
			requestResponse = yield imageRequest;
		
		// TODO: we should have an improved approach when dealing with various errors from back-end.
		} catch(error) {
			const translations: IErrorTranslation = yield select(selectors.getTranslationForErrors, instanceId);

			yield put(receiveSendImageError(instanceId, {
				messageKey,
				response: {
					action: ACTION.CMDP_SUPLOADPIC,
					commands: [],
					values: {},
					params: {},
					result: {
						code: ResultCode.NOT_ENOUGH_MONEY,
						reason: translations.error
					}
				}
			}));
		}

        if (!isEmpty(requestResponse)) {
            const { text } = requestResponse;

            try {
                const response = JSON.parse(text);
                const { result: { code }} = response;

                if (code !== VXControlCodes.SUCCESS_EXIT_CODE) {
                    yield put(receiveSendImageError(instanceId, { response, messageKey }));
                }
            } catch(error) {}
        }
    }
};

const receiveSendImageErrorSaga = function* ({ payload }: Action<IPayload<IErrorData>>) {
	const { instanceId, value: { messageKey, response }} = payload;
	yield put(onReceiveError(instanceId, response));
	yield put(removeMessage(instanceId, messageKey));
}

const watchSagas = function* () {
	yield takeEvery(REQUEST_SEND_IMAGE, requestSendImageSaga);
    yield takeEvery(RECEIVE_SEND_IMAGE_ERROR, receiveSendImageErrorSaga);
};

export default watchSagas;
