import { Action } from 'redux-actions';
import { filter, find, get, isEmpty } from 'lodash';
import { put, select, takeEvery } from "redux-saga/effects";
import {
    ACTION,
    CMDC_BACKENDPAYLOAD,
    CMDC_CHANNEL,
    CMDC_CMSG,
    CMDP_NOOP_RESPONSE,
    ICOMMAND,
	IBackEndPayload
} from 'vxcontrol-client-lib';

import types from './types';
import utils from '../../utils';
import { ActionIds } from '../../redux/interfaces';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import { IPartner } from '../../redux/store';
import selectors from '../../redux/selectors';
import { receiveChannelUpdate } from './channel';
import AppService from '../../services/AppService';
import { receiveMessageFromNoop } from './message';
import { setIsVip } from '../../redux/modules/user';
import { getMessagePrice } from '../../redux/selectors/model';
import { BackenPayloadTypes } from '../../static/constants/constants';
import { setFreeModelMessages, setIsOnline, setMessagePrice, setModelOnlineState } from "../../redux/modules/model";
import {
    initializeQuantitiesToInventoryGifts,
    setAutoRecharge,
    setChatFeatures,
    setFreePlatformMessages,
    setOneClickRecharge
} from '../../redux/modules/session';

const createNoopActionType = createActionType(ActionIds.NOOP, { isSagaAction: true });

// Actions
export const RECEIVE_NOOP = createNoopActionType("RECEIVE_NOOP");
const RECEIVE_BACKEND_PAYLOAD = createNoopActionType("RECEIVE_BACKEND_PAYLOAD");
const RECEIVE_ONLINE_STATE = createNoopActionType("RECEIVE_ONLINE_STATE");

// Action Creators
export const onReceiveNoop = createAction<CMDP_NOOP_RESPONSE>(RECEIVE_NOOP);
const receiveBackendPayload = createAction<CMDC_BACKENDPAYLOAD>(RECEIVE_BACKEND_PAYLOAD);
const receiveOnlineState = createAction(RECEIVE_ONLINE_STATE);

// Sagas
const receiveNoopSaga = function* ({ payload }: Action<IPayload<CMDP_NOOP_RESPONSE>>) {
    const { instanceId, value: response } = payload;
    const partner: IPartner = yield select(selectors.getPartner, instanceId);

    // We filter the commands and call their respective sagas
    if (!isEmpty(response) && !isEmpty(response.commands)) {

        // Check for CMDC_MSG
        const receivedMessages = filter(response.commands, (command: ICOMMAND) =>
            command.action === ACTION.CMDC_CMSG
        ) as CMDC_CMSG[];

        if (!isEmpty(receivedMessages)) {
            let message: CMDC_CMSG;

            for (message of receivedMessages) {
                yield put(receiveMessageFromNoop(instanceId, message));
            }
        }

        // Check for CMDC_CHANNEL
        const channelUpdate = find(response.commands, (command: ICOMMAND) =>
            !isEmpty(command.params) &&
            command.action === ACTION.CMDC_CHANNEL &&
            command.params.partnerId === partner.id &&
            command.params.partnerKey === partner.key
        ) as CMDC_CHANNEL;

        if (channelUpdate) {
            yield put(receiveChannelUpdate(instanceId, channelUpdate));
        }

        // Check for ONLINE_STATE
        const filteredCommands = filter(response.commands, command =>
            !isEmpty(command.params) &&
            command.action === ACTION.CMDC_ONLINESTATE &&
            command.params.partnerId === partner.id &&
            command.params.partnerKey === partner.key
        );

        if (!isEmpty(filteredCommands)) {
            yield put(receiveOnlineState(instanceId, filteredCommands));
        }

        // Check for CMDC_BACKENPAYLOAD
        const backenPayloads: CMDC_BACKENDPAYLOAD[] = filter(response.commands,
            (command: ICOMMAND) => command.action === ACTION.CMDC_BACKENDPAYLOAD
        );

        for (const backenPayload of backenPayloads) {
            yield put(receiveBackendPayload(instanceId, backenPayload));
        }
    }
};

/**
 * User's information may update (free messages, bought gifts, payment amounts etc.)
 * and we receive the update from the server in the CMDC_BACKENPAYLOAD response when this happens
 * @param action
 */
const receiveBackendPayloadSaga = function* ({ payload }: Action<IPayload<CMDC_BACKENDPAYLOAD>>) {
    const { instanceId, value } = payload;

    if (isEmpty(get(value, "payload.data"))) {
        return;
    }

	const responseData = value.payload as IBackEndPayload;
    const { type } = responseData;
    const data = responseData.data as types.ILoginGuestResponse;

    const isChannelUpdate = type === BackenPayloadTypes.channel;
    const isGuestUpdate = type === BackenPayloadTypes.guest;

    if (!isEmpty(data.vip)) {
        yield put(setIsVip(instanceId, data.vip));
    }

    if (isGuestUpdate && !isEmpty(data.messages)) {
        yield put(setFreePlatformMessages(instanceId, data.messages.freeCount));
    }

    if (isChannelUpdate && data.freeMessagesCount) {
        yield put(setFreeModelMessages(instanceId, data.freeMessagesCount));
    }

    const messagePrice = yield select(getMessagePrice, instanceId);
    if (isChannelUpdate && data.messagePrice !== messagePrice) {
        yield put(setMessagePrice(instanceId, data.messagePrice));
    }

    if (!isEmpty(data.autoRecharge)) {
        yield put(setAutoRecharge(instanceId, data.autoRecharge));
    }

    if (!isEmpty(data.oneClickRecharge)) {
        yield put(setOneClickRecharge(instanceId, data.oneClickRecharge));
    }

    if (!isEmpty(data.chatFeatures)) {
        yield put(setChatFeatures(instanceId, data.chatFeatures));
    }

    if (!isEmpty(data.boughtMessengerGifts)) {
        yield put(initializeQuantitiesToInventoryGifts(instanceId, data.boughtMessengerGifts));
    }
};

/**
 * Sets model's online state and sets the audio state of the stream
 * between the model and the client, based on model's configuration
 */
const receiveOnlineStateSaga = function* ({ payload }: Action<IPayload>) {
    const { instanceId, value } = payload;

    const onlineStateCommand = value.find(
        (command: any) => command.action === ACTION.CMDC_ONLINESTATE
    );

    if (isEmpty(onlineStateCommand) || isEmpty(onlineStateCommand.params)) {
        return;
    }

    // We use JSON parse to convert 'true/false' string to boolean value
    const isOnline: boolean = JSON.parse(onlineStateCommand.params.online) || false;

    yield put(setModelOnlineState(instanceId,
        utils.getOnlineStateFromResponseParams(onlineStateCommand.params))
    );

    yield put(setIsOnline(instanceId, isOnline));

    const isModelOnline: boolean = yield select(selectors.isModelOnline, instanceId);
    AppService.getService(instanceId).onModelStatusChange(isModelOnline);
};

const watchSagas = function* () {
    yield takeEvery(RECEIVE_NOOP, receiveNoopSaga);
    yield takeEvery(RECEIVE_ONLINE_STATE, receiveOnlineStateSaga);
    yield takeEvery(RECEIVE_BACKEND_PAYLOAD, receiveBackendPayloadSaga);
};

export default watchSagas;
