import { isMobile } from "react-device-detect";

import config from "../static/config";
import { IViewHandlerProps, IViewProviderProps } from "./interfaces";

/**
 * Return flags on how the messenger / videochat app should behave based on the resolution / format
 * @param props ViewHandlerProps
 * @return IViewProviderProps
 */
const getViewProviderParams = (props: IViewHandlerProps): IViewProviderProps => {

    // Initialize
    let result: IViewProviderProps = {
        isMobile,
		isPortrait: !props.isLandscape,
		isLandscape: props.isLandscape,

        isFullscreen: props.isFullscreen,
        isVideoChat: props.isVideoChat,
        containerWidth: props.containerWidth,
        containerHeight: props.containerHeight,
        originalVideoChatWidth: 0,
        originalVideoChatHeight: 0,
        videoChatRatio: 0,
        videoChatWidth: 0,
        videoChatHeight: 0,
        videoChatOptimalWidth: 0,
        videoChatOptimalHeight: 0,
        messengerVideoModeWidth: 0,
        messengerVideoModeHeight: 0,
        isKeyboard: props.isKeyboard,
        shouldRenderOnlyVideochat: props.shouldRenderOnlyVideochat,
        hasActionButtons: props.hasActionButtons,
        switchToPortraitWidth: props.switchToPortraitWidth
	};

    // We are in VideoChat mode
    if (props.resolution && result.isVideoChat) {
        // Save the original width / height of the stream
        result.originalVideoChatWidth = props.resolution.width;
        result.originalVideoChatHeight = props.resolution.height;

        // Get the VideoChat's width / height ratio
        result.videoChatRatio = Math.round((props.resolution.width / props.resolution.height) * 10000) / 10000;

        // Handle mobile or desktop
        result = isMobile ? handleMobileVideoChat(result) : handleDesktopVideoChat(result);
	}

    return result;
};

/**
 * Handle videochat in desktop mode
 * @param result IViewProviderProps
 * @return IViewProviderProps
 */
const handleDesktopVideoChat = (result: IViewProviderProps): IViewProviderProps => {
    // Handle Videochat in fullscreen mode
    if (result.isFullscreen) {
        return handleDesktopFullscreen(result);
    }

	// Handle Videochat in portrait mode
    if (result.containerWidth <= result.switchToPortraitWidth) {
        return handleDesktopPortraitVideoChat(result);
    }

    // Handle Videochat in landscape mode
    return handleDesktopLandscapeVideoChat(result);
};

/**
 * Handle videochat in desktop landscape mode
 * @param result IViewProviderProps
 * @return IViewProviderProps
 */
const handleDesktopLandscapeVideoChat = (result: IViewProviderProps): IViewProviderProps => {
    result.isPortrait = false;
    result.isLandscape = true;
    const { messengerWidthPercentage, videoChatWidthPercentage } = config.viewSizes;

    // Get the width of the messenger based on the window size, in VideoChat mode
    result.messengerVideoModeWidth = Math.round(result.containerWidth * messengerWidthPercentage);

    // Calculate the VideoChat optimal width and height based on the window's size
    result.videoChatOptimalWidth = Math.round(result.containerWidth * videoChatWidthPercentage);
    result.videoChatOptimalHeight = Math.round(result.videoChatOptimalWidth / result.videoChatRatio);

    // Calculate the videochat width and height based on the optimal sizes stored in the config
    result.videoChatWidth = Math.round((
        result.videoChatOptimalWidth + result.messengerVideoModeWidth >= result.containerWidth)
            ? result.containerWidth - result.messengerVideoModeWidth
            : result.videoChatOptimalWidth
    );

    result.videoChatHeight = Math.round(result.videoChatWidth / result.videoChatRatio);

    // If we have a vertical resize and the VideoChat height si larger than window's height
    // then resize everything and recalculate the new width based on the height of the VideoChat
    if (result.videoChatHeight > result.containerHeight) {
        result.videoChatHeight = result.containerHeight;
        result.videoChatWidth = Math.round(result.videoChatHeight * result.videoChatRatio);
    }

    result.messengerVideoModeHeight = result.videoChatHeight;

    return result;
};

/**
 * Handle videochat in desktop portrait mode
 * @param result IViewProviderProps
 * @return IViewProviderProps
 */
const handleDesktopPortraitVideoChat = (result: IViewProviderProps): IViewProviderProps => {
    result.isPortrait = true;
    result.isLandscape = false;

    // Try to set the videochat's width as wide as the window's width
    result.videoChatWidth = result.containerWidth;

    // Adjust the height to maintain the width / height ratio
    result.videoChatHeight = Math.round(result.videoChatWidth / result.videoChatRatio);

    // If the messenger window is not tall enough then resize the videochat to fit the messenger in the screen
    if (result.containerHeight - result.videoChatHeight < config.viewSizes.messengerPortraitMinHeight) {
        result.videoChatHeight = result.containerHeight - config.viewSizes.messengerPortraitMinHeight;
        result.videoChatWidth = Math.round(result.videoChatHeight * result.videoChatRatio);
    }

    return result;
};

/**
 * Handle videochat in fullscreen mode
 * @param result IViewProviderProps
 * @return IViewProviderProps
 */
const handleDesktopFullscreen = (result: IViewProviderProps): IViewProviderProps => {
    // Get the videochat's height by extracting from the window's width, the size of the top and bottom bars
    const { topBarHeight, bottomBarHeight } = config.viewSizes.fullScreen;
    result.videoChatHeight = result.containerHeight - topBarHeight - bottomBarHeight;

    // Recalculate the width based on the height maintaining the width / height ratio
    result.videoChatWidth = Math.round(result.videoChatHeight * result.videoChatRatio);

    return result;
};

/**
 * Handle videochat in mobile mode
 * @param result IViewProviderProps
 * @return IViewProviderProps
 */
const handleMobileVideoChat = (result: IViewProviderProps): IViewProviderProps => {
	if (result.isFullscreen) {
		return handleMobileFullscreenVideoChat(result);
	}

	return result.isPortrait
		? handleMobilePortraitVideoChat(result)
    	: handleDesktopLandscapeVideoChat(result);
}

/**
 * Handle Fullscreen videochat in mobile mode
 * @param result IViewProviderProps
 * @return IViewProviderProps
 */
const handleMobileFullscreenVideoChat = (result: IViewProviderProps): IViewProviderProps => {
    const windowRatio = Math.round((result.containerWidth / result.containerHeight) * 10000) / 10000;

    // unused areas on left and right sides
    if (windowRatio > result.videoChatRatio) {
        result.videoChatHeight = result.containerHeight;
        result.videoChatWidth = Math.round(result.videoChatHeight * result.videoChatRatio);
    // unused areas on top and bottom
    } else {
        result.videoChatWidth = result.containerWidth;
        result.videoChatHeight = Math.round(result.videoChatWidth / result.videoChatRatio);
    }

    return result;
};

/**
 * Handle portrait videochat in mobile mode
 * @param result IViewProviderProps
 * @return IViewProviderProps
 */
const handleMobilePortraitVideoChat = (result: IViewProviderProps): IViewProviderProps => {
    result.videoChatWidth = result.containerWidth;
    // Adjust the height to maintain the width / height ratio
    result.videoChatHeight = Math.round(result.videoChatWidth / result.videoChatRatio);

    // Get the optimal height of the videochat by subtracting the top bar's height and the bottom bar's height
    const { topBarHeight, bottomBarHeight, bottomActionButtonsBarHeight } = config.viewSizes.mobile;

    let videoChatHeight = result.containerHeight - topBarHeight - bottomBarHeight;

    // if no action buttons are rendered, we gain some extra space as height
    if (!result.hasActionButtons) {
        videoChatHeight += bottomActionButtonsBarHeight;
    }

    // Recalculate the width based on the new height if current is larger than optimal
    if (result.videoChatHeight > videoChatHeight) {
        result.videoChatHeight = videoChatHeight;
        result.videoChatWidth = Math.round(result.videoChatHeight * result.videoChatRatio);
    }

    return result;
};

export default getViewProviderParams;
