import styled from "styled-components";
import { isMacOs } from "react-device-detect";

interface IEmoticonProps {
    position?: string;
    className?: string
}

export const Emoticon = styled.span`
    display: inline-flex;
    background-size: 1000%;
    background-repeat: no-repeat;
     
    ${isMacOs
		? `
			width: 1.2em;
			margin: 0 2px 0 0;
		`
		: `
			width: auto;
		`
	}
    
    ${(props: IEmoticonProps) => props.position
        ? `
            ${props.position};
            width: 23px;
            height: 23px;
        `
        : `
            background: none;
            height: auto;
            font-size: 1.2em;
        `
    };
`;

