import * as React from 'react';

import utils from '../../../utils';
import VersionBox from './VersionBox';
import { ITheme } from '../../../themes';
import IStore from '../../../redux/store';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import { openVersion } from '../../../redux/modules/messenger';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IComponentProps {
    theme?: ITheme;
}

interface IStoreProps {
    // Store properties
    isVersionOpen: boolean;

    // Action creators
    openVersion: ActionDispatcher;
}

type IProps = IStoreProps & IComponentProps & IInstanceProps;

class VersionBoxContainer extends React.Component<IProps> {
    componentDidMount() {
        document.addEventListener('keydown', this.keyDownListener);
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.keyDownListener);
    }

    render() {
        return(
            <VersionBox
                isVersionOpen={this.props.isVersionOpen}
                version={utils.versionNumber}
            />
        );
    }

    private keyDownListener = (event: any) => {
        if (event.ctrlKey && event.altKey && event.key === 'v') {
            this.props.openVersion(this.props.instanceId);
        }
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isVersionOpen: selectors.isVersionOpen(store, instanceId)
});


const mapDispatchToProps = {
    openVersion
};

export default withRedux<IComponentProps>(VersionBoxContainer, mapStateToProps, mapDispatchToProps);
