import * as React from 'react';

import utils from "../../../utils";
import selectors from '../../../redux/selectors';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import ToyOptionWithTooltip from "./ToyOptionWithTooltip";
import { IBoundingRect } from "../../common/AdvancedTooltip/interfaces";
import { IProps as ToyOptionProps } from "../../common/ToyOption/ToyOption";
import IStore from '../../../redux/store';

export interface IPartialComponentProps extends ToyOptionProps {
    tooltipImage: string;
    tooltipTitle: string;
    tooltipText: string;
	limitingContainerRef?: any;

    width?: number;
    height?: number;
}

interface IStoreProps {
    isResponsive: boolean;
}

interface IState {
    boundingRect: IBoundingRect;
}

type IComponentProps = IPartialComponentProps;
export type IProps = IComponentProps & IStoreProps & IInstanceProps;

class ToyOptionWithTooltipContainer extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            boundingRect: this.getBoundingRect()
        }
    }

    componentDidMount() {
        window.addEventListener('resize', this.updateBoundingRect);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateBoundingRect);
    }

    render() {
        return (
            <ToyOptionWithTooltip
                {...this.props}
                boundingRect={this.state.boundingRect} />
        );
    }

    private updateBoundingRect = () => {
        this.setState({ boundingRect: this.getBoundingRect() });
    };

    private getBoundingRect = (): IBoundingRect => {
    	if (this.props.limitingContainerRef) {
    		const clientRect = (this.props.limitingContainerRef).getBoundingClientRect();

    		return {
    			left: clientRect.left,
				right: clientRect.right - 15,
				top: clientRect.top,
				bottom: clientRect.bottom
			} as IBoundingRect;
		}

        const { width, height } = utils.getWindowDimensions();

        // limiting tooltip to not go closer that 15 pixels from the screen edges
        return {
            left: 15,
            right: width - 15,
            top: 15,
            bottom: height - 15
        } as IBoundingRect;
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isResponsive: selectors.isResponsive(store, instanceId)
});

export default withRedux<IComponentProps>(ToyOptionWithTooltipContainer, mapStateToProps);
