import * as React from 'react';
import SymbolSectionContainer from '../../specific/SymbolSection/SymbolSectionContainer';
import { DropDownContent } from '../../specific/MessageSection/style';
import MessageSectionButtonsContainer from '../../specific/MessageSectionButtons';
import UserPromptContainer from '../../specific/UserPrompt';
import PerfectScrollbarContainer from '../PerfectScrollbar';
import ActionSectionContainer from '../../specific/ActionSection';
import { ActionSectionWrapper } from './style';

interface IProps {
    isResponsive: boolean;
    isFullscreen: boolean;
    isDropDownOpen: boolean;
    isAnySectionOpen: boolean;
    hasActionSectionInDropDown: boolean;
    areAnyPromptsOpenInResponsiveMode: boolean;
	areAllPopUpsClosed: boolean;
	isGiftSectionOpen: boolean;
	isEmoticonSectionOpen: boolean;
	shouldUseDropdownAnimation: boolean;
}

const MessageSectionPopups = (props: IProps) => (
    <>
        <SymbolSectionContainer />

        {!props.isFullscreen && props.isResponsive && (
            <DropDownContent
                isAnySectionOpen={props.isAnySectionOpen}
                isOpen={props.isDropDownOpen}
                shouldUseDropdownAnimation={props.shouldUseDropdownAnimation}
            >
                <PerfectScrollbarContainer hasFullWidth={true}>
                    <MessageSectionButtonsContainer />

                    {props.hasActionSectionInDropDown && (
						<ActionSectionWrapper>
							<ActionSectionContainer />
						</ActionSectionWrapper>
					)}
                </PerfectScrollbarContainer>
            </DropDownContent>
        )}

        <UserPromptContainer />

    </>
);

export default MessageSectionPopups;
