import * as React from 'react';
import { Emoticon as EmoticonWrapper } from './style';
import { ContentState } from "draft-js";
import { IEmoticon } from "../../../redux/store";
import utils from "../../../utils";

interface IProps {
    contentState: ContentState;
    entityKey: string;
    children: any;
    emoticons: IEmoticon[];
}

const EmoticonDecorator = (props: IProps) => {
    const { contentState, entityKey } = props;
    const icon = contentState.getEntity(entityKey).getData().entity;
    const emoticon = props.emoticons.find((e: IEmoticon) => e.text === icon) || props.emoticons[0];

    return (
        <EmoticonWrapper
            className={utils.getEmoticonClass(emoticon)}
            position={emoticon.position}
            data-entity={icon}>
            {props.children}
        </EmoticonWrapper>
    );
};

export default EmoticonDecorator;