import { DraftHandleValue, Editor as DraftJSEditor, EditorState } from 'draft-js';
import * as React from 'react';
import { MessengerBox } from '../MessageSection/style';
import { isEmpty } from 'lodash';

interface IProps {
	isInteractive: boolean;
	shouldIncreaseMessageFontSize: boolean;
	editorState: EditorState;
	placeholder: string;
	editorRef?: React.RefObject<DraftJSEditor>;

	onChange: any;
	handleReturn: any;
	handleBeforeInput: any;
	onUpArrow: (event: any) => void;
	onDownArrow: (event: any) => void;
	handlePastedText: (text: string, html: string | undefined) => DraftHandleValue;
}

const Editor = (props: IProps) => (
	<MessengerBox
		hasEditor={true}
		isInteractive={props.isInteractive}
		shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}
		hasText={!isEmpty(props.editorState.getCurrentContent().getPlainText())}
	>
		<DraftJSEditor
			ref={props.editorRef}
			editorState={props.editorState}
			readOnly={!props.isInteractive}
			placeholder={props.placeholder}
			handleBeforeInput={props.handleBeforeInput}
			handleReturn={props.handleReturn}
			handlePastedText={props.handlePastedText}
			onChange={props.onChange}
			onUpArrow={props.onUpArrow}
			onDownArrow={props.onDownArrow}
			autoComplete={'off'}
			autoCorrect={'off'}
			autoCapitalize={'off'}
			spellCheck={false}
		/>
	</MessengerBox>
);

export default Editor;
