import { isEmpty } from 'lodash';
import * as React from 'react';
import { isMobile } from "react-device-detect";
import ReactTooltip from 'react-tooltip';
import TooltipWrapper from './style';

export interface ITooltipProps {
    id: string;
    text?: string;
    children: any;
    isHtml?: boolean;
    isClickable?: boolean;
    isLight?: boolean;
    delayHide?: number;
    isSolid?: boolean;
}

const Tooltip = (props: ITooltipProps) => (
    isMobile
        ? props.children
        : (
            <>
                {!isEmpty(props.id) && !isEmpty(props.text) &&
                    <TooltipWrapper>
                        <div 
                            data-tip={props.text}
                            data-for={props.id}
                            data-html={props.isHtml as boolean}
                        >
                            {props.children}
                        </div>

                        <ReactTooltip
                            id={props.id}
                            place={"top"}
                            className={'hover-tooltip'}
                            type={props.isLight ? 'light' : 'dark'}
                            effect={props.isSolid ? 'solid' : 'float'}
                            clickable={props.isClickable || false}
                            delayHide={props.delayHide || 0}
                        />
                    </TooltipWrapper>
                }
            </>
        )
);

export default Tooltip;