import * as React from 'react';
import { MessageEmoticonWrapper } from './style';
import utils from "../../../utils";
import { IEmoticon } from "../../../redux/store";

interface IProps {
    icon: string;
    emoticons: IEmoticon[];
    children?: any;
}

const MessageEmoticon = (props: IProps) => {
    const icon = decodeURIComponent(props.icon);
    
    const emoticon = props.emoticons.find(
        (emoticonItem: IEmoticon) => emoticonItem.text === icon
    ) || props.emoticons[0];

    return (
        <MessageEmoticonWrapper
            className={utils.getEmoticonClass(emoticon)}
            position={emoticon.position}
            data-entity={icon}>
            {props.children}
            <span>{utils.emoticonCodeToPrintableChar(emoticon.code)}</span>
        </MessageEmoticonWrapper>
    );
};

export default MessageEmoticon;
