import { map, isArray } from 'lodash';
import * as React from 'react';
import 'react-perfect-scrollbar/dist/css/styles.css';
import { IGroupMessage, IMessage } from '../../../redux/store';
import GroupContainer from '../../common/Group';
import { MessageList } from './style';
import { IChatBoxProps, CONTENT_UPDATE_TYPE } from './types';
import { getMessagesToRender } from './utils';

const ChatBox = (props: IChatBoxProps) => {
	const messages: any[] = [];

	// Grouped messages
	if (!isArray(props.organizedMessages)) {
		Object.keys(props.organizedMessages).forEach(key => {
			const groupedMessages: React.ReactElement[] = map(props.organizedMessages[key],
				(groupedMessage: (IMessage | IGroupMessage), index: number) =>
					getMessagesToRender(groupedMessage, index, props, key)
			);

			const dayGroupedMessages: React.ReactElement =
				<GroupContainer
					key={key}
					datetime={key}
					showArrow={true}
					hasSectionsInitiallyOpened={props.hasHistorySectionsOpened}
					isDayGroup={true}

					onContentUpdate={props.onContentUpdate(CONTENT_UPDATE_TYPE.GROUP)}
				>
					{groupedMessages}
				</GroupContainer>

			messages.push(props.hasMessagesGrouped ? dayGroupedMessages : groupedMessages);
		});
	} else {
		// Plain messages
		props.organizedMessages.forEach((message: IMessage, index: number) => {
			messages.push(getMessagesToRender(message, index, props));
		});
	}

	return (
		<MessageList isMobileLandscape={props.isMobileLandscape}>
			{messages}
		</MessageList>
	);
};

export default ChatBox;
