import * as React from 'react';
import Arrow, { ArrowDirection } from '../Arrow';
import { WindowTitleWrapper, WindowWrapper, WindowContentWrapper } from './style';
import PerfectScrollbarContainer from '../PerfectScrollbar';

export interface IWindowBoxProps {
    isOpen: boolean;
    isResponsive: boolean;
    children?: any;
    customCloseIcon?: JSX.Element;
    title?: string | JSX.Element;
    isWindowForUserPrompt?: boolean;
    hideHeader?: boolean;
    isFullScreen: boolean;
	shouldUseDropdownAnimation: boolean;

    handleClose: () => void;
}

const WindowBox = (props: IWindowBoxProps) => {
    const { LEFT, DOWN } = ArrowDirection;

    const arrowCloseIcon = (
        <Arrow
            isResponsive={props.isResponsive}
            arrowDirection={props.isResponsive ? LEFT : DOWN}
            onClick={props.handleClose}
        />
    );

    const customCloseIcon = (
        <div onClick={props.handleClose}>
            {props.customCloseIcon}
        </div>
    );

    const windowTitleIcon = props.customCloseIcon ? customCloseIcon : arrowCloseIcon;

    const responsiveTitle = (
        <>
            {windowTitleIcon}
            {props.title}
        </>
    );

    const nonResponsiveTitle = (
        <>
            {props.title}
            {windowTitleIcon}
        </>
    );

    const header = (
        <WindowTitleWrapper>
            {props.isResponsive ? responsiveTitle : nonResponsiveTitle}
        </WindowTitleWrapper>
    );

    return (
        <WindowWrapper
            isOpen={props.isOpen}
            isResponsive={props.isResponsive}
            isFullScreen={props.isFullScreen}
            isWindowForUserPrompt={props.isWindowForUserPrompt}
            shouldUseDropdownAnimation={props.shouldUseDropdownAnimation}
        >
            <PerfectScrollbarContainer>
                <WindowContentWrapper>
                    {!props.hideHeader && header}
                    {props.children}
                </WindowContentWrapper>
            </PerfectScrollbarContainer>
        </WindowWrapper>
    );
};

export default WindowBox;
