import * as React from 'react';
import 'react-perfect-scrollbar/dist/css/styles.css';
import reactStringReplace from 'react-string-replace';
import { forEach, get, includes, isEmpty, map, replace } from 'lodash';

import utils from '../../../utils';
import MessageGift from '../MessageGift';
import MessageEmoticon from '../MessageEmoticon';
import constants from "../../../static/constants";
import { IEmoticon, IGift, IGroupMessage, IMessage, MessageContentType, MessageDirection } from '../../../redux/store';
import { ImageUrlContentWrapper, ImageUrlTextWrapper, MessageItem, MessageList } from './style';
import GroupContainer from '../../common/Group';
import { CONTENT_UPDATE_TYPE, IChatBoxProps } from './types';
import ImageMessageContainer from '../ImageMessage';
import ImageLoader from '../../common/ImageLoader';
import AudioPlayerContainer from '../AudioPlayer';
import MessageContainer from '../Message';

const getImgWidth = (imgMessage: IMessage) =>
	imgMessage.modalContent?.imgWidth && parseInt(imgMessage.modalContent?.imgWidth, 10) || 0;

const getImgHeight = (imgMessage: IMessage) =>
	imgMessage.modalContent?.imgHeight && parseInt(imgMessage.modalContent?.imgHeight, 10) || 0;

/**
 * Translates the text from the editor to the message rendered inside the bubble.
 * In this process, emoticons and gifts shortcuts are replaced by their image.
 * Also, new lines separators are translated for HTML
 */
export const prepareTextMessageForDisplaying = (
	emoticons: IEmoticon[],
	gifts: IGift[],
	message: string,
	mentionOptions: string[]
): any => {
	let result: any[] = [];
	const lines = message.split(constants.MESSAGE_LINE_SEPARATOR);

	lines.forEach((line: string, index: number) => {
		// We transform the emoticons and gifts shortcuts into their images
		result = result.concat(replaceTextWithSpecialCharacters(
			emoticons,
			gifts,
			mentionOptions,
			line,
			index
		));

		result = result.concat();
		if (index < lines.length - 1) {
			result.push(<br key={index} />)
		}
	});

	// We will apply bold styling on all group chat mentions

	return result;
};

/**
 * Being given a line of text, it replaces the emoticons shortcuts and gifts shortcuts
 * with their corresponding images + the group chat mentions with elements with different styling
 * @param emoticons - supported emoticons
 * @param gifts - supported gifts
 * @param groupChatParticipants - active group chat participants
 * @param messageLine - one line of the message
 * @param lineIndex - index of the message line in the whole message
 */
const replaceTextWithSpecialCharacters = (
	emoticons: IEmoticon[],
	gifts: IGift[],
	mentionOptions: string[],
	messageLine: string,
	lineIndex: number
): any => {
	const emoticonIcons = map(emoticons, emoticon => emoticon.text);
	const giftIcons = map(gifts, gift => utils.getGiftEncoding(gift));
	const mentions = map(mentionOptions, participant => `@${participant}`);

	const specialCharactersRegex = utils.getSpecialCharactersRegex(
		emoticons,
		giftIcons,
		mentions
	);

	return reactStringReplace(messageLine, specialCharactersRegex, (match: string, index: number) => {
		const elementKey = `${lineIndex}_${match}_${index}`;

		if (includes(emoticonIcons, match)) {
			return (
				<MessageEmoticon
					key={elementKey}
					icon={match}
					emoticons={emoticons}
				/>
			);
		}

		if (includes(giftIcons, match)) {
			return (
				<MessageGift
					key={elementKey}
					icon={match}
					gifts={gifts}
					hasEnlargedContent={isGiftMessage(gifts, messageLine)}
				/>
			);
		}

		if (includes(mentions, match)) {
			return (
				<b key={elementKey}>{match}</b>
			);
		}

		return match;
	});
};

/**
 *
 * @return {boolean} true if the message is of type text and
 * contains only one emoticon and no other element (text, gifts and so on).
 */
export const isMessageWithJustOneEmoticon = (emoticons: IEmoticon[], content: any): boolean => {
	if (typeof content !== "string") {
		return false;
	}

	const message = content as string;
	const emoticonRegex = utils.getSpecialCharactersRegex(emoticons);
	const emoticonMatch = message.match(emoticonRegex);
	const emoticonCount = emoticonMatch ? emoticonMatch.length : 0;
	const textWithoutEmoticons = message.replace(emoticonRegex, '');
	const textWithoutEmoticonsAndSpaces = textWithoutEmoticons.replace(/\s+/g, '');

	return emoticonCount === 1 && textWithoutEmoticonsAndSpaces.length === 0;
};

/**
 * A gift message represents a message that contains only gifts -
 * no text, emoticons or other elements.
 */
const isGiftMessage = (gifts: IGift[], content: any): boolean => {
	if (typeof content !== "string") {
		return false;
	}

	let encoding: RegExp;
	let message = content as string;
	message = replace(message, " ", "");

	forEach(gifts, (gift: IGift) => {
		encoding = new RegExp(utils.getGiftEncoding(gift), "g");
		message = replace(message, encoding, "");
	});

	return isEmpty(message);
};

export const getMessagesToRender =
	(groupedMessage: (IMessage | IGroupMessage),
	 index: number,
	 props: IChatBoxProps,
	 key?: string) => {
		const isGroup = utils.isGroupMessage(groupedMessage);
		const timestamp = utils.getTimestampForMessage(groupedMessage.dateCreated);
		const opacity = utils.messageOpacity(
			key ? props.organizedMessages[key].length : props.organizedMessages.length,
			index,
			props.isFullscreen
		);

		if (!isGroup) {
			const message = groupedMessage as IMessage;
			return getMessageListing(message, opacity, props);
		}

		const groupMessage = groupedMessage as IGroupMessage;
		const group = map(groupMessage.messages, (message: IMessage, groupIndex: number) =>
			getMessageListing(message, opacity, props, true, groupIndex)
		);

		const messageList = (
			<MessageList key={`${index}_${timestamp}`}>
				{group}
			</MessageList>
		);

		return (
			<GroupContainer
				key={`${index}_${timestamp}`}
				datetime={groupedMessage.dateCreated}
				showArrow={!isEmpty(group)}
				hasSectionsInitiallyOpened={props.hasHistorySectionsOpened}

				onContentUpdate={props.onContentUpdate(CONTENT_UPDATE_TYPE.GROUP)}
			>
				{messageList}
			</GroupContainer>
		);
	}

export const getMessageListing = (message: IMessage, opacity: number, props: IChatBoxProps, isGroupMessage?: boolean, groupIndex?: number) => {
	const { emoticons, gifts, mentionOptions } = props;

	const getTextMessage = () => message.content
		&& prepareTextMessageForDisplaying(
			emoticons,
			gifts,
			message.content as string,
			mentionOptions
		);

	const getImageUrlMessage = () => {
		const text = message.content.text
			&& prepareTextMessageForDisplaying(
				emoticons,
				gifts,
				message.content.text as string,
				mentionOptions
			);

		return (
			<ImageUrlContentWrapper href={message.content.link}>
				<ImageMessageContainer
					id={message.id}
					src={message.content.imgSrc}
					containerWidth={props.width}
					imgWidth={getImgWidth(message)}
					imgHeight={getImgHeight(message)}
					direction={message.direction}
				/>

				<ImageUrlTextWrapper>{text}</ImageUrlTextWrapper>
			</ImageUrlContentWrapper>
		);
	};

	const getImageMessage = () => message.isOnLoading ? <ImageLoader /> : (
		<ImageMessageContainer
			id={message.id}
			src={message.content}
			onClick={props.setModalContent(message.modalContent)}
			containerWidth={props.width}
			imgWidth={getImgWidth(message)}
			imgHeight={getImgHeight(message)}
			direction={message.direction}
		/>
	);

	const getAudioMessage = () => message.content && message.content.audioMp3 &&
		<AudioPlayerContainer audioSrc={message.content.audioMp3} />

	const messageContent = [];
	messageContent[MessageContentType.TEXT] = getTextMessage;
	messageContent[MessageContentType.IMAGE] = getImageMessage;
	messageContent[MessageContentType.IMAGE_URL] = getImageUrlMessage;
	messageContent[MessageContentType.AUDIO] = getAudioMessage;

	const defaultMessageContent = getTextMessage;

	return (
		<MessageItem
			key={message.id}
			opacity={opacity}
			isGroupMessage={isGroupMessage}
		>
			<MessageContainer
				id={message.id}
				type={message.msgType}
				toy={message.toy}
				isSystem={message.isSystem}
				contentType={message.contentType}
				isFullscreen={props.isFullscreen}
				isResponsive={props.isResponsive}
				dateCreated={message.dateCreated}
				username={message.username}
				usernameColor={message.usernameColor}
				direction={message.direction || MessageDirection.none}
				audioSrc={message.content && message.content.audioMp3}
				isMessageWithJustOneEmoticon={isMessageWithJustOneEmoticon(props.emoticons, message.content)}
				groupIndex={groupIndex}
				isImage={includes([MessageContentType.IMAGE, MessageContentType.IMAGE_URL], message.contentType)}

				onContentUpdate={props.onContentUpdate(CONTENT_UPDATE_TYPE.MESSAGES, message)}
			>
				{message.contentType
					? get(messageContent, message.contentType, defaultMessageContent)()
					: defaultMessageContent()
				}
			</MessageContainer>
		</MessageItem>
	)
};
