import * as React from 'react';
import * as base64 from 'base-64';
import { findIndex, forEach, includes, isEmpty, isEqual, isUndefined, last, map, throttle } from 'lodash';
import {
	CharacterMetadata,
	CompositeDecorator,
	ContentBlock,
	DraftHandleValue,
	EditorState,
	EntityInstance,
	Modifier,
	SelectionState,
} from 'draft-js';

import Editor from './Editor';
import utils from '../../../utils';
import GiftDecorator from '../GiftDecorator';
import EmoticonDecorator from '../EmoticonDecorator';
import selectors from '../../../redux/selectors';
import constants from '../../../static/constants';
import { ActionDispatcher } from '../../../redux';
import { decreaseCurrentGiftQuantity, IHandleQuantitiesPayload } from '../../../redux/modules/session';
import IStore, {
	ActionResponseType,
	IActionResponse,
	IEmoticon,
	IGift,
	IStoredEditorState,
	ITranslation
} from '../../../redux/store';
import {
	closeMentionSection,
	openMentionSection,
	resetSymbols,
	setActionResponse,
	setMentionQuery,
	setStoredEditorState
} from "../../../redux/modules/messenger";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import MentionDecorator from '../MentionDecorator';

enum EntityType {
	EMOTICON = "EMOTICON",
	GIFT = "GIFT",
	TEXT = "TEXT",
	MENTION = "MENTION"
}

interface IState {
	editorState: EditorState;
	dropDownOpen: boolean;
	emoticonRegexp: string;
	mentionPosition?: number;
}

interface IComponentProps {
	emoticon?: string;
	gift?: string;
	mention?: string;
	placeholder: string;
	shouldResetEditorState: boolean;

	sendMessage: () => void;
	onTextChanged: (text: string) => void;
}

interface IStoreProps {
	gifts: IGift[];
	hasEditor: boolean;
	isInteractive: boolean;
	isVideoChatInGroupMode: boolean;
	shouldCloseMobileKeyboard: boolean;
	isMentionSectionOpen: boolean;
	shouldDisplayCustomMentionSection: boolean;
	shouldIncreaseMessageFontSize: boolean;
	messagePrice: number;
	translation: ITranslation;
	emoticons: IEmoticon[];
	storedEditorState: IStoredEditorState;
	mentionOptions: string[];
	mentionQuery: string;

	setActionResponse: ActionDispatcher<IActionResponse>;
	setStoredEditorState: ActionDispatcher<IStoredEditorState>;
	openMentionSection: ActionDispatcher;
	closeMentionSection: ActionDispatcher;
	resetSymbols: ActionDispatcher;
	decreaseCurrentGiftQuantity: ActionDispatcher<IHandleQuantitiesPayload>;
	setMentionQuery: ActionDispatcher<string>;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

class EditorContainer extends React.Component<IProps, IState> {
	constructor(props: IProps) {
		super(props);

		this.state = {
			editorState: EditorState.createEmpty(this.getCompositeDecorator(props.emoticons)),
			emoticonRegexp: this.getEmoticonsRegexp(props.emoticons),
			dropDownOpen: false
		};

		this.throttledStoreUpdate = throttle(this.updateStore, 1000);
	}

	private throttledStoreUpdate: (editorState: EditorState) => void;

	private editorRef: any;

	/**
	 * When pasting a text, especially from a message already sent,
	 * we can get useful data about the gifts from the associated pasted html;
	 * here we get gifts id, encoded base64
	 * @param gifts - gifts we are currently dealing with
	 * @param html - pasted html (if any)
	 */
	private static getGiftEntitiesDataFromPastedHTML = (gifts: IGift[], html?: string): string[] => {
		const giftsEntitiesData: string[] = [];
		if (html) {
			const wrappingElement = document.createElement('div');
			wrappingElement.innerHTML = html.trim();
			const giftEncodedIds = gifts.map(gift => base64.encode(gift.id.toString()));
			wrappingElement.querySelectorAll('span[data-entity]')
				.forEach((element: Element) => {
					const entityData = element.getAttribute('data-entity');
					if (entityData && includes(giftEncodedIds, entityData)) {
						giftsEntitiesData.push(entityData);
					}
				});
		}
		return giftsEntitiesData;
	};

	componentDidMount() {
		const storedEditorState: IStoredEditorState = this.props.storedEditorState;

		if (!isEmpty(storedEditorState.text) || !isEmpty(storedEditorState.gifts)) {
			// reconstitute the editor store from the stored text and gifts
			this.setContentFromTextAndGiftIds(
				storedEditorState.text,
				storedEditorState.gifts.map((gift: IGift) => base64.encode(gift.id.toString()))
			);
		}

		this.editorRef = React.createRef<HTMLElement>();
	}

	componentDidUpdate(prevProps: IProps, prevState: IState): void {
		if (this.props.isVideoChatInGroupMode) {
			const editorState = this.getStoringEditorState(this.state.editorState);

			const cursorPosition = this.state.editorState.getSelection().getFocusOffset();
			const slicedText = editorState.text.slice(0, cursorPosition);
			const mentionPosition = slicedText.lastIndexOf(constants.mentionSpecialCharacter);

			const mentionQuery = slicedText.slice(mentionPosition + 1, cursorPosition);

			if (this.props.shouldDisplayCustomMentionSection
				&& utils.hasActiveMentions(this.props.mentionOptions, mentionQuery)
				&& editorState.text !== '') {
				this.props.openMentionSection(this.props.instanceId);
			}

			if (!this.props.shouldDisplayCustomMentionSection) {
				if (!isEqual(this.props.mentionQuery, mentionQuery)) {
					this.props.setMentionQuery(this.props.instanceId, mentionQuery);
				}

				if (prevState.mentionPosition !== mentionPosition) {
					this.setState({ mentionPosition });
				}

				if (mentionPosition === -1) {
					if (this.props.isMentionSectionOpen) {
						this.props.closeMentionSection(this.props.instanceId);
					}
				} else {
					const shouldOpenMentionSection = this.props.isVideoChatInGroupMode &&
						utils.shouldOpenMentionSection(this.props.mentionOptions, mentionQuery, this.props.isMentionSectionOpen);

					if (shouldOpenMentionSection) {
						if (!this.props.isMentionSectionOpen) {
							this.props.openMentionSection(this.props.instanceId);
						}
					} else {
						if (this.props.isMentionSectionOpen) {
							this.props.closeMentionSection(this.props.instanceId);
						}
					}
				}
			}
		}


		/** consider updated emoticons, being them default or backend provided */
		if (!isEqual(prevProps.emoticons, this.props.emoticons)) {
			this.updateEditorState(EditorState.createEmpty(this.getCompositeDecorator(this.props.emoticons)));

			this.setState({
				emoticonRegexp: this.getEmoticonsRegexp(this.props.emoticons)
			})
		}

		/** Gift is always reset to undefined after it has been added */
		if (!prevProps.gift && this.props.gift) {
			this.addGift(this.props.gift);
			this.props.resetSymbols(this.props.instanceId);
		}

		/** Emoticon is always reset to undefined after it has been added */
		if (!prevProps.emoticon && this.props.emoticon) {
			this.addEmoticon(this.props.emoticon);
			this.props.resetSymbols(this.props.instanceId);
		}

		if (!prevProps.mention && this.props.mention) {
			this.addMention(this.props.mention);
			this.props.resetSymbols(this.props.instanceId);
		}

		/** If the message was sent by the parent component, reset editor's store */
		if (this.props.shouldResetEditorState && !prevProps.shouldResetEditorState) {
			this.resetEditorState();
		}

		/**
		 * We update the current quantities based on the inserted gifts in the editor.
		 * If the message has been sent, we keep the current values without adding them
		 * back and make the app look buggy
		 */
		if (this.state.editorState !== prevState.editorState) {
			const plainText = this.getPlainText();

			/** We handle changes only for inventory gifts (the ones with initialQuantity) */
			forEach(this.props.gifts, (gift: IGift) => {
				if (gift.initialQuantity) {
					/** We get the number of occurrences for this inventory gift */
					const encodedGiftId: string = base64.encode(gift.id.toString());
					const matches = plainText.match(new RegExp(encodedGiftId, "gim"));
					const quantityToHandle = matches ? matches.length : 0;

					/** Handle our gift with its updated quantity in our editor */
					this.props.decreaseCurrentGiftQuantity(this.props.instanceId, {
						giftId: gift.id,
						quantityToHandle
					});
				}
			});
		}
	}

	render() {
		return (
			<Editor
				editorState={this.state.editorState}
				isInteractive={this.props.isInteractive}
				placeholder={this.props.placeholder}
				editorRef={this.editorRef}
				shouldIncreaseMessageFontSize={this.props.shouldIncreaseMessageFontSize}

				onChange={this.onChange}
				handleReturn={this.handleReturn}
				handlePastedText={this.handlePastedText}
				handleBeforeInput={this.handleBeforeInput}
				onUpArrow={this.onUpArrow}
				onDownArrow={this.onDownArrow}
			/>
		);
	}


	private static handleEmoticon = (contentBlock: any, callback: any, contentState: any) => {
		EditorContainer.findEntityRange(contentBlock, callback, contentState, EntityType.EMOTICON);
	};

	private static handleGift = (contentBlock: any, callback: any, contentState: any) => {
		EditorContainer.findEntityRange(contentBlock, callback, contentState, EntityType.GIFT);
	};

	private static handleMention = (contentBlock: any, callback: any, contentState: any) => {
		EditorContainer.findEntityRange(contentBlock, callback, contentState, EntityType.MENTION);
	};

	private static findEntityRange = (contentBlock: any, callback: any, contentState: any, type: any) => {
		contentBlock.findEntityRanges(
			(character: CharacterMetadata) => {
				const entityKey = character.getEntity();
				if (entityKey === null) {
					return false;
				}
				return contentState.getEntity(entityKey).getType() === type;
			},
			callback
		);
	};

	private closeMobileKeboardForSmallResolutions = () => {
		if (this.editorRef.current && this.props.shouldCloseMobileKeyboard) {
			this.editorRef.current.blur();
		}
	};

	private getCompositeDecorator = (emoticons: IEmoticon[]) => new CompositeDecorator([{
		strategy: EditorContainer.handleEmoticon,
		component: EmoticonDecorator,
		props: { emoticons }
	}, {
		strategy: EditorContainer.handleGift,
		component: GiftDecorator,
		props: {
			gifts: this.props.gifts
		}
	}, {
		strategy: EditorContainer.handleMention,
		component: MentionDecorator
	}]);

	private getEmoticonsRegexp = (emoticons: IEmoticon[]) => utils.getEmoticonsRegexp(emoticons);

	private onChange = (editorState: EditorState) => {
		this.updateEditorState(editorState);
	};

	private getStoringEditorState = (editorState: EditorState): IStoredEditorState => {
		return {
			text: this.getPlainText(editorState),
			gifts: this.getGiftsFromEditorState(editorState)
		}
	};

	private updateStore = (editorState: EditorState) => {
		this.props.setStoredEditorState(this.props.instanceId, this.getStoringEditorState(editorState));
	};

	private updateEditorState = (editorState: EditorState, callback?: () => void) => {
		const oldText = this.getPlainText(this.state.editorState);
		const text = this.getPlainText(editorState);

		const { instanceId } = this.props;

		if (utils.hasExceededTextLimit(text)) {
			this.props.setActionResponse(instanceId, {
				text: this.props.translation.error.textLimit,
				type: ActionResponseType.WARNING
			});

			return;
		}

		if (!isEqual(text, oldText)) {
			this.props.onTextChanged(text);
			this.throttledStoreUpdate(editorState);
		}

		this.setState({ editorState }, callback);
	};

	private getCursorPosition = () => this.state.editorState.getSelection().getFocusOffset();

	private onUpArrow = (event) => {
		if (this.props.isMentionSectionOpen) {
			event.preventDefault();
		}
	};

	private onDownArrow = (event) => {
		if (this.props.isMentionSectionOpen) {
			event.preventDefault();
		}
	};

	private handleBeforeInput = (char: string): DraftHandleValue => {
		let handled = false;
		let { editorState } = this.state;

		const blockKey = editorState.getSelection().getStartKey();
		const existingText = editorState.getCurrentContent().getBlockForKey(blockKey).getText();
		const position = this.getCursorPosition();
		const newText = existingText.substr(0, position) + char + existingText.substr(position);

		if (utils.hasExceededTextLimit(newText)) {
			this.props.setActionResponse(this.props.instanceId, {
				text: this.props.translation.error.textLimit,
				type: ActionResponseType.WARNING
			});

			return "handled";
		}

		const findEmoticonCallback = (start: number, end: number) => {
			const emoticon = newText.substr(start, end - start);
			editorState = this.getEditorStateWithEntity(emoticon, char, start, end, EntityType.EMOTICON);

			this.updateEditorState(
				EditorState.forceSelection(
					editorState,
					editorState.getCurrentContent().getSelectionAfter()
				)
			);

			handled = true;
		};

		utils.findEmoticonWithRegex(newText, this.state.emoticonRegexp, findEmoticonCallback);

		if (char === constants.mentionSpecialCharacter && this.props.isVideoChatInGroupMode) {
			const selectionState = this.state.editorState.getSelection();
			const focusOffset = selectionState.getFocusOffset();

			this.props.openMentionSection(this.props.instanceId, false);
			this.setState({
				mentionPosition: focusOffset
			});
		}

		return handled ? "handled" : "not-handled";
	};

	private getCharBehindIcon = (emoticonShortcut: string) => {
		const { emoticons } = this.props;
		const emoticon =
			emoticons.find((emoticonItem: IEmoticon) => emoticonItem.text === emoticonShortcut) || emoticons[0];

		return utils.emoticonCodeToPrintableChar(emoticon.code);
	};

	private getEditorStateWithEntity = (
		entity: string,
		insertedChars: string,
		start: number,
		end: number,
		entityType: EntityType = EntityType.EMOTICON,
		currentEditorState?: EditorState
	): EditorState => {
		/** Get editor's content store */
		const editorState = currentEditorState || this.state.editorState;
		const contentState = editorState.getCurrentContent();

		/** We create a new entity. Text words should be kept mutable for future changes */
		const mutabilityType = entityType === EntityType.TEXT
			? "MUTABLE"
			: "IMMUTABLE";

		const contentStateWithEntity = contentState.createEntity(entityType, mutabilityType, {
			entity
		});

		const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
		const blockKey = editorState.getSelection().getStartKey();

		const selection = new SelectionState({
			anchorKey: blockKey,
			anchorOffset: start,
			focusOffset: end,
			focusKey: blockKey
		});

		const hiddenTextUnderEntity: { [entityType: string]: string } = {};
		hiddenTextUnderEntity[EntityType.EMOTICON] = this.getCharBehindIcon(entity);
		hiddenTextUnderEntity[EntityType.GIFT] = utils.CHAR_BEHIND_GIFTS;
		hiddenTextUnderEntity[EntityType.TEXT] = entity;
		hiddenTextUnderEntity[EntityType.MENTION] = `@${entity}`;

		let newContentState = Modifier.replaceText(
			contentState,
			selection,
			hiddenTextUnderEntity[entityType],
			undefined,
			entityKey
		);

		if (entityType === EntityType.MENTION) {
			const newSelectionState = newContentState.getSelectionAfter();

			newContentState = Modifier.insertText(
				newContentState,
				newSelectionState,
				' '
			);
		}

		const newEditorState = EditorState.push(
			editorState,
			newContentState,
			'insert-characters'
		);

		return EditorState.forceSelection(
			newEditorState,
			newEditorState.getCurrentContent().getSelectionAfter()
		);
	};

	private getPlainText = (editorState: EditorState = this.state.editorState) => {
		let result = "";
		let selectedEntity: any;

		const content = editorState.getCurrentContent();
		const blocks: ContentBlock[] = content.getBlocksAsArray();
		const lastBlock = last(blocks);

		blocks.forEach((block: ContentBlock) => {
			let previousPosition = 0;
			const plainText = block.getText();

			block.findEntityRanges(
				(character: CharacterMetadata) => {
					const entityKey = character.getEntity();

					if (entityKey !== null) {
						selectedEntity = content.getEntity(entityKey);
						const entityType = selectedEntity.getType();

						return entityType === EntityType.EMOTICON || entityType === EntityType.GIFT;
					}

					return false;
				},
				(start, end) => {
					result += plainText.substr(previousPosition, start - previousPosition) +
						selectedEntity.getData().entity;
					previousPosition = end;
				}
			);

			result += plainText.substring(previousPosition);

			if (block !== lastBlock) {
				result += constants.MESSAGE_LINE_SEPARATOR;
			}
		});

		return result;
	};

	private getGiftsFromEditorState = (editorState: EditorState = this.state.editorState) => {
		const gifts: IGift[] = [];
		const content = editorState.getCurrentContent();
		let entity: EntityInstance;

		content.getBlocksAsArray().forEach((block) => {
			block.findEntityRanges(
				(character) => {
					if (character.getEntity() !== null) {
						entity = content.getEntity(character.getEntity());
						return entity.getType() === EntityType.GIFT;
					}

					return false;
				},
				() => {
					const foundGift: IGift | undefined = this.props.gifts
						.find((gift: IGift) => base64.encode(gift.id.toString()) === entity.getData().entity
						);

					if (foundGift) {
						gifts.push(foundGift);
					}
				}
			);
		});

		return gifts;
	};

	private onTextMessageAdd = () => {
		if (this.getPlainText()) {
			/** Send the message to the API Endpoint */
			this.props.sendMessage();

			/** Reset our editor store */
			this.resetEditorState();
		}
	};

	private handleReturn = (event: KeyboardEvent): DraftHandleValue => {
		if (this.props.isMentionSectionOpen) {
			return "handled";
		}

		if (!event.shiftKey) {
			this.onTextMessageAdd();

			return "handled";
		}

		return "not-handled";
	};

	private addEmoticon = (emoticon: string): void => {
		const newEditorState = this.getEditorStateAfterAddingEntity(
			this.state.editorState,
			emoticon,
			EntityType.EMOTICON
		);

		this.updateEditorState(newEditorState);
	};

	private addGift = (gift: string): void => {
		const newEditorState = this.getEditorStateAfterAddingEntity(
			this.state.editorState,
			gift,
			EntityType.GIFT
		);

		this.updateEditorState(newEditorState);
	};

	private addMention = (mention: string): void => {
		const newEditorState = this.getEditorStateAfterAddingEntity(
			this.state.editorState,
			mention,
			EntityType.MENTION
		);

		this.updateEditorState(newEditorState);
	};

	/** Adds an entity at the current cursor's position */
	private getEditorStateAfterAddingEntity = (
		editorState: EditorState,
		entity: string,
		entityType: EntityType
	): EditorState => {

		/** Get current text and cursor's position */
		const selection = editorState.getSelection();
		const focusOffset = selection.getFocusOffset();
		let startPosition = selection.getAnchorOffset();

		if (entityType === EntityType.MENTION) {
			const startPositionForMention = !isUndefined(this.state.mentionPosition)
				? this.state.mentionPosition
				: selection.getAnchorOffset();
			const startPositionForCustomMention = focusOffset;

			startPosition = this.props.shouldDisplayCustomMentionSection
				? startPositionForCustomMention
				: startPositionForMention;
		}

		const endPosition = entityType === EntityType.MENTION
			? focusOffset
			: focusOffset + entity.length;

		/** Create new current text store with the inserted entity */
		const newEditorState = this.getEditorStateWithEntity(
			entity,
			entity,
			startPosition,
			endPosition,
			entityType,
			editorState
		);

		return newEditorState;
	};

	/** Apply editor's emoticons & gifts decorations on copy-pasted texts */
	private handlePastedText = (pastedText: string, html: string | undefined): DraftHandleValue => {
		/** Process the copy-paste event only if our text is not empty */
		if (!pastedText) {
			return "not-handled";
		}

		const { gifts } = this.props;
		const pastedGiftEntitiesData: string[] = EditorContainer.getGiftEntitiesDataFromPastedHTML(gifts, html);

		// remove new lines characters, introduced by draft-js
		const text = pastedText.replace(/\n/g, '');

		this.setContentFromTextAndGiftIds(text, pastedGiftEntitiesData);

		return "handled";
	};

	/**
	 * @param {string} plainText returned by content store (the one with special chars for emoticons and gifts
	 * @param {string[]} contentGiftsEncodedIds - array of encoded ids for the gifts presumed to be in the plain text
	 */
	private setContentFromTextAndGiftIds = (plainText: string, contentGiftsEncodedIds: string[]) => {
		const { emoticons, gifts } = this.props;
		const emoticonsChars = emoticons.map((emoticon: IEmoticon) => ({
				text: emoticon.text,
				char: utils.emoticonCodeToPrintableChar(emoticon.code)
			})
		);

		const giftSplittingDelimiter = utils.CHAR_BEHIND_GIFTS;
		const emoticonSplittingDelimiters = emoticonsChars.map(item => item.char).join('|');

		const emoticonsAndGiftSplittingDelimiters = new RegExp(
			`(${emoticonSplittingDelimiters}|${giftSplittingDelimiter})`, 'gim'
		);

		// In some context, the emoticons and gifts do not come as special chars,
		// but as their plain text shortcut, therefore, we need to map to their chars
		let text = plainText;
		emoticonsChars.forEach(emoticonChar => {
			text = text.replace(utils.itemToRegex(emoticonChar.text), emoticonChar.char);
		});

		gifts.forEach(gift => {
			const giftId = utils.itemToRegex(utils.getGiftEncoding(gift));
			text = text.replace(giftId, giftSplittingDelimiter);
		});

		const textSplitByEmoticonsAndGifts = text.split(emoticonsAndGiftSplittingDelimiters);

		// replace emoticons with shortcuts ( :) ;) :D :P ....) and gifts with their encoded ids
		let giftIndex = 0;
		const textSplitAndDecoded: string[] = textSplitByEmoticonsAndGifts
			.map((textEmoticonOrGiftId: string) => {
				const emoticonCharIndex = findIndex(emoticonsChars,
					emoticonChar => textEmoticonOrGiftId === emoticonChar.char
				);

				// found an emoticon in the text; replace it with its shortcut
				if (emoticonCharIndex !== -1) {
					return emoticonsChars[emoticonCharIndex].text;
				}

				// it is a gift in the provided text; replace it with its encoded id
				if (textEmoticonOrGiftId === utils.CHAR_BEHIND_GIFTS) {
					return contentGiftsEncodedIds[giftIndex++];
				}

				// it is a text; leave it as it was
				return textEmoticonOrGiftId;
			});

		/** Obtain the emoticons and gifts values for comparing our entities */
		const emoticonIcons: string[] = utils.getEmoticonsShortcuts(this.props.emoticons);
		const giftsEncodedIds: string[] = map(this.props.gifts, (gift: IGift) => utils.getGiftEncoding(gift));

		/** For each entity, update editor's content based on the correct entity type */
		let currentEditorState = this.state.editorState;

		forEach(textSplitAndDecoded, (splitEntity: string) => {
			if (!splitEntity) {
				// this happens when we get no clue about the pasted entity from the associated html
				return;
			}

			let entityType = EntityType.TEXT;

			if (includes(emoticonIcons, splitEntity)) {
				entityType = EntityType.EMOTICON;
			}
			if (includes(giftsEncodedIds, splitEntity)) {
				entityType = EntityType.GIFT;
			}

			/** Get the new editor store */
			currentEditorState = this.getEditorStateAfterAddingEntity(
				currentEditorState,
				splitEntity,
				entityType
			);
		});

		/** Update editor's store */
		this.updateEditorState(currentEditorState);
	};

	private resetEditorState = () => {
		this.updateEditorState(EditorState.moveFocusToEnd(
			EditorState.createEmpty(this.getCompositeDecorator(this.props.emoticons))),
			this.closeMobileKeboardForSmallResolutions
		);
	};
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	hasEditor: selectors.hasEditor(store, instanceId),
	gifts: selectors.getTranslatedGifts(store, instanceId),
	translation: selectors.getTranslation(store, instanceId),
	isInteractive: selectors.isInteractive(store, instanceId),
	messagePrice: selectors.getMessagePrice(store, instanceId),
	emoticons: selectors.getEmoticons(store, instanceId),
	storedEditorState: selectors.getEditorState(store, instanceId),
	shouldCloseMobileKeyboard: selectors.shouldCloseMobileKeyboard(store, instanceId),
	isMentionSectionOpen: selectors.isMentionSectionOpen(store, instanceId),
	mentionOptions: selectors.getMentionOptions(store, instanceId),
	isVideoChatInGroupMode: selectors.isVideoChatInGroupMode(store, instanceId),
	shouldDisplayCustomMentionSection: selectors.shouldDisplayCustomMentionSection(store, instanceId),
	mentionQuery: selectors.getMentionQuery(store, instanceId),
	shouldIncreaseMessageFontSize: selectors.getShouldIncreaseMessageFontSize(store, instanceId)
});

const mapDispatchToProps = {
	decreaseCurrentGiftQuantity,
	setStoredEditorState,
	setActionResponse,
	openMentionSection,
	closeMentionSection,
	resetSymbols,
	setMentionQuery
};

export default withRedux<IComponentProps>(EditorContainer, mapStateToProps, mapDispatchToProps);
