import * as React from 'react';
import { isEmpty } from 'lodash';

import constants from '../../../static/constants'
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import FullScreenLightBox from './FullScreenLightBox';
import IStore, { IModalContent } from '../../../redux/store';
import { resetModalContent } from '../../../redux/modules/messenger';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IComponentProps {
    closeLightbox?: any;
}

interface IStoreProps {
    modalContent: IModalContent;

    resetModalContent: ActionDispatcher;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

class FullScreenLightBoxContainer extends React.PureComponent<IProps> {
    componentDidMount() {
        document.addEventListener("keydown", this.closeModal, false);
    }

    componentWillUnmount() {
        document.removeEventListener("keydown", this.closeModal);
    }

    render() {
        return (
            <>
                {!isEmpty(this.props.modalContent.imgSrc) &&
                    <FullScreenLightBox
                        modalContent={this.props.modalContent}
                        closeLightbox={this.resetModalContent}
                    />
                }
            </>
        );
    }

    private closeModal = (event) => {
        if (event.keyCode === constants.ESC_KEY_CODE) {
            this.resetModalContent();
        }
    }

    private resetModalContent = () => {
        this.props.resetModalContent(this.props.instanceId);
    }
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    modalContent: selectors.getModalContent(store, instanceId)
});

const mapDispatchToProps = {
    resetModalContent
};

export default withRedux<IComponentProps>(FullScreenLightBoxContainer, mapStateToProps, mapDispatchToProps);