import styled from "styled-components";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface IBaseProps {
	isToySectionMinimizied: boolean;
}

interface IInlineArrow {
    isResponsive: boolean;
}

export const Wrapper = withCustomClasses<IBaseProps>(styled.div<IBaseProps>`
    cursor: pointer;
    
    ${props => {
        const { primaryColor, secondaryColor } = props.theme.colors.toySection;
        
        return props.isToySectionMinimizied
			? `
	            background-color: ${props.theme.colors.toySection.headerSectionBackground};
	            border-color: ${props.theme.colors.toySection.headerSectionBorderColor};
	            border-width: 1px;
	            border-style: solid;
	            padding: 10px;
	            height: 25px;
            `
	        :  `
	            color: ${props.theme.colors.toySection.text};
	            background-image: -webkit-gradient(linear,left top,right top,from(${primaryColor}),to(${secondaryColor}));
	            background-image: linear-gradient(to right,${primaryColor},#${secondaryColor});
	            padding: 10px 0;
            `;
    }}
`, [DomCustomClass.ToySectionHeader]);

const Inline = styled.div`
    display: inline-block;
`;

export const InlineArrow = styled(Inline)<IInlineArrow>`
    float: ${props => props.isResponsive ? 'left' : 'right'};
    ${props => props.isResponsive && `margin-right: 15px;`}
    ${props => props.isResponsive && `margin-left: -12px;`}
    font-size: 18px;
    height: 100%;
    display: flex;
    align-items: center;
`;

export const InlineTitleElement = styled(Inline)<IBaseProps>`
    color: ${props => {
        const { primaryColor, text } = props.theme.colors.toySection;
        
		return props.isToySectionMinimizied ? primaryColor : text;
    }};

    font-weight: ${props => props.theme.typography.weight.medium};

    font-size: 20px;
    margin-right: 5px;
`;

export const InlineLink = styled(Inline)`
    color: ${props => props.theme.colors.toySection.headerSectionLink};
    
    height: 18px;
    font-size: 15px;
    font-weight: 400;
    margin-left: 8px;
    text-decoration: underline;
`;
