import * as React from "react";
import ReactDOM from "react-dom";
import { isEqual } from 'lodash';

import {
    BottomContentWrapper, Content,
    DownArrow,
    LeftArrow,
    LeftContentWrapper,
    RightArrow,
    RightContentWrapper,
    TopContentWrapper,
    UpArrow
} from "./style";
import {ICorrections} from "./interfaces";
import {canAdjustHorizontally, canAdjustVertically, getBoundingRect, getRectWithIntegralValues} from "./utils";
import {isUndefined} from "util";
import {IBoundingRect, IDimensions, TooltipPlacement} from "../AdvancedTooltip/interfaces";

interface IProps {
    children: any;
    target?: any;
    placement?: TooltipPlacement;
    boundingRect?: IBoundingRect;
}

interface IContentProps {
	isHidden?: boolean;
}

type IState = ICorrections & IDimensions & IContentProps;

/**
 * @class react class component dealing with tooltips displayed with absolute positioning
 */
class AdvancedTooltipContentContainer extends React.Component<IProps, IState> {

    constructor(props) {
        super(props);

        this.state = {
            width: 0,
            height: 0,
            horizontal: 0,
            vertical: 0,
			isHidden: true
        };
    }

    private isUnmounted: boolean;

    componentDidMount() {
        this.isUnmounted = false;
        this.grabDimensionsAndCorrections(false);
    }

    componentDidUpdate(prevProps: IProps, prevState: IState) {
        this.grabDimensionsAndCorrections(!isEqual(this.props.boundingRect, prevProps.boundingRect));
    }

    componentWillUnmount() {
        this.isUnmounted = true;
    }

    render() {
        const content = <Content
			isHidden={this.state.isHidden}>
			{this.props.children}
		</Content>;

        const contents: {[key: string]: React.ReactElement} = {};

        contents[TooltipPlacement.TOP] = (
            <TopContentWrapper {...this.state}>
                {content}
                <DownArrow {...this.state} />
            </TopContentWrapper>
        );

        contents[TooltipPlacement.BOTTOM] = (
            <BottomContentWrapper {...this.state}>
                <UpArrow {...this.state} />
                {content}
            </BottomContentWrapper>
        );

        contents[TooltipPlacement.LEFT] = (
            <LeftContentWrapper {...this.state} >
                {content}
                <RightArrow {...this.state} />
            </LeftContentWrapper>
        );

        contents[TooltipPlacement.RIGHT] = (
            <RightContentWrapper {...this.state} >
                <LeftArrow {...this.state}/>
                {content}
            </RightContentWrapper>
        );

        return (
            <>
                {contents[this.props.placement || TooltipPlacement.TOP]}
            </>
        );
    }

    /**
     * call the provided callback in an animation frame context
     * @param callback
     */
    private doInAnimationFrame = (callback: (node) => any) => {
        window.requestAnimationFrame(() => {
            if (!this.isUnmounted) {
                const node: any = ReactDOM.findDOMNode(this);
                if (!isUndefined(node)) {
                    callback(node);
                }
            }
        });
    };

    private grabDimensionsAndCorrections = (isBoundRectChanged: boolean) => {
        this.doInAnimationFrame((node) => {
            const { width, height } = node.getBoundingClientRect();

            if (this.state.width !== width || this.state.height !== height || isBoundRectChanged) {
                this.setState({ width, height }, () => {
                    this.updateCorrections();
                });
            }

			setTimeout(() => {
				if (!this.isUnmounted) {
					this.setState({ isHidden: false });
				}
			}, 100);
        });
    };

    private updateCorrections = () => {
        this.doInAnimationFrame((node) => {
            const { horizontal, vertical } =
                this.getViewportCorrections(node.getBoundingClientRect());

            if (horizontal !== this.state.horizontal || vertical !== this.state.vertical) {
                this.setState({ horizontal, vertical });
            }
        });
	};

    private getViewportCorrections = (rect: ClientRect | DOMRect): ICorrections => {

        let { horizontal, vertical } = this.state;

        const { top, bottom, left, right } = getRectWithIntegralValues(rect);
        const boundingRect: IBoundingRect = getRectWithIntegralValues(getBoundingRect(this.props.boundingRect));

        let isCorrected = false;

        if (canAdjustHorizontally(this.props.placement)) {
            const shouldApplyPositiveCorrection = left < boundingRect.left;
            const shouldApplyNegativeCorrection = right > boundingRect.right;

            if (shouldApplyPositiveCorrection) {
                // adjust horizontally with a positive value
                horizontal += boundingRect.left - left;
                isCorrected = true;
            }

            if (shouldApplyNegativeCorrection) {
                // adjust horizontally with a negative value
                horizontal += boundingRect.right - right;
                isCorrected = true;
            }

            if (horizontal && !isCorrected) {

                // check again if we can narrow the existing horizontal correction
                if (!shouldApplyPositiveCorrection && horizontal >= 0) {
                    const spaceAvailableToLeft = left - boundingRect.left;
                    // re-adjust horizontal correction (but keep it positive or zero)
                    horizontal = Math.max(0, horizontal - spaceAvailableToLeft);
                } else if (!shouldApplyNegativeCorrection && horizontal <= 0) {
                    const spaceAvailableToRight = boundingRect.right - right;
                    // re-adjust horizontal correction (but keep it negative or zero)
                    horizontal = Math.min(0, horizontal + spaceAvailableToRight);
                }
            }
        }

        if (canAdjustVertically(this.props.placement)) {
            const shouldApplyPositiveCorrection = top < boundingRect.top;
            const shouldApplyNegativeCorrection = bottom > boundingRect.bottom;


            if (shouldApplyPositiveCorrection) {
                // adjust vertically with a positive value
                vertical += boundingRect.top - top;
                isCorrected = true;
            }

            if (shouldApplyNegativeCorrection) {
                // adjust vertically with a negative value
                vertical += boundingRect.bottom - bottom;
                isCorrected = true;
            }

            if (vertical && !isCorrected) {

                // check again if we can narrow the existing vertical correction
                if (!shouldApplyPositiveCorrection && vertical >= 0) {
                    const spaceAvailableToTop = top - boundingRect.top;
                    // re-adjust vertical correction (but keep it positive or zero)
                    vertical = Math.max(0, vertical - spaceAvailableToTop);
                } else if (!shouldApplyNegativeCorrection && vertical < 0) {
                    const spaceAvailableToBottom = boundingRect.bottom - bottom;
                    // re-adjust vertical correction (but keep it negative or zero)
                    vertical = Math.min(0, vertical + spaceAvailableToBottom);
                }
            }
        }

        return { horizontal, vertical };
    };
}

export default AdvancedTooltipContentContainer;
