import { set } from 'lodash';
import styled from 'styled-components';
import { isMobile } from 'react-device-detect';

import config from '../../../static/config';
import withIdentifier, { DomIdentifier } from '../../hoc/withIdentifier';

interface IMentionSectionWrapperProps {
	isOpen: boolean;
	shouldDisplayCustomMentionSection: boolean;
	isFullScreen: boolean;
}

export const selectStyles = ({ isFullScreen, shouldDisplayCustomMentionSection }) => {
	const styles = {
		container: () => ({
			position: 'relative',
			boxSizing: 'border-box',
			width: `calc(100% + ${isMobile || shouldDisplayCustomMentionSection ? 0 : 12}px)`,
			bottom: isFullScreen ? '100%' : '1px',
			zIndex: '4'
		}),
		menu: (provided) => ({
			...provided,
			top: 'auto',
			bottom: '0',
			left: isMobile
				? '5px'
				: '0',
			margin: '0',
			padding: '0',
			width: isMobile
				? 'calc(100% - 10px)'
				: shouldDisplayCustomMentionSection ? '100%' : 'auto',
			position: 'relative',
			borderRadius: '0'
		}),
		menuList: (provided) => ({
			...provided,
			padding: '0',
			maxHeight: '150px'
		}),
		option: (_, state) => ({
			backgroundColor: state.isFocused ? '#ECECEE' : 'auto',
			color: 'black',
			padding: '8px 10px',
			cursor: 'pointer'
		}),
	};

	if (!isMobile) {
		set(styles.menu, 'boxShadow', 'none');
		set(styles.menu, 'minWidth', '212px');
		set(styles.menu, 'maxWidth', '90%');
	}

	if (isFullScreen) {
		set(styles.container, 'left', '-5x');
	}

	return styles;
};

export const getMentionSectionWrapper = (identifier: DomIdentifier) => withIdentifier<IMentionSectionWrapperProps>(
	styled.div<IMentionSectionWrapperProps>`
		max-height: ${props => props.isOpen ? '100%' : '0'};
		
		${props => props.shouldDisplayCustomMentionSection
		? `
				bottom: ${props.isFullScreen ? '100%' : '0'};
				transition: max-height ${config.popupsTransitionTime}s ease-in-out;
				transition-delay: ${config.popupsTransitionTime};
				overflow: hidden;
				width: 100%;
			`
		: `
				bottom: ${props.isFullScreen ? '100%' : '10px'};
				${isMobile ? `width: 100%;` : `left: 10px;`}
			`
	}
		
		position: absolute;
	`, identifier
);

/**
 * Set styles for a certain div, but without styled components
 * because currently, the reference for a styled components element is not coming right.
 * FUTURE TODO try to use styled components again when upgrading the library
 * @param props
 */
export const getStylesForMentionSectionContainer = (props: IMentionSectionWrapperProps) => {
	if (!isMobile && !props.shouldDisplayCustomMentionSection) {
		return {};
	}

	let height = 0;

	if (props.isOpen) {
		height = props.shouldDisplayCustomMentionSection ? 180 : 150;
	}

	return {
		position: "absolute" as "absolute",
		left: 0,
		bottom: 0,
		width: '100%',
		height
	};
};
