import * as React from 'react';
import { RefObject } from 'react';
import * as cuid from 'cuid';
import { VideoChatContainerBlanket } from './style';

interface IProps {
    isFrozen: boolean;
    container: HTMLElement | null;
    shouldHideVideochatControls: boolean;

    displayVideoChatControls: () => void;
    onCreateContainer: (container: HTMLDivElement) => any;
}

class VideoChat extends React.Component<IProps> {

    private ref: RefObject<HTMLDivElement>;

    constructor(props: IProps) {
        super(props);
        this.ref = React.createRef();
    }

    componentDidMount() {
        const componentNode = this.ref.current;

        if (componentNode) {
            const existingContainer = this.props.container;
            if (existingContainer) {
                componentNode.appendChild(existingContainer);
            } else {
                const newContainer = document.createElement('div');
                newContainer.setAttribute('style', "width: 100%; height: 100%;");
                newContainer.setAttribute("id", cuid.slug());
                componentNode.appendChild(newContainer);
                this.props.onCreateContainer(newContainer);
            }
        }
    }

    render() {
        return (
            <>
                <div ref={this.ref} style={this.getStyle()} />
                {this.props.shouldHideVideochatControls &&
                    <VideoChatContainerBlanket
                        onClick={this.props.displayVideoChatControls}
                        onMouseMove={this.props.displayVideoChatControls}
                    />
                }
            </>
        );
    }

    private getStyle = (): React.CSSProperties => {
        const style: React.CSSProperties = {
            width: "100%",
            height: "100%"
        };

        if (this.props.isFrozen) {
            style.transition = ".5s ease";
            style.opacity = 0.3;
        }

        return style;
    };
}

export default VideoChat;
