import { get } from 'lodash';

import { IData } from '../redux/store';
import store from '../redux/configureStore';
import { hasActionButtons } from '../redux/selectors/common';
import { getInitialInstanceData } from '../redux/initialStore';
import { closeMessenger as closeMessengerAction } from '../redux/modules/messenger';
import constants from '../static/constants';

export const closeMessenger = (instanceId?: string) => {
    store.dispatch(closeMessengerAction(instanceId || constants.defaultInstanceId));
};

const shouldReturnStore: boolean = process.env.GET_STORE_AVAILABLE === 'true';

export const getStore = (instanceId: string): IData | undefined => {
    if (!shouldReturnStore) {
        return;
    }

    const state = store.getState();

    return hasActionButtons(state, instanceId)
        ? get(state, instanceId)
        : getInitialInstanceData();
};
