import * as React from 'react';

import { onTimeIsOver } from './utils';
import selectors from '../../../redux/selectors';
import LivePreviewTextTimer from "./LivePreviewTextTimer";
import Store from '../../../redux/store';
import { setLivePreviewTextTimeLeft } from "../../../redux/modules/videochat";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps } from "./types";

const onUnmount = (props: IProps) => (time?: number) => {
    props.setLivePreviewTextTimeLeft(props.instanceId, time);
}

const LivePreviewTextTimerContainer = (props: IProps) => {
    const currentTime = props.timeLimit - props.timeLeft;

    return (
        <LivePreviewTextTimer
            translation={props.translation}
            totalTime={props.timeLimit}
            currentTime={currentTime}
            onUnmount={onUnmount(props)}
            onTimeIsOver={onTimeIsOver(props)}
        />
    );
};

const mapStateToProps = (store: Store, { instanceId }: IInstanceProps) => ({
    translation: selectors.getTranslation(store, instanceId),
    timeLimit: selectors.getLivePreviewTextTimeLimit(store, instanceId),
    timeLeft: selectors.getLivePreviewTextTimeLeft(store, instanceId)
});


const mapDispatchToProps = {
    setLivePreviewTextTimeLeft
};

export default withRedux(LivePreviewTextTimerContainer, mapStateToProps, mapDispatchToProps);
