import { get } from "lodash";
import { createSelector } from "reselect";

import IStore from "../store";
import { getInitialInstanceData } from "../initialStore";

export const getSession = (store: IStore, instanceId: string | undefined) => {
	const id = instanceId as string;
	const data = get(store, id) || getInitialInstanceData();
	return data.session;
};

/**
 * @return {string} id of the video chat session
 */
export const getSessionId = createSelector(getSession, session => session.sessionID);

/**
 * @return {Currency} the active currency received from the init response. For VXPages we have Credits, for others, euro or dollar
 */
export const getCurrency = createSelector(getSession, session => session.currency);

/**
 * @return {IChat | undefined} - structure containing identifiers used to create a vchat-core instance
 */
export const getChat = createSelector(getSession, session => session.chat);

/**
 * @return {IChat | undefined} - structure containing identifiers from the parent platform
 * in order to create a vchat-core instance
 */
export const getExternallyProvidedChat = createSelector(getSession, session => ({
	clientId: session.chat?.clientId,
	server: session.chat?.server,
	externallyProvided: true
}));

/**
 * @return {string | undefined} - returns chatId of the chat session
 */
export const getChatId = createSelector(getChat, chat => chat?.chatId);

/**
 * @return {boolean} - true if chat (conf) was provided by the external platform
 */
export const isChatExternallyProvided = createSelector(getSession, session =>
	(session.chat && session.chat.externallyProvided) as boolean);

/**
 * @return {ChatFeatures} - flags about different messenger features that could be activated or not
 */
export const getChatFeatures = createSelector(getSession, session => session.chatFeatures);

/**
 * @return {boolean} - true if the user has not been yet charged and he is not a VIP
 */
export const isNotChargedNorVip = createSelector(getChatFeatures,
	chatFeatures => chatFeatures.livePreviewDurationVideo <= 0
);

/**
 * @return {boolean} if the client has free messages for this model
 */
export const hasFreePlatformChatDuration = createSelector(getChatFeatures,
	chatFeatures => chatFeatures.freeChatDuration > 0
)

/**
 * @deprecated - use getTranslatedGifts instead
 * @return {IGift[]} - list of gifts
 */
export const getGifts = createSelector(getSession, session => session.gifts);

export const getActiveGifts = createSelector(getGifts, gifts => gifts.filter(gift => gift.active));

/**
 * @return {GiftCategory[]} - list of gift categories
 */
export const getGiftCategories = createSelector(getSession, session => session.giftCategories);

/**
 * @return {number} - the last amount recharged by the user into his balance
 */
export const getLastRechargedAmount = createSelector(getSession, session =>
	session.lastChargedAmount || { value: 0, currency: 'EUR' });

/**
 * @return {OneClickRecharge} - details about one click recharge feature
 * (possibility to add some predefined amounts to his balance with a single click)
 * being available for the logged in user
 */
export const getOneClickRecharge = createSelector(getSession, session => session.oneClickRecharge);

/**
 * @return {boolean} - true if one click recharge is avialable for the current logged in user
 */
export const isOneClickRechargeAvailable = createSelector(getOneClickRecharge, oneClickRecharge => oneClickRecharge.available);

/**
 * @return {number[]} - list of predefined amounts for one click recharge feature
 * (empty if that feature is not available)
 */
export const getOneClickRechargeAmounts = createSelector(getOneClickRecharge,
	oneClickRecharge => oneClickRecharge.available && oneClickRecharge.amounts || []);

/**
 * @return {number | undefined} - default chosen amount for one click recharge feature;
 * undefined if one click recharge is not available
 */
export const getOneClickRechargeDefaultAmount = createSelector(getOneClickRecharge,
	oneClickRecharge => oneClickRecharge.available && oneClickRecharge.defaultAmount || undefined);

/**
 * @return {string} - JWT web token used to authenticate a user
 */
export const getWebtoken = createSelector(getSession, session => session.webtoken);

/**
 * @return {string} - string containing the channel identifier, in the form of
 * <user_platform_id>.<user_id>-<model_platforn_id>.<model_id>
 */
export const getChannelId = createSelector(getSession, session => session.channelId);

export const getUploadMediaUrl = createSelector(getSession, selection => selection.uploadMediaUrl);

/**
 * @return {boolean} - true if the user can send message without being charged for that
 */
export const hasFreePlatformMessages = createSelector(getSession, session => session.freeMessages > 0);
