import * as React from 'react';

import IStore from '../../../redux/store';
import ImageUploader from './ImageUploader';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import { setIsMobileMenuOpen } from '../../../redux/modules/videochat';
import { requestSendImage } from '../../../sagas/websockets/image';
import { resetImageUploadStatus } from "../../../redux/modules/messenger";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc//withRedux';

interface IStoreProps {
    wasRequestedAnImageUpload: boolean;

    requestSendImage: ActionDispatcher<any>;
    setIsMobileMenuOpen: ActionDispatcher<boolean>;
    resetImageUploadStatus: ActionDispatcher;
}

type IProps = IInstanceProps & IStoreProps;

class ImageUploaderContainer extends React.Component<IProps> {
    private ref: HTMLInputElement | null;

    componentDidUpdate(prevProps: IProps) {
        if (this.props.wasRequestedAnImageUpload &&
            !prevProps.wasRequestedAnImageUpload &&
            this.ref
        ) {
            this.ref.click();
            this.props.resetImageUploadStatus(this.props.instanceId);
        }
    }

    render() {
        return (
            <ImageUploader
                inputRefCallback={this.inputRefCallback}
                onFileSelect={this.onFileSelect}
            />
        )
    }

    private onFileSelect = (event: any): void => {
        /** The user may select CANCEL, so no message should be sent in this context */
        if (event.target.files.length > 0) {
            /** Send the image to the saga middleware in order to send it to the server */
            this.props.requestSendImage(this.props.instanceId, event.target.files[0]);

            /** Reset file upload in order to send the same file consecutively */
            event.target.value = null;
        }

        this.props.setIsMobileMenuOpen(this.props.instanceId, false);
    };

    private inputRefCallback = (ref: HTMLInputElement) => {
        if (ref) {
            this.ref = ref;
        }
    }
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    wasRequestedAnImageUpload: selectors.wasRequestedAnImageUpload(store, instanceId)
});

const mapDispatchToProps = {
    setIsMobileMenuOpen,
    requestSendImage,
    resetImageUploadStatus
};

export default withRedux(ImageUploaderContainer, mapStateToProps, mapDispatchToProps);