// Reducer
const messenger = handleActions({
	[RESET_STORE]: (state: IMessenger, action: AnyAction) => getInitialInstanceData().messenger,

	[RESET_EDITOR_STATE]: produce((draft: IMessenger, action: AnyAction) => {
		draft.editorState = getInitialInstanceData().messenger.editorState;
	}),

	[OPEN_MESSENGER]: produce((draft: IMessenger, action: AnyAction) => {
		draft.isOpen = true;
	}),

	[CLOSE_MESSENGER]: produce((draft: IMessenger, action: AnyAction) => {
		draft.isOpen = false;
	}),

	[ADD_MESSAGE]: produce((draft: IMessenger, { payload }: Action<IMessage>) => {
		// Update the value for colors if this is a group chat message
		if (payload.usernameColor) {
			const color = find(draft.groupChatColors,
				groupColor => isEqual(groupColor.color, payload.usernameColor)
			);

			if (color && payload.username) {
				const alreadyHasUser = find(color.users, user => isEqual(user, payload.username));

				if (!alreadyHasUser) {
					color.users.push(payload.username);
				}
			}
		}

		draft.messages.push(payload);
	}),

	[UPDATE_MESSAGE]: produce((draft: IMessenger, { payload }: Action<IMessage>) => {
		draft.messages = map(draft.messages,
			(message: IMessage) => message.messageKey === payload.messageKey ? payload : message
		);
	}),

	[UPDATE_IMAGE_MESSAGE_WHEN_LOADED]: produce((draft: IMessenger, { payload }: Action<IImageLoadedPayload>) => {
		const { messageKey, imgSrc, imgSrcSet, imgWidth, imgHeight } = payload;

		draft.messages = map(draft.messages, (message: IMessage) =>
			message.isOnLoading
				? {
					...message,
					messageKey,
					content: imgSrc,
					isOnLoading: false,
					modalContent: {
						imgSrc,
						imgSrcSet,
						imgWidth,
						imgHeight
					}
				}
				: message
		);
	}),

	[INSERT_ALL_MESSAGES]: produce((draft: IMessenger, { payload }: Action<IMessage[]>) => {
		draft.messages.unshift(...payload);
	}),

	[REMOVE_ALL_MESSAGES]: produce((draft: IMessenger, action: AnyAction) => {
		remove(draft.messages);
	}),

	[REMOVE_MESSAGE]: produce((draft: IMessenger, { payload }: Action<string>) => {
		draft.messages = filter(draft.messages,
			(message: IMessage) => message.messageKey !== payload
		);
	}),

	[ADD_FAILED_MESSAGE]: produce((draft: IMessenger, { payload: text }: Action<string>) => {
		draft.failedMessages.push(text);
	}),

	[CLEAR_FAILED_MESSAGES]: produce((draft: IMessenger) => {
		draft.failedMessages = [];
	}),

	[SET_SHOULD_RESEND_FAILED_MESSAGES]: produce((draft: IMessenger, { payload: shouldResend }: Action<boolean>) => {
		draft.shouldResendFailedMessages = shouldResend;
	}),

	[SET_EMOTICONS]: produce((draft: IMessenger, { payload }: Action<IEmoticon[]>) => {
		draft.emoticons = payload;
	}),

	[SET_IS_TEXT_ENABLED]: produce((draft: IMessenger, { payload }: Action<boolean>) => {
		draft.isTextEnabled = payload;
	}),

	[SET_MODAL_CONTENT]: produce((draft: IMessenger, { payload }: Action<IModalContent>) => {
		draft.modalContent = payload;
	}),

	[RESET_MODAL_CONTENT]: produce((draft: IMessenger, action: AnyAction) => {
		draft.modalContent = getInitialInstanceData().messenger.modalContent;
	}),

	[SET_ACTION_RESPONSE]: produce((draft: IMessenger, { payload }: Action<IActionResponse>) => {
		draft.actionResponse = payload;
	}),

	[RESET_ACTION_RESPONSE]: produce((draft: IMessenger, action: AnyAction) => {
		draft.actionResponse = getInitialInstanceData().messenger.actionResponse;
	}),

	[OPEN_CONTACT_NOTE]: produce((draft: IMessenger, action: AnyAction) => {
		draft.contactNote.isOpen = true;
	}),

	[CLOSE_CONTACT_NOTE]: produce((draft: IMessenger, action: AnyAction) => {
		draft.contactNote.isOpen = false;
	}),

	[SET_CONTACT_NOTE]: produce((draft: IMessenger, { payload }: Action<string>) => {
		draft.contactNote.text = payload;
	}),

	[RESET_CONTACT_NOTE]: produce((draft: IMessenger, action: AnyAction) => {
		draft.contactNote = getInitialInstanceData().messenger.contactNote;
	}),

	[OPEN_EMOTICON_SECTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState = {
			...draft.windowToggleState,
			isEmoticonSectionOpen: true,
			isGiftSectionOpen: false,
			isToySectionOpen: false,
			userPrompts: getInitialInstanceData().messenger.windowToggleState.userPrompts
		};

		if (draft.shouldDisplayCustomMentionSection) {
			draft.isMentionSectionOpen = false;
		}
	}),

	[CLOSE_EMOTICON_SECTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.isEmoticonSectionOpen = false;
	}),

	[OPEN_TOY_SECTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState = {
			...draft.windowToggleState,
			isToySectionOpen: true,
			isGiftSectionOpen: false,
			isEmoticonSectionOpen: false,
			userPrompts: getInitialInstanceData().messenger.windowToggleState.userPrompts
		};

		if (draft.shouldDisplayCustomMentionSection) {
			draft.isMentionSectionOpen = false;
		}
	}),

	[CLOSE_TOY_SECTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.isToySectionOpen = false;

		if (draft.isResponsive) {
			draft.windowToggleState.isDropDownOpen = false;
		}
	}),

	[TOGGLE_TOY_SECTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.isToySectionOpen = !draft.windowToggleState.isToySectionOpen;

		if (draft.windowToggleState.isToySectionOpen) {
			draft.windowToggleState.isEmoticonSectionOpen = false;
			draft.windowToggleState.isGiftSectionOpen = false;
			draft.windowToggleState.userPrompts = getInitialInstanceData().messenger.windowToggleState.userPrompts;
		}
	}),

	[CLOSE_SECTIONS]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState = {
			...draft.windowToggleState,
			isEmoticonSectionOpen: false,
			isGiftSectionOpen: false,
			isToySectionOpen: false,
			isDropDownOpen: false
		};

		if (draft.shouldDisplayCustomMentionSection) {
			draft.isMentionSectionOpen = false;
		}
	}),

	[OPEN_GIFT_SECTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState = {
			...draft.windowToggleState,
			isGiftSectionOpen: true,
			isToySectionOpen: false,
			isEmoticonSectionOpen: false,
			userPrompts: getInitialInstanceData().messenger.windowToggleState.userPrompts
		};

		if (draft.shouldDisplayCustomMentionSection) {
			draft.isMentionSectionOpen = false;
		}
	}),

	[TOGGLE_DROP_DOWN]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.isDropDownOpen = !draft.windowToggleState.isDropDownOpen;
	}),

	[CLOSE_DROP_DOWN]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.isDropDownOpen = false;
	}),

	[CLOSE_GIFT_SECTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.isGiftSectionOpen = false;
	}),

	[OPEN_USER_PROMPT_SECTION]: produce((draft: IMessenger, { payload }: Action<IQuestion>) => {
		if (draft.isResponsive) {
			draft.windowToggleState.userPrompts = uniqBy(
				draft.windowToggleState.userPrompts.concat(payload),
				(item: IQuestion) => item.type
			);
		} else {
			const { userPrompts } = getInitialInstanceData().messenger.windowToggleState;
			draft.windowToggleState.isGiftSectionOpen = false;
			draft.windowToggleState.isEmoticonSectionOpen = false;
			draft.windowToggleState.userPrompts = userPrompts.concat(payload)
		}

		if (draft.shouldDisplayCustomMentionSection) {
			draft.isMentionSectionOpen = false;
		}
	}),

	[CLOSE_USER_PROMPT_SECTION]: produce((draft: IMessenger, { payload }: Action<QuestionType>) => {
		draft.windowToggleState.userPrompts = filter(
			draft.windowToggleState.userPrompts,
			(item: IQuestion) => item.type !== payload
		);
	}),

	[RESET_USER_PROMPTS]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.userPrompts = getInitialInstanceData().messenger.windowToggleState.userPrompts;
	}),

	[OPEN_COUNTDOWN_WARNING]: produce((draft: IMessenger, { payload }: Action<ICountdownWarning>) => {
		draft.windowToggleState.countdownWarning = payload;
	}),

	[CLOSE_COUNTDOWN_WARNING]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.countdownWarning = getInitialInstanceData().messenger.windowToggleState.countdownWarning;
	}),

	[OPEN_VERSION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.isVersionOpen = true;
	}),

	[CLOSE_VERSION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.isVersionOpen = false;
	}),

	[SET_MESSENGER_LOADING_STATUS]: produce((draft: IMessenger, { payload }: Action<LoadingStatus>) => {
		draft.loadingStatus = payload;
	}),

	[SET_HAS_EDITOR]: produce((draft: IMessenger, { payload }: Action<boolean>) => {
		draft.hasEditor = payload;
	}),

	[SET_ACTIVE_GIFT]: produce((draft: IMessenger, { payload }: Action<string>) => {
		draft.activeSymbols.gift = payload;
	}),

	[SET_ACTIVE_EMOTICON]: produce((draft: IMessenger, { payload }: Action<string>) => {
		draft.activeSymbols.emoticon = payload;
	}),

	[SET_ACTIVE_MENTION]: produce((draft: IMessenger, { payload }: Action<string>) => {
		draft.activeSymbols.mention = payload;
	}),

	[SET_FOCUSED_MENTION_OPTION]: produce((draft: IMessenger, { payload }: Action<string>) => {
		draft.focusedMentionOption = payload;
	}),

	[RESET_FOCUSED_MENTION_OPTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.focusedMentionOption = getInitialInstanceData().messenger.focusedMentionOption;
	}),

	[RESET_SYMBOLS]: produce((draft: IMessenger, action: AnyAction) => {
		draft.activeSymbols = getInitialInstanceData().messenger.activeSymbols;
	}),

	[SET_IS_RESPONSIVE]: produce((draft: IMessenger, { payload }: Action<boolean>) => {
		draft.isResponsive = payload;

		if (draft.isResponsive) {
			const { isEmoticonSectionOpen, isGiftSectionOpen, isToySectionOpen } = draft.windowToggleState;
			const hasAnySectionOpen = isEmoticonSectionOpen || isGiftSectionOpen || isToySectionOpen;
			draft.windowToggleState.isDropDownOpen = hasAnySectionOpen;
		}
	}),

	[SET_STORED_EDITOR_STATE]: produce((draft: IMessenger, { payload }: Action<IStoredEditorState>) => {
		draft.editorState = payload;
	}),

	[REQUEST_IMAGE_UPLOAD]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.requestImageUpload = true;
	}),

	[RESET_IMAGE_UPLOAD_STATUS]: produce((draft: IMessenger, action: AnyAction) => {
		draft.windowToggleState.requestImageUpload = false;
	}),

	[SET_IS_RECORDING_AUDIO_MESSAGE]: produce((draft: IMessenger, { payload }: Action<boolean>) => {
		draft.audioMessage.isRecording = payload;
	}),

	[SET_IS_MIC_ENABLED]: produce((draft: IMessenger, { payload }: Action<boolean>) => {
		draft.audioMessage.isMicEnabled = payload;
	}),

	[SET_MENTION_QUERY]: produce((draft: IMessenger, { payload }: Action<string>) => {
		draft.mentionQuery = payload;
	}),

	[CLOSE_LOAD_ACCOUNT_USER_PROMPTS]: produce((draft: IMessenger, action: AnyAction) => {
		const { LOAD_ACCOUNT, LOAD_ACCOUNT_AUTO_RECHARGE, LOAD_ACCOUNT_ONE_CLICK } = QuestionType;

		const userPrompts = filter(draft.windowToggleState.userPrompts,
			(userPrompt: IQuestion) => includes(
				[LOAD_ACCOUNT, LOAD_ACCOUNT_AUTO_RECHARGE, LOAD_ACCOUNT_ONE_CLICK],
				userPrompt.type
			)
		);

		draft.windowToggleState.userPrompts = userPrompts;
	}),

	[SET_TOY_OPTIONS]: produce((draft: IMessenger, { payload }: Action<IToyOption[]>) => {
		draft.toyOptions = payload;
	}),

	[SET_HAS_TOOLTIP_OPEN]: produce((draft: IMessenger, { payload: { hasTooltip, id } }: AnyAction) => {
		draft.toyOptions = map(draft.toyOptions, (option: IToyOption) => ({
			...option,
			hasTooltipOpen: hasTooltip && option.id === id
		}));
	}),

	[CLOSE_TOY_OPTION_TOOLTIP]: produce((draft: IMessenger, action: AnyAction) => {
		draft.toyOptions = map(draft.toyOptions, (option: IToyOption) => ({
			...option,
			hasTooltipOpen: false
		}));
	}),

	[ACTIVATE_TOY_OPTION]: produce((draft: IMessenger, { payload }: Action<number>) => {
		draft.toyOptions = map(draft.toyOptions, (option: IToyOption) => ({
			...option,
			isPlaying: option.id === payload,
			unlockedBy: filter(option.unlockedBy, optionId => optionId !== payload)
		}));
	}),

	[INACTIVATE_TOY_OPTION]: produce((draft: IMessenger, action: AnyAction) => {
		draft.toyOptions = map(draft.toyOptions, (option: IToyOption) => ({
			...option,
			isPlaying: false
		}));
	}),

	[INACTIVATE_TOY_MESSAGE]: produce((draft: IMessenger, { payload }: Action<string>) => {
		draft.messages = map(draft.messages, (message: IMessage) => {
			if (message.toy && message.toy.isActive) {
				message.toy.text = payload;
				message.toy.isActive = false;
				message.dateCreated = moment().toString();
			}

			return message;
		});
	}),

	[SET_SHOULD_SEND_MESSAGE]: produce((draft: IMessenger, { payload }: Action<boolean>) => {
		draft.audioMessage.shouldSendMessage = payload;
	}),

	[SET_SECONDS_ELAPSED]: produce((draft: IMessenger, { payload }: Action<number>) => {
		draft.audioMessage.secondsElapsed = payload;
	}),

	[SET_GROUP_CHAT_COLORS]: produce((draft: IMessenger, { payload }: Action<IGroupChatColor[]>) => {
		draft.groupChatColors = payload;
	}),

	[OPEN_MENTION_SECTION]: produce((draft: IMessenger, { payload }: Action<boolean>) => {
		draft.isMentionSectionOpen = true;
		draft.shouldDisplayCustomMentionSection = payload || false;

		if (draft.shouldDisplayCustomMentionSection) {
			draft.windowToggleState = {
				...draft.windowToggleState,
				isToySectionOpen: false,
				isGiftSectionOpen: false,
				isEmoticonSectionOpen: false,
				userPrompts: getInitialInstanceData().messenger.windowToggleState.userPrompts
			};
		}
	}),

	[CLOSE_MENTION_SECTION]: produce((draft: IMessenger) => {
		draft.isMentionSectionOpen = false;
		draft.mentionQuery = getInitialInstanceData().messenger.mentionQuery;
	}),

	[ADD_MENTION]: produce((draft: IMessenger) => {
		draft.isMentionSectionOpen = false;
	})
}, getInitialInstanceData().messenger);

export default messenger;
