import styled, {ThemedStyledProps} from "styled-components";

interface ICheckedIconWrapperProps {
    className?: string;
    strokeColor?: string;
    fillColor?: string;
    hoverStrokeColor?: string;
    hoverFillColor?: string;
    width?: string;
    height?: string;
}

const defaultProps = (props: ThemedStyledProps<ICheckedIconWrapperProps, any>) => ({
    width: '32px',
    height: '32px',
    strokeColor: props.theme && props.theme.colors.checkedStrokeColor,
    fillColor: props.theme && props.theme.colors.checkedFillColor,
    hoverStrokeColor: props.theme && props.theme.colors.checkedStrokeColor,
    hoverFillColor: props.theme && props.theme.colors.checkedFillColor,
    ...props,
});

/**
 * Styling for stateless component used to render a custom checkbox, able to have different stroke (contour) and fill color
 */
export const CheckedIconWrapper = styled.span.attrs({ className: `checkedIcon` })<ICheckedIconWrapperProps>`
    ${(props) => {
        const withDefaults = defaultProps(props);

        return (
            `
                > svg {
                    width: ${withDefaults.width};
                    height: ${withDefaults.height};
                    stroke-width: 2px;
                    stroke: ${withDefaults.strokeColor};
                    fill: ${withDefaults.fillColor};
                    &:hover {
                        stroke: ${withDefaults.hoverStrokeColor};
                        fill: ${withDefaults.hoverFillColor};
                    };
                }
            `
        );
    }}
`;
