import * as React from 'react';
import { ITranslation } from '../../../redux/store';
import VideoChatBaseButton from '../../common/VideoChatBaseButton';

interface IProps {
    isFullScreen: boolean;
    translation: ITranslation;
    toggleFullscreen: () => void;
}

const VideoChatToggleFullscreen = (props: IProps) => {
    const { leaveFullscreen, enterFullscreen } = props.translation.chat; 
    const text = props.isFullScreen ? leaveFullscreen : enterFullscreen;
    const className = props.isFullScreen ? "-icon-fullscreen-close" : "-icon-fullscreen-open";

    return (
        <VideoChatBaseButton
            text={text}
            id="videochat-toggle-fullscreen-button"
            onClick={props.toggleFullscreen}
        >
            <i className={className} />
        </VideoChatBaseButton>
    );
};

export default VideoChatToggleFullscreen;