import * as React from 'react';
import { find, isEmpty } from 'lodash';
import { withTheme } from 'styled-components';
import { withResizeDetector } from 'react-resize-detector';

import config from '../../../static/config';
import utils from '../../../utils';
import MessageSection from './MessageSection';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import { MessageSectionContainerWrapper } from './style';
import { requestSendMessage } from '../../../sagas/websockets/message';
import { syncQuantitiesOnMessageSent } from '../../../redux/modules/session';
import IStore, { IGiftPage, IGift, ITranslation, ICurrency } from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import {
    closeEmoticonSection,
    closeGiftSection,
    closeSections,
    openEmoticonSection,
	closeToySection,
    setIsResponsive,
    toggleDropDown,
    toggleToySection,
    setIsRecordingAudioMessage
} from '../../../redux/modules/messenger';
import { ITheme } from '../../../themes';

interface IState {
    text: string;

    // When we send a message, we have to reset the text from children components
    shouldResetEditorState: boolean;

    // This property is used only on default editor
    giftText: string;
}

interface IIResizeDetector {
    width: number;
    height: number;
}

interface IStoreProps {
    isEmoticonSectionOpen: boolean;
    isGiftSectionOpen: boolean;
	isToySectionOpen: boolean;
    isVideoChatOpen: boolean;
    isInteractive: boolean;
	hasEditor: boolean;
	theme: ITheme;
    currency: ICurrency;
    hasFreeMessages: boolean;
    isResponsive: boolean;
    giftPages: IGiftPage[];
    gifts: IGift[];
    messagePrice: number;
    translation: ITranslation;
    isDropDownOpen: boolean;
    activeEmoticon: string;
    activeGift: string;
    activeMention: string;
    isFullscreen: boolean;
    hasSendMessageButton: boolean;
    hasAudioMessages: boolean;
    hasOnlySendButton: boolean;
    hasToyControlButton: boolean;
    shouldDisplayMessageSectionButtons: boolean;
    isAnyTextInEditor: boolean;

    closeSections: ActionDispatcher;
    toggleDropDown: ActionDispatcher;
    closeGiftSection: ActionDispatcher;
    openEmoticonSection: ActionDispatcher;
    closeEmoticonSection: ActionDispatcher;
	closeToySection: ActionDispatcher;
    toggleToySection: ActionDispatcher;
    syncQuantitiesOnMessageSent: ActionDispatcher;
    requestSendMessage: ActionDispatcher<string>;
    setIsResponsive: ActionDispatcher<boolean>;
    setIsRecordingAudioMessage: ActionDispatcher<boolean>;
}

type IProps = IInstanceProps & IStoreProps & IIResizeDetector;

class MessageSectionContainer extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            text: "",
            giftText: "",
            shouldResetEditorState: false
        };
    }

    componentDidUpdate(prevProps: IProps) {
		const { theme, instanceId } = this.props;
		const isResponsive = this.props.width < theme.layout.messageSection.responsiveBreakpointWidth;

        if (prevProps.isResponsive !== isResponsive) {
            this.props.setIsResponsive(instanceId, isResponsive);
        }
    }

    render() {
        let gift: IGift | undefined;

        if (this.props.activeGift) {
            const giftId = utils.getGiftDecoded(this.props.activeGift);
            gift = find(this.props.gifts, (g: IGift) => g.id === giftId);
        }

        const hasContent = !isEmpty(this.state.text) || !isEmpty(this.state.giftText);

        return (
            <MessageSectionContainerWrapper minMessageSectionHeight={config.viewSizes.minMessageSectionHeight}>
                <MessageSection
                    hasContent={hasContent}
                    gift={gift}
                    emoticon={this.props.activeEmoticon}
                    mention={this.props.activeMention}
                    hasEditor={this.props.hasEditor}
                    currency={this.props.currency}
                    shouldResetEditorState={this.state.shouldResetEditorState}
                    isGiftSectionOpen={this.props.isGiftSectionOpen}
                    isFullscreen={this.props.isFullscreen}
                    isInteractive={this.props.isInteractive}
                    isDropDownOpen={this.props.isDropDownOpen}
                    isVideoChatOpen={this.props.isVideoChatOpen}
                    isEmoticonSectionOpen={this.props.isEmoticonSectionOpen}
                    expandDropdown={this.expandDropdown}
                    isResponsive={this.props.isResponsive}
                    hasFreeMessages={this.props.hasFreeMessages}
                    hasSendMessageButton={this.props.hasSendMessageButton}
                    hasOnlySendButton={this.props.hasOnlySendButton}
                    hasToyControlButton={this.props.hasToyControlButton}
                    shouldDisplayMessageSectionButtons={this.props.shouldDisplayMessageSectionButtons}
                    messagePrice={this.props.messagePrice}
                    translation={this.props.translation}
                    isAnyTextInEditor={this.props.isAnyTextInEditor}
                    hasAudioMessages={this.props.hasAudioMessages}
                    onStartRecording={this.onStartRecording}

                    sendMessage={this.sendMessage}
                    onTextChanged={this.onTextChanged}
                    toggleToySection={this.toggleToySection}
                    onGiftTextChanged={this.onGiftTextChanged}
                />
            </MessageSectionContainerWrapper>
        );
    }

    private toggleToySection = () => {
        this.props.toggleToySection(this.props.instanceId);
    };

    private sendMessage = () => {
        const { instanceId } = this.props;
		const { text, giftText } = this.state;

        if (!isEmpty(text) || !isEmpty(giftText)) {
			const messageText = !isEmpty(giftText) ? `${giftText} ${text}` : text;
			this.props.requestSendMessage(instanceId, messageText);

            this.setState({
                text: "",
                giftText: "",
                shouldResetEditorState: true
            });
        }

        this.props.syncQuantitiesOnMessageSent(instanceId);
        this.props.closeSections(instanceId);
    };

    private onGiftTextChanged = (giftText: string) => {
        this.setState({
            giftText,
            shouldResetEditorState: false
        });
    };

    private onTextChanged = (text: string) => {
        this.setState({
            text,
            shouldResetEditorState: false
        });
    };

    private expandDropdown = () => {
        const { instanceId } = this.props;

        if (!this.props.isInteractive) {
            return;
        }

        if (this.props.isResponsive) {
            this.props.toggleDropDown(instanceId);
        }

        if (this.props.isEmoticonSectionOpen) {
            this.props.closeEmoticonSection(instanceId);
        }

	    if (this.props.isToySectionOpen) {
		    this.props.closeToySection(instanceId);
	    }

        if (this.props.isGiftSectionOpen) {
            this.props.closeGiftSection(instanceId);
        }
    };

    private onStartRecording = () => {
        this.props.setIsRecordingAudioMessage(this.props.instanceId, true);
    }
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    giftPages: selectors.getGiftPages(store, instanceId),
    gifts: selectors.getTranslatedGifts(store, instanceId),
    hasEditor: selectors.hasEditor(store, instanceId),
    hasFreeMessages: selectors.hasFreeMessages(store, instanceId),
    currency: selectors.getCurrency(store, instanceId),
    isEmoticonSectionOpen: selectors.isEmoticonSectionOpen(store, instanceId),
    isGiftSectionOpen: selectors.isGiftSectionOpen(store, instanceId),
    isToySectionOpen: selectors.isToySectionOpen(store, instanceId),
    isInteractive: selectors.isInteractive(store, instanceId),
    messagePrice: selectors.getMessagePrice(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    isDropDownOpen: selectors.isDropDownMenuOpen(store, instanceId),
    isVideoChatOpen: selectors.isVideoChatOpen(store, instanceId),
    activeEmoticon: selectors.getActiveEmoticon(store, instanceId),
    activeGift: selectors.getActiveGift(store, instanceId),
    activeMention: selectors.getActiveMention(store, instanceId),
    isResponsive: selectors.isResponsive(store, instanceId),
    isFullscreen: selectors.isFullScreen(store, instanceId),
    hasSendMessageButton: selectors.hasSendMessageButton(store, instanceId),
    hasOnlySendButton: selectors.hasOnlySendButton(store, instanceId),
    hasAudioMessages: selectors.hasAudioMessages(store, instanceId),
    shouldDisplayMessageSectionButtons: selectors.shouldDisplayMessageSectionButtons(store, instanceId),
    hasToyControlButton: selectors.hasToyControlButton(store, instanceId),
    isAnyTextInEditor: selectors.isAnyTextInEditor(store, instanceId)
});

const mapDispatchToProps = {
    syncQuantitiesOnMessageSent,
    requestSendMessage,
    closeEmoticonSection,
    openEmoticonSection,
    closeGiftSection,
    toggleToySection,
    toggleDropDown,
    closeSections,
    setIsResponsive,
    setIsRecordingAudioMessage,
	closeToySection
};

export default withRedux(MessageSectionContainer, mapStateToProps, mapDispatchToProps, {
	withResizeDetector,
	withTheme 
});
