import * as React from 'react';
import { get, isFunction, toLower} from 'lodash';
import { DomCustomClass } from "./types";

const hasDomIdentifiers: boolean = toLower(process.env.HAS_DOM_IDENTIFIERS) === "true";

type ClassesCallback<Q> = (p: Q) => DomCustomClass[];

const withCustomClasses = <P extends {}>(WrappedComponent, classes: DomCustomClass[] | ClassesCallback<P>) => {
    return class extends React.Component<P | P & React.HTMLAttributes<HTMLDivElement>> {
        constructor(props: P) {
            super(props);
        }

        render() {
            const exitingClassNames = get(this.props, "className");
            const newClasses: string = isFunction(classes) ? classes(this.props as P).join(' ') : classes.join(' ');
            const className = exitingClassNames ? `${exitingClassNames}  ${newClasses}` : newClasses;

            return (hasDomIdentifiers
                    ? <WrappedComponent {...this.props} className={className} />
                    : <WrappedComponent {...this.props} />
            );
        }
    };
};

export default withCustomClasses;
