import { isUndefined } from "lodash";
import utils from "../../../utils";
import {IBoundingRect, TooltipPlacement} from "../AdvancedTooltip/interfaces";

export const getBoundingRect = (boundingRect?: IBoundingRect): IBoundingRect => {
    const viewPortDimensions = utils.getWindowDimensions();

    return boundingRect || {
        top: 0,
        bottom: viewPortDimensions.height,
        left: 0,
        right: viewPortDimensions.width
    };
};

type Rect = ClientRect | DOMRect | IBoundingRect;
/**
 * Transform fractional values inside the rectangle into integral values, by applying Math.floor
 * @param rect
 */
export const getRectWithIntegralValues = (rect: Rect) => ({
    top: Math.floor(rect.top),
    bottom: Math.floor(rect.bottom),
    left: Math.floor(rect.left),
    right: Math.floor(rect.right)
});

export const canAdjustHorizontally = (placement?: TooltipPlacement): boolean =>
    isUndefined(placement) ||
        placement === TooltipPlacement.TOP ||
        placement === TooltipPlacement.BOTTOM;

export const canAdjustVertically = (placement?: TooltipPlacement): boolean =>
    placement === TooltipPlacement.LEFT ||
    placement === TooltipPlacement.RIGHT;
