import * as React from 'react';
import { filter, forEach, isEmpty, isEqual, reduce, size } from 'lodash';

import utils from '../../../utils';
import DefaultEditor from './DefaultEditor';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { decreaseCurrentGiftQuantity, IHandleQuantitiesPayload } from '../../../redux/modules/session';
import IStore, {
    ActionResponseType,
    IActionResponse,
    ICurrency,
    IGift,
    IStoredEditorState,
    ITranslation
} from '../../../redux/store';
import {
	closeMentionSection,
	openMentionSection,
	setMentionQuery,
	resetSymbols,
	setActionResponse,
	setStoredEditorState
} from "../../../redux/modules/messenger";
import constants from '../../../static/constants';

interface IComponentProps {
    emoticon?: string;
    mention?: string;
    placeholder: string;
    isInteractive: boolean;
    shouldResetEditorState: boolean;
    gift?: IGift;

    sendMessage: () => void;
    onTextChanged: (text: string) => void;
    onGiftTextChanged: (giftText: string) => void;
}

interface IStoreProps {
    gifts: IGift[];
    currency: ICurrency;
    translation: ITranslation;
    storedEditorState: IStoredEditorState;
    isMentionSectionOpen: boolean;
	shouldIncreaseMessageFontSize: boolean;

    setActionResponse: ActionDispatcher<IActionResponse>;
    resetSymbols: ActionDispatcher;
    setStoredEditorState: ActionDispatcher<IStoredEditorState>;
    decreaseCurrentGiftQuantity: ActionDispatcher<IHandleQuantitiesPayload>;
    openMentionSection: ActionDispatcher;
    closeMentionSection: ActionDispatcher;
    setMentionQuery: ActionDispatcher<string>;
	isVideoChatInGroupMode: boolean;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

class DefaultEditorContainer extends React.Component<IProps> {
    componentDidMount() {
        const { text, gifts } = this.props.storedEditorState;

        // let the parent components know about our new loaded stored text and gifts
        if (!isEmpty(text)) {
            this.props.onTextChanged(text);
        }

        if (!isEmpty(gifts)) {
            this.props.onGiftTextChanged(this.getGiftText(gifts));
        }
    }

    componentDidUpdate(prevProps: IProps) {
        const { text, gifts } = this.props.storedEditorState;
        const latestCharacter = text.slice(-1);
        const mentionPosition = text.lastIndexOf(constants.mentionSpecialCharacter);
        const shouldCloseMentionSection = this.props.isMentionSectionOpen
            && text.length < prevProps.storedEditorState.text.length;

        if (latestCharacter === constants.mentionSpecialCharacter && this.props.isVideoChatInGroupMode) {
            this.props.openMentionSection(this.props.instanceId, false);
        }

        if (this.props.isMentionSectionOpen && mentionPosition !== -1) {
            const mentionQuery = text.slice(mentionPosition + 1);

            this.props.setMentionQuery(this.props.instanceId, mentionQuery);
        }

        if (shouldCloseMentionSection) {
            this.props.closeMentionSection(this.props.instanceId);
        }

        // Update the store & restart the added gift
        if (this.props.gift &&
            this.props.gift !== prevProps.gift &&
            isEqual(this.props.gifts, prevProps.gifts)
        ) {
            this.setInsertedGifts(gifts.concat(this.props.gift));
            this.props.resetSymbols(this.props.instanceId);
        }

        if (this.props.emoticon && this.props.emoticon !== prevProps.emoticon) {
            const newText = `${text}${this.props.emoticon}`;
            this.updateStoredEditorState({ text: newText, gifts });

            this.props.resetSymbols(this.props.instanceId);
        }

        if (!prevProps.mention && this.props.mention && this.props.isVideoChatInGroupMode) {
            const slicedText = text.slice(0, mentionPosition);

            this.updateStoredEditorState({ text: `${slicedText}${constants.mentionSpecialCharacter}${this.props.mention}`, gifts });
            this.props.closeMentionSection(this.props.instanceId);
            this.props.resetSymbols(this.props.instanceId);
        }


        // Reset both text and gifts if a message has been sent
        if (this.props.shouldResetEditorState &&
            this.props.shouldResetEditorState !== prevProps.shouldResetEditorState
        ) {
            this.updateStoredEditorState({ text: "", gifts: [] });
            return;
        }

        // Update gifts stock
        forEach(this.props.gifts, (gift: IGift) => {
            if (gift.initialQuantity) {
                const quantityToHandle = size(filter(gifts, (g: IGift) => g.id === gift.id));

                this.props.decreaseCurrentGiftQuantity(this.props.instanceId, {
                    giftId: gift.id,
                    quantityToHandle
                });
            }
        });
    }

    render() {
        const { text, gifts } = this.props.storedEditorState;

        return (
            <DefaultEditor
                text={text}
                currency={this.props.currency}
                gifts={this.props.gifts}
                insertedGifts={gifts}
                placeholder={this.props.placeholder}
                isInteractive={this.props.isInteractive}
                shouldIncreaseMessageFontSize={this.props.shouldIncreaseMessageFontSize}

                onChange={this.onChange}
                onKeyDown={this.onKeyDown}
                onRemoveGiftClick={this.onRemoveGiftClick}
            />
        );
    }

    private onChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
        this.updateStoredEditorState({
            text: event.target.value,
            gifts: this.props.storedEditorState.gifts
        });
    };

    private updateStoredEditorState = ({ text, gifts }: IStoredEditorState) => {
        const { instanceId } = this.props;

        if (utils.hasExceededTextLimit(text)) {
            this.props.setActionResponse(instanceId, {
                text: this.props.translation.error.textLimit,
                type: ActionResponseType.WARNING
            });

            return;
        }

        const { storedEditorState, onTextChanged, onGiftTextChanged } = this.props;
        const hasModifiedText = !isEqual(text, storedEditorState.text);
        const hasModifiedGifts = !isEqual(gifts, storedEditorState.gifts);

        if (hasModifiedText) {
            onTextChanged(text);
        }

        if (hasModifiedGifts) {
            onGiftTextChanged(this.getGiftText(gifts));
        }

        if (hasModifiedText || hasModifiedGifts) {
            this.props.setStoredEditorState(instanceId, { text, gifts });
        }
    };

    private onKeyDown = (event: React.KeyboardEvent<HTMLTextAreaElement>) => {
        if (event.keyCode === constants.ENTER_KEY_CODE && !event.shiftKey) {
			event.preventDefault();

            if (!this.props.isMentionSectionOpen) {
				this.props.sendMessage();
			}
        }
    };

    private onRemoveGiftClick = () => {
        const { gifts } = this.props.storedEditorState;
        this.setInsertedGifts(gifts.slice(0, -1));
    };

    private setInsertedGifts = (gifts: IGift[]) => {
        this.updateStoredEditorState({
            text: this.props.storedEditorState.text,
            gifts
        });
    };

    private getGiftText = (gifts: IGift[]) => reduce(gifts,
        (prevText, gift: IGift) => `${prevText}${utils.getGiftEncoding(gift)}`,
        ""
    );
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    currency: selectors.getCurrency(store, instanceId),
    gifts: selectors.getTranslatedGifts(store, instanceId),
    storedEditorState: selectors.getEditorState(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    isMentionSectionOpen: selectors.isMentionSectionOpen(store, instanceId),
	isVideoChatInGroupMode: selectors.isVideoChatInGroupMode(store, instanceId),
	shouldIncreaseMessageFontSize: selectors.getShouldIncreaseMessageFontSize(store, instanceId)
});

const mapDispatchToProps = {
    decreaseCurrentGiftQuantity,
    setStoredEditorState,
    setActionResponse,
    resetSymbols,
    openMentionSection,
    closeMentionSection,
    setMentionQuery
};

export default withRedux<IComponentProps>(DefaultEditorContainer, mapStateToProps, mapDispatchToProps);
