import { isEmpty } from 'lodash';
import { Action } from 'redux-actions';
import { put, select, takeEvery } from "redux-saga/effects";
import { ACTION, CMDP_GETONLINESTATE, CMDP_GETONLINESTATE_RESPONSE } from 'vxcontrol-client-lib';

import utils from "../../utils";
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import AppService from '../../services/AppService';
import ApiService from '../../services/ApiService';
import { ISession, VideoChatType } from '../../redux/store';
import { setIsOnline, setModelOnlineState } from "../../redux/modules/model";
import { ActionIds } from '../../redux/interfaces';

const createOnlineStateActionType = createActionType(ActionIds.ONLINE_STATE, { isSagaAction: true });

// Actions
const REQUEST_ONLINE_STATE = createOnlineStateActionType("REQUEST_ONLINE_STATE");
export const RECEIVE_ONLINE_STATE = createOnlineStateActionType("RECEIVE_ONLINE_STATE");

// Action Creators
const requestOnlineState = createAction(REQUEST_ONLINE_STATE);
const onReceiveOnlineState = createAction<CMDP_GETONLINESTATE_RESPONSE>(RECEIVE_ONLINE_STATE);

// Sagas
/** Saga for sending the online state request */
const requestOnlineStateSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const session: ISession = yield select(selectors.getSession, instanceId);
    const { channelId, sessionID } = session;

    const command: CMDP_GETONLINESTATE = {
        action: ACTION.CMDP_GETONLINESTATE,
        params: {
            channelId,
            sessionID
        }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

/**
 * Saga to deal with the model's online state we get from the backend
 */
const receiveOnlineStateSaga = function* ({ payload }: Action<IPayload<CMDP_GETONLINESTATE_RESPONSE>>) {
    const { instanceId, value: response } = payload;

    if (isEmpty(response) || isEmpty(response.commands)) {
        return;
    }

    const { commands } = response;

    const onlineStateCommand = commands.find(command => command.action === ACTION.CMDC_ONLINESTATE);
    if (!onlineStateCommand || isEmpty(onlineStateCommand.params)) {
        return;
    }

    // We compare the supported streams by the model and if they have
    // been updated, we call the parent platform hook for notification
    const onlineState = utils.getOnlineStateFromResponseParams(onlineStateCommand.params);
    if (isEmpty(onlineState)) {
        return;
    }

    const { supportsChat, supportsPreviewChat, supportsVoyeurChat } = onlineState;

    const isModelSupportingChat = yield select(selectors.isModelSupportingChat, instanceId);
    const isModelSupportingPreviewChat = yield select(selectors.isModelSupportingPreviewChat, instanceId);
    const isModelSupportingVoyeurChat = yield select(selectors.isModelSupportingVoyeurChat, instanceId);

    const hasChatSupportChanged = isModelSupportingChat !== supportsChat;
    const hasPreviewSupportChanged = isModelSupportingPreviewChat !== supportsPreviewChat;
    const hasVoyeurSupportChanged = isModelSupportingVoyeurChat !== supportsVoyeurChat;

    if (hasChatSupportChanged || hasPreviewSupportChanged || hasVoyeurSupportChanged) {
        const videoChatTypes: VideoChatType[] = [];

        if (supportsChat) {
            videoChatTypes.push(VideoChatType.LIVECHAT);
        }

        if (supportsPreviewChat) {
            videoChatTypes.push(VideoChatType.LIVE_PREVIEW);
        }

        if (supportsVoyeurChat) {
            videoChatTypes.push(VideoChatType.VOYEUR_CHAT);
        }

        AppService.getService(instanceId).onSupportedVideoChatTypes(videoChatTypes);
    }

    // We use JSON parse to convert 'true/false' string to boolean value
    try {
        const isOnline: boolean = JSON.parse(onlineStateCommand.params.online) || false;
        yield put(setModelOnlineState(instanceId, onlineState));
        yield put(setIsOnline(instanceId, isOnline));
    } catch {}
};

const watchSagas = function* () {
    yield takeEvery(REQUEST_ONLINE_STATE, requestOnlineStateSaga);
    yield takeEvery(RECEIVE_ONLINE_STATE, receiveOnlineStateSaga);
};

export { requestOnlineState, onReceiveOnlineState, requestOnlineStateSaga, receiveOnlineStateSaga };
export default watchSagas;
