import * as React from 'react';

import Group from './Group';
import selectors from '../../../redux/selectors';
import IStore, {ITranslation} from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

export interface IComponentProps {
    datetime: string;
    showArrow: boolean;
    className?: string;
    hasSectionsInitiallyOpened?: boolean;
    isDayGroup?: boolean;
    children?: any;

    /** The Group component could render a specific information text given as prop */
    informationText?: any;
    onContentUpdate?: () => void;
}

interface IStoreProps {
    translation: ITranslation;
	language: string;
	shouldIncreaseMessageFontSize: boolean;
}

interface IState {
    isOpen: boolean;
}

export type IProps = IComponentProps & IStoreProps & IInstanceProps;

class GroupContainer extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = { isOpen: false };
    }

    componentDidMount() {
        this.setState({
            isOpen: this.props.hasSectionsInitiallyOpened as boolean
        });
    }

    render() {
        return (
            <Group
                className={this.props.className}
                children={this.props.children}
                informationText={this.props.informationText}
                datetime={this.props.datetime}
                showArrow={this.props.showArrow}
                isOpen={this.state.isOpen}
                translation={this.props.translation}
                onClick={this.onClick}
                isDayGroup={this.props.isDayGroup}
                language={this.props.language}
                shouldIncreaseMessageFontSize={this.props.shouldIncreaseMessageFontSize}
            />
        );
    }

    private onClick = () => {
        this.setState((state: IState) => ({
            ...state,
            isOpen: !state.isOpen
        }));

        if (this.props.onContentUpdate) {
            this.props.onContentUpdate();
        }
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    translation: selectors.getTranslation(store, instanceId),
	language: selectors.getLanguage(store, instanceId),
	shouldIncreaseMessageFontSize: selectors.getShouldIncreaseMessageFontSize(store, instanceId)
});

export default withRedux<IComponentProps>(GroupContainer, mapStateToProps);
