import styled from "styled-components";
import { isMacOs } from "react-device-detect";

import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface IEmoticonProps {
    position?: string;
    className?: string
}

export const MessageEmoticonWrapper = withCustomClasses<IEmoticonProps>(styled.span`
    margin: 0;
    display: inline-block;
    background-size: 1000%;
    background-repeat: no-repeat;
    vertical-align: middle;
    
    ${isMacOs
		? `
			width: 1.2em;
			margin: 0 2px 0 0;
		`
		: `
			width: auto;
		`
	}

    ${(props: IEmoticonProps) => props.position
        ? `
            width: 20px;
            height: 20px;
        `
        : `
            background: none;
            height: auto;
            font-size: 1.2em;
            text-align: center;
        `
    };
`, [DomCustomClass.MessageEmoticon]);

