import styled from 'styled-components';

import config from "../../../static/config";
import { BaseButton } from '../../common/BaseButton/style';
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface ICam2CamButtonWrapper {
    isSelected?: boolean;
}

export const Cam2CamButtonWrapper = withCustomClasses<ICam2CamButtonWrapper>(styled(BaseButton)<ICam2CamButtonWrapper>`
    ${props =>`
        background-color: ${props.theme.colors.cam2CamBackgroundColor};
        border: ${props.theme.colors.cam2CamBackgroundColor};
        color: ${props.theme.colors.videoChatControlsText};
    `}
    
    &:hover {
        ${props => `
            background-color: ${props.theme.colors.cam2CamHoverColor};
            border-color: ${props.theme.colors.cam2CamHoverColor};
        `}
    }
    
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    text-align: center;
    padding: 5px 5px;
    position: relative;

    i {
        font-size: ${config.viewSizes.actionSectionButton}px;
        position: relative;
        display: inline-block;
        padding-right: 12px;
    }
`, [DomCustomClass.Cam2CamButton]);

export const Cam2CamDisabledButtonWrapper = withCustomClasses(styled(BaseButton).attrs({ disabled: true })`
    background-color: ${(props) => props.theme.colors.cam2CamDisabledColor};
    border: 1px solid ${(props) => props.theme.colors.cam2CamDisabledColor};
    color: ${(props) => props.theme.colors.primaryColor};
    text-align: center;
    width: 98%;

    &:hover {
        background-color: ${(props) => props.theme.colors.cam2CamDisabledColor};
        border-color: ${(props) => props.theme.colors.cam2CamDisabledColor};
        color: #fff;
    }   

    i {
        font-size: ${config.viewSizes.actionSectionButton}px;
        position: relative;
        display: inline-block;
        padding-right: 12px;
    }
`, [DomCustomClass.Cam2CamButton, DomCustomClass.Disabled]);

export const Cam2CamEnabledIndicator = styled.div`
    background-color: #43B31C;
    border-radius: 50%;
    
    width: 10px;
    height: 10px;
    
    position: absolute;
    top: 5px;
    right: 5px;
`;
