import * as React from 'react';

import EmoticonSection from './EmoticonSection';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import IStore, { ITranslation, IEmoticon } from '../../../redux/store';
import { closeEmoticonSection } from '../../../redux/modules/messenger';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IComponentProps {
    isOpen: boolean;
    translation: ITranslation;
    isResponsive?: boolean;

    emoticonClick: (emoticon: string) => () => void;
}

interface IStoreProps {
    emoticons: IEmoticon[];

    closeEmoticonSection: ActionDispatcher;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

const closeSection = (props: IProps) => () => {
    props.closeEmoticonSection(props.instanceId);
};

const EmoticonSectionContainer = (props: IProps) => (
    <EmoticonSection
        isOpen={props.isOpen}
        emoticons={props.emoticons}
        isResponsive={props.isResponsive}
        translation={props.translation}

        emoticonClick={props.emoticonClick}
        closeEmoticonSection={closeSection(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    emoticons: selectors.getEmoticons(store, instanceId)
});

const mapDispatchToProps = {
    closeEmoticonSection
};

export default withRedux<IComponentProps>(EmoticonSectionContainer, mapStateToProps, mapDispatchToProps);
