import * as React from 'react';
import Tooltip from '../../../features/common/Tooltip';
import { ICurrency, ITranslation } from '../../../redux/store';
import utils from '../../../utils';
import { BaseButtonContent, FirstRowText, SecondRowText } from '../../common/BaseButton/style';
import { VoyeurCamButtonWrapper } from './style';
import BaseButtonContainer from '../../common/BaseButton';

interface IProps {
    price: number;
    currency: ICurrency;
    translation: ITranslation;
    shouldDisplayIcon?: boolean;
    isForActionSection?: boolean;

    onClick: () => void;
}

const VoyeurCamButton = (props: IProps) => {
    const { tooltipVoyeur, voyeurCam } = props.translation.chat;

    const voyeurCamPrice = utils.translationWithValues(
        props.translation.common.perMinute, 
        { price: `${props.price} ${props.currency.symbol}` }
    );

    const voyeurCamButton = (
        <VoyeurCamButtonWrapper>
			{utils.getShouldDisplayIcon(props.shouldDisplayIcon) &&
            	<i className={"-icon-voyeur-full"} />
			}
			
			<BaseButtonContent>
				<FirstRowText>{voyeurCam}</FirstRowText>
				<SecondRowText>{voyeurCamPrice}</SecondRowText>
			</BaseButtonContent>
        </VoyeurCamButtonWrapper>
    );

    const voyeurCamButtonWithTooltip = (
        <Tooltip id={"voyeur-cam"} text={tooltipVoyeur}>
            {voyeurCamButton}         
        </Tooltip>
    );

    return (
        <BaseButtonContainer
            isForActionSection={props.isForActionSection}
            onClick={props.onClick}
        >
            {tooltipVoyeur
                ? voyeurCamButtonWithTooltip
                : voyeurCamButton
            }
        </BaseButtonContainer>
    );
};

export default VoyeurCamButton;
