import { Action } from "redux-actions";
import { call, put, race, select, take, takeEvery } from "redux-saga/effects";

import loggers from "../../loggers";
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import { getChatInstanceSaga } from "./chat";
import selectors from "../../redux/selectors";
import { ActionIds } from "../../redux/interfaces";
import AppService from "../../services/AppService";
import ChatService from "../../services/ChatService";
import { setActionResponse } from "../../redux/modules/messenger";
import { setInformation } from "../../redux/modules/videochat";
import { setLastChargedAmount } from "../../redux/modules/session";
import { ActionResponseType, ICurrency, CurrencyName, VideochatInformationType } from "../../redux/store";
import {takePattern} from "../utils";

const createRechargeActionType = createActionType(ActionIds.RECHARGE, { isSagaAction: true });

// Actions
const RECHARGE_EXTERNAL = createRechargeActionType("RECHARGE_EXTERNAL");
const RECHARGE_FROM_CHAT = createRechargeActionType("RECHARGE_FROM_CHAT");
const RECHARGE_FROM_CHAT_SUCCESS = createRechargeActionType("RECHARGE_FROM_CHAT_SUCCESS");
const RECHARGE_FROM_CHAT_FAIL = createRechargeActionType("RECHARGE_FROM_CHAT_FAIL");

// Action Creators
export const rechargeExternal = createAction(RECHARGE_EXTERNAL);
export const rechargeFromChat = createAction<number>(RECHARGE_FROM_CHAT);
export const rechargeFromChatSuccess = createAction(RECHARGE_FROM_CHAT_SUCCESS);
export const rechargeFromChatFail = createAction(RECHARGE_FROM_CHAT_FAIL);

// Sagas
const rechargeExternalSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    yield call(AppService.getService(instanceId).onRecharge);
};

/** We receive the recharge amount from the vchat-core library */
const rechargeFromChatSaga = function* ({ payload }: Action<IPayload<number>>) {
    const { instanceId, value } = payload;
    const chat = yield call(getChatInstanceSaga, false, instanceId);

    if (chat.chatConf.error) {
        loggers.saga.debug('RECHARGE - could not get chat(conf); aborting');
        return;
    }

    if (chat.chatInstance.error) {
        loggers.saga.debug(
            'RECHARGE -trying to get a chat to make a recharge raised this error:',
            { details: { error: chat.chatInstance.error }}
        );

        yield put(rechargeFromChatFail(instanceId, chat.chatInstance.error));
        return;
    }

    const currency: ICurrency = yield select(selectors.getCurrency, instanceId);

    // credits are sent as they are, but for other currencies, API is working with "cents";
    // so since we are storing the currency values (Dollars, Euros), we should multiply them hundred times
    const chargingAmount = currency.name === CurrencyName.Credits ? value : value * 100;

    const { error } = yield call(ChatService.getService(instanceId).sendCharge, chat.chatInstance.result, chargingAmount);

    if (error) {
        yield put(rechargeFromChatFail(instanceId, error));
    } else {
        yield put(setLastChargedAmount(instanceId, value));
        yield put(rechargeFromChatSuccess(instanceId));
    }
};

const rechargeFromChatFailSaga = function* ({ payload }: Action<IPayload>) {
    const { instanceId, value } = payload;

    yield put(setActionResponse(instanceId, {
        text: value.reason,
        type: ActionResponseType.ERROR
    }));

    const isOneClickRechargeAvailable = yield select(selectors.isOneClickRechargeAvailable, instanceId);

    yield put(setInformation(instanceId,
        isOneClickRechargeAvailable
            ? VideochatInformationType.OUT_OF_MONEY_ONE_CLICK_RECHARGE
            : VideochatInformationType.OUT_OF_MONEY
    ));
};

/**
 * this can be used to see whether the recharge from chat succeeded or failed.
 * E.g.
 * const {result, error} = yield call(getResultOfRechargeFromChatSaga);
 */
export const getResultOfRechargeFromChatSaga = function* ({ payload }: Action<IPayload>) {
    const { instanceId } = payload;

    const result = yield race({
        result: take(takePattern(RECHARGE_FROM_CHAT_SUCCESS, instanceId)),
        error: take(takePattern(RECHARGE_FROM_CHAT_FAIL, instanceId))
    });

    return result;
};

const watchSagas = function* () {
    yield takeEvery(RECHARGE_EXTERNAL, rechargeExternalSaga);
    yield takeEvery(RECHARGE_FROM_CHAT, rechargeFromChatSaga);
    yield takeEvery(RECHARGE_FROM_CHAT_FAIL, rechargeFromChatFailSaga);
};

export default watchSagas;
