import * as React from 'react';

import BaseButton from './BaseButton';
import IStore from '../../../redux/store';
import selectors from '../../../redux/selectors';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    isResponsive: boolean;
    isFullScreen: boolean;
}

interface IComponentProps {
    children: any;
    isForActionSection?: boolean;

    onClick?: () => void;
}

type IProps = IStoreProps & IComponentProps & IInstanceProps;

const BaseButtonContainer = (props: IProps) => (
    <BaseButton
        isResponsive={props.isResponsive}
        isFullScreen={props.isFullScreen}
        isForActionSection={props.isForActionSection}
        onClick={props.onClick}
        children={props.children}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isFullScreen: selectors.isFullScreen(store, instanceId),
    isResponsive: selectors.isResponsive(store, instanceId)
});

export default withRedux<IComponentProps>(BaseButtonContainer, mapStateToProps);