import styled from "styled-components";

interface IWrapperProps {
    isFullscreen: boolean;
	isForInformationDialog?: boolean;
}

export const ActionSectionWrapper = styled.div.attrs({ className: 'action-section' })<IWrapperProps>`
    margin: ${props => props.isFullscreen ? `5px 5px 5px 10px` : `5px 5px 10px 10px`};
    
    ${props => props.isForInformationDialog && `
        width: 100%;
        max-width: 500px;
    ` };
    
    height: 45px;
    background-color: transparent;
    display: flex;
    align-content: center;
    justify-content: center;
    overflow: hidden;    
`;
