import * as React from 'react';
import { InstanceContext } from '../../../utils/utils';

export interface IInstanceProps {
    instanceId: string;
}

const withInstanceInfo = <P extends object>(WrappedComponent: React.ComponentType<P & IInstanceProps>) => {
    type IProps = P & IInstanceProps;

    return class extends React.Component<IProps> {
        render() {
            return (
                <InstanceContext.Consumer>
                    {context => (
                        <WrappedComponent
                            {...this.props}
                            instanceId={context}    
                        />
                    )}
                </InstanceContext.Consumer>
            );
        }
    };
};

export default withInstanceInfo;
