import * as React from 'react';
import { isMobile, withOrientationChange } from 'react-device-detect';

import VideoChat from './VideoChat';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import CenterChild, { PortraitInformationDialogCenterChild } from '../../common/CenterChild';
import { VideoChatContainerWrapper } from './style';
import PreloaderContainer from '../../common/Preloader';
import { setQuality, setShouldHideVideochatControls, setIsMobileMenuOpen } from '../../../redux/modules/videochat';
import IStore, { VideochatInformationType } from '../../../redux/store';
import { createVideoChatPlayer } from "../../../sagas/common/videochat";
import InformationDialogContainer from '../../common/InformationDialog';
import AVSCam2CamPopupContainer from "../AVSCam2CamPopup/AVSCam2CamPopupContainer";
import PlayerService from "../../../services/PlayerService";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import constants from '../../../static/constants';
import PlayButtonContainer from "../PlayButton";

interface IStoreProps {
	informationType: VideochatInformationType;
	shouldRenderRegularInformationDialog: boolean;
	shouldRenderPreloader: boolean;
	shouldHideVideochatControls: boolean;
	isMobileMenuOpen: boolean;
	isAutoplayRestricted: boolean;

	setQuality: ActionDispatcher<boolean>;
	createVideoChatPlayer: ActionDispatcher<HTMLElement>;
	setShouldHideVideochatControls: ActionDispatcher<boolean>;
	setIsMobileMenuOpen: ActionDispatcher<boolean>;
}

/*
 **	These flags are only applicable for mobile devices
 */
interface IOrientationProps {
	isLandscape: boolean;
	isPortrait: boolean;
}

type IProps = IInstanceProps & IStoreProps & IOrientationProps;

class VideoChatContainer extends React.Component<IProps> {

	constructor(props: IProps) {
		super(props);
		this.playerContainer = PlayerService.getService(props.instanceId).getPlayerContainer();
	}

	private playerContainer: HTMLElement | null;

	componentDidMount() {
		const { instanceId } = this.props;
		this.props.setQuality(instanceId, isMobile);

		if (this.playerContainer) {
			this.props.createVideoChatPlayer(instanceId, this.playerContainer);
		}

		this.hideVideoChatControlsTimeout();
	}

	render() {
		const { isPortrait: isMobilePortrait } = this.props;
		const isPreviewFrozen = this.props.informationType === VideochatInformationType.PREVIEW_JUST_FINISHED;
		const CenterChildComponent = isMobilePortrait ? PortraitInformationDialogCenterChild : CenterChild;

		return (
			<VideoChatContainerWrapper>
				{this.props.shouldRenderRegularInformationDialog &&
					<CenterChildComponent>
						<InformationDialogContainer/>
					</CenterChildComponent>
				}

				{!this.props.isAutoplayRestricted &&
				<PreloaderContainer shouldAdjustVertically={isMobilePortrait}/>}

				<PlayButtonContainer/>

				<AVSCam2CamPopupContainer/>

				<VideoChat
					container={this.playerContainer}
					isFrozen={isPreviewFrozen}
					onCreateContainer={this.onCreateContainer}
					shouldHideVideochatControls={this.props.shouldHideVideochatControls}
					displayVideoChatControls={this.displayVideoChatControls}
				/>
			</VideoChatContainerWrapper>
		)
	}

	private displayVideoChatControls = () => {
		if (this.props.shouldHideVideochatControls) {
			this.props.setShouldHideVideochatControls(this.props.instanceId, false);
			this.hideVideoChatControlsTimeout();
		}
	};

	private hideVideoChatControlsTimeout = () => {
		setTimeout(() => {
			if (this.props.isMobileMenuOpen) {
				this.props.setIsMobileMenuOpen(this.props.instanceId, false);
			}

			this.props.setShouldHideVideochatControls(this.props.instanceId, true);
		}, constants.shouldHideVideoChatControlsTimeoutTime)
	};

	private onCreateContainer = (element: HTMLElement) => {
		this.props.createVideoChatPlayer(this.props.instanceId, element);
	};
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	informationType: selectors.getInformationType(store, instanceId),
	shouldRenderRegularInformationDialog: selectors.shouldRenderRegularInformationDialog(store, instanceId),
	shouldRenderPreloader: selectors.shouldRenderPreloader(store, instanceId),
	shouldHideVideochatControls: selectors.shouldHideVideochatControls(store, instanceId),
	isMobileMenuOpen: selectors.isMobileMenuOpen(store, instanceId),
	isAutoplayRestricted: selectors.isAutoplayRestricted(store, instanceId)
});

const mapDispatchToProps = {
	setQuality,
	createVideoChatPlayer,
	setShouldHideVideochatControls,
	setIsMobileMenuOpen
};

export default withRedux(VideoChatContainer, mapStateToProps, mapDispatchToProps, { withOrientationChange });
