import * as React from 'react';

import Message from './Message';
import selectors from '../../../redux/selectors';
import IStore, { MessageDirection, ITranslation, IToyMessageInfo, MessageContentType, MsgType } from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IComponentProps {
    id: string;
    isSystem: boolean;
    dateCreated: string;
    type?: MsgType;
    toy?: IToyMessageInfo;
    direction: MessageDirection;
    contentType?: MessageContentType;
    isImage?: boolean;
    audioSrc?: string;
    username?: string;
    usernameColor?: string;
	groupIndex?: number;
    isFullscreen: boolean;
    isResponsive: boolean;
    isMessageWithJustOneEmoticon?: boolean;
    children: any;

    onContentUpdate?: () => any;
}

interface IStoreProps {
    // Properties
    isMobileLandscape: boolean;
    hasModelProfilePicture: boolean;
    isConsecutiveGroupMessage: boolean;
	isLastConsecutiveGroupMessage: boolean;
	isSoloMessage: boolean;
    modelProfilePicture: any;
    translation: ITranslation;
    isVideoChatInGroupMode: boolean;
    groupChatClientName: string;
    hasClientName: boolean;
	shouldIncreaseMessageFontSize: boolean;
    modelName: string;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

class MessageContainer extends React.Component<IProps> {

    componentDidUpdate(): void {
        if (this.props.onContentUpdate) {
            this.props.onContentUpdate();
        }
    }

    render() {
        return (
            <Message
                id={this.props.id}
                dateCreated={this.props.dateCreated}
                direction={this.props.direction}
                type={this.props.type}
                toy={this.props.toy}
                audioSrc={this.props.audioSrc}
                contentType={this.props.contentType}
                isSystem={this.props.isSystem}
                isImage={this.props.isImage}
                isFullscreen={this.props.isFullscreen}
                isResponsive={this.props.isResponsive}
                username={this.props.username}
                groupChatClientName={this.props.groupChatClientName}
                hasClientName={this.props.hasClientName}
                usernameColor={this.props.usernameColor}
                translation={this.props.translation}
                hasModelProfilePicture={this.props.hasModelProfilePicture}
                isMobileLandscape={this.props.isMobileLandscape}
                isMessageWithJustOneEmoticon={this.props.isMessageWithJustOneEmoticon}
                isConsecutiveGroupMessage={this.props.isConsecutiveGroupMessage}
				isLastConsecutiveGroupMessage={this.props.isLastConsecutiveGroupMessage}
				isSoloMessage={this.props.isSoloMessage}
                modelProfilePicture={this.props.modelProfilePicture}
                isVideoChatInGroupMode={this.props.isVideoChatInGroupMode}
                modelName={this.props.modelName}
                groupIndex={this.props.groupIndex}
                shouldIncreaseMessageFontSize={this.props.shouldIncreaseMessageFontSize}
            >
                {this.props.children}
            </Message>
        );
    }
}

const mapStateToProps = (store: IStore, { instanceId, id }: IProps) => ({
    modelProfilePicture: selectors.getModelProfilePicture(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    hasModelProfilePicture: selectors.hasModelProfilePicture(store, instanceId),
    isMobileLandscape: selectors.isMobileLandscape(store, instanceId),
    isConsecutiveGroupMessage: selectors.isConsecutiveGroupMessage(id)(store, instanceId),
	isLastConsecutiveGroupMessage: selectors.isLastConsecutiveGroupMessage(id)(store, instanceId),
	isSoloMessage: selectors.isSoloMessage(id)(store, instanceId),
    groupChatClientName: selectors.getGroupChatClientName(store, instanceId),
    isVideoChatInGroupMode: selectors.isVideoChatInGroupMode(store, instanceId),
    hasClientName: selectors.hasClientName(store, instanceId),
    modelName: selectors.getModelName(store, instanceId),
	shouldIncreaseMessageFontSize: selectors.getShouldIncreaseMessageFontSize(store, instanceId)
});

export default withRedux<IComponentProps>(MessageContainer, mapStateToProps);
