import * as React from 'react';
import ChatBoxContainer from '../../features/specific/ChatBox';
import UserPromptContainer from '../../features/specific/UserPrompt/UserPromptContainer';
import VideoChatContainer from '../../features/specific/Videochat/VideoChatContainer';
import VideoChatActionSectionContainer
    from '../../features/specific/VideochatActionSection/VideoChatActionSectionContainer';
import { IViewProviderProps } from '../interfaces';
import { PromptsWrapper, VideoChatContainerWrapper, VideoChatMobileLandscapeViewWrapper } from './style';
import GiftSectionContainer from '../../features/specific/GiftSection';
import CountdownWarningContainer from '../../features/specific/CountdownWarning';
import ActionResponseContainer from '../../features/common/ActionResponse';
import ToySectionContainer from '../../features/specific/ToySection';

interface IComponentProps {
    isGiftSectionOpen: boolean;
    isToySectionOpen: boolean;
	isMobileLandscape: boolean;

    onGiftClick: (gift: string) => any;
}

type IProps = IComponentProps & IViewProviderProps;

const VideoChatMobileLandscapeView = (props: IProps) => (
    <VideoChatMobileLandscapeViewWrapper  {...props}>
        <VideoChatContainerWrapper {...props}>
            <VideoChatContainer />

            <VideoChatActionSectionContainer />

            <ChatBoxContainer />

            <PromptsWrapper isMobileLandscape={props.isMobileLandscape}>
                <GiftSectionContainer
                    isOpen={props.isGiftSectionOpen}
                    onGiftClick={props.onGiftClick}
                />

                <ToySectionContainer isOpen={props.isToySectionOpen} />

                <CountdownWarningContainer />

                <ActionResponseContainer />

                <UserPromptContainer />
            </PromptsWrapper>
        </VideoChatContainerWrapper>
    </VideoChatMobileLandscapeViewWrapper>
);

export default VideoChatMobileLandscapeView;
