import { IContainerProps as IProps } from './types';
import { VideoChatType } from '../../../redux/store';

export const onLiveChatButtonClick = (props: IProps) => () => {
    const { LIVECHAT, FREECHAT } = VideoChatType;
    const videoChatType = props.hasFreeChatDuration ? FREECHAT : LIVECHAT;

    props.startVideoChatType(props.instanceId, videoChatType);
};
