import { eventChannel } from "redux-saga";
import {call, put, spawn, take, takeEvery} from "redux-saga/effects";
import {ACTION, IRESPONSE} from "vxcontrol-client-lib";

import ApiService from "../../services/ApiService";
import { onReceiveChannels } from './channel';
import { onReceiveGetChannelInfo, onReceiveSetChannelInfo } from './channelInfo';
import { onReceiveError } from './handleError';
import { onReceiveHistory } from './history';
import { onReceiveInit } from './init';
import { onReceiveLogin } from './login';
import { onReceiveNewChannel } from './newChannel';
import { onReceiveNoop } from './noop';
import { onReceiveOnlineState } from './onlineState';
import { onReceiveQueryBackend } from './queryBackend';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import { ActionIds } from "../../redux/interfaces";
import { Action } from "redux-actions";

const handlers: { [commandType: string]: any } = {};
handlers[ACTION.CMDP_INIT] = onReceiveInit;
handlers[ACTION.CMDP_LOGIN] = onReceiveLogin;
handlers[ACTION.CMDP_NOOP] = onReceiveNoop;
handlers[ACTION.CMDP_GETCHANNELS] = onReceiveChannels;
handlers[ACTION.CMDP_NEWCHANNEL] = onReceiveNewChannel;
handlers[ACTION.CMDP_GETHISTORY] = onReceiveHistory;
handlers[ACTION.CMDP_GETONLINESTATE] = onReceiveOnlineState;
handlers[ACTION.CMDP_QUERYBACKEND] = onReceiveQueryBackend;
handlers[ACTION.CMDP_GETCHANNELINFO] = onReceiveGetChannelInfo;
handlers[ACTION.CMDP_SETCHANNELINFO] = onReceiveSetChannelInfo;

interface IApiPayload {
	response: IRESPONSE;
}

const createApiEventsActionType = createActionType(ActionIds.API_EVENTS, { isSagaAction: true });

// Actions
const API_RESPONSE_OK = createApiEventsActionType("API_RESPONSE_OK");
const API_RESPONSE_ERROR = createApiEventsActionType("API_RESPONSE_ERROR");

// Action Creators
const apiResponseOK = createAction<IApiPayload>(API_RESPONSE_OK);
const apiResponseError = createAction<IApiPayload>(API_RESPONSE_ERROR);

const subscribeToReceiveOKListener = emitter => {
	ApiService.registerOKListener((instanceId: string, response: IRESPONSE) => {
		emitter(apiResponseOK(instanceId, { response }));
	});
};

const subscribeToReceiveErrorListener = emitter => {
	ApiService.registerErrorListener((instanceId: string, response: IRESPONSE) => {
		emitter(apiResponseError(instanceId, { response }));
	});
};

const apiResponseOKSaga = function*({ payload }: Action<IPayload<IApiPayload>>) {
	const { instanceId, value: { response }} = payload;

	if (handlers[response.action]) {
		yield put(handlers[response.action](instanceId, response));
	}
};

const apiResponseErrorSaga = function*({ payload }: Action<IPayload<IApiPayload>>) {
	const { instanceId, value } = payload;
	yield put(onReceiveError(instanceId, value.response));
};

const apiEventsChannel = () => {
	return eventChannel(emitter => {
		subscribeToReceiveOKListener(emitter);
		subscribeToReceiveErrorListener(emitter);

		return () => {
			// no need for un-subscribing; saga's live exceeds the app live
		}
	});
};

const listenForApiEventsSaga = function* () {
	const apiEvents = yield call(apiEventsChannel);

	try {
		while (true) {
			const action = yield take(apiEvents);
			yield put(action);
		}
	} finally { }
};

const watchSaga = function* () {
	yield spawn(listenForApiEventsSaga);
	yield takeEvery(API_RESPONSE_OK, apiResponseOKSaga);
	yield takeEvery(API_RESPONSE_ERROR, apiResponseErrorSaga);

};

export default watchSaga;
