import * as React from 'react';
import { LoaderWrapper} from './style';
import { MessengerWrapper } from '../Messenger/style';
import Loader from 'react-loader-spinner';
import config from "../../../static/config";
import {ThemedStyledProps, withTheme} from "styled-components";
import { ITheme } from '../../../themes';

type IProps = ThemedStyledProps<any, ITheme>;

const MessengerLoader = (props: IProps) => (
    <MessengerWrapper>
        <LoaderWrapper>
            <Loader
                type={"Oval"}
                color={props.theme ? props.theme.colors.messengerLoader : '#AAA'}
                width={config.viewSizes.messengerLoaderSize}
                height={config.viewSizes.messengerLoaderSize}
            />
        </LoaderWrapper>
    </MessengerWrapper>
);

export default withTheme<IProps, ITheme>(MessengerLoader);