import * as React from 'react';

import { getDefaultGift } from './utils';
import UserPromptTip from './UserPromptTip';
import config from "../../../static/config";
import selectors from '../../../redux/selectors';
import UserPromptAnonymousTip from "./UserPromptAnonymousTip";
import IStore, { IGift } from '../../../redux/store';
import { requestSendAnonymousTip, requestSendTip } from '../../../sagas/common/videochat';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps, IContainerComponentProps as IComponentProps } from "./types";

const sendTip = (props: IProps, isAnonymous: boolean = false) => () => {
    if (isAnonymous) {
        props.requestSendAnonymousTip(props.instanceId)
    } else {
        props.requestSendTip(props.instanceId);
    }
};

const getTip = (props: IProps) => (
    <UserPromptTip
        tip={props.tip}
        currency={props.currency}
        isResponsive={props.isResponsive}
        sendTip={sendTip(props)}
        translation={props.translation}
        imageSize={config.tipImageSize}
    />
);

const getAnnonymousTip = (props: IProps) => {
    // We should always render the dollar gift as the anonymous tip.
    const defaultGift: IGift | undefined = getDefaultGift(props);

    return (
        <>
            {defaultGift &&
                <UserPromptAnonymousTip
                    tip={defaultGift}
                    currency={props.currency}
                    isResponsive={props.isResponsive}
                    sendTip={sendTip(props, true)}
                    translation={props.translation}
                    imageSize={config.tipImageSize}
                />
            }
        </>
    );
};


const UserPromptTipContainer = (props: IProps) => props.isAnonymous ? getAnnonymousTip(props) : getTip(props);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    currency: selectors.getCurrency(store, instanceId),
    isResponsive: selectors.isResponsive(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    tip: selectors.getTip(store, instanceId),
    gifts: selectors.getTranslatedGifts(store, instanceId)
});

const mapDispatchToProps = {
    requestSendTip,
    requestSendAnonymousTip
};

export default withRedux<IComponentProps>(UserPromptTipContainer, mapStateToProps, mapDispatchToProps);
