import { find, get, isEmpty, map } from 'lodash';
import * as React from 'react';
import { IBundle, IGiftPage, ITranslation, ICurrency } from '../../../redux/store';
import config from '../../../static/config';
import Arrow, { ArrowDirection } from '../../common/Arrow';
import GiftPageContainer from '../GiftPage';
import GiftTab from '../GiftTab';
import PurchaseBundlePage, { IGiftItemForPurchaseList } from '../PurchaseBundlePage';
import { BoldText, GiftPagesWrapper, GiftPaginatorWrapper, GiftsWrapper, GiftWindowBox, GiftWindowWrapper, RegularText, ArrowWrapper } from './style';
import PerfectScrollbarContainer from '../../common/PerfectScrollbar';

export interface IGiftSectionProps {
    activeIndex: number;
    activeBundle?: IBundle;
    bundleGifts: IGiftItemForPurchaseList[];
    giftPages: IGiftPage[];
    translation: ITranslation;
    specialGiftPages: config.ISpecialGiftPages;
    currency: ICurrency;
    isResponsive?: boolean
    isOpen: boolean;
    isMobileLandscape?: boolean;
    scrollbar: React.Ref<any>;
	isFullScreen: boolean;
	shouldUseDropdownAnimation: boolean;

    closeGiftSection: () => any;
    onBundlePurchase: () => void;
    onBundlePageClose: () => void;
    changeTab: (activeIndex: number) => () => void;
    onGiftClick: (giftEncodedId: string) => () => void;
    onBundleGiftClick: (giftEncodedId: string) => () => void;
}

const GiftSection = (props: IGiftSectionProps) => {
    // FUTURE TODO: for the time being we have the hardcode the classNames based
    // on id's value as the current API does not provide any information about the icons.
    let giftClassNames: { [prop: number]: string };
    giftClassNames = {
        1: "-icon-heart-line",
        2: "-icon-vx-winter-messenger",
        3: "-icon-vx-summer-messenger",
        4: "-icon-vx-fetish-messenger"
    };

    const { inventoryGiftId, giftBundlesId } = props.specialGiftPages;
    giftClassNames[inventoryGiftId] = "-icon-vx-user-outline";
    giftClassNames[giftBundlesId] = "-icon-merchandise-sale-lite";

    const giftTabs = map(props.giftPages, (page: IGiftPage) => {
        /** Get the hard-coded className with fallback on first icon */
        const giftClassName = get(giftClassNames, page.id.toString(), giftClassNames["1"]);

        return (
            <GiftTab
                key={page.id}
                changeTab={props.changeTab}
                giftPage={page}
                className={giftClassName}
                isActive={props.activeIndex === page.id}
                translation={props.translation}
            />
        );
    });

    // The active standard gift page (standard means not the bundle page)
    const activeGiftPage = find(props.giftPages, (page: IGiftPage) => page.id === props.activeIndex);
    const isBundleGiftPage = !!activeGiftPage && activeGiftPage.id === giftBundlesId;
    const isInventoryGiftPage = !!activeGiftPage && activeGiftPage.id === inventoryGiftId;

    const onGiftClick = isBundleGiftPage ? props.onBundleGiftClick : props.onGiftClick;

    const { giftBundles, bundlesReducedPrice } = props.translation.common;

    // We create the titles for bundle page and inventory page
    let title;
    if (isInventoryGiftPage) {
        title = <RegularText>{props.translation.user.boughtGiftsHeading}</RegularText>;
    }

    if (isBundleGiftPage) {
        title = (
            <>
                <RegularText>{giftBundles}</RegularText> <BoldText>{bundlesReducedPrice}</BoldText>
            </>
        );
    }

    const standardGiftPage = activeGiftPage && (
        <GiftPageContainer
            key={activeGiftPage.id}
            page={activeGiftPage.gifts}
            activePage={props.activeIndex}
            onGiftClick={onGiftClick}
            isBundleGiftPage={isBundleGiftPage}
            title={title}
        />
    );

    const { activeBundle } = props;

    // The bundle gifts page
    const bundleGiftPage = activeBundle && (
        <PurchaseBundlePage
            imageUrl={activeBundle.imageUrl}
            name={activeBundle.name}
            gifts={props.bundleGifts}
            currency={props.currency}
            price={activeBundle.price}
            translation={props.translation}
            onClose={props.onBundlePageClose}
            onBundlePurchase={props.onBundlePurchase}
        />
    );

    const shouldRenderBundleGiftPage = activeGiftPage
        && activeGiftPage.id === giftBundlesId
        && !isEmpty(props.activeBundle);

    return (
        <GiftWindowWrapper
            isOpen={props.isOpen}
            isResponsive={props.isResponsive}
            isMobileLandscape={props.isMobileLandscape}
            isFullScreen={props.isFullScreen}
            shouldUseDropdownAnimation={props.shouldUseDropdownAnimation}
        >
            <GiftWindowBox>
                <GiftPaginatorWrapper isResponsive={props.isResponsive}>
	                <ArrowWrapper>
	                    <Arrow
	                        onClick={props.closeGiftSection}
	                        arrowDirection={props.isResponsive ? ArrowDirection.LEFT : ArrowDirection.DOWN}
	                        isResponsive={props.isResponsive}
	                    />
	                </ArrowWrapper>

                    <GiftsWrapper>
                        {giftTabs}
                    </GiftsWrapper>

                </GiftPaginatorWrapper>

                <GiftPagesWrapper>

                    {!!(bundleGiftPage || standardGiftPage) &&
                        <PerfectScrollbarContainer ref={props.scrollbar}>
                            {shouldRenderBundleGiftPage ? bundleGiftPage : standardGiftPage}
                        </PerfectScrollbarContainer>
                    }

                </GiftPagesWrapper>
            </GiftWindowBox>
        </GiftWindowWrapper>

    );
};

export default GiftSection;
