import * as React from 'react';

import AudioPlayer from './AudioPlayer';


interface IProps {
    audioSrc: string;
}

interface IState {
    duration: number;
    currentTime: number;
    isPlaying: boolean;
    clickedTime?: number;
}

class AudioPlayerContainer extends React.Component<IProps, IState> {
    private readonly audioRef: any;

    constructor(props) {
        super(props);

        this.audioRef = React.createRef<HTMLElement>();
        this.state = {
            duration: 0,
            currentTime: 0,
            isPlaying: false,
            clickedTime: 0
        };
    }

    componentDidMount() {
        const audioElement = this.audioRef.current;

        audioElement.addEventListener("loadedmetadata", this.setAudioData);
        audioElement.addEventListener("timeupdate", this.setAudioTime);
        audioElement.addEventListener('ended', this.stopPlaying);
    }

    componentWillUnmount(): void {
        const audioElement = this.audioRef.current;

        audioElement.removeEventListener("loadedmetadata", this.setAudioData);
        audioElement.removeEventListener("timeupdate", this.setAudioTime);
        audioElement.removeEventListener("ended", this.stopPlaying);
    }

    componentDidUpdate(_, prevState: IState) {
        const { clickedTime, isPlaying, currentTime } = this.state;
        const audioElement = this.audioRef.current;

        if (prevState.isPlaying !== isPlaying) {
            if (isPlaying) {
                audioElement.play();
            } else {
                audioElement.pause();
            }
        }

        if (clickedTime && clickedTime !== currentTime) {
            audioElement.currentTime = clickedTime;
            this.setState({ clickedTime: undefined });
        }
    }

    render() {
        return (
            <AudioPlayer
                audioRef={this.audioRef}
                audioSrc={this.props.audioSrc}
                isPlaying={this.state.isPlaying}
                currentTime={this.state.currentTime}
                duration={this.state.duration}

                setAudioTime={this.setAudioTime}
                setClickedTime={this.setClickedTime}
                startPlaying={this.startPlaying}
                stopPlaying={this.stopPlaying}
            />
        )
    }

    private setAudioTime = () => {
        const audioElement = this.audioRef.current;

        this.setState({ currentTime: audioElement.currentTime })
    };

    private setAudioData = () => {
        const audioElement = this.audioRef.current;

        this.setState({
            currentTime: audioElement.currentTime,
            duration: audioElement.duration
        });
    };

    private setClickedTime = (clickedTime: number) => {
        this.setState({
            clickedTime
        })
    };

    private startPlaying = () => {
        this.setState({ isPlaying: true });
    };

    private stopPlaying = () => {
        this.setState({ isPlaying: false });
    };
}

export default AudioPlayerContainer;
