import * as React from 'react';

import UserPrompt from './UserPrompt';
import { onCloseUserPrompt } from './utils';
import selectors from '../../../redux/selectors';
import { answerUserPromptSection, closeUserPromptSection, IAnswerUserPromptPayload } from '../../../redux/modules/messenger';
import IStore from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps, IContainerComponentProps as IComponentProps } from "./types";

const onAnswer = (props: IProps) => (answer: IAnswerUserPromptPayload) => {
    props.answerUserPromptSection(props.instanceId, answer);
};

const UserPromptContainer = (props: IProps) => (
    <UserPrompt
        className={props.className}
        currency={props.currency}
        prompts={props.userPrompts}
        isResponsive={props.isResponsive}
        translation={props.translation}
        isMobileLandscape={props.isMobileLandscape}
        isGiftSectionOpen={props.isGiftSectionOpen}
        isTipPromptOpen={props.isTipPromptOpen}
        isAnyUserPromptOpen={props.isAnyUserPromptOpen}

        closeUserPrompt={onCloseUserPrompt(props)}
        onAnswer={onAnswer(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    currency: selectors.getCurrency(store, instanceId),
    isResponsive: selectors.isResponsive(store, instanceId),
    areAnyUserPromptsOpen: selectors.areAnyUserPromptsOpen (store, instanceId),
    userPrompts: selectors.getUserPrompts(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    isGiftSectionOpen: selectors.isGiftSectionOpen(store, instanceId),
    isTipPromptOpen: selectors.isTipPromptOpen(store, instanceId),
    isAnyUserPromptOpen: selectors.isAnyUserPromptOpen(store, instanceId),
    isMobileLandscape: selectors.isMobileLandscape(store, instanceId)
});

const mapDispatchToProps = {
    closeUserPromptSection,
    answerUserPromptSection
};

export default withRedux<IComponentProps>(UserPromptContainer, mapStateToProps, mapDispatchToProps);
