import * as React from 'react';
import onClickOutside, { InjectedOnClickOutProps } from 'react-onclickoutside';

import ContactNote from './ContactNoteDialog';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import IStore, { IContactNote, ITranslation } from '../../../redux/store';
import { requestSetContactNote } from '../../../sagas/websockets/channelInfo';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    contactNote: IContactNote;
    translation: ITranslation;

    requestSetContactNote: ActionDispatcher<string>;
}

type IProps = IInstanceProps & IStoreProps & InjectedOnClickOutProps;

interface IState {
    text: string;
}

class ContactNoteDialogContainer extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            text: ""
        };
    }
    
    componentDidMount() {
        this.setState({
            text: this.props.contactNote.text
        });
    }

    render() {
        return (
            <ContactNote 
                onCloseClick={this.onCloseClick}
                onInputChange={this.onInputChange}
                contactNoteText={this.state.text}
                translation={this.props.translation}
            />
        );
    }

    private onCloseClick = () => {
        this.props.requestSetContactNote(this.props.instanceId, this.state.text);
    };

    private onInputChange = (event: any) => {
        this.setState({
            text: event.target.value
        });
    }

    // @ts-ignore
    private handleClickOutside = () => {
        this.props.requestSetContactNote(this.props.instanceId, this.state.text);
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    contactNote: selectors.getContactNote(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

const mapDispatchToProps = {
    requestSetContactNote
};

export default withRedux(
    onClickOutside(ContactNoteDialogContainer, { excludeScrollbar: true }),
    mapStateToProps,
    mapDispatchToProps
);
