import React from 'react';
import { Props as ReactSelectProps } from 'react-select';

import MentionMenu from './MentionMenu';
import IStore, { ITranslation } from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import selectors from '../../../redux/selectors';
import withRedux from '../../hoc/withRedux';
import {
	closeMentionSection,
	setActiveMention
} from '../../../redux/modules/messenger';
import { ActionDispatcher } from '../../../redux';

interface IStoreProps {
	shouldDisplayCustomMentionSection: boolean;
	participantsNumber: number;
	modelName: string;
	translation: ITranslation;

	closeMentionSection: ActionDispatcher;
	setActiveMention: ActionDispatcher<string>;
}

type IProps = IStoreProps & ReactSelectProps & IInstanceProps;

const MentionMenuContainer = (props: IProps) => {
	const onModelClick = () => {
		props.setActiveMention(props.instanceId, props.modelName);
		props.closeMentionSection(props.instanceId);
	};

	const onCloseMenu = () => {
		props.closeMentionSection(props.instanceId);
	};

	return (
		<MentionMenu
			{...props}
			shouldDisplayCustomMentionSection={props.shouldDisplayCustomMentionSection}
			participantsNumber={props.participantsNumber}
			modelName={props.modelName}
			onModelClick={onModelClick}
			onCloseMenu={onCloseMenu}
			translation={props.translation}
		/>
	);
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	shouldDisplayCustomMentionSection: selectors.shouldDisplayCustomMentionSection(store, instanceId),
	participantsNumber: selectors.getGroupChatParticipantsNumber(store, instanceId),
	modelName: selectors.getModelName(store, instanceId),
	translation: selectors.getTranslation(store, instanceId),
});

const mapDispatchToProps = {
	closeMentionSection,
	setActiveMention
};

export default withRedux(MentionMenuContainer, mapStateToProps, mapDispatchToProps);
