import { includes, get } from 'lodash';
import { createSelector } from "reselect";
import { isMobile } from 'react-device-detect';

import IStore, { VideochatInformationType, VideoChatType, IVideochat } from "../store";
import { getInitialInstanceData } from '../initialStore';

export const getVideoChat = (store: IStore, instanceId: string | undefined) => {
    const id = instanceId as string;
    const data = get(store, id) || getInitialInstanceData();

    return data.videochat;
};

/**
 * @return {VideochatInformationType | undefined} - type of the informational pop-up to be displayed
 * when the video stream is interrupted for various reasons
 */
export const getInformationType = createSelector(getVideoChat, selection => selection.informationType);

/**
 * @return {boolean} - true if the current information type to be displayed when the stream ends
 * is implying that the messenger itself will be closed as well
 */
export const isExitingInformationType = createSelector(getVideoChat,
    selection => !!selection.informationType &&
        includes([
            VideochatInformationType.LIVECHAT_SESSION_ENDED,
            VideochatInformationType.EJECTED,
            VideochatInformationType.HOST_OFFLINE,
            VideochatInformationType.PRIVATE_CHAT,
            VideochatInformationType.VOICECALL_ENDED,
            VideochatInformationType.ERROR_OCCURRED,
            VideochatInformationType.OUT_OF_MONEY
        ], selection.informationType)
);

/**
 * @return {PreChatInformationType | undefined} - type of the informational pop-up to be displayed
 * before the video stream is actually started
 */
export const getPreChatInformationType = createSelector(getVideoChat,
    selection => selection.preChatInformationType);

/**
 * @return {IResolution | undefined} - suggested width and height of the video stream, obtained from the API
 */
export const getInitialResolution = createSelector(getVideoChat,
    selection => selection.initialResolution);

/**
 * @return {IQualityBoundaries} - details about the qualities of the video stream, to be feed to the video player
 */
export const getVideoChatQualityBoundaries = createSelector(getVideoChat, selection => selection.qualityBoundaries);

/**
 * @return {VideoChatType | undefined} - type of the started video stream
 */
export const getVideoChatType = createSelector(getVideoChat, selection => selection.type);

/**
 * @return {boolean} - true if video chat is in live chat mode
 */
export const isVideoChatInLiveChatMode = createSelector(getVideoChatType,
    videoChatType => videoChatType === VideoChatType.LIVECHAT
);

/**
 * @return {boolean} - true if video chat is in free chat mode
 */
export const isVideoChatInFreeChatMode = createSelector(getVideoChatType,
    videoChatType => videoChatType === VideoChatType.FREECHAT
);

/**
 * @return {boolean} - true if video chat is in preview mode
 */
export const isVideoChatInPreviewMode = createSelector(getVideoChatType,
    videoChatType => videoChatType === VideoChatType.LIVE_PREVIEW
);

/**
 * @return {boolean} - true if video chat is in free mode (the client has free duration chat)
 */
export const isVideoChatInFreeMode = createSelector(getVideoChatType,
    videoChatType => videoChatType === VideoChatType.FREECHAT
);

/**
 * @return {boolean} - true if the videochat type is preview or freechat, in order
 * to render the continue with livechat dialog when the free seconds have been consumed.
 */
export const shouldDisplayContinueWithLiveChat = createSelector(
    [isVideoChatInPreviewMode, isVideoChatInFreeMode],
    (isInPreview, isInFreeChat) => isInPreview || isInFreeChat
);

/**
 * @return {boolean} - true if video chat is in voyeur mode
 */
export const isVideoChatInVoyeurMode = createSelector(getVideoChatType,
    videoChatType => videoChatType === VideoChatType.VOYEUR_CHAT
);


/**
 * @return {boolean} - true if video chat is in group chat mode
 */
export const isVideoChatInGroupMode = createSelector(getVideoChatType,
    videoChatType => videoChatType === VideoChatType.GROUP_CHAT
);

/**
 * @return {boolean} - true if video chat is in voyeur mode,
 * but with a model currently being in a private chat with someone else
 */
export const isVideoChatInVoyeurPrivateMode = createSelector(getVideoChatType,
    videoChatType => videoChatType === VideoChatType.VOYEUR_PRIVATE_CHAT
);

/**
 * @return {VideoChatType | undefined} - type of the last successfully started video stream
 */
export const getLastSuccessfulVideoChatType = createSelector(getVideoChat, selection => selection.lastSuccessfulType);

/**
 * @return {VideoChatType | undefined} - type of the video chat session to be automatically started
 */
export const getInitialVideoChatType = createSelector(getVideoChat,selection => selection.initialVideoChatType);

/**
 * @return {boolean} true if the model cannot be heard
 */
export const getVideoChatIsMutedByModel = createSelector(getVideoChat, selection => selection.isMutedByModel);

/**
 * @return {boolean} true if the client muted the audio stream
 */
export const getVideoChatIsMutedByClient = createSelector(getVideoChat, selection => selection.isMutedByClient);

/**
 * @return {boolean} true if the video chat is initially muted due to browser's restrictions
 */
export const displayVolumeAsInitiallyMuted = createSelector(getVideoChat, selection =>
    selection.isMutedByClient && selection.isInitiallyMuted);

/**
 * @return {boolean} true to display the hint for initially muted button due to browser's restrictions
 */
export const isInitiallyMutedHintDisplayed = createSelector(getVideoChat, selection =>
    selection.isInitiallyMutedHintDisplayed);

/**
 * @return {boolean} true if cam to cam feature was activated or not
 * (user turned on his web cam and send video stream to the model)
 */
export const isCam2CamActive = createSelector(getVideoChat, selection => selection.cam2Cam.isActive);

/**
 * @return {boolean} true if the cam2cam feature is enabled
 */
export const isCam2CamEnabled = createSelector(getVideoChat, videochat => videochat.cam2Cam.isEnabled);

/**
 * @return {boolean} true if cam to cam video area is minimized
 * (usually because of overall video available area being to small)
 */
export const isCam2CamMinimized = createSelector(getVideoChat, selection => selection.cam2Cam.isMinimized);

/**
 * @return {ICameraDevice[]} list of available cameras on the current device
 */
export const getCam2CamCameras = createSelector(getVideoChat, selection => selection.cam2Cam.cameras);

/**
 * @return {ICameraDevice | null} selected camera (if more that one are available) to be used for the cam2cam
 */
export const getCam2CamSelectedCamera = createSelector(getVideoChat, (videochat: IVideochat) =>
    videochat.cam2Cam.selectedCameraId
        ? videochat.cam2Cam.cameras.find(camera => camera.deviceId === videochat.cam2Cam.selectedCameraId)
        : null
);

/**
 * @return {boolean} true if the messenger is in full screen mode
 */
export const isFullScreen = createSelector(getVideoChat, selection => selection.isFullscreen);

/**
 * @return {boolean} true if the video chat mode is open
 */
export const isVideoChatOpen = createSelector(getVideoChat, selection => selection.isOpen);

/**
 * @return {number} available time in seconds (correlated with the actual available money)
 * before video chat is turned off
 */
export const getVideoChatTimeLeft = createSelector(getVideoChat,
    selection => selection.timeLeft);

/**
 * @return {boolean} true if the available time left is under a critical threshold
 */
export const isVideoChatTimeLeftBelowThreshold = createSelector(getVideoChat,
    selection => selection.isTimeLeftBelowThreshold);

/**
 * @return {number} available time in seconds for the video chat session,
 * got in the previous notification from the API
 */
export const getVideoChatPreviousTimeLeft = createSelector(getVideoChat,
    selection => selection.previousTimeLeft);

/**
 * @return {number} initial time in seconds to send messages to the model,
 * after a live preview session has ended
 */
export const getLivePreviewTextTimeLimit = createSelector(getVideoChat,
    selection => selection.livePreviewTextTimeLimit);

/**
 * @return {number} available left time (in seconds) to send messages to the model,
 * after a live preview session has ended
 */
export const getLivePreviewTextTimeLeft = createSelector(getVideoChat,
    selection => selection.livePreviewTextTimeLeft);

/**
 * @return {boolean} - whether pre-chat information dialog should be displayed or not
 */
export const shouldRenderPreChatInformationDialog = createSelector(getVideoChat,
    selection => selection.preChatInformationType
        && !selection.isPlaying
        && !selection.type
);

/**
 * @return {boolean} - Whether chat information dialog can be displayed or not.
 * This means that a minimum set of requirements is fulfilled in order to display the informational pop up
 */
export const canRenderInformationDialog = createSelector([getVideoChat],
    (selection) => !!selection.informationType
        && !selection.isPlaying
);

/**
 * @return {boolean} true if a regular chat information dialog should be displayed
 */
export const shouldRenderRegularInformationDialog = createSelector([canRenderInformationDialog, isExitingInformationType],
    (canRender, isExiting) => canRender && !isExiting);

/**
 * @return {boolean} - true if  the (video) session is starting - this logic
 * will not apply for self-streaming (age verification scenario)
 */
export const isStarting = createSelector(getVideoChat,
	videoChat => videoChat.isOpen
		&& !videoChat.isPlaying
		&& !videoChat.informationType
		&& videoChat.type !== VideoChatType.NO_DOWNSTREAM
);

/**
 * @return {boolean} - true if the vide stream is playing
 */
export const isPlaying = createSelector(getVideoChat, videochat => videochat.isPlaying);

/**
 * Display or not the messenger section during a video session
 * @return {boolean}
 */
export const shouldRenderOnlyVideochat = createSelector(getVideoChat, videochat => videochat.shouldRenderOnlyVideochat);

/**
 * @return {boolean} - true if the drop-down menu for mobile devices is open
 */
export const isMobileMenuOpen = createSelector(getVideoChat, videochat => videochat.isMobileMenuOpen);

/**
 * @return {ExitEvent} - the last exiting error for video chat
 */
export const getExitEvent = createSelector(getVideoChat, videoChat => videoChat.exitEvent);

/**
 * @return {boolean} true if the messenger was open in AVS (age-verification-system) mode;
 */
export const isInAVSMode = createSelector(getInitialVideoChatType, selection =>
    selection === VideoChatType.NO_DOWNSTREAM
);

/**
 * @return {boolean} - true if  the (video) pre-loader should be rendered;
 * Note: it should be rendered if videochat is open, video is not playing, not avs mode
 * and there is no information dialog
 */
export const shouldRenderPreloader = createSelector([isStarting, isInAVSMode],
    (starting: boolean, avsMode: boolean) => starting && !avsMode
);

export const resumeChatTimestamp = createSelector(getVideoChat, videochat => videochat.resumeChatTimestamp);

export const isMobileLandscape = createSelector(isFullScreen, (isFullscreen: boolean) => isFullscreen && isMobile);

export const shouldHideVideochatControls = createSelector([getVideoChat, isPlaying],
		(videochat, isVideochatPlaying: boolean) => isVideochatPlaying && videochat.shouldHideVideochatControls);

/** Number of participants in a group chat */
export const getGroupChatParticipantsNumber = createSelector(getVideoChat,
    videochat => videochat.groupChatParticipants.length
);

/** The list of group chat participants withing a group chat sessions */
export const getGroupChatParticipants = createSelector(getVideoChat,
	videochat => videochat.groupChatParticipants
);

/** Client's name/alies used for starting a group chat session */
export const getGroupChatClientName = createSelector(getVideoChat,
    videochat => videochat.groupChatClientName
);

/**
 * @return {boolean} true if the number of participants in a group chat should be displayed
 */
export const shouldRenderGroupChatParticipants = createSelector(getVideoChat,
    videochat => videochat.type === VideoChatType.GROUP_CHAT
        && !!videochat.groupChatParticipants.length
);

/**
 * @return {boolean} true if video stream is restricted by autoplay policies
 */
export const isAutoplayRestricted = createSelector(getVideoChat,
    videochat => !!videochat.isAutoplayRestricted
);
