import * as React from 'react';
import {ModalInformationDialogWrapper} from "./style";
import InformationDialogContainer from "../../common/InformationDialog/InformationDialogContainer";
import PreChatInformationDialogContainer from "../../common/PreChatInformationDialog/PreChatInformationDialogContainer";

export interface IModalInformationDialogProps {
    shouldRenderExitingInformationDialog: boolean;
    shouldRenderPreChatInformationDialog: boolean;
}

const ModalInformationDialog = (props: IModalInformationDialogProps) => (
    <>
        {props.shouldRenderExitingInformationDialog && (
            <ModalInformationDialogWrapper>
                <InformationDialogContainer />
            </ModalInformationDialogWrapper>
        )}

        {props.shouldRenderPreChatInformationDialog && (
            <ModalInformationDialogWrapper>
                <PreChatInformationDialogContainer />
            </ModalInformationDialogWrapper>
        )}
    </>
);


export default ModalInformationDialog;