import * as moment from "moment";
import * as React from "react";
import { isEmpty } from "lodash";

import IMessageProps from "./interfaces";
import config from "../../../static/config";
import SimpleModelPicture from "../../common/SimpleModelPicture";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses";
import GroupContainer, { IComponentProps as IGroupContainerProps } from '../../common/Group';
import {
    ClientMessage,
    ClientMessageWrapper,
    ContentWrapper,
    FullscreenReceivedMessageWrapper,
    FullscreenSystemMessage,
    FullscreenSystemMessageWrapper,
    GroupChatModelInfoWrapper,
    GroupChatModelName,
    GroupChatUsername,
    MessageContentWrapper,
    ProfilePictureWrapper,
    ReceivedMessageAndImageWrapper,
    ReceivedMessageWrapper,
    ToyMessageWrapper,
	GroupChatPictureWrapper
} from "./style";
import ToyMessage from "../../../features/common/ToyMessage";
import { IToyMessageInfo } from "../../../redux/store";

export const getMessageTime = (dateCreated: string) => moment(dateCreated).format("LT");

/**
 * Returns the message sent by the current client as a React element.
 */
export const getClientMessageComponent = (
	content: any,
	timeIndicatorComponent: React.ReactElement,
	props: IMessageProps,
	shouldRenderAudioMessage?: boolean
) => {
	const shouldRenderClientName = props.isVideoChatInGroupMode
		&& props.hasClientName
		&& !props.isConsecutiveGroupMessage
		&& !isEmpty(props.groupChatClientName);

	return (
		<ClientMessageWrapper
			id={props.id}
			isFullscreen={props.isFullscreen}
			isMobileLandscape={props.isMobileLandscape}
			isConsecutiveGroupMessage={props.isConsecutiveGroupMessage}
			isLastConsecutiveGroupMessage={props.isLastConsecutiveGroupMessage}
			isSoloMessage={props.isSoloMessage}
		>
			{hasTimeIndicator(props) && timeIndicatorComponent}

			<ClientMessage
				hasImage={props.isImage}
				isFullscreen={props.isFullscreen}
				isConsecutiveGroupMessage={props.isConsecutiveGroupMessage}
				isVideoChatInGroupMode={props.isVideoChatInGroupMode}
				shouldRenderAudioMessage={shouldRenderAudioMessage}
			>
				{shouldRenderClientName &&
					<GroupChatUsername isFromClient={true}>
						{props.groupChatClientName}
					</GroupChatUsername>
				}

				<ContentWrapper
					isVideoChatInGroupMode={props.isVideoChatInGroupMode}
					isMessageWithJustOneEmoticon={props.isMessageWithJustOneEmoticon}
					shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}
				>
					{content}
				</ContentWrapper>
			</ClientMessage>
		</ClientMessageWrapper>
	);
};

/**
 * Returns the message sent by the model as a React element
 */
export const getReceivedMessageComponent = (
    content: any,
    timeIndicatorComponent: React.ReactElement,
    props: IMessageProps,
    shouldHavePicture: boolean = true,
    isGroupChatUserMessage: boolean = false
) => {
    const shouldRenderModelProfilePicture = !props.isFullscreen
        && !props.isResponsive
        && props.hasModelProfilePicture
        && shouldHavePicture
        && !props.isConsecutiveGroupMessage;

    const messageContent = (
		<MessageContentWrapper
			hasImage={props.isImage}
			isConsecutiveGroupMessage={props.isConsecutiveGroupMessage}
			isVideoChatInGroupMode={props.isVideoChatInGroupMode}
			isGroupChatUserMessage={isGroupChatUserMessage}
			isFullscreen={props.isFullscreen}
		>
			{content}
		</MessageContentWrapper>
    );

	const isContextWithoutProfilePicture = props.isResponsive
		|| props.isFullscreen
		|| !isEmpty(props.username)
		|| props.isVideoChatInGroupMode;

	const messageData = (
        <ReceivedMessageWrapper
            id={props.id}
            isConsecutiveGroupMessage={props.isConsecutiveGroupMessage}
			isLastConsecutiveGroupMessage={props.isLastConsecutiveGroupMessage}
			isSoloMessage={props.isSoloMessage}
			hasProfilePicture={!isContextWithoutProfilePicture}
        >
            {messageContent}
        </ReceivedMessageWrapper>
    );

    const fullscreenMessage = (
        <FullscreenReceivedMessageWrapper id={props.id}>
            {messageData}
        </FullscreenReceivedMessageWrapper>
    );

    const receivedMessage = <>
        {shouldRenderModelProfilePicture &&
			<ProfilePictureWrapper>
				<SimpleModelPicture
					picture={props.modelProfilePicture}
					size={config.profilePictureSizes.small}
				/>
			</ProfilePictureWrapper>
        }

        {props.isFullscreen ? fullscreenMessage : messageData}
    </>;

    const groupModeReceivedMessage = <>
        {props.isFullscreen
            ? fullscreenMessage
            : (<ReceivedMessageWrapper>
                    {messageContent}
                </ReceivedMessageWrapper>
            )
        }
	</>;

    return (
        <ReceivedMessageAndImageWrapper
            id={props.id}
            isMobileLandscape={props.isMobileLandscape}
            isConsecutiveGroupMessage={props.isConsecutiveGroupMessage}
			isLastConsecutiveGroupMessage={props.isLastConsecutiveGroupMessage}
			isSoloMessage={props.isSoloMessage}
            isContextWithoutProfilePicture={isContextWithoutProfilePicture}
        >
            {props.isVideoChatInGroupMode
                ? groupModeReceivedMessage
                : receivedMessage
            }

			{hasTimeIndicator(props) && timeIndicatorComponent}
        </ReceivedMessageAndImageWrapper>
    );
};

/**
 * Creates a static group component with automated test identifier
 */
const SystemMessage = withCustomClasses<IGroupContainerProps>(GroupContainer,
    [DomCustomClass.Message, DomCustomClass.SystemMessage]
);

/**
 * Returns the system message received from the backend as a React element
 */
export const getSystemMessage = (content: any, props: IMessageProps) => {
    const systemMessageComponent = (
        <div id={props.id}>
            <SystemMessage
                datetime={props.dateCreated}
                informationText={props.children}
                showArrow={false}
            />
        </div>
    );

    const fullscreenSystemMessageComponent = (
        <FullscreenSystemMessageWrapper id={props.id}>
            <FullscreenSystemMessage shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}>
                {content}
            </FullscreenSystemMessage>
        </FullscreenSystemMessageWrapper>
    );

    return props.isFullscreen
        ? fullscreenSystemMessageComponent
        : systemMessageComponent;
};

/**
 *
 * @param toy
 */
export const getToyMessage = (toy: IToyMessageInfo, messageId?: string, groupIndex?: number) => (
    <ToyMessageWrapper
        id={messageId}
        isActive={toy.isActive}
        shouldApplyMargin={groupIndex === 1}
	>
        <ToyMessage {...toy} />
    </ToyMessageWrapper>
);

export const getGroupChatUserMessage = (
    content: any,
    timeIndicatorComponent: React.ReactElement,
    props: IMessageProps
) => {
    const groupMessageContent = (
        <>
            {!props.isConsecutiveGroupMessage &&
				<GroupChatUsername color={props.usernameColor as string}>
					{props.username}
				</GroupChatUsername>
            }
            <ContentWrapper
                isVideoChatInGroupMode={props.isVideoChatInGroupMode}
                isGroupUserMessage={true}
				isMessageWithJustOneEmoticon={props.isMessageWithJustOneEmoticon}
				shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}
            >
                {content}
            </ContentWrapper>
        </>
    );

    return getReceivedMessageComponent(groupMessageContent, timeIndicatorComponent, props, false, true);
};

export const getModelMessageComponent = (
    content: any,
    timeIndicatorComponent: React.ReactElement,
    props: IMessageProps
) => {
    const hasGroupChatInfo = props.isVideoChatInGroupMode
        && !props.isConsecutiveGroupMessage;

    const messageContent = (
        <>
            {hasGroupChatInfo && (
                <GroupChatModelInfoWrapper>
					{props.hasModelProfilePicture &&
						<GroupChatPictureWrapper>
							<SimpleModelPicture
								picture={props.modelProfilePicture}
								size={config.profilePictureSizes.extraSmall}
								isGroupChatMode={props.isVideoChatInGroupMode}
							/>
						</GroupChatPictureWrapper>
					}

					<GroupChatModelName isVideoChatInGroupMode={props.isVideoChatInGroupMode}>
						{props.modelName}
					</GroupChatModelName>
                </GroupChatModelInfoWrapper>
            )}
            <ContentWrapper
                isVideoChatInGroupMode={props.isVideoChatInGroupMode}
                isModelMessage={true}
				isMessageWithJustOneEmoticon={props.isMessageWithJustOneEmoticon}
				shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}
            >
                {content}
            </ContentWrapper>
        </>
    );

	return getReceivedMessageComponent(messageContent,
		timeIndicatorComponent,
		props,
		props.hasModelProfilePicture
	);
};

const hasTimeIndicator = (props: IMessageProps) => !props.isFullscreen;
