import * as React from 'react';

import { IViewProviderProps } from '../interfaces';
import IStore from '../../redux/store';
import selectors from '../../redux/selectors';
import { requestSendMessage } from '../../sagas/websockets/message';
import VideoChatMobileLandscapeView from './VideoChatMobileLandscapeView';
import { setIsMobileMenuOpen } from '../../redux/modules/videochat';
import { closeGiftSection } from '../../redux/modules/messenger';
import withRedux from '../../features/hoc/withRedux';
import { IInstanceProps } from '../../features/hoc/withInstanceInfo';
import { ActionDispatcher } from '../../redux';

interface IStoreProps {
    isGiftSectionOpen: boolean;
    isToySectionOpen: boolean;
	isMobileLandscape: boolean;

    requestSendMessage: ActionDispatcher<string>;
    setIsMobileMenuOpen: ActionDispatcher<boolean>;
    closeGiftSection: ActionDispatcher;
}

type IComponentProps = IViewProviderProps;
type IProps = IComponentProps & IStoreProps & IInstanceProps;

class VideoChatMobileLandscapeViewContainer extends React.Component<IProps> {
    render() {
        return (
            <VideoChatMobileLandscapeView
                onGiftClick={this.onGiftClick}
                {...this.props}
            />
        );
    }

    private onGiftClick = (gift: string) => () => {
        const { instanceId } = this.props;

        this.props.requestSendMessage(instanceId, decodeURIComponent(gift));
        this.props.setIsMobileMenuOpen(instanceId, false);
        this.props.closeGiftSection(instanceId);
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isGiftSectionOpen: selectors.isGiftSectionOpen(store, instanceId),
    isToySectionOpen: selectors.isToySectionOpen(store, instanceId),
	isMobileLandscape: selectors.isMobileLandscape(store, instanceId)
});

const mapDispatchToProps = {
    requestSendMessage,
    setIsMobileMenuOpen,
    closeGiftSection
};

export default withRedux<IComponentProps>(VideoChatMobileLandscapeViewContainer, mapStateToProps, mapDispatchToProps);
