import { get } from 'lodash';
import { produce } from 'immer';
import { AnyAction } from 'redux';
import { Action, handleActions } from "redux-actions";
import { SupportedLanguage } from 'vxcontrol-client-lib';

import { RESET_STORE } from "./app";
import { ActionIds, IPreferenceActionPayload } from '../interfaces';
import { IUser, Translations } from '../store';
import { createAction, createActionType } from '../utils';
import { getInitialInstanceData } from "../initialStore";
import { PreferenceName } from "../../services/AppService/types";

const createUserActionType = createActionType(ActionIds.USER, { isReduxAction: true });

// Actions
export const SET_LANGUAGE = createUserActionType("SET_LANGUAGE");
export const SET_IS_VIP = createUserActionType("SET_IS_VIP");
export const SET_TRANSLATIONS = createUserActionType("SET_TRANSLATIONS");
export const SET_USER_ONLINE_STATE = createUserActionType("SET_USER_ONLINE_STATE");
export const SET_USER_KEY = createUserActionType("SET_USER_KEY");
export const SET_PREFERENCE = createUserActionType("SET_PREFERENCE");

// Action Creators
export const setLanguage = createAction<SupportedLanguage>(SET_LANGUAGE);
export const setIsVip = createAction<boolean>(SET_IS_VIP);
export const setTranslations = createAction<Translations>(SET_TRANSLATIONS);
export const setUserOnlineState = createAction<boolean>(SET_USER_ONLINE_STATE);
export const setUserKey = createAction<string>(SET_USER_KEY);
export const setPreference = createAction<IPreferenceActionPayload>(SET_PREFERENCE);

// Reducer
const user = handleActions<IUser>({
	[RESET_STORE]: produce((draft: IUser, { payload }) => ({
		...getInitialInstanceData().user,
		isOnline: get(navigator, "online", true)
	})),

	[SET_LANGUAGE]: produce((draft: IUser, { payload }: AnyAction) => {
		draft.language = payload;
	}),

	[SET_TRANSLATIONS]: produce((draft: IUser, { payload }: AnyAction) => {
		const { EN, DE, ES } = payload;
		draft.translations = { en: EN, de: DE, es: ES };
	}),

	[SET_IS_VIP]: produce((draft: IUser, { payload }: Action<boolean>) => {
		draft.isVip = payload;
	}),

	[SET_USER_ONLINE_STATE]: produce((draft: IUser, { payload }: Action<boolean>) => {
		draft.isOnline = payload;
	}),

	[SET_USER_KEY]: produce((draft: IUser, { payload }: Action<string>) => {
		draft.userKey = payload;
	}),

	[SET_PREFERENCE]: produce((draft: IUser, { payload }: Action<IPreferenceActionPayload>) => {
		const { preferenceName, value } = payload;

		if (preferenceName === PreferenceName.ShouldIncreaseMessageFontSize) {
			draft.preferences[PreferenceName.ShouldIncreaseMessageFontSize] = value as boolean;
		} else if (preferenceName === PreferenceName.VolumeLevel && value !== 0) {
			draft.preferences[PreferenceName.VolumeLevel] = value as number;
		}
	}),
}, getInitialInstanceData().user);

export default user;
