import styled from 'styled-components';
import withCustomClasses, {DomCustomClass} from "../../hoc/withCustomClasses/";
import utils from '../../../utils';

interface IGroupWrapperProps {
	showArrow?: boolean;
}

interface IGroupContentWrapperProps {
	isOpen?: boolean;
}

interface IGroupTextWrapper {
	shouldIncreaseMessageFontSize?: boolean;
}

export const GroupContentWrapper = styled.div<IGroupContentWrapperProps>`
	${({isOpen}) => !isOpen ? `
        width: 0;
        height: 0;
        overflow: hidden;
    ` : ''}
`;

export const GroupWrapper = withCustomClasses<IGroupWrapperProps>(styled.div<IGroupWrapperProps>`
	font-family: ${(props) => props.theme.typography.fontFamily};
	font-weight: ${(props) => props.theme.typography.weight.regular};
	color: ${(props) => props.theme.colors.groupTextColor};
	width: ${(props) => props.showArrow ? '93%' : '100%'};
	border-top: 1px solid ${(props) => props.theme.colors.groupTextColor};
	font-style: normal;
	font-size: 12px;
	text-align: center;
	margin: 15px auto;
	display: block;
	clear: both;
`, [DomCustomClass.Group]);

export const GroupTextWrapper = styled.span<IGroupTextWrapper>`
	background: ${(props) => props.theme.colors.groupColor};

	font-size: ${({shouldIncreaseMessageFontSize}) => utils.computeMessageFontSize(shouldIncreaseMessageFontSize, 12)};
	display: inline-block;
	position: relative;
	text-align: center;
	padding: 5px 15px;
	top: -14px;
`;

export const TextArrow = styled.a`
	display: inline-block;
	cursor: pointer;
	margin-right: 5px;
`;
