import * as React from 'react';

import IStore from '../../../redux/store';
import selectors from '../../../redux/selectors';
import ModalInformationDialog from "./ModalInformationDialog";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    shouldRenderExitingInformationDialog: boolean;
    shouldRenderPreChatInformationDialog: boolean;
}

type IProps = IStoreProps & IInstanceProps;

const ModalInformationDialogContainer = (props: IProps) => (
    <ModalInformationDialog
        shouldRenderExitingInformationDialog={props.shouldRenderExitingInformationDialog}
        shouldRenderPreChatInformationDialog={props.shouldRenderPreChatInformationDialog}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    shouldRenderExitingInformationDialog: selectors.shouldRenderExitingInformationDialog(store, instanceId),
    shouldRenderPreChatInformationDialog: selectors.shouldRenderPreChatInformationDialog(store, instanceId)
});

export default withRedux(ModalInformationDialogContainer, mapStateToProps);
