import * as React from 'react';
import { upperFirst } from 'lodash';
import { withOrientationChange } from "react-device-detect";

import utils from '../../../utils';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import InformationDialog from './InformationDialog';
import AppService from '../../../services/AppService';
import { closeMessenger } from "../../../redux/modules/messenger";
import { resetInformation } from '../../../redux/modules/videochat';
import RechargeSelectionButtons from "../RechargeSelectionButtons";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { Content, LivePreviewTextElement, TimerWrapper } from './style';
import { rechargeExternal, rechargeFromChat } from "../../../sagas/common/recharge";
import LiveChatButtonContainer from '../../specific/LiveChatButton/LiveChatButtonContainer';
import IStore, { ICurrency, ITranslation, VideochatInformationType, VideoChatType } from '../../../redux/store';
import { resumeExistingVideochat, resumePreviousVideochat, startVideoChatType } from "../../../sagas/common/videochat";
import LivePreviewTextTimerContainer from "../../specific/LivePreviewTextTimer/LivePreviewTextTimerContainer";

/*
 **	These flags are only applicable for mobile devices
 */
interface IOrientationProps {
	isLandscape: boolean;
	isPortrait: boolean;
}

interface IInformationDialog {
	title: string;
	text?: string;
	button?: any;
	buttonText?: string;
	closeTooltip?: string;
	cancelText?: string;
	hasModelProfilePicture: boolean;
	hasCloseButton: boolean;
	children?: JSX.Element;

	onButtonClick?: () => void;
}

interface IStoreProps {
	modelName: string;
	currency: ICurrency;
	translation: ITranslation;
	informationType: VideochatInformationType;
	lastRechargedAmount: number;
	oneClickRechargeAmounts: number[];
	oneClickRechargeDefaultAmount?: number;
	isUserOnline: boolean;
	canRenderInformationDialog: boolean;
	isModelSupportingVoyeurToPrivateChat: boolean;
	isVideoChatOpen: boolean;
	hasModelProfilePicture: boolean;

	closeMessenger: ActionDispatcher;
	resetInformation: ActionDispatcher;
	rechargeExternal: ActionDispatcher;
	resumeExistingVideochat: ActionDispatcher;
	resumePreviousVideochat: ActionDispatcher;
	rechargeFromChat: ActionDispatcher<number>;
	startVideoChatType: ActionDispatcher<VideoChatType>;
}

type IProps = IStoreProps & IInstanceProps & IOrientationProps;

interface IState {
	selectedAmount?: number;
}

class InformationDialogContainer extends React.Component<IProps, IState> {
	constructor(props: IProps) {
		super(props);

		this.state = {
			selectedAmount: props.oneClickRechargeDefaultAmount
		}
	}

	render() {
		const infoDictionary = this.getInformations();
		const { informationType, isLandscape: isMobileLandscape } = this.props;
		const information = informationType && infoDictionary[informationType];

		return (
			<>
				{information && this.props.canRenderInformationDialog &&
					<InformationDialog
						{...information}
						isVideoChatOpen={this.props.isVideoChatOpen}
						onCloseClick={this.onCloseClick}
						isMobileLandscape={isMobileLandscape}
						informationType={this.props.informationType}
					/>
				}
			</>
		);
	}

	private onCloseClick = () => {
		const { instanceId } = this.props;
		this.props.resetInformation(instanceId);
		this.props.closeMessenger(instanceId);
	};

	private onVerifyAgeClick = () => {
		AppService.getService(this.props.instanceId).onAgeVerification();
	};

	private outOfMoneyButtonClick = () => {
		const { instanceId } = this.props;
		this.props.rechargeExternal(instanceId);
		this.props.resetInformation(instanceId);
	};

	private outOfMoneyOneClickButtonClick = (selectedAmount?: number) => () => {
		const { instanceId } = this.props;

		if (selectedAmount) {
			this.props.rechargeFromChat(instanceId, selectedAmount);
			this.props.resetInformation(instanceId);
		}
	};

	private thankYouButtonClick = () => {
		const { instanceId } = this.props;
		this.props.resetInformation(instanceId);
		this.props.resumeExistingVideochat(instanceId);
	};

	private continueChatButtonClick = () => {
		const { instanceId } = this.props;

		if (this.props.isUserOnline) {
			this.props.resetInformation(instanceId);
			this.props.resumePreviousVideochat(instanceId);
		}
	};

	private startVoyeurButtonClick = () => {
		const { instanceId } = this.props;
		this.props.resetInformation(instanceId);
		this.props.startVideoChatType(instanceId, VideoChatType.VOYEUR_PRIVATE_CHAT);
	};


	/**
	 * Create the mapping between the information type and the information object.
	 * This mapping is implemented in render for having a clean access to translations.
	 */
	private getInformations = () => {
		const modelname = this.props.modelName;
		const {
			oneClickRechargeAmounts,
			oneClickRechargeDefaultAmount,
			isModelSupportingVoyeurToPrivateChat
		} = this.props;
		const { chat, common: { close, cancel, thankYou, paymentSuccess }, error, user } = this.props.translation;
		const cancelText = upperFirst(cancel);

		const informations: { [informationType: string]: IInformationDialog } = {};
		informations[VideochatInformationType.OFFLINE_CONNECTION] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: user.youAreOffline,
			text: utils.translationWithValues(chat.checkConnection, { modelname }),
			buttonText: chat.continue,
			onButtonClick: this.continueChatButtonClick,
			cancelText
		};

		informations[VideochatInformationType.ERROR_OCCURRED] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: error.error,
			text: utils.translationWithValues(chat.actorWaiting, { modelname }),
			buttonText: chat.continue,
			onButtonClick: this.continueChatButtonClick,
			cancelText
		};

		informations[VideochatInformationType.OUT_OF_MONEY] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: chat.noMoney,
			text: chat.exitLoadAgain,
			buttonText: chat.charge,
			onButtonClick: this.outOfMoneyButtonClick,
			cancelText
		};

		informations[VideochatInformationType.OUT_OF_MONEY_ONE_CLICK_RECHARGE] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: chat.noMoney,
			text: chat.exitLoadAgain,
			children: (
				<RechargeSelectionButtons
					amounts={oneClickRechargeAmounts}
					currency={this.props.currency}
					defaultAmount={oneClickRechargeDefaultAmount}
					onSelect={(selectedAmount: number) => {
						this.setState({ selectedAmount })
					}}
					hasSubmit={false}
				/>
			),
			buttonText: chat.charge,
			onButtonClick: this.outOfMoneyOneClickButtonClick(this.state.selectedAmount),
			cancelText
		};

		informations[VideochatInformationType.VERIFY_AGE] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: false,
			title: chat.verifyAge,
			text: utils.translationWithValues(chat.avsNeeded, { modelname }),
			buttonText: chat.verifyAge,
			onButtonClick: this.onVerifyAgeClick,
			cancelText
		};

		informations[VideochatInformationType.PREVIEW_JUST_FINISHED] = {
			hasModelProfilePicture: false,
			hasCloseButton: false,
			title: chat.previewEndHeadline,
			children: (
				<>
					<TimerWrapper>
						<LivePreviewTextTimerContainer/>
					</TimerWrapper>

					<LivePreviewTextElement>
						{utils.translationWithValues(chat.previewEndCountdown, { modelname })}
					</LivePreviewTextElement>
				</>
			),
			button: (
				<Content>
					<LiveChatButtonContainer/>
				</Content>
			),
		};

		informations[VideochatInformationType.THANK_YOU_ONE_CLICK_RECHARGE] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: thankYou,
			text: utils.translationWithValues(chat.chargeSuccess, {
				amount: `${this.props.lastRechargedAmount} ${this.props.currency.name}`
			}),
			buttonText: chat.continue,
			onButtonClick: this.thankYouButtonClick,
			cancelText
		};

		informations[VideochatInformationType.THANK_YOU] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: thankYou,
			text: paymentSuccess,
			buttonText: chat.continue,
			onButtonClick: this.thankYouButtonClick,
			cancelText
		};

		informations[VideochatInformationType.LIVECHAT_SESSION_ENDED] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: chat.popup.ended,
			text: chat.popup.ended
		};

		informations[VideochatInformationType.EJECTED] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: chat.interrupted,
			text: utils.translationWithValues(chat.popup.kicked, { modelname })
		};

		informations[VideochatInformationType.HOST_OFFLINE] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: chat.popup.ended,
			text: utils.translationWithValues(chat.popup.offline3, { modelname })
		};

		informations[VideochatInformationType.PRIVATE_CHAT] = {
			hasModelProfilePicture: this.props.hasModelProfilePicture,
			hasCloseButton: true,
			closeTooltip: close,
			title: chat.interrupted,
			buttonText: isModelSupportingVoyeurToPrivateChat ? chat.startVoyeur : undefined,
			onButtonClick: this.startVoyeurButtonClick,
			text: utils.translationWithValues(isModelSupportingVoyeurToPrivateChat
				? chat.popup.privateChat2
				: chat.popup.privateChat1, { modelname }
			)
		};

		return informations;
	};
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	informationType: selectors.getInformationType(store, instanceId),
	modelName: selectors.getModelName(store, instanceId),
	currency: selectors.getCurrency(store, instanceId),
	translation: selectors.getTranslation(store, instanceId),
	lastRechargedAmount: selectors.getLastRechargedAmount(store, instanceId),
	oneClickRechargeAmounts: selectors.getOneClickRechargeAmounts(store, instanceId),
	oneClickRechargeDefaultAmount: selectors.getOneClickRechargeDefaultAmount(store, instanceId),
	isUserOnline: selectors.isUserOnline(store, instanceId),
	canRenderInformationDialog: selectors.canRenderInformationDialog(store, instanceId),
	isModelSupportingVoyeurToPrivateChat: selectors.isModelSupportingVoyeurToPrivateChat(store, instanceId),
	isVideoChatOpen: selectors.isVideoChatOpen(store, instanceId),
	hasModelProfilePicture: selectors.hasModelProfilePicture(store, instanceId)
});

const mapDispatchToProps = {
	resetInformation,
	rechargeFromChat,
	rechargeExternal,
	closeMessenger,
	resumeExistingVideochat,
	resumePreviousVideochat,
	startVideoChatType
};

export default withRedux(InformationDialogContainer, mapStateToProps, mapDispatchToProps, { withOrientationChange });
