import * as React from 'react';
import { isIE } from 'react-device-detect';
import { isEmpty } from 'lodash';

import { ITranslation } from '../../../redux/store';
import { BaseButtonContent, FirstRowText } from '../../common/BaseButton/style';
import { Cam2CamButtonWrapper, Cam2CamDisabledButtonWrapper, Cam2CamEnabledIndicator } from './style';
import Tooltip from '../../common/Tooltip/Tooltip';
import utils from '../../../utils'
import BaseButtonContainer from '../../common/BaseButton';

export interface IProps {
    translation: ITranslation;
    isSelected: boolean;
    shouldDisplayIcon?: boolean;
    updateBrowserLink?: string;
    isForActionSection?: boolean;

    onClick: () => any;
}

const Cam2CamButton = (props: IProps) => {
    const {
        camToCam,
        browserOutdatedTagLine: updateBrowserTagLine,
        browserOutdated: updateBrowser,
        browserOutdatedHrefText: updateBrowserLinkText
    } = props.translation.chat;


    const cam2camButton = (
        <>
            {props.isSelected && <Cam2CamEnabledIndicator />}

			{utils.getShouldDisplayIcon(props.shouldDisplayIcon) &&
            	<i className={"-icon-webcam-c2c-full"} />
            }
            
			<BaseButtonContent>
				<FirstRowText>{camToCam}</FirstRowText>
			</BaseButtonContent>
        </>
    );

    const text = (`
        <p class="update-browser">${updateBrowser}</p>
        <p>${updateBrowserTagLine}</p>
        
        ${!isEmpty(props.updateBrowserLink)
            ? `<a href="${props.updateBrowserLink}">${updateBrowserLinkText}</a>`
            : ''
        }
    `);

    const tooltipProps = {
        id: "cam2cam-disabled-button",
        text,
        isHtml: true,
        isClickable: true,
        isSolid: true,
        isLight: true,
        delayHide: 500
    };

    const disabledCam2CamButton = (
        <BaseButtonContainer isForActionSection={props.isForActionSection}>
            <Tooltip {...tooltipProps} >
                <Cam2CamDisabledButtonWrapper>
                    {cam2camButton}
                </Cam2CamDisabledButtonWrapper>
            </Tooltip>
        </BaseButtonContainer>
    );

    const enabledCam2CamButton = (
        <BaseButtonContainer
            onClick={props.onClick}
            isForActionSection={props.isForActionSection}
        >
            <Cam2CamButtonWrapper isSelected={props.isSelected}>
                {cam2camButton}
            </Cam2CamButtonWrapper>
        </BaseButtonContainer>
    );

	// IE-SPECIFIC: The cam2cam button is disabled on any IE browser.
    return (
        <>
            {isIE ? disabledCam2CamButton : enabledCam2CamButton}
        </>
    )
};

export default Cam2CamButton;
