import React from 'react';
import Select from 'react-select'

import { getMentionSectionWrapper, selectStyles } from './style';
import CustomMentionOptionContainer from '../CustomMentionOption';
import MentionMenuContainer from '../MentionMenu';
import { DomIdentifier } from '../../hoc/withIdentifier';
import {IStateOption} from "./types";

export interface IProps {
	mentionOptions: IStateOption[];
	isOpen: boolean;
	isFullScreen: boolean;
	shouldDisplayCustomMentionSection: boolean;
	inputValue: string;
	focusedOptionId?: string;

	onChange: (value: any) => void;
	onMouseFocus: (id: string) => void;
}

const MentionSection = (props: IProps) => {
	const { shouldDisplayCustomMentionSection, isFullScreen, isOpen } = props;

	const CustomOption = (innerProps) => (
		<div data-id={innerProps.data.id} >
			<CustomMentionOptionContainer {...innerProps}
				  onMouseFocus={props.onMouseFocus}
			/>
		</div>
	);

	const components = {
		Control: () => <></>,
		NoOptionsMessage: () => <></>,
		Option: CustomOption,
		Menu: MentionMenuContainer
	};

	const MentionSectionWrapper = getMentionSectionWrapper(
		props.shouldDisplayCustomMentionSection
			? DomIdentifier.CustomMentionSection
			: DomIdentifier.MentionSection
	);

	return (
		<MentionSectionWrapper
			isFullScreen={isFullScreen}
			isOpen={isOpen}
			shouldDisplayCustomMentionSection={shouldDisplayCustomMentionSection}
		>
			<Select
				options={props.mentionOptions}
				styles={selectStyles({ isFullScreen, shouldDisplayCustomMentionSection })}
				components={components}
				menuIsOpen={shouldDisplayCustomMentionSection || props.isOpen}
				onChange={props.onChange}
				inputValue={props.inputValue}
				isFocused={true}
			/>
		</MentionSectionWrapper>
	);
};

export default MentionSection;
