import * as React from 'react';

import CountdownWarning from './CountdownWarning';
import selectors from "../../../redux/selectors";
import { ActionDispatcher } from "../../../redux";
import { rechargeExternal } from '../../../sagas/common/recharge';
import { openCountdownWarning } from '../../../redux/modules/messenger';
import {closeFullscreen, setVideoChatTimeLimit} from '../../../redux/modules/videochat';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import IStore, { ICountdownWarning, ITranslation, KnownAnswerType } from '../../../redux/store';
import { openLoadAccountOneClickPrompt, IOpenLoadAccountPayload } from "../../../sagas/common/userPrompt";

interface IComponentProps {
    className?: string;
}

export interface IStoreProps {
    isResponsive: boolean;
    isFullscreen: boolean;
    isOneClickRechargeAvailable: boolean;
    translation: ITranslation;
    warning?: ICountdownWarning;

    rechargeExternal: ActionDispatcher;
    closeFullscreen: ActionDispatcher;
    openCountdownWarning: ActionDispatcher<ICountdownWarning>;
    setVideoChatTimeLimit: ActionDispatcher<number>;
    openLoadAccountOneClickPrompt: ActionDispatcher<IOpenLoadAccountPayload>
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

class CountdownWarningContainer extends React.Component<IProps> {
    render() {
        return (
            <>
                {this.props.warning &&
                    <CountdownWarning
                        onAnswer={this.openRecharge}
                        onTimeIsOver={this.onTimeIsOver}
                        translation={this.props.translation}
                        warning={this.props.warning}
                        isResponsive={this.props.isResponsive}
                        isFullscreen={this.props.isFullscreen}
                        updateCountdownWarningTime={this.updateCountdownWarningTime}
                    />
                }
            </>
        );
    }

    private openRecharge = () => {
        const { instanceId } = this.props;

        if (this.props.isOneClickRechargeAvailable) {
            this.props.openLoadAccountOneClickPrompt(instanceId, { answer: KnownAnswerType.OK });
            return;
        }

        if (this.props.isFullscreen) {
            this.props.closeFullscreen(instanceId);
        }

        this.props.rechargeExternal(instanceId);
    };

    private updateCountdownWarningTime = (time: number) => {
        if (this.props.warning) {
            this.props.openCountdownWarning(this.props.instanceId, {
                ...this.props.warning,
                time
            });
        }
    };

    private onTimeIsOver = () => {
        this.props.setVideoChatTimeLimit(this.props.instanceId, 0);
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    translation: selectors.getTranslation(store, instanceId),
    isOneClickRechargeAvailable: selectors.isOneClickRechargeAvailable(store, instanceId),
    isResponsive: selectors.isResponsive(store, instanceId),
    isFullscreen: selectors.isFullScreen(store, instanceId),
    warning: selectors.getCountdownWarning(store, instanceId)
});

const mapDispatchToProps = {
    openLoadAccountOneClickPrompt,
    rechargeExternal,
    openCountdownWarning,
    closeFullscreen,
    setVideoChatTimeLimit
};

export default withRedux<IComponentProps>(CountdownWarningContainer, mapStateToProps, mapDispatchToProps);