import { chain, filter, find, get, isEmpty, some } from 'lodash';
import { createSelector } from "reselect";

import utils from '../../utils';
import config from "../../static/config";
import IStore, { IMessage, IMessenger, IQuestion, LoadingStatus, QuestionType } from "../store";
import { getInitialInstanceData } from "../initialStore";

export const getMessenger = (store: IStore, instanceId: string | undefined) => {
	const id = instanceId as string;
	const data = get(store, id) || getInitialInstanceData();
	return data.messenger;
};

/**
 * @return {WindowSectionState} - a slice of the store keeping status for:
 * - emoticons section - if it is open or not
 * - gift section - if it is open or not
 * - user prompts - those sliding areas asking for refill, verifying age, sending tip, etc
 * - countdown warnings - countdown watch warnings, like: running out of money, running out of time
 */
export const getWindowToggleState = createSelector(getMessenger, selection => selection.windowToggleState);

/**
 * @return {Question[]} - a slice of the store keeping status of the user prompts
 * (those sliding areas asking for balance refill, verifying age, sending tip, etc)
 */
export const getUserPrompts = createSelector(getWindowToggleState, selection => selection.userPrompts);

export const hasLoadAccountUserPrompts = createSelector(getUserPrompts,
	(userPrompts: IQuestion[]) => some(userPrompts, (prompt) => utils.isLoadAccountUserPrompt(prompt.type))
);

export const hasSendTipPromptOpen = createSelector(getUserPrompts,
	(userPrompts: IQuestion[]) => some(userPrompts, (userPrompt) => userPrompt.type === QuestionType.SEND_ANONYMOUS_TIP)
);

/**
 * @return {ActionResponse | undefined} action response
 * (sliding element informing about success or failure of a certain API request
 */
export const getActionResponse = createSelector(getMessenger, selection => selection.actionResponse);

/**
 * @return {IMessage[]} - messages to be displayed in the messenging area
 */
export const getMessages = createSelector(getMessenger, messenger => filter(
	messenger.messages,
	(message: IMessage) => !message.isDeleted
));

/**
 * @return {IMessage} - message to meet the condition(s) of provided predicate
 */
export const findMessage = (predicate: (message: IMessage) => boolean) =>
	createSelector(getMessages, selection => find(selection, predicate));

/**
 * @return {string[]} - messages not sent because token has expired
 */
export const getFailedMessages = createSelector(getMessenger, selection => selection.failedMessages);

/**
 * @return {}boolean} true if failed messages should be sent again
 */
export const shouldResendFailedMessages = createSelector(getMessenger, selection => selection.shouldResendFailedMessages);

/**
 * @return {IEmoticon[]} - list of available emoticons obtained either from the back end APIs or the default list
 */
export const getEmoticons = createSelector(getMessenger, selection =>
	isEmpty(selection.emoticons)
		? config.defaultEmoticons
		: selection.emoticons
);

export const getModalContent = createSelector(getMessenger, selection => selection.modalContent);

/**
 * @return {LoadingStatus} the messenger loading status with details about how the loading process ended
 * (with success or failure)
 */
export const getMessengerLoadingStatus = createSelector(getMessenger, selection => selection.loadingStatus);

/**
 * @return {boolean} true if the messenger is still loading
 */
export const isMessengerLoading = createSelector(getMessengerLoadingStatus, selection =>
	selection === LoadingStatus.LOADING);

/**
 * @return {boolean} true if the messenger is open
 */
export const isOpen = createSelector(getMessenger, selection => selection.isOpen);

/**
 * @return {IContactNote} - text note about the current model and if the contact note editor is open or not
 */
export const getContactNote = createSelector(getMessenger, selection => selection.contactNote);

/**
 * @return {boolean} true if the contact note section is open
 */
export const isContactNoteOpen = createSelector(getContactNote, contactNote => contactNote.isOpen);

/**
 * @return {boolean} true if the model has attached a note about her
 */
export const hasContactNoteText = createSelector(getContactNote,
	selection => !isEmpty(selection.text)
);

/**
 * @return {boolean} - true if the user can write text messages to a model
 */
export const isTextEnabled = createSelector(getMessenger, messenger => messenger.isTextEnabled);

/**
 * @return {boolean} - true if currently there is an user prompt displayed on the screen
 */
export const isAnyUserPromptOpen = createSelector(getUserPrompts,
	(userPrompts: IQuestion[]) => !isEmpty(userPrompts)
);

/**
 * @return {boolean} - true if currently there is a displayed user prompt asking for close chat confirmation
 */
export const isCloseChatUserPromptOpen = createSelector(getUserPrompts,
	(userPrompts: IQuestion[]) =>
		userPrompts.some((prompt: IQuestion) => prompt.type === QuestionType.CLOSE_CHAT)
);

/**
 * @return {boolean} true if drop down menu is open
 */
export const isDropDownMenuOpen = createSelector(getWindowToggleState,
	selection => selection.isDropDownOpen
);

/**
 * @return {boolean} true if the toy section is open
 */
export const isToySectionOpen = createSelector(getWindowToggleState,
	windowToggleState => windowToggleState.isToySectionOpen
);

/**
 * @return {boolean} true if the box displaying the messenger's version is open
 */
export const isVersionOpen = createSelector(getWindowToggleState, selection => selection.isVersionOpen);

/**
 * @return {boolean} - true if draft.js editor is working on the current browser or os
 */
export const hasEditor = createSelector(getMessenger, messenger => messenger.hasEditor);

/**
 * @returns {string/undefined} - returns active gift or undefined if there is none selected
 */
export const getActiveGift = createSelector(getMessenger, messenger => messenger.activeSymbols.gift);

/**
 * @returns {string/undefined} - returns active emoticon or undefined if there is none selected
 */
export const getActiveEmoticon = createSelector(getMessenger, messenger => messenger.activeSymbols.emoticon);

/**
 * @returns {string/undefined} - returns active mention or undefined if there is none selected
 */
export const getActiveMention = createSelector(getMessenger, messenger => messenger.activeSymbols.mention);

/**
 * @return {StoredEditorState} current store of the editor (containing text, emoticons, gifts)
 */
export const getEditorState = createSelector(getMessenger, messenger => messenger.editorState);

/**
 * @return {boolean} - true if there any user prompts open
 */
export const areAnyUserPromptsOpen = createSelector(getWindowToggleState, selection =>
	!isEmpty(selection.userPrompts)
);

/**
 * @return {boolean} - true if the messenger is in responsive mode
 */
export const isResponsive = createSelector(getMessenger, messenger => messenger.isResponsive);

/**
 * return {boolean} true if the image upload dialog was requested to appear
 */
export const wasRequestedAnImageUpload = createSelector(
	getWindowToggleState,
	selection => !!selection.requestImageUpload
);

/**
 * return {boolean} true if the anonymous tip is open
 */
export const isTipPromptOpen = createSelector(
	getUserPrompts,
	(userPrompts: IQuestion[]) => some(userPrompts, (userPrompt) =>
		userPrompt.type === QuestionType.SEND_ANONYMOUS_TIP ||
		userPrompt.type === QuestionType.SEND_TIP)
);

/**
 * @return {IToyOptions[]} if the model has activated the toy control feature and the client
 * is in an active LIVECHAT, the client will have acces to the toy section, where
 * he has multiple vibration options from which to choose. It returns these choices.
 */
export const getToyOptions = createSelector(getMessenger,
	(messenger: IMessenger) => messenger.toyOptions
);

/**
 * @return {IToyOption} the active toy option if there is one
 */
export const getActiveToyOption = createSelector(getMessenger,
	(messenger: IMessenger) => find(messenger.toyOptions, option => option.isPlaying)
);

/**
 * @return {boolean} true if any section is open
 */
export const isAnySectionOpen = createSelector(getWindowToggleState, windowState =>
	windowState.isEmoticonSectionOpen ||
	windowState.isGiftSectionOpen ||
	windowState.isToySectionOpen
);

/**
 * @return {boolean} true id we have any user prompt open when
 * the messenger is rendered in a responsive context
 */
export const areAnyPromptsOpenInResponsiveMode = createSelector(
	[getMessenger, areAnyUserPromptsOpen],
	(messenger: IMessenger, areUserPromptsOpen: boolean) => messenger.isResponsive && areUserPromptsOpen
);


export const getLastMessage = createSelector(getMessages, (messages: IMessage[]) =>
	chain(messages)
		.orderBy((message) => message.dateCreated, "desc")
		.first()
		.value()
);

export const isAnyTextInEditor = createSelector(
	getMessenger,
	(messenger: IMessenger) => !isEmpty(messenger.editorState.text)
);

export const getShouldSendMessage = createSelector(
	getMessenger,
	(messenger: IMessenger) => messenger.audioMessage.shouldSendMessage
);

export const getSecondsElapsed = createSelector(
	getMessenger,
	(messenger: IMessenger) => messenger.audioMessage.secondsElapsed
);

export const isRecording = createSelector(getMessenger,
	(messenger: IMessenger) => messenger.audioMessage.isRecording
);

/**
 * @return {boolean} true if the microphone is enabled by user.
 */

export const isMicEnabled = createSelector(getMessenger,
	(messenger: IMessenger) => messenger.audioMessage.isMicEnabled
);

/**
 * Returns a list of colors, which will be used only in group chat mode
 * for rendering the username for other clients' messages.
 */
export const getGroupChatColors = createSelector(getMessenger,
	messenger => messenger.groupChatColors
);

export const getMentionQuery = createSelector(getMessenger,
	(messenger: IMessenger) => messenger.mentionQuery
);

/**
 * return focused option in mentions section
 */
export const getFocusedMentionOption = createSelector(getMessenger,
	(messenger: IMessenger) => messenger.focusedMentionOption
);

export const shouldDisplayCustomMentionSection = createSelector(getMessenger,
	(messenger: IMessenger) => messenger.shouldDisplayCustomMentionSection
);
