import * as React from 'react';

import selectors from '../../../redux/selectors';
import IStore from '../../../redux/store';
import VideoChatActionSection from './VideoChatActionSection';
import { VideoChatActionSectionContainerWrapper } from './style';
import {
	toggleFullscreen,
	toggleMobileMenu,
	toggleSound,
	hideInitialMutedHint,
	reloadPlayer,
	setVolume
} from './utils';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import {
	hideInitiallyMutedHint,
	setIsInitiallyMuted,
	setIsMobileMenuOpen
} from '../../../redux/modules/videochat';

import { setPreference } from '../../../redux/modules/user';
import { IContainerProps as IProps, IContainerComponentProps as IComponentProps } from "./types";

const VideoChatActionSectionContainer = (props: IProps) => (
    <VideoChatActionSectionContainerWrapper shouldHideVideochatControls={props.shouldHideVideochatControls}>
        <VideoChatActionSection
            isFullscreen={props.isFullscreen}
            isMutedByModel={props.isMutedByModel}
            isMobileMenuOpen={props.isMobileMenuOpen}
            isMutedByClient={props.isMutedByClient}
			clientVolume={props.clientVolume}
            displayVolumeAsInitiallyMuted={props.displayVolumeAsInitiallyMuted}
            isInitiallyMutedHintDisplayed={props.isInitiallyMutedHintDisplayed}
            hasFullscreen={props.hasFullscreen}
            translation={props.translation}

            toggleFullscreen={toggleFullscreen(props)}
            toggleMobileMenu={toggleMobileMenu(props)}
            toggleSound={toggleSound(props)}
            hideInitiallyMutedHint={hideInitialMutedHint(props)}
            reloadPlayer={reloadPlayer(props)}
			setClientVolume={setVolume(props)}
        />
    </VideoChatActionSectionContainerWrapper>
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    translation: selectors.getTranslation(store, instanceId),
    isFullscreen: selectors.isFullScreen(store, instanceId),
    isMutedByModel: selectors.getVideoChatIsMutedByModel(store, instanceId),
    isMobileMenuOpen: selectors.isMobileMenuOpen(store, instanceId),
    isMutedByClient: selectors.getVideoChatIsMutedByClient(store, instanceId),
	clientVolume: selectors.getClientVolume(store, instanceId),
	notMutedClientVolume: selectors.getNotMutedClientVolume(store, instanceId),
    displayVolumeAsInitiallyMuted: selectors.displayVolumeAsInitiallyMuted(store, instanceId),
    isInitiallyMutedHintDisplayed: selectors.isInitiallyMutedHintDisplayed(store, instanceId),
    hasFullscreen: selectors.hasFullscreen(store, instanceId),
    shouldHideVideochatControls: selectors.shouldHideVideochatControls(store, instanceId)
});

const mapDispatchToProps = {
    setIsMobileMenuOpen,
    hideInitiallyMutedHint,
    setIsInitiallyMuted,
	setPreference
};

export default withRedux<IComponentProps>(VideoChatActionSectionContainer, mapStateToProps, mapDispatchToProps);
