import * as React from 'react';
import { isIOS } from "react-device-detect";
import MobileFooter from '../../features/common/MobileFooter';
import MobileHeaderContainer from '../../features/common/MobileHeader/MobileHeaderContainer';
import ChatBoxContainer from '../../features/specific/ChatBox';
import VideoChatContainer from '../../features/specific/Videochat/VideoChatContainer';
import VideoChatActionSectionContainer
    from '../../features/specific/VideochatActionSection/VideoChatActionSectionContainer';
import { IViewProviderProps } from '../interfaces';
import {
    BodySection,
    VideoChatContainerAligner,
    VideoChatContainerWrapper,
    VideoChatPortraitViewWrapper
} from './style';
import MessageSectionPopupsContainer from '../../features/common/MessageSectionPopups';
import CountdownWarningContainer from '../../features/specific/CountdownWarning';
import MentionSectionContainer from '../../features/specific/MentionSection';

const VideoChatMobilePortraitView = (props: IViewProviderProps) => {
    const showChatBoxAbove: boolean = props.isKeyboard && isIOS;
	const bodySection = <>
		<BodySection>
			<ChatBoxContainer />
			<MessageSectionPopupsContainer />
			<MentionSectionContainer />
		</BodySection>

		<CountdownWarningContainer />
	</>;

    return (
        <VideoChatPortraitViewWrapper {...props}>
            <MobileHeaderContainer />

            {showChatBoxAbove && bodySection}

            <div>
                <VideoChatContainerAligner>

                    <VideoChatContainerWrapper {...props}>
                        <VideoChatContainer isMobilePortrait={true} />
                        <VideoChatActionSectionContainer />
                    </VideoChatContainerWrapper>

                </VideoChatContainerAligner>
            </div>

            {!showChatBoxAbove && bodySection}

            <MobileFooter />
        </VideoChatPortraitViewWrapper>
    );
};

export default VideoChatMobilePortraitView;
