import styled from "styled-components";
import withIdentifier, { DomIdentifier } from "../../hoc/withIdentifier";

interface IEmoticonStyleProps {
    key: string;
    title: string;
    position?: string;
    className?: string;
    onClick: any;
}

export const EmoticonWrapper = withIdentifier(styled.div.attrs({className: 'EmoticonWrapper'})`
    width: auto;
    text-align: left;
    padding-bottom: 5px;
`, DomIdentifier.EmoticonWrapper);

export const EmoticonStyle = styled.div<IEmoticonStyleProps>`
    ${(props: IEmoticonStyleProps) => props.position
        ? `
            ${props.position};
            width: 32px;
            height: 32px;
        `
        : `
            width: auto;
            height: auto;
            background: none;
            font-size: 25px;
        `
    };

    margin: 2px 3px;
    display: inline-block;
    cursor: pointer;
    background-size: 1000%;

    &:hover {
        filter: drop-shadow(0 0 3px ProgressBarKnob);
    }
`;
