import * as React from 'react';

import ToySectionHeader from './ToySectionHeader';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import IStore, { ITranslation } from '../../../redux/store';
import { toggleToySection } from '../../../redux/modules/messenger';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    isResponsive: boolean;
    isToySectionOpen: boolean;
    translations: ITranslation;

    toggleToySection: ActionDispatcher;
}

interface IComponentProps {
	isToySectionMinimizied: boolean;
}

type IProps = IStoreProps & IInstanceProps & IComponentProps;

const onToyHeaderClick = (props: IProps) => () => {
    props.toggleToySection(props.instanceId);
};

const ToySectionHeaderContainer = (props: IProps) => {
    const { toyControl, toyLearnMore } = props.translations.chat.toyControl;

    return (
        <ToySectionHeader
            title={toyControl}
            link={toyLearnMore}
            isOpen={props.isToySectionOpen}
            isResponsive={props.isResponsive}
            isToySectionMinimizied={props.isToySectionMinimizied}

            onClick={onToyHeaderClick(props)}
        />
    );
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isResponsive: selectors.isResponsive(store, instanceId),
    isToySectionOpen: selectors.isToySectionOpen(store, instanceId),
    translations: selectors.getTranslation(store, instanceId)
});

const mapDispatchToProps = {
    toggleToySection
};

export default withRedux(ToySectionHeaderContainer, mapStateToProps, mapDispatchToProps);
