import React, { Component } from 'react';

import AudioMessageUploader from './AudioMessageUploader';
import IStore, { IMessage } from '../../../redux/store';
import selectors from '../../../redux/selectors';
import {
    addMessage,
    setIsRecordingAudioMessage,
    setSecondsElapsed,
    setShouldSendMessage
} from "../../../redux/modules/messenger";
import { getMinutes, getSeconds } from './utils'
import {ActionDispatcher} from "../../../redux";
import {IInstanceProps} from "../../hoc/withInstanceInfo";
import withRedux from "../../hoc/withRedux";

interface IState {
    secondsElapsed: number;
}

interface IStoreProps {
    // Properties
    isRecording: boolean;
    shouldSendMessage: boolean;
    isMicEnabled: boolean;
    secondsElapsed: number;


    addMessage: ActionDispatcher<IMessage>;
    setIsRecordingAudioMessage: ActionDispatcher<boolean>;
    setIsMicEnabled: ActionDispatcher<boolean>;
    setSecondsElapsed: ActionDispatcher<number>;
    setShouldSendMessage: ActionDispatcher<boolean>;
}

type IProps = IStoreProps & IInstanceProps;

class AudioMessageUploaderContainer extends Component<IProps, IState> {
    private countdown: any;

    constructor(props: IProps) {
        super(props);

        this.state = {
            secondsElapsed: 0
        }
    }

    componentDidMount() {
        if (this.props.isRecording && this.props.isMicEnabled) {
            this.countdown = setInterval(() => {
                this.incrementSecondElapsed();
            }, 1000);
        }
    }

    componentDidUpdate(prevProps: IProps) {
        if (this.props.secondsElapsed !== prevProps.secondsElapsed) {
            this.setState((oldState: IState) => ({
                secondsElapsed: this.props.secondsElapsed + 1
            }));
        }

        if (this.props.isMicEnabled && !prevProps.isMicEnabled) {
            this.countdown = setInterval(() => {
                this.incrementSecondElapsed();
            }, 1000);
        }

        if (this.props.isRecording && !prevProps.isRecording && this.props.isMicEnabled) {
            this.setState((oldState: IState) => ({
                secondsElapsed: this.props.secondsElapsed
            }));

            this.countdown = setInterval(() => {
                this.incrementSecondElapsed();
            }, 1000);
        }

        if (!this.props.isRecording && prevProps.isRecording && this.props.isMicEnabled) {
            clearInterval(this.countdown);

            this.resetSecondsElapsed();
        }
    }

    componentWillUnmount(): void {
        this.props.setSecondsElapsed(this.props.instanceId, this.state.secondsElapsed);
    }

    render() {
        return (
            <AudioMessageUploader
                isRecording={this.props.isRecording}
                shouldSendMessage={this.props.shouldSendMessage}
                stopRecording={this.stopRecording}
                sendAudioMessage={this.sendAudioMessage}
                recordingSeconds={getSeconds(this.state.secondsElapsed)}
                recordingMinutes={getMinutes(this.state.secondsElapsed)}
            />
        );
    }

    private incrementSecondElapsed = () => {
        this.setState((oldState: IState) => ({
            secondsElapsed: oldState.secondsElapsed + 1
        }));
    };

    private stopRecording = () => {
        const { instanceId } = this.props;

        this.props.setIsRecordingAudioMessage(instanceId, false);
        this.props.setShouldSendMessage(instanceId, false);
        this.resetSecondsElapsed();
    };

    private sendAudioMessage = () => {
        const { instanceId } = this.props;

        this.props.setIsRecordingAudioMessage(instanceId, false);
        this.props.setShouldSendMessage(instanceId, true);
        this.resetSecondsElapsed();
    };

    private resetSecondsElapsed = () => {
        const { instanceId } = this.props;

        this.props.setSecondsElapsed(instanceId, 0);
        this.setState((oldState: IState) => ({
            secondsElapsed: 0
        }));
    }
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isRecording: selectors.isRecording(store, instanceId),
    secondsElapsed: selectors.getSecondsElapsed(store, instanceId),
    shouldSendMessage: selectors.getShouldSendMessage(store, instanceId),
    isMicEnabled: selectors.isMicEnabled(store, instanceId)
});

const mapDispatchToProps = {
    addMessage,
    setIsRecordingAudioMessage,
    setSecondsElapsed,
    setShouldSendMessage
};

export default withRedux(AudioMessageUploaderContainer, mapStateToProps, mapDispatchToProps);
