import * as React from 'react';

import { IProps } from "./types";
import { onSelectCamera } from "./utils";
import IStore from "../../../redux/store";
import CamerasSection from "./CamerasSection";
import selectors from "../../../redux/selectors";
import withRedux from '../../hoc/withRedux';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import { setCam2CamSelectedCamera } from "../../../redux/modules/videochat";

const CamerasSectionContainer = (props: IProps) => (
    <CamerasSection
        cameras={props.cameras}
        selectedCamera={props.selectedCamera}
        onSelectCamera={onSelectCamera(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    cameras: selectors.getCam2CamCameras(store, instanceId),
    selectedCamera: selectors.getCam2CamSelectedCamera(store, instanceId)
});

const mapDispatchToProps = {
    setCam2CamSelectedCamera
};

export default withRedux(CamerasSectionContainer, mapStateToProps, mapDispatchToProps);
