import * as React from 'react';
import CenterChild from "../../common/CenterChild";
import {
    ConfiguredPreloader,
    DefaultPreloader,
    PreloaderSubTitle,
    PreloaderTextWrapper,
    PreloaderTitle
} from "./style";

interface IProps {
    preloader?: string;
    title: string;
    subtitle: string;
	hasPreloaderPaymentText?: boolean;
	shouldAdjustVertically?: boolean;
}

const Preloader = (props: IProps) => (
    <>
        {props.hasPreloaderPaymentText &&
        <PreloaderTextWrapper shouldAdjustVertically={props.shouldAdjustVertically}>
            <PreloaderTitle>{props.title}</PreloaderTitle>
            <PreloaderSubTitle>{props.subtitle}</PreloaderSubTitle>
        </PreloaderTextWrapper>
        }

        <CenterChild>
            {props.preloader
                ? <ConfiguredPreloader preloader={props.preloader} />
                : <DefaultPreloader />
            }
        </CenterChild>
    </>
);

export default Preloader;
