import { Action } from 'redux-actions';
import { findLast, isEmpty } from 'lodash';
import { put, select, takeEvery, call } from 'redux-saga/effects';
import {
	ACTION,
	CMDC_CHANNELINFO,
	CMDP_GETCHANNELINFO,
	CMDP_GETCHANNELINFO_RESPONSE,
	CMDP_SETCHANNELINFO,
	CMDP_SETCHANNELINFO_RESPONSE,
	ICOMMAND,
	SupportedLanguage,
	IChannelInfoBackEndPayload
} from 'vxcontrol-client-lib';

import utils from '../../utils';
import selectors from '../../redux/selectors';
import ApiService from '../../services/ApiService';
import { IContactNote } from '../../redux/store';
import { ActionIds } from '../../redux/interfaces';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import { closeContactNote, resetContactNote, setContactNote } from '../../redux/modules/messenger';
import {
	resetModelName,
	resetMotto,
	resetProfilePicture,
	setFavorite,
	setFreeModelMessages,
	setModelFreeChatDuration,
	setModelName,
	setProfilePicture,
	setModelPrices,
	setMotto
} from '../../redux/modules/model';

const createChannelInfoActionType = createActionType(ActionIds.CHANNEL_INFO, { isSagaAction: true });

// Actions
const REQUEST_GET_CHANNEL_INFO = createChannelInfoActionType("REQUEST_GET_CHANNEL_INFO");
export const RECEIVE_GET_CHANNEL_INFO = createChannelInfoActionType("RECEIVE_GET_CHANNEL_INFO");
const REQUEST_SET_CONTACT_NOTE = createChannelInfoActionType("REQUEST_SET_CONTACT_NOTE");
const REQUEST_SET_FAVORITE = createChannelInfoActionType("REQUEST_SET_FAVORITE");
const RECEIVE_SET_CHANNEL_INFO = createChannelInfoActionType("RECEIVE_SET_CHANNEL_INFO");

// Action Creators
export const requestGetChannelInfo = createAction(REQUEST_GET_CHANNEL_INFO);
export const requestSetContactNote = createAction<string>(REQUEST_SET_CONTACT_NOTE);
export const requestSetFavorite = createAction<boolean>(REQUEST_SET_FAVORITE);
export const onReceiveGetChannelInfo = createAction<ICOMMAND>(RECEIVE_GET_CHANNEL_INFO);
export const onReceiveSetChannelInfo = createAction<ICOMMAND>(RECEIVE_SET_CHANNEL_INFO);

/** Set contact note and model's favorite in the Redux store */
const setFavoriteAndContactNoteSaga = function*(instanceId: string, note: string, regularCustomer: boolean) {
    yield put(note ? setContactNote(instanceId, note) : resetContactNote(instanceId));
	yield put(setFavorite(instanceId, regularCustomer));
}

/** Sagas for Get Channel Info */
const requestGetChannelInfoSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const channelId = yield select(selectors.getChannelId, instanceId);

    const command: CMDP_GETCHANNELINFO = {
        action: ACTION.CMDP_GETCHANNELINFO,
        params: { channelId }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

const requestSetChannelInfoSaga = function* ({ payload, type }: Action<IPayload>) {
    const { instanceId, value } = payload;

    const channelId: string = yield select(selectors.getChannelId, instanceId);
    const sessionID: string = yield select(selectors.getSessionId, instanceId);
    const contactNote: IContactNote = yield select(selectors.getContactNote, instanceId);
    const isModelFavorite: boolean = yield select(selectors.isModelFavorite, instanceId);

    // Set contact note and model's favorite status params for our request
    const note: string = type === REQUEST_SET_CONTACT_NOTE ? value : contactNote.text;
    const isFavorite: boolean = type === REQUEST_SET_FAVORITE ? value : isModelFavorite;
    const regularCustomer = isFavorite ? "true" : "false";

    const command: CMDP_SETCHANNELINFO = {
        action: ACTION.CMDP_SETCHANNELINFO,
        params: {
            channelId,
            sessionID,
            note,
            regularCustomer
        }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

const receiveSetChannelInfoSaga = function* ({ payload }: Action<IPayload<CMDP_SETCHANNELINFO_RESPONSE>>) {
    const { instanceId, value } = payload;

    if (isEmpty(value) || isEmpty(value.params)) {
        return;
    }

    const { note, regularCustomer } = value.params;
    yield call(setFavoriteAndContactNoteSaga, instanceId, note || '', regularCustomer === 'true');

    /** Close contact note dialog before backend's response for faster feedback */
    const contactNote: IContactNote = yield select(selectors.getContactNote, instanceId);
    if (contactNote.isOpen) {
        yield put(closeContactNote(instanceId));
    }
};

const receiveGetChannelInfoSaga = function* ({ payload }: Action<IPayload<CMDP_GETCHANNELINFO_RESPONSE>>) {
    const { instanceId, value } = payload;

    if (isEmpty(value) || isEmpty(value.payload)) {
        return;
	}

	const data = value.payload as IChannelInfoBackEndPayload;

    // Set client's free messages and videochat time for this specific model
    const { freeChatDuration, freeMessagesCount } = data;
    yield put(setFreeModelMessages(instanceId, freeMessagesCount));
    yield put(setModelFreeChatDuration(instanceId, freeChatDuration));

    // Set model's prices for all the not-free features the client will have in the messenger
    if (data) {
        yield put(setModelPrices(instanceId, {
            messagePrice: data.messagePrice || 0,
            videoChatPrice: data.videoChatPrice || 0,
            voyeurChatPrice: data.voyeurChatPrice || 0
        }));
    }

    const modelInfo = findLast(value.commands,
        (command: ICOMMAND) => command.action === ACTION.CMDC_CHANNELINFO
    ) as CMDC_CHANNELINFO;

    if (isEmpty(modelInfo) || isEmpty(modelInfo.params)) {
        return;
    }

    const { ualias, imgSrc, regularCustomer, note } = modelInfo.params;
    const modelInfoPayload = modelInfo.params.payload;

	yield call(setFavoriteAndContactNoteSaga, instanceId, note || '', regularCustomer === 'true');

    yield put(ualias ? setModelName(instanceId, ualias) : resetModelName(instanceId));
    yield put(imgSrc ? setProfilePicture(instanceId, imgSrc) : resetProfilePicture(instanceId));

    /**
     * Set model's motto if we receive an undeleted one.
     * Reset motto to its initial value if there comes
     * no payload in the response or it has been deleted.
     */
    if (modelInfoPayload) {
        try {
            const mottoPayload = JSON.parse(modelInfoPayload as string);
            if (isEmpty(mottoPayload) || isEmpty(mottoPayload.motto)) {
                yield put(resetMotto(instanceId));
                return;
            }

            const { en, de, es, deleted } = mottoPayload.motto;

            if (deleted) {
                yield put(resetMotto(instanceId));
            } else {
                const language: SupportedLanguage = yield select(selectors.getLanguage, instanceId);
                const motto = utils.getValueByLanguage(language, {
					EN: en,
					DE: de,
					ES: es
				});

                yield put(setMotto(instanceId, motto));
            }
        } catch {
            yield put(resetMotto(instanceId));
        }

    } else {
        yield put(resetMotto(instanceId));
    }
};

/** Mapping sagas to actions */
const watchAll = function* () {
    yield takeEvery(REQUEST_GET_CHANNEL_INFO, requestGetChannelInfoSaga);
    yield takeEvery(RECEIVE_GET_CHANNEL_INFO, receiveGetChannelInfoSaga);

    yield takeEvery(REQUEST_SET_FAVORITE, requestSetChannelInfoSaga);
    yield takeEvery(REQUEST_SET_CONTACT_NOTE, requestSetChannelInfoSaga);
    yield takeEvery(RECEIVE_SET_CHANNEL_INFO, receiveSetChannelInfoSaga);
};

export default watchAll;

