// tslint:disable:no-console
import * as moment from 'moment';

enum LEVEL {
    TRACE = 'TRACE',
    DEBUG = 'DEBUG',
    WARNING = 'WARNING',
    ERROR = 'ERROR',
    NONE = 'NONE'
}

interface ILogger {
    error: (text: string, details?: any) => void;
    warning: (text: string, details?: any) => void;
    debug: (text: string, details?: any) => void;
    trace: (text: string, details?: any) => void;
}

const environmentLevel = process.env.LOG_LEVEL as LEVEL || LEVEL.NONE;

const logEntry = (loggerName: string, text: string, details?: any): string =>
    `[${loggerName}][${moment().format('HH:mm:ss.SSS')}] ${text} ${details ? `
${JSON.stringify(details)}` : ''}`;


const canLogTrace = (level: LEVEL): boolean => level === LEVEL.TRACE;

const canLogDebug = (level: LEVEL): boolean =>
    level === LEVEL.DEBUG || canLogTrace(level);

const canLogWarning = (level: LEVEL): boolean =>
    level === LEVEL.WARNING || canLogDebug(level);

const canLogError = (level: LEVEL): boolean =>
    level === LEVEL.ERROR || canLogWarning(level);

const getLogger = (level: LEVEL, name: string): ILogger => ({
    error: (text: string, details?: any) => {
        if (canLogError(level)) {
            console.log(logEntry(name, text, details));
        }
    },

    warning: (text: string, details?: any) => {
        if (canLogWarning(level)) {
            console.log(logEntry(name, text, details));
        }
    },

    debug: (text: string, details?: any) => {
        if (canLogDebug(level)) {
            console.log(logEntry(name, text, details));
        }
    },

    trace: (text: string, details?: any) => {
        if (canLogTrace(level)) {
            console.log(logEntry(name, text, details));
        }
    }
});

/** Exported loggers */

export const chat = getLogger(environmentLevel, 'CHAT');
export const player = getLogger(environmentLevel, 'PLAYER');
export const saga = getLogger(environmentLevel, 'SAGA');
export const component = getLogger(environmentLevel, 'COMPONENT');



