import styled, { css, keyframes } from "styled-components";

interface IWrapperProps {
    isRecording: boolean;
}

const activePulsation = keyframes`
    0% { opacity: 0.1; }
    100% { opacity: 1; }
`;

const animation = css`
	animation: ${activePulsation} 1s infinite ease-in-out;
`;

export const Wrapper = styled.div<IWrapperProps>`
    ${({ isRecording }: IWrapperProps) => !isRecording && `
        transition-property: display;
        transition-delay: 250ms;
    `}
    left: ${({ isRecording }: IWrapperProps) => isRecording ? '0' : '100%'};
    opacity: ${({ isRecording }: IWrapperProps) => isRecording ? '1' : '0'};
    
    display: flex;
    transition: all 0.5s;
    
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 3;
    bottom: 0;
    background-color: white;
    flex-direction: column-reverse;
    
`;

export const BaseIcon = styled.i`
    position: relative;
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 20px;
    z-index: 0;
    transition: transform .5s ease-in-out;
    
    width: 50px;
    align-content: center;
    justify-content: center;
`;

export const RecordingIcon = styled(BaseIcon)`
    color: red;
    width: 20px;
    margin-left: 15px;
    margin-right: 5px;
    ${animation};
`;

export const SendIcon = styled(BaseIcon)`
    color: ${(props) => props.theme.colors.sendMessageButton};
    cursor: pointer;
`;

export const AudioAnalyserContent = styled.div`
    display: flex;
    height: 100%;
    justify-content: space-between;
`;

export const RecordingAreaContent = styled.div`
    display: flex;
    align-items: center;
`;

export const CountDownWrapper = styled.div`
    width: 100px;
`;

export const CancelRecordingWrapper = styled.div`
    cursor: pointer;
    
    &:hover {
        color: ${(props) => props.theme.colors.cancelRecordingHover};
    }
`;
