import * as React from 'react';
import { toLower} from 'lodash';
import { DomIdentifier } from "./types";

const hasDomIdentifiers: boolean = toLower(process.env.HAS_DOM_IDENTIFIERS) === "true";

const withIdentifier = <P extends {}>(WrappedComponent, id: DomIdentifier) => {

    return class extends React.Component<P | P & React.HTMLAttributes<HTMLElement>> {
        constructor(props) {
            super(props)
        }

        render() {
            return(hasDomIdentifiers
                    ? <WrappedComponent  {...this.props} id={id} />
                    : <WrappedComponent {...this.props} />
            );
        }
    };
};

export default withIdentifier;