import * as cuid from 'cuid';
import request from 'superagent';
import * as moment from 'moment';
import { Action } from 'redux-actions';
import { call, put, select, takeEvery } from "redux-saga/effects";
import { find, includes, isEmpty, isUndefined, some, upperFirst } from 'lodash';
import { ACTION, CMDC_CMSG, CMDP_MSG, SystemMessageKey, EnumMessageType, EnumMessageDirection, SupportedLanguage } from 'vxcontrol-client-lib';

import loggers from "../../loggers";
import { ActionIds } from '../../redux/interfaces';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import ApiService from '../../services/ApiService';
import ChatService from '../../services/ChatService';
import {IOnMessagePayload} from '../../services/types';
import {switchVoyeur2LiveChat} from "../common/videochat";
import {
    activateToyOption,
    addMessage,
    clearFailedMessages,
    closeSections,
    inactivateToyMessage,
    inactivateToyOption,
    removeMessage,
    setActionResponse,
    updateImageMessageWhenLoaded
} from '../../redux/modules/messenger';
import {
    ActionResponseType,
    ICurrency,
    IGift,
    IMessage,
    ISession,
    IToyOption,
    ITranslation,
    MessageContentType,
    MessageDirection,
	MsgType
} from '../../redux/store';
import types from './types';
import utils from '../../utils';

interface IReceiveToyMessagePayload {
    option: IToyOption;
    message: IMessage;
}

const createMessageActionType = createActionType(ActionIds.MESSAGE, { isSagaAction: true });

// Actions
const REQUEST_SEND_MESSAGE = createMessageActionType("REQUEST_SEND_MESSAGE");
const REQUEST_SEND_VIBRATION_MESSAGE = createMessageActionType("REQUEST_SEND_VIBRATION_MESSAGE");
const RECEIVE_TOY_MESSAGE = createMessageActionType("RECEIVE_TOY_MESSAGE");
const REQUEST_SEND_AUDIO_MESSAGE = createMessageActionType("REQUEST_SEND_AUDIO_MESSAGE");

const RECEIVE_SEND_MESSAGE = createMessageActionType("RECEIVE_SEND_MESSAGE");
const RECEIVE_MESSAGE_FROM_NOOP = createMessageActionType("RECEIVE_MESSAGE_FROM_NOOP");
const RECEIVE_MESSAGE_FROM_CHAT = createMessageActionType("RECEIVE_MESSAGE_FROM_CHAT");
const RESEND_FAILED_MESSAGES = createMessageActionType("RESEND_FAILED_MESSAGES");

// Action Creators
export const requestSendMessage = createAction<string | IToyOption>(REQUEST_SEND_MESSAGE);
export const requestSendVibrationMessage = createAction<IToyOption>(REQUEST_SEND_VIBRATION_MESSAGE);
export const requestSendAudioMessage = createAction<Blob>(REQUEST_SEND_AUDIO_MESSAGE);
export const receiveMessageFromChat = createAction<IOnMessagePayload>(RECEIVE_MESSAGE_FROM_CHAT);
export const receiveMessageFromNoop = createAction<CMDC_CMSG>(RECEIVE_MESSAGE_FROM_NOOP);
export const receiveToyMessage = createAction<IReceiveToyMessagePayload>(RECEIVE_TOY_MESSAGE);
export const receiveSendMessage = createAction<CMDC_CMSG>(RECEIVE_SEND_MESSAGE);
export const resendFailedMessages = createAction(RESEND_FAILED_MESSAGES);

// Sagas
/** Sends through websocket the data for sending a new message on a specific channel */
const requestSendMessageSaga = function* ({ payload, type }: Action<IPayload<string | IToyOption | Blob>>) {
    const { instanceId, value } = payload;

    const isTextEnabled: boolean = yield select(selectors.isTextEnabled, instanceId);
    const gifts: IGift[] = yield select(selectors.getGifts, instanceId);
    const session: ISession = yield select(selectors.getSession, instanceId);

    const { channelId, sessionID, chat } = session;
    const useChat = isTextEnabled && !isEmpty(chat);

    const isVibrationMessage = type === REQUEST_SEND_VIBRATION_MESSAGE;
    const isAudioMessage = type === REQUEST_SEND_AUDIO_MESSAGE;
    const messageKey = cuid.slug();

    let messageText: string = "";
    let giftIdsFromMessage: number[] = [];
    let audioFile: any;

    if (isVibrationMessage) {
        const toyContent = value as IToyOption;

        /** The vibration will have the same codification with a gift sent through a chat session */
        messageText = utils.encodeForChatCommunication(toyContent);

        /** The vibration will be sent a sole gift to the model */
        giftIdsFromMessage = [toyContent.id];
    } else if (isAudioMessage) {
        const audioBlob: Blob = value as Blob;

        audioFile = new File([audioBlob],
            `audio_message_${messageKey}.mp3`,
            { type: "audio/mp3" });
    } else {
        const textContent = value as string;

        messageText = utils.getTextMessageToSend(gifts, textContent, useChat);
        giftIdsFromMessage = utils.getGiftsFromMessage(gifts, textContent);
    }

    /** It returns an array of gift ids from a message */
    const hasGifts = !isEmpty(giftIdsFromMessage) && isTextEnabled;

    /**
     * We will send the message through the vxcontrol-client-lib api
     * if we are in the minified messenger or message has gifts
     */
    const shouldSendThroughVXControl = !useChat || hasGifts;

	/**
	 * Since there is not implementation in backend of switching to livechat
	 * (for messages send through vx-control) if we send a gift in voyeur
	 * we are forcing it here
	 */
	const isVideoChatInVoyeurMode: boolean = yield select(selectors.isVideoChatInVoyeurMode, instanceId);

	if (!isEmpty(chat) && isVideoChatInVoyeurMode) {
		yield put(switchVoyeur2LiveChat(instanceId));
	}

    if (shouldSendThroughVXControl) {
        if (isAudioMessage) {
            const uploadMediaUrl = yield select(selectors.getUploadMediaUrl, instanceId);
            const webtoken = yield select(selectors.getWebtoken, instanceId);

            const queryStringParams = {
                action: ACTION.CMDP_SUPLOADMEDIA,
                messageKey: encodeURIComponent(messageKey),
                format: 'json',
                strip: 'true',
                sessionID,
                webtoken,
                channelId
            };

            const url = `${uploadMediaUrl}${utils.queryString(queryStringParams)}`;

            const formData = new FormData();
            formData.append(audioFile.name, audioFile, audioFile.name);

            yield call(async () => {
                await request.post(url).send(formData);
            });

            return;
        }

        // Create the standard command for sending a message
        const command: CMDP_MSG = {
            action: ACTION.CMDP_MSG,
            params: {
                sessionID,
                channelId,
                messageKey,
                text: messageText
            }
        };

        if (hasGifts) {
            command.params.gift = giftIdsFromMessage.join(",");

            if (chat) {
                command.params.chatID = chat.chatId;
            }
        }

        ApiService.getService(instanceId).sendCommand(command);
    }

    if (!isEmpty(chat) && !isEmpty(messageText) && !hasGifts) {
        const response = yield call(ChatService.getService(instanceId).sendMessage, messageText);

	    const message: IMessage = {
		    id: cuid.slug(),
		    messageKey: cuid.slug(),
		    dateCreated: moment().toString(),
		    content: messageText,
		    direction: MessageDirection.out,
		    isSystem: false,
		    isNew: true,
	    };

	    if (chat) {
		    message.chatId = chat.chatId;
	    }

	    // We are adding the user message if there was no error after sendMessage
        if (!response.isError) {
	        yield put(addMessage(instanceId, message));
	    }
    } else if (!isEmpty(chat) && isAudioMessage) {
        yield call(ChatService.getService(instanceId).sendMediaFile, audioFile, messageKey);
    }

    /**
     * After sending the message, it does not matter the communication channel,
     * we should close the gift section and emoticon section. It does
     * not matter if it's successful or not our send message request.
     */
    yield put(closeSections(instanceId));
};

export const receiveMessageFromChatSaga = function* ({ payload }: Action<IPayload<IOnMessagePayload>>) {
    const { instanceId, value: messagePayload } = payload;
	const { contentType } = messagePayload;

    if (isEmpty(messagePayload)) {
        return;
	}

    /**
     * Image message behavior
     */
    if (!isUndefined(messagePayload.imgSrc) && !isUndefined(messagePayload.key)) {
        yield put(updateImageMessageWhenLoaded(instanceId, {
	        messageKey: messagePayload.key,
            imgSrc: messagePayload.imgSrc,
            imgSrcSet: messagePayload.imgSrcSet,
            imgWidth: messagePayload.imgWidth,
            imgHeight: messagePayload.imgHeight
        }));

        return;
    }

    const { text, direction, username, msgType } = messagePayload;
    const chat = yield select(selectors.getChat, instanceId);

    if (direction === MessageDirection.out) {
        return;
    }

    const isGroupChatMode = yield select(selectors.isVideoChatInGroupMode, instanceId);
    const groupChatColors = yield select(selectors.getGroupChatColors, instanceId);
    const shouldHaveUsernameColor = isGroupChatMode && !isEmpty(username);

    const message: IMessage = {
        id: cuid.slug(),
        messageKey: cuid.slug(),
        dateCreated: moment().toString(),
        content: text || "",
        direction: direction || MessageDirection.in,
		isSystem: msgType === MsgType.SYSTEM,
		msgType,
        isNew: true,
        username,
        usernameColor: shouldHaveUsernameColor
            ? utils.getColorForUsername(groupChatColors, username)
            : undefined
    };

    if (chat) {
        message.chatId = chat.chatId;
	}

    // audio messages
    if (!isUndefined(contentType) && contentType === MessageContentType.AUDIO) {
		message.contentType = MessageContentType.AUDIO;
		message.direction = MessageDirection.out;
		message.content = {
			audioMp3: messagePayload.audioSrc
		}
    }

    loggers.saga.debug('Received message from chat:', { details: messagePayload });

    yield put(addMessage(instanceId, message));
};

/**
 * Receives the response after sending a message and,
 * if it is successful, the message is saved in the Redux store.
 */
export const receiveSendMessageSaga = function* ({ payload }: Action<IPayload<CMDC_CMSG>>) {
    const { instanceId, value: response } = payload;

    if (isEmpty(response) || isEmpty(response.params)) {
        return;
    }

    const chat = yield select(selectors.getChat, instanceId);
    const session: ISession = yield select(selectors.getSession, instanceId);
    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);
    const isChatExternallyProvided: boolean = yield select(selectors.isChatExternallyProvided, instanceId);
    const isAudio = !isEmpty(response.params.mediaType) && response.params.mediaType === "Audio";

    /** Defensive programming -
     * if the message is not for our current channel, do nothing
     * if chat isChatExternallyProvided let onMessage handle message addition
     */
    if ((session.channelId !== response.params.channelId) || isChatExternallyProvided) {
        return;
    }

	const { messageId, direction, messageKey, msgType, text, isDeleted } = response.params;

    const partialMessage: IMessage = {
        id: messageId || cuid.slug(),
		messageKey: messageKey || cuid.slug(),
		dateCreated: moment().toString(),
		direction: direction  === EnumMessageDirection.IN
			? MessageDirection.in
			: MessageDirection.out,
        isSystem: msgType === EnumMessageType.SYSTEM,
        isDeleted: isDeleted === "1",
        chatId: undefined,
        isNew: true,
        content: null
	};

	if (msgType) {
		partialMessage.msgType = types.messageTypes[msgType];
	}
    if (chat) {
        partialMessage.chatId = chat.chatId;
    }

    // We will handle different a received toy message
    const toyOptions = yield select(selectors.getToyOptions, instanceId);
    const toyOption = find(toyOptions, option => includes(text, option.name));

    if (toyOption) {
        yield put(receiveToyMessage(instanceId, {
            option:toyOption,
            message: partialMessage
        }));

        return;
    }

    /** If the message is an image */
    if (!isUndefined(response.params.imgSrc)) {

        /** If the client is the one sending the image just stop the loading animation and update message's id */
        if (response.params.direction === EnumMessageDirection.OUT) {
            const messages: IMessage = yield select(selectors.getMessages, instanceId);
            const imageMessage = find(messages,
                (message: IMessage) => message.messageKey === partialMessage.messageKey
            );

            if (imageMessage) {
                yield put(updateImageMessageWhenLoaded(instanceId, {
                    messageKey: partialMessage.messageKey,
                    imgSrc: response.params.imgSrc,
                    imgSrcSet: response.params.imgSrcSet,
                    imgWidth: response.params.imgWidth,
                    imgHeight: response.params.imgHeight
                }));

                /** Add the successful image upload message in the Redux store */
                yield put(setActionResponse(instanceId, {
                    text: translation.common.imageUploadOk,
                    type: ActionResponseType.SUCCESSFUL
                }));
            }
        } else {
            /** If the model is the one sending the image, add the message in the Redux store */
            yield put(addMessage(instanceId, {
                ...partialMessage,
                content: response.params.imgSrc,
                modalContent: {
                    imgSrc: response.params.imgSrc,
                    imgSrcSet: response.params.imgSrcSet,
                    imgWidth: response.params.imgWidth,
					imgHeight: response.params.imgHeight
                },
                contentType: MessageContentType.IMAGE,
                isNew: true
            }));
        }
    } else if (isAudio) {
        // If we received an audio message
        if (response.params.audioMp3) {
            yield put(addMessage(instanceId,{
                ...partialMessage,
                content: {
                    audioM4a: response.params.audioM4a,
                    audioMp3: response.params.audioMp3,
                    audioOgg: response.params.audioOgg
                },
                contentType: MessageContentType.AUDIO,
                isNew: true
            }));
        }
    } else {
        // If we received a text message, encode gifts in our format and add it in the Redux store.
        const gifts = yield select(selectors.getGifts, instanceId);

        const content = utils.getReceivedTextMessage(
            gifts,
            text || "",
            utils.isMessageFromChat(response.params)
        );

        yield put(addMessage(instanceId, {
            ...partialMessage,
            content,
            contentType: MessageContentType.TEXT
        }));
    }
};

/**
 * We should call the received message saga if we received a new message.
 * NOOP may contain an already existing message when any action is executed on that message:
 * For instance, when the model reads the message => it comes again on the NOOP.
 */
const receiveMessageFromNoopSaga = function* ({ payload }: Action<IPayload<CMDC_CMSG>>) {
	const { instanceId, value: receivedMessageCommand } = payload;
	
    if (isEmpty(receivedMessageCommand) || isEmpty(receivedMessageCommand.params)) {
        return;
	}
	
    const { msgType, messageKey, gift } = receivedMessageCommand.params;
	const hasGifts = !isEmpty(gift);

    /**
     * We ignore all the messages from vchat-core instance from the model.
     * (There are handled by the onMessage function from the vchat-core
     * instance and when these communication channels will be unified, we
     * will change this)
     * We also ignore all messages from client except the ones which contain gifts
     * (those are being send through vxcontrol and handled by NOOP command), handling mechanism
     * for user messages is in requestSendMessageSaga in message.ts
     */
    if (msgType === EnumMessageType.CHAT && !hasGifts) {
        return;
    }

    /** Always add the system messages */
    if (msgType === EnumMessageType.SYSTEM) {
        const isChatActive = yield select(selectors.getChat,instanceId);

        /** When the toy option has finished its active duration, we receive a special message */
        if (messageKey === SystemMessageKey.h_session_toy_stop) {
            const translation: ITranslation = yield select(selectors.getTranslation, instanceId);
            const { lovenseFinished } = translation.chat.toyControl;

            yield put(inactivateToyOption(instanceId));
            yield put(inactivateToyMessage(instanceId, lovenseFinished));

            const chatId = yield select(selectors.getChatId, instanceId);

            yield put(addMessage(instanceId, {
                id: cuid.slug(),
                messageKey: cuid.slug(),
				dateCreated: moment().toString(),
				msgType: MsgType.SYSTEM,
                chatId,
                content: lovenseFinished,
                direction: MessageDirection.none,
                isSystem: true,
            }));
        }

        /** If chat is active we let receiveMessageFromChatSaga to handle system message */
        if (!isChatActive) {
            yield put(receiveSendMessage(instanceId, receivedMessageCommand));
        }
    } else {
        const messages: IMessage[] = yield select(selectors.getMessages, instanceId);

        /** Non-system messages should be added only if they are new to the customer */
        const isNew = !some(messages,
            (message: IMessage) => message.id === receivedMessageCommand.params.messageId
        );

        if (isNew) {
            yield put(receiveSendMessage(instanceId, receivedMessageCommand));
        }
    }
};

const receiveToyMessageSaga = function*({ payload }: Action<IPayload<IReceiveToyMessagePayload>>) {
	const { instanceId, value } = payload;
    const { option, message } = value;

    if (isEmpty(value)) {
        return;
    }

    const language: SupportedLanguage = yield select(selectors.getLanguage, instanceId);
    const chatId = yield select(selectors.getChatId, instanceId);
    const currency: ICurrency = yield select(selectors.getCurrency, instanceId);
    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);

    if (isEmpty(option)) {
        return;
    }

    // Firstly, we activate the option
    yield put(activateToyOption(instanceId, option.id));

    // Secondly, we remove the initial "Toy Available" system message, if it's the case
    const defaultToyMessage = yield select(selectors.findMessage(
        (mess: IMessage) => (mess.toy && mess.toy.isDefault) as boolean
    ), instanceId);

    if (!isEmpty(defaultToyMessage)) {
        yield put(removeMessage(instanceId, defaultToyMessage.messageKey));
    }

    // Thirdly, we insert the start toy option system message
    const { lovenseStartedMessage } = translation.chat.toyControl;
    const toyTranslation = utils.getItemTranslation(language, option.translations);

    yield put(addMessage(instanceId, {
        id: cuid.slug(),
		messageKey: cuid.slug(),
		msgType: MsgType.TOY,
        dateCreated: moment().toString(),
        direction: MessageDirection.none,
        isSystem: true,
        chatId,
        content: utils.translationWithValues(lovenseStartedMessage, {
            name: upperFirst(toyTranslation.value),
            price: option.price,
            currency: currency.symbol
        }),
    }));

    // Lastly, we add the active toy message
    const toyOptionActive: IToyOption | undefined = yield select(selectors.getActiveToyOption,instanceId);

    if (!toyOptionActive) {
        return;
    }

    const toyMessage = utils.convertToToyMessage(
        message,
        toyOptionActive,
        translation.chat.toyControl
    );

    yield put(addMessage(instanceId, toyMessage));
};

const resendFailedMessagesSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const failedMessages: string[] = yield select(selectors.getFailedMessages, instanceId);

    yield put(clearFailedMessages(instanceId));

    for(const message of failedMessages) {
        yield put(requestSendMessage(instanceId, message));
    }
};

const watchSagas = function* () {
    yield takeEvery(REQUEST_SEND_MESSAGE, requestSendMessageSaga);
    yield takeEvery(REQUEST_SEND_VIBRATION_MESSAGE, requestSendMessageSaga);
    yield takeEvery(REQUEST_SEND_AUDIO_MESSAGE, requestSendMessageSaga);

    yield takeEvery(RECEIVE_SEND_MESSAGE, receiveSendMessageSaga);
    yield takeEvery(RECEIVE_MESSAGE_FROM_NOOP, receiveMessageFromNoopSaga);
    yield takeEvery(RECEIVE_MESSAGE_FROM_CHAT, receiveMessageFromChatSaga);
    yield takeEvery(RECEIVE_TOY_MESSAGE, receiveToyMessageSaga);
    yield takeEvery(RESEND_FAILED_MESSAGES, resendFailedMessagesSaga);
};

export default watchSagas;
