import * as React from 'react';
import { find, isEmpty, upperFirst } from "lodash";
import { SupportedLanguage } from 'vxcontrol-client-lib';

import utils from "../../../utils";
import ToyOption from "./ToyOption";
import selectors from "../../../redux/selectors";
import { ActionDispatcher } from "../../../redux";
import { setHasTooltipOpen, ISetHasTooltipPayload } from "../../../redux/modules/messenger";
import ToyOptionWithTooltipContainer from "../../specific/ToyOptionWithTooltip";
import { IInstanceProps } from "../../hoc/withInstanceInfo";
import withRedux from "../../hoc/withRedux";
import Store, { ICurrency, IToyOption, ITranslation } from "../../../redux/store";

export interface IComponentProps {
    id: number;
    isAvailable?: boolean;
    limitingContainerRef?: any;
}

interface IStoreProps {
    currency: ICurrency;
    toyOptions: IToyOption[];
    language: SupportedLanguage;
    translation: ITranslation;
	isResponsive: boolean;

    setHasTooltipOpen: ActionDispatcher<ISetHasTooltipPayload>;
}

enum InitiallyLockedToyNames {
    DildocontrolIntensityHigh = "DildocontrolIntensityHigh",
    DildocontrolIntensityRandom = "DildocontrolIntensityRandom"
}

interface ITooltipTexts {
    title: string;
    text: string;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

class ToyOptionContainer extends React.Component<IProps> {
    render() {
        const { currency, language, translation } = this.props;
        const option: IToyOption = this.getOption();
        const toyTranslation = utils.getItemTranslation(language, option.translations);
        const name = upperFirst(toyTranslation.value);

        let durationText: string = "";
        const { toyDuration } = translation.chat.toyControl;

        if (option.duration) {
            durationText = utils.translationWithValues(toyDuration, { value: option.duration / 1000 });
        }

        const lockedTooltipTexts = this.getLockedTooltipTexts(option.name, translation);

        const props = {
            image: option.imageUrlWhite,
            hoverImage: option.imageUrl,
            name,
            currency: currency.symbol,
            price: option.price,
            durationText,
            isLocked: this.isOptionLocked(option),
            isPlaying: option.isPlaying,
            isAvailable: this.props.isAvailable,
            hasTooltipOpen: option.hasTooltipOpen,
	        isResponsive: this.props.isResponsive
        };

        const isOptionLockedAndHasTooltip = this.isOptionLocked(option) && props.hasTooltipOpen;

        return (isOptionLockedAndHasTooltip
                ? <ToyOptionWithTooltipContainer
                        {...props}
                        tooltipImage={option.imageUrl}
                        tooltipText={lockedTooltipTexts.text}
                        tooltipTitle={lockedTooltipTexts.title}
						limitingContainerRef={this.props.limitingContainerRef}
                />
                : <ToyOption {...props} />
        );
    }

    private getOption = (): IToyOption =>
        find(this.props.toyOptions, (option) => option.id === this.props.id) as IToyOption;

    private isOptionLocked = (option: IToyOption): boolean => !isEmpty(option.unlockedBy);

    private getLockedTooltipTexts = (name: InitiallyLockedToyNames | string, translation: ITranslation): ITooltipTexts => {
        const { toyControl: { highTooltipText, highTooltipTitle, randomTooltipText, randomTooltipTitle } } = translation.chat;
        const texts: { [key: string]: ITooltipTexts } = {};

        texts[InitiallyLockedToyNames.DildocontrolIntensityHigh] = {
            text: highTooltipText,
            title: highTooltipTitle
        };
        texts[InitiallyLockedToyNames.DildocontrolIntensityRandom] = {
            text: randomTooltipText,
            title: randomTooltipTitle
        };

        return texts[name] || { text: '', title: '' };
    };
};

const mapStateToProps = (store: Store, { instanceId }: IInstanceProps) => ({
    currency: selectors.getCurrency(store, instanceId),
    toyOptions: selectors.getToyOptions(store, instanceId),
    language: selectors.getLanguage(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
	isResponsive: selectors.isResponsive(store, instanceId)
});

const mapDispatchToProps = {
    setHasTooltipOpen
};

export default withRedux<IComponentProps>(ToyOptionContainer, mapStateToProps, mapDispatchToProps);
