import { IContainerProps as IProps } from "./types";

export const emoticonClick = (props: IProps) => (emoticonShortcut: string) => () => {
    props.setActiveEmoticon(props.instanceId, emoticonShortcut);
};

export const giftClick = (props: IProps) => (giftShortcut: string) => () => {
    props.setActiveGift(props.instanceId, giftShortcut);
};
