import { get } from "lodash";
import {createSelector} from "reselect";

import IStore, { IHooks } from "../store";
import { getInitialInstanceData } from "../initialStore";

export const getHooks = (store: IStore, instanceId: string | undefined) => {
    const id = instanceId as string;
    const data = get(store, id) || getInitialInstanceData();
    return data.hooks;
};


/**
 * @param hookName
 * @return array of arguments (if any) for the previous call of the specified hook
 */
export const geHook = (hookName: keyof IHooks) => createSelector(
    getHooks,
    hooks => hooks[hookName]
);
