import styled from "styled-components";
import { isIE } from 'react-device-detect';
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";
import { HeaderAlignment } from "./types";

interface IWrapperProps {
    minHeaderHeight: number;
    alignment: HeaderAlignment;
}

// IE-SPECIFIC: different styling for our messenger's header on any IE browser.
export const Wrapper = withCustomClasses<IWrapperProps>(styled.div<IWrapperProps>`
    ${!isIE && `flex-basis: ${({ minHeaderHeight }: IWrapperProps) => minHeaderHeight}px;`}
    text-align: ${({ alignment }: IWrapperProps) => alignment};
    
    display: block;
    position: relative;
    box-sizing: border-box;
    z-index: 2;
    
    &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0px;
        width: 100%;
        height: 3px;
        background: -webkit-gradient(linear,left top, left bottom,color-stop(0, #c3c6c9),to(#dedfe1));
        background: linear-gradient(to bottom,#c3c6c9 0,#dedfe1 100%);
    }
`, [DomCustomClass.Header]);
