import styled from 'styled-components';

interface IProfileNameWrapperProps {
    hasModelProfilePicture: boolean;
}

interface IProfileOnlineBulletProps {
    isModelOnline: boolean;
}

export interface IProfileNameProps {
    title?: string;
    onClick: () => void;
}

export const ProfileInfoInnerWrapper = styled.div.attrs({ className: 'profile-wrapper' })`
    margin-top: 0;
`;

export const ProfileNameWrapper = styled.div.attrs({ className: 'profile-name-wrapper' })<IProfileNameWrapperProps>`
    ${props => !props.hasModelProfilePicture ? 'padding-top: 12px' : 'padding: 5px 0'};
    
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
`;

export const ActionSectionWrapper = styled.div.attrs({ className: 'action-section-wrapper' })`
    width: 100%;
`;

export const ProfileName = styled.div<IProfileNameProps>`
    ${props => props.title && `
        cursor: pointer;
        
        &:hover {
            color: ${props.theme.colors.profileLinkHover};
        }
    `}
    
    font-family: ${(props) => props.theme.typography.fontFamily};
    color: ${(props) => props.theme.colors.profileLink};   
    font-weight: ${(props) => props.theme.typography.weight.medium};

    font-size: 23px;
    display: inline-block;
    text-decoration: none;
`;

export const Motto = styled.div`
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-weight: ${(props) => props.theme.typography.weight.regular};
    color: ${(props) => props.theme.colors.profileMotto};

    width: 80%;
    margin: 5px auto 15px;
    text-align: center;
    font-size: 14px;
`;

export const FeatureWrapper = styled.div`
    margin-left: 7px;
    display: inline-block;
`;

export const ProfileOnlineBullet = styled.span<IProfileOnlineBulletProps>`
    height: 7px;
    width: 7px;
    background-color: ${props => props.isModelOnline 
        ? props.theme.colors.onlineBulletColor
        : props.theme.colors.offlineBulletColor
    };
    border-radius: 50%;
    display: inline-block;
    margin-right: 7px;
`;
