// Sagas
const startVideoChatTypeSaga = function* ({ payload }: Action<IPayload<VideoChatType>>) {
    const { instanceId, value } = payload;
    yield spawn(removeDialogsIfAnySaga, instanceId);
    yield put(resetExitEvent(instanceId));
    const newConfig = undefined;

    switch (value) {
        case VideoChatType.GROUP_CHAT: {
            yield spawn(startGroupChatSaga, instanceId, newConfig);
            break;
        }

        case VideoChatType.LIVECHAT: {
            yield spawn(startLiveChatSaga, instanceId, newConfig);
            break;
        }

        case VideoChatType.FREECHAT: {
            yield spawn(startFreeChatSaga, instanceId, newConfig);
            break;
        }

        case VideoChatType.LIVE_PREVIEW: {
            yield spawn(startLivePreviewChatSaga, instanceId, newConfig);
            break;
        }

        case VideoChatType.PRIVATE_CHAT: {
            yield spawn(startPrivateChatSaga, instanceId, false);
            break;
        }

        case VideoChatType.VOYEUR_CHAT: {
            yield spawn(startVoyeurChatSaga, instanceId, newConfig);
            break;
        }

        case VideoChatType.VOYEUR_PRIVATE_CHAT: {
            yield spawn(startVoyeurPrivateChatSaga, instanceId, newConfig);
            break;
        }

        case VideoChatType.NO_DOWNSTREAM: {
            yield spawn(startChatWithoutStreamSaga, instanceId, newConfig);
            break;
        }

        case VideoChatType.CONTENT_PARTNERS_CHAT: {
            yield spawn(startContentPartnersChatSaga, instanceId, newConfig);
            break;
        }
    }
};

const resumeExistingVideoChatSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const existingType: VideoChatType = yield select(selectors.getVideoChatType, instanceId);
    yield spawn(resumeVideoChatSaga, instanceId, existingType);
};

const resumePreviousVideoChatSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const previousType: VideoChatType = yield select(selectors.getLastSuccessfulVideoChatType, instanceId);
    yield spawn(resumeVideoChatSaga, instanceId, previousType);
};

const resumeInitialVideoChatSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const initialType: VideoChatType = yield select(selectors.getInitialVideoChatType, instanceId);
    yield spawn(resumeVideoChatSaga, instanceId, initialType);
};

const resumeVideoChatSaga = function* (instanceId: string, type: VideoChatType) {
    yield put(resetExitEvent(instanceId));

    const chatConfig = yield select(selectors.getChat, instanceId);
    const requestNewConfig: boolean = !chatConfig;

    switch (type) {
        case VideoChatType.GROUP_CHAT: {
            yield spawn(startGroupChatSaga, instanceId, requestNewConfig);
            break;
        }

        case VideoChatType.LIVECHAT: {
            yield spawn(startLiveChatSaga, instanceId, requestNewConfig);
            break;
        }

        case VideoChatType.FREECHAT: {
            yield spawn(startFreeChatSaga, instanceId, requestNewConfig);
            break;
        }

        case VideoChatType.LIVE_PREVIEW: {
            yield spawn(startLivePreviewChatSaga, instanceId, requestNewConfig);
            break;
        }

        case VideoChatType.PRIVATE_CHAT: {
            yield spawn(startPrivateChatSaga, instanceId, requestNewConfig);
            break;
        }

        case VideoChatType.VOYEUR_CHAT: {
            yield spawn(startVoyeurChatSaga, instanceId, requestNewConfig);
            break;
        }

        case VideoChatType.VOYEUR_PRIVATE_CHAT: {
            yield spawn(startVoyeurPrivateChatSaga, instanceId, requestNewConfig);
            break;
        }

        case VideoChatType.NO_DOWNSTREAM: {
            yield spawn(startChatWithoutStreamSaga, instanceId, requestNewConfig);
            break;
        }
    }

    // When resume the stream, also add the cam2cam feature if it was active
    const isCam2CamActive = yield select(selectors.isCam2CamActive, instanceId);
    if (isCam2CamActive) {
        yield call(startCam2CamSaga, instanceId);
    }
};

const startChatWithoutStreamSaga = function* (instanceId: string, requestNewConfig: boolean = true) {
    const chat: IGetChatInstanceSagaResult = yield call(
        getChatInstanceSaga,
        requestNewConfig,
        instanceId
    );

    if (chat.chatConf.error || chat.chatInstance?.error) {
        yield put(closeVideochat(instanceId));
        loggers.saga.debug('trying to start chat without any downstream raised this error:',
            { details: { error: chat.chatConf.error || chat.chatInstance?.error } });
        yield put(cancelStartingVideoChatType(instanceId, VideoChatType.NO_DOWNSTREAM));
        return;
    }

    if (requestNewConfig && chat.chatInstance?.result) {
        const isTextEnabled = yield call(ChatService.getService(instanceId).startText, chat.chatInstance?.result);
        yield put(setIsTextEnabled(instanceId, isTextEnabled));
    }

    const videoChatType = yield select(selectors.getVideoChatType, instanceId);
    if (videoChatType !== VideoChatType.NO_DOWNSTREAM) {
        yield put(setType(instanceId, VideoChatType.NO_DOWNSTREAM));
    }
};

const startLiveChatSaga = function* (instanceId: string, requestNewConfig: boolean = true) {
    const { chatInstance, error } =
		yield call (initiateChatSaga, instanceId, VideoChatType.LIVECHAT, requestNewConfig);

    if (error) {
    	return;
	}

    if (requestNewConfig) {
        const isTextEnabled = yield call(ChatService.getService(instanceId).startText, chatInstance);
        yield put(setIsTextEnabled(instanceId, isTextEnabled));
    }

    const videoChatType = yield select(selectors.getVideoChatType, instanceId);
    if (videoChatType !== VideoChatType.LIVECHAT) {
        yield put(setType(instanceId, VideoChatType.LIVECHAT));
    }

    // We need to add a toy system message only the first time we start the stream.
    // In this saga we will also enter if we swap between fullscreen and normal screen and
    // we don't want to render the system message in this case.
    if (requestNewConfig) {
        yield put(displaySystemToyMessage(instanceId));
    }
};


const startContentPartnersChatSaga = function* (instanceId: string, requestNewConfig: boolean = true) {
	const { chatInstance, error } =
		yield call (initiateChatSaga, instanceId, VideoChatType.CONTENT_PARTNERS_CHAT, requestNewConfig);

	if (error) {
		return;
	}

	yield put(setType(instanceId, VideoChatType.CONTENT_PARTNERS_CHAT));

	if (requestNewConfig) {
		// do not activate yet the text feature if chat is in voyeur mode;
		// it will be triggered when sending the first message
		if (chatInstance && !chatInstance.isInVoyeurMode) {
			const isTextEnabled = yield call(ChatService.getService(instanceId).startText, chatInstance);
			yield put(setIsTextEnabled(instanceId, isTextEnabled));
		}
	}
};

