const startPrivateChatSaga = function* (instanceId: string, requestNewConfig: boolean = true) {
    const { isSuccessful, chat}: IPreChecksResult =
        yield call(precheckSaga, instanceId, VideoChatType.PRIVATE_CHAT, requestNewConfig);

    if (!isSuccessful || !chat) {
        // pre-checks failed
        yield put(cancelStartingVideoChatType(instanceId, VideoChatType.PRIVATE_CHAT));
        return;
    }

    if (chat.chatInstance?.error) {
        yield put(cancelStartingVideoChatType(instanceId, VideoChatType.PRIVATE_CHAT));
        yield put(closeVideochat(instanceId));
        loggers.saga.debug('trying to start live private chat raised this error:',
            { details: { error: chat.chatInstance?.error } });
        return;
    }

    if (requestNewConfig && chat.chatInstance?.result) {
        const isTextEnabled = yield call(ChatService.getService(instanceId).startText, chat.chatInstance?.result);
        yield put(setIsTextEnabled(instanceId, isTextEnabled));
    }

    if (chat.chatInstance?.result && !chat.chatInstance.result.singleMode) {
        loggers.saga.debug('Chat instance not in single mode; starting single mode');
        yield call(ChatService.getService(instanceId).startPrivateChat, chat.chatInstance.result);
        loggers.saga.debug('Chat instance was not in single mode; single mode successfully initiated');
    }

    const isPlaying = yield select(selectors.isPlaying, instanceId);
    if (!isPlaying && chat.chatInstance?.result) {
        loggers.saga.debug('Stream was paused; re-start the streaming');

        const { error } = yield call(
            playTheStreamSaga,
            chat.chatInstance?.result,
            VideoChatType.PRIVATE_CHAT,
            instanceId
        );

        if (error) {
            yield put(cancelStartingVideoChatType(instanceId, VideoChatType.PRIVATE_CHAT));
            return;
        }

        loggers.saga.debug('Stream was paused; streaming successfully re-started');
    }

    // don't worry: next steps will be triggered by chat events
};

const startVoyeurChatSaga = function* (instanceId: string, requestNewConfig: boolean = true) {
	const { error } =
		yield call (initiateChatSaga, instanceId, VideoChatType.VOYEUR_CHAT, requestNewConfig);

	if (error) {
		return;
	}

    const videoChatType = yield select(selectors.getVideoChatType, instanceId);
    if (videoChatType !== VideoChatType.VOYEUR_CHAT) {
        yield put(setType(instanceId, VideoChatType.VOYEUR_CHAT));

        // If the messenger is open inside a content partner, all tips will be removed
        const shouldOpenAnonymousTipPrompt = yield select(selectors.shouldOpenAnonymousTipPrompt, instanceId);
        if (shouldOpenAnonymousTipPrompt) {
            const chatId = yield select(selectors.getChatId, instanceId);
            yield put(openAnonymousTipPrompt(instanceId, chatId));
        }
    }
};

const startVoyeurPrivateChatSaga = function* (instanceId: string, requestNewConfig: boolean = true) {
	const { error } =
		yield call (initiateChatSaga, instanceId, VideoChatType.VOYEUR_PRIVATE_CHAT, requestNewConfig);

	if (error) {
		return;
	}

    yield put(setIsTextEnabled(instanceId, false));

    const videoChatType = yield select(selectors.getVideoChatType, instanceId);
    if (videoChatType !== VideoChatType.VOYEUR_PRIVATE_CHAT) {
        yield put(setType(instanceId, VideoChatType.VOYEUR_PRIVATE_CHAT));
    }
};

const initiateChatSaga = function* (instanceId: string, videoChatType: VideoChatType,
										  requestNewConfig: boolean = true) {
	const { isSuccessful, chat}: IPreChecksResult =
		yield call(precheckSaga, instanceId, videoChatType, requestNewConfig);

	if (!isSuccessful || !chat) {
		// pre-checks failed
		yield put(cancelStartingVideoChatType(instanceId, videoChatType));
		return { error: true };
	}

	const chatError = chat.chatInstance?.error;

	if (chatError) {
		yield put(cancelStartingVideoChatType(instanceId, videoChatType));
		yield put(closeVideochat(instanceId));
		loggers.saga.debug(`trying to start ${videoChatType} raised this error:'`,
			{ details: { error: chatError }}
		);

		return { error: true };
	}

	if (chat.chatInstance?.result) {
		const { error } = yield call(
			playTheStreamSaga,
			chat.chatInstance?.result,
			videoChatType,
			instanceId
		);

		if (error) {
			yield put(cancelStartingVideoChatType(instanceId, videoChatType));
			return { error: true };
		}
	}

	return { chatInstance: chat.chatInstance?.result };
};

const playTheStreamSaga = function* (chatInstance: CoreChat, videoChatType: VideoChatType, instanceId: string) {
    const { LIVE_PREVIEW, FREECHAT } = VideoChatType;

    const sources: PromiseResult<SourceSet, any> = yield call(
        ChatService.getService(instanceId).getStream,
        chatInstance,
        videoChatType === LIVE_PREVIEW || videoChatType === FREECHAT
    );

    if (sources.error) {
        yield put(closeVideochat(instanceId));
        loggers.saga.debug(`trying to start ${videoChatType} raised this error:`,
            { details: { error: sources.error } });
        return { error: true };
    }

    const streamHasStopped = yield call(
        stopStreamIfVideoChatWasClosedSaga,
        `${videoChatType} deliberately closed by the user; stream aborted`,
        instanceId
    );

    if (streamHasStopped) {
        return { error: true };
    }

    PlayerService.getService(instanceId).play(sources.result);

    yield take(takePattern(SET_PLAYING_STATUS, instanceId));
    const isPlaying = yield select(selectors.isPlaying, instanceId);

    if (isPlaying) {
        loggers.saga.debug('Stream started to play');
    }

    return isPlaying ? { ok: true } : { error: true };
};

const createVideoChatPlayerSaga = function* ({ payload }: Action<IPayload<HTMLElement>>) {
    const { instanceId, value } = payload;
    const playerService = PlayerService.getService(instanceId);

	const qualityBoundaries = yield select(selectors.getVideoChatQualityBoundaries, instanceId);
	const initialVideochatVolume = yield select(selectors.getInitialVideochatVolume, instanceId);
    playerService.createPlayer(value, qualityBoundaries, initialVideochatVolume);

    const isMutedByClient = yield select(selectors.getVideoChatIsMutedByClient, instanceId);
    const notMutedClientVolume = yield select(selectors.getNotMutedClientVolume, instanceId);
    if (isMutedByClient) {
        playerService.mutePlayer();
    } else {
    	playerService.setVolume(notMutedClientVolume);
	}

    yield put(resumeExistingVideochat(instanceId));
};

const stopVideoChatPlayerSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    PlayerService.getService(instanceId).stopPlayer();
};

const closeVideoChatPlayerSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    PlayerService.getService(instanceId).closePlayer();
};

const publishVideoChatPlayerSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    // publish the player on the existing stream
    const chat: IGetChatInstanceSagaResult = yield call(getChatInstanceSaga, false, instanceId);
    if (chat.chatConf.error) {
        loggers.saga.debug('VIDEOCHAT - could not get chat to publish; aborting');
        return;
    }

    if (chat.chatInstance?.error) {
        yield put(setCam2CamInactive(instanceId));
        loggers.saga.debug('trying to get a chat on which to publish own camera raised this error:',
            { details: { error: chat.chatInstance.error } });
        return;
    }

	if (chat.chatInstance?.result) {
		const targets: PromiseResult<TargetSet, any> =
			yield call(ChatService.getService(instanceId).getUpstream, chat.chatInstance?.result);

		if (targets.error) {
			yield put(setCam2CamInactive(instanceId));
			loggers.saga.debug('trying to get an upstream on which to publish own camera raised this error:',
				{ details: { error: targets.error } });
			return;
		}

		PlayerService.getService(instanceId).publishCamera(targets.result);
	}
};

/**
 * This is closing the video chat player, stream and the video mode
 */
const closeVideoChatSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    yield put(stopVideoChat(instanceId));

    const isExitingInformationType = yield select(selectors.isExitingInformationType, instanceId);
    const errorType = yield select(selectors.getInformationType, instanceId);

    if (errorType && !isExitingInformationType) {
        yield put(resetInformation(instanceId));
    }

    yield put(closeVideoChatPlayer(instanceId));

    const isChatExternallyProvided = yield select(selectors.isChatExternallyProvided, instanceId);
    const hasExitPopups = yield select(selectors.hasExitPopups, instanceId);

    if (isChatExternallyProvided && !hasExitPopups) {
        // no reason to have the messenger open
        yield put(closeMessenger(instanceId));
    }

    yield put(resetChat(instanceId));
    yield put(resetType(instanceId));
};

/**
 * This is stopping the video chat player and stream, but will let the messenger in videochat mode
 */
const stopVideoChatSaga = function* ({ payload }: Action<IPayload<boolean>>) {
    const { instanceId, value } = payload;

    const isCam2CamActive = yield select(selectors.isCam2CamActive, instanceId);
    if (isCam2CamActive) {
        yield call(stopCam2CamSaga, instanceId);
    }


    const videoChatType = yield select(selectors.getVideoChatType, instanceId);
    if (videoChatType) {
        yield put(stopVideoChatPlayer(instanceId));
        ChatService.getService(instanceId).closeChat(value);
    }

    const countdownWarning = yield select(selectors.getCountdownWarning, instanceId);
    if (countdownWarning) {
        yield put(closeCountdownWarning(instanceId));
    }

    const isFullScreen = yield select(selectors.isFullScreen, instanceId);
    if (isFullScreen) {
        yield put(closeFullscreen(instanceId));
	}

	const isPlaying = yield select(selectors.isPlaying, instanceId);
    if (isPlaying) {
        yield put(setPlayingStatus(instanceId, false));
    }
};

const sendAnonymousTipFromVoyeurSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const chat: IGetChatInstanceSagaResult = yield call(getChatInstanceSaga, false, instanceId);
    if (chat.chatConf.error) {
        loggers.saga.debug('VIDEOCHAT - could not get chat to send tip; aborting');
        return;
    }

    if (chat.chatInstance?.error) {
        yield put(setCam2CamInactive(instanceId));
        loggers.saga.debug('trying to get a chat on which to send a tip raised this error:',
            { details: { error: chat.chatInstance.error } });
        return;
    }

    if (chat.chatInstance?.result) {
	    /** Send the tip (for sending x tips => the correct amount is x * 100) */
	    const { error } = yield call(ChatService.getService(instanceId).sendTip, chat.chatInstance?.result, 100);

	    /**
	     * If the action was successful, our payload will be undefined,
	     * so we should hard-code the message.
	     */
	    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);
	    const { voyeurTipFail, voyeurTipOk } = translation.chat;

	    /** Action response for sending the tip action */
	    const actionResponse: IActionResponse = {
		    text: error ? voyeurTipFail : voyeurTipOk,
		    type: isEmpty(error) ? ActionResponseType.SUCCESSFUL : ActionResponseType.ERROR
	    };

	    /** Close the anonymous user prompt */
	    yield put(closeUserPromptSection(instanceId, QuestionType.SEND_ANONYMOUS_TIP));

	    /** Render the action response based on promise's answer */
	    yield put(setActionResponse(instanceId, actionResponse));
    }
};

const sendTipSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const chat: IGetChatInstanceSagaResult = yield call(getChatInstanceSaga, false, instanceId);
    if (chat.chatConf.error) {
        loggers.saga.debug('VIDEOCHAT - could not get chat to send tip; aborting');
        return;
    }

    if (chat.chatInstance?.error) {
        yield put(setCam2CamInactive(instanceId));
        loggers.saga.debug('trying to get a chat on which to send a tip raised this error:',
            { details: { error: chat.chatInstance.error } });
        return;
    }

    /** Send the tip */
    const tipOption: ITipOption = yield select(selectors.getTip, instanceId);
    const tipValue = (tipOption.currency.name === CurrencyName.Credits ? tipOption.value : tipOption.value * 100);

    if(chat.chatInstance?.result) {
	    const { error } = yield call(ChatService.getService(instanceId).sendTip, chat.chatInstance?.result, tipValue);

	    /**
	     * If the action was successful, our payload will be undefined,
	     * so we should hard-code the message.
	     */
	    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);
	    const { tipFail, tipOk } = translation.chat;

	    /** Action response for sending the tip action */
	    const actionResponse: IActionResponse = {
		    text: error ? tipFail : tipOk,
		    type: isEmpty(error) ? ActionResponseType.SUCCESSFUL : ActionResponseType.ERROR
	    };

	    /** Close the anonymous user prompt */
	    yield put(closeUserPromptSection(instanceId, QuestionType.SEND_TIP));

	    /** Render the action response based on promise's answer */
	    yield put(setActionResponse(instanceId, actionResponse));
    }
};


const switchVoyeurPrivate2VoyeurSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const isVideoChatInVoyeurPrivateMode = yield select(
        selectors.isVideoChatInVoyeurPrivateMode,
        instanceId
    );

    if (isVideoChatInVoyeurPrivateMode) {
        // we can use the same chat data, chat instance and stream; only change to full voyeur
        yield put(setType(instanceId, VideoChatType.VOYEUR_CHAT));
    }
};

const switchVoyeurToChatSaga = function* ({ payload }: Action<IPayload<string>>) {
    const { instanceId, value } = payload;
    const isVideoChatInVoyeurMode = yield select(selectors.isVideoChatInVoyeurMode, instanceId);

    if (isVideoChatInVoyeurMode) {
        const chat: IGetChatInstanceSagaResult =
            yield call(getChatInstanceSaga, false, instanceId, types.SubActionType.STARTVOYEURCHAT);

        if (chat.chatConf.error || chat.chatInstance?.error) {
            yield put(closeVideochat(instanceId));
            loggers.saga.debug('could not get voyeur chat to reuse for live chat; aborting');
            return;
        }

        if (chat.chatInstance?.result) {
	        // try to enable texting, thus migrating to a normal chat mode
	        const isTextEnabled = yield call(
		        ChatService.getService(instanceId).startText,
		        chat.chatInstance?.result
	        );

	        if (isTextEnabled) {
		        // send the message, which was not previously sent
		        if (value) {
			        ChatService.getService(instanceId).sendMessage(value);
		        }

		        // close the send tip user prompt if it's open
		        const hasSendTipPromptOpen = yield select(selectors.hasSendTipPromptOpen, instanceId);
		        if (hasSendTipPromptOpen) {
			        yield put(closeUserPromptSection(instanceId, QuestionType.SEND_ANONYMOUS_TIP));
		        }

		        // we can use the same chat data, chat instance and stream; only change to chat mode
		        // based on the free chat duration value
		        const { LIVECHAT, FREECHAT } = VideoChatType;
		        const hasFreeChatDuration = yield select(selectors.hasFreeChatDuration, instanceId);
		        const videoChatType = hasFreeChatDuration ? FREECHAT : LIVECHAT;
		        yield put(setType(instanceId, videoChatType));

		        // We wait to receive the message, which made the switch between voyeur
		        // and livechat. Afterwards, we will add the default system toy message
		        yield take(takePattern(ADD_MESSAGE, instanceId))
		        yield put(displaySystemToyMessage(instanceId));
	        }
        }
    }
};

/**
 * If the video chat was closed by the user, close the chat instance to forcefully stop the stream;
 * Useful for the following corner case:
 * 1. User under 18s open the video chat, but the host has to approve it first
 * 2. User close the video chat;
 * 3. Host approves the chat and the stream starts flowing.
 *
 * Unfortunately, it takes a while for the stream to "figure out"
 * that no video player is using it so it could close itself, from here this approach
 *
 * @param logEntry
 * @return {boolean} true if video chat was closed
 */
const stopStreamIfVideoChatWasClosedSaga = function* (logEntry: string, instanceId: string) {
    const isVideoChatOpen = yield select(selectors.isVideoChatOpen, instanceId);

    if (!isVideoChatOpen) {
        yield call(ChatService.getService(instanceId).closeChat, true);
        loggers.saga.debug(logEntry);
        return true;
    }

    return false;
};

const removeDialogsIfAnySaga = function* (instanceId: string) {
    const informationType = yield select(selectors.getInformationType, instanceId);
    if (informationType) {
        yield put(resetInformation(instanceId));
    }

    const preChatInformationType = yield select(selectors.getPreChatInformationType, instanceId);
    if (preChatInformationType) {
        yield put(resetPreChatInformation(instanceId));
    }
};

const onExitingInformationTypeSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const isExitingInformationType = yield select(selectors.isExitingInformationType, instanceId);

    if (isExitingInformationType) {
        yield put(closeVideochat(instanceId));
    }
};

const onTextMutedUpdatedSaga = function* ({ payload }: Action<IPayload<boolean>>) {
    const { instanceId, value } = payload;
    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);

    yield put(setIsTextEnabled(instanceId, value));

    if (!value) {
        yield put(setActionResponse(instanceId, {
            text: translation.chat.isMuted,
            type: ActionResponseType.WARNING
        }))
    }
};

const displaySystemToyMessageSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    const hasToyControl = yield select(selectors.hasToyControl, instanceId);
    if (!hasToyControl) {
        return;
    }

    const chatId = yield select(selectors.getChatId, instanceId);
    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);

    yield put (addMessage(instanceId, {
        id: cuid.slug(),
        messageKey: cuid.slug(),
        dateCreated: moment().toString(),
        chatId,
        content: null,
        direction: MessageDirection.none,
        msgType: MsgType.TOY,
		isSystem: false,
        isNew: true,
        toy: {
            text: translation.chat.toyControl.lovenseAvailable,
            isDefault: true
        }
    }));
}

const setIsMutedByClientSaga = function* ({ payload: { instanceId, value } }: Action<IPayload<IPreferenceActionPayload>>) {
	const { preferenceName, value: preferenceValue } = value;

	if (preferenceName !== PreferenceName.VolumeLevel) {
		return;
	}

	if (preferenceValue === 0) {
		yield put(setIsMutedByClient(instanceId, true));
	}
};

const watchSagas = function* () {
    yield takeEvery(REQUEST_SEND_TIP, sendTipSaga);
    yield takeEvery(REQUEST_SEND_ANONYMOUS_TIP, sendAnonymousTipFromVoyeurSaga);

    yield takeEvery(START_VIDEOCHAT_TYPE, startVideoChatTypeSaga);
    yield takeEvery(RESUME_EXISTING_VIDEOCHAT, resumeExistingVideoChatSaga);
    yield takeEvery(RESUME_PREVIOUS_VIDEOCHAT, resumePreviousVideoChatSaga);
    yield takeEvery(RESUME_INITIAL_VIDEOCHAT, resumeInitialVideoChatSaga);

    yield takeEvery(CREATE_VIDEOCHAT_PLAYER, createVideoChatPlayerSaga);
    yield takeEvery(STOP_VIDEOCHAT_PLAYER, stopVideoChatPlayerSaga);
    yield takeEvery(CLOSE_VIDEOCHAT_PLAYER, closeVideoChatPlayerSaga);

    yield takeEvery(CLOSE_VIDEOCHAT, closeVideoChatSaga);
    yield takeEvery(STOP_VIDEOCHAT, stopVideoChatSaga);
    yield takeEvery(PUBLISH_VIDEOCHAT_PLAYER, publishVideoChatPlayerSaga);
    yield takeEvery(SWITCH_VOYEUR_PRIVATE_2_VOYEUR, switchVoyeurPrivate2VoyeurSaga);
    yield takeEvery(SWITCH_VOYEUR_2_LIVECHAT, switchVoyeurToChatSaga);
    yield takeEvery(SET_INFORMATION, onExitingInformationTypeSaga);
    yield takeEvery(ON_TEXT_MUTED_UPDATED, onTextMutedUpdatedSaga);
    yield takeEvery(DISPLAY_TOY_SYSTEM_MESSAGE, displaySystemToyMessageSaga);
    yield takeEvery(SET_PREFERENCE, setIsMutedByClientSaga);
};

export default watchSagas;
