import { filter, find } from 'lodash';
import {
	ICOMMAND,
	IRESPONSE,
	VXControlConfig,
	VXControlSession,
	VxControlAgent,
	SupportedLanguage,
	EnumInitDataFormat,
	ResultCode
} from 'vxcontrol-client-lib';

import utils from '../utils';
import { VXControlCodes } from "../static/constants/errorCodes";

interface IListeners {
	receiveOKListener: ((instanceId: string, response: IRESPONSE) => any),
	receiveErrorListener: ((instanceId: string, response: IRESPONSE) => any)
}

/** Creates the connection to the VXControl API endpoint */
class ApiService {
    private static services: ApiService[] = [];
    private static listeners: Partial<IListeners> = {};

    constructor(instanceId: string) {
        this.instanceId = instanceId;
    }

    private msnSession: VXControlSession | null;
    private language: SupportedLanguage;
    private instanceId: string;

    static registerOKListener(listener: (instanceId: string, response: IRESPONSE) => any) {
    	ApiService.listeners.receiveOKListener = listener;
	}

    static registerErrorListener(listener: (instanceId: string, response: IRESPONSE) => any) {
    	ApiService.listeners.receiveErrorListener = listener;
	}

    static addNewConnection = (instanceId: string) => {
        ApiService.services.push(new ApiService(instanceId));
    };

    static getService = (instanceId: string) => {
		const apiService = find(ApiService.services,
			service => service.getInstanceId() === instanceId
		);

        if (!apiService) {
            const service = new ApiService(instanceId);
            ApiService.services.push(service);
            return service;
		}

        return apiService;
    };

    static destroy = (instanceId: string) => {
        const apiService = find(ApiService.services, service => service.getInstanceId() === instanceId);

        if (apiService) {
            apiService.closeConnection();
            ApiService.services = filter(ApiService.services, service => service.getInstanceId() !== instanceId);
        }
    };

    getInstanceId = () => this.instanceId;

    /**
     * Starts the connection to the VXControl endpoint.
     * The INIT request is sent automatically after the websocket
     * connection is ready.
     */
    connect = (webtoken: string, language: SupportedLanguage) => {
        if (!this.msnSession) {
            /** Compose the settings for the API connection */
            const settings: VXControlConfig = new VXControlConfig();
            settings.agent = VxControlAgent.WEB;
            settings.useWS = true;
            settings.version = utils.versionNumber;
            settings.webtoken = webtoken;
            settings.language = language;
            settings.jsonp = true;
            settings.initdata = EnumInitDataFormat.VX_MESSENGER;
            settings.commandHandler = this.handleCommandResponse;

            /** For debugging purposes, you could use the console as an event logger for the API connection */
            // settings.logger = console;

            /** Create the session for our API connection */
            this.msnSession = new VXControlSession(settings);
        }

        this.updateLanguage(language);
    };

    updateLanguage = (language: SupportedLanguage) => {
        this.language = language;
    };

    /**
     * Logging out from the VXControl API; currently not used,
     * since the mother platform, as a webtoken provider, should manage the logout too
     */
    logout = () => {
        if (this.msnSession) {
            this.msnSession.logout(VXControlCodes.SUCCESS_EXIT_CODE);
        }
    };

    /**
     * Close the websocket from the client part
     */
    closeConnection = () => {
        if (this.msnSession) {
            this.msnSession.close();
            this.msnSession = null;
        }
    };

    /** Sends the command to the VXControl endpoint */
    sendCommand = (command: ICOMMAND) => {
        if (this.msnSession) {
            if (this.language) {
                command.params.language = this.language;
            }

            this.msnSession.sendCommand(command);
        }
    };

    /**
     * Handles the responses from the VXControl endpoint and based
     * by command's type, we call the receive saga logic by dispatching action creators
     */
    private handleCommandResponse = (command: IRESPONSE) => {
        if (command.result.code === ResultCode.OK) {
        	if (ApiService.listeners.receiveOKListener) {
				ApiService.listeners.receiveOKListener(this.instanceId, command);
			}
        } else {
            // Global error handling if the response code is not OK
			if (ApiService.listeners.receiveErrorListener) {
				ApiService.listeners.receiveErrorListener(this.instanceId, command);
			}
        }
    };
}

export default ApiService;
