import * as React from 'react';

import Arrow, { ArrowDirection } from '../Arrow';
import { InlineTitleElement, InlineLink, InlineArrow, Wrapper } from './style';

interface IProps {
    isOpen: boolean;
    link: string;
    title: string;
    isResponsive: boolean;
	isToySectionMinimizied: boolean;

    onClick: () => void;
}

const ToySectionHeader = (props: IProps) => {
    let arrowDirection = props.isOpen ? ArrowDirection.DOWN : ArrowDirection.UP;
    arrowDirection = props.isResponsive ? ArrowDirection.LEFT : arrowDirection;

    return (
        <Wrapper
            isToySectionMinimizied={props.isToySectionMinimizied}
            onClick={props.onClick}
        >
            <InlineArrow isResponsive={props.isResponsive}>
                <Arrow
                    arrowDirection={arrowDirection}
                    isResponsive={props.isResponsive}
                />
            </InlineArrow>

            <InlineTitleElement
                className={"-icon-toy-control"}
                isToySectionMinimizied={props.isToySectionMinimizied}
            />

            <InlineTitleElement
	            isToySectionMinimizied={props.isToySectionMinimizied}
            >
                {props.title}
            </InlineTitleElement>

            {props.isToySectionMinimizied && <InlineLink>{props.link}</InlineLink>}
        </Wrapper>
    );
}

export default ToySectionHeader;
