import { get } from "lodash";
import {createSelector} from "reselect";

import utils from "../../utils";
import IStore, { IUser, ITranslation } from "../store";
import { getInitialInstanceData } from "../initialStore";
import { PreferenceName } from "../../services/AppService/types";

export const getUser = (store: IStore, instanceId: string | undefined) => {
    const id = instanceId as string;
    const data = get(store, id) || getInitialInstanceData();
    return data.user; 
};

/**
 * @return {ITranslation} - an object containing all translation for the current language
 */
export const getTranslation = createSelector(getUser,
	(user: IUser) => utils.getTranslation(user.language, user.translations)
);

/**
 * We will take only the texts for error contexts
 */
export const getTranslationForErrors = createSelector(getTranslation,
    (translation: ITranslation) => translation.error
);

/**
 * @return {number} client side volume setting, but not considering the muting flag
 */
export const getNotMutedClientVolume = createSelector(getUser, (user: IUser) =>
	user.preferences[PreferenceName.VolumeLevel]);

/**
 * @return {SupportedLanguage} - current selected language; at this moment, only en, de and es are supported
 */
export const getLanguage = createSelector(getUser, (user: IUser) => user.language);

/**
 * @return {string | undefined} - current user key, provided by the hosting platform
 */
export const getUserKey = createSelector(getUser, (user: IUser) => user.userKey);

/**
 * @return {boolean} - true if the current user is currently online
 */
export const isUserOnline = createSelector(getUser, (user: IUser) => user.isOnline as boolean);

/**
 * @return {boolean} - true if the user is currently offline (disconnected)
 */
export const isUserOffline = createSelector(getUser, (user: IUser) => !user.isOnline);

/**
 * @return {boolean} - true if the user's account is a VIP one
 */
export const isVip = createSelector(getUser, (user: IUser) => user.isVip);

/**
 * @return {boolean} shouldIncreaseMessageFontSize preference
 */
export const getShouldIncreaseMessageFontSize = createSelector(getUser,
	(user: IUser) => user.preferences[PreferenceName.ShouldIncreaseMessageFontSize]
);
