import { has, isUndefined } from 'lodash';
import Rollbar from "rollbar";
import * as React from 'react';
import { Provider } from 'react-redux';

import App from './App';
import store from '../redux/configureStore';
import { IPlatformProps } from '../redux/store';
import { InstanceContext/*, versionNumber*/ } from '../utils/utils';
import { initializeMessengerInstance } from '../redux/reducers/rootReducer';
import { rollbarAccessToken } from '../static/config/config';
import constants from '../static/constants';

type IProps = IPlatformProps;

interface IState {
	rollbar?: Rollbar;
}

class AppContainer extends React.Component<IProps, IState> {
	instanceId: string;

	constructor(props: IProps) {
		super(props);

		// Initialize the messenger's instance in the Redux store
		this.instanceId = props.id || constants.defaultInstanceId;
		store.dispatch(initializeMessengerInstance(this.instanceId, this.instanceId));

		this.state = {
			rollbar: undefined
		};
	}

	componentDidMount() {
		this.setupProductionDebug();
	}

	componentDidUpdate() {
		this.setupProductionDebug();
	}

	render() {
		const isInstanceInitialized = has(store.getState(), this.instanceId);

		return (
			<>
				{isInstanceInitialized && (
					<Provider store={store}>
						<InstanceContext.Provider value={this.instanceId}>
							<App
								{...this.props}
								instanceId={this.instanceId}
							/>
						</InstanceContext.Provider>
					</Provider>
				)}
			</>
		);
	}

	private setupProductionDebug = () => {
		if (this.props.features?.hasProductionDebugEnabled && isUndefined(this.state.rollbar)) {
			this.setState({
				rollbar: new Rollbar({
					accessToken: rollbarAccessToken,
					captureUncaught: true,
					captureUnhandledRejections: true,
					payload: {
						environment: process.env.ROLLBAR_LEVEL,
						client: {
							javascript: {
								source_map_enabled: true,
								guess_uncaught_frames: true,
								code_version: '0.36.0_1'
							}
						}
					}
				})
			});
		}
	};
}

export default AppContainer;
