import * as React from 'react';

import selectors from '../../../redux/selectors';
import MessageSectionButtons from './MessageSectionButtons';
import IStore from '../../../redux/store';
import { openTipPrompt } from "../../../sagas/common/userPrompt";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps } from "./types";

import {
    closeEmoticonSection,
    closeGiftSection,
    openEmoticonSection,
    openGiftSection,
    requestImageUpload,
    toggleDropDown,
    toggleToySection as toggleToySectionAction
} from '../../../redux/modules/messenger';

import {
    toggleGiftSection,
    toggleEmoticonSection,
    toggleToySection
} from './utils'

const requestUpload = (props: IProps) => () => {
    props.requestImageUpload(props.instanceId);
};

const onTipClick = (props: IProps) => () => {
    props.openTipPrompt(props.instanceId);
};

const MessageSectionButtonsContainer = (props: IProps) => (
    <MessageSectionButtons
        hasEmoticons={props.hasEmoticons}
        hasImageUpload={props.hasImageUpload}
        shouldShowTipping={props.shouldShowTipping}
        isResponsive={props.isResponsive}
        isDropDownOpen={props.isDropDownOpen}
        isGiftSectionOpen={props.isGiftSectionOpen}
        isToySectionOpen={props.isToySectionOpen}
        hasToyControl={props.hasToyControl}
        isGiftButtonVisible={props.isGiftButtonVisible}
        isEmoticonSectionOpen={props.isEmoticonSectionOpen}
        translation={props.translation}

        onGiftClick={toggleGiftSection(props)}
        onEmoticonClick={toggleEmoticonSection(props)}
        requestImageUpload={requestUpload(props)}
        onToyClick={toggleToySection(props)}
        onTipClick={onTipClick(props)}
    />
);


const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    hasEmoticons: selectors.hasEmoticons(store, instanceId),
    hasImageUpload: selectors.hasImageUpload(store, instanceId),
    shouldShowTipping: selectors.shouldShowTipping(store, instanceId),
    isResponsive: selectors.isResponsive(store, instanceId),
    isInteractive: selectors.isInteractive(store, instanceId),
    isDropDownOpen: selectors.isDropDownMenuOpen(store, instanceId),
    isGiftSectionOpen: selectors.isGiftSectionOpen(store, instanceId),
    isToySectionOpen: selectors.isToySectionOpen(store, instanceId),
    hasToyControl: selectors.hasToyControl(store, instanceId),
    isGiftButtonVisible: selectors.isGiftButtonVisible(store, instanceId),
    isEmoticonSectionOpen: selectors.isEmoticonSectionOpen(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

const mapDispatchToProps = {
    toggleDropDown,
    toggleToySectionAction,
    openGiftSection,
    closeGiftSection,
    openEmoticonSection,
    closeEmoticonSection,
    requestImageUpload,
    openTipPrompt
};

export default withRedux(MessageSectionButtonsContainer, mapStateToProps, mapDispatchToProps);
