import * as React from 'react';

import {Inline, Text, Title, TooltipContentWrapper, Wrapper} from "./style";
import ToyOption, {IProps as ToyOptionProps} from '../../common/ToyOption/ToyOption';
import AdvancedTooltip from "../../common/AdvancedTooltip";
import {IBoundingRect} from "../../common/AdvancedTooltip/interfaces";

export interface IProps extends ToyOptionProps {
    tooltipImage: string;
    tooltipTitle: string;
    tooltipText: string;
    isResponsive: boolean;

    boundingRect?: IBoundingRect;
}

const ToyOptionWithTooltip = (props: IProps) => {
    const { tooltipText, tooltipTitle } = props;

    const target = (
        <Inline>
            <ToyOption
                {...props}
                image={props.image}
                hasTooltipOpen={true}
                isLocked={true}
                isPlaying={false}
                isAvailable={false}
            />
        </Inline>
    );

    return (
        <Wrapper>
            <AdvancedTooltip
                boundingRect={props.boundingRect}
                target={target}
            >
                <TooltipContentWrapper isResponsive={props.isResponsive}>
                    <Inline>
                        <Title isResponsive={props.isResponsive}>
	                        {props.name} - {tooltipTitle}
                        </Title>
                        <Text isResponsive={props.isResponsive}>{tooltipText}</Text>
                    </Inline>
                </TooltipContentWrapper>
            </AdvancedTooltip>
        </Wrapper>
    );
};

export default ToyOptionWithTooltip;
