import * as React from 'react';
import * as ReactDOM from 'react-dom';
import Scrollbar, { ScrollBarProps } from 'react-perfect-scrollbar';
import { isMobile } from 'react-device-detect';
import 'react-perfect-scrollbar/dist/css/styles.css';
import ITheme from '../../../themes/interfaces';

export enum ScrollPosition {
    TOP = 'TOP',
    BOTTOM = 'BOTTOM',
    BETWEEN = 'BETWEEN'
}

interface IComponentProps {
    className?: string;
    shouldScrollTop?: boolean;
    shouldUnsetScroll?: boolean;
    backgroundColor?: string;
    isFullscreen?: boolean;
    theme?: ITheme;
    hasFullWidth?: boolean;
	hasFullHeight?: boolean;
	config?: ScrollBarProps;

    onScroll?: (position: ScrollPosition) => void;
}

type IProps = IComponentProps;

class PerfectScrollbarContainer extends React.Component<IProps> {
    private static RailYClassName = 'ps__rail-y';
    private static ThumbYClassName = 'ps__thumb-y';

    protected scrollBarRef: any;
    private scrollTimer: any;

    componentDidMount() {
        const element = ReactDOM.findDOMNode(this) as any;
        element.addEventListener("scroll", this.handleScroll);
        element.addEventListener("wheel", this.handleWheel);
    }

    componentWillUnmount() {
        const element = ReactDOM.findDOMNode(this) as any;
        element.removeEventListener("scroll", this.handleScroll);
        element.removeEventListener("wheel", this.handleWheel);
    }

    render() {
        return (
			<Scrollbar
				ref={this.refCallback}
				className={this.props.className}
				{...this.props.config}
			>
                {this.props.children}
            </Scrollbar>
        );
    }

    private handleScroll = event => {
        if (this.scrollTimer !== null) {
            clearTimeout(this.scrollTimer);
        }

        this.scrollTimer = setTimeout(() => {
            if (this.props.onScroll) {
                const position: ScrollPosition = this.isOnTop(event.target)
                    ? ScrollPosition.TOP
                    : this.isOnBottom(event.target)
                        ? ScrollPosition.BOTTOM
                        : ScrollPosition.BETWEEN;

                this.props.onScroll(position);
            }
        }, 500);
	};

    private handleWheel = event => {
		event.preventDefault();
		event.stopPropagation();
	};

    private isOnTop = (element: HTMLElement): boolean => !!element && element.scrollTop === 0;

    private isOnBottom = (element: HTMLElement): boolean => {
        let railY: HTMLElement | null;
        const railYElements: HTMLCollectionOf<Element> = element
            .getElementsByClassName(PerfectScrollbarContainer.RailYClassName);
        railY = railYElements[0] as HTMLElement || null;

        if (railY) {
            const railYHeight: number = railY.clientHeight;

            let thumbY: HTMLElement | null;
            const thumbYElements: HTMLCollectionOf<Element> = railY
                .getElementsByClassName(PerfectScrollbarContainer.ThumbYClassName);
            thumbY = thumbYElements[0] as HTMLElement || null;

            if (thumbY) {
                const thumbYHeight: number = thumbY.clientHeight;
                const thumbYTop: number = thumbY.offsetTop;

                return railYHeight === thumbYTop + thumbYHeight;
            }
        }

        return false;
    };

    private refCallback = (ref) => {
        if (ref) {
            this.scrollBarRef = ref;

            if (!isMobile && this.props.isFullscreen) {
                ref._container.style.backgroundColor = 'transparent';
            } else {
                if (this.props.theme) {
                    ref._container.style.backgroundColor = this.props.theme.colors.chatBoxContainer;
                }
            }

            if (this.props.hasFullWidth) {
                ref._container.style.width = '100%';
            }

            if (this.props.hasFullHeight) {
                ref._container.style.height = '100%';
            }

            if (this.props.shouldUnsetScroll) {
                ref._container.style.position = "unset";
            }
        }
    };

    scrollDown = () => new Promise<void>((resolve) => {
		this.updateScroll().then(() => {
			if (this.scrollBarRef._container) {
				const { scrollHeight, clientHeight } = this.scrollBarRef._container;
				this.scrollBarRef._container.scrollTop = scrollHeight - clientHeight;
			}
			resolve();
		});
    });

    scrollUp = () => new Promise<void>((resolve) => {
        this.updateScroll().then(() => {
            if (this.scrollBarRef._container) {
                this.scrollBarRef._container.scrollTop = 0;
            }
            resolve();
        });
    });

    updateScroll = () => new Promise<void>((resolve) => {
	    this.scrollBarRef.updateScroll();
	    resolve();
	});

    scrollYTo = (anchorId?: string) => {
        this.updateScroll().then(() => {
            if (!anchorId) {
                this.scrollDown();
                return;
            }

            const { _container: container} = this.scrollBarRef;
            if (container) {
                const containerRect = container.getBoundingClientRect();
                const containerY = containerRect.y;

                const anchorElement: HTMLElement | null =
                    document.getElementById(anchorId);

                if (anchorElement) {
                    const anchorRect = anchorElement.getBoundingClientRect();
                    const anchorY = anchorRect.y;

                    // scroll such the anchor element becomes visible on top
                    container.scrollTop = anchorY - containerY;
                }
            }
        });
    };
}

export default PerfectScrollbarContainer;
