import * as React from 'react';

import LiveChatButton from './LiveChatButton';
import { onLiveChatButtonClick } from './utils';
import selectors from '../../../redux/selectors';
import { startVideoChatType } from "../../../sagas/common/videochat";
import IStore from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import {
	IContainerProps as IProps,
	IContainerComponentProps as IComponentProps
} from "./types";

const LiveChatButtonContainer = (props: IProps) => (
    <LiveChatButton
        currency={props.currency}
        price={props.price}
        translation={props.translation}
        shouldRenderPrice={!props.hasFreeChatDuration}
        isForActionSection={props.isForActionSection}
        shouldDisplayIcon={props.shouldDisplayIcon}
        onClick={onLiveChatButtonClick(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    currency: selectors.getCurrency(store, instanceId),
    hasFreeChatDuration: selectors.hasFreeChatDuration(store, instanceId),
    price: selectors.getLiveChatPrice(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

const mapDispatchToProps = {
    startVideoChatType
};

export default withRedux<IComponentProps>(LiveChatButtonContainer, mapStateToProps, mapDispatchToProps);
