import * as React from 'react';
import { DialogButtonColor, DialogButtonContent, DialogButtonWrapper } from './style';

interface IProps {
    color: DialogButtonColor;
    text?: string;
    onClick?: () => any;
}
const DialogButton = (props: IProps) => (
    <DialogButtonWrapper
        color={props.color}
        onClick={props.onClick}
    >
        <DialogButtonContent>
            {props.text}
        </DialogButtonContent>
    </DialogButtonWrapper>
);

export default DialogButton;