import { Action } from "redux-actions";
import { isUndefined } from "lodash";

import { ActionCreator, IPayload, ActionIds } from "./interfaces";

interface ICreateActionTypePayload {
    isReduxAction?: boolean;
    isSagaAction?: boolean;
}

/** This function will receive the desired action and will return an action creator for it */
export const createAction: ActionCreator = (type) => (instanceId, payload?) => {
    const action: Action<IPayload> = {
        type,
        payload: {
            instanceId: instanceId || "",
            value: isUndefined(payload) ? null : payload
        }
    };

    return action;
};

/**
 * 
 * @param id represents the file's name within action type creation
 * @param payload represents if the action should be scoped as a Redux or as a Saga action
 * @param action represents the name of the action
 */
export const createActionType = (id: ActionIds, payload: ICreateActionTypePayload) => (action: string) => {
    const scope = payload.isReduxAction ? "redux" : "sagas";

    return `app/${scope}/${id}/${action}`;
};