import * as React from 'react';
import { isUndefined } from 'lodash';

import IStore from '../../../redux/store';
import CloseButton from './CloseButton';
import selectors from '../../../redux/selectors';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import {
    ActionResponseCloseWrapper,
    FullscreenHeaderCloseWrapper,
    ImageModalCloseWrapper,
    InformationDialogCloseWrapper,
    MessengerCloseWrapper,
    MobileHeaderCloseWrapper,
    PreChatInformationCloseWrapper,
	GroupChatHeaderCloseWrapper
} from './style';

export enum CloseButtonType {
    MESSENGER = 'MESSENGER',
    MOBILE_HEADER = 'MOBILE_HEADER',
    INFORMATION_DIALOG = 'INFORMATION_DIALOG',
    PRECHAT_INFORMATION_DIALOG = 'PRECHAT_INFORMATION_DIALOG',
    ACTION_RESPONSE = 'ACTION_RESPONSE',
    FULLSCREEN_HEADER = 'PRECHAT_INFORMATION_DIALOG',
    IMAGE_MODAL = 'IMAGE_MODAL',
	GROUP_CHAT_HEADER = 'GROUP_CHAT_HEADER'
}

interface IComponentProps {
    hoverText?: string;
    iconClassName?: string;
    closeButtonType: CloseButtonType;

    /**
     * We use this flag for close buttons in InformationDialog. We basically ignore hasCloseButtonFromStore
     * when this flag is set.
     * - hasCloseButtonFromStore is only related for the buttons with which we close whole messenger;
     */
    hasCloseButton?: boolean;

    onClick: any;
}

interface IStoreProps {
    hasCloseButtonFromStore: boolean;
}

type IProps = IStoreProps & IComponentProps & IInstanceProps;

const getCloseButtonWrapper = (type: CloseButtonType) => {
    const types = {};

    types[CloseButtonType.MESSENGER] = MessengerCloseWrapper;
    types[CloseButtonType.MOBILE_HEADER] = MobileHeaderCloseWrapper;
    types[CloseButtonType.INFORMATION_DIALOG] = InformationDialogCloseWrapper;
    types[CloseButtonType.PRECHAT_INFORMATION_DIALOG] = PreChatInformationCloseWrapper;
    types[CloseButtonType.ACTION_RESPONSE] = ActionResponseCloseWrapper;
    types[CloseButtonType.FULLSCREEN_HEADER] = FullscreenHeaderCloseWrapper;
    types[CloseButtonType.IMAGE_MODAL] = ImageModalCloseWrapper;
    types[CloseButtonType.GROUP_CHAT_HEADER] = GroupChatHeaderCloseWrapper;

    return types[type] || types[CloseButtonType.MESSENGER];
};


const CloseButtonContainer = (props: IProps) => {
    const hasCloseButton = isUndefined(props.hasCloseButton)
        ? props.hasCloseButtonFromStore
        : props.hasCloseButton;

    const iconClassName = isUndefined(props.iconClassName)
        ? '-icon-close-full'
        : props.iconClassName;

    const onClick = () => {
    	props.onClick();
    	throw new Error ('closing test error triggered');
	};

    return (
        <>
            {hasCloseButton &&
                <CloseButton
                    className={'close-button'}
                    onClick={onClick}
                    hoverText={props.hoverText}
                    closeButtonWrapper={getCloseButtonWrapper(props.closeButtonType)}
                    iconClassName={iconClassName}
                />
            }
        </>
    );
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    hasCloseButtonFromStore: selectors.hasCloseButton(store, instanceId)
});

export default withRedux<IComponentProps>(CloseButtonContainer, mapStateToProps);
