import * as React from 'react';
import { ITranslation } from '../../../redux/store';
import utils from '../../../utils';
import { VipSingleRowText } from '../../common/BaseButton/style';
import Tooltip from '../../common/Tooltip';
import { VipLivePreviewButtonWrapper } from './style';
import { BaseButtonContent } from '../../common/BaseButton/style';
import BaseButtonContainer from '../../common/BaseButton';

interface IProps {
    seconds: number;
    translation: ITranslation;
    shouldDisplayIcon?: boolean;
    isForActionSection?: boolean;

    onClick: () => void;
}

const VipLivePreviewButton = (props: IProps) => {
    const { tooltipPreview, livePreview } = props.translation.chat;
    const seconds = { seconds: props.seconds };
    const name = `${props.translation.user.vip} ${livePreview}`;

    const vipPreviewButton = (
        <VipLivePreviewButtonWrapper>
			{utils.getShouldDisplayIcon(props.shouldDisplayIcon) &&
            	<i className={"-icon-person-vip"} />
			}
			
			<BaseButtonContent>
				<VipSingleRowText>{name}</VipSingleRowText>
			</BaseButtonContent>
        </VipLivePreviewButtonWrapper>
    );

    const vipPreviewButtonWithTooltip = (
        <Tooltip
            id={"vip-live-preview"}
            text={utils.translationWithValues(tooltipPreview, seconds)} 
        >
            {vipPreviewButton}
        </Tooltip>
    );

    return (
        <BaseButtonContainer
            onClick={props.onClick}
            isForActionSection={props.isForActionSection}
        >
            {tooltipPreview
                ? vipPreviewButtonWithTooltip
                : vipPreviewButton
            }
        </BaseButtonContainer>
    );
};

export default VipLivePreviewButton;
