import React from 'react';
import { Props as ReactSelectProps } from 'react-select';

import CustomMentionOption from './CustomMentionOption';
import IStore from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import selectors from '../../../redux/selectors';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    modelName: string,
    modelPicture: string;
	focusedMentionOption: string;
}

interface IComponentProps {
	focusedOptionId?: string;
	onMouseFocus: (id: string) => void;
}

type IProps = IStoreProps &IComponentProps & ReactSelectProps;

const CustomMentionOptionContainer = (props: IProps) => (
    <CustomMentionOption
		{...props}
		modelName={props.modelName}
		modelPicture={props.modelPicture}
		focusedOption={props.focusedMentionOption}
		onMouseFocus={props.onMouseFocus}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    modelName: selectors.getModelName(store, instanceId),
    modelPicture: selectors.getModelProfilePicture(store, instanceId),
	focusedMentionOption: selectors.getFocusedMentionOption(store, instanceId)
});

export default withRedux<IComponentProps>(CustomMentionOptionContainer, mapStateToProps);
