import * as React from 'react';
import MessengerContainer from '../../features/specific/Messenger/MessengerContainer';
import VideoChatContainer from '../../features/specific/Videochat/VideoChatContainer';
import VideoChatActionSectionContainer
    from '../../features/specific/VideochatActionSection/VideoChatActionSectionContainer';
import { IViewProviderProps } from '../interfaces';
import { MessengerContainerWrapper, VideoChatContainerWrapper, VideoChatPortraitViewWrapper } from './style';

const VideoChatPortraitView = (props: IViewProviderProps) => (
    <VideoChatPortraitViewWrapper>

        <VideoChatContainerWrapper {...props}>
            <VideoChatContainer />
            {!props.shouldRenderOnlyVideochat && <VideoChatActionSectionContainer />}
        </VideoChatContainerWrapper>

        {!props.shouldRenderOnlyVideochat && <MessengerContainerWrapper {...props}>
            <MessengerContainer />
        </MessengerContainerWrapper>}

    </VideoChatPortraitViewWrapper>
);

export default VideoChatPortraitView;
