import { Action } from "redux-actions";
import { ACTION } from "vxcontrol-client-lib";
import { select, takeEvery } from "redux-saga/effects";

import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from "../../redux/selectors";
import ApiService from "../../services/ApiService";
import { ActionIds } from "../../redux/interfaces";

const createGiftsActionType = createActionType(ActionIds.GIFTS, { isSagaAction: true });

// Actions
const REQUEST_PURCHASE_GIFT_BUNDLE = createGiftsActionType("REQUEST_PURCHASE_GIFT_BUNDLE");

// Action Creators
export const requestPurchaseGiftBundles = createAction<string>(REQUEST_PURCHASE_GIFT_BUNDLE);

/** It receives gift bundle's id that should be purchased for the user */
const requestPurchaseGiftBundleSaga = function* ({ payload }: Action<IPayload<string>>) {
    const { instanceId, value } = payload;

    const subaction = "BUYGIFTBUNDLE";
    const webtoken: string = yield select(selectors.getWebtoken, instanceId);

    const command = {
        action: ACTION.CMDP_QUERYBACKEND,
        params: {
            bundleId: value,
            subaction,
            webtoken
        }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

const watchSagas = function* () {
    yield takeEvery(REQUEST_PURCHASE_GIFT_BUNDLE, requestPurchaseGiftBundleSaga);
};

export default watchSagas;
