import { Action } from 'redux-actions';
import {get, includes, isEmpty} from 'lodash';
import {put, select, takeEvery} from "redux-saga/effects";
import {ACTION, IRESPONSE, ResultCode} from 'vxcontrol-client-lib';

import types from './types';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import {onReceiveChatError} from "../common/chat";
import AppService from '../../services/AppService';
import { resetChat } from "../../redux/modules/session";
import { translationWithValues } from "../../utils/utils";
import { ActionResponseType, LoadingStatus } from '../../redux/store';
import { addFailedMessage, setActionResponse, setMessengerLoadingStatus } from '../../redux/modules/messenger';
import { ActionIds } from '../../redux/interfaces';

const createHandleErrorActionType = createActionType(ActionIds.HANDLE_ERROR, { isSagaAction: true });

/** Actions */
export const RECEIVE_ERROR = createHandleErrorActionType("RECEIVE_ERROR");

/** Action Creators */
export const onReceiveError = createAction<IRESPONSE>(RECEIVE_ERROR);

const initActions = [
    ACTION.CMDP_LOGIN,
    ACTION.CMDP_INIT,
    ACTION.CMDP_GETCHANNELS,
    ACTION.CMDP_GETCHANNELINFO,
    ACTION.CMDP_GETONLINESTATE
];

const subactionsForGetChat = [
    types.SubActionType.STARTVIDEOCHAT,
    types.SubActionType.STARTFREECHAT,
    types.SubActionType.STARTLIVEPREVIEW,
    types.SubActionType.STARTVOYEURCHAT,
    types.SubActionType.STARTVOYEUR2SCHAT
];

const actionsNeedingWebToken = [
    ACTION.CMDP_INIT,
    ACTION.CMDP_LOGIN,
    ACTION.CMDP_QUERYBACKEND,
    ACTION.CMDP_MSG,
    ACTION.CMDP_SETCHANNELINFO,
    ACTION.CMDP_GETCHANNELINFO,
    ACTION.CMDP_SUPLOADPIC
];

/** codes of the errors that won't be displayed in an error action response */
const hiddenErrorCodes = [
    ResultCode.MODEL_IS_IN_PRIVATE_CHAT,
    ResultCode.SOFT_CHAT,
    ResultCode.UNKNOWN_ERROR
];

const receiveErrorSaga = function* ({ payload }: Action<IPayload<IRESPONSE>>) {
    const { instanceId, value: command } = payload;

    /** Retrieve the error message from the server */
    if (isEmpty(command) || isEmpty(command.result)) {
        return;
    }

    /** Additional error handling when it's a CMDP_QUERYBACKEND error (chat related) */
    if (command.action === ACTION.CMDP_QUERYBACKEND &&
        includes(subactionsForGetChat, command.params.subaction)
    ) {
        yield put(resetChat(instanceId));
        yield put(onReceiveChatError(instanceId, command.result));
    }

    const { code } = command.result;
	const {
		NO_CREDIT_VIDEOCHAT, NOT_ENOUGH_MONEY, INVALID_PARAMETER, INVALID_WEB_TOKEN
	} = ResultCode;

    /**
     * If we receive any NO_CREDIT error, we call the parent platform's onRecharge hook,
     * render the error and close the gift section if it's the case.
     */
    if (code === NOT_ENOUGH_MONEY || code === NO_CREDIT_VIDEOCHAT ) {
        AppService.getService(instanceId).onRecharge();
    }

    /** If the messenger is not initiated correctly, set the proper loading status */
    if (includes(initActions, command.action)) {
        yield put(setMessengerLoadingStatus(instanceId, LoadingStatus.ERROR));
    }

    /** we can assume (because there is no better detection mechanism) that the webtoken was the invalid parameter */
    const isWebTokenInvalid = (code === INVALID_PARAMETER || code === INVALID_WEB_TOKEN)
        && includes(actionsNeedingWebToken, command.action);

    if (isWebTokenInvalid) {
        if (command.action === ACTION.CMDP_MSG) {
            const text =  get(command, "params.text");

            if (text) {
                yield put(addFailedMessage(instanceId, text));
            }
        }

        AppService.getService(instanceId).onFailedWebToken(command.result);

        return;
    }

    // filter out the errors not meant to be displayed in the red ribbon
    if (includes(hiddenErrorCodes, command.result.code)) {
        return;
    }

    const modelName = yield select(selectors.getModelName, instanceId);

    yield put(setActionResponse(instanceId, {
        text: translationWithValues(command.result.reason, {
            modelname: modelName
        }),
        type: ActionResponseType.ERROR
    }));
};

const watchSagas = function* () {
    yield takeEvery(RECEIVE_ERROR, receiveErrorSaga);
};

export default watchSagas;
