import * as moment from 'moment';

export const getDayGroupDate = (datetime: string, language: string): string => {
	const momentDate = moment(datetime, moment.defaultFormat).locale(language);
	const daysAgo = moment().diff(momentDate, 'days');
	const groupTimeLastWeek = daysAgo === 0 ? 'Today' : momentDate.format('dddd');

	return daysAgo < 7
		? groupTimeLastWeek
		: momentDate.format('dddd, DD MMMM YYYY');
};

export const getChatGroupTime = (datetime: string): string => {
	const momentDate = moment(datetime);


	return momentDate.format("LT");
};
