import * as React from 'react';
import { FullscreenHeaderWrapper } from './style';
import CloseButtonContainer, { CloseButtonType } from '../CloseButton/CloseButtonContainer';

interface IProps {
    closeText: string;
    exitFullscreen: () => void;
}

const FullscreenHeader = (props: IProps) => (
    <FullscreenHeaderWrapper>
        <CloseButtonContainer
            onClick={props.exitFullscreen}
            hoverText={props.closeText}
            closeButtonType={CloseButtonType.FULLSCREEN_HEADER}
        />
    </FullscreenHeaderWrapper>
);

export default FullscreenHeader;
