import * as React from 'react';
import { chain, find, first, includes, map } from 'lodash';

import utils from '../../../utils';
import GiftSection from './GiftSection';
import config from '../../../static/config';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import { IGiftItemForPurchaseList } from '../PurchaseBundlePage';
import { closeGiftSection } from '../../../redux/modules/messenger';
import { openGiftBundlesPage } from '../../../sagas/specific/analytics';
import { requestPurchaseGiftBundles } from '../../../sagas/websockets/gifts';
import IStore, { IBundle, IGiftPage, IGift, ITranslation, ICurrency } from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IState {
    activeIndex: number;
    activeBundleIndex?: string;
}

interface IComponentProps {
    isOpen: boolean;
    isResponsive?: boolean;

    onGiftClick: (giftIcon: string) => () => void;
}

interface IStoreProps {
    gifts: IGift[];
    currency: ICurrency;
    giftPages: IGiftPage[];
    giftBundles: IBundle[];
    translation: ITranslation;
    isMobileLandscape: boolean;
	shouldUseDropdownAnimation: boolean;
	isFullScreen: boolean;

    closeGiftSection: ActionDispatcher;
    openGiftBundlesPage: ActionDispatcher;
    requestPurchaseGiftBundles: ActionDispatcher<string>;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

class GiftSectionContainer extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.scrollbar = React.createRef();

        this.state = {
            activeIndex: this.getFirstPageIndex()
        };
    }

    scrollbar: any;

    render() {
        const activeBundle = find(this.props.giftBundles, 
            (bundle: IBundle) => bundle.id.toString() === this.state.activeBundleIndex
        );

        // When we have an active bundle, we must send for rendering gifts name and quantity.
        let bundleGifts: IGiftItemForPurchaseList[] = [];
        if (activeBundle) {
            const bundleGiftIds = map(activeBundle.gifts, gift => gift.gift.id);

            bundleGifts = chain(this.props.gifts)
                .filter((gift: IGift) => includes(bundleGiftIds, gift.id))
                .map((gift: IGift) => {
                    const giftFromBundle = find(activeBundle.gifts, g => g.gift.id === gift.id);

                    return ({
                        name: gift.name,
                        quantity: (giftFromBundle && giftFromBundle.quantity) || 0
                    });
                })
                .value() as IGiftItemForPurchaseList[];
        }

        return (
            <GiftSection
                activeIndex={this.state.activeIndex}
                activeBundle={activeBundle}
                bundleGifts={bundleGifts}
                isOpen={this.props.isOpen}
                currency={this.props.currency}
                giftPages={this.props.giftPages}
                specialGiftPages={config.specialGiftPages}
                translation={this.props.translation}
                isResponsive={this.props.isResponsive}
                isMobileLandscape={this.props.isMobileLandscape}
                isFullScreen={this.props.isFullScreen}
                shouldUseDropdownAnimation={this.props.shouldUseDropdownAnimation}

                scrollbar={this.scrollbar}
                changeTab={this.changeTab}
                onGiftClick={this.props.onGiftClick}
                onBundleGiftClick={this.onBundleGiftClick}
                onBundlePurchase={this.onBundlePurchase}
                onBundlePageClose={this.onBundlePageClose}
                closeGiftSection={this.closeGiftSection}
            />
        );
    }

    private closeGiftSection = () => {
        this.props.closeGiftSection(this.props.instanceId)
    }

    private onBundlePurchase = () => {
        if (this.state.activeBundleIndex) {
            this.props.requestPurchaseGiftBundles(
                this.props.instanceId,
                this.state.activeBundleIndex
            );

            this.setState({
                activeIndex: this.getFirstPageIndex(),
                activeBundleIndex: undefined
            });
        }
    };

    private onBundlePageClose = () => this.setState({ activeBundleIndex: undefined });

    private onBundleGiftClick = (encodedBundleId: string) => () => {
        const activeBundleIndex = utils.getGiftDecoded(encodedBundleId).toString();
        this.setState({activeBundleIndex});
    };

    private getFirstPageIndex = (): number => {
        const firstPage = first(this.props.giftPages);
        return (firstPage && firstPage.id) || 1;
    };

    private changeTab = (activeIndex: number) => (): void => {
        const scrollbar = this.scrollbar.current;
        if (scrollbar) {
            scrollbar.scrollUp();
        }

        this.setState({ activeIndex });

        // Dispatch action for Google Analytics tracking
        const giftBundlesStaticId = 5;
        if (activeIndex === giftBundlesStaticId) {
            this.props.openGiftBundlesPage(this.props.instanceId);
        }
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    currency:selectors.getCurrency(store, instanceId),
    gifts: selectors.getTranslatedGifts(store, instanceId),
    giftPages: selectors.getGiftPages(store, instanceId),
    giftBundles: selectors.getGiftBundles(store, instanceId),
    isMobileLandscape: selectors.isMobileLandscape(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
	isFullScreen: selectors.isFullScreen(store, instanceId),
	shouldUseDropdownAnimation: selectors.shouldUseDropdownAnimation(store, instanceId)
});

const mapDispatchToProps = {
    closeGiftSection,
    requestPurchaseGiftBundles,
    openGiftBundlesPage
};

export default withRedux<IComponentProps>(GiftSectionContainer, mapStateToProps, mapDispatchToProps);
