import * as React from 'react';
import {
    AVSCam2CamPopupButton,
    AVSCam2CamPopupContent,
    AVSCam2CamPopupMainText,
    AVSCam2CamPopupSmallText,
    AVSCam2CamPopupWrapper
} from "./style";
import withIdentifier, { DomIdentifier } from "../../hoc/withIdentifier";

interface IProps {
    smallText: string;
    mainText: string;
    buttonText: string;
    onButtonClick: () => void;
}

const AVSCam2CamPopup = (props: IProps) => (
    <AVSCam2CamPopupWrapper>
        <AVSCam2CamPopupContent>
            <AVSCam2CamPopupSmallText>{props.smallText}</AVSCam2CamPopupSmallText>
            <AVSCam2CamPopupMainText>{props.mainText}</AVSCam2CamPopupMainText>

            <AVSCam2CamPopupButton onClick={props.onButtonClick}>
                {props.buttonText}
            </AVSCam2CamPopupButton>

        </AVSCam2CamPopupContent>
        
    </AVSCam2CamPopupWrapper>
);

export default withIdentifier<IProps>(AVSCam2CamPopup, DomIdentifier.AvsCam2CamPopup);
