import styled, { css, keyframes } from 'styled-components';

interface IPreloaderAnimationProps {
    preloader: string;
}

interface IPreloaderTextWrapper {
	shouldAdjustVertically?: boolean;
}

const staticLogoPulse = keyframes`
    0%,100%{opacity:.1}
    50%{opacity:1}}
`;

const animation = css`
    animation: ${staticLogoPulse} 3s infinite ease-in-out;
`;

const PreloaderAnimation = styled.div`
    ${animation}
`;

export const ConfiguredPreloader = styled(PreloaderAnimation)<IPreloaderAnimationProps>`
    ${props => `background: url(${props.preloader}) no-repeat center center`};
    
    width: 10vw;
    height: 10vw;
    background-size: auto 100%;
`;

export const DefaultPreloader = styled(PreloaderAnimation).attrs({ className: "-icon-three-dots-full" })`
    font-size: 10vw;
    color: white;
`;

export const PreloaderTitle = styled.p`
    font-family: ${props => props.theme.typography.fontFamily};
    font-weight: ${props => props.theme.typography.weight.bold};
    font-size: 2vw;
    color: #FFFFFF;
    margin: 25px 0 10px 0;
`;

export const PreloaderSubTitle = styled.p`
    font-family: ${props => props.theme.typography.fontFamily};
    font-weight: ${props => props.theme.typography.weight.regular};
    font-size: 1.5vw;
    color: #FFFFFF;
    margin: 0px;
`;

export const PreloaderTextWrapper = styled.div<IPreloaderTextWrapper>`
	${({ shouldAdjustVertically }) => shouldAdjustVertically ? "margin-top: 20px;" : ""}

	width: 100%;
    position: absolute;
	text-align: center;
`;
