import * as React from 'react';
import { isMobile } from "react-device-detect";

import { ITranslation } from '../../../redux/store';
import { ThemedStyledProps, withTheme } from "styled-components";

import VideoChatBaseButton from '../VideoChatBaseButton';
import ITheme from "../../../themes/interfaces";
import InitiallyMutedTooltip from '../InitiallyMutedTooltip';
import {VideoChatVolumeWrapper, VolumeSliderWrapper} from "./style";
import VideoChatVolumeSlider from "../VideoChatVolumeSlider/VideoChatVolumeSlider";

interface IProps {
    isMutedByClient: boolean;
    displayVolumeAsInitiallyMuted?: boolean;
    isInitiallyMutedHintDisplayed?: boolean;
    translation: ITranslation;
    volume: number;
    theme?: ITheme;

    toggleSound: () => void;
    hideInitiallyMutedHint?: () => void;
    setVolume: (volume: number) => void;
}

const VideoChatVolume = (props: ThemedStyledProps<IProps, ITheme>) => {
    const { actionMute, actionUnmute } = props.translation.chat;
    const text = props.isMutedByClient ?  actionUnmute : actionMute;
    const className = props.isMutedByClient ? '-icon-speaker-muted-full' : '-icon-speaker-full';

    const mutedButtonWithTooltip = (
        <InitiallyMutedTooltip
            translation={props.translation}
            onClose={props.hideInitiallyMutedHint}
        >
            <VideoChatBaseButton
                id={"videochat-volume-button"}
                onClick={props.toggleSound}
                backgroundColor={props.theme.colors.initiallyMutedButtonBackground}
            >
                <i className={className} />
            </VideoChatBaseButton>
        </InitiallyMutedTooltip>
    );

    const mutedButtonWithoutTooltip = (
        <VideoChatBaseButton
            id={"videochat-volume-button"}
            onClick={props.toggleSound}
            backgroundColor={props.theme.colors.initiallyMutedButtonBackground}
        >
            <i className={className} />
        </VideoChatBaseButton>
    );

    const initiallyMutedButton = (props.isInitiallyMutedHintDisplayed
        ? mutedButtonWithTooltip
        : mutedButtonWithoutTooltip
    );

    const regularVolumeButton = (
        <VideoChatBaseButton
            id={"videochat-volume-button"}
            text={text}
            onClick={props.toggleSound}>
            <i className={className} />
        </VideoChatBaseButton>
    );

    const hasSlider = !isMobile;

    return (
    	<VideoChatVolumeWrapper hasSlider={hasSlider}>
			{props.displayVolumeAsInitiallyMuted ? initiallyMutedButton : regularVolumeButton}
			{hasSlider &&
				<VolumeSliderWrapper>
					<VideoChatVolumeSlider
						value={props.volume * 100}
						onChange={handleVolumeChange(props)}
						aria-labelledby="continuous-slider"
					/>
				</VolumeSliderWrapper>
			}
		</VideoChatVolumeWrapper>
	);
};

export const handleVolumeChange = (props: IProps) => (_, value: number) => {
	if (props.setVolume) {
		props.setVolume(value / 100);
	}
};

export default withTheme<IProps, ITheme>(VideoChatVolume);
