import * as React from 'react';

import {ICameraDevice} from "../../../redux/store";
import {
    CheckedWrapper,
    FrontBackIconWrapper,
    FrontBackLabelWrapper,
    FrontBackWrapper,
    IconWrapper,
    LabelWrapper,
    Wrapper
} from "./style";

interface IProps {
    camera: ICameraDevice;
    isSelected: boolean;

    onClick?: (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

const NormalCameraOption = (props: IProps) => (
    <Wrapper
        onClick={props.onClick}
        isSelected={props.isSelected}>

        <IconWrapper>
            <div className="-icon-webcam-single-full" />
        </IconWrapper>

        <LabelWrapper>{props.camera.label}</LabelWrapper>

        {props.isSelected &&
            <CheckedWrapper>
                <div className="-icon-success-rounded-full" />
            </CheckedWrapper>
        }
    </Wrapper>
);

const FrontBackCameraOption = (props: IProps) => {
    const iconClassName = props.camera.isFront ? '-icon-smartphone-frontcam-full' : '-icon-smartphone-full';

    return (
        <FrontBackWrapper
            onClick={props.onClick}
            isSelected={props.isSelected}>

            <FrontBackIconWrapper>
                <div className={iconClassName}/>
            </FrontBackIconWrapper>

            <FrontBackLabelWrapper>{props.camera.label}</FrontBackLabelWrapper>

            {props.isSelected &&
                <CheckedWrapper>
                    <div className="-icon-success-rounded-full" />
                </CheckedWrapper>
            }
        </FrontBackWrapper>
    )
};

const CameraOption = (props: IProps) => (
    props.camera.isFront || props.camera.isBack
        ? <FrontBackCameraOption {...props} />
        : <NormalCameraOption {...props} />
);

export default CameraOption;
