import {Action, createAction} from "redux-actions";
import { eventChannel } from "redux-saga";
import {call, put, select, spawn, take, takeEvery} from "redux-saga/effects";
import { isIE } from "react-device-detect";

import config from "../../static/config";
import selectors from '../../redux/selectors';
import {ActionIds} from "../../redux/interfaces";
import { IPayload } from '../../redux/interfaces';
import { createActionType } from '../../redux/utils';
import { requestHistory } from '../websockets/history';
import { screenfull } from '../../utils';
import { closeFullscreen, CLOSE_FULLSCREEN, openFullscreen } from "../../redux/modules/videochat";

const createFullscreenActionType = createActionType(ActionIds.FULLSCREEN, { isSagaAction: true });

// Actions
const FULLSCREEN_CHANGED = createFullscreenActionType("FULLSCREEN_CHANGED");

// Action Creators
const fullscreenChanged = createAction<HTMLElement>(FULLSCREEN_CHANGED);

const fullscreenChangedSaga = function* (action: Action<HTMLElement>) {
	// IE-SPECIFIC: using a different property to retrieve the element before
	// going fullscreen on any IE browser.
    const element: HTMLElement | null = isIE
        ? (document as any).msFullscreenElement
        : action.payload;

    let instanceId: string | null;

    if (element && element.getAttribute) {
        instanceId = element.getAttribute(config.messengerInstanceIdAttribute);
    }  else {
        // we don't know what was the HTML element going on/ off full screen;
        // try to take instanceId from the store, if we already have one instance in full screen
        instanceId = yield select(selectors.getFullScreenInstance);
    }

    if (instanceId) {
        yield put(
			screenfull.isFullscreen()
				? openFullscreen(instanceId)
				: closeFullscreen(instanceId)
		)
    }
};

export const closeFullScreenSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    // We need to request history because it gets erased on openFullscreen action
	yield put(requestHistory(instanceId));
	screenfull.exit();
};

const subscribeToScreenfullChange = emitter => {
	screenfull.onChange((args: any) => {
		emitter(fullscreenChanged(args.target));
	});
};

const screenfullEventsChannel = () => {
    return eventChannel(emitter => {
        subscribeToScreenfullChange(emitter);

        return () => {
            // TODO unsubscribe function
        }
    });
};

const screenfullEventsListenerSaga = function* () {
    const screenFullEvents = yield call(screenfullEventsChannel);
    try {
        while (true) {
            const action = yield take(screenFullEvents);
            yield put(action);
        }
    } finally {
        // finished
    }
};

const watchSagas = function* () {
    yield takeEvery(CLOSE_FULLSCREEN, closeFullScreenSaga);
    yield takeEvery(FULLSCREEN_CHANGED, fullscreenChangedSaga);

    yield spawn(screenfullEventsListenerSaga);
};

export default watchSagas;
