import * as React from 'react';
import { RefObject } from 'react';

import AudioPlayerBar from './AudioPlayerBar';

interface IProps {
    currentTime: number;
    duration: number;

    setClickedTime: (clickedTime: number) => void;
}

class AudioPlayerBarContainer extends React.Component<IProps> {

    constructor(props) {
        super(props);
        this.audioPlayerBarRef = React.createRef();
    }

    render() {
        const { currentTime, duration } = this.props;
        const currentPercentage = (currentTime / duration) * 100;

        return (
            <AudioPlayerBar
                currentPercentage={currentPercentage}
                audioPlayerBarRef={this.audioPlayerBarRef}
                onMouseDown={e => this.handleTimeDrag(e)}
            />
        );

    }

    componentWillUnmount(): void {
        document.removeEventListener("mouseup", () => {
            document.removeEventListener("mousemove", this.updateTimeOnMove);
        });
    }

    private readonly audioPlayerBarRef: RefObject<HTMLDivElement>;

    private calcClickedTime = (e: React.MouseEvent) => {
        const { current } = this.audioPlayerBarRef;

        if (!current) {
            return;
        }

        const clickPositionInPage = e.pageX;
        const barStart = current.getBoundingClientRect().left + window.scrollX;
        const barWidth = current.offsetWidth;
        const clickPositionInBar = clickPositionInPage - barStart;
        const timePerPixel = this.props.duration / barWidth;

        return timePerPixel * clickPositionInBar;
    }

    private handleTimeDrag = (e: React.MouseEvent) => {
        const clickedTime = this.calcClickedTime(e);

        if (clickedTime) {
            e.persist();
            this.props.setClickedTime(clickedTime);
        }

        document.addEventListener("mousemove", this.updateTimeOnMove);

        document.addEventListener("mouseup", () => {
            document.removeEventListener("mousemove", this.updateTimeOnMove);
        });
    }

    private updateTimeOnMove = eMove => {
        const clickedTime = this.calcClickedTime(eMove);

        if (clickedTime) {
            this.props.setClickedTime(clickedTime);
        }
    };
}

export default AudioPlayerBarContainer;
