import styled from "styled-components";
import { SelectedButtonColor } from "./types";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface ISelectionButtonProps {
    selected?: boolean;
    selectedButtonColor?: SelectedButtonColor;
    className?: string;
}

export const SelectionButton = withCustomClasses<ISelectionButtonProps>(styled.div<ISelectionButtonProps>`
    margin: 10px;
    border-radius: 3px;
    cursor: pointer;
    position: relative;
    
    & .label {
        padding: 10px 30px;
        display: flex;
        text-align: center;
        font-size: 16px;
        
        &.nowrap {
            white-space: nowrap;
        }
    }
    
    & .checkedIcon {
        right: -15px;
        bottom: 0;
        position: absolute;
    }
    ${props => `
        border: 1px solid ${props.theme.colors.messageEditorTextColor};
        color:  ${props.theme.colors.messageEditorTextColor};
        font-family: ${props.theme.typography.fontFamily};
        font-weight: ${props.theme.typography.weight.regular};
    `}
    
    ${props => {
        if (props.selected) {
           const { colors: { selectedButton }} = props.theme;
           
           const backgroundColor = props.selectedButtonColor === SelectedButtonColor.BLACK
               ? selectedButton.blackBackground
               : selectedButton.defaultBackground;
           
           const backgroundHoverColor = props.selectedButtonColor === SelectedButtonColor.BLACK
               ? selectedButton.blackBackgroundHover
               : selectedButton.defaultBackgroundHover;
           
           return `
                background-color: ${backgroundColor};
                color: ${selectedButton.text};
                border-color: inherit;
                
                &:hover {
                    background-color: ${backgroundHoverColor};
                    & > span > svg {
                        fill: ${backgroundHoverColor};
                    }
                }
                
                & > span > svg {
                    position: relative;
                    width: 32px;
                    height: 32px;
                    stroke-width: 1px;
                    stroke: ${selectedButton.text};
                    fill: ${backgroundColor};
                }
           `;
        }
        
        return '';
    }}

	@media(max-width: 660px) {
		margin: 5px;
	}
`, [DomCustomClass.SelectionButton]);

export const SelectionButtons = withCustomClasses(styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
`, [DomCustomClass.SelectionButtons]);
