import {get} from 'lodash';
import * as React from 'react';
import {AnswerType, ICurrency, IQuestion, ITranslation, KnownAnswerType, QuestionType} from '../../../../redux/store';
import {translationWithValues} from "../../../../utils/utils";
import Timer from "../../../common/Timer/Timer";
import {UserPromptTitle as UserPromptTitleWrapper} from "../style";

export interface IUserPromptTitleProps {
    question: IQuestion;
    currency: ICurrency;
    translation: ITranslation;
    
    onAnswer: (answer: AnswerType) => any;
}

const GetTitle = (props: IUserPromptTitleProps) => {
    const { question : { type, payload }} = props;
    const modelname = payload && payload.modelName;

    const { exitLoad, autoRecharge, voyeurToSingleInfo, closeConfirmation1,
        closeConfirmation2, privateChat, chooseCamera
    } = props.translation.chat;
    
    const privateChatText = privateChat || '';

    const questions: {[questionType: string]: string} = {};
    questions[QuestionType.LOAD_ACCOUNT] = exitLoad;
    questions[QuestionType.LOAD_ACCOUNT_ONE_CLICK] = exitLoad;
    questions[QuestionType.LOAD_ACCOUNT_AUTO_RECHARGE] = autoRecharge;
    questions[QuestionType.VERIFY_AGE] = props.translation.chat.needAvs;
    questions[QuestionType.SPY_PRIVATE_CHAT] = `${voyeurToSingleInfo}`;
    questions[QuestionType.CLOSE_CHAT] = `${closeConfirmation1} \n ${closeConfirmation2}`;
    questions[QuestionType.INVITE_PRIVATE_CHAT] = privateChatText;
    questions[QuestionType.CHOOSE_CAMERA] = `${chooseCamera}:`;

    const closeChatTitle = (
        <>
            {translationWithValues(questions[type], { modelname })}
        </>
    );

    const loadAutoRechargeTitle = (
        <>
            <Timer totalTime={3} onTimeIsOver={() => props.onAnswer(KnownAnswerType.OK)}/>
            <div>
                {translationWithValues(questions[type], {
                    amount: payload && `${payload.defaultRecharge}${props.currency.symbol}`
                })}
            </div>
        </>
    );

    const defaultTitle = (
        <>
            {get(questions, type, "")}
        </>
    );

    const titles: {[ type: string]: any} = {};
    titles[QuestionType.SPY_PRIVATE_CHAT] = closeChatTitle;
    titles[QuestionType.CLOSE_CHAT] = closeChatTitle;
    titles[QuestionType.LOAD_ACCOUNT_AUTO_RECHARGE] = loadAutoRechargeTitle;

    return titles[type] || defaultTitle;
};

const UserPromptTitle = (props: IUserPromptTitleProps) => (
    <UserPromptTitleWrapper>
        <GetTitle {...props} />
    </UserPromptTitleWrapper>
);

export default UserPromptTitle;