import { isEmpty } from 'lodash';
import { Action } from 'redux-actions';
import { ACTION } from 'vxcontrol-client-lib';
import { put, select, takeEvery } from 'redux-saga/effects';

import types from './types';
import utils from '../../utils';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import ApiService from '../../services/ApiService';
import { setChat } from '../../redux/modules/session';
import { openVideochat, setInitialResolution } from '../../redux/modules/videochat';
import { SubActionType, IChatResponse } from './types/types';
import { ActionIds } from '../../redux/interfaces';

const createChatActionType = createActionType(ActionIds.CHAT_SAGA_WEBSOCKETS, { isSagaAction: true });

// Actions
const REQUEST_CHAT = createChatActionType("REQUEST_CHAT");
const RECEIVE_CHAT = createChatActionType("RECEIVE_CHAT");

// Action Creators
export const requestChat = createAction<SubActionType>(REQUEST_CHAT);
export const receiveChat = createAction<IChatResponse>(RECEIVE_CHAT);

export const requestChatSaga = function*({ payload }: Action<IPayload<SubActionType>>) {
    const { instanceId, value } = payload;

    const sessionID: string = yield select(selectors.getSessionId, instanceId);
    const channelId: string = yield select(selectors.getChannelId, instanceId);
    const hasFreeChatDuration = yield select(selectors.hasFreeChatDuration, instanceId);

    let subaction = value || types.SubActionType.STARTVIDEOCHAT;
    if (hasFreeChatDuration && value === types.SubActionType.STARTVIDEOCHAT) {
        subaction = types.SubActionType.STARTFREECHAT;
    }

    const command = {
        action: ACTION.CMDP_QUERYBACKEND,
        params: {
            sessionID,
            subaction,
            channelId
        }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

export const receiveChatSaga = function*({ payload }: Action<IPayload<IChatResponse>>) {
    const { instanceId, value } = payload;
    if (isEmpty(value)) {
        return;
    }

    // Add the chat
	yield put(setChat(instanceId, utils.getChatFromChatResponse(value)));

	const resolution = utils.getResolutionFromChatResponse(value);
	if (resolution.width > 0 && resolution.height > 0) {
		yield put(setInitialResolution(instanceId, resolution));
	}

    // Open the videochat
    yield put(openVideochat(instanceId));
};

const watchSagas = function*() {
    yield takeEvery(REQUEST_CHAT, requestChatSaga);
    yield takeEvery(RECEIVE_CHAT, receiveChatSaga);
};

export default watchSagas;

