import * as React from 'react';
import { LightBoxWrapper } from './style';
import CloseButtonContainer, { CloseButtonType } from '../CloseButton/CloseButtonContainer';
import { IModalContent } from '../../../redux/store';

interface IProps {
    modalContent: IModalContent;
    closeLightbox: any;
}

const FullScreenLightBox = (props: IProps) => (
    <LightBoxWrapper onClick={props.closeLightbox}>

        <img
            src={props.modalContent.imgSrc}
            srcSet={props.modalContent.imgSrcSet}
            width={props.modalContent.imgWidth}
        />

        <CloseButtonContainer
            closeButtonType={CloseButtonType.IMAGE_MODAL}
            hasCloseButton={true}
            onClick={props.closeLightbox}
        />

    </LightBoxWrapper>
);

export default FullScreenLightBox;
