import styled from 'styled-components';
import config from '../../static/config';
import {IViewProviderProps} from '../interfaces';

export const FullscreenViewWrapper = styled.div`
   overflow: hidden;
   display: flex;
   flex-direction: column;

   .chatbox-wrapper {
        ${(props: IViewProviderProps) => props.isFullscreen && 'position: absolute;'}
        
        max-height: ${(props: IViewProviderProps) => (props.videoChatHeight) 
            ? `${Math.round(props.videoChatHeight * 0.95) - config.viewSizes.fullScreen.topBarHeight}px` 
            : 'auto'
        };
        bottom: ${config.viewSizes.fullScreen.chatBoxBottomOffset}px;
        padding-bottom: 10px;
        
        display: flex;
        flex-direction: column;
        flex: 1;
        width: 35%;
        background: none;
        right: 0;
    }
`;

export const VideoChatContainerWrapper = styled.div`
    background-color: #151617;
    width: ${(props: IViewProviderProps) => `${props.containerWidth}px`};
    height: ${(props: IViewProviderProps) => `${props.videoChatHeight}px`};
`;
