import styled from 'styled-components';
import { BaseButtonContent } from '../BaseButton/style';
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

export enum DialogButtonColor {
    GREEN = "GREEN",
    BLUE = "BLUE",
    BLACK = "BLACK"
};

export interface IDialogButtonWrapperProps {
    color: DialogButtonColor;
};

const getRgbColor = (props: IDialogButtonWrapperProps): string => {
    switch (props.color) {
        case DialogButtonColor.GREEN:
            return '#43B31C';
        case DialogButtonColor.BLUE:
            return '#3B8EE8';
        default:
            return '#3E3F40';
    }
};

const getRgbHoverColor = (props: IDialogButtonWrapperProps): string => {
    switch (props.color) {
        case DialogButtonColor.GREEN:
            return '#66E03B';
        case DialogButtonColor.BLUE:
            return '#3B8EE8';
        default:
            return '#626365';
    }
};

export const DialogButtonWrapper = withCustomClasses<IDialogButtonWrapperProps>(styled.button<IDialogButtonWrapperProps>`
    ${(props) => `
        background-color: ${getRgbColor(props)};
        border: 1px solid ${getRgbColor(props)};
        
        &:hover {
            background-color: ${getRgbHoverColor(props)};
            border-color:${getRgbHoverColor(props)};
        }
    `}
    
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-weight: ${(props) => props.theme.typography.weight.regular};
    font-size: 16px;
    
    height: 44px;
    border-radius: 3px;
    cursor: pointer;
    transition: all .15s linear;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFFFFF;
    padding: 5px 11px;
    text-align: center;

    i {
        position: relative;
        display: inline-block;
        font-size: 22px;
        padding: 0 5px;
    }
`, [DomCustomClass.DialogButton]);

export const DialogButtonContent = styled(BaseButtonContent)`    
    width: auto;
    text-transform: none;
`;
