import * as React from 'react';
import { ICurrency, IGift, ITranslation } from '../../../redux/store';
import utils from '../../../utils';
import Gift from '../Gift';
import { GiftBundlePageTitle, GiftPageWrapper, GiftsWrapper } from './style';

interface IProps {
    page: IGift[];
    activePage: number;
    currency: ICurrency;
    translation: ITranslation;
    isBundleGiftPage: boolean;
    title?: React.ReactElement;

    onGiftClick: (giftIcon: string) => () => void;
}

const GiftPage = (props: IProps) => (
    <GiftPageWrapper>
        {props.title &&
            <GiftBundlePageTitle>{props.title}</GiftBundlePageTitle>
        }

        <GiftsWrapper>
            {props.page.map((gift: IGift, index) =>
                <Gift
                    key={index}
                    title={gift.name}
                    url={gift.imageUrl}
                    price={gift.price}
                    initialPrice={gift.initialPrice}
                    quantity={gift.currentQuantity}
                    isBundleGiftPage={props.isBundleGiftPage}
                    icon={utils.getGiftEncoding(gift)}
                    currency={props.currency.symbol}
                    translation={props.translation}
                    onGiftClick={props.onGiftClick}
                />
            )}
        </GiftsWrapper>
        
    </GiftPageWrapper>
);


export default GiftPage;
