import * as moment from "moment";
import { Action } from "redux-actions";
import { put, race, select, spawn, take, takeEvery } from 'redux-saga/effects';

import selectors from "../../redux/selectors";
import { IPayload } from "../../redux/interfaces";
import { CANCEL_STARTING_VIDEOCHAT_TYPE } from "./videochat";
import { closeCountdownWarning, openCountdownWarning } from "../../redux/modules/messenger";
import { setInformation, SET_VIDEOCHAT_TIME_LIMIT, SET_TYPE } from "../../redux/modules/videochat";
import { CountdownWarningType, VideochatInformationType } from "../../redux/store";
import {takePattern} from "../utils";

export const videoChatLimitSaga = function* (action: Action<IPayload>) {
    yield spawn(openLiveChatWarningSaga, action);
    yield spawn(openLiveChatInformationDialogSaga, action);
};

/** We receive how many seconds are left in this chat session and, if it's the case, open the dialog */
const openLiveChatInformationDialogSaga = function* ({ payload }: Action<IPayload<number>>) {
    const { instanceId, value } = payload;
    const {
        OUT_OF_MONEY, OUT_OF_MONEY_ONE_CLICK_RECHARGE, THANK_YOU, THANK_YOU_ONE_CLICK_RECHARGE
    }  = VideochatInformationType;

    const isVideoChatInSpendingMode = yield select(selectors.isVideoChatInSpendingMode, instanceId);
    if (!isVideoChatInSpendingMode) {
        return;
    }

    let isOneClickRechargeAvailable: boolean;

    if (value <= 0) {
        // no time (and implicitly money) left
        isOneClickRechargeAvailable = yield select(selectors.isOneClickRechargeAvailable, instanceId);

        yield put(setInformation(instanceId,
            isOneClickRechargeAvailable ? OUT_OF_MONEY_ONE_CLICK_RECHARGE : OUT_OF_MONEY
        ));
    } else {
        const previousTimeLeft = yield select(selectors.getVideoChatPreviousTimeLeft, instanceId);

        if (previousTimeLeft <= 0) {
            // we can safely assume that a recharge happened after the stream was interrupted for out of money reasons
            isOneClickRechargeAvailable = yield select(selectors.isOneClickRechargeAvailable,instanceId);

            yield put(setInformation(instanceId,
                isOneClickRechargeAvailable ? THANK_YOU_ONE_CLICK_RECHARGE : THANK_YOU
            ));
        }
    }
};

const openLiveChatWarningSaga = function* ({ payload }: Action<IPayload<number>>) {
    const { instanceId, value } = payload;
    const videoChatType = yield select(selectors.getVideoChatType, instanceId);

    // if there is no video chat type set yet, then
    // wait for the video chat to either start or fail,
    // otherwise we don't know if we are in spending mode (aka money involved) or not
    if (!videoChatType) {
        yield race({
            success: take(takePattern(SET_TYPE, instanceId)),
            fail: take(takePattern(CANCEL_STARTING_VIDEOCHAT_TYPE, instanceId))
        });
    }

    const isVideoChatInSpendingMode = yield select(selectors.isVideoChatInSpendingMode, instanceId);
    if (!isVideoChatInSpendingMode) {
        return;
    }

    const isTimeLeftBelowThreshold = yield select(selectors.isVideoChatTimeLeftBelowThreshold, instanceId);
    if (!isTimeLeftBelowThreshold || value <= 0) {
        const countdownWarning = yield select(selectors.getCountdownWarning, instanceId);
        if (countdownWarning) {
            yield put(closeCountdownWarning(instanceId));
        }
        return;
    }

    const firstMoment = moment();
    const isOneClickRechargeAvailable = yield select(selectors.isOneClickRechargeAvailable, instanceId);
    const adjustment = moment().diff(firstMoment, 'seconds');

    yield put(openCountdownWarning(instanceId, {
        time: value - adjustment,
        type: isOneClickRechargeAvailable
            ? CountdownWarningType.ONE_CLICK_OUT_OF_BALANCE
            : CountdownWarningType.OUT_OF_BALANCE
    }));
};

const watchSagas = function* () {
    yield takeEvery(SET_VIDEOCHAT_TIME_LIMIT, videoChatLimitSaga);
};

export default watchSagas;
