import * as React from 'react';

import WindowBox from "./WindowBox";
import IStore from "../../../redux/store";
import selectors from "../../../redux/selectors";
import { IInstanceProps } from "../../hoc/withInstanceInfo";
import withRedux from "../../hoc/withRedux";

interface IComponentProps {
    children: any;
    isOpen: boolean;
    hideHeader?: boolean;
    isWindowForUserPrompt?: boolean;
    customCloseIcon?: JSX.Element;
    title?: string | JSX.Element;

    handleClose: () => void;
}

interface IStoreProps {
    isResponsive: boolean;
    isFullScreen: boolean;
	shouldUseDropdownAnimation: boolean;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

const WindowBoxContainer = (props: IProps) => (
    <WindowBox
        isWindowForUserPrompt={props.isWindowForUserPrompt}
        customCloseIcon={props.customCloseIcon}
        hideHeader={props.hideHeader}
        isOpen={props.isOpen}
        isResponsive={props.isResponsive}
        title={props.title}
        handleClose={props.handleClose}
        isFullScreen={props.isFullScreen}
        shouldUseDropdownAnimation={props.shouldUseDropdownAnimation}
    >
        {props.children}
    </WindowBox>
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isResponsive: selectors.isResponsive(store, instanceId),
    isFullScreen: selectors.isFullScreen(store, instanceId),
	shouldUseDropdownAnimation: selectors.shouldUseDropdownAnimation(store, instanceId)
});

export default withRedux<IComponentProps>(WindowBoxContainer, mapStateToProps);
