import { produce } from "immer";
import { AnyAction } from "redux";
import { handleActions, Action } from "redux-actions";

import {RESET_STORE} from "./app";
import { createAction, createActionType } from "../utils";
import { getInitialInstanceData } from "../initialStore";
import { IModel, IPartner, IOnlineState, IModelPrices } from "../store";
import { ActionIds } from "../interfaces";

const createModelActionType = createActionType(ActionIds.MODEL, { isReduxAction: true });

// Actions
export const SET_PARTNER = createModelActionType("SET_PARTNER");
export const RESET_MODEL = createModelActionType("RESET_MODEL");
export const SET_FAVORITE = createModelActionType("SET_FAVORITE");

export const SET_MODEL_NAME = createModelActionType("SET_MODEL_NAME");
export const RESET_MODEL_NAME = createModelActionType("RESET_MODEL_NAME");

export const SET_MOTTO = createModelActionType("SET_MOTTO");
export const RESET_MOTTO = createModelActionType("RESET_MOTTO");

export const SET_MODEL_PRICES = createModelActionType("SET_MODEL_PRICES");
export const SET_MESSAGE_PRICE = createModelActionType("SET_MESSAGE_PRICE");

export const SET_PROFILE_PICTURE = createModelActionType("SET_PROFILE_PICTURE");
export const RESET_PROFILE_PICTURE = createModelActionType("RESET_PROFILE_PICTURE");

export const SET_MODEL_ONLINE_STATE = createModelActionType("SET_MODEL_ONLINE_STATE");
export const SET_IS_ONLINE = createModelActionType("SET_IS_ONLINE");
export const SET_CHAT_ABILITY = createModelActionType("SET_CHAT_ABILITY");
export const SET_LIVE_PREVIEW_ABILITY = createModelActionType("SET_LIVE_PREVIEW_ABILITY");
export const SET_PRIVATE_CHAT_ABILITY = createModelActionType("SET_PRIVATE_CHAT_ABILITY");
export const SET_TIP_ABILITY = createModelActionType("SET_TIP_ABILITY");

export const SET_FREE_MESSAGES = createModelActionType("SET_FREE_MESSAGES");
export const SET_FREE_CHAT_DURATION = createModelActionType("SET_FREE_CHAT_DURATION");
export const RESET_FREE_CHAT_DURATION = createModelActionType("RESET_FREE_CHAT_DURATION");

// Action Creators
export const resetModel = createAction(RESET_MODEL);
export const setPartner = createAction<IPartner | undefined>(SET_PARTNER);
export const setProfilePicture = createAction<string>(SET_PROFILE_PICTURE);
export const resetProfilePicture = createAction(RESET_PROFILE_PICTURE);

export const setModelName = createAction<string>(SET_MODEL_NAME);
export const resetModelName = createAction(RESET_MODEL_NAME);

export const setModelPrices = createAction<IModelPrices>(SET_MODEL_PRICES);
export const setMessagePrice = createAction<number>(SET_MESSAGE_PRICE);
export const setFavorite = createAction<boolean>(SET_FAVORITE);

export const setMotto = createAction<string>(SET_MOTTO);
export const resetMotto = createAction(RESET_MOTTO);

export const setModelOnlineState = createAction<IOnlineState>(SET_MODEL_ONLINE_STATE);
export const setIsOnline = createAction<boolean>(SET_IS_ONLINE);
export const setChatAbility = createAction<boolean>(SET_CHAT_ABILITY);
export const setLivePreviewAbility = createAction<boolean>(SET_LIVE_PREVIEW_ABILITY);
export const setPrivateChatAbility = createAction<boolean>(SET_PRIVATE_CHAT_ABILITY);
export const setTipAbility = createAction<boolean>(SET_TIP_ABILITY);

export const setFreeModelMessages = createAction<number>(SET_FREE_MESSAGES);
export const setModelFreeChatDuration = createAction<number>(SET_FREE_CHAT_DURATION);
export const resetModelFreeChatDuration = createAction(RESET_FREE_CHAT_DURATION);

// Reducer
const model = handleActions({
    [RESET_STORE]: (state: IModel, action: AnyAction) => getInitialInstanceData().model,

    [RESET_MODEL]: produce((draft: IModel, action: AnyAction) => ({
        ...getInitialInstanceData().model,
        partner: draft.partner
    })),

    [SET_PROFILE_PICTURE]: produce((draft: IModel, { payload }: Action<string>) => {
        draft.profilePicture = payload;
    }),

    [RESET_PROFILE_PICTURE]: produce((draft: IModel, action: AnyAction) => {
        draft.profilePicture= getInitialInstanceData().model.profilePicture;
    }),

    [SET_MODEL_NAME]: produce((draft: IModel, { payload }: Action<string>) => {
        draft.name = payload;
    }),

    [RESET_MODEL_NAME]: produce((draft: IModel, { payload }: Action<string>) => {
        draft.name= getInitialInstanceData().model.name;
    }),

    [SET_MODEL_PRICES]: produce((draft: IModel, { payload }: Action<IModelPrices>) => {
        draft.prices = payload;
    }),

    [SET_MESSAGE_PRICE]: produce((draft: IModel, { payload }: Action<number>) => {
        draft.prices.messagePrice = payload;
    }),

    [SET_PARTNER]: produce((draft: IModel, { payload }: Action<IPartner>) => {
        draft.partner = payload;
    }),

    [SET_FAVORITE]: produce((draft: IModel, { payload }: Action<boolean>) => {
        draft.isFavorite = payload;
    }),

    [SET_MOTTO]: produce((draft: IModel, { payload }: Action<string>) => {
        draft.motto = payload;
    }),

    [RESET_MOTTO]: produce((draft: IModel, action: AnyAction) => {
        draft.motto= getInitialInstanceData().model.motto;
    }),

    [SET_MODEL_ONLINE_STATE]: produce((draft: IModel, { payload }: Action<IOnlineState>) => {
        draft.onlineState = payload;
    }),

    [SET_IS_ONLINE]: produce((draft: IModel, { payload }: Action<boolean>) => {
        draft.isOnline = payload;
    }),

    [SET_CHAT_ABILITY]: produce((draft: IModel, { payload }: Action<boolean>) => {
        draft.onlineState = draft.onlineState || {};
        draft.onlineState.supportsChat = payload;
    }),

    [SET_LIVE_PREVIEW_ABILITY]: produce((draft: IModel, { payload }: Action<boolean>) => {
        draft.onlineState = draft.onlineState || {};
        draft.onlineState.supportsPreviewChat = payload;
    }),

    [SET_PRIVATE_CHAT_ABILITY]: produce((draft: IModel, { payload }: Action<boolean>) => {
        draft.onlineState = draft.onlineState || {};
        draft.onlineState.supportsPrivateChat = payload;
    }),

    [SET_TIP_ABILITY]: produce((draft: IModel, { payload }: Action<boolean>) => {
        draft.onlineState = draft.onlineState || {};
        draft.onlineState.supportsTip = payload;
    }),

    [SET_FREE_MESSAGES]: produce((draft: IModel, { payload }: Action<number>) => {
        draft.freeMessages = payload;
    }),

    [SET_FREE_CHAT_DURATION]: produce((draft: IModel, { payload }: Action<number>) => {
        draft.freeChatDuration = payload;
    }),

    [RESET_FREE_CHAT_DURATION]: produce((draft: IModel, action: AnyAction) => {
        draft.freeChatDuration = 0;
    })
}, getInitialInstanceData().model);

export default model;
