import styled from 'styled-components';
import config from '../../../static/config'
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface IBaseButtonWrapperProps {
    isResponsive?: boolean;
    isFullScreen?: boolean;
    isForActionSection?: boolean;
}

export interface IBaseButtonProps {
    isSelected?: boolean
}

export const BaseButtonWrapper = withCustomClasses<IBaseButtonWrapperProps>(styled.div<IBaseButtonWrapperProps>`
    font-family: ${(props) => props.theme.typography.fontFamily};
	font-weight: ${(props) => props.theme.typography.weight.medium};
	
    ${(props: IBaseButtonWrapperProps) => props.isFullScreen 
        && `max-width: ${config.viewSizes.maxButtonWidthInFullscreen}px;`}
    ${(props: IBaseButtonWrapperProps) => props.isForActionSection && 'max-width: 50%;'}
    
	font-size: 16px;
	line-height: 15px;
    display: inline-block;
    clear: both;
    width: 100%;
    box-sizing: border-box;
`, [DomCustomClass.ActionSectionBaseButton]);

export const BaseButton = styled.button<IBaseButtonProps>`
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-weight: ${(props) => props.theme.typography.weight.regular};
    
    font-size: 12px;
    height: 44px;
    border-radius: 3px;
    cursor: pointer;
    transition: all .15s linear;
    display: flex;
    align-items: center;
    justify-content: center;
`;

export const BaseButtonContent = styled.span`
    text-align: left;
    display: inline-block;
    text-transform: uppercase;
    font-family: "Roboto Condensed",sans-serif;
`;

const BaseRowText = styled.span`
    text-align: left;
    display: block;
`;

export const FirstRowText = styled(BaseRowText)`
    font-size: 16px;
    white-space: nowrap;
`;

export const SingleRowText = styled(BaseRowText)`
    font-size: 14px;
`;

export const SecondRowText = styled(BaseRowText)`
    font-size: 11px;
`;

export const VipSingleRowText = styled(BaseRowText)`
    font-weight: ${props => props.theme.typography.weight.medium};
    
    font-size: 16px;
    line-height: 15px;
    width: min-content;
`;
