import * as React from 'react';

import Preloader from "./Preloader";
import selectors from '../../../redux/selectors';
import IStore, { ITranslation } from "../../../redux/store";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    shouldRenderPreloader: boolean;
    hasPreloaderPaymentText?: boolean;
    preloader?: string;
    translation: ITranslation
}

interface IComponentProps {
	shouldAdjustVertically?: boolean;
}

type IProps = IStoreProps & IInstanceProps & IComponentProps;

const PreloaderContainer = (props: IProps) => {
	const { freeConnection, billingStart } = props.translation.chat;

    return (
        <>
            {props.shouldRenderPreloader &&
                <Preloader
					title={freeConnection}
					subtitle={billingStart}
					preloader={props.preloader}
					shouldAdjustVertically={props.shouldAdjustVertically}
					hasPreloaderPaymentText={props.hasPreloaderPaymentText}
                />
            }
        </>
    );
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	preloader: selectors.getPreloader(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    shouldRenderPreloader: selectors.shouldRenderPreloader(store, instanceId),
    hasPreloaderPaymentText: selectors.hasPreloaderPaymentText(store, instanceId)
});

export default withRedux(PreloaderContainer, mapStateToProps);
