import styled from "styled-components";

interface ITimerWrapperProps {
    leftAngle: number;
    rightAngle: number;
}

export interface ITimerStylingProps {
    className?: string;
    size: number;
    thickness: number;
    filledColor: string;
    unfilledColor: string;
    backgroundColor: string;
    timerTextColor: string;
}

type Props = ITimerWrapperProps & ITimerStylingProps;

/**
 * Styling for a stateless component to display a stopwatch like animated circle
 */
export const TimerWrapper = styled.div.attrs({ className: "timeAnimatedCircle" })<Props>` 
    ${props => {

        return `
            width: ${props.size}px;
            height: ${props.size}px;
            min-width: ${props.size}px;
            min-height: ${props.size}px;
            position: relative;
            
            & .counter {
                position: absolute;
                top: 50%;
                left: 50%;
                z-index: 4;
                transform: translate(-50%, -50%);
                color: ${props.timerTextColor};
                font-family: ${props.theme.typography.fontFamily};
                font-weight: ${props.theme.typography.weight.regular};
                font-size: 24px;
            }
            
            & .outer-shadow, .inner-shadow {
                z-index: 4;
                position: absolute;
                width: 100%;
                height: 100%;
                border-radius: 100%;
            }
            
            & .inner-shadow {
                top: 50%;
                left: 50%;
                width: ${props.size - 2 * props.thickness}px;
                height: ${props.size - 2 * props.thickness}px; 
                margin-left: ${-props.size/2 + props.thickness}px;
                margin-top: ${-props.size/2 + props.thickness}px;
                background-color: ${props.backgroundColor};
            }
            
            & .hold {
                position: absolute;
                width: 100%;
                height: 100%;
                clip: rect(0px, ${props.size}px, ${props.size}px,  ${props.size / 2}px);
                border-radius: 100%;
                background-color: ${props.unfilledColor};
            }
            
            & .fill {
                background-color: ${props.filledColor};
                position: absolute;
                width: 100%;
                height: 100%;
                border-radius: 100%;
                clip: rect(0px, ${props.size/2}px, ${props.size}px, 0px);
            }
            
            & .left .fill {
                z-index: 1;
                transform: rotate(${props.leftAngle}deg);
            }

            .right {
                z-index: 3;
                -webkit-transform: rotate(180deg);
                -moz-transform: rotate(180deg);
                transform: rotate(180deg);
            }
            
            .right .fill {  
                z-index: 3;
                -webkit-transform: rotate(${props.rightAngle}deg);
                -moz-transform: rotate(${props.rightAngle}deg);
                transform: rotate(${props.rightAngle}deg);

            }
        `
    }}
`;
