import * as React from 'react';
import { IModel } from '../../../redux/store';
import { MobileProfileInfoWrapper, MobileProfileName, MobileProfileOnline } from './style';
import CloseButtonContainer, { CloseButtonType } from '../../common/CloseButton/CloseButtonContainer';

interface IProps {
    model: IModel;
    closeText: string;
    closeMessenger: () => void;
}

const MobileProfileInfo = (props: IProps) => (
    <MobileProfileInfoWrapper>
        <MobileProfileOnline />

        <MobileProfileName>
            {props.model.name}
        </MobileProfileName>

        <CloseButtonContainer
            onClick={props.closeMessenger}
            hoverText={props.closeText}
            closeButtonType={CloseButtonType.MOBILE_HEADER}
        />

    </MobileProfileInfoWrapper>
);

export default MobileProfileInfo;
