import * as React from 'react';
import { Component } from 'react';
import { isEqual } from 'lodash';

import MentionSection from './MentionSection';
import IStore from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import selectors from '../../../redux/selectors';
import withRedux from '../../hoc/withRedux';
import {
	closeMentionSection,
	resetFocusedMentionOption,
	setActiveMention,
	setFocusedMentionOption
} from '../../../redux/modules/messenger';
import { IProps, IStateOption } from "./types";
import constants from "../../../static/constants";
import { getNextOption, getOptions, getPreviousOption, scrollTo } from "./utils";
import { getStylesForMentionSectionContainer } from "./style";

interface IState {
	options: IStateOption[];
}

class MentionSectionContainer extends Component<IProps, IState> {

	constructor(props: IProps) {
		super(props);

		this.elemRef = React.createRef<HTMLElement>();

		const options: IStateOption[] = getOptions(props.mentionOptions, props.mentionQuery);

		this.state = {
			options
		}
	}

	private readonly elemRef: any;

	private isMouseOver?: boolean;

	private mouseFocusedOptionId?: string;
	private keyboardFocusedOptionId?: string;

	componentDidMount() {
		document.addEventListener("keydown", this.handleKeyDown);

		const element = this.elemRef.current;
		if (element) {
			element.addEventListener("mouseenter", this.handleMouseEnter);
			element.addEventListener("mouseleave", this.handleMouseLeave);
		}
	}

	shouldComponentUpdate(nextProps:IProps, nextState: IState) {
		const props = this.props;

		if (!isEqual(nextProps.focusedMentionOption, this.props.focusedMentionOption)) {
			this.keyboardFocusedOptionId = nextProps.focusedMentionOption;
			scrollTo(this.elemRef, this.keyboardFocusedOptionId);
		}

		// take care to not re-render if only "focusedMentionOption" was change,
		// otherwise this dumb react-select will do a scroll-up
		return (!isEqual(nextProps, props) && isEqual(nextProps.focusedMentionOption, props.focusedMentionOption))
			|| !isEqual(nextState, this.state);
	}

	componentWillUnmount() {
		document.removeEventListener("keydown", this.handleKeyDown);

		const element = this.elemRef.current;
		if (element) {
			element.removeEventListener("mouseenter", this.handleMouseEnter);
			element.removeEventListener("mouseleave", this.handleMouseLeave);
		}
	}

	componentDidUpdate(prevProps: IProps, prevState: IState) {
		const mentionsChanged = !isEqual(prevProps.mentionOptions, this.props.mentionOptions);
		const queryChanged = !isEqual(prevProps.mentionQuery, this.props.mentionQuery)
		if (mentionsChanged || queryChanged) {

			const options: IStateOption[] =
				getOptions(this.props.mentionOptions, this.props.mentionQuery);

			this.setState({
				options
			});
		}

		if (!isEqual(prevProps.isMentionSectionOpen, this.props.isMentionSectionOpen) &&
			!this.props.isMentionSectionOpen) {
			this.props.resetFocusedMentionOption(this.props.instanceId);
		}
	}

    render() {
		return (
			<div ref={this.elemRef}
				style={getStylesForMentionSectionContainer({
					isOpen: this.props.isMentionSectionOpen,
					shouldDisplayCustomMentionSection: this.props.shouldDisplayCustomMentionSection,
					isFullScreen: this.props.isFullScreen
				})}>
				<MentionSection
					mentionOptions={this.state.options}
					isOpen={this.props.isMentionSectionOpen}
					onChange={this.onChange}
					inputValue={this.props.mentionQuery}
					isFullScreen={this.props.isFullScreen}
					shouldDisplayCustomMentionSection={this.props.shouldDisplayCustomMentionSection}
					onMouseFocus={this.handleMouseFocus}
				/>
			</div>
        );
    }


	/**
	 * Triggered when mouse enters over the options list
	 */
	private handleMouseEnter = () => {
		if (!this.isMouseOver) {
			this.props.resetFocusedMentionOption(this.props.instanceId);
		}
		this.isMouseOver = true;
	};

	/**
	 * Triggered when mouse leaves the options list
	 */
	private handleMouseLeave = () => {
		this.isMouseOver = false;

		if (this.mouseFocusedOptionId) {
			this.props.setFocusedMentionOption(this.props.instanceId, this.mouseFocusedOptionId);
			this.mouseFocusedOptionId = undefined;
		}
	};

	/**
	 * Triggered when mouse is focusing an option in the list
	 * @param id of the focused option
	 */
	private handleMouseFocus = (id: string) => {
		this.mouseFocusedOptionId = id;
	};

	/**
	 * Triggered when a key is pressed
	 * @param event
	 */
	private handleKeyDown = (event) => {
		const handlers = {};
		handlers[constants.ARROW_UP_KEY_CODE] = this.handleArrowUp;
		handlers[constants.ARROW_DOWN_KEY_CODE] = this.handleArrowDown;
		handlers[constants.ENTER_KEY_CODE] = this.handleEnter;

		if (this.props.isMentionSectionOpen && handlers[event.keyCode]) {
			handlers[event.keyCode]();
		}
	};

	/**
	 * Triggered when arrow up key is pressed
	 */
	private handleArrowUp = () => {
		// focus previous option
		const previousOption = getPreviousOption(this.state.options, this.keyboardFocusedOptionId);

		if (previousOption) {
			this.props.setFocusedMentionOption(this.props.instanceId, previousOption);
		}
	};

	/**
	 * Triggered when arrow down key is pressed
	 */
	private handleArrowDown = () => {
		// focus next option
		const nextOption = getNextOption(this.state.options, this.keyboardFocusedOptionId);

		if (nextOption) {
			this.props.setFocusedMentionOption(this.props.instanceId, nextOption);
		}
	};

	/**
	 * Triggered when Enter key is pressed
	 */
	private handleEnter = () => {
		const mention = this.state.options
			.find(option => option.id === this.keyboardFocusedOptionId);

		if (mention) {
			this.onChange(mention);
		}
	};

	private onChange = (mention) => {
		this.props.setActiveMention(this.props.instanceId, mention.value);
		this.props.closeMentionSection(this.props.instanceId);
	};
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    mentionOptions: selectors.getMentionOptions(store, instanceId),
    focusedMentionOption: selectors.getFocusedMentionOption(store, instanceId),
    isMentionSectionOpen: selectors.isMentionSectionOpen(store, instanceId),
    mentionQuery: selectors.getMentionQuery(store, instanceId),
    modelName: selectors.getModelName(store, instanceId),
	isFullScreen: selectors.isFullScreen(store, instanceId),
	shouldDisplayCustomMentionSection: selectors.shouldDisplayCustomMentionSection(store, instanceId)
});

const mapDispatchToProps = {
    closeMentionSection,
    setActiveMention,
	setFocusedMentionOption,
	resetFocusedMentionOption
};

export default withRedux(MentionSectionContainer, mapStateToProps, mapDispatchToProps);
