import styled from "styled-components";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface ITipImageProps {
    imageUrl: string;
    imageSize: number;
}

interface IUserPromptTipWrapperProps {
    isResponsive?: boolean;
}

export const UserPromptTipWrapper = withCustomClasses<IUserPromptTipWrapperProps>(styled.div<IUserPromptTipWrapperProps>`
    ${props => props.isResponsive && "margin-bottom: 30px;"}
    color: ${props => props.theme.colors.userPrompt.tipColor};
    
    & p {
        font-size: 12px;
    }

    text-align: center;
`, [DomCustomClass.UserPromptTips]);

export const UserPromptTipTitle = styled.div`
    font-family: ${props => props.theme.typography.fontFamily};
    font-weight: ${props => props.theme.typography.weight.bold};
    font-size: 18px;
    margin-bottom: 10px;
    margin-top: 15px;
`;

export const UserPromptTipSubTitle = styled(UserPromptTipTitle)`
    font-size: 15px;
`;

export const UserPromptTipButton = withCustomClasses(styled.div`
    font-family: ${props => props.theme.typography.fontFamily};
    font-weight: ${props => props.theme.typography.weight.regular};
    background-color: ${props => props.theme.colors.userPrompt.tipButtonBackgroundColor};
    color: ${props => props.theme.colors.userPrompt.tipButtonColor};
    
    &:hover {
        background-color: ${props => props.theme.colors.userPrompt.tipButtonBackgroundHoverColor};
    }
    
    display: inline-block;
    padding: 10px 15px;
    margin: 10px;
    border-radius: 5px;
    cursor: pointer;
    position: relative;
    border: 1px solid #888888;
    border-color: inherit;
    font-size: 16px;
    transition: all .15s linear;
`, [DomCustomClass.UserPromptTipButton]);

export const TipImage = styled.div<ITipImageProps>`
    background-image: url(${props => props.imageUrl});
    width: ${props => props.imageSize}px;
    height: ${props => props.imageSize}px;
    background-size: ${props => props.imageSize}px;
    
    background-position: center;
    margin: 0 auto;
`;

export const Text = styled.div`
	font-size: 15px;
`;

export const TipPrice = styled(Text)`
    color: ${props => props.theme.colors.userPrompt.tipPriceColor};
`;
