import styled from "styled-components";
import withCustomClasses from "../../hoc/withCustomClasses/withCustomClasses";
import {DomCustomClass} from "../../hoc/withCustomClasses";

interface IWrapper {
    isSelected: boolean;
}

export const Wrapper = withCustomClasses<IWrapper>(styled.div<IWrapper>`
    ${props => (
        props.isSelected
            ? `border: 1px solid ${props.theme.colors.cameraSelection.option.selectedColor};`
            : `border: 1px solid ${props.theme.colors.cameraSelection.option.defaultColor};`
    )}

    border-radius: 2px;
    
    width: 100%;
    min-height: 50px;
    
    display: flex;
    flex-direction: row;
    
    position: relative;
    margin: 10px 0;
    cursor: pointer;
`, [DomCustomClass.CameraOption]);

export const FrontBackWrapper = withCustomClasses<IWrapper>(styled(Wrapper)<IWrapper>`
    justify-content: center;
    flex-flow: column;
    
    margin: 10px;    
`, [DomCustomClass.CameraOption]);

export const IconWrapper = styled.div`
    font-size: 18px;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
`;

export const FrontBackIconWrapper = styled(IconWrapper)`
    flex-direction: row;
    
    font-size: 4em;
    padding: 0;
    margin: 20px 15% 10px 15%;
`;

export const LabelWrapper = styled.div`
    font-family: Roboto;
    text-transform: capitalize;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
	margin-right: 30px;
	font-size: 15px;
`;

export const FrontBackLabelWrapper = styled(LabelWrapper)`
    text-align: center;
    margin: 10px 15% 20px 15%;
`;

export const CheckedWrapper = styled.div`
    ${props => `
        color: ${props.theme.colors.cameraSelection.option.selectedColor};
    `}
    
    position: absolute;
    top: 15px;
    right: 10px;
    
    font-size: 15px;
`;
