import { isEmpty } from 'lodash';
import * as React from 'react';
import { AnswerType, ICurrency } from '../../../redux/store';
import SelectionButtons, { ISelectionButtonsProps } from "../SelectionButtons/SelectionButtons";

export interface IRechargeSelectionButtonsProps{
    amounts?: number[];
    defaultAmount?: number;
    currency: ICurrency;
    hasSubmit: boolean;
    submitLabel?: string;
    onSelect: (amount: number | AnswerType) => any;
};

const RechargeSelectionButtons = (props: IRechargeSelectionButtonsProps) => {
    if(!props.amounts || isEmpty(props.amounts)) {
        return null;
    }

    const selectionButtonProps: ISelectionButtonsProps<number> = {
        onSelect: props.onSelect,
        options: props.amounts.map((amount: number) => ({
            value: amount,
            label: `${amount} ${props.currency.symbol}`,
            selected: !isEmpty(props.defaultAmount) && props.defaultAmount=== amount
        }))
    };

    if (props.hasSubmit) {
        selectionButtonProps.submit = {
            label: props.submitLabel || ''
        };
    }

    return <SelectionButtons<number> {...selectionButtonProps}/>
};

export default RechargeSelectionButtons;
