import { isEqual } from "lodash";
import GA from "react-ga";

export enum AnalyticsCategories {
    MESSENGER = "Messenger",
    VIDEOCHAT = "Videochat"
}

export enum AnalyticsActions {
    CONTACT_NOTE = "The client opened the contact note",
    GIFT_BUNDLES = "The client opened the gifts bundles tab",
    TOY_SECTION = "The client opened the toy section",
    MUTE_MODEL = "The client has muted the model",
    FULLSCREEN_VIDEOCHAT = "The client is watching the stream in fullscreen"
}

class AnalyticsService {
	private static key: string;

    static initialize (newKey: string) {
		if (!isEqual(this.key, newKey)) {
			this.key = newKey;
			GA.initialize(newKey);
		}
    }

    static triggerEvent (category: AnalyticsCategories, action: AnalyticsActions) {
        GA.event({ category, action });
    }
}

export default AnalyticsService;
