import { get } from 'lodash';
import { RESULT } from "vxcontrol-client-lib";
import { call, put, race, select, take } from "redux-saga/effects";

import utils from "../../utils";
import loggers from "../../loggers";
import types from "../websockets/types";
import { IChat } from "../../redux/store";
import { ActionIds } from '../../redux/interfaces';
import { createAction, createActionType } from '../../redux/utils';
import selectors from "../../redux/selectors";
import { requestChat } from "../websockets/chat";
import AppService from "../../services/AppService";
import { PromiseResult } from "../../services/types";
import { SET_CHAT } from "../../redux/modules/session";
import {setExitEvent} from "../../redux/modules/videochat";
import { closeMessenger } from "../../redux/modules/messenger";
import ChatService from "../../services/ChatService";
import { CoreChat, GET_CHAT_ERROR } from "../../services/ChatService/types";
import { takePattern } from "../utils";
import { VXControlCodes } from '../../static/constants/errorCodes';

const createChatActionType = createActionType(ActionIds.CHAT_SAGA_COMMON, { isSagaAction: true });

// Actions
const RECEIVE_CHAT_ERROR = createChatActionType("RECEIVE_CHAT_ERROR");

// Action Creators
export const onReceiveChatError = createAction<RESULT>(RECEIVE_CHAT_ERROR);

// Sagas
const getChatSaga = function* (instanceId: string, type?: types.SubActionType) {
    yield put(requestChat(instanceId, type));

    const result = yield race({
        result: take(takePattern(SET_CHAT, instanceId)),
        error: take(takePattern(RECEIVE_CHAT_ERROR, instanceId))
    });

    return result;
};

export interface IGetChatInstanceSagaResult {
    chatConf: {
        /** Chat configuration we're getting from CMDP_QUERYBACKEND */
        result?: IChat;
        /** Error got while trying to get a chat configuration */
        error?: RESULT | true;
    };

    chatInstance?: {
        result?: CoreChat;
        /** Error got while trying to initialize a CoreChat instance (the one of vchat-core) */
        error?: any;
    }
}

export const getChatInstanceSaga = function* (
    requestNewConfig: boolean,
    instanceId: string,
    type: types.SubActionType = types.SubActionType.STARTVIDEOCHAT
) {
        let chatConfError = true;

        const chatService = ChatService.getService(instanceId);

        let chatConf: IChat = yield select(selectors.getChat, instanceId);
        const isChatExternallyProvided = yield select(selectors.isChatExternallyProvided, instanceId);

        // require chat data only if it was not externally provided
        if (!isChatExternallyProvided) {
            if (requestNewConfig) {
                const { error } = yield call(getChatSaga, instanceId, type);
                chatConfError = error ? error.payload.value : chatConfError;
            }

            chatConf = yield select(selectors.getChat, instanceId);

            if (!chatConf) {
                // if chat conf cannot be achieved because there are not enough money, call the external recharge hook
                if (get(chatConfError, "code") === VXControlCodes.NO_CREDIT_VIDEOCHAT) {
                    AppService.getService(instanceId).onRecharge();
                }

                return { chatConf: { error: chatConfError }};
            }
        }

        const platformProps = yield select(selectors.getPlatformProps, instanceId);

        let chatInstance: PromiseResult<CoreChat, any> = yield call(chatService.getChat,
            utils.getChatConfig(chatConf, platformProps)
        );

        if (chatInstance.error && isChatExternallyProvided) {
            // let the external platform knows about the failed chat initialization
            // based on provided chat data
            loggers.saga.debug(
                'could not create a chat instance based on externally provided chat config'
            );

            AppService.getService(instanceId).onFailedChatConfig(chatInstance.error);
            yield put(setExitEvent(instanceId, { exitCode: chatInstance.error.code }));
            yield put(closeMessenger(instanceId));

        } else if (chatInstance.error === GET_CHAT_ERROR.BAD_CHAT_CONFIG) {
            // chat config is outdated; try requesting a new one
            const { error } = yield call(getChatSaga, instanceId, type);
            chatConf = yield select(selectors.getChat, instanceId);

            if (!chatConf) {
                loggers.saga.debug(
                    'could not get chat (conf); tried to get a new one, but with no success; aborting'
                );

                return { chatConf: { error: error.payload.value }};
            }

            chatInstance = yield call(chatService.getChat, utils.getChatConfig(chatConf, platformProps));
        }

        return { chatConf: { result: chatConf }, chatInstance };
    };
