import * as React from 'react';

import selectors from '../../../redux/selectors';
import IStore, { IModel, ITranslation } from '../../../redux/store';
import MobileProfileInfo from './MobileProfileInfo';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { clickCloseMessengerButton } from '../../../sagas/common/ui';
import { ActionDispatcher } from '../../../redux';

interface IStoreProps {
    model: IModel;
    translation: ITranslation;

    clickCloseMessengerButton: ActionDispatcher;
}

type IProps = IInstanceProps & IStoreProps;

const closeMessenger = (props: IProps) => () => {
    props.clickCloseMessengerButton(props.instanceId);
};

const MobileProfileInfoContainer = (props: IProps) => (
    <MobileProfileInfo
        model={props.model}
        closeMessenger={closeMessenger(props)}
        closeText={props.translation.common.close}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    model: selectors.getModel(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
});

const mapDispatchToProps = {
    clickCloseMessengerButton
};

export default withRedux(MobileProfileInfoContainer, mapStateToProps, mapDispatchToProps);
