import React from 'react';
import withRedux from '../../hoc/withRedux';
import { isMobile } from 'react-device-detect';

import FontSizeSwitcher from './FontSizeSwitcher';
import IStore, { IMessage } from '../../../redux/store';
import selectors from '../../../redux/selectors';
import { setPreference } from '../../../redux/modules/user'
import { ActionDispatcher } from '../../../redux';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import { PreferenceName } from "../../../services/AppService/types";
import { IPreferenceActionPayload } from "../../../redux/interfaces";

interface IStoreProps {
	shouldIncreaseMessageFontSize: boolean;
	isFullscreen: boolean;
	messages: IMessage[];

	setPreference: ActionDispatcher<IPreferenceActionPayload>;
}

type IProps = IStoreProps & IInstanceProps;

const FontSizeSwitcherContainer = (props: IProps) => {
	const shouldDisplayFontSizeSwitcher = !props.isFullscreen && !isMobile;

	const toggleFontSize = () => {
		props.setPreference(props.instanceId, {
			value: !props.shouldIncreaseMessageFontSize,
			preferenceName: PreferenceName.ShouldIncreaseMessageFontSize
		});
	}

	return (
		<>
			{shouldDisplayFontSizeSwitcher && <FontSizeSwitcher
				toggleFontSize={toggleFontSize}
				shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}
			/>}
		</>
	);
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	shouldIncreaseMessageFontSize: selectors.getShouldIncreaseMessageFontSize(store, instanceId),
	messages: selectors.getActiveMessages(store, instanceId),
	isFullscreen: selectors.isFullScreen(store, instanceId)
});

const mapDispatchToProps = {
	setPreference
};

export default withRedux(FontSizeSwitcherContainer, mapStateToProps, mapDispatchToProps);
