import { isMobile } from 'react-device-detect';
import styled from "styled-components";
import config from '../../../static/config'
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";
import MessageSectionPopupWrapper from '../MessageSectionPopupWrapper';

interface IWindowBoxWrapperProps {
    isOpen: boolean;
    isFullScreen: boolean;
	shouldUseDropdownAnimation: boolean;
    isResponsive?: boolean;
    isWindowForUserPrompt?: boolean;
}

export const WindowContentWrapper = styled.div`
    padding: 10px;
`;

export const WindowWrapper = withCustomClasses<IWindowBoxWrapperProps>(styled(MessageSectionPopupWrapper)<IWindowBoxWrapperProps>`
  ${(props) => `background-color: ${props.theme.colors.windowBackground};`}
  position: ${props => props.isFullScreen && isMobile ? 'relative' : 'absolute'};
  
  ${props => `height: ${(props.isResponsive && !props.isFullScreen) ? '100%' : 'auto'};`}
  /* Logic for all component which use WindowWrapper except UserPrompt */
  ${props => {
  	if (props.isWindowForUserPrompt) {
	    return '';
    }

	if (props.isOpen) {
		return props.isResponsive
			? `left: 0; ${!props.isFullScreen && 'max-height: 100%;'}`
		    : 'max-height: 350px;'
    }

    if (props.shouldUseDropdownAnimation) {
	    return `max-height: 0;`;
    }
	    
	return props.isResponsive ? 'left: 100%;' : 'max-height: 0;';
  }}
  
  ${props => !props.isWindowForUserPrompt && `
    /* we need .5s timeout for responsive context because we have side transition with DropDownMenu */
	transition: ${props.isResponsive
		? `${props.shouldUseDropdownAnimation ? 'max-height' : 'left'} ${config.popupsTransitionTime}s`
		: `max-height ${config.popupsTransitionTime}s`} ease-in-out;
      
	transition-delay: ${!props.isResponsive && props.isOpen ? `${config.popupsTransitionTime}s` : 'unset'};
  `}
  
  /* Logic for UserPrompt TransitionGroup classes */
  &.enter {
    max-height: 0px;
  }
  
  &.enter.enterActive {
    max-height: 350px;
    transition: max-height .5s ease-in-out;
    ${props => !props.isResponsive && `transition-delay: ${config.popupsTransitionTime}s;`}
  }
  
  &.exit {
    max-height: 350px;
  }
  
  &.exit.exitActive {
    max-height: 0px;
    transition: max-height .5s ease-in-out; 
  }

  width: ${props => props.isResponsive && props.isFullScreen ? '100vw' : '100%'};
  overflow: hidden;
  box-sizing: border-box;
  bottom: 0;
  z-index: 5;
`, [DomCustomClass.WindowBox]);

export const WindowTitleWrapper = styled.div`
    display: flex;
    align-items: center;
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-weight: ${(props) => props.theme.typography.weight.regular};
    border-bottom: 1px solid #888;
    margin-bottom: 10px;
    padding: 0px 0 10px;
    text-align: left;
    color: #888;
    min-height: 15px;
    font-size: 14px;
    vertical-align: middle;
    position: relative;
    box-sizing: content-box;

    i {
        position: absolute;
        right: 5px;
        color: #888888;
        cursor: pointer;
    }
`;
