import * as React from 'react';
import { isMobile } from 'react-device-detect';

import IStore, { ITranslation } from "../../../redux/store";
import { IInstanceProps } from "../../hoc/withInstanceInfo";
import GroupChatParticipants from "./GroupChatParticipants";
import selectors from "../../../redux/selectors";
import withRedux from "../../hoc/withRedux";
import { openMentionSection, setMentionQuery } from '../../../redux/modules/messenger';
import { ActionDispatcher } from '../../../redux';

interface IStoreProps {
	participants: number;
	shouldRenderParticipants: boolean;
	translation: ITranslation;
	hasGroupChatIndicator: boolean;

	openMentionSection: ActionDispatcher;
	setMentionQuery: ActionDispatcher<string>;
}

type IProps = IInstanceProps & IStoreProps;

const GroupChatParticipantsContainer = (props: IProps) => {
	const openMentionMenu = () => {
		if (!isMobile) {
			props.openMentionSection(props.instanceId, true);
			props.setMentionQuery(props.instanceId, '');
		}
	};

	return (
		<>
			{props.shouldRenderParticipants && props.hasGroupChatIndicator &&
				<GroupChatParticipants
					participants={props.participants}
					translation={props.translation}
					openMentionSection={openMentionMenu}
				/>
			}
		</>
	);
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	translation: selectors.getTranslation(store, instanceId),
	participants: selectors.getGroupChatParticipantsNumber(store, instanceId),
	shouldRenderParticipants: selectors.shouldRenderGroupChatParticipants(store, instanceId),
	hasGroupChatIndicator: selectors.hasGroupChatIndicator(store, instanceId)
});

const mapDispatchToProps = {
	openMentionSection,
	setMentionQuery
};

export default withRedux(GroupChatParticipantsContainer, mapStateToProps, mapDispatchToProps);
