import * as React from 'react';
import { IGiftPage, ITranslation } from '../../../redux/store';
import { GiftTabWrapper } from './style';

interface IProps {
    changeTab: (activeIndex: number) => () => void;
    giftPage: IGiftPage;
    translation: ITranslation;
    className: string;
    isActive: boolean;
}

const GiftTab = (props: IProps) => (
    <GiftTabWrapper 
        className={props.className} 
        title={props.giftPage.name}
        isActive={props.isActive}
        onClick={props.changeTab(props.giftPage.id)}
    />
);

export default GiftTab;
