import styled from "styled-components";

interface IOptionImageText {
    isActive?: boolean;
}

export const Logo = styled.div`
    color: ${props => props.theme.colors.toySection.primaryColor};
    
    font-size: 50px;
`;

export const OptionImage = styled.i<IOptionImageText>`
	${props => {
		const { primaryColor, inactiveToyMessageText } = props.theme.colors.toySection;
	
		return `color: ${props.isActive ? primaryColor : inactiveToyMessageText};`;
	}}

    font-size: 40px;
    padding: 5px;
`;

export const Text = styled.div<IOptionImageText>`
    color: ${props => props.theme.colors.toySection.text};

    ${props => {
        const { primaryColor, secondaryColor, inactiveToyMessageText } = props.theme.colors.toySection;

        return props.isActive
            ? `background-image: linear-gradient(180deg,${primaryColor}, ${secondaryColor});`
            : `background-color: ${inactiveToyMessageText};`
        }
    }

    border-radius: 2px;
    font-size: 11px;
    text-transform: uppercase;
    padding: 2px 3px 1px;
`;
