import * as React from 'react';
import { map, some } from "lodash";
import { isMobile } from "react-device-detect";

import { InlineOption, OptionsWrapper, Text, Wrapper } from "./style";
import ToyOptionContainer from "../../common/ToyOption";
import { IToyOptionStructure } from "./types";
import ToySectionHeaderContainer from "../../common/ToySectionHeader";
import PerfectScrollbarContainer from "../../common/PerfectScrollbar";

interface IProps {
    text: string;
    isOpen: boolean;
	isFullScreen: boolean;
	shouldUseDropdownAnimation: boolean;
    isResponsive?: boolean;
    isMobileLandscape?: boolean;
    options: IToyOptionStructure[];
    toySectionRef?: any;

    onOptionClick: (id: number) => (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
    closeToyOptionTooltip: () => void;
	onGetReference?: (ref: any) => void;
}

const ToySection = (props: IProps) => {
    const hasActiveOption = some(props.options, (option) => option.isPlaying);
    const hasLimitedWidth = (isMobile || props.isResponsive) as boolean;

    const options = map(props.options, (option) => (
        <InlineOption
            key={option.name}
            hasLimitedWidth={hasLimitedWidth}
            onClick={props.onOptionClick(option.id)}
        >
            <ToyOptionContainer
                id={option.id}
                isAvailable={!hasActiveOption || (hasActiveOption && option.isPlaying)}
				limitingContainerRef={props.toySectionRef}
            />
        </InlineOption>
    ));

    return (
        <Wrapper
			innerRef={props.onGetReference}
            isOpen={props.isOpen}
			shouldUseDropdownAnimation={props.shouldUseDropdownAnimation}
            isMobileLandscape={props.isMobileLandscape}
            isResponsive={props.isResponsive}
            onClick={props.closeToyOptionTooltip}
            isFullScreen={props.isFullScreen}
        >
            <PerfectScrollbarContainer>
                <ToySectionHeaderContainer isToySectionMinimizied={false} />
                <Text isReponsive={props.isResponsive}>{props.text}</Text>
                <OptionsWrapper>{options}</OptionsWrapper>
            </PerfectScrollbarContainer>
        </Wrapper>
    );
};

export default ToySection;
