import * as React from 'react';
import config from '../../../static/config';
import SimpleModelPicture from '../../common/SimpleModelPicture';
import Tooltip from '../../common/Tooltip';
import { ProfilePictureWrapper } from './style';

interface IProps {
    picture: string;
    isOnline: boolean;
    hasOnProfileClick: boolean;
    hasModelProfilePicture?: boolean;
	isContactNoteOpen: boolean;
	tooltipText?: string;

    onClick?: () => void;
}

const ProfilePicture = (props: IProps) => {
    const profilePicture = (
        <SimpleModelPicture
            picture={props.picture}
            isOnline={props.isOnline}
			size={config.profilePictureSizes.big}
			hasOverlay={props.isContactNoteOpen}
            hasCursorPointer={props.hasOnProfileClick}
            onClick={props.onClick}
        />
    );

    const profilePictureWithTooltip = (
        <Tooltip
            id={"visit-profile"}
            text={props.tooltipText}
        >
            {profilePicture}
        </Tooltip>
    );

    return (
        <ProfilePictureWrapper>
            {props.tooltipText && props.hasOnProfileClick
                ? profilePictureWithTooltip
                : profilePicture
            }
        </ProfilePictureWrapper>
    );
};

export default ProfilePicture;
