import * as React from 'react';
import { AnswerType, ICurrency, IQuestion, ITranslation, KnownAnswerType } from '../../../../redux/store';
import utils from "../../../../utils";
import RechargeSelectionButtons from "../../../common/RechargeSelectionButtons";
import SelectionButtons, { SelectedButtonColor } from "../../../common/SelectionButtons";
import { UserPromptContent as UserPromptContentWrapper } from './../style';
import UserPromptTipContainer from "../../UserPromptTip/UserPromptTipContainer";
import CamerasSectionContainer from "../../CamerasSection/CamerasSectionContainer";

export interface IUserPromptContentProps {
    currency: ICurrency;
    question: IQuestion;
    translation: ITranslation;
    
    onAnswer: (answer: AnswerType) => any;
}

const contentProvider = (props: IUserPromptContentProps) => {
    const {question: { type, payload }} = props;
    const { yes, no } = props.translation.common;
    const { chargeNow, verifyAge, adultContentInfo } = props.translation.chat;

    const yesNoOptions = [{
        label: yes,
        value: KnownAnswerType.YES,
        selected: true
    }, {
        label: no,
        value: KnownAnswerType.NO
    }];

    const loadAccountComponent = (
        <SelectionButtons<AnswerType>
            key={type}
            options={[]}
            submit={{
                label: chargeNow,
                answer: KnownAnswerType.YES
            }}
            onSelect={props.onAnswer}
        />
    );

    const amounts = payload && payload.amounts;
    const defaultAmount = payload && payload.defaultAmount;
    const loadAccountOneClickComponent = (
        <RechargeSelectionButtons
            amounts={amounts}
            currency={props.currency}
            hasSubmit={true}
            defaultAmount={defaultAmount}
            submitLabel={chargeNow}
            onSelect={props.onAnswer}
        />
    );

    const verifyAgeComponent = (
        <>
            <span className={"additionalText"}>
                {utils.translationWithValues(
                    adultContentInfo,
                    { modelname: payload && payload.modelName }
                )}
            </span>

            <SelectionButtons<AnswerType>
                key={type}
                options={[]}
                submit={{
                    label: verifyAge,
                    answer: KnownAnswerType.OK
                }}
                onSelect={props.onAnswer}
            />
        </>
    );

    const closeChatComponent = (
        <SelectionButtons<AnswerType>
            key={type}
            options={yesNoOptions}
            selectedButtonColor={SelectedButtonColor.BLACK}
            onSelect={props.onAnswer}
        />
    );

    const invitePrivateChatComponent = (
        <>

			<span className={"additionalText"}>
                {payload && payload.privateChatText}
            </span>
			
            <SelectionButtons<AnswerType>
                key={type}
                options={yesNoOptions}
                onSelect={props.onAnswer}
            />
        </>

    );

    const userPrompts: {[ type: string]: any} = {};
    userPrompts.LOAD_ACCOUNT = loadAccountComponent;
    userPrompts.LOAD_ACCOUNT_ONE_CLICK = loadAccountOneClickComponent;
    userPrompts.VERIFY_AGE = verifyAgeComponent;
    userPrompts.SPY_PRIVATE_CHAT = closeChatComponent;
    userPrompts.CLOSE_CHAT = closeChatComponent;
    userPrompts.INVITE_PRIVATE_CHAT = invitePrivateChatComponent;
    userPrompts.SEND_ANONYMOUS_TIP = <UserPromptTipContainer isAnonymous={true}/>;
    userPrompts.SEND_TIP = <UserPromptTipContainer />;
    userPrompts.CHOOSE_CAMERA = <CamerasSectionContainer />;

    return userPrompts[type];
};

const UserPromptContent = (props: IUserPromptContentProps) => (
    <UserPromptContentWrapper>
        {contentProvider(props)}
    </UserPromptContentWrapper>
);

export default UserPromptContent;
