import * as React from 'react';
import { isIOS, isMobile } from 'react-device-detect';
import { ITranslation } from '../../../redux/store';
import VideoChatVolume from '../../common/VideoChatVolume';
import VideoChatMobile from '../VideoChatMobile';
import VideoChatReload from '../VideoChatReload';
import VideoChatToggleFullscreen from '../VideoChatToggleFullsceen';
import { VideoChatActionSectionWrapper } from './style';

interface IProps {
    isFullscreen: boolean;
    isMobileMenuOpen: boolean;
    isMutedByModel: boolean;
    isMutedByClient: boolean;
	clientVolume: number;
    displayVolumeAsInitiallyMuted: boolean;
    isInitiallyMutedHintDisplayed: boolean;
    hasFullscreen: boolean;
    translation: ITranslation;

    toggleFullscreen: () => void;
    toggleMobileMenu: () => void;
    toggleSound: () => void;
    hideInitiallyMutedHint: () => void;
    reloadPlayer: () => void;
	setClientVolume: (volume: number) => void;
}

const VideoChatActionSection = (props: IProps) => {

    const videoChatVolume = props.isMutedByModel && (
        <VideoChatVolume
            isMutedByClient={props.isMutedByClient}
            displayVolumeAsInitiallyMuted={props.displayVolumeAsInitiallyMuted}
            isInitiallyMutedHintDisplayed={props.isInitiallyMutedHintDisplayed}
            translation={props.translation}
			volume={props.clientVolume}
            toggleSound={props.toggleSound}
            hideInitiallyMutedHint={props.hideInitiallyMutedHint}
			setVolume={props.setClientVolume}
        />
    );

    const videoChatActionSectionButtons = (
        <>
            {videoChatVolume}

            {props.hasFullscreen &&
                <VideoChatToggleFullscreen
                    isFullScreen={props.isFullscreen}
                    translation={props.translation}
                    toggleFullscreen={props.toggleFullscreen}
                />
            }
        </>
    );

    const videoChatMobileActionsSectionButtons = (
        <>
            {videoChatVolume}

            {isIOS &&
                <VideoChatReload
                    translation={props.translation}
                    onClick={props.reloadPlayer}
                />
            }

            {props.isFullscreen &&
                <VideoChatMobile
                    isMobileMenuOpen={props.isMobileMenuOpen}
                    toggleMobileMenu={props.toggleMobileMenu}
                />
            }
        </>
    );

    return (
        <VideoChatActionSectionWrapper>
            {isMobile ? videoChatMobileActionsSectionButtons : videoChatActionSectionButtons}
        </VideoChatActionSectionWrapper>
    );
};

export default VideoChatActionSection;
