import config from '../../../static/config';
import {IProps} from "./types";
import {isUndefined} from "util";

export const getImageLoaderSize = (props: IProps): number => {
	const { imageLoaderSize } = config.viewSizes;

	if (isUndefined(props.width) || isUndefined(props.height)) {
		return imageLoaderSize;
	}

	const imageMinSize = Math.min(props.width, props.height);

	return Math.min(imageMinSize, imageLoaderSize);
};
