import { isEmpty } from 'lodash';
import * as React from 'react';
import { AnswerType, KnownAnswerType } from '../../../redux/store';
import CheckedIcon from "../../common/CheckedIcon/CheckedIcon";
import { SelectionButton as SelectionButtonWrapper, SelectionButtons as SelectionButtonsWrapper } from './style';
import { SelectedButtonColor} from "./types";

export interface ISelectionOption<T> {
    value: T;
    label: string;
    selected?: boolean;
}

export interface ISelectionButtonsProps<T> {
    options: ISelectionOption<T>[];
    submit?: {
        label: string;
        answer?: T | AnswerType;
    };
    selectedButtonColor?: SelectedButtonColor;
    onSelect: (answer: T | AnswerType) => any;
}

interface IState<T> {
    options: ISelectionOption<T>[];
}

class SelectionButtons <T>
    extends React.Component<ISelectionButtonsProps<T>, IState<T>> {
    constructor(props: any) {
        super(props);
        this.state = {
            options: props.options.map((option: ISelectionOption<T>) => ({ ...option }))
        };
    }

    render() {
        return (
            <SelectionButtonsWrapper>
                {this.state.options.map((option: ISelectionOption<T>) => (
                   <SelectionButtonWrapper
                       key={JSON.stringify(option.value)}
                       selected={option.selected}
                       selectedButtonColor={this.props.selectedButtonColor}
                       className="optionButton"
                       onClick={() => this.onClickOption(option.value)}
                   >
                       <span className="label nowrap">{option.label}</span>
                       {option.selected && this.hasSubmitFeature() && <CheckedIcon/>}
                   </SelectionButtonWrapper>
                   ))
                }

                {this.props.submit &&
                    <SelectionButtonWrapper
                        onClick={this.onSubmit}
                        className="submitButton"
                        selected={true}
                    >
                        <span className="label nowrap">
                            {this.props.submit.label}
                        </span>
                    </SelectionButtonWrapper>
                }
            </SelectionButtonsWrapper>
        );
    }

    private onClickOption = (value: any) => {
        this.selectOptionInState(value).then(() => {
            if (!this.hasSubmitFeature()) {
                const selectedOption = this.getSelectedOptionFromState();
                if (selectedOption) {
                    this.props.onSelect(selectedOption.value)
                }
            }
        });
    };

    private onSubmit = () => {
        if (isEmpty(this.state.options)) {
            // trigger default answer
            this.props
                .onSelect((this.props.submit && this.props.submit.answer) || KnownAnswerType.OK);
        } else {
            const selectedOption = this.getSelectedOptionFromState();
            if (selectedOption) {
                this.props.onSelect(selectedOption.value);
            }
        }
    };

    private hasSubmitFeature = () => !!this.props.submit;

    private selectOptionInState = (value: any): Promise<IState<T>> => {
         return new Promise((resolve) => {
             this.setState((oldState: IState<T>) => ({
                 options: oldState.options
                     .map((option: ISelectionOption<T>) => (
                         {...option, selected: option.value === value}
                     ))
             }), () => resolve(this.state)) ;
         });
    };

    private getSelectedOptionFromState = () => (
        this.state.options.find((option: ISelectionOption<T>) => !!option.selected)
    )
}

export default SelectionButtons;
