import { get } from "lodash";
import {createSelector} from "reselect";

import IStore, { IOptions } from "../store";
import config from "../../static/config";
import { getInitialInstanceData } from "../initialStore";

// IE-SPECIFIC: Audio Messages are not currently working on any IE browser.
export const getOptions = (store: IStore, instanceId: string | undefined): IOptions => {
    const id = instanceId as string;
    const data = get(store, id) || getInitialInstanceData();

	return data.options;
};

/**
 * @return the max width in pixels by which the messenger will stay in the portrait mode
 */
export const getSwitchToPortraitWidth = createSelector(getOptions, options =>
    options.switchToPortraitWidth || config.viewSizes.switchToPortraitWidth
);

/**
 * @return the tipping information that will be displayed in the chat
 * after the customer has sent a tip to the model.
 */
export const getTip = createSelector(getOptions,
	options => options.tip || config.defaultTipOption
);

/**
 * @returns {number | undefined} the initial videochat volume if it has
 * been configured by the parent platform. The value will be a float between 0 and 1.
 */
export const getInitialVideochatVolume = createSelector(getOptions,
	options => options.initialVideochatVolume	
);

/**
 * @return {string} - the preloader CDN image address for when the stream is loading. If no preloader
 * has been sent from the parent platform, we will use our visit-x default preloader.
 */
export const getPreloader = createSelector(getOptions, options => options.preloader);

/**
 * @return {string} - the link presented to the user to update his presumable outdated browser
 */
export const getUpdateBrowserLink = createSelector(getOptions, options => options.updateBrowserLink);

/**
 * @return {string} - the Google Analytics key for metric tracking
 */
export const getAnalyticsKey = createSelector(getOptions, options => options.analyticsKey);

/**
 * @return {string} - the censored content image
 */
export const getCensoredContentImage = createSelector(getOptions, options => options.censoredContentImage);
