import { isEmpty } from "lodash";
import { Action } from "redux-actions";
import { all, call, put, race, select, take, takeEvery, delay } from "redux-saga/effects";

import selectors from "../../redux/selectors";
import constants from "../../static/constants";
import { startVideoChatType } from "./videochat";
import ApiService from "../../services/ApiService";
import { createAction } from "../../redux/utils";
import { IPayload } from "../../redux/interfaces";
import { RECEIVE_ERROR } from "../websockets/handleError";
import { resendFailedMessages } from "../websockets/message";
import AnalyticsService from "../../services/AnalyticsService";
import { RECEIVE_ONLINE_STATE } from "../websockets/onlineState";
import { RECEIVE_LOGIN, requestLogin } from "../websockets/login";
import { resetModel, SET_MODEL_ONLINE_STATE, SET_PARTNER } from "../../redux/modules/model";
import { RECEIVE_GET_CHANNEL_INFO } from "../websockets/channelInfo";
import { SET_LANGUAGE, SET_USER_KEY } from "../../redux/modules/user";
import { RECEIVE_CHANNELS, requestChannel } from "../websockets/channel";
import { openVideochat, SET_INITIAL_VIDEOCHAT_TYPE } from "../../redux/modules/videochat";
import { resetChannel, SET_CHANNEL, SET_SESSION, SET_WEBTOKEN } from "../../redux/modules/session";
import { ActionResponseType, IChat, IErrorTranslation, LoadingStatus, IPartner, VideoChatType, IOptions } from "../../redux/store";
import {
    OPEN_MESSENGER,
    removeAllMessages,
    setActionResponse,
    setMessengerLoadingStatus,
    setShouldResendFailedMessages,
	resetEditorState
} from "../../redux/modules/messenger";
import { takePattern } from "../utils";
import { SET_OPTIONS } from "../../redux/modules/options";

/**
 * we are assuming that user key will be set
 * only when different than its store's counterpart
 */
const setUserKeySaga = function* ({ payload: { instanceId } }: Action<IPayload>) {
    const webToken = yield select(selectors.getWebtoken, instanceId);
    const language = yield select(selectors.getLanguage, instanceId);

    // means that we were already logged in
    if (webToken && language) {
        // now we should re-login, with the newer user key
        yield call(setWebTokenSaga, createAction("CALL_SET_WEBTOKEN_SAGA")(instanceId));
    }
};

/**
 * we are assuming that language will be set
 * only when different than its store's counterpart
 */
const setLanguageSaga = function* ({ payload: { instanceId } }: Action<IPayload>) {
    const language = yield select(selectors.getLanguage, instanceId);
    ApiService.getService(instanceId).updateLanguage(language);

    const webToken = yield select(selectors.getWebtoken, instanceId);
    const userKey = yield select(selectors.getUserKey, instanceId);

    if (webToken && userKey) {
        // means that we were already logged in, but
        // now we should re-login, with the newer language
        yield call(setWebTokenSaga, createAction("CALL_SET_WEBTOKEN_SAGA")(instanceId));
    }
};

/**
 * we are assuming that web token will be set
 * only when different than its store's counterpart
 */
const setWebTokenSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    // Mark messenger's loading status
    yield put(setMessengerLoadingStatus(instanceId, LoadingStatus.LOADING));

    const webToken = yield select(selectors.getWebtoken, instanceId);
    yield put(requestLogin(instanceId, webToken));
};

/**
 * we are assuming that the partner will be set
 * only when different than its store's counterpart
 * @param action
 */
export const setPartnerSaga = function* ({ payload: { instanceId } }: Action<IPayload>) {
    // Execute the logic for setting the partner only if we had
    // a successful login response from the back-end
    const sessionId = yield select(selectors.getSessionId, instanceId);

    if (!sessionId) {
        const loginResult = yield race({
            login: take(takePattern(RECEIVE_LOGIN, instanceId)),
            error: take(takePattern(RECEIVE_ERROR, instanceId))
        });

        if (!loginResult.login) {
            return;
        }
    }

    yield put(removeAllMessages(instanceId));
	yield put(resetEditorState(instanceId));

    // We need to reset the existing channel, otherwise the subsequent
    // requestChannel / receiveChannel won't do a thing
    yield put(resetChannel(instanceId));
    yield put(resetModel(instanceId));

    const partner: IPartner = yield select(selectors.getPartner, instanceId);
    if (!partner) {
        return;
    }

    const messengerLoadingStatus = yield select(selectors.getMessengerLoadingStatus, instanceId);
    // mark messenger's loading status
    if (messengerLoadingStatus !== LoadingStatus.LOADING) {
        yield put(setMessengerLoadingStatus(instanceId, LoadingStatus.LOADING));
    }

    const sessionID = yield select(selectors.getSessionId, instanceId);
    // We wait for the RECEIVE_LOGIN if we don't have the sessionID yet
    if (!sessionID) {
        const { timeout } = yield race({
            response: take(takePattern(SET_SESSION, instanceId)),
            timeout: delay(constants.loginWaitingTime)
        });

        if (timeout) {
            yield put(setMessengerLoadingStatus(instanceId, LoadingStatus.NONE));

            const errorTranslations: IErrorTranslation = yield select(
                selectors.getTranslationForErrors,
                instanceId
            );

            yield put(setActionResponse(instanceId, {
                text: errorTranslations.initFail,
                type: ActionResponseType.ERROR
            }));

            return;
        }
    }

    yield put(requestChannel(instanceId));

    const { success } = yield race({
        success: all([
            take(takePattern(RECEIVE_CHANNELS, instanceId)),
            take(takePattern(RECEIVE_GET_CHANNEL_INFO, instanceId)),
            take(takePattern(RECEIVE_ONLINE_STATE, instanceId))
        ]),
        error: take(takePattern(RECEIVE_ERROR, instanceId))
    });

    const { ERROR, SUCCESS } = LoadingStatus;
    yield put(setMessengerLoadingStatus(instanceId, success ? SUCCESS : ERROR));

    const shouldResendFailedMessages = yield select(selectors.shouldResendFailedMessages, instanceId);
    if (success && shouldResendFailedMessages) {
        yield put(setShouldResendFailedMessages(instanceId, false));
        yield put(resendFailedMessages(instanceId));
    }
};

export const setInitialVideoChatTypeSaga = function* ({ payload }: Action<IPayload<VideoChatType>>) {
    const { instanceId, value } = payload;

    // check if the messenger is already open; if not, wait for it to be opened
    const isOpen = yield select(selectors.isOpen, instanceId);
    if (!isOpen) {
        yield take(takePattern(OPEN_MESSENGER, instanceId));
    }

    yield put(openVideochat(instanceId));

    const chat: IChat = yield select(selectors.getChat, instanceId);

    if (!chat || !chat.clientId || !chat.server) {
        // should wait for the web token based init flow to be ended
        const channelId = yield select(selectors.getChannelId, instanceId);
        if (!channelId) {
            yield take(takePattern(SET_CHANNEL, instanceId));
        }

        const sessionId = yield select(selectors.getSessionId, instanceId);
        if (!sessionId) {
            yield take(takePattern(SET_SESSION, instanceId));
        }

        const onlineState = yield select(selectors.getOnlineState, instanceId);
        if (!onlineState) {
            yield take(SET_MODEL_ONLINE_STATE);
        }
    }

    yield put(startVideoChatType(instanceId, value));
};

const initializeGoogleAnalyticsSaga = function*({ payload: { value }}: Action<IPayload<IOptions>>) {
    if (!isEmpty(value.analyticsKey)) {
        AnalyticsService.initialize(value.analyticsKey as string);
    }
}

const watchSagas = function* () {
    yield takeEvery(SET_USER_KEY, setUserKeySaga);
    yield takeEvery(SET_WEBTOKEN, setWebTokenSaga);
    yield takeEvery(SET_PARTNER, setPartnerSaga);
    yield takeEvery(SET_LANGUAGE, setLanguageSaga);
    yield takeEvery(SET_OPTIONS, initializeGoogleAnalyticsSaga);
    yield takeEvery(SET_INITIAL_VIDEOCHAT_TYPE, setInitialVideoChatTypeSaga);
};

export default watchSagas;
