import { handleActions, Action } from "redux-actions";

import { createAction, createActionType } from "../utils";
import { IOptions } from "../store";
import { getInitialInstanceData } from "../initialStore";
import { ActionIds } from "../interfaces";
import { AnyAction } from "redux";
import { RESET_STORE } from "./app";

const createOptionsActionType = createActionType(ActionIds.OPTIONS, { isReduxAction: true });

// Actions
export const SET_OPTIONS = createOptionsActionType("SET_OPTIONS");

// Action Creators
export const setOptions = createAction<IOptions>(SET_OPTIONS);


// Reducer
const options = handleActions({
    [RESET_STORE]: (_: IOptions, action: AnyAction) => getInitialInstanceData().options,
    [SET_OPTIONS]: (_: IOptions, { payload }: Action<IOptions>) => payload
}, getInitialInstanceData().options);

export default options;
