import * as React from 'react';
import { ThemedStyledProps, withTheme } from "styled-components";
import ITheme from "../../../themes/interfaces";
import Timer from "../../common/Timer";
import { ITranslation } from "../../../redux/store";

import { TimeFormatter, TextTimeFormatter, NumberTimeFormatter} from './style';

interface IProps {
    totalTime: number;
    currentTime: number;

    theme: ITheme;
    translation: ITranslation;

    onUnmount?: (remainingTime: number) => any;
    onTimeIsOver?: () => any;
};

const timeFormatter = (translation: ITranslation) => (remainingTime: number) => (
    <TimeFormatter>
        <NumberTimeFormatter>{remainingTime}</NumberTimeFormatter>
        <TextTimeFormatter>{translation.common.seconds}</TextTimeFormatter>
    </TimeFormatter>
);

const LivePreviewTextTimer = (props: ThemedStyledProps<IProps, ITheme>) => (
    <Timer
        totalTime={props.totalTime}
        currentTime={props.currentTime}
        styling={{
            size: 108,
            thickness: 3,
            filledColor: props.theme.colors.livePreviewTimer.fillColor,
            unfilledColor: props.theme.colors.livePreviewTimer.unfillColor,
            timerTextColor: props.theme.colors.livePreviewTimer.textColor,
            backgroundColor: props.theme.colors.livePreviewTimer.bgColor
        }}
        timeFormatter={timeFormatter(props.translation)}
        onTimeIsOver={props.onTimeIsOver}
        onUnmount={props.onUnmount}
    />
);

export default withTheme<IProps, ITheme>(LivePreviewTextTimer);