import * as React from 'react';

import Cam2CamButton from './Cam2CamButton';
import selectors from '../../../redux/selectors';
import { ActionDispatcher} from '../../../redux';
import IStore, { ITranslation } from '../../../redux/store';
import { toggleCam2Cam } from "../../../sagas/common/videochat/cam2Cam";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IComponentProps {
    isResponsive?: boolean;
    shouldDisplayIcon?: boolean;
    isForActionSection?: boolean;
}

interface IStoreProps {
    translation: ITranslation;
    isCam2CamActive: boolean;
	updateBrowserLink?: string;

    toggleCam2Cam: ActionDispatcher;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

const onCam2CamButtonClick = (props: IProps) => () => {
    props.toggleCam2Cam(props.instanceId);
};

const Cam2CamButtonContainer = (props: IProps) => (
    <Cam2CamButton
        translation={props.translation}
        isSelected={props.isCam2CamActive}
        isForActionSection={props.isForActionSection}
        shouldDisplayIcon={props.shouldDisplayIcon}
        updateBrowserLink={props.updateBrowserLink}
        onClick={onCam2CamButtonClick(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    translation: selectors.getTranslation(store, instanceId),
    isCam2CamActive: selectors.isCam2CamActive(store, instanceId),
	updateBrowserLink: selectors.getUpdateBrowserLink(store, instanceId)
});

const mapDispatchToProps = {
    toggleCam2Cam
};

export default withRedux<IComponentProps>(Cam2CamButtonContainer, mapStateToProps, mapDispatchToProps);
