import { chain, findIndex, startsWith } from "lodash";

import { IStateOption } from "./types";

/**
 * Convert participants to options
 * @param participants
 * @param query
 */
export const  getOptions = (participants: string[], query: string) =>
	chain(participants)
		.filter(participant => query ? startsWith(participant, query) : true)
		.map((participant) =>
			({
				id: participant,
				value: participant,
				label: participant
			})
		)
		.value() as IStateOption[];

/**
 * Get previous option
 * @param options - options list
 * @param currentOptionId - id of the current focused option
 * @return id of the previous option, or undefined if options are empty
 */
export const getPreviousOption = (options: IStateOption[], currentOptionId: string | undefined): string | undefined => {
	const optionsLength = options.length;

	if (!optionsLength) {
		return undefined;
	}

	const currentFocusIndex = currentOptionId
		? findIndex(options, (option) => option.id === currentOptionId)
		: 0;

	const previousIsInBounds = currentFocusIndex - 1 >= 0;
	const previousIndex = previousIsInBounds ? currentFocusIndex - 1 : optionsLength -1;

	return options[previousIndex].id;
};

/**
 * Get next option
 * @param options - options list
 * @param currentOptionId - id of the current focused option
 * @return id of the previous option, or undefined if options are empty
 */
export const getNextOption = (options: IStateOption[], currentOptionId: string | undefined): string | undefined => {
	const optionsLength = options.length;

	if (!optionsLength) {
		return undefined;
	}

	const currentFocusIndex = currentOptionId
		? findIndex(options, (option) => option.id === currentOptionId)
		: -1;

	const nextIsInBounds = currentFocusIndex + 1 < optionsLength;
	const nextIndex = nextIsInBounds ? currentFocusIndex + 1 : 0;
	return options[nextIndex].id;
};


/**
 * Get the HTML element for the entire selection list
 * @param elemRef - component's reference
 */
export const getMenuElement = (elemRef: React.RefObject<HTMLElement>) => {
	const element = elemRef.current;
	// found no better choice for selecting react-select list element :-(
	return element?.querySelector('div[class*="MenuList"]');
};

/**
 * Get the HTML element for a certain option in selection list
 * @param elemRef - component's reference
 * @param optionId - id of the option
 */
export const getOptionElement = (elemRef: React.RefObject<HTMLElement>, optionId: string) => {
	const element = elemRef.current;
	return element?.querySelector(`div[data-id="${optionId}"]`);
};

/**
 * Scrolls the list to the specified option
 * @param elemRef - component's reference
 * @param optionId - id of the option
 */
export const scrollTo = (elemRef: React.RefObject<HTMLElement>, optionId: string ) => {

	if (!optionId) {
		return;
	}

	const menuElement = getMenuElement(elemRef);
	const optionElement = getOptionElement(elemRef, optionId);
	if (menuElement && optionElement) {
		const menuRect= menuElement.getBoundingClientRect();
		const optionRect= optionElement.getBoundingClientRect();

		const scrollDelta = getScrollDelta(optionRect, menuRect);
		if (scrollDelta) {
			menuElement.scrollTop += scrollDelta;
		}
	}
};

/**
 * Return the amount of more (or less) scroll to be applied on the selection list
 * to have a certain target element visible
 * @param targetRect
 * @param containerRect
 */
export const getScrollDelta = (targetRect: DOMRect, containerRect: DOMRect) => {
	if (targetRect.bottom > containerRect.bottom) {
		return targetRect.bottom - containerRect.bottom;
	}

	if (targetRect.top < containerRect.top) {
		return targetRect.top - containerRect.top;
	}

	return 0;
};


