import styled from 'styled-components';
import { ArrowDirection } from './types';

interface IArrowWrapperProps {
    arrowDirection: ArrowDirection,
    isResponsive?: boolean
}

export const ArrowWrapper = styled.i.attrs(
    { className: (props: any) => props.arrowDirection }
)<IArrowWrapperProps>`
    ${props => props.isResponsive ? 'margin-left: 10px' : 'margin-right: 10px'};
    
    ${
        /* Had to hardcode this because -icon* has position: absolute set and overwrites this change */
        props => props.isResponsive && 'position: relative !important;'
    }
    
    cursor: pointer;
`;
