import { produce } from "immer";
import { AnyAction } from "redux";
import { handleActions, Action } from "redux-actions";
import { filter, findIndex } from 'lodash';

import { createAction, createActionType } from "../utils";
import {RESET_STORE} from "./app";
import config from '../../static/config';
import { ExitCode } from "../../services/ChatService/types";
import { getInitialInstanceData } from '../initialStore';
import {
    PreChatInformationType,
    IResolution,
    IVideochat,
    VideochatInformationType,
    VideoChatType,
    ICameraDevice,
    IExitEvent
} from "../store";
import { ActionIds } from "../interfaces";

export interface ISetExitMessagePayload {
    exitCode: ExitCode | number;
    exitMessage?: string;
}

const createVideochatActionType = createActionType(ActionIds.VIDEOCHAT_REDUX, { isReduxAction: true });

// Actions
export const SET_QUALITY = createVideochatActionType("SET_QUALITY");
export const SET_IS_MOBILE_MENU_OPEN = createVideochatActionType("SET_IS_MOBILE_MENU_OPEN");

export const OPEN_FULLSCREEN = createVideochatActionType("OPEN_FULLSCREEN");
export const CLOSE_FULLSCREEN = createVideochatActionType("CLOSE_FULLSCREEN");
export const TOGGLE_FULLSCREEN = createVideochatActionType("TOGGLE_FULLSCREEN");

export const SET_IS_MUTED_BY_CLIENT = createVideochatActionType("SET_IS_MUTED_BY_CLIENT");
export const SET_IS_INITIALLY_MUTED = createVideochatActionType("SET_IS_INITIALLY_MUTED");
export const DISPLAY_INITIALLY_MUTED_HINT = createVideochatActionType("DISPLAY_INITIALLY_MUTED_HINT");
export const HIDE_INITIALLY_MUTED_HINT = createVideochatActionType("HIDE_INITIALLY_MUTED_HINT");

export const SET_CAM2CAM_ACTIVE = createVideochatActionType("SET_CAM2CAM_ACTIVE");
export const SET_CAM2CAM_INACTIVE = createVideochatActionType("SET_CAM2CAM_INACTIVE");
export const SET_CAM2CAM_CAMERAS = createVideochatActionType("SET_CAM2CAM_CAMERAS");
export const SET_CAM2CAM_SELECTED_CAMERA = createVideochatActionType("SET_CAM2CAM_SELECTED_CAMERA");
export const RESET_CAM2CAM_SELECTED_CAMERA = createVideochatActionType("RESET_CAM2CAM_SELECTED_CAMERA");

export const CAM2CAM_MINIMIZE = createVideochatActionType("CAM2CAM_MINIMIZE");
export const CAM2CAM_MAXIMIZE = createVideochatActionType("CAM2CAM_MAXIMIZE");
export const SET_CAM2CAM_STATUS = createVideochatActionType("SET_CAM2CAM_STATUS");

export const SET_INFORMATION = createVideochatActionType("SET_INFORMATION");
export const RESET_INFORMATION = createVideochatActionType("RESET_INFORMATION");

export const SET_PRE_CHAT_INFORMATION = createVideochatActionType("SET_PRE_CHAT_INFORMATION");
export const RESET_PRE_CHAT_INFORMATION = createVideochatActionType("RESET_PRE_CHAT_INFORMATION");

export const OPEN_VIDEOCHAT = createVideochatActionType("OPEN_VIDEOCHAT");
export const CLOSE_VIDEOCHAT = createVideochatActionType("CLOSE_VIDEOCHAT");

export const SET_TYPE = createVideochatActionType("SET_TYPE");
export const RESET_TYPE = createVideochatActionType("RESET_TYPE");

export const SET_INITIAL_VIDEOCHAT_TYPE = createVideochatActionType("SET_INITIAL_VIDEOCHAT_TYPE");
export const RESET_INITIAL_VIDEOCHAT_TYPE = createVideochatActionType("RESET_INITIAL_VIDEOCHAT_TYPE");

export const SET_VIDEOCHAT_TIME_LIMIT = createVideochatActionType("SET_VIDEOCHAT_TIME_LIMIT");
export const SET_LIVE_PREVIEW_TIME_LIMIT = createVideochatActionType("SET_LIVE_PREVIEW_TIME_LIMIT");
export const SET_LIVE_PREVIEW_TEXT_TIME_LIMIT = createVideochatActionType("SET_LIVE_PREVIEW_TEXT_TIME_LIMIT");
export const SET_LIVE_PREVIEW_TEXT_TIME_LEFT = createVideochatActionType("SET_LIVE_PREVIEW_TEXT_TIME_LEFT");
export const SET_VIDEOCHAT_TIME_LIMIT_BELOW_THRESHOLD = createVideochatActionType("SET_VIDEOCHAT_TIME_LIMIT_BELOW_THRESHOLD");

export const SET_IS_MUTED_BY_MODEL = createVideochatActionType("SET_IS_MUTED_BY_MODEL");
export const SET_INITIAL_RESOLUTION = createVideochatActionType("SET_INITIAL_RESOLUTION");
export const SET_PLAYING_STATUS = createVideochatActionType("SET_PLAYING_STATUS");

export const SHOULD_RENDER_ONLY_VIDEOCHAT = createVideochatActionType("SHOULD_RENDER_ONLY_VIDEOCHAT");

export const SET_EXIT_EVENT = createVideochatActionType("SET_EXIT_EVENT");
export const RESET_EXIT_EVENT = createVideochatActionType("RESET_EXIT_EVENT");

export const SET_RESUME_CHAT_TIMESTAMP = createVideochatActionType("SET_RESUME_CHAT_TIMESTAMP");
export const SET_SHOULD_HIDE_VIDEOCHAT_CONTROLS = createVideochatActionType("SET_SHOULD_HIDE_VIDEOCHAT_CONTROLS");

export const SET_IS_GROUP_CHAT = createVideochatActionType("SET_IS_GROUP_CHAT");

export const SET_IS_IN_VOYEUR_MODE = createVideochatActionType("SET_IS_IN_VOYEUR_MODE");

export const SET_GROUP_CHAT_PARTICIPANTS = createVideochatActionType("SET_GROUP_CHAT_PARTICIPANTS");
export const ADD_GROUP_CHAT_PARTICIPANT = createVideochatActionType("ADD_GROUP_CHAT_PARTICIPANT");
export const REMOVE_GROUP_CHAT_PARTICIPANT = createVideochatActionType("REMOVE_GROUP_CHAT_PARTICIPANT");
export const SET_GROUP_CHAT_CLIENTNAME = createVideochatActionType("SET_GROUP_CHAT_CLIENTNAME");

export const SET_IS_AUTOPLAY_RESTRICTED = createVideochatActionType("SET_IS_AUTOPLAY_RESTRICTED");

// Action Creators
export const setQuality = createAction<boolean>(SET_QUALITY);

export const openFullscreen = createAction(OPEN_FULLSCREEN);
export const closeFullscreen = createAction(CLOSE_FULLSCREEN);
export const toggleFullscreen = createAction(TOGGLE_FULLSCREEN);

export const setCam2CamActive = createAction(SET_CAM2CAM_ACTIVE);
export const setCam2CamInactive = createAction(SET_CAM2CAM_INACTIVE);
export const setCam2CamCameras = createAction<ICameraDevice[]>(SET_CAM2CAM_CAMERAS);
export const setCam2CamSelectedCamera = createAction<string>(SET_CAM2CAM_SELECTED_CAMERA);
export const resetCam2CamSelectedCamera = createAction(RESET_CAM2CAM_SELECTED_CAMERA);
export const cam2CamMinimize = createAction(CAM2CAM_MINIMIZE);
export const cam2CamMaximize = createAction(CAM2CAM_MAXIMIZE);
export const setCam2CamStatus = createAction(SET_CAM2CAM_STATUS);
export const setShouldHideVideochatControls = createAction<boolean>(SET_SHOULD_HIDE_VIDEOCHAT_CONTROLS);

export const setInformation = createAction<VideochatInformationType>(SET_INFORMATION);
export const resetInformation = createAction(RESET_INFORMATION);

export const setPreChatInformation = createAction<PreChatInformationType>(SET_PRE_CHAT_INFORMATION);
export const resetPreChatInformation = createAction(RESET_PRE_CHAT_INFORMATION);

export const openVideochat = createAction(OPEN_VIDEOCHAT);
export const closeVideochat = createAction(CLOSE_VIDEOCHAT);

export const setType = createAction<VideoChatType>(SET_TYPE);
export const resetType = createAction(RESET_TYPE);
export const setInitialVideoChatType = createAction<VideoChatType>(SET_INITIAL_VIDEOCHAT_TYPE);
export const resetInitialVideoChatType = createAction(RESET_INITIAL_VIDEOCHAT_TYPE);

export const setVideoChatTimeLimit = createAction<number>(SET_VIDEOCHAT_TIME_LIMIT);
export const setVideoChatTimeLimitBelowThreshold = createAction<boolean>(SET_VIDEOCHAT_TIME_LIMIT_BELOW_THRESHOLD);
export const setLivePreviewTimeLimit = createAction<number>(SET_LIVE_PREVIEW_TIME_LIMIT);
export const setLivePreviewTextTimeLeft = createAction<number>(SET_LIVE_PREVIEW_TEXT_TIME_LEFT);
export const setLivePreviewTextTimeLimit = createAction<number>(SET_LIVE_PREVIEW_TEXT_TIME_LIMIT);

export const setHasAudio = createAction<boolean>(SET_IS_MUTED_BY_MODEL);
export const setIsMutedByClient = createAction<boolean>(SET_IS_MUTED_BY_CLIENT);
export const setIsInitiallyMuted = createAction<boolean>(SET_IS_INITIALLY_MUTED);
export const displayInitiallyMutedHint = createAction(DISPLAY_INITIALLY_MUTED_HINT);
export const hideInitiallyMutedHint = createAction(HIDE_INITIALLY_MUTED_HINT);

export const setPlayingStatus = createAction<boolean>(SET_PLAYING_STATUS);
export const setInitialResolution = createAction<IResolution>(SET_INITIAL_RESOLUTION);
export const setIsMobileMenuOpen = createAction<boolean>(SET_IS_MOBILE_MENU_OPEN);

export const setShouldRenderOnlyVideochat = createAction<boolean>(SHOULD_RENDER_ONLY_VIDEOCHAT);

export const setExitEvent = createAction<ISetExitMessagePayload>(SET_EXIT_EVENT);

export const resetExitEvent = createAction(RESET_EXIT_EVENT);
export const setResumeChatTimestamp = createAction<Date>(SET_RESUME_CHAT_TIMESTAMP);

export const setIsGroupChat = createAction<boolean>(SET_IS_GROUP_CHAT);
export const setIsInVoyeurMode = createAction<boolean>(SET_IS_IN_VOYEUR_MODE);

export const setGroupChatParticipants = createAction<string[]>(SET_GROUP_CHAT_PARTICIPANTS);
export const addGroupChatParticipant = createAction<string>(ADD_GROUP_CHAT_PARTICIPANT);
export const removeGroupChatParticipant = createAction<string>(REMOVE_GROUP_CHAT_PARTICIPANT);
export const setGroupChatClientName = createAction<string>(SET_GROUP_CHAT_CLIENTNAME);

export const setIsAutoplayRestricted = createAction<boolean>(SET_IS_AUTOPLAY_RESTRICTED);

// Reducer
const videochat = handleActions({
    [RESET_STORE]: (state: IVideochat, action: AnyAction) => getInitialInstanceData().videochat,

    [SET_QUALITY]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        const { BAD_CONN_MAX_RESOLUTION, BAD_CONN_MAX_RESOLUTION_DESKTOP } = config.ChatSettings;
        draft.qualityBoundaries.medium= payload
            ? BAD_CONN_MAX_RESOLUTION
            :BAD_CONN_MAX_RESOLUTION_DESKTOP;

        draft.qualityBoundaries.good= payload?500:1000;
    }),

    [OPEN_FULLSCREEN]: produce((draft: IVideochat, action: AnyAction) => {
        draft.isFullscreen=true;
    }),

    [CLOSE_FULLSCREEN]: produce((draft: IVideochat, action: AnyAction) => {
        draft.isFullscreen= false;
    }),

    [TOGGLE_FULLSCREEN]: produce((draft: IVideochat, action: AnyAction) => {
        draft.isFullscreen= !draft.isFullscreen;
    }),

    [SET_CAM2CAM_ACTIVE]: produce((draft: IVideochat, action: AnyAction) => {
        draft.cam2Cam.isActive=true;
    }),

    [SET_CAM2CAM_INACTIVE]: produce((draft: IVideochat, action: AnyAction) => {
        draft.cam2Cam.isActive= false;
    }),

    [SET_CAM2CAM_CAMERAS]: produce((draft: IVideochat, { payload }: Action<ICameraDevice[]>) => {
        draft.cam2Cam.cameras=payload;
    }),

    [SET_CAM2CAM_SELECTED_CAMERA]: produce((draft: IVideochat, { payload }: Action<string>) => {
        draft.cam2Cam.selectedCameraId= payload;
    }),

    [RESET_CAM2CAM_SELECTED_CAMERA]: produce((draft: IVideochat, _: AnyAction) => {
        draft.cam2Cam.selectedCameraId= getInitialInstanceData().videochat.cam2Cam.selectedCameraId;
    }),

    [CAM2CAM_MINIMIZE]: produce((draft: IVideochat, _: AnyAction) => {
        draft.cam2Cam.isMinimized=true;
    }),

    [CAM2CAM_MAXIMIZE]: produce((draft: IVideochat, _: AnyAction) => {
        draft.cam2Cam.isMinimized=false;
	}),

	[SET_CAM2CAM_STATUS]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.cam2Cam.isEnabled = payload;
	}),

    [SET_INFORMATION]: produce((draft: IVideochat, { payload }: Action<VideochatInformationType>) => {
        draft.informationType= payload;
    }),

    [RESET_INFORMATION]: produce((draft: IVideochat, _: AnyAction) => {
        draft.informationType= getInitialInstanceData().videochat.informationType;
    }),

    [SET_PRE_CHAT_INFORMATION]: produce((draft: IVideochat, { payload }: Action< PreChatInformationType>) => {
        draft.preChatInformationType= payload;
    }),

    [RESET_PRE_CHAT_INFORMATION]: produce((draft: IVideochat, action: AnyAction) => {
        draft.preChatInformationType= getInitialInstanceData().videochat.preChatInformationType;
    }),

    [OPEN_VIDEOCHAT]: produce((draft: IVideochat, action: AnyAction) => {
        draft.isOpen=true;
    }),

    [CLOSE_VIDEOCHAT]: produce((draft: IVideochat, action: AnyAction) => {
        draft.isOpen= false;
    }),

    [SET_TYPE]: produce((draft: IVideochat, { payload }: Action<VideoChatType>) => {
        let type: VideoChatType = payload;

    	// for CONTENT_PARTNERS_CHAT we need kind of a special treatment:
        // if vchat-core said it is in voyeur mode, we will use that info instead
		if (type === VideoChatType.CONTENT_PARTNERS_CHAT) {
			type = draft.isInVoyeurMode ? VideoChatType.VOYEUR_CHAT: VideoChatType.LIVECHAT;
		}

		// if vchat-core said it's a group chat, we will use that info instead
		if (draft.isGroupChat) {
			type = VideoChatType.GROUP_CHAT;
		}

		draft.type= type;
        draft.lastSuccessfulType = type;
    }),

    [RESET_TYPE]: produce((draft: IVideochat, action: AnyAction) => {
        draft.type= getInitialInstanceData().videochat.type;
    }),

    [SET_INITIAL_VIDEOCHAT_TYPE]: produce((draft: IVideochat, { payload }: Action<VideoChatType>) => {
        draft.initialVideoChatType= payload;
    }),

    [RESET_INITIAL_VIDEOCHAT_TYPE]: produce((draft: IVideochat, _: AnyAction) => {
        draft.initialVideoChatType= getInitialInstanceData().videochat.initialVideoChatType;
    }),

    [SET_VIDEOCHAT_TIME_LIMIT]: produce((draft: IVideochat, { payload }: Action<number>) => {
        draft.previousTimeLeft=draft.timeLeft;
        draft.timeLeft= payload;
    }),

    [SET_VIDEOCHAT_TIME_LIMIT_BELOW_THRESHOLD]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.isTimeLeftBelowThreshold= payload;
    }),

    [SET_LIVE_PREVIEW_TIME_LIMIT]: produce((draft: IVideochat, { payload }: Action<number>) => {
        draft.livePreviewTimeLeft= payload;
    }),

    [SET_LIVE_PREVIEW_TEXT_TIME_LIMIT]: produce((draft: IVideochat, { payload }: Action<number>) => {
        draft.livePreviewTextTimeLimit= payload;
        draft.livePreviewTextTimeLeft= payload;
    }),

    [SET_LIVE_PREVIEW_TEXT_TIME_LEFT]: produce((draft: IVideochat, { payload }: Action<number>) => {
        draft.livePreviewTextTimeLeft= payload;
    }),

    [SET_IS_MUTED_BY_MODEL]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.isMutedByModel= payload;
    }),

    [SET_PLAYING_STATUS]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.isPlaying= payload;
    }),

    [SET_INITIAL_RESOLUTION]: produce((draft: IVideochat, { payload }: Action<IResolution>) => {
        draft.initialResolution = payload;
    }),

    [SET_IS_MOBILE_MENU_OPEN]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.isMobileMenuOpen= payload;
    }),

    [SET_IS_MUTED_BY_CLIENT]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.isMutedByClient= payload;
    }),

    [SET_IS_INITIALLY_MUTED]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.isInitiallyMuted= payload;
    }),

    [DISPLAY_INITIALLY_MUTED_HINT]: produce((draft: IVideochat, action: AnyAction) => {
        draft.isInitiallyMutedHintDisplayed= true;
    }),

    [HIDE_INITIALLY_MUTED_HINT]: produce((draft: IVideochat, action: AnyAction) => {
        draft.isInitiallyMutedHintDisplayed= false;
    }),

    [SET_EXIT_EVENT]: produce((draft: IVideochat, { payload }: Action<IExitEvent>) => {
        draft.exitEvent= payload;
    }),

    [RESET_EXIT_EVENT]: produce((draft: IVideochat, action: AnyAction) => {
        draft.exitEvent= getInitialInstanceData().videochat.exitEvent;
    }),

    [SET_RESUME_CHAT_TIMESTAMP]: produce((draft: IVideochat, { payload }: Action<Date>) => {
        draft.resumeChatTimestamp= payload;
    }),

    [SET_SHOULD_HIDE_VIDEOCHAT_CONTROLS]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.shouldHideVideochatControls= payload;
    }),

    [SET_IS_GROUP_CHAT]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.isGroupChat= payload;
    }),

    [SET_IS_IN_VOYEUR_MODE]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
        draft.isInVoyeurMode= payload;
    }),

    [SET_GROUP_CHAT_PARTICIPANTS]: produce((draft: IVideochat, { payload }: Action<string[]>) => {
        draft.groupChatParticipants= payload;
    }),

    [SET_GROUP_CHAT_CLIENTNAME]: produce((draft: IVideochat, { payload }: Action<string>) => {
        draft.groupChatClientName = payload;
    }),

    [ADD_GROUP_CHAT_PARTICIPANT]: produce((draft: IVideochat, { payload }: Action<string>) => {
        draft.groupChatParticipants= draft.groupChatParticipants.concat(payload);
    }),

    [REMOVE_GROUP_CHAT_PARTICIPANT]: produce((draft: IVideochat, { payload }: Action<string>) => {
        const foundIndex = findIndex(draft.groupChatParticipants, participant => participant === payload);
        if (foundIndex >= 0) {
            draft.groupChatParticipants=
                filter(draft.groupChatParticipants, (participant, index) =>index !== foundIndex);
        }
    }),

	[SET_IS_AUTOPLAY_RESTRICTED]: produce((draft: IVideochat, { payload }: Action<boolean>) => {
		draft.isAutoplayRestricted= payload;
	}),

}, getInitialInstanceData().videochat);

export default videochat;
