import createSagaMiddleware from 'redux-saga';
import { applyMiddleware, compose, createStore } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';

import rootSaga from '../sagas/rootSaga';
import { RECEIVE_NOOP } from '../sagas/websockets/noop';
import { DECREASE_CURRENT_GIFT_QUANTITY } from './modules/session';
import rootReducer from './reducers/rootReducer';
import initialStore from './initialStore';

/** Check if we have redux dev tools active */
const { IS_REDUX_DEV_TOOLS_ACTIVE } = process.env;
const isReduxDevActive = IS_REDUX_DEV_TOOLS_ACTIVE === "true";

/** Saga Middleware */
const sagaMiddleware = createSagaMiddleware();

// @ts-ignore
const composeEnhancers = isReduxDevActive
    ? composeWithDevTools({ 
        actionsBlacklist: [RECEIVE_NOOP, DECREASE_CURRENT_GIFT_QUANTITY] 
    })
    : compose;

const enhancer = composeEnhancers(
    applyMiddleware(sagaMiddleware)
);

/** Store */
const store = createStore(
    rootReducer,
    initialStore,
    enhancer
);

sagaMiddleware.run(rootSaga);

export default store;
