import * as React from 'react';
import { ITranslation } from '../../../redux/store';
import { BaseButtonContent, FirstRowText } from '../../common/BaseButton/style';
import { PrivateChatButtonWrapper } from './style';
import utils from '../../../utils';
import BaseButtonContainer from '../../common/BaseButton';

interface IProps {
    shouldDisplayIcon?: boolean;
    isForActionSection?: boolean;
    translation: ITranslation;

    onClick: () => void;
}

const PrivateChatButton = (props: IProps) => (
    <BaseButtonContainer
        onClick={props.onClick}
        isForActionSection={props.isForActionSection}
    >
        <PrivateChatButtonWrapper>
			{utils.getShouldDisplayIcon(props.shouldDisplayIcon) &&
            	<i className={"-icon-webcam-private-full"} />
			}

			<BaseButtonContent>
				<FirstRowText>{props.translation.chat.privateChat}</FirstRowText>
			</BaseButtonContent>
        </PrivateChatButtonWrapper>
    </BaseButtonContainer>
);

export default PrivateChatButton;
