import * as React from 'react';
import { IEmoticon, ITranslation } from '../../../redux/store';
import WindowBoxContainer from '../../common/WindowBox';
import { EmoticonStyle, EmoticonWrapper } from './style';
import utils from "../../../utils";

export interface IEmoticonSectionProps {
    isOpen: boolean;
    translation: ITranslation;
    isResponsive?: boolean;
    emoticons: IEmoticon[];

    closeEmoticonSection: () => any;
    emoticonClick: (emoticon: string) => () => void;
}

const EmoticonSection = (props: IEmoticonSectionProps) => (
    <WindowBoxContainer
        isOpen={props.isOpen}
        title={props.translation.message.selectEmoticon}
        handleClose={props.closeEmoticonSection}
    >
        <EmoticonWrapper>
            {props.emoticons.map((emoticon: IEmoticon) => (
                <EmoticonStyle
                    key={emoticon.text}
                    className={utils.getEmoticonClass(emoticon)}
                    position={emoticon.position}
                    title={emoticon.text}
                    onClick={props.emoticonClick(emoticon.text)}
                >
                    {utils.emoticonCodeToPrintableChar(emoticon.code)}
                </EmoticonStyle>
            ))}
        </EmoticonWrapper>
    </WindowBoxContainer>
);

export default EmoticonSection;
