import { all } from 'redux-saga/effects';
import chatSagas from "./chat";
import fullScreenSagas from "./fullscreen";
import userEventsSagas from "./userEvents";
import analyticsSagas from "./analytics";

/**
 * Combines all the listening sagas and
 * it represents the entire middleware.
 */
export default function* rootSaga() {
    yield all([
        chatSagas(),
        fullScreenSagas(),
        userEventsSagas(),
        analyticsSagas()
    ]);
}