import React, { FC } from 'react';
import { Title, Subtitle, Wrapper, ModelTitle } from './style';

interface IProps {
    title: string;
    modelName: string;
    subtitle?: string;
}

export const MessageTypeHeader: FC<IProps> = (props: IProps) => (
    <Wrapper>
        <Title>{props.title}</Title> <ModelTitle>{props.modelName}</ModelTitle>
        {props.subtitle && <Subtitle>{props.subtitle}</Subtitle>}
    </Wrapper>
)

export default MessageTypeHeader;
