import { handleActions, Action } from "redux-actions";

import { createAction, createActionType } from "../utils";
import { IFeatures } from "../store";
import { getInitialInstanceData } from "../initialStore";
import { ActionIds } from "../interfaces";
import { RESET_STORE } from "./app";
import { AnyAction } from "redux";

const createFeatureActionType = createActionType(ActionIds.FEATURES, { isReduxAction: true });

// Actions
const SET_FEATURES = createFeatureActionType("SET_FEATURES");

// Action Creators
export const setFeatures = createAction<IFeatures>(SET_FEATURES);

// Reducer
const features = handleActions<IFeatures>({
    [RESET_STORE]: (state: IFeatures, action: AnyAction) => getInitialInstanceData().features,

    [SET_FEATURES]: (state: IFeatures, { payload }: Action<IFeatures>) => payload
}, getInitialInstanceData().features);

export default features;
