import * as React from 'react';
import { PictureOverlay, Picture, PictureWrapper } from './style';
import { ISimpleModelPictureProps } from "./types";

const SimpleModelPicture = (props: ISimpleModelPictureProps) => (
    <>
		{props.hasOverlay && <PictureOverlay size={props.size} />}
		
		<PictureWrapper {...props}>
			{props.picture && <Picture src={props.picture} />}
		</PictureWrapper>
	</>
);

export default SimpleModelPicture;
