import { isNull, isUndefined } from 'lodash';
import { ITimerStylingProps } from './style';
import Theme from "../../../themes/interfaces";

export const getTimeLeft = (totalTime: number, currentTime?: number): number => {
    return Math.round(
        !isNull(currentTime) && !isUndefined(currentTime)
            ? Math.max(0, totalTime - currentTime)
            : totalTime);
};

export const clearTickHandler = (handler?: any) => {
    if (handler) {
        clearInterval(handler);
    }
};

export const getLeftAngle = (totalTime: number, currentTime: number): number =>
    Math.min(180, Math.round(currentTime * 360 / totalTime));

export const getRightAngle = (totalTime: number, currentTime: number): number =>
    Math.max(0, Math.round(180 * (2 * currentTime / totalTime - 1)));

export const defaultTimerStylingProps = (theme: Theme, props?: ITimerStylingProps) => ({
    size: 64,
    thickness: 10,
    filledColor: theme.colors.timerFillColor,
    unfilledColor: theme.colors.timerUnfillColor,
    backgroundColor: theme.colors.timerBgColor,
    timerTextColor: theme.colors.timerTextColor,
    ...props
});