import { all } from 'redux-saga/effects';
import channelSagas from './channel';
import channelInfoSagas from './channelInfo';
import chatSagas from './chat';
import giftSagas from './gifts';
import handleErrorSagas from './handleError';
import historySagas from './history';
import imageSagas from './image';
import initSagas from './init';
import loginSagas from './login';
import messageSagas from './message';
import newChannelSagas from './newChannel';
import noopSagas from './noop';
import onlineStateSagas from './onlineState';
import queryBackendSagas from './queryBackend';
import apiEventsSagas from './apiEvents';


/**
 * Combines all the listening sagas and
 * it represents the entire middleware.
 */
export default function* rootSaga() {
    yield all([
        chatSagas(),
        channelSagas(),
        channelInfoSagas(),
        queryBackendSagas(),
        giftSagas(),
        historySagas(),
        initSagas(),
        onlineStateSagas(),
        imageSagas(),
        loginSagas(),
        handleErrorSagas(),
        newChannelSagas(),
        noopSagas(),
        messageSagas(),
		apiEventsSagas()
    ]);
}
