// Create the Context for sending the instanceId to all components
export const InstanceContext = React.createContext("");

// This is no normal whitespace!
export const CHAR_BEHIND_GIFTS = "";

export const versionNumber: string = process.env.VERSION_NUMBER || '';

/**
 * Convert the emoticon code got from the back end into the special character which can be inserted into HTML
 * @param code
 */
export const emoticonCodeToPrintableChar = (code: string) => {
	const codePoint = parseInt(code.substring(1), 16);
	return String.fromCodePoint(codePoint);
};

export const itemToRegex = (item: string): string => {
	return item
		.replace(')', '\\)')
		.replace('(', '\\(')
		.replace('?', '\\?')
		.replace('*', '\\*')
		.replace('|', '\\|')
		.replace('[', '\\[')
		.replace('/', '\\/');
};

export const getEmoticonsRegexp = (emoticons: IEmoticon[]): string => `(${getEmoticonsPattern(emoticons)})`;

export const getEmoticonsPattern = (emoticons: IEmoticon[]): string =>
	emoticons.reduce((prevResult: string, emoticon: IEmoticon, index: number) => (
		prevResult + itemToRegex(emoticon.text) + (emoticons.length - 1 === index ? '' : '\|')
	), '');

export const getEmoticonsShortcuts = (emoticons: IEmoticon[]) =>
	map(emoticons, (emoticon: IEmoticon) => emoticon.text);

export const getGiftEncoding = (gift: IGift): string => base64.encode(gift.id.toString());

export const getGiftDecoded = (id: string): number => parseInt(base64.decode(id), 10);

export const getRegexForMessage = (specialChars: string[]): string => {
	const regexSeparator = "|";

	let pattern = "";
	forEach(specialChars, (chars: string) => {
		pattern += itemToRegex(chars) + regexSeparator;
	});

	/** Remove the regex separator from our pattern's end */
	pattern = pattern.substring(0, pattern.length - 1);

	return pattern;
};

/**
 * Get the CSS class to be used for an emoticon
 * @param emoticon
 */
export const getEmoticonClass = (emoticon: IEmoticon) => emoticon.position ? 'emoticon-background' : 'emoticon';

export const getSpecialCharactersRegex = (
	emoticons: IEmoticon[],
	giftIcons: string[] = [],
	mentions: string[] = []
): RegExp => {
	const regexSeparator = "|";
	let specialCharactersPattern = "";

	/** Compose the pattern for emoticons */
	specialCharactersPattern += getEmoticonsPattern(emoticons);

	if (!isEmpty(giftIcons)) {
		specialCharactersPattern += `${regexSeparator}${getRegexForMessage(giftIcons)}`;
	}
	if (!isEmpty(mentions)) {
		specialCharactersPattern += `${regexSeparator}${getRegexForMessage(mentions)}`;
	}

	return new RegExp(`(${specialCharactersPattern})`, "gim");
};

/**
 * Get the translated name for gift's name
 * @param gift IGift interface for the concept of gift
 * @param language SupportedLanguage interface, for instance: "en", "de"
 */
export const getTranslatedGiftName = (gift: IGift, language: SupportedLanguage): string => {
	const giftTranslation = find(gift.translations,
		(translation: IGiftTranslation) => translation.language === language
	);

	return giftTranslation ? giftTranslation.value : gift.name;
}

export const findEmoticonWithRegex = (text, pattern, callback) => {
	let matchArr;
	let start;
	const regexp = new RegExp(pattern, 'gi');

	while ((matchArr = regexp.exec(text)) !== null) {
		start = matchArr.index;
		callback(start, start + matchArr[0].length);
	}
};

/**
 * Being given a translated template and a key: value dictionary,
 * it replaces all '{key}' occurrences in template with the actual provided values
 * @param translation
 * @param values
 */
export const translationWithValues = (translation: string, values: { [key: string]: string | number | undefined }) => (
	Object.keys(values).reduce((accumulator, key) =>
			accumulator.replace(new RegExp(`\{${key}\}`, "g"), `${get(values, key, '')}`),
		translation || '')
);

/**
 * Return the timestamp based on the value and time unit (days/weeks/month)
 */
export const getTimestamp = (value: number | Date, timeUnit?: "days" | "weeks" | "month"): number => {
	const timeFormat = "YYYY-MM-DD";

	return timeUnit && typeof value === "number"
		? moment(moment().add(value as number, timeUnit).format(timeFormat)).valueOf()
		: moment(moment(value).format(timeFormat)).valueOf();
};

export const getSimplifiedDate = (date: string): string => moment(date).format("DD/MM/YYYY");

export const checkIfMessageIsConsecutive = (messages: IMessage[], id: string) => {
	const messageIndex = findIndex(messages, m => m.id === id);

	// The first message received can't be considered consecutive
	if (messageIndex === 0) {
		return false;
	}

	const currentMessage = messages[messageIndex];
	const prevMessage = messages[messageIndex - 1];

	if (isEmpty(currentMessage) || isEmpty(prevMessage)) {
		return false;
	}

	// System messages are not part of the algorithm for consecutive messages
	if (currentMessage.isSystem || prevMessage.isSystem) {
		return false;
	}

	// Messages that have not been written in the same day
	// can't be considered consecutive messages
	if (!isEqual(
		getSimplifiedDate(currentMessage.dateCreated),
		getSimplifiedDate(prevMessage.dateCreated)
	)) {
		return false;
	}

	const isTextSession = prevMessage.msgType === MsgType.MESSENGER
		&& currentMessage.msgType === MsgType.MESSENGER;

	const hasSameChatSession = prevMessage.msgType === MsgType.CHAT
		&& currentMessage.msgType === MsgType.CHAT
		&& isEqual(prevMessage.chatId, currentMessage.chatId);

	// Messages from different livechat/text sessions can be compared
	// and considered to be consecutive
	if (!isTextSession && !hasSameChatSession) {
		return false;
	}

	// If has the same username with the previous message
	const isConsecutiveFromUser = isEqual(prevMessage.username, currentMessage.username)
		&& !isEmpty(currentMessage.username);

	// The model has no value for the username property, but the message direction is in
	const isConsecutiveFromModel = currentMessage.direction === MessageDirection.in
		&& prevMessage.direction === MessageDirection.in
		&& isEmpty(currentMessage.username)
		&& isEmpty(prevMessage.username);

	// The client is the only entity with message's direction to be out
	const isConsecutiveFromClient = currentMessage.direction === MessageDirection.out
		&& prevMessage.direction === MessageDirection.out;

	return isConsecutiveFromUser || isConsecutiveFromModel || isConsecutiveFromClient;
};

export const checkIfMessageIsLastConsecutive = (messages: IMessage[], id: string) => {
	if (!checkIfMessageIsConsecutive(messages, id)) {
		return false;
	}

	const messageIndex = findIndex(messages, m => m.id === id);
	const nextMessage = messages[messageIndex + 1];

	if (!nextMessage) {
		return true;
	}

	return !checkIfMessageIsConsecutive(messages, nextMessage.id);
};

export const checkIfMessageIsSolo = (messages: IMessage[], id: string) => {
	if (checkIfMessageIsConsecutive(messages, id)) {
		return false;
	}

	const messageIndex = findIndex(messages, m => m.id === id);
	const nextMessage = messages[messageIndex + 1];

	if (!nextMessage) {
		return true;
	}

	return !checkIfMessageIsConsecutive(messages, nextMessage.id);
};

/**
 * Return the array of timestamps for all the groups from the chat box
 */
export const getGroupTimestamps = (): { [key: string]: number } => {
	const days = "days";
	const weeks = "weeks";
	const months = "month";

	const groupTimestamps: { [key: string]: number } = {};
	groupTimestamps[GroupTimestamp.SIXMONTHSAGO] = getTimestamp(-6, months);
	groupTimestamps[GroupTimestamp.THREEMONTHSAGO] = getTimestamp(-3, months);
	groupTimestamps[GroupTimestamp.TWOMONTHSAGO] = getTimestamp(-2, months);
	groupTimestamps[GroupTimestamp.ONEMONTHAGO] = getTimestamp(-1, months);
	groupTimestamps[GroupTimestamp.THREEWEEKSAGO] = getTimestamp(-3, weeks);
	groupTimestamps[GroupTimestamp.TWOWEEKSAGO] = getTimestamp(-2, weeks);
	groupTimestamps[GroupTimestamp.ONEWEEKAGO] = getTimestamp(-1, weeks);
	groupTimestamps[GroupTimestamp.YESTERDAY] = getTimestamp(-1, days);
	groupTimestamps[GroupTimestamp.TODAY] = getTodayTimestamp();

	return groupTimestamps;
};

export const getOrganizedMessages = (
	messages: IMessage[],
	currentChatId?: string
): { [index: string]: (IGroupMessage | IMessage)[] } => {
	const messagesGroupedByDay = groupBy(messages, (message: IMessage | IGroupMessage) => moment(message.dateCreated).format('YYYY-MM-DD'));

	Object.keys(messagesGroupedByDay).forEach(key => {
			let organizedMessages: (IGroupMessage | IMessage)[] = [];
			const groupedMessages = groupBy(messagesGroupedByDay[key], (message: IMessage) => message.chatId);

			forEach(groupedMessages, (group: IMessage[]) => {
				const referenceMessage = group[0];

				const isSystemMessage = referenceMessage.msgType === MsgType.SYSTEM;
				const isMessageOfCurrentChat = isEqual(referenceMessage.chatId, currentChatId);

				const isChatGroup = !isMessageOfCurrentChat
					&& (group.length > 1 || (isSystemMessage && !isUndefined(referenceMessage.chatId)));

				if (isChatGroup) {
					organizedMessages.push({
						dateCreated: group[0].dateCreated,
						messages: group
					});
				} else {
					organizedMessages = organizedMessages.concat(group);
				}
			});

			organizedMessages = sortBy(organizedMessages, message => moment(message.dateCreated));

			messagesGroupedByDay[key] = organizedMessages;

		}
	);

	return messagesGroupedByDay;
};

export const isGroupMessage = (message: (IMessage | IGroupMessage)) => has(message, 'messages');

export const getTimestampForMessage = (dateCreated: string) => moment(dateCreated).toDate().getTime();

/**
 * Timestamp for today, returns the timestamp for it
 */
export const getTodayTimestamp = () => getTimestamp(new Date());

/**
 * Returns timestamp in which the value is framing in. The default is the
 * largest timestamp possible - six months.
 */
export const getGroupTimestampForMessage = (value: number): number => {
	const groupTimestamps = getGroupTimestamps();

	for (const timestamp in GroupTimestamp) {
		if (value >= groupTimestamps[timestamp]) {
			return groupTimestamps[timestamp];
		}
	}

	return groupTimestamps[GroupTimestamp.SIXMONTHSAGO];
};

/**
 * Format a number to have two decimals; useful for money amounts
 * @param value
 */
export const withTwoDecimals = (value: number) => Number(value).toFixed(2);

/**
 * Deep diff between two object, using lodash; useful for debugging purposes
 * @param  {Object} object Object compared
 * @param  {Object} base   Object to compare with
 * @return {Object}        Return a new object who represent the diff
 */
export const getDifference = (object, base) => transform(object, (result: any, value, key) => {
	if (!isEqual(value, base[key])) {
		result[key] = isObject(value) && isObject(base[key])
			? getDifference(value, base[key])
			: value;
	}
});

/**
 * Reconcile the existing messages with the ones we got when calling websockets for history
 * @param existingMessages
 * @param historyCommands
 * @param recentMessagesMaxLimit
 * @return messages to be added, if any
 */
export const reconcileMessages = (
	existingMessages: IMessage[],
	historyCommands: CMDC_CMSG[],
	recentMessagesMaxLimit: number,
	gifts: IGift[]
): IMessage[] => {

	/** Apply pagination */
	let commands = historyCommands.slice(existingMessages.length);
	commands = reverse(commands);
	commands = takeRight(commands, recentMessagesMaxLimit);

	/** If there are no more history messages, do not dispatch redux actions anymore */
	if (isEmpty(commands)) {
		return [];
	}

	return map(commands, (command: CMDC_CMSG) => {
		const params = command.params;
		const isAudio = !isEmpty(params.mediaType) && params.mediaType === "Audio";
		const isImage = isEmpty(params.text);
		const isImageUrl = !isUndefined(params.imgSrc) && !isUndefined(params.link);
		let contentType: MessageContentType;
		let content;

		if (isAudio) {
			contentType = MessageContentType.AUDIO;
			content = {
				audioM4a: params.audioM4a,
				audioMp3: params.audioMp3,
				audioOgg: params.audioOgg
			};
		} else if (isImageUrl) {
			contentType = MessageContentType.IMAGE_URL;
			content = {
				text: getReceivedTextMessage(gifts, params.text, isMessageFromChat(params)),
				imgSrc: params.imgSrc,
				link: params.link
			}
		} else if (isImage) {
			contentType = MessageContentType.IMAGE;
			content = params.imgSrc;
		} else {
			contentType = MessageContentType.TEXT;
			content = getReceivedTextMessage(gifts, params.text, isMessageFromChat(params));
		}

		// Get the CDN address for the original(big) picture
		let modalContent: IModalContent | undefined;

		if (params.imgSrcSet) {
			modalContent = {
				imgSrc: params.imgSrc || '',
				imgSrcSet: params.imgSrcSet,
				imgWidth: params.imgWidth,
				imgHeight: params.imgHeight
			}
		}

		const partialMessage: IMessage = {
			id: params.messageId || cuid.slug(),
			isSystem: params.msgType === "sys",
			messageKey: params.messageKey || cuid.slug(),
			dateCreated: getDateCreatedForMessage(params.time as string),
			direction: params.direction === EnumMessageDirection.IN
				? MessageDirection.in
				: MessageDirection.out,
			isDeleted: params.isDeleted === "1",
			isFromHistory: true,
			modalContent,
			contentType,
			content
		};

		if (params.chatID) {
			partialMessage.chatId = params.chatID;
		}

		if (params.msgType) {
			partialMessage.msgType = params.msgType as any as MsgType;
		}

		return partialMessage;
	});
};

export const hashify = (object: any): string => {
	return JSON.stringify(object);
};

export const isMobilePhone = (isMobile: boolean, isTablet: boolean) => isMobile && !isTablet;

/** This function works only on Android devices */
export const isMobileOrientationLandscape = (isTablet: boolean = false) => {
	const orientationAngle: number = Math.abs((window.screen as any).orientation.angle);

	return orientationAngle === 90 || orientationAngle === 270;
};

/** It may apply for both gifts and toy options */
export const encodeForChatCommunication = (item: IGift | IToyOption) => `[${item.name}]`;

/** It will apply only for gifts - there are no toy options without chat */
const encodeForMessengerCommunication = (gift: IGift) => `${gift.id}_${gift.name}`;

/**
 * Replaces the gifts encodings (based on their channel of communication) with our
 * base64.encode(giftId) and return the new message to be stored in Redux.
 *
 * @param gifts - the array with all the active gifts that maybe were set
 * @param message the text sent
 * @param isFromChat a flag to apply the correct decoding of the message
 */
export const getReceivedTextMessage = (
	gifts: IGift[],
	message: string = "",
	isFromChat: boolean
) => {
	let text = message;

	forEach(gifts, (gift: IGift) => {
		if (isFromChat) {
			let chatEncoding = encodeForChatCommunication(gift);
			chatEncoding = replace(chatEncoding, "[", "\\[");
			chatEncoding = replace(chatEncoding, "]", "\\]");
			chatEncoding = replace(chatEncoding, "-", CHAR_BEHIND_GIFTS);
			text = replace(text, "-", CHAR_BEHIND_GIFTS);

			text = replace(
				text,
				new RegExp(chatEncoding, "gm"),
				base64.encode(gift.id.toString())
			);

			text = replace(text, CHAR_BEHIND_GIFTS, "-");
		} else {
			text = replace(text,
				new RegExp(encodeForMessengerCommunication(gift), "gm"),
				base64.encode(gift.id.toString())
			);
		}
	});

	return text;
};

/**
 * Replaces the gifts with their encoding based on the channel of communication
 * and returns the new modified message to be sent to the model.
 * @param gifts
 * @param message
 * @param useChat
 */
export const getTextMessageToSend = (gifts: IGift[], message: string, useChat: boolean): string => {
	let giftIdRegex: any;
	let patternToReplaceWith: any;
	let text = message;

	forEach(gifts, (gift: IGift) => {
		giftIdRegex = new RegExp(getGiftEncoding(gift), "g");

		patternToReplaceWith = useChat
			? encodeForChatCommunication(gift)
			: encodeForMessengerCommunication(gift);

		text = text.replace(giftIdRegex, patternToReplaceWith);
	});

	return text;
};

/** Returns the array of gift ids from a message text in their correct order */
export const getGiftsFromMessage = (gifts: IGift[], message: string): number[] => {
	let result;
	let giftIdRegex;
	const giftIds: { position: number, id: number }[] = [];

	forEach(gifts, (gift: IGift) => {
		giftIdRegex = new RegExp(getGiftEncoding(gift), "g");
		const matches = message.match(giftIdRegex);

		if (!isEmpty(matches)) {
			while (result = giftIdRegex.exec(message)) {
				giftIds.push({
					id: gift.id,
					position: result.index
				});
			}
		}
	});

	return chain(giftIds)
		.sortBy(giftId => giftId.position)
		.map(giftId => giftId.id)
		.value() as number[];
};

/** A JOIN between giftCategories and gifts coming from the INIT request */
export const getGiftPages = (session: ISession, language: SupportedLanguage, bundleTabTitle: string): IGiftPage[] => {
	const { gifts, giftCategories, giftBundles } = session;

	const translatedGifts = map(gifts, (gift: IGift) => ({
		...gift,
		name: getTranslatedGiftName(gift, language)
	}));

	/**
	 * Gift pages hover text should be translated
	 */
	const giftPages: IGiftPage[] = map(giftCategories, (category: IGiftCategory) => {
		const translation = getItemTranslation(language, category.translations);

		return ({
			id: category.id,
			name: translation.value,
			translations: category.translations,
			gifts: filter(translatedGifts,
				(gift: IGift) => gift.categoryId === category.id && gift.active
			)
		});
	});

	const inventoryGifts: IGift[] = filter(translatedGifts,
		(gift: IGift) => !!gift.initialQuantity && gift.initialQuantity > 0
	);

	if (!isEmpty(inventoryGifts)) {
		giftPages[0].gifts = inventoryGifts;
	}

	const { giftBundlesId } = config.specialGiftPages;

	/** Add the gift bundles as the last gift page => the gifts name should be translated */
	const giftBundlePage: IGiftPage = {
		id: giftBundlesId,
		name: bundleTabTitle,
		translations: [],
		gifts: map(giftBundles, (bundle: IBundle) => {

			const translation = getItemTranslation(language, bundle.translations);

			return ({
				id: bundle.id,
				active: bundle.active,
				categoryId: giftBundlesId,
				name: translation.value,
				imageUrl: bundle.imageUrl,
				translations: bundle.translations,
				initialPrice: getInitialBundlePrice(translatedGifts, bundle),
				price: bundle.price
			});
		})
	};

	giftPages.push(giftBundlePage);

	/** Return only the non-empty gift pages */
	return filter(giftPages, (page: IGiftPage) => !isEmpty(page.gifts));
};

const getInitialBundlePrice = (gifts: IGift[], bundle: IBundle): number => {
	let initialPrice = 0;

	forEach(bundle.gifts, (giftFromBundle: IBundleGift) => {
		const gift = find(gifts, (g: IGift) => g.id === giftFromBundle.gift.id);

		if (gift) {
			initialPrice += giftFromBundle.quantity * gift.price;
		}
	});

	return initialPrice;
};

