export const getPreselectedOneClickAmount =
	(amounts: number[], expectedAmount = config.mostCommonRechargeValue): number => {
		let retVal = 0;
		if (!isEmpty(amounts)) {
			retVal = amounts.reduce((acc, item) => {
				const origDiff = Math.abs(acc - expectedAmount);
				const currDiff = Math.abs(item - expectedAmount);

				if (currDiff < origDiff) {
					acc = item;
				}

				return acc;
			}, Math.max.apply(null, amounts));
		}

		return retVal;
	};

/**
 * Get the containing DOM node for the entire VXMessenger;
 * Caveat: it assumes that only one messenger is open at a certain moment
 * FUTURE TODO: try to get the node in a more Reactful way;
 * currently having troubles with styled-components and React Ref
 */
export const getMessengerNode = (instanceId: string): Element | undefined =>
	document.querySelector(
		`.${config.messengerClassName}[${config.messengerInstanceIdAttribute}="${instanceId}"]`
	) || undefined;

export const getWindowDimensions = (): IResolution => {
	// kind of an workaround for this bug: https://bugs.webkit.org/show_bug.cgi?id=170595
	const windowSizeIsBogus = isIOS && !isSafari;
	const documentElement = document.documentElement;
	const body = document.getElementsByTagName('body')[0];

	const width = (windowSizeIsBogus ? 0 : window.innerWidth)
		|| (documentElement && documentElement.clientWidth) || body.clientWidth;
	const height = (windowSizeIsBogus ? 0 : window.innerHeight)
		|| (documentElement && documentElement.clientHeight) || body.clientHeight;

	return { width, height }
};

export const isMessageFromChat = (message: MessageParams) => !isEmpty(message.chatID);

export const isOutOfMoneyInformationDialog = (informationType: VideochatInformationType) => {
	const { OUT_OF_MONEY, OUT_OF_MONEY_ONE_CLICK_RECHARGE } = VideochatInformationType;

	return includes(
		[OUT_OF_MONEY, OUT_OF_MONEY_ONE_CLICK_RECHARGE],
		informationType
	);
};

export const isLoadAccountUserPrompt = (userPromptType: QuestionType) => {
	const { LOAD_ACCOUNT, LOAD_ACCOUNT_AUTO_RECHARGE, LOAD_ACCOUNT_ONE_CLICK } = QuestionType;

	return includes(
		[LOAD_ACCOUNT, LOAD_ACCOUNT_AUTO_RECHARGE, LOAD_ACCOUNT_ONE_CLICK],
		userPromptType
	);
};

/**
 * Get the desired value based on the current language.
 */
export const getValueByLanguage = (
	language: string | SupportedLanguage,
	values = SupportedLanguage
) => {
	const formattedLanguage = toUpper(language) || SupportedLanguage.EN;
	return values[formattedLanguage] || values.EN;
};

/**
 * Get the translation based on the current language.
 */
export const getTranslation = (
	language: SupportedLanguage,
	storedTranslations: Translations
) => storedTranslations[language] || storedTranslations.en;

/**
 * Get the name translated for different back-end item's based on the current language
 * (examples: gift's name, gift bundle's name or toy control option's name)
 */
export const getItemTranslation = (
	language: SupportedLanguage,
	translations: IGiftTranslation[]
): IGiftTranslation => {
	const defaultTranslation = find(translations,
		giftTranslation => giftTranslation.language === SupportedLanguage.EN
	) as IGiftTranslation;

	return find(translations,
		giftTranslation => giftTranslation.language === language
	) || defaultTranslation;
};

/**
 * Checks if the chat was reseted before/20 minutes ago;
 */
export const shouldResetChat = (timestamp: Date) => {
	if (!timestamp) {
		return true;
	}

	const timestampDifference = (new Date()).getTime() - timestamp.getTime();

	return (timestampDifference / 60000) >= constants.resetChatInterval;
};


/**
 * Counts the clicks number, if intervalBetweenClicks is reached clickCouter is reseted.
 */
export const clickCounter = (intervalBetweenClicks: number) => {
	let lastClickTimestamp: Date;
	let clickCount: number = 1;

	const onClick = () => {
		const currentTimestamp: Date = new Date();

		if (lastClickTimestamp instanceof Date) {
			const timeElapsed = currentTimestamp.getTime() - lastClickTimestamp.getTime();
			clickCount = timeElapsed > intervalBetweenClicks ? 1 : clickCount + 1;
		}

		lastClickTimestamp = currentTimestamp;
	};

	const getClickCount = () => clickCount;

	return { onClick, getClickCount };
};

/**
 * Used to calculate the delay of transition group for user prompts
 * we need 2xtimeInSeconds. Because it needs to wait timeInSeconds before the transition
 * and timeInSeconds to execute de transition
 */
export const getTransitionTimeInMilliseconds = (timeInSeconds: number) => timeInSeconds * 2 * 1000;

/**
 * It verifies if the hook given as param has been configured by the parent platform
 */
export const hasHookConfigured = (hook: any) => !isUndefined(hook);

/**
 * After replacing all gifts with just one character and emoticons with the length
 * of their shortcut, we will check if the message exceeds the text max limit set in config.
 * @param text
 */
export const hasExceededTextLimit = (text: string): boolean => text.length > config.maxTextCharLimit;

/**
 * If the message is NOT of type TOY, we will return it unaltered.
 * If the message is of type TOY, we will update some properties.
 */
export const convertToToyMessage = (
	message: IMessage,
	toyOption: IToyOption,
	translation: IToyControlTranslation
): IMessage => {

	const { lovenseFinished, lovenseVibrating } = translation;
	const text = toyOption.isPlaying ? lovenseVibrating : lovenseFinished;

	return ({
		...message,
		content: null,
		msgType: MsgType.TOY,
		direction: MessageDirection.none,
		isSystem: false,
		toy: {
			text,
			activeImage: toyOption.imageUrl,
			inactiveImage: toyOption.imageUrlDeactivated,
			isActive: toyOption.isPlaying,
			isDefault: false
		}
	});
};

export const getCameraCloseButton = () => {
	const cameraCloseButton = document.createElement('i');
	cameraCloseButton.className = '-icon-close-full';
	cameraCloseButton.setAttribute('style',
		"font-size: 14px; cursor: pointer; color: #888");

	return cameraCloseButton;
};

export const getFormattedDuration = (duration: number): string => {
	const seconds = `0${Math.round(duration % 60)}`.slice(-2);
	const minutes = `${Math.floor(duration / 60)}`;

	return `${minutes}:${seconds}`
};

export const queryString = params => Object.keys(params).map(key => `${key}=${params[key]}`).join('&');

/**
 * Being given an error code, it returns the type of the Pre-Chat Information Dialog to be displayed,
 * or undefined if none should be displayed
 * @param code
 */
export const getPrechatByErrorCode: (code: ExitCode | VXControlCodes | MessengerSpecificErrorCodes)
	=> PreChatInformationType | undefined
	= (code: ExitCode | VXControlCodes) => {

	const codesWithoutPrechat: (ExitCode | VXControlCodes | MessengerSpecificErrorCodes)[] = [
		VXControlCodes.NO_CREDIT_VIDEOCHAT
	];

	if (includes(codesWithoutPrechat, code)) {
		return;
	}

	const codesMapping: { [key: number]: PreChatInformationType } = {};
	codesMapping[MessengerSpecificErrorCodes.GUEST_OFFLINE] = PreChatInformationType.GUEST_OFFLINE;

	codesMapping[ExitCode.PREVIEW_OFFLINE] = PreChatInformationType.HOST_OFFLINE;
	codesMapping[VXControlCodes.MODEL_IS_OFFLINE] = PreChatInformationType.HOST_OFFLINE;

	codesMapping[ExitCode.HOSTSINGLE] = PreChatInformationType.PRIVATE_CHAT;
	codesMapping[ExitCode.PREVIEW_SINGLE] = PreChatInformationType.PRIVATE_CHAT;
	codesMapping[VXControlCodes.MODEL_IS_IN_PRIVATE_CHAT] = PreChatInformationType.PRIVATE_CHAT;

	codesMapping[ExitCode.PREVIEW_ACCESS_DENIE] = PreChatInformationType.CHARGE_ACCOUNT_OR_VIP;

	codesMapping[VXControlCodes.PREVIEW_LIMIT_EXCEEDED] = PreChatInformationType.LIMIT_REACHED;
	codesMapping[ExitCode.PREVIEW_LIMIT_HOUR] = PreChatInformationType.LIMIT_REACHED;
	codesMapping[ExitCode.PREVIEW_LIMIT_HOST] = PreChatInformationType.LIMIT_REACHED;

	codesMapping[ExitCode.FSKDENIED] = PreChatInformationType.VERIFY_AGE;
	codesMapping[ExitCode.PREVIEW_FSK_DENIED] = PreChatInformationType.VERIFY_AGE;
	codesMapping[VXControlCodes.SOFT_CHAT] = PreChatInformationType.VERIFY_AGE;

	codesMapping[ExitCode.PREVIEW_DISABLED] = PreChatInformationType.NOT_AVAILABLE;

	codesMapping[ExitCode.ERROR] = PreChatInformationType.CONNECTION_ERROR;

	return codesMapping[code] || PreChatInformationType.CONNECTION_ERROR;
};

/**
 * Renders the color with which the username should be renderd during
 * a group chat mode. It will return the color with fewest active usernames.
 * @param colors all the group chat colors available for usage
 */
export const getColorForUsername = (colors: IGroupChatColor[], username?: string): string => {
	const alreadyDefinedColor = find(colors, groupColor =>
		!isEmpty(find(groupColor.users, coloredUsername => isEqual(coloredUsername, username))
		));

	return alreadyDefinedColor?.color ||
		chain(colors)
			.sortBy(groupColor => size(groupColor.users))
			.map(groupColor => groupColor.color)
			.first()
			.valueOf() as string;
};


export const shouldOpenMentionSection = (participants: string[], query: string, isMentionSectionOpen: boolean) => {
	if (participants.includes(query) && !isMentionSectionOpen) {
		return false;
	}

	return !isEmpty(find(participants, participant => participant.startsWith(query)));
};

export const hasActiveMentions = (participants: string[], query: string) =>
	!isEmpty(find(participants, participant => participant.startsWith(query)));

export const computeMessageFontSize = (shouldIncreaseMessageFontSize, fontSize) =>
	shouldIncreaseMessageFontSize ? `calc(${fontSize}px + ${constants.messageFontSizePercentage}%)` : `${fontSize}px`;

export const defaultPreferenceStore: IPreferenceStore = {
	getPreference: (preferenceName: PreferenceName, instanceId?: string) => {
		const preferencesKey = `${instanceId || constants.defaultInstanceId}_preferences`;
		let preferences = localStorage.getItem(preferencesKey);

		if (preferences) {
			preferences = JSON.parse(preferences);
			if (preferences) {
				const preferenceValue = preferences[preferenceName];

				if (preferenceValue) {
					if (preferenceName === PreferenceName.VolumeLevel) {
						return parseFloat(preferenceValue);
					} else if (preferenceName === PreferenceName.ShouldIncreaseMessageFontSize) {
						return preferenceValue;
					}
				}
			}
		}

		return undefined;
	},
	setPreference: (preferenceName: PreferenceName, value: boolean | number, instanceId?: string) => {
		const preferencesKey = `${instanceId || constants.defaultInstanceId}_preferences`;
		let preferences = localStorage.getItem(preferencesKey);

		if (preferences) {
			preferences = JSON.parse(preferences);

			if (preferences) {
				preferences[preferenceName] = value;
			}

			localStorage.setItem(preferencesKey, JSON.stringify(preferences));
		} else {
			localStorage.setItem(preferencesKey, JSON.stringify({
				[preferenceName]: value
			}));
		}
	}
}
