import * as React from 'react';
import { ICountdownWarning, CountdownWarningType, ITranslation } from '../../../redux/store';
import Timer from '../../common/Timer';
import { CountdownWarningContentWrapper, CountdownWarningWrapper } from './style';
import config from "../../../static/config";

export interface ICountdownWarningProps {
    translation: ITranslation;
    warning: ICountdownWarning;
    isResponsive: boolean;
    isFullscreen: boolean;

    onAnswer: () => any;
    onTimeIsOver?: () => any;
    updateCountdownWarningTime?: (time: number) => any;
}

/**
 * Stateless component used to deal with various types of countdown warnings
 * @param props
 * @constructor
 */
const CountdownWarning = (props: ICountdownWarningProps) => {
    const { warning: { type, time } } = props;
    const { lowBalance, chargeNow } = props.translation.chat;
    const totalTime = config.videoChatTimeLimits.liveChatStartWarning;

    /** Translations for warnings */
    const warningTranslations: { [warningType: string]: string } = {};
    warningTranslations[CountdownWarningType.OUT_OF_BALANCE] = lowBalance;
    warningTranslations[CountdownWarningType.ONE_CLICK_OUT_OF_BALANCE] = lowBalance;

    const rechargeNowLink = <a
        onClick={props.onAnswer}
        className="rechargeNowLink"
    >
        {chargeNow}
    </a>;

    const outOfBalanceWarning = (
        <>
            <Timer
                currentTime={totalTime - time}
                totalTime={totalTime}
                onUnmount={props.updateCountdownWarningTime}
                onTimeIsOver={props.onTimeIsOver}
            />
            <span className="textWarning">
                <span>
                    {warningTranslations[type]}
                    <span className="rechargeNowWrapper">{rechargeNowLink}</span>
                </span>
            </span>
        </>
    );

    const warnings: { [warningType: string]: any } = {};
    warnings[CountdownWarningType.OUT_OF_BALANCE] = outOfBalanceWarning;
    warnings[CountdownWarningType.ONE_CLICK_OUT_OF_BALANCE] = outOfBalanceWarning;

    return (
        <CountdownWarningWrapper
            isFullscreen={props.isFullscreen}
            isResponsive={props.isResponsive}
            warning={props.warning}
            translation={props.translation}
        >
            {props.warning &&
                <CountdownWarningContentWrapper>
                    {warnings[type]}
                </CountdownWarningContentWrapper>
            }
        </CountdownWarningWrapper>
    );
};

export default CountdownWarning;
