import * as React from 'react';

import { onButtonClick } from './utils';
import AVSCam2CamPopup from "./AVSCam2CamPopup";
import selectors from '../../../redux/selectors';
import IStore from '../../../redux/store';
import { toggleCam2Cam } from "../../../sagas/common/videochat/cam2Cam";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps } from "./types";

const AVSCam2CamPopupContainer = (props: IProps) => {
    const { hint, requestAccess, allowAccess } = props.translation.avs;

    return (
        <>
            {props.shouldRenderAVSCam2CamPopup && (
                <AVSCam2CamPopup
                    smallText={hint}
                    mainText={requestAccess}
                    buttonText={allowAccess}
                    onButtonClick={onButtonClick(props)}
                />
            )}
        </>
    );
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    translation: selectors.getTranslation(store, instanceId),
    shouldRenderAVSCam2CamPopup: selectors.shouldRenderAVSCam2CamPopup(store, instanceId)
});

const mapDispatchToProps = {
    toggleCam2Cam
};

export default withRedux(AVSCam2CamPopupContainer, mapStateToProps, mapDispatchToProps);
