import * as React from 'react';

import MobileMenu from './MobileMenu';
import selectors from '../../../redux/selectors';
import { startVideoChatType } from '../../../sagas/common/videochat';
import { setIsMobileMenuOpen } from '../../../redux/modules/videochat';
import { toggleCam2Cam } from '../../../sagas/common/videochat/cam2Cam';
import IStore from '../../../redux/store';
import { onToyButtonClick, onActionButtonClick, toggleGiftSection } from './utils';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps } from "./types";

import {
    closeGiftSection,
    closeMessenger,
    openGiftSection,
    toggleToySection,
    requestImageUpload
} from '../../../redux/modules/messenger';

const applyToggleCam2Cam = (props: IProps) => () => {
    props.toggleCam2Cam(props.instanceId);
};

const requestUpload = (props: IProps) => () => {
    props.requestImageUpload(props.instanceId);
};

const close = (props: IProps) => () => {
    props.closeMessenger(props.instanceId);
};

const MobileMenuContainer = (props: IProps) => (
    <MobileMenu
        translation={props.translation}
        buttonsVisibility={props.buttonsVisibility}
        isGiftButtonVisible={props.isGiftButtonVisible}
        isToyButtonVisible={props.isToyButtonVisible}
        hasImageUpload={props.hasImageUpload}

        switchVideoChatMode={onActionButtonClick(props)}
        toggleCam2Cam={applyToggleCam2Cam(props)}
        toggleGiftSection={toggleGiftSection(props)}
        toggleToySection={onToyButtonClick(props)}
        requestImageUpload={requestUpload(props)}
        closeMessenger={close(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    translation: selectors.getTranslation(store, instanceId),
    isGiftSectionOpen: selectors.isGiftSectionOpen(store, instanceId),
    isGiftButtonVisible: selectors.isGiftButtonVisible(store, instanceId),
    isToySectionOpen: selectors.isToySectionOpen(store, instanceId),
    isToyButtonVisible: selectors.hasToyControlButton(store, instanceId),
    hasImageUpload: selectors.hasImageUpload(store, instanceId),

    buttonsVisibility: {
        liveChat: selectors.canInitiateLiveChat(store, instanceId),
        privateChat: selectors.canInitiatePrivateChat(store, instanceId),
        cam2Cam: selectors.canInitiateCam2Cam(store, instanceId)
    }
});

const mapDispatchToProps = {
    startVideoChatType,
    toggleCam2Cam,
    closeMessenger,
    openGiftSection,
    closeGiftSection,
    toggleToySection,
    requestImageUpload,
    setIsMobileMenuOpen
};

export default withRedux(MobileMenuContainer, mapStateToProps, mapDispatchToProps);
