import * as React from 'react';
import { BaseIcon, ControlsWrapper, CurrentTimeWrapper, PlayerWrapper, RecordingIcon } from './style'
import AudioPlayerBarContainer from '../AudioPlayerBar';
import utils from '../../../utils';

interface IProps {
    audioRef: any;
    audioSrc: string;
    isPlaying: boolean;
    currentTime: number;
    duration: number;

    setAudioTime: () => void;
    setClickedTime: (clickedTime: number) => void;
    stopPlaying: () => void;
    startPlaying: () => void;
}

const AudioPlayer = (props: IProps) => (
    <PlayerWrapper>
        <audio ref={props.audioRef} src={props.audioSrc} />

        <ControlsWrapper>
            <RecordingIcon className={'-icon-voice-recording-full'} />

            <CurrentTimeWrapper>
                {utils.getFormattedDuration(props.currentTime)}
            </CurrentTimeWrapper>

            <BaseIcon
                className={props.isPlaying ? '-icon-pause-full' : '-icon-single-play-full'}
                onClick={props.isPlaying ? props.stopPlaying : props.startPlaying}
            />

            <AudioPlayerBarContainer
                duration={props.duration}
                currentTime={props.currentTime}
                setClickedTime={props.setClickedTime}
            />
        </ControlsWrapper>
    </PlayerWrapper>
);

export default AudioPlayer;
