import * as React from 'react';
import { IChat, ITranslation } from '../../../redux/store';
import ProfileInfoContainer from '../ProfileInfo';
import ProfilePictureContainer from '../ProfilePicture';

interface IProps {
    chat?: IChat;
    motto: string;
    isVideochatOpen: boolean;
    hasModelProfilePicture?: boolean;
    hasModelPictureInProfile: boolean;
    translation: ITranslation;

    onClickProfilePicture: () => any;
}

const Profile = (props: IProps) => {
    const {visitProfile} = props.translation.user;

    return (
        <>
            {props.hasModelPictureInProfile && 
                <ProfilePictureContainer
                    tooltipText={visitProfile}
                    onClick={props.onClickProfilePicture}
                />
            }

            <ProfileInfoContainer tooltipText={visitProfile}>
                {props.motto}
            </ProfileInfoContainer>
        </>
    );
};

export default Profile;
