import * as React from 'react';
import { isTablet } from 'react-device-detect';

import { ITranslation } from '../../../redux/store';
import { getBaseButtonContent } from './utils';
import { MessageSectionButtonsWrapper } from './style';
import { EmoticonsButton, GiftButton, ImageUploadButton, SendTipButton, ToyButton } from './automation';

interface IProps {
    hasEmoticons: boolean;
    hasImageUpload: boolean;
    shouldShowTipping: boolean;
    isResponsive?: boolean;
    isDropDownOpen?: boolean;
    isGiftSectionOpen: boolean;
    isToySectionOpen: boolean;
    hasToyControl: boolean;
    isGiftButtonVisible?: boolean;
    isEmoticonSectionOpen: boolean;
    translation: ITranslation;

    onGiftClick: () => void;
    onEmoticonClick: () => void;
    onToyClick: () => void;
    onTipClick?: () => void;
    requestImageUpload: () => void;
}

const MessageSectionButtons = (props: IProps) => {
    const { toyControl } = props.translation.chat.toyControl;
    const { insertEmoticons, sendImage, tip4 } = props.translation.chat;
    const { menuSendGift, menuSendPhotos, menuSendEmoticon } = props.translation.message;
    const isToyButtonVisible = props.hasToyControl && (props.isResponsive || isTablet);

    return (
        <MessageSectionButtonsWrapper>
            {props.hasEmoticons &&
                <EmoticonsButton
                    title={insertEmoticons}
                    isInverted={props.isEmoticonSectionOpen}
                    isDropDownOpen={props.isDropDownOpen}
                    isResponsive={props.isResponsive}
                    onClick={props.onEmoticonClick}
                >
                    {getBaseButtonContent("-icon-smile-full", menuSendEmoticon, props.isResponsive)}
                </EmoticonsButton>

            }

            {props.hasImageUpload &&
                <ImageUploadButton
                    title={sendImage}
                    isDropDownOpen={props.isDropDownOpen}
                    isResponsive={props.isResponsive}
                    onClick={props.requestImageUpload}
                >

                    {getBaseButtonContent("-icon-image-full", menuSendPhotos, props.isResponsive)}
                </ImageUploadButton>
            }

            {props.shouldShowTipping &&
                <SendTipButton
                    title={tip4}
                    isDropDownOpen={props.isDropDownOpen}
                    isResponsive={props.isResponsive}
                    onClick={props.onTipClick}
                >
                    {getBaseButtonContent("-icon-euro-line", tip4, props.isResponsive)}
                </SendTipButton>
            }

            {props.isGiftButtonVisible &&
                <GiftButton
                    title={props.translation.message.sendGifts}
                    isInverted={props.isGiftSectionOpen}
                    isDropDownOpen={props.isDropDownOpen}
                    isResponsive={props.isResponsive}
                    onClick={props.onGiftClick}
                >
                    {getBaseButtonContent("-icon-gift-full", menuSendGift, props.isResponsive)}
                </GiftButton>
            }

            {isToyButtonVisible &&
                <ToyButton
                    title={toyControl}
                    isInverted={props.isToySectionOpen}
                    isDropDownOpen={props.isDropDownOpen}
                    isResponsive={props.isResponsive}
                    onClick={props.onToyClick}
                >
                    {getBaseButtonContent("-icon-toy-control", toyControl, props.isResponsive)}
                </ToyButton>
            }
        </MessageSectionButtonsWrapper>
    );
};

export default MessageSectionButtons;

