import * as React from 'react';
import { isEmpty } from 'lodash';

import { VideoChatType } from '../../../redux/store';
import Cam2CamButtonContainer from '../Cam2CamButton/Cam2CamButtonContainer';
import LiveChatButtonContainer from '../LiveChatButton/LiveChatButtonContainer';
import LiveChatPreviewButtonContainer from '../LiveChatPreviewButton/LiveChatPreviewButtonContainer';
import PrivateChatButtonContainer from '../PrivateChatButton/PrivateChatButtonContainer';
import VipLivePreviewButtonContainer from '../VipLivePreviewButton/VipLivePreviewButtonContainer';
import VoyeurCamButtonContainer from '../VoyeurCamButton/VoyeurCamButtonContainer';
import { ActionSectionWrapper } from './style';
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

export interface IProps {
	isVip: boolean;
	isFullscreen: boolean;
	shouldDisplayIcon: boolean;
	isForInformationDialog?: boolean;

	buttonsVisibility: {
		liveChat: boolean;
		livePreview: boolean;
		privateChat: boolean;
		voyeurChat: boolean;
		cam2Cam: boolean;
	}
}

const ActionSection = (props: IProps) => {
	/**
	 * Create a dictionary for keeping the buttons:
	 * the key is the button type or videochat type (they cover the same concept)
	 * and the value is the JSX for rendering the element
	 */
	const buttons: JSX.Element[] = [];
	const { buttonsVisibility } = props;

	/** Create the livechat button */
	if (buttonsVisibility.liveChat) {
		buttons.push(
			<LiveChatButtonContainer
				key={VideoChatType.LIVECHAT}
				isForActionSection={true}
				shouldDisplayIcon={props.shouldDisplayIcon}
			/>
		);
	}

	/** Based on the VIP feature of the client, we create the desired live preview button */
	if (buttonsVisibility.livePreview) {
		buttons.push(
			props.isVip
				? <VipLivePreviewButtonContainer
					key={VideoChatType.LIVE_PREVIEW}
					shouldDisplayIcon={props.shouldDisplayIcon}
					isForActionSection={true}
				/>
				: <LiveChatPreviewButtonContainer
					isForActionSection={true}
					key={VideoChatType.LIVE_PREVIEW}
					shouldDisplayIcon={props.shouldDisplayIcon}
				/>
		);
	}

	/** Create the voyeur/voyeurToPrivateChat cam button */
	if (buttonsVisibility.voyeurChat) {
		buttons.push(
			<VoyeurCamButtonContainer
				key={VideoChatType.VOYEUR_CHAT}
				shouldDisplayIcon={props.shouldDisplayIcon}
				isForActionSection={true}
			/>
		);
	}

	/** Create the private chat button */
	if (buttonsVisibility.privateChat) {
		buttons.push(
			<PrivateChatButtonContainer
				key={VideoChatType.PRIVATE_CHAT}
				shouldDisplayIcon={props.shouldDisplayIcon}
				isForActionSection={true}
			/>
		);
	}

	/** Create the cam2cam button */
	if (buttonsVisibility.cam2Cam) {
		buttons.push(
			<Cam2CamButtonContainer
				key={VideoChatType.CAM2CAM}
				shouldDisplayIcon={props.shouldDisplayIcon}
				isForActionSection={true}
			/>
		);
	}
	return (
		<>
			{!isEmpty(buttons) && <ActionSectionWrapper {...props}> {buttons} </ActionSectionWrapper>}
		</>
	);
};

export default withCustomClasses<IProps>(ActionSection, [DomCustomClass.ActionSection]);

