import * as React from 'react';
import ProfilePictureContainer from '../../specific/ProfilePicture/ProfilePictureContainer';
import DialogButton from "../DialogButton/DialogButton";
import { DialogButtonColor } from "../DialogButton/style";
import {
    ButtonSection,
    ContentWrapper,
    PreChatInformationDialogWrapper,
    ProfilePictureWrapper,
    TextElement
} from './style';
import CloseButtonContainer, { CloseButtonType } from '../CloseButton/CloseButtonContainer';

export interface IPreChatInformationDialogProps {
    text: string;
    closeButtonText?: string;
    hasCloseButton: boolean;
    onCloseClick?: () => any;
    buttons?: JSX.Element;
    otherContent?: JSX.Element;
    hasModelProfilePicture?: boolean;
}

const PreChatInformationDialog = (props: IPreChatInformationDialogProps) => (
    <PreChatInformationDialogWrapper>
        <CloseButtonContainer
            onClick={props.onCloseClick}
            hoverText={props.closeButtonText}
            closeButtonType={CloseButtonType.PRECHAT_INFORMATION_DIALOG}
            iconClassName={'-icon-close-full'}
        />

        <ContentWrapper>
            {props.hasModelProfilePicture &&
                <ProfilePictureWrapper>
                    <ProfilePictureContainer />
                </ProfilePictureWrapper>
            }

            <TextElement>{props.text}</TextElement>

            <ButtonSection>
                {props.buttons}
                {props.hasCloseButton &&
                    <DialogButton
                        text={props.closeButtonText}
                        color={DialogButtonColor.BLACK}
                        onClick={props.onCloseClick}
                    />
                }
            </ButtonSection>

            {props.otherContent}
        </ContentWrapper>
    </PreChatInformationDialogWrapper>
);

export default PreChatInformationDialog;
