import React from 'react';
import { Component } from 'react';
import { components, Props as ReactSelectProps } from 'react-select';

import { ModelMentionOptionWrapper, ModelName } from './style';
import SimpleModelPicture from '../../common/SimpleModelPicture';
import config from '../../../static/config';

interface IComponentProps {
	modelName: string;
	modelPicture: string;
	focusedOption?: string;

	onMouseFocus: (id: string) => void;
}

type IProps = ReactSelectProps & IComponentProps;

class CustomMentionOption extends Component<IProps> {
	componentDidUpdate(prevProps: IProps) {
		if (!prevProps.isFocused && this.props.isFocused) {
			// it is a mouse focus
			this.props.onMouseFocus(this.props.data.id);
		}
	}

	render() {
		const isFocused = this.props.focusedOption
			? this.props.focusedOption === this.props.data.id
			: this.props.isFocused;

		const optionWrapper = (
			<ModelMentionOptionWrapper>
				<SimpleModelPicture
					picture={this.props.modelPicture}
					size={config.customMentionPictureSize}
					isForGroupChatMentions={true}
				/>
				<ModelName>
					{this.props.modelName}
				</ModelName>
			</ModelMentionOptionWrapper>
		);

		return (
			<components.Option
				{...this.props}
				isFocused={isFocused}
				children={
					this.props.data.label === this.props.modelName
					? optionWrapper
					: this.props.children
				}
			/>
		);
	}
}


export default CustomMentionOption;
