import * as React from 'react';

import MobileHeader from './MobileHeader';
import selectors from '../../../redux/selectors';
import IStore from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    shouldHideVideochatControls: boolean;
}

type IProps = IStoreProps & IInstanceProps;

const MobileHeaderContainer = (props: IProps) => (
    <MobileHeader
        shouldHideVideochatControls={props.shouldHideVideochatControls}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    shouldHideVideochatControls: selectors.shouldHideVideochatControls(store, instanceId)
});

export default withRedux(MobileHeaderContainer, mapStateToProps);
