import { isEmpty, some } from 'lodash';
import { Action } from 'redux-actions';
import { call, put, select, takeEvery, delay } from 'redux-saga/effects';

import loggers from "../../loggers";
import config from '../../static/config';
import constants from '../../static/constants';
import { rechargeFromChat } from "./recharge";
import selectors from '../../redux/selectors';
import AppService from '../../services/AppService';
import { ActionIds } from '../../redux/interfaces';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import ChatService from "../../services/ChatService";
import PlayerService from "../../services/PlayerService";
import { PrivateChatInvitationAnswer } from '../../services/types';
import { getChatInstanceSaga, IGetChatInstanceSagaResult } from "./chat";
import { startVideoChatType, stopVideoChat, stopVideoChatPlayer } from "./videochat";
import {
    ANSWER_USER_PROMPT_SECTION,
    CLOSE_USER_PROMPT_SECTION,
    closeMessenger,
    closeUserPromptSection,
    openUserPromptSection,
    resetUserPrompts,
    setActionResponse,
    IAnswerUserPromptPayload
} from "../../redux/modules/messenger";
import {
    ActionResponseType,
    AnswerType,
    ICameraDevice,
    IQuestion,
    KnownAnswerType,
    QuestionType,
    VideochatInformationType,
    VideoChatType,
    ICountdownWarning
} from '../../redux/store';
import {setInformation, closeVideochat, SET_CAM2CAM_SELECTED_CAMERA} from '../../redux/modules/videochat';

export interface IOpenLoadAccountPayload {
    warning?: ICountdownWarning;
    answer?: AnswerType;
}

const createUserPromptActionType = createActionType(ActionIds.USER_PROMPT, { isSagaAction: true });

// Actions
export const OPEN_CLOSE_CHAT_PROMPT = createUserPromptActionType("OPEN_CLOSE_CHAT_PROMPT");
export const OPEN_PRIVATE_CHAT_PROMPT = createUserPromptActionType("OPEN_PRIVATE_CHAT_PROMPT");
export const OPEN_SPY_PRIVATE_CHAT_PROMPT = createUserPromptActionType("OPEN_SPY_PRIVATE_CHAT_PROMPT");
export const OPEN_LOAD_ACCOUNT_ONE_CLICK_PROMPT = createUserPromptActionType("OPEN_LOAD_ACCOUNT_ONE_CLICK_PROMPT");
export const OPEN_ANONYMOUS_TIP_PROMPT = createUserPromptActionType("OPEN_ANONYMOUS_TIP_PROMPT");
export const OPEN_TIP_PROMPT = createUserPromptActionType("OPEN_TIP_PROMPT");
export const OPEN_VERIFY_AGE_PROMPT = createUserPromptActionType("OPEN_VERIFY_AGE_PROMPT");
export const CLOSE_ALL_USER_PROMPTS = createUserPromptActionType("CLOSE_ALL_USER_PROMPTS");
export const OPEN_CHOOSE_CAMERA_PROMPT = createUserPromptActionType("OPEN_CHOOSE_CAMERA_PROMPT");

// Action Creators
export const closeAllUserPrompts = createAction(CLOSE_ALL_USER_PROMPTS);
export const openCloseChatPrompt = createAction(OPEN_CLOSE_CHAT_PROMPT);
export const openPrivateChatPrompt = createAction<string>(OPEN_PRIVATE_CHAT_PROMPT);
export const openSpyPrivateChatPrompt = createAction(OPEN_SPY_PRIVATE_CHAT_PROMPT);
export const openLoadAccountOneClickPrompt = createAction<IOpenLoadAccountPayload>(OPEN_LOAD_ACCOUNT_ONE_CLICK_PROMPT);
export const openAnonymousTipPrompt = createAction<string | undefined>(OPEN_ANONYMOUS_TIP_PROMPT);
export const openTipPrompt = createAction(OPEN_TIP_PROMPT);
export const openVerifyAgePrompt = createAction(OPEN_VERIFY_AGE_PROMPT);
export const openChooseCameraPrompt = createAction(OPEN_CHOOSE_CAMERA_PROMPT);

// Sagas
export const openPromptSaga = function* ({ type, payload }: Action<IPayload>) {
    const { instanceId, value } = payload;
    const isTipOpen = yield select(selectors.isTipPromptOpen, instanceId);

    switch (type) {
        case OPEN_CHOOSE_CAMERA_PROMPT: {
            yield put(openUserPromptSection(instanceId, {
                type: QuestionType.CHOOSE_CAMERA
            }));
            break;
        }

        case OPEN_CLOSE_CHAT_PROMPT: {
            const modelName = yield select(selectors.getModelName, instanceId);

            if (isTipOpen) {
                yield put(closeUserPromptSection(instanceId, QuestionType.SEND_ANONYMOUS_TIP));
            }

            yield put(openUserPromptSection(instanceId, {
                type: QuestionType.CLOSE_CHAT,
                payload: {
                    modelName
                }
            }));
            break;
        }

        case OPEN_PRIVATE_CHAT_PROMPT:
            yield put(openUserPromptSection(instanceId, {
                type: QuestionType.INVITE_PRIVATE_CHAT,
                payload: {
                    privateChatText: value
                }
            }));
            break;

        case OPEN_LOAD_ACCOUNT_ONE_CLICK_PROMPT:
            const session = yield select(selectors.getSession, instanceId);
            const isOneClickRechargeAvailable: boolean = session.oneClickRecharge.available;

            if (isTipOpen) {
                yield put(closeUserPromptSection(instanceId, QuestionType.SEND_ANONYMOUS_TIP));
            }

            if (!isOneClickRechargeAvailable) {
                return;
            }

            const amounts = session.oneClickRecharge.amounts;

            if (!amounts || isEmpty(amounts)) {
                yield put(setActionResponse(instanceId, {
                    // FUTURE TODO: Peter will update the translations from the server for this
                    text: "Amounts not available",
                    type: ActionResponseType.ERROR
                }));
                return;
            }

            yield put(openUserPromptSection(instanceId, {
                type: QuestionType.LOAD_ACCOUNT_ONE_CLICK,
                payload: {
                    amounts: session.oneClickRecharge.available
                        && session.oneClickRecharge.amounts
                        || [],
                    defaultAmount: session.oneClickRecharge.available
                        && session.oneClickRecharge.defaultAmount
                }
            }));
            break;

        case OPEN_ANONYMOUS_TIP_PROMPT:
            const initialChatId = value;
            yield delay(config.videoChatTimeLimits.anonymousTip * 1000);
            const currentChatId = yield select(selectors.getChatId, instanceId);
            const isVideoChatInVoyeurMode = yield select(selectors.isVideoChatInVoyeurMode, instanceId);

            /** Display the anonymous tip only if the voyeur type is still active after the delay */
            if (isVideoChatInVoyeurMode && initialChatId === currentChatId) {
                yield put(openUserPromptSection(instanceId, {
                    type: QuestionType.SEND_ANONYMOUS_TIP
                }));
            }
            break;

        case OPEN_TIP_PROMPT:
            yield put(openUserPromptSection(instanceId, {
                type: QuestionType.SEND_TIP
            }));
            break;

        case OPEN_SPY_PRIVATE_CHAT_PROMPT: {
            yield put(stopVideoChatPlayer(instanceId));

            const modelName = yield select(selectors.getModelName, instanceId);
            yield put(openUserPromptSection(instanceId, {
                type: QuestionType.SPY_PRIVATE_CHAT,
                payload: { modelName }
            }));
            break;
        }

        case OPEN_VERIFY_AGE_PROMPT: {
            const modelName = yield select(selectors.getModelName, instanceId);
            yield put(openUserPromptSection(instanceId, {
                type: QuestionType.VERIFY_AGE,
                payload: { modelName }
            }));
            break;
        }
    }
};

export const dealWithAnswerSaga = function* ({ payload }: Action<IPayload<IAnswerUserPromptPayload>>) {
    const { instanceId, value } = payload;
    const questionType: QuestionType = value.question.type;
    const answer: AnswerType = value.answer;

    switch (questionType) {
        case QuestionType.CLOSE_CHAT:
            if (answer ===  KnownAnswerType.YES) {
                yield put(stopVideoChat(instanceId, true));
                yield put(closeAllUserPrompts(instanceId));

                // check if the exit popups are supposed to be handled externally or not
                const hasExitPopups = yield select(selectors.hasExitPopups, instanceId);

                yield put(hasExitPopups
                    ? setInformation(instanceId, VideochatInformationType.LIVECHAT_SESSION_ENDED)
                    : closeMessenger(instanceId));
            }
            break;

        case QuestionType.INVITE_PRIVATE_CHAT: {
            const chat: IGetChatInstanceSagaResult = yield call(getChatInstanceSaga, false, instanceId);

            if (chat.chatConf.error) {
                loggers.saga.debug('USER PROMPT - could not get chat; aborting');
                return;
            }

            if (chat.chatInstance?.error) {
                return;
            }

            if (chat.chatInstance?.result) {
                const { yes, no } = PrivateChatInvitationAnswer;

                ChatService.getService(instanceId).sendQueryResponse(
                    chat.chatInstance.result,
                    constants.QUERYSINGLE,
                    answer === KnownAnswerType.YES ? yes : no
                );
            }
            break;
        }

        case QuestionType.SPY_PRIVATE_CHAT:
            yield put(answer === KnownAnswerType.YES
                ? startVideoChatType(instanceId, VideoChatType.VOYEUR_PRIVATE_CHAT)
                : closeVideochat(instanceId)
            );
            break;

        case QuestionType.LOAD_ACCOUNT_ONE_CLICK:
            yield put(rechargeFromChat(instanceId, answer as number));
            break;

        case QuestionType.VERIFY_AGE:
            AppService.getService(instanceId).onAgeVerification();
            break;
    }

    // close user prompt
    yield put(closeUserPromptSection(instanceId, questionType));
};

/**
 * If we close a private chat invitation, we will send "no" as answer
 * to the singlechat back-end from the vchat-core library.
 */
const closeUserPromptSaga = function* ({ payload }: Action<IPayload<QuestionType>>) {
    const { instanceId, value } = payload;
    const chat: IGetChatInstanceSagaResult = yield call(getChatInstanceSaga, false, instanceId);

    if (value === QuestionType.INVITE_PRIVATE_CHAT && chat.chatInstance?.result) {
        ChatService.getService(instanceId).sendQueryResponse(
            chat.chatInstance.result,
            constants.QUERYSINGLE,
            PrivateChatInvitationAnswer.no
        );
    }
};

const closeAllUserPromptsSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const userPrompts = yield select(selectors.getUserPrompts, instanceId);
    const chat: IGetChatInstanceSagaResult = yield call(getChatInstanceSaga, false, instanceId);

    const isInvitePrivateChatOpen: boolean = some(userPrompts, (userPrompt: IQuestion) =>
            userPrompt.type === QuestionType.INVITE_PRIVATE_CHAT
    );

    if (isInvitePrivateChatOpen && chat.chatInstance?.result) {
        ChatService.getService(instanceId).sendQueryResponse(
            chat.chatInstance.result,
            constants.QUERYSINGLE,
            PrivateChatInvitationAnswer.no
        );
    }

    yield put(resetUserPrompts(instanceId));
};


export const setSelectedCameraSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const selectedCamera: ICameraDevice = yield select(selectors.getCam2CamSelectedCamera, instanceId);

    yield delay(500);
    yield put(closeUserPromptSection(instanceId, QuestionType.CHOOSE_CAMERA));
    PlayerService.getService(instanceId).selectCamera(selectedCamera);
};


const watchSagas = function* () {
    yield takeEvery(OPEN_CLOSE_CHAT_PROMPT, openPromptSaga);
    yield takeEvery(OPEN_CHOOSE_CAMERA_PROMPT, openPromptSaga);
    yield takeEvery(OPEN_PRIVATE_CHAT_PROMPT, openPromptSaga);
    yield takeEvery(OPEN_SPY_PRIVATE_CHAT_PROMPT, openPromptSaga);
    yield takeEvery(OPEN_LOAD_ACCOUNT_ONE_CLICK_PROMPT, openPromptSaga);
    yield takeEvery(OPEN_ANONYMOUS_TIP_PROMPT, openPromptSaga);
    yield takeEvery(OPEN_TIP_PROMPT, openPromptSaga);
    yield takeEvery(OPEN_VERIFY_AGE_PROMPT, openPromptSaga);
    yield takeEvery(CLOSE_ALL_USER_PROMPTS, closeAllUserPromptsSaga);

    yield takeEvery(ANSWER_USER_PROMPT_SECTION, dealWithAnswerSaga);

    yield takeEvery(CLOSE_USER_PROMPT_SECTION, closeUserPromptSaga);

    // answer to the camera selection prompt
    yield takeEvery(SET_CAM2CAM_SELECTED_CAMERA, setSelectedCameraSaga);
};

export default watchSagas;
