import { get } from "lodash";
import { createSelector } from "reselect";
import { isIE } from "react-device-detect";

import IStore, { IFeatures } from "../store";
import { getInitialInstanceData } from "../initialStore";

export const getFeatures = (store: IStore, instanceId: string | undefined) => {
	const id = instanceId as string;
	const data = get(store, id) || getInitialInstanceData();
	return data.features;
};

/**
 * @return {boolean} if true preloader text is hidden
 */
export const hasPreloaderPaymentText = createSelector(getFeatures,
	(features: IFeatures) => features.hasPreloaderPaymentText
);

/**
 * @return {boolean} true if the messenger has the "favorite" feature active
 */
export const hasFavoriteFeature = createSelector(getFeatures, (features: IFeatures) => features.hasFavorite);

/**
 * @return {boolean} true if the messenger has the "contact note" feature active
 */
export const hasContactNoteFeature = createSelector(getFeatures, (features: IFeatures) => features.hasContactNote);

/**
 * @return {boolean} true if the messenger has "gifts" feature active
 */
export const hasGiftsFeature = createSelector(getFeatures, (features: IFeatures) => features.hasGifts);

/**
 * @return {boolean} true if the messenger has the toy control feature active
 */
export const hasToyControlFeature = createSelector(getFeatures, (features: IFeatures) => features.hasToyControl);

/**
 * @return {boolean} true if the messenger has the "exit pop-ups" feature active
 * (means that the exit informational pop-ups displayed are the default ones implemented in vxmessenger)
 * Useful when the platform intends to display its own customized exit pop-ups
 */
export const hasExitPopups = createSelector(getFeatures, (features: IFeatures) => features.hasExitPopups);

/**
 * @return {boolean} true if the messenger has the "modelProfilePicture" feature active
 */
export const hasModelProfilePicture = createSelector(getFeatures, (features: IFeatures) => features.hasModelProfilePicture);

/**
 * @return {boolean} true if the messenger has the "modelMotto" feature active
 */
export const hasModelMotto = createSelector(getFeatures, (features: IFeatures) => features.hasModelMotto);

/**
 * @return {boolean} true if the messenger has the "modelName" feature active
 */
export const hasModelName = createSelector(getFeatures, (features: IFeatures) => features.hasModelName);

/**
 * @return {boolean} true if the messenger has the "cam2Cam" feature active
 */
export const hasCam2Cam = createSelector(getFeatures, (features: IFeatures) => features.hasCam2Cam);

/**
 * @return {boolean} true if the messenger has the "voyeur" feature active
 */
export const hasVoyeur = createSelector(getFeatures, (features: IFeatures) => features.hasVoyeur);

/**
 * @return {boolean} true if the messenger has the "privateChat" feature active
 */
export const hasPrivateChat = createSelector(getFeatures, (features: IFeatures) => features.hasPrivateChat);

/**
 * @return {boolean} true if the messenger has the "closeButton" feature active
 */
export const hasCloseButton = createSelector(getFeatures, (features: IFeatures) => features.hasCloseButton);

/**
 * @return {boolean} true if the messenger has the fullscreen feature active. If not we will never
 * render the fullscreen button in the videochat container.
 */
export const hasFullscreen = createSelector(getFeatures, (features: IFeatures) => features.hasFullscreen);

/**
 * @return {boolean} true if the messenger has the emoticons button rendered (functionality such as
 * using the shortcut in the textbox to transform the text into emoticon will be kept).
 */
export const hasEmoticons = createSelector(getFeatures, (features: IFeatures) => features.hasEmoticons);

/**
 * @return {boolean} true if the client will have the ability to upload an image
 */
export const hasImageUploadFeature = createSelector(getFeatures, (features: IFeatures) => features.hasImageUpload);

/**
 * @return {boolean} true if the arrow for sending the message to the model on click will be rendered
 */
export const hasSendMessageButton = createSelector(getFeatures, (features: IFeatures) => features.hasSendMessageButton);

/**
 * @return {boolean} true if the history messages sections from the chatbox will be opened from the start
 */
export const hasHistorySectionsOpened = createSelector(getFeatures, (features: IFeatures) => features.hasHistorySectionsOpened);

/**
 * @return {boolean} true if the messenger will contain the past conversation between the client and the model
 */
export const hasHistoryMessages = createSelector(getFeatures, (features: IFeatures) => features.hasHistoryMessages);

/*
 * @return {boolean} true if the history messages will be grouped based on their previous livechat sessions
 */
export const hasMessagesGrouped = createSelector(getFeatures, (features: IFeatures) => features.hasMessagesGrouped);

export const hasGroupChatIndicator = createSelector(getFeatures, (features: IFeatures) => features.hasGroupChatIndicator);

/**
 * @returns {boolean} true if messenger's header is visit-x
 * custom containing only messages info.
 */
export const isMessageTypeHeader = createSelector(getFeatures,
	(features: IFeatures) => features.isMessageTypeHeader as boolean
);

/**
 * @returns {boolean} true if the parent platform has configured the messenger
 * to enable the client the functionality of sending audio messages
 */
export const hasAudioMessageFeature = createSelector(getFeatures,
	(features: IFeatures) => !isIE && features.hasAudioMessages
);

/**
 * @returns {boolean} true if the parent platform has configured the messenger
 * to render the name of the client in the message bubble when the stream
 * is in group chat mode
 */
export const hasClientName = createSelector(getFeatures,
	(features: IFeatures) => features.hasGroupChatClientName
);


/**
 * @returns {boolean} true if the parent platform has configured the messenger
 * to render the fullscreen layout when the messenger has a stream active
 * inside a landscape container (width > height)
 */
export const hasFullscreenLayoutOnLandscape = createSelector(getFeatures,
	(features: IFeatures) => features.hasFullscreenLayoutOnLandscape
)

/**
 * @return {boolean} if true, when clicking outisde the messenger, we will trigger our custom logic
 */
export const hasBehaviorOnClickOutside = createSelector(getFeatures, (features: IFeatures) => features.hasBehaviorOnClickOutside);

/**
 * @return {boolean} if the parent platforn has configured the messenger to have
 * the tipping features enabled - both custom tipping when a stream is active and
 * the anonymous tip when starting a voyeur chat
 */
export const hasTips = createSelector(getFeatures, (features: IFeatures) => features.hasTips);


/**
 * @return {boolean} if true, censored content will be shown
 */
export const getShowCensoredContent = createSelector(getFeatures,
	(features: IFeatures) => features.showCensoredContent
);
