import React, { FC } from 'react';
import {
    AudioAnalyserContent,
    CancelRecordingWrapper,
    CountDownWrapper,
    RecordingAreaContent,
    RecordingIcon,
    SendIcon,
    Wrapper
} from './style';

interface IProps {
    isRecording: boolean;
    shouldSendMessage: boolean;
    recordingSeconds: string;
    recordingMinutes: string;

    stopRecording: () => void;
    sendAudioMessage: () => void;
}

export const AudioMessageUploader: FC<IProps> = (props: IProps) => {
    const countDown: string = `${props.recordingMinutes}:${props.recordingSeconds}`;

    return (
        <Wrapper isRecording={props.isRecording}>
            <AudioAnalyserContent>
                <RecordingAreaContent>
                    <RecordingIcon className={"-icon-voice-recording-full"} />
                    <CountDownWrapper>
                        {countDown}
                    </CountDownWrapper>
                    <CancelRecordingWrapper onClick={props.stopRecording}>Cancel</CancelRecordingWrapper>
                </RecordingAreaContent>
                <SendIcon className={"-icon-send-chat-full"} onClick={props.sendAudioMessage} />
            </AudioAnalyserContent>
        </Wrapper>
    )
};

export default AudioMessageUploader;
