import * as React from "react";

import { ICamerasSplit, IProps } from "./types";
import { ICameraDevice } from "../../../redux/store";

export const onSelectCamera = (props: IProps) => (id:string) => (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    props.setCam2CamSelectedCamera(props.instanceId, id);
};

export const extractFrontAndBackCameras = (cameras: ICameraDevice[]): ICamerasSplit => {
    const frontAndBack: ICameraDevice[] = [];
    const others: ICameraDevice[] = [];

    cameras.forEach((camera: ICameraDevice) => {
        (camera.isBack || camera.isFront ? frontAndBack : others).push(camera);
    });

    return { frontAndBack, others };
}
