import constants from '../../../static/constants';
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";
import ITheme from "../../../themes/interfaces";
import utils from '../../../utils';

interface IContentWrapperProps {
    isVideoChatInGroupMode?: boolean;
    isModelMessage?: boolean;
    isGroupUserMessage?: boolean;
	isMessageWithJustOneEmoticon?: boolean;
	shouldIncreaseMessageFontSize: boolean;
}

interface IGroupChatModelNameProps {
    isVideoChatInGroupMode?: boolean;
}

interface IFullscreenSystemMessage {
	shouldIncreaseMessageFontSize: boolean;
}

interface IClientMessageWrapperProps {
	isMobileLandscape?: boolean;
	isFullscreen?: boolean;
    isConsecutiveGroupMessage?: boolean;
	isLastConsecutiveGroupMessage?: boolean;
	isSoloMessage?: boolean;
}

interface IModelMessageAndImageWrapperProps {
    isMobileLandscape?: boolean;
    isConsecutiveGroupMessage?: boolean;
	isLastConsecutiveGroupMessage?: boolean;
	isSoloMessage?: boolean;

    /**
     * In some messenger contexts (fullscreen or responsive rendering),
     * the profile picture to the left of the message bubble is not rendered
     */
    isContextWithoutProfilePicture?: boolean;
}

interface IModelProfilePictureProps {
    picture?: string;
}

interface ITimeSectionProps {
	shouldIncreaseMessageFontSize: boolean;
	direction?: MessageDirection;
	isConsecutiveGroupMessage?: boolean;
}

interface IMessageBaseProps {
	hasImage?: boolean;
	isFullscreen?: boolean;
    isConsecutiveGroupMessage?: boolean;
    isVideoChatInGroupMode?: boolean;
    isGroupChatUserMessage?: boolean
	shouldRenderAudioMessage?: boolean;
}

interface IToyMessageWrapper {
    isActive?: boolean;
	shouldApplyMargin?: boolean;
}

interface IGroupUsername {
    color?: string;
    isFromClient?: boolean;
}

interface IModelMessageWrapper {
    isConsecutiveGroupMessage?: boolean;
	isLastConsecutiveGroupMessage?: boolean;
	isSoloMessage?: boolean;
	hasProfilePicture?: boolean;
}

export const getModelMessageBackgroundColor = (
	theme: ITheme,
	isVideoChatInGroupMode?: boolean,
	isGroupChatUserMessage?: boolean
) => {
	const { groupChat, modelMessageBackground } = theme.colors;

	if (isVideoChatInGroupMode) {
		return isGroupChatUserMessage
			? groupChat.userMessageBackground
			: groupChat.modelMessageBackground;
	}

	return modelMessageBackground;
};

export const getClientMessageBackgroundColor = (
	theme: ITheme,
	isVideoChatInGroupMode?: boolean
) => isVideoChatInGroupMode
	? theme.colors.groupChat.clientMessageBackground
	: theme.colors.clientMessageBackground;

export const getClientMessagePadding = (props: IClientMessageWrapperProps) => {
	const { isConsecutiveGroupMessage, isLastConsecutiveGroupMessage, isSoloMessage, isMobileLandscape } = props;

	if (isConsecutiveGroupMessage) {
		return isLastConsecutiveGroupMessage ? '0 0 10px' :`0 15px 0 0`;
	}

	return isSoloMessage ? `10px 15px 20px 0`	: `10px 15px ${isMobileLandscape ? 0 : 15}px 0`
};

export const getReceivedMessagePadding = (props: IModelMessageAndImageWrapperProps) => {
	const { isConsecutiveGroupMessage, isLastConsecutiveGroupMessage, isSoloMessage, isMobileLandscape } = props;

	if (isConsecutiveGroupMessage) {
		return isLastConsecutiveGroupMessage ? '0 0 10px' : '0';
	}

	return isSoloMessage
		? `10px 15px ${isMobileLandscape ? 5 : 20}px ${constants.receivedMessageAndImageWrapperPaddingRight}px`
		: `10px 0 ${isMobileLandscape ? 5 : 20}px ${constants.receivedMessageAndImageWrapperPaddingRight}px`;
};

export const getMaxWidthForReceivedMessage = (props: IModelMessageWrapper) => {
	const { isConsecutiveGroupMessage, hasProfilePicture} = props;

	if (!isConsecutiveGroupMessage) {
		return '70%';
	}

	return `calc(70% + ${hasProfilePicture ? 45 : 10}px)`;
};

