export const ToyMessageWrapper = withCustomClasses<IToyMessageWrapper>(styled.div<IToyMessageWrapper>`
	${props => props.shouldApplyMargin && 'margin-top: -30px;'}
	
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
	padding: 10px 0 10px 0;
`, (props: IToyMessageWrapper) => {
        const classes = [DomCustomClass.Message, DomCustomClass.ToyMessage];
        if (!props.isActive) {
            classes.push(DomCustomClass.ToyMessageInactive);
        }
        return classes;
    }
);

