export const ContentWrapper = styled.div<IContentWrapperProps>`
	color: ${({ isVideoChatInGroupMode, isModelMessage, isGroupUserMessage, theme }) => {
		const { messageText, groupChat } = theme.colors;

		if (!isVideoChatInGroupMode) {
			return messageText;
		}

		if (isModelMessage) {
			return groupChat.modelMessageText;
		}

		return isGroupUserMessage
			? groupChat.userMessageText
			: groupChat.clientMessageText;
	}};

	font-size: ${(props) => {
		if (props.isMessageWithJustOneEmoticon) {
			return constants.emoticonSize.large
		}

		return props.shouldIncreaseMessageFontSize
			? (100 + constants.messageFontSizePercentage) * constants.emoticonSize.small / 100
			: constants.emoticonSize.small;
	}}px;
	
	width: 100%;
`;

export const FullscreenSystemMessageWrapper = styled.div`
    display: inline-block;
    width: 100%;
    position: relative;
    clear: both;
`;

