import * as React from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';

import withInstanceInfo from '../withInstanceInfo';

const withRedux = <T extends any>(
    WrappedComponent: React.ComponentType | React.FC<any>,
    mapStateToProps: any = null,
    mapDispatchToProps: any = null,
    additionalHOCs: any = {}
) => {
    const hocs = Object.values(additionalHOCs) as any;

    return compose(
        ...hocs,
        withInstanceInfo,
        connect(mapStateToProps, mapDispatchToProps)
    )(WrappedComponent) as React.ComponentType<T>;
}

export default withRedux;
