import * as React from 'react';
import { isUndefined, isEmpty } from 'lodash'

import { TimeSection } from './style';
import IMessageProps from './interfaces';
import AudioPlayerContainer from '../../specific/AudioPlayer';
import { MessageContentType, MessageDirection, IToyMessageInfo } from '../../../redux/store';
import { getToyMessage, getSystemMessage, getClientMessageComponent, getModelMessageComponent, getGroupChatUserMessage, getMessageTime } from './utils';
import { MsgType } from '../../../redux/store';

const Message = (props: IMessageProps) => {
    const timeIndicatorComponent = (
		<TimeSection
			direction={props.direction}
			isConsecutiveGroupMessage={props.isConsecutiveGroupMessage}
			shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}
		>
			<div>{getMessageTime(props.dateCreated)}</div>
		</TimeSection>
    );

    const shouldRenderAudioMessage = props.contentType === MessageContentType.AUDIO
        && !isUndefined(props.audioSrc);

    const messageContent = shouldRenderAudioMessage
        ? <AudioPlayerContainer audioSrc={props.audioSrc as string} />
        : props.children;

    const isMessageReceived = !props.isSystem && props.direction === MessageDirection.in;

    const messageContext = {
        isSystem: props.isSystem,
        isToy: props.type === MsgType.TOY && !isEmpty(props.toy),
        isFromClient: !props.isSystem && props.direction === MessageDirection.out,
        isFromModel: isMessageReceived && isEmpty(props.username),
        isFromGroupUser: isMessageReceived && !isEmpty(props.username)
    };

    return (
        <>
            {messageContext.isSystem && getSystemMessage(messageContent, props)}

            {messageContext.isToy && getToyMessage(props.toy as IToyMessageInfo, props.id, props.groupIndex)}

            {messageContext.isFromClient && getClientMessageComponent(messageContent, timeIndicatorComponent, props, shouldRenderAudioMessage)}

            {messageContext.isFromModel && getModelMessageComponent(messageContent, timeIndicatorComponent, props)}

            {messageContext.isFromGroupUser && getGroupChatUserMessage(messageContent, timeIndicatorComponent, props)}
        </>
    );
};

export default Message;
