import * as React from 'react';

import utils from '../../utils';
import { IViewProviderProps } from '../interfaces';
import FullscreenView from '../FullscreenView';
import MessengerView from '../MessengerView/MessengerView';
import VideoChatLandscapeView from '../VideoChatLandscapeView';
import VideoChatMobilePortraitView from '../VideoChatMobilePortraitView';
import VideoChatPortraitView from '../VideoChatPortraitView';
import VideoChatMobileLandscapeViewContainer from '../VideoChatMobileLandscapeView';

const ViewSwitcher = (props: IViewProviderProps) => {
    const views: {[hash: string]: React.ReactElement<any>} = {};
    const viewProps = utils.hashify({
        isVideoChat: props.isVideoChat,
        isMobile: props.isMobile,
        isPortrait: props.isPortrait,
        isLandscape: props.isLandscape,
        isFullscreen: props.isFullscreen
	});
	
    const mobilePortraitHash = utils.hashify({
        isVideoChat: true,
        isMobile: true,
        isPortrait: true,
        isLandscape: false,
        isFullscreen: false,
    });

    const mobileLandscapeHash = utils.hashify({
        isVideoChat: true,
        isMobile: true,
        isPortrait: false,
        isLandscape: true,
        isFullscreen: false
    });

    const desktopFullscreenHash = utils.hashify({
        isVideoChat: true,
        isMobile: false,
        isPortrait: false,
        isLandscape: true,
        isFullscreen: true
    });

    const desktopLandscapeHash = utils.hashify({
        isVideoChat: true,
        isMobile: false,
        isPortrait: false,
        isLandscape: true,
        isFullscreen: false,
    });

    const desktopPortraitHash = utils.hashify({
        isVideoChat: true,
        isMobile: false,
        isPortrait: true,
        isLandscape: false,
        isFullscreen: false
    });

    const messengerDesktopLandscapeHash = utils.hashify({
        isVideoChat: false,
        isMobile: false,
        isPortrait: false,
        isLandscape: true,
        isFullscreen: false
    });

    const messengerDesktopPortraitHas = utils.hashify({
        isVideoChat: false,
        isMobile: false,
        isPortrait: true,
        isLandscape: false,
        isFullscreen: false
    });

    const messengerMobilePortraitHash = utils.hashify({
        isVideoChat: false,
        isMobile: true,
        isPortrait: true,
        isLandscape: false,
        isFullscreen: false
    });

    const messengerMobileLandscapeHash = utils.hashify({
        isVideoChat: false,
        isMobile: true,
        isPortrait: false,
        isLandscape: true,
        isFullscreen: false
	});
	
	const messengerMobileFullscreenHash = utils.hashify({
	    isVideoChat: true,
        isMobile: true,
        isPortrait: false,
        isLandscape: true,
        isFullscreen: true	
	});

    views[mobilePortraitHash] = <VideoChatMobilePortraitView {...props} />;
	views[mobileLandscapeHash] = <VideoChatLandscapeView {...props} />;
	views[messengerMobileFullscreenHash] = <VideoChatMobileLandscapeViewContainer {...props} />;
	
    views[desktopFullscreenHash] = <FullscreenView {...props} />;
    views[desktopLandscapeHash] = <VideoChatLandscapeView {...props} />;
    views[desktopPortraitHash] = <VideoChatPortraitView {...props} />;

    // MessengerView is rendered only in following cases: on mobile/desktop
    // with landscape/portrait modes. In other scenarios there is nothing rendered.
    views[messengerDesktopLandscapeHash] = <MessengerView {...props} />;
    views[messengerDesktopPortraitHas] = <MessengerView {...props} />;
    views[messengerMobilePortraitHash] = <MessengerView {...props} />;
	views[messengerMobileLandscapeHash] = <MessengerView {...props} />;

	return views[viewProps] || null;
};

export default ViewSwitcher;
