import * as React from 'react';
import { isEmpty } from 'lodash';

import ActionResponse from './ActionResponse';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { resetActionResponse } from '../../../redux/modules/messenger';
import IStore, { IActionResponse, ITranslation } from '../../../redux/store';

interface IStoreProps {
    translation: ITranslation;
    actionResponse: IActionResponse;

    resetActionResponse: ActionDispatcher;
}

type IProps = IStoreProps & IInstanceProps;

class ActionResponseContainer extends React.Component<IProps> {
    componentDidUpdate(prevProps: IProps) {
        const actionResponseTime = 5000;

        /** The action response message should disappear after 5 seconds. */
        if (isEmpty(prevProps.actionResponse) && !isEmpty(this.props.actionResponse)) {
            setTimeout(this.resetActionResponse, actionResponseTime);
        }
    }

    render() {
        return (
            <>
                {!isEmpty(this.props.actionResponse) &&
                    <ActionResponse
                        actionResponse={this.props.actionResponse}
                        closeText={this.props.translation.common.close}
                        onCloseClick={this.resetActionResponse}
                    />
                }
            </>
        );
    }

    private resetActionResponse = () => {
        this.props.resetActionResponse(this.props.instanceId);
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    actionResponse: selectors.getActionResponse(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

const mapDispatchToProps = {
    resetActionResponse
};

export default withRedux(ActionResponseContainer, mapStateToProps, mapDispatchToProps);