import {isEmpty, sortBy} from 'lodash';
import { Action } from 'redux-actions';
import {put, takeEvery, select} from "redux-saga/effects";
import {CMDP_INIT_RESPONSE, ICOMMAND, ACTION, SupportedLanguage } from 'vxcontrol-client-lib';

import selectors from '../../redux/selectors';
import { ActionIds } from '../../redux/interfaces';
import ApiService from '../../services/ApiService';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import { setTranslations } from '../../redux/modules/user';
import {creditsDeprecatedName} from "../../static/constants/constants";
import {setEmoticons, setToyOptions} from "../../redux/modules/messenger";
import {
    setCurrency,
    setGiftBundles,
    setGiftCategories,
    setGifts,
    setUploadMediaUrl
} from '../../redux/modules/session';
import {
    CurrencyName,
    IBundle,
    ICurrency,
    IEmoticon,
    IGift,
    IGiftCategory,
    IToyOption
} from '../../redux/store';

const createInitActionType = createActionType(ActionIds.INIT, { isSagaAction: true });

// Actions
export const REQUEST_INIT = createInitActionType("REQUEST_INIT");
export const RECEIVE_INIT = createInitActionType("RECEIVE_INIT");

// Action Creators
export const requestInit = createAction(REQUEST_INIT);
const onReceiveInit = createAction<CMDP_INIT_RESPONSE>(RECEIVE_INIT);

/** Saga for sending the init request */
const requestInitSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    const language: SupportedLanguage = yield select(selectors.getLanguage, instanceId);
    const webtoken: string = yield select(selectors.getWebtoken, instanceId);

    const command: ICOMMAND = {
        action: ACTION.CMDP_INIT,
        params: {
            initData: 'vxmessenger',
            language,
            webtoken
        }
    };

    const service = ApiService.getService(instanceId);
    service.connect(webtoken, language);
    service.sendCommand(command);
};

/** We receive the default gifts and the gift categories in which the gifts are organized */
const receiveInitSaga = function*({ payload }: Action<IPayload<CMDP_INIT_RESPONSE>>) {
	const { instanceId, value } = payload;

    if (value && value.values) {
        const { uploadMediaUrl, translations } = value.values;

        if (!isEmpty(translations)) {
            yield put(setTranslations(instanceId, JSON.parse(translations as string)));
        }
        if (!isEmpty(uploadMediaUrl)) {
            yield put(setUploadMediaUrl(instanceId, uploadMediaUrl));
        }
    }

    if (value && value.payload) {

		let { giftBundles } = value.payload;
        if (!isEmpty(giftBundles)) {
            giftBundles = giftBundles as IBundle[];
            giftBundles = sortBy(giftBundles, (giftBundle: IBundle) => giftBundle.id);
            yield put(setGiftBundles(instanceId, giftBundles));
        }

        let { giftCategories } = value.payload;
        if (!isEmpty(giftCategories)) {
            giftCategories = giftCategories as IGiftCategory[];
            giftCategories = sortBy(giftCategories, (category: IGiftCategory) => category.id);
            yield put(setGiftCategories(instanceId, giftCategories));
        }

        /** We only take the active gifts */
        const { gifts } = value.payload;
        if (!isEmpty(gifts)) {
            yield put(setGifts(instanceId, gifts as IGift[]));
        }

        const { emoticons } = value.payload;
        if (!isEmpty(emoticons)) {
            yield put(setEmoticons(instanceId, emoticons as IEmoticon[]));
        }

        const { currency } = value.payload;
        if (!isEmpty(currency)) {
            // currently, there is an inconsistency regarding the Credits name:
            // from vxcontrol-client-lib it came as 'Credits' and from vchat-core as 'VOC'.
            // However, 'Credits' will be deprecated in favor of 'VOC'
            if (currency.name === creditsDeprecatedName) {
                yield put(setCurrency(instanceId, {
                    name: CurrencyName.Credits,
                    symbol: currency.symbol
                }))
            } else {
                yield put(setCurrency(instanceId, currency as ICurrency));
            }

        }

        let toyOptions = value.payload.toyControls as IToyOption[];
        if (!isEmpty(toyOptions)) {
            toyOptions = sortBy(toyOptions, (option: IToyOption) => option.id);
            yield put(setToyOptions(instanceId, toyOptions));
        }
    }
};

const watchSagas = function*() {
    yield takeEvery(REQUEST_INIT, requestInitSaga);
    yield takeEvery(RECEIVE_INIT, receiveInitSaga);
};

export { onReceiveInit, receiveInitSaga };
export default watchSagas;
