import { Action } from "redux-actions";
import { isMobile } from "react-device-detect";
import { put, select, spawn, takeEvery } from 'redux-saga/effects';

import config from '../../../static/config';
import { createAction, createActionType } from "../../../redux/utils";
import { IPayload } from "../../../redux/interfaces";
import selectors from '../../../redux/selectors';
import { VideoChatType } from "../../../redux/store";
import ChatService from '../../../services/ChatService';
import { cam2CamMaximize, cam2CamMinimize } from '../../../redux/modules/videochat';
import PlayerService, { CameraConfig, CameraPosition } from '../../../services/PlayerService';
import { ActionIds } from "../../../redux/interfaces";
import utils from "../../../utils";

export enum AdjustCam2CamType {
    MINIMIZE,
    MAXIMIZE
}

const createCam2CamActionType = createActionType(ActionIds.CAM2CAM, { isSagaAction: true });

// Actions
const TOGGLE_CAM2CAM = createCam2CamActionType("TOGGLE_CAM2CAM");
const MINIMIZE_CAM2CAM = createCam2CamActionType("MINIMIZE_CAM2CAM");
const MAXIMIZE_CAM2CAM = createCam2CamActionType("MAXIMIZE_CAM2CAM");

// Action Creators
export const toggleCam2Cam = createAction(TOGGLE_CAM2CAM);
export const minimizeCam2Cam = createAction(MINIMIZE_CAM2CAM);
export const maximizeCam2Cam = createAction(MAXIMIZE_CAM2CAM);

// Sagas
export const startCam2CamSaga = function* (instanceId: string, width?: number) {
    const { cam2camMobileFrameWidth, cam2camDesktopFrameWidth } = config.viewSizes;
    const defaultWidth = isMobile ? cam2camMobileFrameWidth : cam2camDesktopFrameWidth;
    const initialVideoType = yield select(selectors.getInitialVideoChatType, instanceId);

    const cameraCloseButton = utils.getCameraCloseButton();

    const configuration: CameraConfig = {
        position: initialVideoType === VideoChatType.NO_DOWNSTREAM ? CameraPosition.FullScreen : CameraPosition.TopLeft,
        width: width || defaultWidth,
        closeButtonElement: cameraCloseButton
    };

    PlayerService.getService(instanceId).startCamera(configuration);
};

export const stopCam2CamSaga = function* (instanceId: string) {
    PlayerService.getService(instanceId).stopCamera();
    ChatService.getService(instanceId).stopUpstream();
    yield put(cam2CamMaximize(instanceId));
};

const adjustCam2Cam = function* ({ payload, type }: Action<IPayload>) {
    const { instanceId } = payload;
    const { MINIMIZE, MAXIMIZE } = AdjustCam2CamType;
    const cam2camType = type === MINIMIZE_CAM2CAM ? MINIMIZE : MAXIMIZE;

    const { cam2camMobileFrameWidth, cam2camDesktopFrameWidth } = config.viewSizes;
    const initialWidth = isMobile ? cam2camMobileFrameWidth : cam2camDesktopFrameWidth;
    const width = (cam2camType === AdjustCam2CamType.MINIMIZE) ? initialWidth / 2 : initialWidth;

    PlayerService.getService(instanceId).updateCamera({ width });

    yield put(cam2camType === AdjustCam2CamType.MINIMIZE
        ? cam2CamMinimize(instanceId)
        : cam2CamMaximize(instanceId)
    );
};

const toggleCam2CamSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const isCam2CamActive = yield select(selectors.isCam2CamActive, instanceId);

    yield spawn(isCam2CamActive ? stopCam2CamSaga : startCam2CamSaga, instanceId);
};

const watchSagas = function* () {
    yield takeEvery(TOGGLE_CAM2CAM, toggleCam2CamSaga);
    yield takeEvery(MINIMIZE_CAM2CAM, adjustCam2Cam);
    yield takeEvery(MAXIMIZE_CAM2CAM, adjustCam2Cam);
};

export default watchSagas;
