import * as React from 'react';
import { VersionBox as VersionBoxWrapper } from './style';

export interface IVersionBoxProps {
    isVersionOpen: boolean;
    version: string;
}

const VersionBox = (props: IVersionBoxProps) => (
    <VersionBoxWrapper className={!props.isVersionOpen ? 'hidden' : ''}>
        {props.version}
    </VersionBoxWrapper>
);

export default VersionBox;