import * as React from "react";

import { IGift, ITranslation, ICurrency } from "../../../redux/store";
import {
	Text,
	TipImage,
	TipPrice,
	UserPromptTipButton,
	UserPromptTipSubTitle,
	UserPromptTipTitle,
	UserPromptTipWrapper
} from './style';

interface IProps {
    currency: ICurrency;
    isResponsive?: boolean;
    tip: IGift;
    translation: ITranslation;
    imageSize: number;

    sendTip: () => void;
}

const UserPromptAnonymousTip = (props: IProps) => {
    const { voyeurTip1, voyeurTip2, voyeurTip3, voyeurTip4 } = props.translation.chat;

    return(
        <UserPromptTipWrapper isResponsive={props.isResponsive}>
            <UserPromptTipTitle>{voyeurTip1}</UserPromptTipTitle>
            <UserPromptTipSubTitle>{voyeurTip2}</UserPromptTipSubTitle>

            <Text>({voyeurTip3})</Text>

            <TipImage
                imageUrl={props.tip.imageUrl}
                imageSize={props.imageSize}
            />
            
            <TipPrice>
                {`(${props.tip.price} ${props.currency.symbol})`}
            </TipPrice>

            <UserPromptTipButton onClick={props.sendTip}>
                {voyeurTip4}
            </UserPromptTipButton>
        </UserPromptTipWrapper>
    );
};

export default UserPromptAnonymousTip;
