import * as React from 'react';
import { Image, Text, Wrapper, Lock, PlayingStatus, PlayingStatusWrapper, ImageWrapper } from "./style";  

export interface IProps {
    name: string;
    image: string;
    hoverImage: string;
    currency: string;
    price: number;
    durationText: string;
    isResponsive: boolean;
    isPlaying?: boolean;
    isLocked?: boolean;

    // Only the locked options will have an informative tooltip open
    hasTooltipOpen?: boolean;

    // This property is going to be false if we already have an active
    // toy option active and this one is unlocked
    isAvailable?: boolean;
}

const ToyOption = (props: IProps) => {
    const canPlayOption = props.isAvailable && !props.isLocked;

    return (
        <>
            <Wrapper isClickable={props.isAvailable}>
                <ImageWrapper>
                    {props.isLocked && <Lock className={"-icon-keylock_close"} />}

                    {props.isPlaying && (
                        <PlayingStatusWrapper>
                            <PlayingStatus>Active</PlayingStatus>
                        </PlayingStatusWrapper>
                    )}

                    <Image
                        image={props.image}
                        isActive={props.isPlaying}
                        canPlayOption={canPlayOption}
                    />
                </ImageWrapper>
            </Wrapper>
         
            <Text
	            isResponsive={props.isResponsive}
	            hasTooltipOpen={props.hasTooltipOpen}
            >
                <div>{props.name} {props.currency}{props.price}</div>
                {props.durationText && <div>{props.durationText}</div>}
            </Text>
        </>
    );
}

export default ToyOption;
