import { Action } from 'redux-actions';
import { select, takeEvery } from 'redux-saga/effects';

import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import { TOGGLE_TOY_SECTION, OPEN_CONTACT_NOTE } from '../../redux/modules/messenger';
import AnalyticsService, { AnalyticsCategories, AnalyticsActions } from "../../services/AnalyticsService";
import { SET_IS_MUTED_BY_CLIENT, TOGGLE_FULLSCREEN, OPEN_FULLSCREEN } from '../../redux/modules/videochat';
import { ActionIds } from '../../redux/interfaces';

const createAnalyticsActionType = createActionType(ActionIds.ANALYTICS, { isSagaAction: true });

// Actions
const OPEN_GIFT_BUNDLES_PAGE = createAnalyticsActionType("OPEN_GIFT_BUNDLES_PAGE");

// Action Creators
export const openGiftBundlesPage = createAction(OPEN_GIFT_BUNDLES_PAGE);

// Sagas
const toySectionAnalyticsSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const isToySectionOpen = yield select(selectors.isToySectionOpen, instanceId);

    if (isToySectionOpen) {
        AnalyticsService.triggerEvent(
            AnalyticsCategories.VIDEOCHAT,
            AnalyticsActions.TOY_SECTION
        );
    }
};

const contactNoteAnalyticsSaga = function* () {
    AnalyticsService.triggerEvent(
        AnalyticsCategories.MESSENGER,
        AnalyticsActions.CONTACT_NOTE
    );
};

const giftBundlesAnalyticsSaga = function* () {
    AnalyticsService.triggerEvent(
        AnalyticsCategories.MESSENGER,
        AnalyticsActions.GIFT_BUNDLES
    );
};

const muteModelAnalyticsSaga = function* () {
    AnalyticsService.triggerEvent(
        AnalyticsCategories.VIDEOCHAT,
        AnalyticsActions.MUTE_MODEL
    );
};

const fullscreenAnalyticsSaga = function* ({ payload: { instanceId }, type }: Action<IPayload>) {
    if (type === TOGGLE_FULLSCREEN) {
        const isFullscreen = yield select(selectors.isFullScreen, instanceId);
        if (!isFullscreen) { return; }
    }

    AnalyticsService.triggerEvent(
        AnalyticsCategories.VIDEOCHAT,
        AnalyticsActions.FULLSCREEN_VIDEOCHAT
    );
};

const watchSagas = function*() {
    yield takeEvery(TOGGLE_TOY_SECTION, toySectionAnalyticsSaga);
    yield takeEvery(OPEN_CONTACT_NOTE, contactNoteAnalyticsSaga);
    yield takeEvery(OPEN_GIFT_BUNDLES_PAGE, giftBundlesAnalyticsSaga);
    yield takeEvery(SET_IS_MUTED_BY_CLIENT, muteModelAnalyticsSaga);

    yield takeEvery(TOGGLE_FULLSCREEN, fullscreenAnalyticsSaga);
    yield takeEvery(OPEN_FULLSCREEN, fullscreenAnalyticsSaga);
};

export default watchSagas;
