import * as React from 'react';
import { isIE } from "react-device-detect";

import { ICurrency, IGift, ITranslation } from '../../../redux/store';
import ActionResponseContainer from '../../common/ActionResponse/ActionResponseContainer';
import MessageSectionButtonsContainer from '../MessageSectionButtons';
import {
    DropDownButton,
    IMessengerBaseButtonProps,
    MessengerBaseButton,
    MessengerBaseButtonIcon,
    MessengerButtonsWrapper,
    MessengerSectionWrapper,
    ToyControlBaseButtonIcon,
    ToyControlIcon
} from './style';
import EditorContainer from '../Editor';
import DefaultEditorContainer from '../DefaultEditor';
import utils from '../../../utils';
import withIdentifier, { DomIdentifier } from "../../hoc/withIdentifier";
import AudioMessageUploaderContainer from '../AudioMessageUploader';

const SendMessageButton = withIdentifier<IMessengerBaseButtonProps>(MessengerBaseButton, DomIdentifier.SendMessageButton);
const AudioMessageButton = withIdentifier<IMessengerBaseButtonProps>(MessengerBaseButton, DomIdentifier.AudioMessageButton);

export interface IProps {
    hasContent: boolean;
    emoticon?: string;
    gift?: IGift;
    mention?: string;
    hasEditor: boolean;
    currency: ICurrency;
    hasFreeMessages: boolean;
    isEmoticonSectionOpen: boolean;
    isDropDownOpen: boolean;
    shouldResetEditorState: boolean;
    messagePrice: number;
    translation: ITranslation;
    isGiftSectionOpen: boolean;
    isInteractive: boolean;
    isResponsive: boolean;
    isVideoChatOpen: boolean;
    isFullscreen: boolean;
    hasSendMessageButton: boolean;
    hasOnlySendButton: boolean;
    hasToyControlButton: boolean;
    shouldDisplayMessageSectionButtons: boolean;
    isAnyTextInEditor: boolean;
    hasAudioMessages: boolean;

    expandDropdown: () => void;
    sendMessage: () => void;
    toggleToySection: () => any;
    onTextChanged: (text: string) => void;
    onStartRecording: () => void;
    onGiftTextChanged: (giftText: string) => void;
}

const MessageSection = (props: IProps) => {
    const { writePrice, writeFree, write } = props.translation.message;
    const amount = `${props.messagePrice} ${props.currency.symbol}`;

    /**
     * We should render the default placeholder if we are in videochat.
     * We should render the free placeholder if the user has free messages OR the price is 0.
     * We should render the price placeholder if the price is more than 0 AND has no free messages.
     */
    let placeholder;
    const hasFreeMessagesPlaceholder = props.hasFreeMessages || props.messagePrice === 0;
    const placeholderWithPrice = utils.translationWithValues(writePrice, { amount });

    placeholder = hasFreeMessagesPlaceholder ? writeFree : placeholderWithPrice;
    placeholder = props.isVideoChatOpen ? write : placeholder;

    let encodedGiftId: string | undefined;
    if (props.gift) {
        encodedGiftId = utils.getGiftEncoding(props.gift);
    }

    const editor = (
        <EditorContainer
            emoticon={props.emoticon}
            gift={encodedGiftId}
            mention={props.mention}
            placeholder={placeholder}
            shouldResetEditorState={props.shouldResetEditorState}

            onTextChanged={props.onTextChanged}
            sendMessage={props.sendMessage}
        />
    );

    const defaultEditor = (
        <DefaultEditorContainer
            emoticon={props.emoticon}
            mention={props.mention}
            isInteractive={props.isInteractive}
            placeholder={placeholder}
            gift={props.gift}
            shouldResetEditorState={props.shouldResetEditorState}

            onTextChanged={props.onTextChanged}
            sendMessage={props.sendMessage}
            onGiftTextChanged={props.onGiftTextChanged}
        />
    );

    const responsiveDropDown = (
        <DropDownButton
            isInverted={props.isDropDownOpen}
            onClick={props.expandDropdown}
        >
            <MessengerBaseButtonIcon
                className={"-icon-round-plus"}
                isInverted={props.isDropDownOpen}
            />
        </DropDownButton>
    );

    const sectionButtons = props.isResponsive ? responsiveDropDown : <MessageSectionButtonsContainer />;
    const shouldRenderAudioButton = !props.isAnyTextInEditor && props.hasAudioMessages;
	const shouldRenderSendMessageButton = props.isInteractive && props.hasSendMessageButton;
	const hasButtonsMargin = props.isFullscreen && props.hasToyControlButton;

	// IE-SPECIFIC: the audio messages feature will be disabled on any IE browser.
	const hasAudioMessages = !isIE;

    return (
        <>
            <ActionResponseContainer />

            {hasAudioMessages && <AudioMessageUploaderContainer />}

            <MessengerSectionWrapper isFullscreen={props.isFullscreen}>
                {props.hasEditor ? editor : defaultEditor}

                <MessengerButtonsWrapper hasButtonsMargin={hasButtonsMargin}>
                    {shouldRenderAudioButton
                        ?   <AudioMessageButton
                                title={props.translation.message.sendAudioMessage}
                                onClick={props.onStartRecording}
                            >
                                <MessengerBaseButtonIcon
                                    className={"-icon-voice-recording-full"}
                                    hasLeftSeparator={true}
                                />
                            </AudioMessageButton>
                        : shouldRenderSendMessageButton &&
                            <SendMessageButton
                                isActive={props.hasContent}
                                title={props.translation.message.send}
                                hasOnlySendButton={props.hasOnlySendButton}
                                onClick={props.sendMessage}
                            >
                                <MessengerBaseButtonIcon
                                    className={"-icon-send-chat-full"}
                                    hasLeftSeparator={true}
                                />
                            </SendMessageButton>
                    }

                    {props.shouldDisplayMessageSectionButtons && <>{sectionButtons}</>}

                    {props.hasToyControlButton &&
                        <MessengerBaseButton
                            isActive={props.hasContent}
                            title={props.translation.chat.toyControl.toyControl}
                            onClick={props.toggleToySection}
                        >
                            <ToyControlBaseButtonIcon hasLeftSeparator={true}>
                                <ToyControlIcon className={"-icon-toy-control-vibrating"} />
                            </ToyControlBaseButtonIcon>
                        </MessengerBaseButton>

                    }
                </MessengerButtonsWrapper>

            </MessengerSectionWrapper>
        </>
    );
};

export default MessageSection;

