import { isEmpty } from  'lodash';
import { Action } from "redux-actions";
import { put, select, takeEvery, delay, take } from 'redux-saga/effects';

import { closeApp } from "./close";
import loggers from "../../loggers";
import config from '../../static/config';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import { ActionIds } from '../../redux/interfaces';
import { openCloseChatPrompt } from "./userPrompt";
import AppService from "../../services/AppService";
import { ExitCode } from "../../services/ChatService/types";
import { SET_USER_ONLINE_STATE } from "../../redux/modules/user";
import { isOutOfMoneyInformationDialog } from "../../utils/utils";
import {closeAllUserPrompts} from '../../sagas/common/userPrompt';
import { setHooks } from '../../redux/modules/hooks';
import { resetModelFreeChatDuration } from "../../redux/modules/model";
import { hasLoadAccountUserPrompts } from "../../redux/selectors/messenger";
import { resetPlatformFreeChatDuration, SET_CHAT_ID } from "../../redux/modules/session";
import {
    CLOSE_MESSENGER,
    closeLoadAccountUserPrompts,
    closeMessenger,
    closeVersion,
    OPEN_VERSION
} from '../../redux/modules/messenger';
import {
    CLOSE_VIDEOCHAT,
    closeVideochat,
    displayInitiallyMutedHint,
    hideInitiallyMutedHint,
    OPEN_VIDEOCHAT,
    RESET_INFORMATION,
    resetExitEvent,
    resetPreChatInformation,
    SET_INFORMATION,
    SET_IS_INITIALLY_MUTED,
    setInformation
} from "../../redux/modules/videochat";
import {
    IChatFeatures,
    VideochatInformationType, VideoChatType
} from '../../redux/store';
import {
    startVideoChatType,
    stopVideoChat,
    stopVideoChatPlayer
} from "./videochat";
import { takePattern } from '../utils';

const createUIActionType = createActionType(ActionIds.UI, { isSagaAction: true });

// Actions
export const CLICK_CLOSE_MESSENGER_BUTTON = createUIActionType("CLICK_CLOSE_MESSENGER_BUTTON");
export const DISPLAY_CONTINUE_WITH_LIVECHAT = createUIActionType("DISPLAY_CONTINUE_WITH_LIVECHAT");
export const CLOSE_PRECHAT_DIALOG = createUIActionType("CLOSE_PRECHAT_DIALOG");
export const ACCEPT_TO_SPY_FROM_PRECHAT_DIALOG = createUIActionType("ACCEPT_TO_SPY_FROM_PRECHAT_DIALOG");

// Action Creators
export const displayContinueWithLiveChatDialog = createAction(DISPLAY_CONTINUE_WITH_LIVECHAT);
export const clickCloseMessengerButton = createAction(CLICK_CLOSE_MESSENGER_BUTTON);
export const closePrechatDialog = createAction(CLOSE_PRECHAT_DIALOG);
export const acceptToSpyFromPrechatDialog = createAction(ACCEPT_TO_SPY_FROM_PRECHAT_DIALOG);

// Sagas
export const triggerCloseMessengerSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    loggers.saga.debug('Closing messenger...');

    const exitEvent = yield select(selectors.getExitEvent, instanceId);
    const exitCode: ExitCode | undefined = exitEvent && exitEvent.exitCode;
    const exitMessage: string | undefined = exitEvent && exitEvent.exitMessage;

    // Call the external hooks
    const appService = AppService.getService(instanceId);
    appService.onExit(exitCode || ExitCode.USER, exitMessage);
	const isVideoChatOpen = yield select(selectors.isVideoChatOpen, instanceId);
	const chatId = yield select(selectors.getChatId, instanceId);

    if (isVideoChatOpen) {
        appService.onVideoChatAction(false, chatId);
    }

    yield put(closeApp(instanceId));

    loggers.saga.debug('Messenger closed');
};

export const displayContinueWithLiveChatDialogSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    let shouldDisplayContinueWithLiveChat = yield select(selectors.shouldDisplayContinueWithLiveChat, instanceId);

    /**
     * If preview chat, after preview time passes, the player should
     * freeze and render an information pop-up for selecting livechat.
     */
    if (shouldDisplayContinueWithLiveChat) {
        const chatFeatures: IChatFeatures = yield select(selectors.getChatFeatures, instanceId);
        const hasFreeChatDuration: boolean = yield select(selectors.hasFreeChatDuration, instanceId);
        const freeModelChatDuration: number = yield select(selectors.getFreeModelChatDuration, instanceId);
        const isVideoChatInPreviewMode = yield select(selectors.isVideoChatInPreviewMode, instanceId);

        const { freeChatDuration, livePreviewDurationVideo } = chatFeatures;
        const totalFreeChatDuration =  hasFreeChatDuration ? freeChatDuration + freeModelChatDuration : 0;
        const previewTime = isVideoChatInPreviewMode ? livePreviewDurationVideo : totalFreeChatDuration;

        yield delay(previewTime * 1000);

        /**
         * After the delay, we check if the videochat type is still PREVIEW.
         * The customer may switch to livechat before the delay time and this
         * behavior should not be activated.
         */
        shouldDisplayContinueWithLiveChat = yield select(selectors.shouldDisplayContinueWithLiveChat, instanceId);

        if (shouldDisplayContinueWithLiveChat) {
            const isVideoChatInFreeChatMode = yield select(selectors.isVideoChatInFreeChatMode, instanceId);

            if (isVideoChatInFreeChatMode) {
                yield put(resetPlatformFreeChatDuration(instanceId));
                yield put(resetModelFreeChatDuration(instanceId));
            }

            yield put(stopVideoChatPlayer(instanceId));
            loggers.saga.debug('Live preview has finished');

            yield put(setInformation(instanceId, VideochatInformationType.PREVIEW_JUST_FINISHED));
        }
    }
};

export const displayUserOfflineMessageSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    const shouldRenderUserOfflineInformation = yield select(selectors.shouldRenderUserOfflineInformation, instanceId);

    if (shouldRenderUserOfflineInformation) {
        yield put(stopVideoChatPlayer(instanceId));
        yield put(setInformation(instanceId, VideochatInformationType.OFFLINE_CONNECTION));
    }
};

export const triggerPreChatInformationSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    yield put(closeVideochat(instanceId));
};

export const reactToInformationDialogSaga =  function* ({ payload }: Action<IPayload<VideochatInformationType>>) {
    const { instanceId, value } = payload;

    const isOutOfMoneyInformation = isOutOfMoneyInformationDialog(value);
    const hasLoadAccountPrompts = yield select(hasLoadAccountUserPrompts, instanceId);

    if (isOutOfMoneyInformation && hasLoadAccountPrompts) {
        yield put(closeLoadAccountUserPrompts(instanceId));
    }

    const hasExitPopup = yield select(selectors.hasExitPopups, instanceId);

    if (hasExitPopup) {
        const isExitingInformation = yield select(selectors.isExitingInformationType, instanceId);
        const lastCallArguments = yield select(selectors.geHook('onExitPopupAction'), instanceId);

        if (isEmpty(lastCallArguments) || isExitingInformation !== lastCallArguments[0]) {
			AppService.getService(instanceId).onExitPopupAction(isExitingInformation);

            yield put(setHooks(instanceId, {
                name: 'onExitPopupAction',
                args: [isExitingInformation]
            }));
        }
    }
};

export const triggerClickCloseButtonSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    const videoChatType = yield select(selectors.getVideoChatType, instanceId);
    const isCloseChatUserPromptOpen = yield select(selectors.isCloseChatUserPromptOpen, instanceId);
    const isFullScreen = yield select(selectors.isFullScreen, instanceId);

    if (videoChatType) {
        if (isCloseChatUserPromptOpen || isFullScreen) {

            // user prompt already open? => stop video chat voluntarily
            yield put(stopVideoChat(instanceId, true));
            yield put(closeAllUserPrompts(instanceId));

            // check if the exit popups are supposed to be handled externally or not
            const hasExitPopups = yield select(selectors.hasExitPopups, instanceId);

            yield put(hasExitPopups
                ? setInformation(instanceId, VideochatInformationType.LIVECHAT_SESSION_ENDED)
                : closeMessenger(instanceId)
            );

        } else {
            yield put(openCloseChatPrompt(instanceId));
        }
    } else {
        // no video chat is running => close it directly;
        yield put(closeMessenger(instanceId));
    }
};

export const triggerOpenCloseVideoChatSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
	const isVideoChatOpen = yield select(selectors.isVideoChatOpen, instanceId);
	const isChatExternallyProvided = yield select(selectors.isChatExternallyProvided, instanceId);
	let chatId = yield select(selectors.getChatId, instanceId);

	// When chat is externally provided, we receive only the clientId and server values.
	// The chatID value comes from the vchat-core initialization, by which we should wait.
	if (isEmpty(chatId) && isChatExternallyProvided) {
		const { payload: { value }} = yield take(takePattern(SET_CHAT_ID, instanceId));
		chatId = value;
	}

    AppService.getService(instanceId).onVideoChatAction(isVideoChatOpen, chatId);
};

export const openVersionSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    yield delay(config.versionDisplayDuration * 1000);
    yield put(closeVersion(instanceId));
};

/** We receive a flag, with which we can see if the model is muted  */
export const initiallyMutedSaga = function* ({ payload }: Action<IPayload>) {
    const { instanceId, value } = payload;

    if (value) {
        yield put(displayInitiallyMutedHint(instanceId));
        yield delay(10000);
        yield put(hideInitiallyMutedHint(instanceId));
    }
};

export const closePrechatDialogSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const initialVideoType: VideoChatType = yield select(selectors.getInitialVideoChatType, instanceId);

    if(initialVideoType) {
        // exit event has been populated in the store
        // just before displaying the pre-chat information dialog
        yield put(closeMessenger(instanceId));
    } else {
        yield put(resetExitEvent(instanceId));
        yield put(closeVideochat(instanceId));
    }

    // close pre-chat dialog
    yield put(resetPreChatInformation(instanceId));
};

export const acceptToSpyFromPrechatDialogSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    yield put(stopVideoChat(instanceId, true));
    yield put(startVideoChatType(instanceId, VideoChatType.VOYEUR_PRIVATE_CHAT));
};

const watchSagas = function*() {
    // when the messenger close button is clicked
    yield takeEvery(CLICK_CLOSE_MESSENGER_BUTTON, triggerClickCloseButtonSaga);
    yield takeEvery(CLOSE_MESSENGER, triggerCloseMessengerSaga);

    yield takeEvery(DISPLAY_CONTINUE_WITH_LIVECHAT, displayContinueWithLiveChatDialogSaga);
    yield takeEvery(SET_USER_ONLINE_STATE, displayUserOfflineMessageSaga);

    yield takeEvery(SET_INFORMATION, reactToInformationDialogSaga);
    yield takeEvery(RESET_INFORMATION, reactToInformationDialogSaga);

    // informing the platform about video mode on or off
    yield takeEvery(OPEN_VIDEOCHAT, triggerOpenCloseVideoChatSaga);
    yield takeEvery(CLOSE_VIDEOCHAT, triggerOpenCloseVideoChatSaga);

    // when the version box is open
    yield takeEvery(OPEN_VERSION, openVersionSaga);

    // deal with initially muted button
    yield takeEvery(SET_IS_INITIALLY_MUTED, initiallyMutedSaga);

    // when the close button of a pre-chat information dialog is clicked
    yield takeEvery(CLOSE_PRECHAT_DIALOG, closePrechatDialogSaga);

    // when 'yes' button of a pre-chat information dialog asking to spy as voyeur is clicked
    yield takeEvery(ACCEPT_TO_SPY_FROM_PRECHAT_DIALOG, acceptToSpyFromPrechatDialogSaga);
};

export default watchSagas;
