import produce from "immer";
import { Action } from "redux-actions";
import { has, isEqual, includes, isEmpty } from "lodash";

import IStore from "../store";
import { IPayload, ActionIds } from "../interfaces";
import { createAction, createActionType } from "../utils";
import initialStore, { getInitialInstanceData } from "../initialStore";
import features from '../modules/features';
import options from '../modules/options';
import hooks from '../modules/hooks';
import messenger from '../modules/messenger';
import model from '../modules/model';
import platformProps from '../modules/platformProps';
import session from '../modules/session';
import user from '../modules/user';
import videochat from '../modules/videochat';
import { RESET_STORE } from "../modules/app";

const createRootActionType = createActionType(ActionIds.ROOT, { isReduxAction: true });

// Actions
const INITIALIZE_MESSENGER_INSTANCE = createRootActionType("INITIALIZE_MESSENGER_INSTANCE");

// Action Creators
export const initializeMessengerInstance = createAction<string>(INITIALIZE_MESSENGER_INSTANCE);

/**
 * The root reducer has the responsability of mapping the desired messenger
 * instance with the received action and alter the data in a pure function.
 */
const rootReducer = (store: IStore = initialStore, action: Action<IPayload>): IStore => produce(store, draft => {
    if (isEmpty(action.payload)) {
        return draft;
    }

    const { payload: { instanceId, value: payload }, type } = action;
    const hasInstanceInitialized = has(draft, instanceId);

    if (isEqual(type, INITIALIZE_MESSENGER_INSTANCE) && !hasInstanceInitialized) {
        draft[instanceId] = getInitialInstanceData();
        return draft;
    }

    if (!hasInstanceInitialized) {
        return draft;
    }

    const reducerAction = { type, payload };

    const isGlobalAction = includes([RESET_STORE],type);

    // We will call the helper reducer we need based on our action type
    if (includes(type, ActionIds.FEATURES) || isGlobalAction) {
        draft[instanceId].features = features(draft[instanceId].features, reducerAction);
	}
	
	if (includes(type, ActionIds.OPTIONS) || isGlobalAction) {
        draft[instanceId].options = options(draft[instanceId].options, reducerAction);
    }

    if (includes(type, ActionIds.HOOKS) || isGlobalAction) {
        draft[instanceId].hooks = hooks(draft[instanceId].hooks, reducerAction);
    }

    if (includes(type, ActionIds.MESSENGER) || isGlobalAction) {
        draft[instanceId].messenger = messenger(draft[instanceId].messenger, reducerAction);
    }

    if (includes(type, ActionIds.MODEL) || isGlobalAction) {
        draft[instanceId].model = model(draft[instanceId].model, reducerAction);

    }

    if (includes(type, ActionIds.PLATFORM_PROPS) || isGlobalAction) {
        draft[instanceId].platformProps = platformProps(draft[instanceId].platformProps, reducerAction);
    }

    if (includes(type, ActionIds.SESSION) || isGlobalAction) {
        draft[instanceId].session = session(draft[instanceId].session, reducerAction);
    }

    if (includes(type, ActionIds.USER) || isGlobalAction) {
        draft[instanceId].user = user(draft[instanceId].user, reducerAction);
    }

    if (includes(type, ActionIds.VIDEOCHAT_REDUX) || isGlobalAction) {
        draft[instanceId].videochat = videochat(draft[instanceId].videochat, reducerAction);
	}
	
    return draft;
}) as any;

export default rootReducer;
