import * as React from 'react';
import Tooltip from '../../common/Tooltip';
import { VideoChatButton, VideoChatButtonWrapper } from './style';
import { IVideoChatBaseButtonProps } from "./types";

const VideoChatBaseButton = (props: IVideoChatBaseButtonProps) => (
    <VideoChatButtonWrapper onClick={props.onClick}>
        {props.text && props.id
            ?
                <Tooltip text={props.text} id={props.id}>
                    <VideoChatButton
                        isOpen={props.isOpen}
                        backgroundColor={props.backgroundColor}
                    >
                        {props.children}
                    </VideoChatButton>
                </Tooltip>
            :
                <VideoChatButton
                    isOpen={props.isOpen}
                    backgroundColor={props.backgroundColor}
                >
                    {props.children}
                </VideoChatButton>
        }
    </VideoChatButtonWrapper>
);

export default VideoChatBaseButton;
