import * as React from 'react';

import { ITranslation } from '../../../redux/store';
import Header, { HeaderAlignment } from '../../common/Header';
import ChatBoxContainer from '../ChatBox';
import MessageSectionContainer from '../MessageSection';
import ContactNoteDialogContainer from '../ContactNoteDialog';
import ProfileContainer from '../Profile';
import { BodySection, MessengerOverlay, MessengerWrapper } from './style';
import CloseButtonContainer from '../../common/CloseButton';
import { CloseButtonType } from '../../common/CloseButton/CloseButtonContainer';
import CountdownWarningContainer from '../../specific/CountdownWarning';
import ActionSectionContainer from '../ActionSection';
import ToySectionHeaderContainer from '../../common/ToySectionHeader';
import MessageTypeHeaderContainer from '../MessageTypeHeader';
import MentionSectionContainer from '../MentionSection';

export interface IMessengerProps {
    isContactNoteOpen: boolean;
    hasActionSectionUnderTextbox: boolean;
    shouldRenderToySectionHeader: boolean;
    isMessageTypeHeader?: boolean;
    translation: ITranslation;

    onClose: () => void;
}

const Messenger = (props: IMessengerProps) => {
    const { close } = props.translation.common;

    const headerContent = props.isMessageTypeHeader
        ? <MessageTypeHeaderContainer />
        : <ProfileContainer />;

    const headerContentAlignment = props.isMessageTypeHeader
        ? HeaderAlignment.CENTER
        : HeaderAlignment.LEFT;

    return (
        <MessengerWrapper>
            <Header alignment={headerContentAlignment}>
                {headerContent}

                <CloseButtonContainer
                    onClick={props.onClose}
                    hoverText={close}
                    closeButtonType={CloseButtonType.MESSENGER}
                    iconClassName={'-icon-close-line'}
                />
            </Header>

            <BodySection>
                <ChatBoxContainer />

                <MentionSectionContainer />
            </BodySection>

            <CountdownWarningContainer />

            <MessageSectionContainer />

            {props.shouldRenderToySectionHeader && <ToySectionHeaderContainer isToySectionMinimizied={true}/>}

            {props.hasActionSectionUnderTextbox && <ActionSectionContainer />}

            {props.isContactNoteOpen &&
                <>
                    <MessengerOverlay />
                    <ContactNoteDialogContainer />
                </>
            }
        </MessengerWrapper>
    );
};

export default Messenger;
