import { get, includes } from 'lodash';
import { call, put, select} from "redux-saga/effects";

import { ExitCode } from "../../../services/ChatService/types";
import { getChatInstanceSaga, IGetChatInstanceSagaResult } from "../chat";
import selectors from "../../../redux/selectors";
import types from "../../websockets/types";
import loggers from "../../../loggers";
import { openSpyPrivateChatPrompt, openVerifyAgePrompt } from "../userPrompt";
import { setExitEvent, setPreChatInformation } from "../../../redux/modules/videochat";
import { PreChatInformationType, VideoChatType } from "../../../redux/store";
import { getPrechatByErrorCode } from "../../../utils/utils";
import { MessengerSpecificErrorCodes } from "../../../static/constants/errorCodes";
import { VXControlCodes } from "../../../static/constants/errorCodes";

export interface IPreChecksResult {
    isSuccessful: boolean;
    chat?: IGetChatInstanceSagaResult;
}

const subActions: {[key: string]: types.SubActionType} = {};
subActions[VideoChatType.FREECHAT] = types.SubActionType.STARTFREECHAT;
subActions[VideoChatType.LIVECHAT] = types.SubActionType.STARTVIDEOCHAT;
subActions[VideoChatType.LIVE_PREVIEW] = types.SubActionType.STARTLIVEPREVIEW;
subActions[VideoChatType.NO_DOWNSTREAM] = types.SubActionType.STARTVIDEOCHAT;
subActions[VideoChatType.PRIVATE_CHAT] = types.SubActionType.STARTVIDEOCHAT;
subActions[VideoChatType.VOYEUR_CHAT] = types.SubActionType.STARTVOYEURCHAT;
subActions[VideoChatType.VOYEUR_PRIVATE_CHAT] = types.SubActionType.STARTVOYEUR2SCHAT;

const typesAllowedToSpyVoyeurs: VideoChatType[] = [
    VideoChatType.LIVECHAT,
    VideoChatType.VOYEUR_CHAT
];

/**
 * Saga doing necessary pre-checks before starting a video chat
 * @param videoChatType
 * @return {IPreChecksResult}
 */
const precheckSaga = function* (instanceId: string, videoChatType: VideoChatType, requestNewConfig: boolean) {
    const commonChecksResult: boolean = yield call(commonChecks, instanceId, videoChatType);

    if (!commonChecksResult) {
        return { isSuccessful: false };
    }

    // special checks for live preview, based solely on vxcontrols-client-lib flags
    if (videoChatType === VideoChatType.LIVE_PREVIEW) {
        const isNotChargedNorVip = yield select(selectors.isNotChargedNorVip, instanceId);
        const isChatExternallyProvided = yield select(selectors.isChatExternallyProvided, instanceId);

        if (isNotChargedNorVip && !isChatExternallyProvided) {
            yield call(displayPrechatInformationDialogSaga, instanceId, ExitCode.PREVIEW_ACCESS_DENIE);
            return { isSuccessful: false };
        }
    }

    // everything seems fine by now; try to initiate a video chat and watch over any error, if any
    const chat: IGetChatInstanceSagaResult = yield call(
        getChatInstanceSaga,
        requestNewConfig,
        instanceId,
        subActions[videoChatType]
    );

    const isSuccessful: boolean = yield call(chatBasedChecksSaga, instanceId, chat, videoChatType);

    return { isSuccessful, chat };
};

/**
 * Saga performing common checks for all video type sessions
 * @return {boolean} true if everything went well and session can be started, false otherwise
 */
const commonChecks = function* (instanceId: string, videoChatType: VideoChatType) {
    // check for USER being online
    const isUserOffline = yield select(selectors.isUserOffline, instanceId);
    if (isUserOffline) {
        yield call(displayPrechatInformationDialogSaga, instanceId, MessengerSpecificErrorCodes.GUEST_OFFLINE);
        return false;
    }

    const isChatExternallyProvided = yield select(selectors.isChatExternallyProvided, instanceId);

    // if we can count on the vxcontrol-client-lib API and therefore can use model status flags
    if (!isChatExternallyProvided) {
        // check for MODEL being online
        const isModelOnline = yield select(selectors.isModelOnline, instanceId);
        if (!isModelOnline) {
            yield call(displayPrechatInformationDialogSaga, instanceId, ExitCode.PREVIEW_OFFLINE);
            return false;
        }

        // check for model being in private chat, based in the model status flags only
        const isModelInPrivateChat = yield select(selectors.isModelInPrivateChat, instanceId);
        if (includes(typesAllowedToSpyVoyeurs,videoChatType) && isModelInPrivateChat) {
            const isAcceptinVoyeurs = yield call(offerSwitchingToVoyeurSpySaga, instanceId, videoChatType);

            if (!isAcceptinVoyeurs) {
                loggers.saga.debug('model is in private chat and she does not accept voyeurs either');
                yield call(displayPrechatInformationDialogSaga, instanceId, ExitCode.HOSTSINGLE);
            }

            return false;
        }
    }

    return true;
};

/**
 * Saga performing checks and displaying appropriate dialogs  if necessary,
 * based on the error code (if any) receiving while trying to initialize a video session
 * @param chat
 * @return {boolean} true if everything went well and session can be started, false otherwise
 */
const chatBasedChecksSaga = function* (instanceId, chat: IGetChatInstanceSagaResult, videoChatType: VideoChatType) {
    const chatConfErrorCode = get(chat, 'chatConf.error.code');

    if (!chatConfErrorCode) {
        return true;
    }

    // check for model being in private chat
    if (includes(typesAllowedToSpyVoyeurs,videoChatType)
        && chatConfErrorCode === VXControlCodes.MODEL_IS_IN_PRIVATE_CHAT
        || chatConfErrorCode === ExitCode.HOSTSINGLE) {

        const isAcceptinVoyeurs = yield call(offerSwitchingToVoyeurSpySaga, instanceId, videoChatType);

        if (!isAcceptinVoyeurs) {
            loggers.saga.debug('model is in private chat and she does not accept voyeurs either');
            yield put(setPreChatInformation(instanceId, PreChatInformationType.PRIVATE_CHAT));
        }

        return false;
    }

    const isChatExternallyProvided = yield select(selectors.isChatExternallyProvided, instanceId);
    const initialVideoType: VideoChatType = yield select(selectors.getInitialVideoChatType, instanceId);

    // check for age verification, when should be displayed as user prompt,
    if (!isChatExternallyProvided && !initialVideoType && chatConfErrorCode === VXControlCodes.SOFT_CHAT) {
        yield put(openVerifyAgePrompt(instanceId));

        return false;
    }

    // other checks
    yield call(displayPrechatInformationDialogSaga, instanceId, chatConfErrorCode);

    return false;
};


const offerSwitchingToVoyeurSpySaga = function* (instanceId: string, videoChatType: VideoChatType) {
    // let's see if the model accept voyeurs while in private chat
    const isModelSupportingVoyeurToPrivateChat = yield select(selectors.isModelSupportingVoyeurToPrivateChat, instanceId);

    if (isModelSupportingVoyeurToPrivateChat) {
        loggers.saga.debug('offering to start voyeur to private chat');

        const initialVideoType: VideoChatType = yield select(selectors.getInitialVideoChatType, instanceId);
        if (initialVideoType) {
            // if started with initial video type, we should display a pre-chat dialog
            // offering the possibility to spy
            yield put(setExitEvent(instanceId, { exitCode: ExitCode.HOSTSINGLE }));
            yield put(setPreChatInformation(instanceId, PreChatInformationType.PRIVATE_CHAT_CAN_SPY));
        } else {
            // display the user prompt offering the possibility to spy
            yield put(openSpyPrivateChatPrompt(instanceId));
        }

        return true;
    }

    return false;
};

const displayPrechatInformationDialogSaga = function* (instanceId: string, exitCode: number, exitMessage?: string) {
    yield put(setExitEvent(instanceId, { exitCode, exitMessage }));

	const dialogType: PreChatInformationType | undefined = getPrechatByErrorCode(exitCode);
	if (dialogType) {
		yield put(setPreChatInformation(instanceId, dialogType));
	}
};

export default precheckSaga;
