import * as React from 'react';

import IStore from '../../../redux/store';
import selectors from '../../../redux/selectors';
import MessageSectionPopups from './MessageSectionPopups';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    isResponsive: boolean;
    isDropDownOpen: boolean;
    isFullscreen: boolean;
    hasActionSectionInDropDown: boolean;
    isAnySectionOpen: boolean;
    areAnyPromptsOpenInResponsiveMode: boolean;
	areAllPopUpsClosed: boolean;
	isGiftSectionOpen: boolean;
	isEmoticonSectionOpen: boolean;
	shouldUseDropdownAnimation: boolean;
}

type IProps = IStoreProps & IInstanceProps;

const MessageSectionPopupsContainer = (props: IProps) => (
	<MessageSectionPopups
		isDropDownOpen={props.isDropDownOpen}
		isFullscreen={props.isFullscreen}
		isResponsive={props.isResponsive}
		hasActionSectionInDropDown={props.hasActionSectionInDropDown}
		isAnySectionOpen={props.isAnySectionOpen}
		areAnyPromptsOpenInResponsiveMode={props.areAnyPromptsOpenInResponsiveMode}
		areAllPopUpsClosed={props.areAllPopUpsClosed}
		isGiftSectionOpen={props.isGiftSectionOpen}
		isEmoticonSectionOpen={props.isEmoticonSectionOpen}
		shouldUseDropdownAnimation={props.shouldUseDropdownAnimation}
	/>
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isResponsive: selectors.isResponsive(store, instanceId),
    isDropDownOpen: selectors.isDropDownMenuOpen(store, instanceId),
    isFullscreen: selectors.isFullScreen(store, instanceId),
    hasActionSectionInDropDown: selectors.hasActionSectionInDropDown(store, instanceId),
    isAnySectionOpen: selectors.isAnySectionOpen(store, instanceId),
    areAnyPromptsOpenInResponsiveMode: selectors.areAnyPromptsOpenInResponsiveMode(store, instanceId),
	areAllPopUpsClosed: selectors.areAllPopUpsClosed(store, instanceId),
	isGiftSectionOpen: selectors.isGiftSectionOpen(store, instanceId),
	isEmoticonSectionOpen: selectors.isEmoticonSectionOpen(store, instanceId),
	shouldUseDropdownAnimation: selectors.shouldUseDropdownAnimation(store, instanceId)
});

export default withRedux(MessageSectionPopupsContainer, mapStateToProps);
