import * as React from 'react';
import { ContactNoteIcon } from './style';

interface IProps {
    title: string;
    hasContactNoteText: boolean;

    onClick: () => void;
}

const ContactNote = (props: IProps) => (
    <ContactNoteIcon
		title={props.title}
		className={`-icon-note-${props.hasContactNoteText ? "full" : "line" }`} 
        onClick={props.onClick}
    />
);

export default ContactNote;
