import { filter, find } from 'lodash';

import { ExitCode } from "../ChatService/types";
import { VideoChatType } from "../../redux/store";
import {
	IAgeVerification,
	IBecomeVIP,
	IClickExit,
	IClickProfile,
	IExitPopupAction,
	IOnFailedChatConfig,
	IOnFailedWebToken,
	IOnModelStatusChange,
	IOnSupportedVideoChatTypes,
	IRecharge,
	IVideoChatAction,
	IPreferenceStore,
	IClickImage
} from "./types";
import { defaultPreferenceStore } from "../../utils/utils";

/**
 * Service handling the configured hooks from the parent platform
 */
class AppService {
    private static services: AppService[] = [];

    constructor(instanceId: string) {
        this.instanceId = instanceId;
		this.preferenceStore = defaultPreferenceStore;
    }

    private instanceId: string;

    onClickProfile: IClickProfile;
	onClickImage: IClickImage;
    onBecomeVIP: IBecomeVIP;
	preferenceStore: IPreferenceStore;

    static getService(instanceId: string) {
        let appService = find(AppService.services, service => service.getInstanceId() === instanceId);

        if (!appService) {
            appService = new AppService(instanceId);
            AppService.services.push(appService);
        }

        return appService;
    }

    static destroyService(instanceId: string) {
        const appService = find(AppService.services, service => service.getInstanceId() === instanceId);

        if (appService) {
            AppService.services =
                filter(AppService.services, service => service.getInstanceId() !== instanceId);
        }
    }

    getInstanceId = () => this.instanceId;

    onExit: IClickExit = (code?: ExitCode, message?: string) => {
        /** Do nothing */
    };

    onRecharge: IRecharge = () => {
        /** Do nothing */
    };

    onVideoChatAction: IVideoChatAction = (isVideoChatOpen: boolean, chatId?: string) => {
        /** Do nothing */
    };

    onExitPopupAction: IExitPopupAction = (isExitPopupOpen: boolean) => {
        /** Do nothing */
    };

    onAgeVerification: IAgeVerification = () => {
        /** Do nothing */
    };

    onModelStatusChange: IOnModelStatusChange = (isOnline: boolean) => {
        /** Do nothing */
    };

    onSupportedVideoChatTypes: IOnSupportedVideoChatTypes = (videoChatTypes: VideoChatType[]) => {
        /** Do Nothing */
    };

    onFailedChatConfig: IOnFailedChatConfig = (error: any) => {
        /** Do Nothing */
    };

    onFailedWebToken: IOnFailedWebToken = (error?: any) => {
        /** Do Nothing */
    };
}

export default AppService;
