import * as React from 'react';
import { find, isEmpty, map, some } from "lodash";

import ToySection from "./ToySection";
import { ActionDispatcher } from "../../../redux";
import selectors from "../../../redux/selectors";
import Store, { IToyOption, ITranslation } from "../../../redux/store";
import { requestSendVibrationMessage } from "../../../sagas/websockets/message";
import { closeToyOptionTooltip, closeToySection, setHasTooltipOpen, ISetHasTooltipPayload } from "../../../redux/modules/messenger";
import { IInstanceProps } from "../../hoc/withInstanceInfo";
import withRedux from "../../hoc/withRedux";
import { IToyOptionStructure } from "./types";

interface IComponentProps {
    isOpen: boolean;
}

interface IStoreProps {
	isFullScreen: boolean;
	shouldUseDropdownAnimation: boolean;
    isResponsive?: boolean;
    isMobileLandscape?: boolean;
    toyOptions: IToyOption[];
    translation: ITranslation;

    closeToySection: ActionDispatcher;
    closeToyOptionTooltip: ActionDispatcher;
    requestSendVibrationMessage: ActionDispatcher<IToyOption>;
    setHasTooltipOpen: ActionDispatcher<ISetHasTooltipPayload>;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

interface IState {
	elemRef?: any;
}

class ToySectionContainer extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {};
    }

    render() {
        const { toyDescription } = this.props.translation.chat.toyControl;

        const options: IToyOptionStructure[] = map(this.props.toyOptions, (option: IToyOption) => ({
            ...option,
            isLocked: !isEmpty(option.unlockedBy)
        }));

        return (
            <ToySection
                text={toyDescription}
                options={options}
                isResponsive={this.props.isResponsive}
                isOpen={this.props.isOpen}
                isMobileLandscape={this.props.isMobileLandscape}
                isFullScreen={this.props.isFullScreen}
                shouldUseDropdownAnimation={this.props.shouldUseDropdownAnimation}
				toySectionRef={this.state.elemRef}
                closeToyOptionTooltip={this.closeToyOptionTooltip}

                onOptionClick={this.onOptionClick}
				onGetReference={this.onRefCallback}
            />
        );
    }

    private onRefCallback = (ref: any) => {
    	this.setState({ elemRef: ref });
	};

    private closeToyOptionTooltip = () => {
        this.props.closeToyOptionTooltip(this.props.instanceId)
    };

    private onOptionClick = (id: number) => (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        const { instanceId } = this.props;

        const activeToyOption = find(this.props.toyOptions, option => option.id === id);
        const isAnotherOptionActive = some(this.props.toyOptions, option => option.isPlaying);
        const isOptionLocked = activeToyOption && !isEmpty(activeToyOption.unlockedBy);

        e.stopPropagation();

        // Defensive programming
        if (!activeToyOption) {
            return;
        }

        // No behavior dispatched when clicking on an option while another one is active
        if (isAnotherOptionActive) {
            return;
        }

        if (isOptionLocked) {
            if (!activeToyOption.hasTooltipOpen) {
                this.props.setHasTooltipOpen(instanceId, {
                    id: activeToyOption.id,
                    hasTooltip: true
                })
            }

            return;
        }

        this.props.requestSendVibrationMessage(instanceId, activeToyOption);
        this.props.closeToySection(instanceId);
    };
}

const mapStateToProps = (store: Store, { instanceId }: IInstanceProps) => ({
    isResponsive: selectors.isResponsive(store, instanceId),
    isMobileLandscape: selectors.isMobileLandscape(store, instanceId),
	isFullScreen: selectors.isFullScreen(store, instanceId),
    toyOptions: selectors.getToyOptions(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
	shouldUseDropdownAnimation: selectors.shouldUseDropdownAnimation(store, instanceId),
});

const mapDispatchToProps = {
    closeToySection,
    requestSendVibrationMessage,
    setHasTooltipOpen,
    closeToyOptionTooltip
};

export default withRedux<IComponentProps>(ToySectionContainer, mapStateToProps, mapDispatchToProps);
