import styled from "styled-components";

interface IImageProps {
    width?: number;
    height?: number;
    isHidden?: boolean;
}

export const ImageWrapper = styled.div`
    width: 100%;
    cursor: pointer;
    display: flex;
    justify-content: center;
`;
export const ImageLoaderWrapper = styled.div`
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
`;

export const Image = styled.img<IImageProps>`
    ${({ width }) => width ? `width: ${width}px;` : `width: 100%;`}
    ${({ height }) => height && `height: ${height}px;`}
    
    ${({ isHidden }) => isHidden ? 'opacity: 0;' : ''}
    
    object-fit: cover;
`;
