import styled, { css, keyframes} from "styled-components";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface IImage {
    image: string;
    isActive?: boolean;
    canPlayOption?: boolean;
}

interface IWrapper {
    isClickable?: boolean;
}

interface IText {
    isResponsive: boolean;
	hasTooltipOpen?: boolean;
}

const activePulsation = keyframes`
    0% { opacity: .1; }
    50% { opacity: 1; }
    100% { opacity: .1; }
`;

const animation = css`
	animation: ${activePulsation} 1s infinite ease-in-out;
`;

export const Wrapper = withCustomClasses<IWrapper>(styled.div<IWrapper>`
    ${({ isClickable }: IWrapper) => isClickable && `cursor: pointer;`}

    font-size: 12px;
    text-align: center;
    position: relative;
`, [DomCustomClass.ToyOption]);

export const ImageWrapper = styled.div`
    width: 64px;
    height: 64px;
    margin: auto;
`;

export const Image = styled.div<IImage>`
    ${({ image }: IImage) => `background: url(${image}) no-repeat center center;`}
    ${({ canPlayOption }: IImage) => !canPlayOption && `opacity: .45;`}
    ${({ isActive }: IImage) => isActive && animation}

    width: 100%;
    height: 100%;
    transition: opacity .15s;
    background-size: auto 100%;
`;

export const Text = styled.div<IText>`
    color: ${props => props.theme.colors.toySection.optionText};
    font-size: ${props => props.isResponsive ? '12' : '14'}px;
    ${props => props.hasTooltipOpen && `font-weight: ${props.theme.typography.weight.bold};`}

    min-width: 40px;
    margin-top: 10px;
    text-align: center;
    position: relative;
`;

export const Lock = styled.div`
    font-weight: ${props => props.theme.typography.weight.medium};
    color: ${props => props.theme.colors.toySection.lock};

    position: relative;
    float: right;

    font-size: 25px;
    z-index: 2;
`;

export const PlayingStatusWrapper = styled.div`
    position: absolute;
    z-index: 2;
`;

export const PlayingStatus = styled.div`
    background-color: #000;
    color: #fff;

    margin: 0 auto;
    border-radius: 2px;
    text-transform: uppercase;
    font-size: 11px;
    text-align: center;
    margin: -10px 0 0 5px;
    padding: 1px 9px;
`;
