import { all } from 'redux-saga/effects';
import chatEventsSagas from './chatEvents';
import closeAppSagas from './close';
import errorsSagas from './errors';
import playerEventsSagas from './playerEvents';
import rechargeSagas from './recharge';
import timeLimitsSagas from './timeLimits';
import uiSagas from './ui';
import startSagas from './start';
import userPromptSagas from './userPrompt';
import videoChatSagas from './videochat';
import cam2CamSagas from './videochat/cam2Cam';
import preferencesSagas from './preferences';


/**
 * Combines all the listening sagas and
 * it represents the entire middleware.
 */
export default function* rootSaga() {
    yield all([
        uiSagas(),
        startSagas(),
        userPromptSagas(),
        timeLimitsSagas(),
        rechargeSagas(),
        videoChatSagas(),
        cam2CamSagas(),
        errorsSagas(),
        chatEventsSagas(),
        playerEventsSagas(),
        closeAppSagas(),
		preferencesSagas()
    ]);
}
