import styled from 'styled-components';
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface IGiftTabWrapperProps {
    isActive: boolean;
}

export const GiftTabWrapper = withCustomClasses<IGiftTabWrapperProps>(styled.i<IGiftTabWrapperProps>`
    display: inline-block;
    font-size: 23px;
    color: #888;
    padding: 10px 16px 0px 16px;
    cursor: pointer;

    ${props => props.isActive && `
        border-bottom: 1px solid #FF4D3C;
        color: #FF4D3C;
        &:after {
            content: '';
            display: block;
            width: 0px;
            height: 0px;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;
            border-top: 5px solid #f00;
            margin: 0 auto;
            position: relative;
            bottom: -5px;
        }    
    `}
`, [DomCustomClass.GiftTab]);
