import { isEmpty, map, sumBy } from 'lodash';
import * as React from 'react';
import { ICurrency, IGift } from '../../../redux/store';
import utils from '../../../utils';
import MessageGift from '../MessageGift';
import { MessengerBox } from '../MessageSection/style';
import { DefaultEditorWrapper, Editor, GiftsWrapper, RemoveGiftWrapper, TotalPriceWrapper } from './style';

interface IProps {
    text: string;
    currency: ICurrency;
    gifts: IGift[];
    insertedGifts: IGift[];
    placeholder: string;
    isInteractive: boolean;
	shouldIncreaseMessageFontSize: boolean;

    onRemoveGiftClick: () => void;
    onChange: (event: React.ChangeEvent<HTMLTextAreaElement>) => void;
    onKeyDown: (event: React.KeyboardEvent<HTMLTextAreaElement>) => void;
}

const DefaultEditor = (props: IProps) => {
    const insertedGiftsPrice = sumBy(props.insertedGifts,
        (gift: IGift) => gift.currentQuantity && gift.currentQuantity > 0
            ? 0
            : gift.price
    );

    return (
        <MessengerBox
            hasEditor={false}
            isInteractive={props.isInteractive}
            hasText={!isEmpty(props.text)}
            shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}
        >
            <DefaultEditorWrapper>
                {!isEmpty(props.insertedGifts) &&
                <>
                    <GiftsWrapper>
                        {map(props.insertedGifts, (gift: IGift, index) =>
                            <MessageGift
                                key={`${gift.id}${index}`}
                                icon={utils.getGiftEncoding(gift)}
                                gifts={props.gifts}
                            />
                        )}

                        <TotalPriceWrapper>
                            ({insertedGiftsPrice}{props.currency.symbol})
                        </TotalPriceWrapper>
                    </GiftsWrapper>

                    <RemoveGiftWrapper
                        onClick={props.onRemoveGiftClick}
                        title={"Remove gift"}
                    />
                </>
                }

                <Editor
                    value={props.text}
                    onChange={props.onChange}
                    onKeyDown={props.onKeyDown}
                    placeholder={props.placeholder}
                    disabled={!props.isInteractive}
                />
            </DefaultEditorWrapper>
        </MessengerBox>
    );
};


export default DefaultEditor;
