import * as React from 'react';

import utils from '../../../utils';
import config from '../../../static/config';
import { UserPromptWrapper } from './style';
import CloseIcon from "../../common/CloseIcon";
import UserPromptTitle from "./helpers/UserPromptTitle";
import WindowBoxContainer from "../../common/WindowBox";
import UserPromptContent from "./helpers/UserPromptContent";
import TransitionGroup from "../../common/TransitionGroup";
import { IAnswerUserPromptPayload } from '../../../redux/modules/messenger';
import { AnswerType, ICurrency, IQuestion, QuestionType, ITranslation } from '../../../redux/store';

export interface IProps {
    prompts: IQuestion[];
    currency: ICurrency;
    translation: ITranslation;
    className?: string;
    isAnyUserPromptOpen: boolean;
    isResponsive?: boolean;
    isMobileLandscape?: boolean;
    isGiftSectionOpen?: boolean;
    isTipPromptOpen?: boolean;

    onAnswer: (payload: IAnswerUserPromptPayload) => any;
    closeUserPrompt: (questionType: QuestionType) => () => any;
}

const getTitle = (props: IProps, question: IQuestion) => (
    <UserPromptTitle
        question={question}
        currency={props.currency}
        translation={props.translation}
        onAnswer={(answer: AnswerType) => props.onAnswer({ question, answer })}
    />
);

/**
 * Stateless component used to deal with various types of user prompt interactions
 * @param props
 * @constructor
 */
const UserPrompt = (props: IProps) => (
    <UserPromptWrapper
        isResponsive={props.isResponsive}
        isMobileLandscape={props.isMobileLandscape}
        isGiftSectionOpen={props.isGiftSectionOpen}
        isTipPromptOpen={props.isTipPromptOpen}
        isAnyUserPromptOpen={props.isAnyUserPromptOpen}
    >
        <TransitionGroup<IQuestion>
            timeout={{
                enter: utils.getTransitionTimeInMilliseconds(config.popupsTransitionTime),
                exit: utils.getTransitionTimeInMilliseconds(config.popupsTransitionTime)
            }}
            classNames={{
                enter: 'enter',
                enterActive: 'enterActive',
                enterDone: 'enterDone',
                exit: 'exit',
                exitActive: 'exitActive',
                exitDone: 'exitDone'
            }}
            transitionedItems={props.prompts}
            itemKey={(item: IQuestion) => item.type}
        >
            {(question) => (
                <WindowBoxContainer
                    isOpen={true}
                    isWindowForUserPrompt={true}
                    key={question.type}
                    customCloseIcon={<CloseIcon />}
                    title={getTitle(props, question)}
                    handleClose={props.closeUserPrompt(question.type)}
                >
                        <UserPromptContent
                            currency={props.currency}
                            question={question}
                            translation={props.translation}
                            onAnswer={(answer: AnswerType) => props.onAnswer({ question, answer })}
                        />
                </WindowBoxContainer>
            )}
        </TransitionGroup>
    </UserPromptWrapper>
);

export default UserPrompt;
