import * as React from 'react';
import * as moment from "moment";
import 'draft-js/dist/Draft.css';
import { ThemeProvider } from 'styled-components';
import { SupportedLanguage } from 'vxcontrol-client-lib';
import { withResizeDetector } from 'react-resize-detector';
import { has, isEmpty, isEqual, map, merge } from 'lodash';
import { isAndroid, isIE, isMobile } from 'react-device-detect';

import '../content/fonts/vx-icons/vx-icon.css';
import '../content/style.css';
import { setFeatures } from "../redux/modules/features";
import { setOptions } from "../redux/modules/options";
import { setIsOnline, setPartner } from '../redux/modules/model';
import { getInitialInstanceData } from '../redux/initialStore';
import { setPlatformProps } from '../redux/modules/platformProps';
import { setChat, setWebtoken } from '../redux/modules/session';
import { setLanguage, setPreference, setUserKey } from '../redux/modules/user';
import { resetInitialVideoChatType, setInitialVideoChatType } from "../redux/modules/videochat";
import selectors from '../redux/selectors';
import utils from '../utils';
import constants from '../static/constants';
import config from "../static/config";
import { ActionDispatcher } from '../redux';
import { VXMessengerWrapper } from './style';
import IStore, {
	IChat,
	IConnection,
	IConnectionWithChat,
	IConnectionWithToken,
	IFeatures,
	IGroupChatColor,
	IHooks,
	IOptions,
	IPartner,
	IPlatformProps,
	VideoChatType
} from '../redux/store';
import VersionBoxContainer from '../features/specific/VersionBox';
import { IInstanceProps } from '../features/hoc/withInstanceInfo';
import withRedux from '../features/hoc/withRedux';
import ModalInformationDialogContainer from '../features/specific/ModalInformationDialog';
import ImageUploaderContainer from '../features/specific/ImageUploader';
import AudioAnalyserContainer from '../features/specific/AudioAnalyser';
import ViewProvider from '../views/ViewProvider';
import FullScreenLightBoxContainer from '../features/common/FullScreenLightBox';
import AppService from '../services/AppService';
import defaultTheme, { ITheme } from '../themes';
import { getWindowDimensions } from '../utils/utils';
import {
	openMessenger,
	openVersion,
	setGroupChatColors,
	setHasEditor,
	setShouldResendFailedMessages
} from '../redux/modules/messenger';
import { PreferenceName } from "../services/AppService/types";
import { IPreferenceActionPayload } from "../redux/interfaces";

interface IResizeProps {
	width: number;
	height: number;
}

interface IDispatchProps {
	openMessenger: ActionDispatcher;
	setWebtoken: ActionDispatcher<string>;
	setLanguage: ActionDispatcher<SupportedLanguage>;

	setPlatformProps: ActionDispatcher<IPlatformProps>;
	setFeatures: ActionDispatcher<IFeatures>;
	setOptions: ActionDispatcher<IOptions>;
	setUserKey: ActionDispatcher<string>;
	setShowCensoredContent: ActionDispatcher<boolean>;
	setPartner: ActionDispatcher<IPartner>;
	setChat: ActionDispatcher<IChat>;
	setIsOnline: ActionDispatcher<boolean>;
	setInitialVideoChatType: ActionDispatcher<VideoChatType | undefined>
	setHasEditor: ActionDispatcher<boolean>;
	resetInitialVideoChatType: ActionDispatcher;
	setIsForContentPartners: ActionDispatcher<boolean>;
	openVersion: ActionDispatcher;
	setShouldResendFailedMessages: ActionDispatcher<boolean>;
	setGroupChatColors: ActionDispatcher<IGroupChatColor[]>
	setPreference: ActionDispatcher<IPreferenceActionPayload>
}

type IStoreProps = ReturnType<typeof mapStateToProps>;
type IProps = IPlatformProps
	& IInstanceProps
	& IResizeProps
	& IStoreProps
	& IDispatchProps;

class App extends React.Component<IProps> {
	constructor(props: IProps) {
		super(props);

		this.clickCounter = utils.clickCounter(constants.intervalBetweenClicks);
		moment.locale(props.language || 'en');
	}

	clickCounter: any;

	componentDidMount() {
		const { instanceId } = this.props;
		this.props.openMessenger(instanceId);

		this.setHooks();
		this.setPreferenceStore();
		this.setReduxStoreFromProps();
		this.setPreferences();

		// IE-SPECIFIC: switching to the default editor on any IE browser.
		// Set if we have draft.js functionalities enabled
		const hasEditor = !isIE && !isAndroid;
		this.props.setHasEditor(instanceId, hasEditor);

		// Set platform props for sending them through the vchat-core
		this.props.setPlatformProps(instanceId, this.props);
	}

	componentDidUpdate() {
		this.setReduxStoreFromProps();
	}

	render() {
		const theme: ITheme = merge({}, defaultTheme, this.props.theme);

		const { height: windowHeight, width: windowWidth } = getWindowDimensions();

		const isMessengerInsideContainer = windowHeight !== this.props.height
			|| windowWidth !== this.props.width;

		/**
		 * When messenger is opened on a mobile/tablet device inside a separate container,
		 * we should use only the sizes set by the container
		 */
		const shouldUseFullscreenSizes = this.props.isFullscreen
			&& !(isMessengerInsideContainer && isMobile);

		const containerHeight = shouldUseFullscreenSizes ? windowHeight : this.props.height;
		const containerWidth = shouldUseFullscreenSizes ? windowWidth : this.props.width;

		const wrapperProps = {
			onClick: this.onVXMessengerClick,
			[config.messengerInstanceIdAttribute]: this.props.instanceId
		};

		// IE-SPECIFIC: the audio messages feature will be disabled on any IE browser.
		const hasAudioMessages = !isIE;

		return (
			<VXMessengerWrapper {...wrapperProps}>
				<ThemeProvider theme={theme}>
					<>
						<VersionBoxContainer/>
						<ModalInformationDialogContainer/>
						<ImageUploaderContainer/>

						{hasAudioMessages && <AudioAnalyserContainer/>}

						{!this.props.shouldRenderExitingInformationDialog &&
						<ViewProvider
							containerHeight={containerHeight}
							containerWidth={containerWidth}
						/>
						}

						<FullScreenLightBoxContainer/>
					</>
				</ThemeProvider>
			</VXMessengerWrapper>
		);
	};

	private onVXMessengerClick = () => {
		this.clickCounter.onClick();

		if (isMobile && this.clickCounter.getClickCount() === constants.clickCountLimit) {
			this.props.openVersion(this.props.instanceId);
		}

	};

	private setReduxStoreFromProps = () => {
		const {
			connection, features, language, initialVideoChatType, theme, storedFeatures, options, storedOptions
		} = this.props;

		const platformLanguage = utils.getValueByLanguage(language);
		const shouldSetLanguage = platformLanguage !== this.props.storedLanguage;
		const { instanceId } = this.props;

		const usernameColorSet = theme?.colors?.groupChat?.usernameColorSet;
		const shouldSetColorSet = !isEqual(usernameColorSet,
			defaultTheme.colors.groupChat.usernameColorSet
		);

		if (!isEmpty(usernameColorSet) && shouldSetColorSet) {
			this.props.setGroupChatColors(
				instanceId, map(usernameColorSet, (color: string) => ({
					color,
					users: []
				}))
			);
		}

		if (this.isChatBasedConnection(connection)) {
			const { clientId, server } = connection as IConnectionWithChat;

			this.setChat({ clientId, server, externallyProvided: true });

			if (shouldSetLanguage) {
				this.setLanguage(language);
			}

			// model is considered to be online if started with a chat based connection
			if (!this.props.isModelOnline) {
				this.props.setIsOnline(instanceId, true);
			}
		} else {
			const { partner, userKey, webToken } = connection as IConnectionWithToken;

			const shouldSetUserKey = userKey !== this.props.storedUserKey;
			let shouldSetWebToken = webToken !== this.props.storedWebToken;
			let shouldSetPartner = !isEqual(partner, this.props.storedPartner);

			if (shouldSetUserKey) {
				this.setUserKey(userKey);
				shouldSetWebToken = true;
			}

			if (shouldSetLanguage) {
				this.setLanguage(language);
				shouldSetWebToken = true;
			}

			// webtoken and login is depending on user key and language
			if (shouldSetWebToken) {
				this.setWebToken(webToken);

				if (webToken && !shouldSetPartner) {
					this.props.setShouldResendFailedMessages(instanceId, true);
				}

				shouldSetPartner = true;
			}

			if (shouldSetPartner) {
				this.setPartner(partner);
			}
		}

		this.setInitialVideoChatType(initialVideoChatType);

		// Set features, we compare to features + storedFeatures because some feature may be not set;
		const featuresFromProps: IFeatures = { ...getInitialInstanceData().features, ...features };
		const updatedFeatures = merge({}, storedFeatures, featuresFromProps);
		if (featuresFromProps && !isEqual(updatedFeatures, storedFeatures)) {
			this.props.setFeatures(instanceId, updatedFeatures);
		}

		// Set options, we compare to options + storedOptions because some of them may be not set;
		const updatedOptions = merge({}, storedOptions, options);
		if (options && !isEqual(updatedOptions, storedOptions)) {
			this.props.setOptions(instanceId, updatedOptions);
		}
	};

	private setHooks = () => {
		if (this.props.hooks) {
			const {
				onExit,
				onClickProfile,
				onRecharge,
				onVideoChatAction,
				onExitPopupAction,
				onAgeVerification,
				onModelStatusChange,
				onSupportedVideoChatTypes,
				onFailedChatConfig,
				onFailedWebToken,
				onBecomeVIP,
				onClickImage
			} = this.props.hooks as IHooks;

			const appService: AppService = AppService.getService(this.props.instanceId);

			if (onClickImage) {
				appService.onClickImage = onClickImage;
			}
			if (onClickProfile) {
				appService.onClickProfile = onClickProfile;
			}
			if (onExit) {
				appService.onExit = onExit;
			}
			if (onRecharge) {
				appService.onRecharge = onRecharge;
			}
			if (onVideoChatAction) {
				appService.onVideoChatAction = onVideoChatAction;
			}
			if (onExitPopupAction) {
				appService.onExitPopupAction = onExitPopupAction;
			}
			if (onAgeVerification) {
				appService.onAgeVerification = onAgeVerification;
			}
			if (onModelStatusChange) {
				appService.onModelStatusChange = onModelStatusChange;
			}
			if (onSupportedVideoChatTypes) {
				appService.onSupportedVideoChatTypes = onSupportedVideoChatTypes;
			}
			if (onFailedChatConfig) {
				appService.onFailedChatConfig = onFailedChatConfig;
			}
			if (onFailedWebToken) {
				appService.onFailedWebToken = onFailedWebToken;
			}
			if (onBecomeVIP) {
				appService.onBecomeVIP = onBecomeVIP;
			}
		}
	};

	private setPreferenceStore = () => {
		if (this.props.preferenceStore) {
			const appService: AppService = AppService.getService(this.props.instanceId);
			appService.preferenceStore = this.props.preferenceStore;
		}
	}

	private setWebToken = (webToken: string) => {
		this.props.setWebtoken(this.props.instanceId, webToken);
	};

	private setUserKey = (userKey?: string) => {
		this.props.setUserKey(this.props.instanceId, userKey || "");
	};

	private setPartner = (partner: IPartner) => {
		this.props.setPartner(this.props.instanceId, partner);
	};

	private setChat = (chat: IChat) => {
		const isNewChat = !isEqual(chat, this.props.storedChat);
		if (isNewChat) {
			this.props.setChat(this.props.instanceId, chat);
		}
	};

	private setLanguage = (language: string) => {
		this.props.setLanguage(
			this.props.instanceId,
			utils.getValueByLanguage(language)
		);
		moment.locale(language);
	};

	private setInitialVideoChatType = (type?: VideoChatType) => {
		const { instanceId } = this.props;

		if (type !== this.props.storedInitialVideoChatType) {
			if (type) {
				this.props.setInitialVideoChatType(instanceId, type);
			} else {
				this.props.resetInitialVideoChatType(instanceId);
			}
		}
	};

	private isChatBasedConnection = (connection: IConnection): boolean =>
		has(connection, "clientId") && has(connection, "server");

	private setPreferences = () => {
		const appService = AppService.getService(this.props.instanceId);

		Object.keys(PreferenceName).forEach((preferenceName: PreferenceName) => {
			const preferenceValue = appService.preferenceStore.getPreference(preferenceName);

			if (!preferenceValue) {
				return;
			}

			this.props.setPreference(this.props.instanceId, {
				preferenceName,
				value: preferenceValue
			});
		});
	}

}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	isOpen: selectors.isOpen(store, instanceId),
	isFullscreen: selectors.isFullScreen(store, instanceId),
	isVideoChatOpen: selectors.isVideoChatOpen(store, instanceId),
	shouldRenderExitingInformationDialog: selectors.shouldRenderExitingInformationDialog(store, instanceId),
	isModelOnline: selectors.isModelOnline(store, instanceId),

	storedWebToken: selectors.getWebtoken(store, instanceId),
	storedUserKey: selectors.getUserKey(store, instanceId),
	storedPartner: selectors.getPartner(store, instanceId),

	storedChat: selectors.getExternallyProvidedChat(store, instanceId),

	storedLanguage: selectors.getLanguage(store, instanceId),
	storedFeatures: selectors.getFeatures(store, instanceId),
	storedOptions: selectors.getOptions(store, instanceId),
	storedPreloader: selectors.getPreloader(store, instanceId),
	storedAnalyticsKey: selectors.getAnalyticsKey(store, instanceId),
	storedUpdateBrowserLink: selectors.getUpdateBrowserLink(store, instanceId),
	storedInitialVideoChatType: selectors.getInitialVideoChatType(store, instanceId)
});

const mapDispatchToProps = {
	setPlatformProps,
	setFeatures,
	setOptions,
	setWebtoken,
	setUserKey,
	setPartner,
	setChat,
	setIsOnline,
	setLanguage,
	setInitialVideoChatType,
	setHasEditor,
	resetInitialVideoChatType,
	openMessenger,
	openVersion,
	setShouldResendFailedMessages,
	setGroupChatColors,
	setPreference
};

export default withRedux(App, mapStateToProps, mapDispatchToProps, { withResizeDetector });
