import * as React from 'react';
import Tooltip from '../Tooltip';

interface IProps {
    hoverText?: string;
    closeButtonWrapper: any;
    iconClassName: string;
    className?: string;

    onClick: () => void;
}

const CloseButton = (props: IProps) => {
    const CloseWrapper = props.closeButtonWrapper;
    const iconComponent = <i className={props.iconClassName} />;

    const tooltipComponent = (
        <Tooltip id={"close-chat"} text={props.hoverText}>
            {iconComponent}
        </Tooltip>
    );

    return (
        <CloseWrapper onClick={props.onClick}>
            {props.hoverText ? tooltipComponent : iconComponent}
        </CloseWrapper>
    );
};

export default CloseButton;
