import * as React from 'react';
import { IGift } from '../../../redux/store';
import utils from '../../../utils';
import { MessageGiftWrapper } from './style';

interface IProps {
    icon: string;
    gifts: IGift[];
    hasEnlargedContent?: boolean;
}

const MessageGift = (props: IProps) => {
    const foundGift = props.gifts.find(
        (gift: IGift) => utils.getGiftEncoding(gift) === props.icon
    );

    return (
        <MessageGiftWrapper
            url={foundGift && foundGift.imageUrl}
            data-entity={foundGift &&  utils.getGiftEncoding(foundGift)}
            hasEnlargedContent={props.hasEnlargedContent}
        >
            <span>{utils.CHAR_BEHIND_GIFTS}</span>
        </MessageGiftWrapper>
    );
};

export default MessageGift;