import { Action } from "redux-actions";
import {put, takeEvery} from "redux-saga/effects";

import { createAction, createActionType } from "../../redux/utils";
import { IPayload } from "../../redux/interfaces";
import ApiService from "../../services/ApiService";
import {resetStore} from "../../redux/modules/app";
import PlayerService from "../../services/PlayerService";
import ChatService from "../../services/ChatService";
import { ActionIds } from "../../redux/interfaces";
import AppService from "../../services/AppService";

const createCloseActionType = createActionType(ActionIds.CLOSE, { isSagaAction: true });

// Actions
const CLOSE_APP = createCloseActionType("CLOSE_APP");

// Action Creators
export const closeApp = createAction(CLOSE_APP);

// Saga
const closeAppSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
	// this should always happen before destroying the AppService instance,
	// otherwise a new VXControl connection will start.
    yield put(resetStore(instanceId));

    // release vchat-core and vchat-player resources
    ChatService.destroyService(instanceId);
    PlayerService.destroyService(instanceId);

    // Close the API websocket connection (vxcontrol-lib)
    ApiService.destroy(instanceId);
    AppService.destroyService(instanceId);
};

const watchSagas = function*() {
    yield takeEvery(CLOSE_APP, closeAppSaga);
};

export default watchSagas;
