import * as React from 'react';
import {ITranslation} from '../../../redux/store';
import VideoChatBaseButton, {IVideoChatBaseButtonProps} from '../../common/VideoChatBaseButton';
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface IProps {
    translation: ITranslation;
    onClick: () => any;
}

const ReloadButton = withCustomClasses<IVideoChatBaseButtonProps>(VideoChatBaseButton, [DomCustomClass.ReloadButton]);

const VideoChatReload = (props: IProps) => (
        <ReloadButton
            onClick={props.onClick}
            text={props.translation.common.reload}
        >
            <i className="-icon-reload" />
        </ReloadButton>
);

export default VideoChatReload;