import * as React from 'react';
import { ITranslation } from '../../../redux/store';
import { GroupContentWrapper, GroupTextWrapper, GroupWrapper, TextArrow } from './style';
import { getChatGroupTime, getDayGroupDate } from './utils';

export interface IGroupProps {
	children: any;
	datetime: string;
	language: string;
	shouldIncreaseMessageFontSize: boolean;
	isOpen?: boolean;
	isDayGroup?: boolean;
	showArrow: boolean;
	informationText?: string;
	translation: ITranslation;
	className?: string;

	onClick: () => void;
}

const Group = (props: IGroupProps) => {
	const textArrowClassName = props.isOpen
		? "-icon-single-arrow-up-line"
		: "-icon-single-arrow-down-line";

	return (
		<GroupWrapper
			className={props.className}
			showArrow={props.showArrow}>
			<GroupTextWrapper shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}>
				{props.showArrow && (
					<TextArrow
						className={textArrowClassName}
						onClick={props.onClick}
					/>
				)}

				{props.informationText
					? props.informationText
					: (
						<>
							{props.isDayGroup
								? getDayGroupDate(props.datetime, props.language)
								: `${props.translation.chat.liveChat} ${getChatGroupTime(props.datetime)}`
							}
						</>
					)}
			</GroupTextWrapper>

			<GroupContentWrapper isOpen={props.isOpen}>
				{props.children}
				{!props.isDayGroup && (
					<GroupWrapper>
						<GroupTextWrapper shouldIncreaseMessageFontSize={props.shouldIncreaseMessageFontSize}>
							{props.translation.chat.popup.ended}
						</GroupTextWrapper>
					</GroupWrapper>
				)}
			</GroupContentWrapper>

		</GroupWrapper>
	);
};

export default Group;
