import * as React from 'react';
import { reduce, values } from 'lodash';
import { withResizeDetector } from 'react-resize-detector';

import selectors from '../../../redux/selectors';
import IStore from '../../../redux/store';
import ActionSection from './ActionSection';
import { viewSizes } from '../../../static/config/config';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IResizeDetector {
    height: number;
    width: number;
}

interface IState {
    shouldDisplayIcon: boolean;
}

interface IComponentProps {
	isForInformationDialog?: boolean;
}

type IStoreProps = ReturnType<typeof mapStateToProps>;
type IProps = IStoreProps & IInstanceProps & IResizeDetector & IComponentProps;

class ActionSectionContainer extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            shouldDisplayIcon: true
        }
    }

    componentDidUpdate(prevProps: IProps) {
        if (prevProps.width === this.props.width) { return; }

        const buttonCount = reduce(
            values(this.props.buttonsVisibility),
            (count, isVisibile) => isVisibile ? count + 1 : count,
            0
        );

        const buttonWidth = this.props.width / buttonCount;

        this.setState({
            shouldDisplayIcon: buttonWidth >= viewSizes.maxButtonWithoutIconWidth
        });
    }

    render() {
        return (
            <ActionSection
                isVip={this.props.isVip}
                isFullscreen={this.props.isFullscreen}
                buttonsVisibility={this.props.buttonsVisibility}
                shouldDisplayIcon={this.state.shouldDisplayIcon}
                isForInformationDialog={this.props.isForInformationDialog}
            />
        );
    }
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isVip: selectors.isVip(store, instanceId),
    isFullscreen: selectors.isFullScreen(store, instanceId),
    isResponsive: selectors.isResponsive(store, instanceId),

    buttonsVisibility: {
        liveChat: selectors.canInitiateLiveChat(store, instanceId),
        livePreview: selectors.canInitiateLivePreview(store, instanceId),
        privateChat: selectors.canInitiatePrivateChat(store, instanceId),
        voyeurChat: selectors.canInitiateVoyeurChat(store, instanceId),
        cam2Cam: selectors.canInitiateCam2Cam(store, instanceId)
    }
});

export default withRedux(ActionSectionContainer, mapStateToProps, null, { withResizeDetector });
