import styled from "styled-components";
import {isMobile} from 'react-device-detect';
import {IVideoChatButtonProps} from "./types";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

export const VideoChatButtonWrapper = withCustomClasses(styled.div`
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-weight: ${(props) => props.theme.typography.weight.regular};
    display: inline-flex;
    cursor: pointer;
    margin-right: 5px;
`, [DomCustomClass.VideoChatBaseButton]);

export const VideoChatButton = styled.div<IVideoChatButtonProps>`
    width: 25px;
    height: 25px;
    border-radius: 2px;
    font-size: 18px;
    min-width: 25px;
    transition: all .15s linear;
    vertical-align: top;
    background-color: ${(props) => props.backgroundColor || props.theme.colors.videoChatControlsBackground};
    border: 1px solid ${(props) => props.backgroundColor || props.theme.colors.videoChatControlsBackground};
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: center;

    ${!isMobile && `
        &:hover {
            background-color: ${(props) => props.theme.colors.videoChatControlsBackgroundHover};
            border-color: ${(props) => props.theme.colors.videoChatControlsBackgroundHover};
            color: ${(props) => props.theme.colors.videoChatControlsText};
        }`
    }

    ${(props) => props.isOpen && `
            background-color: ${props.theme.colors.videoChatControlsText};
            border-color: ${props.theme.colors.videoChatControlsBackgroundHover};
            color: ${props.theme.colors.videoChatControlsBackgroundHover};
        `
    }
`;
