import * as React from 'react';

import PlayButton from './PlayButton';
import selectors from '../../../redux/selectors';
import { ActionDispatcher} from '../../../redux';
import IStore from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { setIsAutoplayRestricted } from "../../../redux/modules/videochat";
import { resumeInitialVideochat } from "../../../sagas/common/videochat";

interface IStoreProps {
	isAutoplayRestricted: boolean;
	setIsAutoplayRestricted: ActionDispatcher;
	resumeInitialVideochat: ActionDispatcher;
}

type IProps = IInstanceProps & IStoreProps;

const onPlayButtonClick = (props: IProps) => () => {
	const { instanceId } = props;
	props.resumeInitialVideochat(instanceId);
    props.setIsAutoplayRestricted(instanceId, false);
};

const PlayButtonContainer = (props: IProps) => (
	props.isAutoplayRestricted
	? <PlayButton
		onClick={onPlayButtonClick(props)}
	/>
	: <></>
);

const mapStateToProps = (store: IStore, { instanceId}: IInstanceProps) => ({
	isAutoplayRestricted: selectors.isAutoplayRestricted(store, instanceId)
});

const mapDispatchToProps = {
	setIsAutoplayRestricted,
	resumeInitialVideochat
};

export default withRedux(PlayButtonContainer, mapStateToProps, mapDispatchToProps);
