import * as React from 'react';
import { withTheme } from "styled-components";

import { Image, ImageLoaderWrapper, ImageWrapper } from "./style";
import withRedux from "../../hoc/withRedux/withRedux";
import IStore, { MessageDirection } from "../../../redux/store";
import { IInstanceProps } from "../../hoc/withInstanceInfo";
import selectors from "../../../redux/selectors";
import { IComponentProps, IProps, IState } from "./types";
import { getImageLoaderColor, getSquareImageDimension } from "./utils";
import ImageLoader from "../../common/ImageLoader/ImageLoader";
import AppService from '../../../services/AppService';

class ImageMessageContainer extends React.Component<IProps, IState> {
	constructor(props: IProps) {
		super(props);
		this.state = {
			isLoading: true
		}
	}

	render() {
		const { id, src } = this.props;

		const width = getSquareImageDimension(this.props) || undefined;
		const height = getSquareImageDimension(this.props) || undefined;
		const shouldHideImage = this.props.direction === MessageDirection.in && !this.props.showCensoredContent;

		return (
			<ImageWrapper
				onClick={this.onClick(shouldHideImage)}
				id={id}
			>
				{this.state.isLoading &&
				<ImageLoaderWrapper>
					<ImageLoader
						width={width || 0}
						height={height || 0}
						color={getImageLoaderColor(this.props)}
					/>
				</ImageLoaderWrapper>
				}

				<Image src={shouldHideImage ? this.props.censoredContentImage : src}
					   width={width}
					   height={height}
					   isHidden={this.state.isLoading}
					   onLoad={this.onLoad}
				/>
			</ImageWrapper>
		);
	}

	onClick = shouldHideImage => () => {
		const { censoredContentImage, src, onClick, instanceId } = this.props;
		const appService = AppService.getService(instanceId);

		if (shouldHideImage) {
			appService.onAgeVerification();
			return;
		}
		// @ts-ignore
		if (appService.onClickImage) {
			appService.onClickImage(shouldHideImage ? censoredContentImage : src);
			return;
		}
		if (onClick) {
			onClick();
		}
	};

	onLoad = () => {
		this.setState({ isLoading: false });

		if (this.props.onLoad) {
			this.props.onLoad();
		}
	};
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	isFullscreen: selectors.isFullScreen(store, instanceId),
	showCensoredContent: selectors.getShowCensoredContent(store, instanceId),
	censoredContentImage: selectors.getCensoredContentImage(store, instanceId)
});

export default withRedux<IComponentProps>(ImageMessageContainer, mapStateToProps, null, {
	withTheme
});
