import { Action } from "redux-actions";
import { put, select, takeEvery } from "redux-saga/effects";

import { createAction, createActionType } from "../../redux/utils";
import { IPayload } from "../../redux/interfaces";
import selectors from "../../redux/selectors";
import { ActionIds } from "../../redux/interfaces";
import { stopVideoChatPlayer } from "../common/videochat";
import { setVideoChatTimeLimit } from "../../redux/modules/videochat";

const createChatActionType = createActionType(ActionIds.CHAT_SAGA_SPECIFIC, { isSagaAction: true });

// Actions
const PAUSE_CHAT = createChatActionType("PAUSE_CHAT");

// Action Creators
export const pauseChat = createAction(PAUSE_CHAT);

// Sagas
const pauseChatSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    yield put(stopVideoChatPlayer(instanceId));

    const isVideoChatInLiveChatMode = yield select(selectors.isVideoChatInLiveChatMode, instanceId);
    const isTimeLeftBelowThreshold = yield select(selectors.isVideoChatTimeLeftBelowThreshold, instanceId);

    if (isVideoChatInLiveChatMode && isTimeLeftBelowThreshold) {
        // we should assume that the chat was paused because no time (and implicitly money) is left
        yield put(setVideoChatTimeLimit(instanceId, 0));
    }
};

const watchSagas = function* () {
    yield takeEvery(PAUSE_CHAT, pauseChatSaga);
};

export default watchSagas;
