import React from 'react';
import { FontSizeSwitcherWrapper } from './style';

interface IProps {
	shouldIncreaseMessageFontSize: boolean;

	toggleFontSize: () => void;
}

const FontSizeSwitcher = (props: IProps) => (
	<FontSizeSwitcherWrapper onClick={props.toggleFontSize}>
		<i className={props.shouldIncreaseMessageFontSize ? "-icon-font-size-hover-small" : "-icon-font-size-hover-big"} />
	</FontSizeSwitcherWrapper>
);

export default FontSizeSwitcher;
