import { IProps } from "./types";
import { MessageDirection } from "../../../redux/store";

/**
 * These computations are indeed intriguing and filled with magic numbers,
 * but they are based on the CSS rules between the chat box container and the image element
 * and it is the only way I found to determine the maximum available horizontal dimension
 * for an image to use, but BEFORE the image is loading.
 *
 * Note: please note that this needs to be kept in sync with the aforementioned CSS changes
 */
export const getMaxAvailableWidth = (props: IProps) => {
	const { containerWidth, isFullscreen } = props;

	if (!containerWidth) {
		return 0;
	}

	if (isFullscreen) {
		return (containerWidth - 20) * .7;
	}

	return containerWidth * .93 * .93 * .7 - 33;
};

/**
 * Image in the bubble should be square shaped (as per the newest requirements), so this function get its w = h dimension
 * @param props
 */
export const getSquareImageDimension = (props: IProps): number => {
	const maxAvailableWidth = getMaxAvailableWidth(props);
	const {imgWidth, imgHeight} = props;

	return imgWidth <= maxAvailableWidth
		? Math.min(imgWidth, imgHeight)
		: Math.min(imgHeight, maxAvailableWidth);
};

export const getImageLoaderColor = (props: IProps) =>
	props.direction === MessageDirection.in ? props.theme.colors.clientMessageBackground : undefined;
