import styled from "styled-components";
import config from '../../../static/config';
import withIdentifier, { DomIdentifier } from "../../hoc/withIdentifier";
import MessageSectionPopupWrapper from '../../common/MessageSectionPopupWrapper';

interface IGiftWindowWrapperProps {
    isOpen: boolean;
    isResponsive?: boolean;
    isMobileLandscape?: boolean;
	isFullScreen: boolean;
	shouldUseDropdownAnimation: boolean;
}

interface IGiftPaginatorWrapperProps {
    isResponsive?: boolean;
}

export const GiftPagesWrapper = withIdentifier(styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100% - 40px);
`, DomIdentifier.GiftPages);

export const GiftWindowWrapper = withIdentifier<IGiftWindowWrapperProps>(styled(MessageSectionPopupWrapper)<IGiftWindowWrapperProps>`
    bottom: 0;
    background-color: ${(props) => props.theme.colors.windowBackground};

	${props => props.isResponsive && 'max-height: 100%;'}
    ${props => {
		if (props.isOpen) {
			if (props.isResponsive) {
				return `
					left: 0;
					height: 100%;
				`;
			}
	
			return `height: ${props.isFullScreen ? '303px' : '100%'};`
		}

		if (props.shouldUseDropdownAnimation) {
			return 'height: 0;';
		}
	
		return props.isResponsive ? 'left: 100%;' : 'height: 0;';
	}}
    
    
    ${props => props.isMobileLandscape
        ? `
            position: relative;
            bottom: auto;
        `
        :   'position: absolute;'
    }

    z-index: 5;
    width: 100%;
    overflow: hidden;
    box-sizing: border-box;
    transition: all ${config.popupsTransitionTime}s ease-in-out;
    ${props => !props.isResponsive && props.isOpen && `transition-delay: ${config.popupsTransitionTime}s;`}
`, DomIdentifier.GiftWindow);

export const GiftWindowBox = styled.div`
    display: flex;
    height: 100%;
    flex-direction: column;
    text-align: center;
`;

export const GiftPaginatorWrapper = withIdentifier<IGiftPaginatorWrapperProps>(styled.div.attrs({className: 'GiftPaginatorWrapper'})<IGiftPaginatorWrapperProps>`
    flex-direction: ${(props) => props.isResponsive ? 'row' : 'row-reverse'};
    color: ${(props) => props.theme.colors.giftsCloseButton};
    background-color: ${(props) => props.theme.colors.messengerWrapper};
    
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding-bottom: 0;
    font-weight: 400;
    height: 40px;
    min-height: 40px;
    text-align: center;
    border-bottom: 1px solid #C8C8C8;
    border-top: 1px solid #C8C8C8;
`, DomIdentifier.GiftPaginator);

const BaseText = styled.span`
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-style: italic;
    font-size: 14px;
    letter-spacing: 0.3px;
`;

export const RegularText = styled(BaseText)`
    font-weight: ${props => props.theme.typography.weight.medium};
    color: ${props => props.theme.colors.giftPageTitle};
`;

export const BoldText = styled(BaseText)`
    font-weight: ${props => props.theme.typography.weight.bold};
    color: #3c3d3e;
`;

export const GiftsWrapper = styled.div.attrs({className: 'GiftsWrapper'})`
    display: inline-block;
    width: 100%;
`;

export const ArrowWrapper = styled.div`
	font-size: 18px;
	margin-top: 3px;
`;
