import * as React from 'react';
import { forOwn, isNumber } from 'lodash';
import { isMobile, isIOS, isAndroid } from "react-device-detect";

import config from '../../static/config';
import selectors from '../../redux/selectors';
import { ActionDispatcher } from '../../redux';
import getViewProviderParams from '../viewHandler';
import ViewSwitcher from '../ViewSwticher/ViewSwitcher';
import IStore, { IResolution } from '../../redux/store';
import { maximizeCam2Cam, minimizeCam2Cam } from '../../sagas/common/videochat/cam2Cam';
import { IViewHandlerProps, IViewProviderProps } from '../interfaces';
import { IInstanceProps } from '../../features/hoc/withInstanceInfo';
import {
    closeFullscreen,
    openFullscreen,
    setShouldRenderOnlyVideochat
} from '../../redux/modules/videochat';
import withRedux from '../../features/hoc/withRedux';
import { isMobileOrientationLandscape } from '../../utils/utils';

interface IStoreProps {
    isFullscreen: boolean;
    isVideochatOpen: boolean;
    isCam2CamActive: boolean;
    isCam2CamMinimized: boolean;
    hasActionButtons: boolean;
	switchToPortraitWidth: number;
    shouldRenderOnlyVideochat: boolean;
    initialResolution: IResolution;
	hasFullscreenLayoutOnLandscape: boolean;

    openFullscreen: ActionDispatcher;
    closeFullscreen: ActionDispatcher;
    minimizeCam2Cam: ActionDispatcher;
    maximizeCam2Cam: ActionDispatcher;
    setShouldRenderOnlyVideochat: ActionDispatcher<boolean>;
}

interface IComponentProps {
    containerHeight: number;
    containerWidth: number;
}

interface IState {
    isKeyboard: boolean;
    isLandscape: boolean;
}

type IProps = IStoreProps & IComponentProps & IInstanceProps;

class ViewProvider extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            isKeyboard: false,
			isLandscape: this.isLandscape(props.containerWidth, props.containerHeight)
		};
    }

    componentDidMount() {
        if (isMobile) {
            window.addEventListener("focusin", this.enableKeyboard);
            window.addEventListener("focusout", this.disableKeyboard);
            window.addEventListener("orientationchange", this.updateOrientation);
		}
    }

    componentWillUnmount() {
        if (isMobile) {
            window.removeEventListener("focusin", this.enableKeyboard);
            window.removeEventListener("focusout", this.disableKeyboard);
			window.removeEventListener("orientationchange", this.updateOrientation);
        }
    }

    componentDidUpdate() {
		const { containerWidth: width, containerHeight: height,
			isFullscreen, isVideochatOpen, instanceId
		} = this.props;

		const shouldCloseKeyboard = isMobile && isFullscreen && isVideochatOpen
			&& this.state.isKeyboard;

        const {
            cam2camMinimizeHeight,
            cam2camMinimizeWidth,
            minHeaderHeight,
            minMessageSectionHeight,
            minVideochatHeight
        } = config.viewSizes;

		const minHeightForRenderingVideochatWithMessenger = minHeaderHeight
			+ minMessageSectionHeight
			+ minVideochatHeight;

		const shouldRenderOnlyVideochat = !isMobile
			&& (height < minHeightForRenderingVideochatWithMessenger);

        if (shouldRenderOnlyVideochat !== this.props.shouldRenderOnlyVideochat) {
            this.props.setShouldRenderOnlyVideochat(instanceId, shouldRenderOnlyVideochat);
		}

        // Added to update the keyboard if the device is rotated from portait to landscape in videochat mode on mobile devices
        if (shouldCloseKeyboard) {
            this.setState({ isKeyboard: false });
        }

        if (this.props.isCam2CamActive) {
            const areDimensionsExceeded = width > cam2camMinimizeWidth
                || height > cam2camMinimizeHeight;

            const shouldMinimizeCam2Cam = !areDimensionsExceeded && !this.props.isCam2CamMinimized;
            const shouldMaximizeCam2Cam = areDimensionsExceeded && this.props.isCam2CamMinimized;

            if (shouldMinimizeCam2Cam) {
                this.props.minimizeCam2Cam(instanceId);
            }
            if (shouldMaximizeCam2Cam) {
                this.props.maximizeCam2Cam(instanceId);
            }
		}

		if (this.shouldForceFullscreen()) {
			this.forceFullscreen();
		}

		this.updateOrientation();
    }

    render() {
        const viewHandlerProps: IViewHandlerProps = {
            containerWidth: this.props.containerWidth,
            containerHeight: this.props.containerHeight,
			isLandscape: this.state.isLandscape,
            isVideoChat: this.props.isVideochatOpen,
            resolution: this.props.initialResolution,
            isKeyboard: this.state.isKeyboard,
            hasActionButtons: this.props.hasActionButtons,
            switchToPortraitWidth: this.props.switchToPortraitWidth,
            shouldRenderOnlyVideochat: this.props.shouldRenderOnlyVideochat,
            isFullscreen: this.props.isFullscreen
		};

		const viewProviderParams: IViewProviderProps = getViewProviderParams(viewHandlerProps);

        // Incorrect behavior from ViewSwitcher when we have to force the fullscreen layout,
        // but the isFullscreen flag is still on false. We reach an invalid layout scenario,
        // which can break our application.
		if (this.shouldForceFullscreen() && !this.props.isFullscreen && viewProviderParams.isLandscape) {
			return null;
		}

		// Render the view component only after receiving all the numerical values configured,
		// otherwise we will end up with a flicker when starting the stream.
		let hasAllParamsConfigured = true;
		forOwn(viewProviderParams, value => {
			if (isNumber(value) && isNaN(value)) {
				hasAllParamsConfigured = false;
			}
		});

		if (!hasAllParamsConfigured) {
			return null;
		}

        return (
            <ViewSwitcher {...viewProviderParams} />
        );
	};

	private isLandscape = (width: number, height: number): boolean => isAndroid
		? isMobileOrientationLandscape()
		: width > height;

    private updateOrientation = () => {
		const isLandscape: boolean = this.isLandscape(this.props.containerWidth, this.props.containerHeight);
		if (this.state.isLandscape !== isLandscape) {
			this.setState({ isLandscape });
		}
	};

	private shouldForceFullscreen = () => {
		const { isVideochatOpen, hasFullscreenLayoutOnLandscape } = this.props;

		return hasFullscreenLayoutOnLandscape && isVideochatOpen && isMobile;
	};

	private forceFullscreen = () => {
        const { containerWidth: width, containerHeight: height, instanceId } = this.props;

        if (width > height && !this.props.isFullscreen) {
            // On Android only if the orientation is right change the fullscreen flag
            if (isAndroid && isMobileOrientationLandscape()) {
                this.props.openFullscreen(instanceId);
            }
            // On iOS there are no special conditions since the virtual keyboard doesn't resize the viewport
            else if (isIOS) {
                this.props.openFullscreen(instanceId);
            }
        }

        // Only change the flag if is true
        else if (width < height && this.props.isFullscreen) {
            this.props.closeFullscreen(instanceId);
        }
    };

    private enableKeyboard = (e: any) => {
        // Disable focus on buttons
        if (e && e.target.nodeName === "BUTTON") {
            e.preventDefault();
        } else {
            this.setState({ isKeyboard: true });
        }
    };

    private disableKeyboard = () => {
        this.setState({ isKeyboard: false });
	};
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    initialResolution: selectors.getInitialResolution(store, instanceId),
    isFullscreen: selectors.isFullScreen(store, instanceId),
    isVideochatOpen: selectors.isVideoChatOpen(store, instanceId),
    isCam2CamActive: selectors.isCam2CamActive(store, instanceId),
    isCam2CamMinimized: selectors.isCam2CamMinimized(store, instanceId),
    hasActionButtons: selectors.hasActionButtons(store, instanceId),
	switchToPortraitWidth: selectors.getSwitchToPortraitWidth(store, instanceId),
    shouldRenderOnlyVideochat: selectors.shouldRenderOnlyVideochat(store, instanceId),
	hasFullscreenLayoutOnLandscape: selectors.hasFullscreenLayoutOnLandscape(store, instanceId)
});

const mapDispatchToProps = {
    openFullscreen,
    closeFullscreen,
    setShouldRenderOnlyVideochat,
    minimizeCam2Cam,
    maximizeCam2Cam
};

export default withRedux<IComponentProps>(ViewProvider, mapStateToProps, mapDispatchToProps);
