import * as React from 'react';

import Profile from './Profile';
import { ProfileWrapper } from './style';
import { onClickProfilePicture } from './utils';
import selectors from '../../../redux/selectors';
import IStore from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps } from "./types";

const ProfileContainer = (props: IProps) => (
    <ProfileWrapper>
        <Profile
            motto={props.motto}
            isVideochatOpen={props.isVideochatOpen}
            translation={props.translation}
            onClickProfilePicture={onClickProfilePicture(props)}
            hasModelProfilePicture={props.hasModelProfilePicture}
            hasModelPictureInProfile={props.hasModelPictureInProfile}
        />
    </ProfileWrapper>
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isVideochatOpen: selectors.isVideoChatOpen(store, instanceId),
    motto: selectors.getModelMotto(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    hasModelProfilePicture: selectors.hasModelProfilePicture(store, instanceId),
    hasModelPictureInProfile: selectors.hasModelPictureInProfile(store, instanceId)
});

export default withRedux(ProfileContainer, mapStateToProps);
