import * as React from 'react';

import utils from '../../../utils';
import ProfilePicture from './ProfilePicture';
import selectors from '../../../redux/selectors';
import AppService from '../../../services/AppService';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import IStore from '../../../redux/store';

interface IComponentProps {
    tooltipText?: string;

    onClick?: () => any;
}

interface IStoreProps {
    modelPicture: string;
	isModelOnline: boolean;
	isContactNoteOpen: boolean;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

const ProfilePictureContainer = (props: IProps) => {
    const appService = AppService.getService(props.instanceId);

    const hasOnProfileClick = utils.hasHookConfigured(appService.onClickProfile);

    return (
        <ProfilePicture
            picture={props.modelPicture}
            isOnline={props.isModelOnline}
            tooltipText={props.tooltipText}
			hasOnProfileClick={hasOnProfileClick}
			isContactNoteOpen={props.isContactNoteOpen}
            onClick={props.onClick}
        />
    );
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    modelPicture: selectors.getModelProfilePicture(store, instanceId),
	isModelOnline: selectors.isModelOnline(store, instanceId),
	isContactNoteOpen: selectors.isContactNoteOpen(store, instanceId)
});

export default withRedux<IComponentProps>(ProfilePictureContainer, mapStateToProps);
