import * as React from 'react';

import { ITranslation } from "../../../redux/store";
import { GroupChatParticipantsWrapper } from "./style";
import { translationWithValues } from "../../../utils/utils";

export interface IProps {
	participants: number;
	translation: ITranslation;

	openMentionSection: () => void;
}

const GroupChatParticipants = (props: IProps) => (
	<GroupChatParticipantsWrapper onClick={props.openMentionSection}>
		{props.participants === 1
			? translationWithValues(props.translation.chat.groupChat.singleMember, { value: props.participants })
			: translationWithValues(props.translation.chat.groupChat.members, { value: props.participants })}
	</GroupChatParticipantsWrapper>
);

export default GroupChatParticipants;
