import * as React from 'react';
import { ITranslation, VideoChatType } from '../../../redux/store';
import MobileMenuItem from './MobileMenuItem';
import { ImageUploaderMenuItemWrapper, MobileMenuWrapper } from './style';

interface IProps {
    translation: ITranslation;
    isGiftButtonVisible: boolean;
    isToyButtonVisible: boolean;
    hasImageUpload: boolean;

    buttonsVisibility: {
        liveChat: boolean;
        privateChat: boolean;
        cam2Cam: boolean;
    }

    toggleGiftSection: () => void;
    toggleToySection: () => void;
    closeMessenger: () => void;
    switchVideoChatMode: (type: VideoChatType) => () => void;
    toggleCam2Cam: () => void;
    requestImageUpload: () => void;
}

const MobileMenu = (props: IProps) => {
    /**
     * Create a dictionary for keeping the buttons:
     * the key is the button type or videochat type (they cover the same concept)
     * and the value is the JSX for rendering the element
     */
    const mobileMenu: JSX.Element[] = [];
    const { buttonsVisibility } = props;

    const { stopLiveChat, camToCam, liveChat, privateChat } = props.translation.chat;
    const { sendGifts, sendImage } = props.translation.message;
    const { toyControl } = props.translation.chat.toyControl;

    /** Create the toy button */
    if (props.isToyButtonVisible) {
        mobileMenu.push(
            <MobileMenuItem
                key={"toy-control"}
                className="-icon-toy-control"
                text={toyControl}
                onClick={props.toggleToySection}
            />
        );
    }

    /** Create the livechat button */
    if (buttonsVisibility.liveChat) {
        mobileMenu.push(
            <MobileMenuItem
                key={VideoChatType.LIVECHAT}
                className="-icon-webcam-single-full"
                text={liveChat}
                onClick={props.switchVideoChatMode(VideoChatType.LIVECHAT)}
            />
        );
    }

    /** Create the private chat button */
    if (buttonsVisibility.privateChat) {
        mobileMenu.push(
            <MobileMenuItem
                key={VideoChatType.PRIVATE_CHAT}
                className="-icon-webcam-private-full"
                text={privateChat}
                onClick={props.switchVideoChatMode(VideoChatType.PRIVATE_CHAT)}
            />
        );
    }

    /** Create the cam2cam button */
    if (buttonsVisibility.cam2Cam) {
        mobileMenu.push(
            <MobileMenuItem
                key={VideoChatType.CAM2CAM}
                className="-icon-webcam-c2c-full"
                text={camToCam}
                onClick={props.toggleCam2Cam}
            />
        );
    }
    
    if (props.hasImageUpload) {
        mobileMenu.push(
            <ImageUploaderMenuItemWrapper key="image-uploader">
                <MobileMenuItem
                    className="-icon-image-full"
                    text={sendImage}
                    onClick={props.requestImageUpload}
                />
            </ImageUploaderMenuItemWrapper>
        );
    }

    if (props.isGiftButtonVisible) {
        mobileMenu.push(
            <MobileMenuItem
                key="gift-section"
                className="-icon-gift-full"
                text={sendGifts}
                onClick={props.toggleGiftSection}
            />
        );
    }

    mobileMenu.push(
        <MobileMenuItem
            key="close-videochat"
            className="-icon-close-line"
            text={stopLiveChat}
            onClick={props.closeMessenger}
        />
    );

    return (
        <MobileMenuWrapper>
            {mobileMenu}
        </MobileMenuWrapper>
    )
};

export default MobileMenu;
