import { get } from 'lodash';
import { eventChannel } from "redux-saga";
import {call, put, select, spawn, take, takeEvery} from "redux-saga/effects";
import { AnyAction } from "redux";
import { createAction } from "redux-actions";

import selectors from '../../redux/selectors';
import { setUserOnlineState } from "../../redux/modules/user";
import { createActionType } from "../../redux/utils";
import { ActionIds } from "../../redux/interfaces";

const createUserEventsActionType = createActionType(ActionIds.USER_EVENTS, { isSagaAction: true });

// Actions
const SET_USER_ONLINE_STATE_4_ALL_INSTANCES = createUserEventsActionType("SET_USER_ONLINE_STATE_4_ALL_INSTANCES");

// Action Creators
const setUserOnlineState4AllInstances = createAction<boolean>(SET_USER_ONLINE_STATE_4_ALL_INSTANCES);

const windowEventsChannel = () => {
    return eventChannel(emitter => {

        window.addEventListener('offline', () => {
            emitter(setUserOnlineState4AllInstances(false));
        });
        window.addEventListener('online', () => {
            emitter(setUserOnlineState4AllInstances(true));
        });

        return () => {
            // no need for un-subscribing; saga's live exceeds the app live
        }
    });
};

const windowEventsListenerSaga = function*() {
    const onlineStatus = get(navigator, 'onLine', true);
    yield put(setUserOnlineState4AllInstances(onlineStatus));

    const windowEvents = yield call(windowEventsChannel);

    try {
        while (true) {
            const action = yield take(windowEvents);
            yield put(action);
        }
    } finally {
        // finished
    }
};

const allInstancesOnlineStateSaga = function* (action: AnyAction) {
    const onlineStatus: boolean = action.payload;

    const instanceIds: string[] = yield select(selectors.getInstanceIds);
    for (const instanceId of instanceIds) {
        yield put(setUserOnlineState(instanceId, onlineStatus));
    }
};

const watchSagas = function* () {
    yield spawn(windowEventsListenerSaga);
    yield takeEvery(SET_USER_ONLINE_STATE_4_ALL_INSTANCES, allInstancesOnlineStateSaga);
};

export default watchSagas;
