import * as React from 'react';

import GiftPage from './GiftPage';
import selectors from '../../../redux/selectors';
import IStore, { IGift, ITranslation, ICurrency } from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    currency: ICurrency;
    translation: ITranslation;
}

interface IComponentProps {
    page: IGift[];
    activePage: number;
    isBundleGiftPage: boolean;
    title?: React.ReactElement;

    onGiftClick: (gift: string) => () => void;
}

type IProps = IComponentProps & IStoreProps & IInstanceProps;

const GiftPageContainer = (props: IProps) => (
    <GiftPage
        activePage={props.activePage}
        title={props.title}
        page={props.page}
        currency={props.currency}
        translation={props.translation}
        isBundleGiftPage={props.isBundleGiftPage}

        onGiftClick={props.onGiftClick}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    currency: selectors.getCurrency(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

export default withRedux<IComponentProps>(GiftPageContainer, mapStateToProps);