import * as React from "react";
import { ITranslation, ICurrency, ITipOption} from "../../../redux/store";
import { TipImage, TipPrice, UserPromptTipButton, UserPromptTipSubTitle, UserPromptTipTitle, UserPromptTipWrapper } from './style';

interface IProps {
    currency: ICurrency;
    isResponsive?: boolean;
    tip: ITipOption;
    translation: ITranslation;
    imageSize: number;

    sendTip: () => void;
}

const UserPromptTip = (props: IProps) => {
    const { tip1, tip2, tip3 } = props.translation.chat;

    return (
        <UserPromptTipWrapper isResponsive={props.isResponsive}>
            <UserPromptTipTitle>{tip1}</UserPromptTipTitle>
            <UserPromptTipSubTitle>{tip2}</UserPromptTipSubTitle>

            {props.tip.imageUrl &&
                <TipImage
                    imageUrl={props.tip.imageUrl}
                    imageSize={props.imageSize}
                />
            }
            
            <TipPrice>
                {`(${props.tip.value} ${props.tip.currency.symbol})`}
            </TipPrice>

            <UserPromptTipButton onClick={props.sendTip}>
                {tip3}
            </UserPromptTipButton>
        </UserPromptTipWrapper>
    );
};

export default UserPromptTip;