import * as React from 'react';
import { ITranslation } from '../../../redux/store';
import { ContactNoteCloseButton, ContactNoteInput, ContactNoteTitle, ContactNoteWrapper } from './style';

interface IProps {
    translation: ITranslation;
    contactNoteText: string;

    onCloseClick: () => void;
    onInputChange: (event: any) => void;
}

const ContactNoteDialog = (props: IProps) => {
    const { yourNote, addNote } = props.translation.user;

    return (
        <ContactNoteWrapper>
            <ContactNoteTitle>{yourNote}</ContactNoteTitle>

            <ContactNoteCloseButton>
                <div
                    className={"icon -icon-close-full"} 
                    onClick={props.onCloseClick}
                />
            </ContactNoteCloseButton>
            
            <ContactNoteInput 
                onChange={props.onInputChange}
                value={props.contactNoteText}
                placeholder={addNote} 
            />
        </ContactNoteWrapper>
    );
};

export default ContactNoteDialog;