import { isEmpty, get } from 'lodash';
import { Action } from 'redux-actions';
import { put, select, takeEvery } from "redux-saga/effects";
import { CMDP_NEWCHANNEL, ACTION, CMDP_NEWCHANNEL_RESPONSE } from 'vxcontrol-client-lib';

import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import { IPartner } from '../../redux/store';
import selectors from '../../redux/selectors';
import ApiService from '../../services/ApiService';
import { setChannel } from '../../redux/modules/session';
import { ActionIds } from '../../redux/interfaces';

const createNewChannelActionType = createActionType(ActionIds.NEW_CHANNEL, { isSagaAction: true });

// Actions
const REQUEST_NEW_CHANNEL = createNewChannelActionType("REQUEST_NEW_CHANNEL");
const RECEIVE_NEW_CHANNEL = createNewChannelActionType("RECEIVE_NEW_CHANNEL");

// Action Creators
export const requestNewChannel = createAction(REQUEST_NEW_CHANNEL);
export const onReceiveNewChannel = createAction<CMDP_NEWCHANNEL_RESPONSE>(RECEIVE_NEW_CHANNEL);

// Sagas
/** Sends through websocket the data for creating a new channel */
export const requestNewChannelSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    const sessionID = yield select(selectors.getSessionId, instanceId);
    const partner: IPartner = yield select(selectors.getPartner, instanceId);

    const command: CMDP_NEWCHANNEL = {
        action: ACTION.CMDP_NEWCHANNEL,
        params: {
            sessionID,
            partnerId: partner.id,
            partnerKey: partner.key
        }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

/** If creating a new channel is successful, save the channelId into the Redux store */
export const receiveNewChannelSaga = function* ({ payload }: Action<IPayload<CMDP_NEWCHANNEL_RESPONSE>>) {
    const { instanceId, value: response } = payload;

    if (isEmpty(get(response, "values.channelId"))) {
        return;
    }

    yield put(setChannel(instanceId, response.values.channelId));
};

const watchSagas = function*() {
    yield takeEvery(REQUEST_NEW_CHANNEL, requestNewChannelSaga);
    yield takeEvery(RECEIVE_NEW_CHANNEL, receiveNewChannelSaga);
};

export default watchSagas;
