import React, { Component } from 'react';
import { ReactMic } from '@cleandersonlobo/react-mic';

import selectors from '../../../redux/selectors';
import IStore from '../../../redux/store';
import { requestSendAudioMessage } from '../../../sagas/websockets/message';
import { setIsMicEnabled } from '../../../redux/modules/messenger';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { ActionDispatcher } from "../../../redux";

interface IStoreProps {
    // Properties
    shouldSendMessage: boolean;
    isRecording: boolean;
    isMicEnabled: boolean;
    hasAudioMessages: boolean;

    // Action Creators
    requestSendAudioMessage: ActionDispatcher<Blob>;
    setIsMicEnabled: ActionDispatcher<boolean>;
}

type IProps = IStoreProps & IInstanceProps;

class AudioAnalyserContainer extends Component<IProps> {
    render() {
        return this.props.hasAudioMessages && (
			<ReactMic
				record={this.props.isRecording}
				onStop={this.setAudioSrc}
				mimeType="audio/mp3"
				onData={this.enableMic}
			/>
		);
    }

    private setAudioSrc = (audioBlob: { blob: Blob }) => {
        if (this.props.shouldSendMessage) {
            this.props.requestSendAudioMessage(this.props.instanceId, audioBlob.blob);
        }
    };

    private enableMic = () => {
        if (!this.props.isMicEnabled) {
            this.props.setIsMicEnabled(this.props.instanceId, true);
        }
    }
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    shouldSendMessage: selectors.getShouldSendMessage(store, instanceId),
    isRecording: selectors.isRecording(store, instanceId),
    hasAudioMessages: selectors.hasAudioMessages(store, instanceId),
    isMicEnabled: selectors.isMicEnabled(store, instanceId)
});

const mapDispatchToProps = {
    requestSendAudioMessage,
    setIsMicEnabled
};

export default withRedux(AudioAnalyserContainer, mapStateToProps, mapDispatchToProps);
