import * as React from 'react';
import onClickOutside, { InjectedOnClickOutProps } from 'react-onclickoutside';

import Messenger from './Messenger';
import MessengerLoader from "../MessengerLoader";
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import { clickCloseMessengerButton } from "../../../sagas/common/ui";
import { closeMessenger } from '../../../redux/modules/messenger';
import IStore, { ITranslation } from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    isOpen: boolean;
    isMessengerLoading: boolean;
    isContactNoteOpen: boolean;
    translation: ITranslation;
    shouldCloseMessenger: boolean;
    isMessageTypeHeader: boolean;
    hasActionSectionUnderTextbox: boolean;
    shouldRenderToySectionHeader: boolean;

    closeMessenger: ActionDispatcher;
    clickCloseMessengerButton: ActionDispatcher;
}

type IProps = IInstanceProps & IStoreProps & InjectedOnClickOutProps;

const onClose = (props: IProps) => () => {
    props.clickCloseMessengerButton(props.instanceId);
};

class MessengerContainer extends React.Component<IProps> {
    render() {
        const messengerComponent = (
            <Messenger
                isMessageTypeHeader={this.props.isMessageTypeHeader}
                isContactNoteOpen={this.props.isContactNoteOpen}
                shouldRenderToySectionHeader={this.props.shouldRenderToySectionHeader}
                hasActionSectionUnderTextbox={this.props.hasActionSectionUnderTextbox}
                translation={this.props.translation}

                onClose={onClose(this.props)}
            />
        );

        const messengerOrLoadingComponent = this.props.isMessengerLoading
            ? <MessengerLoader />
            : messengerComponent;

        return (
            <>
                {this.props.isOpen && messengerOrLoadingComponent}
            </>
        );
    }

    /**
     * Click outside should not be allowed
     * when the videochat is open, a picture is open in modal
     * or when the contact note is rendered over the messenger
     */
    // @ts-ignore
    private handleClickOutside = () => {
        if (this.props.shouldCloseMessenger) {
            this.props.closeMessenger(this.props.instanceId);
        }
    };
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isOpen: selectors.isOpen(store, instanceId),
    isMessengerLoading: selectors.isMessengerLoading(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    isContactNoteOpen: selectors.isContactNoteOpen(store, instanceId),
    shouldCloseMessenger: selectors.shouldCloseMessenger(store, instanceId),
    hasActionSectionUnderTextbox: selectors.hasActionSectionUnderTextbox(store, instanceId),
    shouldRenderToySectionHeader: selectors.shouldRenderToySectionHeader(store, instanceId),
    isMessageTypeHeader: selectors.isMessageTypeHeader(store, instanceId)
});

const mapDispatchToProps = {
    closeMessenger,
    clickCloseMessengerButton
};

export default withRedux(
    onClickOutside(MessengerContainer, { excludeScrollbar: true }),
    mapStateToProps,
    mapDispatchToProps
);
