import styled from 'styled-components';
import { ISimpleModelPictureProps } from './types';
import { isUndefined } from 'lodash';
import Overlay from '../Overlay';

interface IPictureOverlayProps {
	size: number;
}

export const Picture = styled.img`
    width: 100%;
    height: 100%;
    border-radius: 100%;
    background-size: auto 100%;
`;

export const PictureWrapper = styled.div.attrs({ className: 'PictureWrapper' })<ISimpleModelPictureProps>`
    width: ${props => props.size}px;
    height: ${props => props.size}px;
    
    ${props => !props.isForGroupChatMentions && `
    	border: 3px solid ${props.isGroupChatMode
			? props.theme.colors.groupChat.pictureBorder
			: isUndefined(props.isOnline)
				? "white"
				: props.isOnline ? '#43B31C' : '#F1F1F1'
		};
    `}
    
    ${props => props.hasCursorPointer && `cursor: pointer;`}

    border-radius: 100%;
`;

export const PictureOverlay = styled(Overlay)<IPictureOverlayProps>`
	${({ size }) => `
		width: ${size}px;
		height: ${size/2 + 3}px;
		border-top-left-radius: ${size/2 + 11}px;
		border-top-right-radius: ${size/2 + 11}px;
	`}
	
	display: flex;
	position: absolute;
	justify-content: center;
	border-bottom: 0;
`;
