import { IContainerProps as IProps } from "./types";
import AppService from '../../../services/AppService';
import { AnswerType, KnownAnswerType, PreChatInformationType } from "../../../redux/store";

export const onCloseClick = (props: IProps) => () => {
    props.closePrechatDialog(props.instanceId, props.informationType);
};

export const onVerifyAgeClick = (props: IProps) => () => {
    AppService.getService(props.instanceId).onAgeVerification();
    props.resetPreChatInformation(props.instanceId);
    props.closeMessenger(props.instanceId);
};

export const onChargeNowClick = (props: IProps) => () => {
    props.rechargeExternal(props.instanceId);
    props.resetPreChatInformation(props.instanceId);
};

export const onVipClick = (props: IProps) => () => {
    AppService.getService(props.instanceId).onBecomeVIP();
    props.resetPreChatInformation(props.instanceId);
};

export const onAnswerToSpyingPrivateChat = (props: IProps) => (answer: AnswerType) => {
    const { instanceId } = props;

    if (answer === KnownAnswerType.YES) {
        props.acceptToSpy(instanceId);
    } else {
        props.closePrechatDialog(instanceId, PreChatInformationType.PRIVATE_CHAT_CAN_SPY);
    }
};
