import { produce } from 'immer';
import { AnyAction } from 'redux';
import { find, map } from 'lodash';
import { Action, handleActions } from 'redux-actions';

import {
	IAutoRecharge,
	IBundle,
	IChat,
	IChatFeatures,
	ICurrency,
	IGift,
	IGiftCategory,
	IOneClickRecharge,
	ISession
} from '../store';
import { createAction, createActionType } from '../utils';
import { RESET_STORE } from "./app";
import types from '../../sagas/websockets/types';
import { getInitialInstanceData } from '../initialStore';
import { ActionIds } from '../interfaces';

export interface IHandleQuantitiesPayload {
	giftId: number;
	quantityToHandle: number;
}

const createSessionActionType = createActionType(ActionIds.SESSION, { isReduxAction: true });

// Actions
export const SET_CHAT = createSessionActionType("SET_CHAT");
export const SET_CHAT_ID = createSessionActionType("SET_CHAT_ID");
export const RESET_CHAT = createSessionActionType("RESET_CHAT");
export const SET_CHANNEL = createSessionActionType("SET_CHANNEL");
export const RESET_CHANNEL = createSessionActionType("RESET_CHANNEL");
export const SET_SESSION = createSessionActionType("SET_SESSION");
export const RESET_SESSION = createSessionActionType("RESET_SESSION");

const SET_CHAT_FEATURES = createSessionActionType("SET_CHAT_FEATURES");
const SET_FREE_MESSAGES = createSessionActionType("SET_FREE_MESSAGES");
const RESET_FREE_CHAT_DURATION = createSessionActionType("RESET_FREE_CHAT_DURATION");
const SET_CURRENCY = createSessionActionType("SET_CURRENCY");
export const SET_WEBTOKEN = createSessionActionType("SET_WEBTOKEN");
export const SET_UPLOAD_MEDIA_URL = createSessionActionType("SET_UPLOAD_MEDIA_URL");

const SET_AUTO_RECHARGE = createSessionActionType("SET_AUTO_RECHARGE");
const SET_ONE_CLICK_RECHARGE = createSessionActionType("SET_ONE_CLICK_RECHARGE");
const SET_LAST_CHARGED_AMOUNT = createSessionActionType("SET_LAST_CHARGED_AMOUNT");
const RESET_LAST_CHARGED_AMOUNT = createSessionActionType("RESET_LAST_CHARGED_AMOUNT");

const SET_GIFTS = createSessionActionType("SET_GIFTS");
const SET_ACTIVE_GIFT_CATEGORIES = createSessionActionType("SET_ACTIVE_GIFT_CATEGORIES");
const SET_ACTIVE_GIFT_BUNDLES = createSessionActionType("SET_ACTIVE_GIFT_BUNDLES");
const INITIALIZE_QUANTITIES_TO_INVENTORY_GIFTS = createSessionActionType("INITIALIZE_QUANTITIES_TO_INVENTORY_GIFTS");
export const DECREASE_CURRENT_GIFT_QUANTITY = createSessionActionType("DECREASE_CURRENT_GIFT_QUANTITY");
const RESET_CURRENT_QUANTITIES = createSessionActionType("RESET_CURRENT_QUANTITIES");

/**
 * When we send a message containing gifts, the initial quantity has to become the current one,
 * until we receive the feedback from the back-end, in order to always have the gift quantities synced.
 */
const SYNC_QUANTITIES_ON_MESSAGE_SENT = createSessionActionType("SYNC_QUANTITIES_ON_MESSAGE_SENT");

// Action Creators
export const setWebtoken = createAction<string>(SET_WEBTOKEN);
export const setUploadMediaUrl = createAction<string>(SET_UPLOAD_MEDIA_URL);
export const setChat = createAction<IChat>(SET_CHAT);
export const setChatId = createAction<string>(SET_CHAT_ID);
export const resetChat = createAction(RESET_CHAT);

export const setFreePlatformMessages = createAction<number>(SET_FREE_MESSAGES);
export const setChatFeatures = createAction<IChatFeatures>(SET_CHAT_FEATURES);
export const setCurrency = createAction<ICurrency>(SET_CURRENCY);

export const setChannel = createAction<string>(SET_CHANNEL);
export const resetChannel = createAction(RESET_CHANNEL);
export const setSession = createAction<string>(SET_SESSION);
export const resetSession = createAction(RESET_SESSION);

export const setGifts = createAction<IGift[]>(SET_GIFTS);
export const setGiftCategories = createAction<IGiftCategory[]>(SET_ACTIVE_GIFT_CATEGORIES);
export const setGiftBundles = createAction<IBundle[]>(SET_ACTIVE_GIFT_BUNDLES);

export const setAutoRecharge = createAction<IAutoRecharge>(SET_AUTO_RECHARGE);
export const setOneClickRecharge = createAction<IOneClickRecharge>(SET_ONE_CLICK_RECHARGE);
export const setLastChargedAmount = createAction<number>(SET_LAST_CHARGED_AMOUNT);
export const resetLastChargedAmount = createAction(SET_LAST_CHARGED_AMOUNT);

export const initializeQuantitiesToInventoryGifts = createAction<types.IInventoryGift[]>(
	INITIALIZE_QUANTITIES_TO_INVENTORY_GIFTS
);

export const decreaseCurrentGiftQuantity = createAction<IHandleQuantitiesPayload>(DECREASE_CURRENT_GIFT_QUANTITY);
export const resetCurrentQuantities = createAction(RESET_CURRENT_QUANTITIES);
export const resetPlatformFreeChatDuration = createAction(RESET_FREE_CHAT_DURATION);
export const syncQuantitiesOnMessageSent = createAction(SYNC_QUANTITIES_ON_MESSAGE_SENT);

// Reducer
const session = handleActions({
	[RESET_STORE]: (state: ISession, action: AnyAction) => getInitialInstanceData().session,

	[SET_CHAT]: produce((draft: ISession, { payload }: Action<IChat>) => {
		draft.chat = payload;
	}),

	[SET_CHAT_ID]: produce((draft: ISession, { payload }: Action<string>) => {
		if (draft.chat) {
			draft.chat.chatId = payload;
		}
	}),

	[RESET_CHAT]: produce((draft: ISession, action: AnyAction) => {
		draft.chat = getInitialInstanceData().session.chat;
	}),

	[SET_CHAT_FEATURES]: produce((draft: ISession, { payload }: Action<IChatFeatures>) => {
		draft.chatFeatures = payload;
	}),

	[RESET_FREE_CHAT_DURATION]: produce((draft: ISession, action: AnyAction) => {
		draft.chatFeatures.freeChatDuration = 0;
	}),

	[SET_CURRENCY]: produce((draft: ISession, { payload }: Action<ICurrency>) => {
		draft.currency = payload;
	}),

	[SET_CHANNEL]: produce((draft: ISession, { payload }: Action<string>) => {
		draft.channelId = payload;
	}),

	[RESET_CHANNEL]: produce((draft: ISession, action: AnyAction) => {
		draft.channelId = getInitialInstanceData().session.channelId;
	}),

	[SET_FREE_MESSAGES]: produce((draft: ISession, { payload }: Action<number>) => {
		draft.freeMessages = payload;
	}),

	[SET_GIFTS]: produce((draft: ISession, { payload }: Action<IGift[]>) => {
		draft.gifts = payload;
	}),

	[SET_ACTIVE_GIFT_CATEGORIES]: produce((draft: ISession, { payload }: Action<IGiftCategory[]>) => {
		draft.giftCategories = payload;
	}),

	[SET_ACTIVE_GIFT_BUNDLES]: produce((draft: ISession, { payload }: Action<IBundle[]>) => {
		draft.giftBundles = payload;
	}),

	[INITIALIZE_QUANTITIES_TO_INVENTORY_GIFTS]: produce((draft: ISession, { payload }: Action<types.IInventoryGift[]>) => {
		draft.gifts = map(draft.gifts, (gift: IGift) => {
			const inventoryGift = find(payload, (invGift: types.IInventoryGift) =>
				invGift.gift.id === gift.id
			);

			if (inventoryGift) {
				gift.initialQuantity = inventoryGift.quantity;
				gift.currentQuantity = inventoryGift.quantity;
			}

			return gift;
		});
	}),

	[DECREASE_CURRENT_GIFT_QUANTITY]: produce((draft: ISession, { payload }: Action<IHandleQuantitiesPayload>) => {
		const giftToDecrease: IGift | undefined = find(draft.gifts,
			(gift: IGift) => gift.id === payload.giftId
		);

		if (giftToDecrease && giftToDecrease.initialQuantity) {
			giftToDecrease.currentQuantity = giftToDecrease.initialQuantity - payload.quantityToHandle;
		}
	}),

	[RESET_CURRENT_QUANTITIES]: produce((draft: ISession, action: AnyAction) => {
		draft.gifts = map(draft.gifts, (gift: IGift) => {
			if (gift.initialQuantity) {
				gift.currentQuantity = gift.initialQuantity;
			}

			return gift;
		});
	}),

	[SYNC_QUANTITIES_ON_MESSAGE_SENT]: produce((draft: ISession, action: AnyAction) => {
		draft.gifts = map(draft.gifts, (gift: IGift) => {
			if (gift.initialQuantity) {
				gift.initialQuantity = gift.currentQuantity;
			}

			return gift;
		});
	}),

	[SET_SESSION]: produce((draft: ISession, { payload }: Action<string>) => {
		draft.sessionID = payload;
	}),

	[SET_WEBTOKEN]: produce((draft: ISession, { payload }: Action<string>) => {
		draft.webtoken = payload;
	}),

	[SET_UPLOAD_MEDIA_URL]: produce((draft: ISession, { payload }: Action<string>) => {
		draft.uploadMediaUrl = payload;
	}),

	[SET_AUTO_RECHARGE]: produce((draft: ISession, { payload }: Action<IAutoRecharge>) => {
		draft.autoRecharge = payload;
	}),

	[SET_ONE_CLICK_RECHARGE]: produce((draft: ISession, { payload }: Action<IOneClickRecharge>) => {
		draft.oneClickRecharge = payload;
	}),

	[SET_LAST_CHARGED_AMOUNT]: produce((draft: ISession, { payload }: Action<number>) => {
		draft.lastChargedAmount = payload;
	}),

	[RESET_LAST_CHARGED_AMOUNT]: produce((draft: ISession, action: AnyAction) => {
		draft.lastChargedAmount = getInitialInstanceData().session.lastChargedAmount;
	})
}, getInitialInstanceData().session);

export default session;
