import * as React from "react";
import { Logo, Text, OptionImage } from "./style";
import { IToyMessageInfo } from "src/redux/store";

type IProps = IToyMessageInfo;

const DefaultToyMessage = () => (
    <Logo className="-icon-toy-control-vibrating" />
); 

const ToyMessage: React.FC<IProps> = ({
    text,
    isActive,
    isDefault
}: IProps) => {
    const isMessageActive = isDefault || isActive;
	const image = <OptionImage
		isActive={isMessageActive}
		className={isMessageActive ? '-icon-toy-control-vibrating' : '-icon-toy-control'}
	/>;

    return (
        <>
            {isDefault
                ? <DefaultToyMessage />
                : image
            }
            
            <Text isActive={isMessageActive}>
                {text}
            </Text>
        </>
    )
};

export default ToyMessage;
