import { isEmpty, filter, find, isUndefined } from "lodash";
import { SourceSet, TargetSet } from "./ChatService/types";
import {
    PlayInfo,
    CameraConfig,
    H5LivePlayerFactory,
    HlsJsPlayerFactory,
    HlsPlayerFactory,
    JpegPlayerFactory,
    Player,
    CameraPosition,
    PlayerHandler
} from 'vchat-player';

import config from "../static/config";
import loggers from "../loggers";
import { IQualityBoundaries } from '../redux/store';

const PlayModes = {
    PROMISE_PLAY_MUTED: 'mpp',
    AUTO_PLAY_MUTED: 'map'
};

interface IInputSelection {
    selectedVideoDevice: MediaDeviceInfo;
    selectedAudioDevice?: MediaDeviceInfo;
}

interface IListeners {
    cameraOnListener: (instanceId: string) => any,
    cameraOffListener: (instanceId: string) => any | null,
    cameraDeniedListener: (instanceId: string) => any,
    playErrorListener: (instanceId: string, level: number, error?: any) => any,
    playStartListener: (instanceId: string) => any,
    playStopListener: (instanceId: string) => any,
    onSendMetricsListener: (instanceId: string, info: any) => any,
    onPlayInfoListener: (instanceId: string, info: PlayInfo) => any,
    onVolumeChangeListener: (instanceId: string, volume: number) => any,
    onInitiallyMutedListener: (instanceId: string, isMuted: boolean) => any,
    onSelectingCameraListener: (instanceId: string, cameras: MediaDeviceInfo[]) => any
}

class PlayerService {
    private static services: PlayerService[] = [];
    private static listeners: Partial<IListeners> = {};

    constructor(instanceId: string) {
        this.instanceId = instanceId;
    }

    private instanceId: string;

    private player: Player | null;
    private reloadCallback;
    private playerContainer: HTMLElement | null;
    private cameraSelectionResolve : (selection: IInputSelection) => void | null;
    private cameraConfig: CameraConfig | undefined | null;
    private isCameraStarted: boolean;

    static getService(instanceId: string) {
        let playerService = find(PlayerService.services, service => service.getInstanceId() === instanceId);

        if (!playerService) {
            playerService = new PlayerService(instanceId);
            PlayerService.services.push(playerService);
        }

        return playerService;
    }

    static destroyService(instanceId: string) {
        const playerService = find(PlayerService.services, service => service.getInstanceId() === instanceId);

        if (playerService) {
            playerService.closePlayer();
            PlayerService.services =
                filter(PlayerService.services, service => service.getInstanceId() !== instanceId);
        }
    }

    static registerCameraOnListener(listener: (instanceId: string) => any) {
        PlayerService.listeners.cameraOnListener = listener;
    };

    static registerCameraOffListener(listener: (instanceId: string) => any) {
        PlayerService.listeners.cameraOffListener = listener;
    };

    static registerCameraDeniedListener(listener: (instanceId: string) => any) {
        PlayerService.listeners.cameraDeniedListener = listener;
    };

    static registerPlayerInfoListener = (listener: (instanceId: string, info: PlayInfo) => any) => {
        PlayerService.listeners.onPlayInfoListener = listener;
    };

    static registerSendMetricsListener = (listener: (instanceId: string, info) => any) => {
        PlayerService.listeners.onSendMetricsListener = listener;
    };

    static registerPlayErrorListener(listener: (instanceId: string, level: number, error?: any) => any) {
        PlayerService.listeners.playErrorListener = listener;
    };

    static registerPlayStartListener(listener: (instanceId: string) => any) {
        PlayerService.listeners.playStartListener = listener;
    };

    static registerPlayStopListener(listener: (instanceId: string) => any) {
        PlayerService.listeners.playStopListener = listener;
    };

    static registerVolumeChangeListener(listener: (instanceId: string, volume: number) => any) {
        PlayerService.listeners.onVolumeChangeListener = listener;
    };

    static registerInitiallyMutedListener(listener: (instanceId: string, isMuted: boolean) => any) {
        PlayerService.listeners.onInitiallyMutedListener = listener;
    };

    static registerSelectingCameraListener(listener: (instanceId: string,cameras: MediaDeviceInfo[]) => any) {
        PlayerService.listeners.onSelectingCameraListener = listener;
    };

    private getPlayerHandler: () => PlayerHandler = () => {
        const { instanceId } = this;

        return {
            onVolumeChange: (value: number) => {
                loggers.player.debug('onVolumeChange ', { details: { value }});

                if (isUndefined(value)) {
					loggers.player.debug('Volume changed to undefined; probably a bug in vchat-player. Ignore it');
					return;
				}

				if (PlayerService.listeners.onVolumeChangeListener) {
                    PlayerService.listeners.onVolumeChangeListener(instanceId, value);
                }
            },

            onPlayerSwitch: (format: any) => {
                loggers.player.debug('onPlayerSwitch ', { details: { format }});
            },

            onPlayerSwitchComplete: (format: any) => {
                loggers.player.debug('onPlayerSwitchComplete ', { details: { format }});
            },

            onError: (error: string | object) => {
                loggers.player.debug('onError ', { details: { error }});
            },

            onSendMetrics: (info: any) => {
                loggers.player.debug('onSendMetrics ', { details: { info }});
                if (isEmpty(info)) {
                    return;
                }

                if (PlayerService.listeners.onSendMetricsListener) {
                    PlayerService.listeners.onSendMetricsListener(instanceId, info);
                }

                const { playMode } = info;
                const { AUTO_PLAY_MUTED, PROMISE_PLAY_MUTED } = PlayModes;

                if (playMode && PlayerService.listeners.onInitiallyMutedListener) {
                    PlayerService.listeners
                        .onInitiallyMutedListener(instanceId, playMode === AUTO_PLAY_MUTED || playMode === PROMISE_PLAY_MUTED);
                }
            },

            onPlayStart: (stream: string) => {
                loggers.player.debug('onPlayStart');
                if (PlayerService.listeners.playStartListener) {
                    PlayerService.listeners.playStartListener(instanceId);
                }
            },

            onPlayStop: () => {
                loggers.player.debug('onPlayStop');
                if (PlayerService.listeners.playStopListener) {
                    PlayerService.listeners.playStopListener(instanceId);
                }
            },

            onPlayInfo: (info: PlayInfo) => {
                loggers.player.debug('onPlayInfo ', { details: { info }});
                if (isEmpty(info)) {
                    return;
                }

                if (PlayerService.listeners.onPlayInfoListener) {
                    PlayerService.listeners.onPlayInfoListener(instanceId, info);
                }
            },

            onPlayError: (level: number, error?: any) => {
                loggers.player.debug('onPlayError ', {details: { level, error }});
                if (PlayerService.listeners.playErrorListener) {
                    PlayerService.listeners.playErrorListener(instanceId, level, error);
                }
            },

            onCameraOn: () => {
                this.isCameraStarted = true;
                loggers.player.debug('onCameraOn');
                if (PlayerService.listeners.cameraOnListener) {
                    PlayerService.listeners.cameraOnListener(instanceId);
                }
            },

            onCameraOff: () => {
                this.isCameraStarted = false;
                loggers.player.debug('onCameraOff');
                if (PlayerService.listeners.cameraOffListener) {
                    PlayerService.listeners.cameraOffListener(instanceId);
                }
            },

            onCameraDenied: () => {
                loggers.player.debug('onCameraDenied');
                if (PlayerService.listeners.cameraDeniedListener) {
                    PlayerService.listeners.cameraDeniedListener(instanceId);
                }
            },

            onPublishStart: () => {
                loggers.player.debug('onPublishStart');
            },

            onPublishStop: () => {
                loggers.player.debug('onPublishStop');
            },

            onSelectInput: (videoInputs: MediaDeviceInfo[]): Promise<IInputSelection> => {
                loggers.player.debug('onSelectInput', { details: { videoInputs }});

                if (PlayerService.listeners.onSelectingCameraListener) {
                    PlayerService.listeners.onSelectingCameraListener(instanceId, videoInputs);
                }

                return new Promise<IInputSelection>((resolve) => {
                    this.cameraSelectionResolve = resolve;
                });
            }
        };
    };

    getInstanceId = () => this.instanceId;

    createPlayer = (
		container: HTMLElement,
		qualityBoundaries: IQualityBoundaries,
		initialVolume?: number
	): Player => {
        const factories = [
            new H5LivePlayerFactory(),
            new HlsJsPlayerFactory({
                waitingTimeout: config.ChatSettings.BAD_CONN_TIMEOUT_WAITING,
                qualityBoundaries
            }),
            new HlsPlayerFactory({
                waitingTimeout: config.ChatSettings.BAD_CONN_TIMEOUT_WAITING,
                qualityBoundaries,
                reloadCallback: (reloadCallback) => {
                    this.reloadCallback = reloadCallback;
                }
            }),
            new JpegPlayerFactory({})
        ];

        // we create it first time or destroy the previous and recreate it
        if (container !== this.playerContainer || !this.player) {
            if (this.player) {
                this.closePlayer();
            }

            this.playerContainer = container;

            this.player = new Player({
                container,
                factories,
                handler: this.getPlayerHandler()
            });

            loggers.player.debug('player created');

			// Parent platform can configure an initial volume for the stream
			if (this.player && !isUndefined(initialVolume)) {
				this.player.volume = initialVolume;
			}
        } else {
            loggers.player.debug('using an existing player instance');
		}


        return this.player;
    };

    getPlayerContainer = (): HTMLElement | null => this.playerContainer;

    /**
     * Reload previously created player
     */
    reloadPlayer = (): void => {
        if (this.reloadCallback) {
			loggers.player.debug('reload player');
			this.reloadCallback();
		}
    };

    mutePlayer = (): void => {
		loggers.player.debug('Muting player by setting its volume to 0');
    	this.setVolume(0);
	};

	/**
	 * Set volume level for the stream
	 * @param volume - number between 0 and 1 (inclusive), where 0 means muted and 1 means fully loud
	 */
	setVolume = (volume: number): void => {
    	if (this.player) {
			loggers.player.debug('Player volume set to ', { details: { volume }});
    		this.player.volume = volume;
		}
	};

    /**
     * Play the provided stream to the previously created player
     * @param sources
     */
    play = (sources: SourceSet): void => {
        if (this.player && sources) {
            try {
                this.player.play(sources);
                loggers.player.debug('player started');
            } catch(error) {
                loggers.player.debug(
                    'Trying to play the stream raised this error: ',
                    { details: { error }});
            }
        }
    };

    /**
     * Start own camera, but don't publish it yet to the upstream
     * @param configuration
     */
    startCamera = (configuration?: CameraConfig): void => {
        if (this.isCameraStarted) {
            loggers.player.debug('camera is started already');
            return;
        }

        const { instanceId } = this;

        if (this.player) {
            this.player.hasCamera().then(
                (hasCamera) => {
                    if (this.player && hasCamera) {
                        this.player.startCamera(configuration)
                            .then(() => {
                                this.cameraConfig = configuration;
                                loggers.player.debug('camera started');
                            })
                            .catch(() => {
                                this.isCameraStarted = false;
                                loggers.player
                                    .debug('camera not started');
                                if (PlayerService.listeners.cameraDeniedListener) {
                                    PlayerService.listeners.cameraDeniedListener(instanceId);
                                }
                            });
                    } else {
                        this.isCameraStarted = false;
                        loggers.player
                            .debug('no camera available');
                        if (PlayerService.listeners.cameraDeniedListener) {
                            PlayerService.listeners.cameraDeniedListener(instanceId);
                        }
                    }
            });
        }
    };

    /**
     * Update aspect of the cam2cam video area
     * @param configuration
     * Note: close button won't be affected
     */
    updateCamera = (configuration: CameraConfig): void => {
        if (this.player && this.cameraConfig) {
            const newConfig: CameraConfig = {
                ...this.cameraConfig,
                ...configuration
            };

            this.player.updateCamera(newConfig);

            loggers.player.debug(
                'camera updated with the following config:',
                { details: { configuration }}
            );
        }
    };

    /**
     * publish own camera on the provided stream
     */
    publishCamera = (targets: TargetSet): void => {
        if (this.player) {
            let published = false;
            try {
                published = this.player.publish(targets);
            } catch (e) {}

            loggers.player.debug(published ? 'camera published' : 'camera not published');
        }
    };

    /**
     * Select a certain camera device from the available ones
     * @param camera
     */
    selectCamera = (camera: MediaDeviceInfo): void => {
        if (this.cameraSelectionResolve) {
            loggers.player.debug('Resolving camera selection with ', { details: { camera }});
            this.cameraSelectionResolve({
                selectedVideoDevice: camera
            });
        }
    };

    /**
     * Stop cam2cam on the previously created player
     */
    stopCamera = () => {
        if (this.player) {
            this.player.unpublish();
            this.player.stopCamera();
            this.cameraConfig = null;
            this.isCameraStarted = false;
            loggers.player.debug('camera stopped');
        }
    }

    closePlayer = (): void => {
        if (this.player) {
            this.stopCamera();
            this.player.destroy();
            this.playerContainer = null;
            this.player = null;
            loggers.player.debug('player closed');
        }
    };

    stopPlayer = (): void => {
        if (this.player) {
            this.stopCamera();
            this.player.stop();
            loggers.player.debug('player stopped');
        }
    };
}

export default PlayerService;

// re-export vchat-player things and use them from here, to avoid testing problems
export { CameraConfig, CameraPosition, Player, PlayInfo };

