import { map } from 'lodash';
import * as React from 'react';
import { ICurrency, ITranslation } from '../../../redux/store';
import utils from '../../../utils';
import Tooltip from '../../common/Tooltip';
import {
	BundleName,
	CloseButton,
	ContainsText,
	Content,
	ContentWrapper,
	Footer,
	Image,
	ImageWrapper,
	PurchaseButton,
	PurchaseGiftItem,
	PurchaseGiftsList,
	TextWrapper,
	GiftBundleInformation
} from './style';

export interface IGiftItemForPurchaseList {
	name: string;
	quantity: number;
}

interface IProps {
	imageUrl: string;
	name: string;
	price: number;
	currency: ICurrency;
	gifts: IGiftItemForPurchaseList[];
	translation: ITranslation;

	onClose: () => void;
	onBundlePurchase: () => void;
}

const PurchaseBundlePage = (props: IProps) => {
	const { contains, bundleInfo, bundleBuy, close } = props.translation.common;

	const purchaseButtonText = utils.translationWithValues(bundleBuy, {
		amount: `${props.price} ${props.currency.symbol}`
	});

	const purchaseGifts = map(props.gifts, (gift) => (
		<PurchaseGiftItem key={gift.name}>
			{gift.quantity} x {gift.name}
		</PurchaseGiftItem>
	));

	return (
		<>
			<Tooltip id={"close-chat"} text={close}>
				<CloseButton
					className={"-icon-close-full"}
					onClick={props.onClose}
				/>
			</Tooltip>

			<ContentWrapper>
				<Content>
					<ImageWrapper>
						<Image src={props.imageUrl} />
					</ImageWrapper>

					<GiftBundleInformation>
						<TextWrapper>
							<BundleName>{props.name}</BundleName>
							<ContainsText>{contains}</ContainsText>

							<PurchaseGiftsList>
								{purchaseGifts}
							</PurchaseGiftsList>
						</TextWrapper>

						<PurchaseButton onClick={props.onBundlePurchase}>
							{purchaseButtonText}
						</PurchaseButton>
					</GiftBundleInformation>
				</Content>
			</ContentWrapper>

			<Footer>
				* {bundleInfo}
			</Footer>
		</>
	);
};

export default PurchaseBundlePage;
