import styled from "styled-components";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

interface IGiftStyle {
    url: string | undefined;
    hasEnlargedContent?: boolean;
}

export const MessageGiftWrapper = withCustomClasses<IGiftStyle>(styled.span<IGiftStyle>`
    background-image: url(${(props: IGiftStyle) => props.url});
    width: ${({ hasEnlargedContent }) => hasEnlargedContent ? '56' : '24'}px;
    height: ${({ hasEnlargedContent }) => hasEnlargedContent ? '56' : '24'}px;
    
    display: inline-block;
    background-size: 100%;
    background-repeat: no-repeat;
    vertical-align: middle;
`, [DomCustomClass.MessageGift]);
