import * as React from 'react';

import Favorite from './Favorite';
import { onClick } from './utils';
import selectors from '../../../redux/selectors';
import IStore from '../../../redux/store';
import { requestSetFavorite } from '../../../sagas/websockets/channelInfo';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps } from "./types";

const FavoriteContainer = (props: IProps) => {
    const className = props.isModelFavorite
        ? "-icon-heart-full"
        : "-icon-heart-line";

    const { addFavorite, removeFavorite } = props.translation.user;
    const title = props.isModelFavorite ? removeFavorite : addFavorite;

    return (
        <Favorite
            className={className}
            title={title}
            onClick={onClick(props)}
        />
    );
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isModelFavorite: selectors.isModelFavorite(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

const mapDispatchToProps = {
    requestSetFavorite
};

export default withRedux(FavoriteContainer, mapStateToProps, mapDispatchToProps);
