import styled from "styled-components";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";
import withIdentifier, {DomIdentifier} from "../../hoc/withIdentifier";

interface ITooltipContentWrapper {
    isResponsive: boolean;
}

interface ITextTitle {
	isResponsive: boolean;
}

export const Wrapper = withCustomClasses(styled.div`
    color: ${props => props.theme.colors.toySection.hoverTooltipText};
    display: inline-block;
    position: relative;
`, [DomCustomClass.ToyOptionWithTooltip]);

export const Title = styled.h3<ITextTitle>`
    color: ${props => props.theme.colors.toySection.primaryColor};
    ${props => !props.isResponsive && 'font-size: 14px;'}
    margin-block-start: auto;
    margin-block-end: auto;
    margin-bottom: 10px;
`;

export const Text = styled.div<ITextTitle>`
    font-size: ${props => !props.isResponsive ? '13' : '12'}px;
`;

export const Inline = styled.div`
    display: inline-block;
`;

export const TooltipContentWrapper = withIdentifier<ITooltipContentWrapper>(styled.div<ITooltipContentWrapper>`
    width: ${props => props.isResponsive ? '240' : '280'}px;
    
    display: flex;
    flex-direction: row;
`, DomIdentifier.TooltipContentWrapper);
