import React from 'react';
import { components, Props as ReactSelectProps } from 'react-select';

import { CustomMenu, Header, ModelName } from './style';
import CloseButtonContainer from '../../common/CloseButton';
import { CloseButtonType } from '../../common/CloseButton/CloseButtonContainer';
import { ITranslation } from '../../../redux/store';
import { translationWithValues } from "../../../utils/utils";

interface IComponentProps {
	shouldDisplayCustomMentionSection: boolean;
	participantsNumber: number;
	modelName: string;
	translation: ITranslation;

	onModelClick: () => null;
	onCloseMenu: () => null;
}

type IProps = IComponentProps & ReactSelectProps;

const MentionMenu = (props: IProps) => (
	props.shouldDisplayCustomMentionSection
		? <CustomMenu>
			<Header>
				{props.participantsNumber === 1
					? translationWithValues(props.translation.chat.groupChat.mentionHeaderPartOneSingle, {value: props.participantsNumber})
					: translationWithValues(props.translation.chat.groupChat.mentionHeaderPartOne, {value: props.participantsNumber})
				}
					<ModelName onClick={props.onModelClick}>
						{` ${props.modelName}`}
					</ModelName>
				{props.translation.chat.groupChat.mentionHeaderPartTwo}
				<CloseButtonContainer
					onClick={props.onCloseMenu}
					closeButtonType={CloseButtonType.GROUP_CHAT_HEADER}
				/>
			</Header>
			<components.Menu {...props} />
		</CustomMenu>
    	: <components.Menu {...props} />
);

export default MentionMenu;
