import moment from 'moment';
import React, { Component } from 'react';

import utils from '../../../utils';
import selectors from '../../../redux/selectors';
import MessageTypeHeader from './MessageTypeHeader';
import IStore, { IMessage, ITranslation } from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    modelName: string;
    lastMessage?: IMessage;
    translation: ITranslation;
}

type IProps = IInstanceProps & IStoreProps;

class MessageTypeHeaderContainer extends Component<IProps> {
    render () {
        let subtitle: string | undefined;
        const { header } = this.props.translation.common;

        if (this.props.lastMessage) {
            const { dateCreated } = this.props.lastMessage;
            const lastMessageDate =  moment(dateCreated).toDate();
            const lastMessageFormattedDate = moment(lastMessageDate, "x").format("DD/MM/YYYY, hh:mm a");
    
            subtitle = utils.translationWithValues(header.lastMessageOn,
                { value: lastMessageFormattedDate }    
            );
        }


        return (
            <MessageTypeHeader
                title={header.messageFrom}
                modelName={this.props.modelName}
                subtitle={subtitle}
            />
        );
    }
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    modelName: selectors.getModelName(store, instanceId),
    lastMessage: selectors.getLastMessage(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

export default withRedux(MessageTypeHeaderContainer, mapStateToProps);
