import * as React from 'react';
import { isNull, isUndefined } from 'lodash';
import { TimerWrapper, ITimerStylingProps } from "./style";
import { defaultTimerStylingProps, getLeftAngle, getRightAngle } from "./utils";
import {ThemedStyledProps, withTheme} from "styled-components";
import ITheme from "../../../themes/interfaces";

export interface IProps {
    /** total time in seconds figured on the timer */
    totalTime: number;

    /** how much time (in seconds) is figured as already being passed on the timer */
    currentTime?: number

    timeFormatter?: (remainingTime: number) => any;
    resets?: number;

    onTimeIsOver?: () => any;
    onTick?: (remainingTime: number) => any;
    onUnmount?: (remainingTime: number) => any;

    styling?: ITimerStylingProps;
    theme: ITheme;
}

export interface IState {
    timeLeft: number;
}

/**
 * Stateful component to display a stopwatch like timer
 */
class Timer extends React.Component<ThemedStyledProps<IProps, ITheme>, IState> {
    constructor(props) {
        super(props);

        this.state = {
            timeLeft: this.timeLeftFromProps(props)
        };
    }

    private handler: any;

    componentDidMount() {
        this.setTickHandler();
    }

    componentDidUpdate(prevProps: IProps) {
        if (prevProps.currentTime !== this.props.currentTime ||
            prevProps.resets !== this.props.resets) {
            this.setState({ timeLeft: this.timeLeftFromProps(this.props) });
            this.setTickHandler();
        }
    }

    componentWillUnmount() {
        this.clearTickHandler();

        if (this.props.onUnmount) {
            this.props.onUnmount(this.state.timeLeft);
        }
    }

    render() {

        const currentTime = this.props.totalTime - this.state.timeLeft;
        const leftAngle = getLeftAngle(this.props.totalTime, currentTime);
        const rightAngle = getRightAngle(this.props.totalTime, currentTime);

        return (
            <TimerWrapper
                leftAngle={leftAngle}
                rightAngle={rightAngle}
                {...defaultTimerStylingProps(this.props.theme, this.props.styling)}
            >
                <div className='outer-shadow' />
                <div className='inner-shadow' />

                <div className='hold left'>
                    <div className='fill' />
                </div>
                <div className='hold right'>
                    <div className='fill' />
                </div>
                <div className="counter">
                    { this.props.timeFormatter
                        ? this.props.timeFormatter(this.state.timeLeft)
                        : this.state.timeLeft
                    }
                </div>
            </TimerWrapper>
        );
    }

    private setTickHandler = () => {
        this.clearTickHandler();
        this.handler = setInterval(() => {
            this.updateCurrentTime(() => {
                if (this.state.timeLeft <= 0) {
                    this.clearTickHandler();

                    if (this.props.onTimeIsOver) {
                        this.props.onTimeIsOver()
                    }
                }

                if (this.props.onTick) {
                    this.props.onTick(this.state.timeLeft);
                }
            })
        }, 1000);
    };

    private clearTickHandler = () => {
        if (this.handler) {
            clearInterval(this.handler);
        }
    };

    private updateCurrentTime = (callback: () => any) => {
        this.setState((prevState) => ({
            timeLeft: Math.max(0, prevState.timeLeft - 1)
        }), callback);
    };

    private timeLeftFromProps = (props: IProps) =>
        Math.round(
            !isNull(props.currentTime) && !isUndefined(props.currentTime)
                ? Math.max(0, props.totalTime - props.currentTime)
                : props.totalTime
        );
}

export default withTheme<IProps, ITheme>(Timer);
