import styled from "styled-components";
import MessageSectionPopupWrapper from '../../common/MessageSectionPopupWrapper';

interface IUserPromptContentProps {
    isResponsive?: boolean
    isMobileLandscape?: boolean;
    isGiftSectionOpen?: boolean;
    isTipPromptOpen?: boolean;
    isAnyUserPromptOpen: boolean;
}

export const UserPromptWrapper = styled(MessageSectionPopupWrapper).attrs({ className: `userPrompt` })<IUserPromptContentProps>`
    ${props => props.isResponsive && `
        background-color: ${props.theme.colors.windowBackground};
        
        display: flex;
        height: ${props.isAnyUserPromptOpen ? '100%' : '0'};
        width: 100%;
        position: absolute;
        bottom: 0;
        z-index: 4;
    `}
   
    ${props => props.isMobileLandscape && `
        position: relative;
        width: 100%;
    `}

    ${props => props.isGiftSectionOpen && !props.isResponsive && props.isTipPromptOpen && `
        position: absolute;
        bottom: 0;
        z-index: 5;
        width: 100%;
        height: 100%;
    `}
`;

export const UserPromptContent = styled.div`
    ${props => props.theme && `
        color:  ${props.theme.colors.messageEditorTextColor};
        font-family: ${props.theme.typography.fontFamily};
        font-weight: ${props.theme.typography.weight.regular};
    `}

    & .additionalText {
        text-align: center;
        margin: 0 5px;
        font-size: 14px;
    }

    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
`;

export const UserPromptTitle = styled.div`
    & .timeAnimatedCircle {
        margin-right: 10px;
    }

    display: flex;
    margin-right: 20px;
`;
