import { takeEvery } from "redux-saga/effects";
import { SET_PREFERENCE } from "../../redux/modules/user";
import { Action } from "redux-actions";
import { IPayload } from "../../redux";
import { IPreferenceActionPayload } from "../../redux/interfaces";
import AppService from "../../services/AppService";

export const setPreferenceSaga = function* ({ payload: { instanceId, value } }: Action<IPayload<IPreferenceActionPayload>>) {
	const { preferenceName, value: preferenceValue} = value;
	const appService = AppService.getService(instanceId);

	appService.preferenceStore.setPreference(
		preferenceName,
		preferenceValue,
		instanceId
	);
};

const watchSagas = function* () {
	yield takeEvery(SET_PREFERENCE, setPreferenceSaga);
};

export default watchSagas;
