import utils, { screenfull } from '../../../utils';
import PlayerService from '../../../services/PlayerService';
import ChatService from '../../../services/ChatService';
import { IContainerProps as IProps } from "./types";

export const toggleFullscreen = (props: IProps) => () => {
	screenfull.toggle(utils.getMessengerNode(props.instanceId));
};

export const toggleMobileMenu = (props: IProps) => () => {
	props.setIsMobileMenuOpen(props.instanceId, !props.isMobileMenuOpen);
};

export const hideInitialMutedHint = (props: IProps) => () => {
	props.hideInitiallyMutedHint(props.instanceId);
};

export const reloadPlayer = (props: IProps) => () => {
	PlayerService.getService(props.instanceId).reloadPlayer();
};

export const toggleSound = (props: IProps) => () => {
	const chatService = ChatService.getService(props.instanceId);
	const playerService = PlayerService.getService(props.instanceId);

	if (props.isMutedByClient) {
		playerService.setVolume(props.notMutedClientVolume);
		chatService.sendAudioState(true);
	} else {
		playerService.mutePlayer();
		chatService.sendAudioState(false);
	}

	props.setIsInitiallyMuted(props.instanceId, false);
};

export const setVolume = (props: IProps) => (volume: number) => {
	const chatService = ChatService.getService(props.instanceId);
	const playerService = PlayerService.getService(props.instanceId);

	if (volume === 0) {
		playerService.mutePlayer();
		chatService.sendAudioState(false);
	} else {
		playerService.setVolume(volume);
		chatService.sendAudioState(true);
	}
};
