import styled from 'styled-components';
import {ICorrections} from "./interfaces";
import {IDimensions} from "../AdvancedTooltip/interfaces";
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

type IContentWrapperProps = IDimensions & ICorrections;

interface IHideableComponentProps {
	isHidden?: boolean;
}

type IArrowProps = IDimensions & ICorrections & IHideableComponentProps;

type IContentProps = IHideableComponentProps;

export const ContentWrapper = withCustomClasses<IContentWrapperProps>(styled.div<IContentWrapperProps>`
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-weight: ${(props) => props.theme.typography.weight.regular};
    
    position: absolute;
    border-radius: 3px;
    z-index: 4;
`, [DomCustomClass.AdvancedTooltipContent]);

export const Content = styled.div<IContentProps>`
    background-color: ${(props) => props.theme.colors.windowBackground};
    
    ${({ isHidden }) => isHidden
		? `
			position: fixed;
			opacity: 0;
		`
		: 'position: relative;'
	}

    padding: 15px;
`;

export const Arrow = styled.div<IArrowProps>`
	${({ isHidden }) => isHidden && 'opacity: 0;'}
    background-color: ${(props) => props.theme.colors.windowBackground};
    
    width: 20px;
    height: 20px;
    transform: rotate(45deg);
`;

export const TopContentWrapper = styled(ContentWrapper)`
    left: ${(props) => `calc(50% - ${props.width}px / 2 + ${props.horizontal}px)`};
    bottom: calc(100% + 5px);
`;

export const BottomContentWrapper = styled(ContentWrapper)`
    left: ${(props) => `calc(50% - ${props.width}px / 2 + ${props.horizontal}px)`};
    top: 27px;
`;

export const LeftContentWrapper = styled(ContentWrapper)`
    top: ${(props) => `calc(50% - ${props.height}px / 2 + ${props.vertical}px)`};
    right: calc(100% + 15px);
    
`;

export const RightContentWrapper = styled(ContentWrapper)`
    bottom: ${(props) => `calc(50% - ${props.height}px / 2 - ${props.vertical}px)`};
    left: calc(100% + 15px);
`;

export const DownArrow = styled(Arrow)`
    margin-left: ${(props) => `calc(${props.width}px / 2 - ${props.horizontal}px - 10px)`};
    
    margin-top: -10px;
`;

export const UpArrow = styled(Arrow)`
    margin-left: ${(props) => `calc(${props.width}px / 2 - ${props.horizontal}px - 10px)`};
    
    margin-bottom: -10px;
`;

export const LeftArrow = styled(Arrow)`
    top: ${(props) => `calc(${props.height}px / 2 - ${props.vertical}px - 10px)`};
    
    position: absolute;
    left: -10px;
    min-width: 20px;
`;

export const RightArrow = styled(Arrow)`
    top: ${(props) => `calc(${props.height}px / 2 - ${props.vertical}px - 10px)`};
    
    position: absolute;
    right: -10px;
    min-width: 20px;
`;
