import * as React from 'react';

import utils from '../../../utils';
import { LinkElement } from './style';
import selectors from '../../../redux/selectors';
import AppService from "../../../services/AppService";
import DialogButton from "../DialogButton/DialogButton";
import { DialogButtonColor } from "../DialogButton/style";
import { rechargeExternal } from "../../../sagas/common/recharge";
import { closeMessenger } from "../../../redux/modules/messenger";
import PreChatInformationDialog from './PreChatInformationDialog';
import { resetPreChatInformation } from '../../../redux/modules/videochat';
import IStore, {
    AnswerType,
    KnownAnswerType,
    PreChatInformationType,
    QuestionType
} from '../../../redux/store';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import ActionSectionContainer from "../../specific/ActionSection/ActionSectionContainer";
import { onAnswerToSpyingPrivateChat, onChargeNowClick, onCloseClick, onVerifyAgeClick, onVipClick } from './utils';
import {acceptToSpyFromPrechatDialog, closePrechatDialog } from "../../../sagas/common/ui";
import SelectionButtons, { SelectedButtonColor } from "../SelectionButtons";
import { IContainerProps as IProps } from "./types";

interface IPreChatInformationDialog {
    text: string;
    closeButtonText?: string;
    hasCloseButton: boolean;
    buttons?: JSX.Element;
    otherContent?: JSX.Element;
    onCloseClick?: () => void;
}

const PreChatInformationDialogContainer = (props: IProps) => {
    /**
     * Create the mapping between the information type and the information object.
     * This mapping is implemented in render for having a clean access to translations.
     */
    const getInformations = () => {
        const modelname = props.modelName;

        const {
            user: { becomeVIP },
            common: { close, yes, no, perMinute },
            chat: {
                verifyAge,
                livePreview,
                vipLivePreview,
                btnChargeNow,
                popup,
                voyeurToSingleInfo
            }
        } = props.translation;

        const livePreviewType = props.isVip ? vipLivePreview : livePreview;

        const yesNoOptions = [{
            label: yes,
            value: KnownAnswerType.YES,
            selected: true
        }, {
            label: no,
            value: KnownAnswerType.NO
        }];

        const privateChatCanSpyText = utils.translationWithValues(voyeurToSingleInfo, { modelname }) +
            ` (${utils.translationWithValues(perMinute.toLowerCase(), { price: `${props.voyeurPrice} ${props.currency.symbol}`})})`;

        const informations: { [informationType: string]: IPreChatInformationDialog } = {};
        informations[PreChatInformationType.LIMIT_REACHED] = {
            text: utils.translationWithValues(popup.previewLimit, { live_preview_type: livePreviewType }),
            hasCloseButton: false,
            buttons: <ActionSectionContainer isForInformationDialog={true}/>
        };

        informations[PreChatInformationType.PRIVATE_CHAT] = {
            text: utils.translationWithValues(popup.privateCam, { modelname }),
            hasCloseButton: false
        };

        informations[PreChatInformationType.PRIVATE_CHAT_CAN_SPY] = {
            text: privateChatCanSpyText,
            hasCloseButton: false,
            buttons: (
                <SelectionButtons<AnswerType>
                    key={QuestionType.SPY_PRIVATE_CHAT}
                    options={yesNoOptions}
                    selectedButtonColor={SelectedButtonColor.DEFAULT}
                    onSelect={onAnswerToSpyingPrivateChat(props)}
                />
            )
        };

        informations[PreChatInformationType.VERIFY_AGE] = {
            text: utils.translationWithValues(popup.avs, { modelname }),
            hasCloseButton: false,
            buttons: (<DialogButton
                text={verifyAge}
                onClick={onVerifyAgeClick(props)}
                color={DialogButtonColor.GREEN}
            />)
        };

        const hasVIPFeature = utils.hasHookConfigured(AppService.getService(props.instanceId).onBecomeVIP);

        informations[PreChatInformationType.CHARGE_ACCOUNT_OR_VIP] = {
            text: hasVIPFeature ? popup.vip : popup.recharge,
            hasCloseButton: false,
            buttons: (
                <DialogButton
                    text={btnChargeNow}
                    onClick={onChargeNowClick(props)}
                    color={DialogButtonColor.BLUE}
                />
            )
        };

        if (hasVIPFeature) {
            informations[PreChatInformationType.CHARGE_ACCOUNT_OR_VIP].otherContent = (
                <LinkElement onClick={onVipClick(props)}>
                    {becomeVIP}
                </LinkElement>
            );
        }

        informations[PreChatInformationType.NOT_AVAILABLE] = {
            text: utils.translationWithValues(popup.previewDisabled,
                { modelname, live_review_type: livePreviewType }),
            hasCloseButton: false,
	        buttons: <ActionSectionContainer isForInformationDialog={true} />
        };

        informations[PreChatInformationType.HOST_OFFLINE] = {
            text: utils.translationWithValues(popup.offline1, { modelname }),
            closeButtonText: close,
            hasCloseButton: true
        };

        informations[PreChatInformationType.GUEST_OFFLINE] = {
            text: popup.offline2,
            closeButtonText: close,
            hasCloseButton: true
        };

        informations[PreChatInformationType.CONNECTION_ERROR] = {
            text: popup.videoConnection,
            closeButtonText: close,
            hasCloseButton: true
        };

        return informations;
    };

    const informationDialogs = getInformations();
    const informationDialog = informationDialogs[props.informationType];

    return (
        <>
            {informationDialog && props.shouldRenderPreChatInformationDialog &&
                <PreChatInformationDialog
                    {...informationDialog}
                    onCloseClick={onCloseClick(props)}
                    hasModelProfilePicture={props.hasModelProfilePicture}
                />
            }
        </>
    );
};

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    isVip: selectors.isVip(store, instanceId),
    modelName: selectors.getModelName(store, instanceId),
    translation: selectors.getTranslation(store, instanceId),
    informationType: selectors.getPreChatInformationType(store, instanceId),
    shouldRenderPreChatInformationDialog: selectors.shouldRenderPreChatInformationDialog(store, instanceId),
    hasModelProfilePicture: selectors.hasModelProfilePicture(store, instanceId),
    voyeurPrice: selectors.getVoyeurCamPrice(store, instanceId),
    currency: selectors.getCurrency(store, instanceId)

});

const mapDispatchToProps = {
    resetPreChatInformation,
    rechargeExternal,
    closeMessenger,
    closePrechatDialog,
    acceptToSpy: acceptToSpyFromPrechatDialog
};

export default withRedux(PreChatInformationDialogContainer, mapStateToProps, mapDispatchToProps);
