import { all } from 'redux-saga/effects';

import commonSagas from './common/rootSaga';
import specificSagas from './specific/rootSaga';
import websocketSagas from './websockets/rootSaga';

/**
 * Combines all the listening sagas and
 * it represents the entire middleware.
 */
export default function* rootSaga() {
    yield all([
        websocketSagas(),
        commonSagas(),
		specificSagas()
    ]);
}
