import { Action } from 'redux-actions';
import { isEmpty, map, find, includes } from 'lodash';
import { put, select, takeEvery } from 'redux-saga/effects';
import { ACTION, CMDP_GETHISTORY, CMDP_GETHISTORY_RESPONSE, EnumSortOrder } from 'vxcontrol-client-lib';

import utils from '../../utils';
import config from '../../static/config';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import ApiService from '../../services/ApiService';
import { insertAllMessages } from '../../redux/modules/messenger';
import { IMessage, IMessenger, ISession, IToyOption, ITranslation } from '../../redux/store';
import { ActionIds } from '../../redux/interfaces';

const createHistoryActionType = createActionType(ActionIds.HISTORY, { isSagaAction: true });

// Actions
const REQUEST_HISTORY = createHistoryActionType("REQUEST_HISTORY");
const RECEIVE_HISTORY = createHistoryActionType("RECEIVE_HISTORY");
const INSERT_MESSAGES_TO_STORE = createHistoryActionType("INSERT_MESSAGES_TO_STORE");

// Action Creators
export const requestHistory = createAction(REQUEST_HISTORY);
export const onReceiveHistory = createAction<CMDP_GETHISTORY_RESPONSE>(RECEIVE_HISTORY);
export const insertMessagesToStore = createAction<IMessage[]>(INSERT_MESSAGES_TO_STORE);

/** Request the list of history messages from the current channel */
export const requestHistorySaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const shouldShowHistory = yield select(selectors.shouldShowHistory, instanceId);
    if (!shouldShowHistory) {
        return;
    }

    const session: ISession = yield select(selectors.getSession, instanceId);
    const { channelId, sessionID } = session;

    const messages: IMessage[] = yield select(selectors.getMessages, instanceId);
    const numberOfMessages = messages.length + config.limitations.maxNumberOfMessages.onGettingHistory;

    const command: CMDP_GETHISTORY = {
        action: ACTION.CMDP_GETHISTORY,
        params: {
            channelId,
            sessionID,
            sort: EnumSortOrder.DESC,
            limit: numberOfMessages.toString()
        }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

/** Receive the last 50 history messages and map them to our structure */
export const receiveHistorySaga = function* ({ payload }: Action<IPayload<CMDP_GETHISTORY_RESPONSE>>) {
    const { instanceId, value } = payload;

    if (isEmpty(value) || isEmpty(value.commands)) {
        return;
    }

    const messenger: IMessenger = yield select(selectors.getMessenger, instanceId);
	const gifts = yield select(selectors.getGifts, instanceId);

    const newMessages = utils.reconcileMessages(
        messenger.messages,
        value.commands,
        config.limitations.maxNumberOfMessages.onGettingHistory,
        gifts
    );

    // After mapping the messages, add them to the Redux store in order to be rendered
    if (!isEmpty(newMessages)) {
        yield put(insertMessagesToStore(instanceId, newMessages));
    }
};

/** Receives a list of messages and insert them in our Redux store */
const insertAllMessagesSaga = function* ({ payload }: Action<IPayload<IMessage[]>>) {
    const { instanceId, value } = payload;

    const toyOptions: IToyOption[] = yield select(selectors.getToyOptions, instanceId);
    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);

    const messages = map(value, (message: IMessage) => {
        const toyOption = find(toyOptions, option => includes(message.content, option.name));

        return toyOption
            ? utils.convertToToyMessage(
                message,
                toyOption,
                translation.chat.toyControl
            )
            : message;
    });

    yield put(insertAllMessages(instanceId, messages));
};

const watchSagas = function* () {
    yield takeEvery(REQUEST_HISTORY, requestHistorySaga);
    yield takeEvery(RECEIVE_HISTORY, receiveHistorySaga);
    yield takeEvery(INSERT_MESSAGES_TO_STORE, insertAllMessagesSaga);
};

export default watchSagas;
