import * as React from 'react';

import ProfileInfo from './ProfileInfo';
import selectors from '../../../redux/selectors';
import { onClickTitle, hasProfileClickHover } from './utils';
import IStore from '../../../redux/store';
import withRedux from '../../hoc/withRedux';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import { IContainerProps as IProps, IContainerComponentProps as IComponentProps } from "./types";

const ProfileInfoContainer = (props: IProps) => (
    <ProfileInfo
        children={props.children}
        hasFavorite={props.hasFavorite}
        hasModelName={props.hasModelName}
        isModelOnline={props.isModelOnline}
        shouldRenderMotto={props.shouldRenderMotto}
        hasProfileClickHover={hasProfileClickHover(props)}
        hasContactNote={props.hasContactNote}
        hasModelProfilePicture={props.hasModelProfilePicture}
        hasActionSectionInProfile={props.hasActionSectionInProfile}
        modelName={props.modelName}
        tooltipText={props.tooltipText}
        translation={props.translation}

        onClickTitle={onClickTitle(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    hasFavorite: selectors.hasFavorite(store, instanceId),
    hasContactNote: selectors.hasContactNote(store, instanceId),
    hasModelName: selectors.hasModelName(store, instanceId),
    hasModelProfilePicture: selectors.hasModelProfilePicture(store, instanceId),
    shouldRenderMotto: selectors.shouldRenderMotto(store, instanceId),
    isModelOnline: selectors.isModelOnline(store, instanceId),
    hasActionSectionInProfile: selectors.hasActionSectionInProfile(store, instanceId),
    modelName: selectors.getModelName(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

export default withRedux<IComponentProps>(ProfileInfoContainer, mapStateToProps);
