import { isEmpty } from "lodash";
import { Action } from "redux-actions";
import { CMDP_QUERYBACKEND } from "vxcontrol-client-lib";
import { put, select, takeEvery } from "redux-saga/effects";

import { receiveChat } from "./chat";
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from "../../redux/selectors";
import { setActionResponse } from "../../redux/modules/messenger";
import { ActionResponseType, IChat, ITranslation } from '../../redux/store';
import { IChatResponse } from "./types/types";
import { ActionIds } from "../../redux/interfaces";

const createQueryBackendActionType = createActionType(ActionIds.QUERY_BACKEND, { isSagaAction: true });

// Actions
const RECEIVE_QUERY_BACKEND = createQueryBackendActionType("RECEIVE_QUERY_BACKEND");

// Action Creators
export const onReceiveQueryBackend = createAction<CMDP_QUERYBACKEND>(RECEIVE_QUERY_BACKEND);

/**
 * This response can be for creating the chat or buying a gift bundle
 * @param action API response for the QUERY_BACKEND request
 */
const receiveQueryBackendSaga = function* ({ payload }: Action<IPayload<CMDP_QUERYBACKEND>>) {
    const { instanceId, value: response } = payload;

    if (isEmpty(response) || isEmpty(response.payload)) {
        return;
    }

    // If we receive a successful purchase of bundle gifts => notify the client
    if (response.payload.bundleGifts) {
        const translation: ITranslation = yield select(selectors.getTranslation, instanceId);

        yield put(setActionResponse(instanceId, {
            text: translation.common.bundleBuySuccess,
            type: ActionResponseType.SUCCESSFUL
        }));
    }

    // Checking for the creating chat request
    const chat: IChat | string = response.payload;
    if (chat !== "false" && (chat as IChat).chatId) {
        yield put(receiveChat(instanceId, chat as IChatResponse));
        return;
    }
};

const watchSagas = function* () {
    yield takeEvery(RECEIVE_QUERY_BACKEND, receiveQueryBackendSaga);
};

export default watchSagas;
