import { Action } from "redux-actions";
import { put, select, takeEvery } from "redux-saga/effects";

import selectors from "../../redux/selectors";
import { stopVideoChatPlayer } from "./videochat";
import { closeFullscreen } from "../../redux/modules/videochat";
import { createAction, createActionType } from "../../redux/utils";
import { IPayload } from "../../redux/interfaces";
import { ActionIds } from "../../redux/interfaces";

interface IPlayerErrorPayload {
    level: number;
    error: any;
}

const createErrorActionType = createActionType(ActionIds.ERRORS, { isSagaAction: true });

// Actions
const PLAY_ERROR = createErrorActionType("PLAY_ERROR");

// Action Creators
export const playError = createAction<IPlayerErrorPayload>(PLAY_ERROR);

// Sagas
export const playErrorSaga = function*({ payload: { instanceId }}: Action<IPayload>) {
    yield put(stopVideoChatPlayer(instanceId));

    const isFullScreen = yield select(selectors.isFullScreen, instanceId);
    if (isFullScreen) {
        yield put(closeFullscreen(instanceId));
    }
};

const watchSagas = function*() {
    yield takeEvery(PLAY_ERROR, playErrorSaga);
};

export default watchSagas;
