import * as React from 'react';

import ContactNote from './ContactNote';
import selectors from '../../../redux/selectors';
import { ActionDispatcher} from '../../../redux';
import IStore, { ITranslation } from '../../../redux/store';
import { openContactNote } from '../../../redux/modules/messenger';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';

interface IStoreProps {
    hasContactNoteText: boolean;
    translation: ITranslation;

    openContactNote: ActionDispatcher;
}

type IProps = IInstanceProps & IStoreProps;

const onContactNoteClick = (props: IProps) => () => {
    props.openContactNote(props.instanceId);
};

const ContactNoteContainer = (props: IProps) => (
    <ContactNote
        hasContactNoteText={props.hasContactNoteText}
        title={props.translation.user.addNote}
        onClick={onContactNoteClick(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId}: IInstanceProps) => ({
    hasContactNoteText: selectors.hasContactNoteText(store, instanceId),
    translation: selectors.getTranslation(store, instanceId)
});

const mapDispatchToProps = {
    openContactNote
};

export default withRedux(ContactNoteContainer, mapStateToProps, mapDispatchToProps);