import { IGift } from '../../../redux/store';
import { dollarGiftStaticData } from '../../../static/constants/constants';
import { IContainerProps as IProps } from "./types";
import { isEmpty, find } from 'lodash';

export const getDefaultGift = (props: IProps): IGift | undefined => {
    if (!isEmpty(props.gifts)) {
        const { id, name } = dollarGiftStaticData;

        return find(
            props.gifts,
            (gift: IGift) => gift.active && (gift.id === id || gift.name === name)
        ) as IGift;
    } else {
        return undefined;
    }
};
