import { find } from 'lodash';
import * as React from 'react';
import { IGift } from '../../../redux/store';
import utils from '../../../utils';
import { GiftDecoratorWrapper } from './style';

interface IProps {
    gifts: IGift[];
    [prop: string]: any;
}

const GiftDecorator = (props: IProps) => {
    const { contentState, entityKey } = props;
    const gift = contentState.getEntity(entityKey).getData().entity;
    const foundGift = find(props.gifts,
        (giftItem: IGift) => utils.getGiftEncoding(giftItem) === gift
    );

    return (
        <GiftDecoratorWrapper
            url={foundGift && foundGift.imageUrl} 
            data-entity={gift}
        >
            {props.children}
        </GiftDecoratorWrapper>
    );
};

export default GiftDecorator;