import { IContainerProps as IProps } from "./types";
import { VideoChatType } from '../../../redux/store';

export const onToyButtonClick = (props: IProps) => () => {
    props.toggleToySection(props.instanceId);
    props.setIsMobileMenuOpen(props.instanceId, false);
};

export const onActionButtonClick = (props: IProps) => (type: VideoChatType) => () => {
    props.startVideoChatType(props.instanceId, type);
};

export const toggleGiftSection = (props: IProps) => () => {
    props.isGiftSectionOpen
        ? props.closeGiftSection(props.instanceId)
        : props.openGiftSection(props.instanceId);
};
