import styled from "styled-components";
import { isMobile } from "react-device-detect";

import config from '../../../static/config';
import withIdentifier from "../../hoc/withIdentifier/withIdentifier";
import {DomIdentifier} from "../../hoc/withIdentifier";
import MessageSectionPopupWrapper from '../../common/MessageSectionPopupWrapper';

interface IWrapper {
	isFullScreen: boolean;
    isResponsive?: boolean;
    isOpen: boolean;
	shouldUseDropdownAnimation: boolean;
    isMobileLandscape?: boolean;
    [props: string]: any;
}

interface IText {
	isReponsive?: boolean;
}

interface IInlineOption {
    hasLimitedWidth: boolean;
}

export const Wrapper = withIdentifier<IWrapper>(styled(MessageSectionPopupWrapper).attrs({className: "toy-section-wrapper"})<IWrapper>`    
    ${props => {
        const { primaryColor, secondaryColor } = props.theme.colors.toySection;

        return `
            background-image: -webkit-gradient(linear,left top,right top,from(${primaryColor}),to(${secondaryColor}));
            background-image: linear-gradient(to right,${primaryColor},${secondaryColor});
        `
    }}

    color: ${props => props.theme.colors.toySection.text};
    ${props => props.isOpen && `transition: all ${config.popupsTransitionTime}s ease-in-out;`}
    
    ${props => props.isResponsive && 'height: 100%;'}
    ${props => {
		if (props.isOpen) {
			if (props.isResponsive && !props.isMobileLandscape)
				return `
					left: 0;
					max-height: 100%;
				`;
			
			return props.isFullScreen ? 'height: 303px;' : 'height: 100%;'
		}

		if (props.shouldUseDropdownAnimation) {
			return 'max-height: 0;';
		}
		
		return props.isResponsive ? 'left: 100%;' : 'height: 0;';
	}}

	position: absolute;
    bottom: 0;
    
    ${props => props.isMobileLandscape && props.isOpen && `
        position: absolute;
        height: 303px;
        bottom: auto;
        z-index: 5;
        top: 0;
    `}

	padding: 0 10px;
    width: 100%;
    box-sizing: border-box;
    
    z-index: 5;
`, DomIdentifier.ToySection);

export const Text = styled.div<IText>`
    color: ${props => props.theme.colors.toySection.text};
    font-family: ${(props) => props.theme.typography.fontFamily};

    font-size: ${(props) => isMobile || props.isReponsive ? "12" : "14"}px;
    margin: 30px 15px 20px 0;
`;

const Inline = styled.div`
    display: inline-block;
`;

export const InlineOption = styled(Inline)<IInlineOption>`
    color: ${props => props.theme.colors.toySection.text};

    margin: ${({ hasLimitedWidth }: IInlineOption) => {
        const value = hasLimitedWidth ? 2 : 5;
        return `0 ${value}% 0 ${value}%`
    }};

    flex-direction: row;
    min-height: 120px;
`;

export const OptionsWrapper = styled.div`
    padding: 0;
    margin: 45px 0 0;
    list-style: none;

    display: flex;
    align-items: center;
    justify-content: center;
    
    position: relative;
    bottom: 0;
    left: 0;
    right: 0;
`;
