import { Action } from "redux-actions";
import { eventChannel } from "redux-saga";
import { call, delay, put, race, select, spawn, take, takeEvery } from "redux-saga/effects";

import loggers from "../../loggers";
import { playError } from "./errors";
import config from "../../static/config";
import { ActionIds, IPayload } from "../../redux/interfaces";
import { createAction, createActionType } from "../../redux/utils";
import selectors from "../../redux/selectors";
import ChatService from "../../services/ChatService";
import { publishVideoChatPlayer } from "./videochat";
import { setActionResponse } from "../../redux/modules/messenger";
import { ActionResponseType, ITranslation } from "../../redux/store";
import PlayerService, { PlayInfo } from "../../services/PlayerService";
import {
	resetCam2CamSelectedCamera,
	setCam2CamActive,
	setCam2CamCameras,
	setCam2CamInactive,
	setInitialResolution,
	setIsAutoplayRestricted,
	setIsInitiallyMuted,
	setIsMutedByClient,
	setPlayingStatus
} from "../../redux/modules/videochat";
import { setPreference } from '../../redux/modules/user';
import { openChooseCameraPrompt } from "./userPrompt";
import { takePattern } from "../utils";
import { PreferenceName } from "../../services/AppService/types";

const createPlayerEventsActionType = createActionType(ActionIds.PLAYER_EVENTS, { isSagaAction: true });

// Actions
const PLAYER_STOPPED = createPlayerEventsActionType("PLAYER_STOPPED");
const NO_CAMERA_DETECTED = createPlayerEventsActionType("NO_CAMERA_DETECTED");
const LOW_STREAM_QUALITY_DETECTED = createPlayerEventsActionType("LOW_STREAM_QUALITY_DETECTED");
const GOOD_STREAM_QUALITY_DETECTED = createPlayerEventsActionType("GOOD_STREAM_QUALITY_DETECTED");
const CHOOSE_CAMERA = createPlayerEventsActionType("CHOOSE_CAMERA");

// Action Creators
const playerStopped = createAction(PLAYER_STOPPED);
const noCameraDetected = createAction(NO_CAMERA_DETECTED);
const lowStreamQualityDetected = createAction(LOW_STREAM_QUALITY_DETECTED);
const goodStreamQualityDetected = createAction(GOOD_STREAM_QUALITY_DETECTED);
const chooseCamera = createAction<MediaDeviceInfo[]>(CHOOSE_CAMERA);

/** Listeners */
const subscribeToCameraOn = emitter => {
    PlayerService.registerCameraOnListener((instanceId: string) => {
        loggers.saga.debug('Camera is on');
        emitter(publishVideoChatPlayer(instanceId));
        emitter(setCam2CamActive(instanceId));
    });
};

const subscribeToCameraOff = emitter => {
    PlayerService.registerCameraOffListener((instanceId: string) => {
        loggers.saga.debug('Camera is off');
        emitter(setCam2CamInactive(instanceId));
    });
};

const subscribeToCameraDenied = emitter => {
    PlayerService.registerCameraDeniedListener((instanceId: string) => {
        loggers.saga.debug('Camera denied or not found');
        emitter(noCameraDetected(instanceId));
    });
};

const subscribeToPlayError = emitter => {
    PlayerService.registerPlayErrorListener((instanceId: string, level: number, error?: any) => {
        emitter(playError(instanceId , { level, error }));
    });
};

const subscribeToVolumeChange = emitter => {
    PlayerService.registerVolumeChangeListener((instanceId: string, volume: number) => {
		loggers.saga.debug('Volume changed to ', {details: { volume }});
    	emitter(setIsMutedByClient(instanceId, volume === 0));
		emitter(setPreference(instanceId,
			{ preferenceName: PreferenceName.VolumeLevel, value: volume }));
    });
};

const subscribeToInitiallyMuted = emitter => {
    PlayerService.registerInitiallyMutedListener((instanceId: string, isMuted: boolean) => {
        loggers.saga.debug(`isInitiallyMuted=${isMuted}`);
        emitter(setIsInitiallyMuted(instanceId, isMuted));
    });
};

const subscribeToSelectingCameras = emitter => {
    PlayerService.registerSelectingCameraListener((instanceId: string, cameras: MediaDeviceInfo[]) => {
        loggers.saga.debug(`available cam2cam cameras=`, { details: { cameras }});
        emitter(chooseCamera(instanceId, cameras));
    });
};

/**
 * Our handler will check if the player received values
 * for width and height, which is the closest condition to having succesfully
 * a player rendered and active, it will inform the parent platform.
 * @param emitter
 */
const subscribeToPlayInfo = emitter => {
    PlayerService.registerPlayerInfoListener((instanceId: string, info: PlayInfo) => {
        if (info.width && info.height && !info.paused) {
            const { width, height } = info;
            emitter(setInitialResolution(instanceId, { width, height }));
            emitter(setPlayingStatus(instanceId, true));
        }

        emitter(info.quality === 'low'
            ? lowStreamQualityDetected(instanceId)
            : goodStreamQualityDetected(instanceId));
    });
};

const subscribeToPlayStart = emitter => {
    PlayerService.registerPlayStartListener((instanceId: string) => {
        loggers.saga.debug('Player started');
    });
};

const subscribeToPlayStop = emitter => {
    PlayerService.registerPlayStopListener((instanceId: string) => {
        loggers.saga.debug('Player stopped');
        emitter(playerStopped(instanceId));
    });
};

const subscribeToSendMetrics = emitter => {
    PlayerService.registerSendMetricsListener((instanceId, info) => {
        loggers.saga.debug("Send matrics with info: ", { details: { info } });
        ChatService.getService(instanceId).sendMetrics(info);

        if (info.playerEvent === 'h5live_warning' && info.code === 1005) {
        	// auto play issue
			loggers.saga.warning('Autoplay restriction!!!');
			emitter(setIsAutoplayRestricted(instanceId, true));
		}
    });
};

const playerEventsChannel = () => {
    return eventChannel(emitter => {
        subscribeToCameraOn(emitter);
        subscribeToCameraOff(emitter);
        subscribeToCameraDenied(emitter);
        subscribeToPlayError(emitter);
        subscribeToPlayStart(emitter);
        subscribeToPlayStop(emitter);
        subscribeToPlayInfo(emitter);
        subscribeToSendMetrics(emitter);
        subscribeToVolumeChange(emitter);
        subscribeToInitiallyMuted(emitter);
        subscribeToSelectingCameras(emitter);

        return () => {
            // no need for un-subscribing; saga's live exceeds the app live
        }
    });
};

// Sagas
const playerStoppedSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
	const isPlaying = yield select(selectors.isPlaying, instanceId);
    if (isPlaying) {
        yield put(setPlayingStatus(instanceId, false));
    }
};

const noCameraDetectedSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const translation: ITranslation = yield select(selectors.getTranslation, instanceId);

    yield put(setActionResponse(instanceId, {
        text: translation.error.noCamera,
        type: ActionResponseType.ERROR
    }))
};

const lowStreamQualityDetectedSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const result = yield race({
        qualityIsStillLow: delay(config.waitUntilReportingBadConnection * 1000),
        qualityEnhanced: take(takePattern(GOOD_STREAM_QUALITY_DETECTED, instanceId))
    });

    if (result.qualityIsStillLow) {
        const translation: ITranslation = yield select(selectors.getTranslation, instanceId);

        yield put(setActionResponse(instanceId, {
            text: translation.warning.connectionPoor,
            type: ActionResponseType.WARNING
        }));
    }
};

const listenForPlayerEventsSaga = function* () {
    const playerEvents = yield call(playerEventsChannel);

    try {
        while (true) {
            const action = yield take(playerEvents);
            yield put(action);
        }
    } finally {
        // finished
    }
};

const chooseCameraSaga = function* ({ payload}: Action<IPayload>) {
    const { instanceId, value: cameras} = payload;
    yield put(resetCam2CamSelectedCamera(instanceId));
    yield put(setCam2CamCameras(instanceId, cameras));
    yield put(openChooseCameraPrompt(instanceId));
};

const watchSaga = function* () {
    yield spawn(listenForPlayerEventsSaga);
    yield takeEvery(PLAYER_STOPPED, playerStoppedSaga);
    yield takeEvery(NO_CAMERA_DETECTED, noCameraDetectedSaga);
    yield takeEvery(LOW_STREAM_QUALITY_DETECTED, lowStreamQualityDetectedSaga);
    yield takeEvery(CHOOSE_CAMERA, chooseCameraSaga);
};

export default watchSaga;
