import * as React from 'react';
import { ICurrency, ITranslation } from '../../../redux/store';
import utils from '../../../utils';
import { BaseButtonContent, FirstRowText, SecondRowText } from '../../common/BaseButton/style';
import Tooltip from '../../common/Tooltip';
import { LiveChatButtonWrapper } from './style';
import BaseButtonContainer from '../../common/BaseButton';

export interface ILiveChatButtonProps {
    price?: number;
    currency: ICurrency;
    translation: ITranslation;
    shouldDisplayIcon?: boolean;
    isForActionSection?: boolean;
    shouldRenderPrice?: boolean;

    onClick: () => void;
}

const LiveChatButton = (props: ILiveChatButtonProps) => {
    const { tooltipVideochat, liveChat } = props.translation.chat;

    const liveChatPrice = utils.translationWithValues(
        props.translation.common.perMinute, 
        { price: `${props.price} ${props.currency.symbol}` }
    );

    const liveChatButton = (
        <LiveChatButtonWrapper>
            {utils.getShouldDisplayIcon(props.shouldDisplayIcon) &&
            	<i className={"-icon-webcam-single-full"} />
			}

			<BaseButtonContent>
				<FirstRowText>{liveChat}</FirstRowText>
				{props.shouldRenderPrice && <SecondRowText>{liveChatPrice}</SecondRowText>}
			</BaseButtonContent>
        </LiveChatButtonWrapper>
    );

    const liveChatButtonWithTooltip = (
        <Tooltip text={tooltipVideochat} id="live-chat">
            {liveChatButton}
        </Tooltip>
    );

    return (
        <BaseButtonContainer
            onClick={props.onClick}
            isForActionSection={props.isForActionSection}
        >
            {tooltipVideochat
                ? liveChatButtonWithTooltip
                : liveChatButton
            }
        </BaseButtonContainer>
    );
};

export default LiveChatButton;
