import * as React from 'react';
import { MobileMenuItemWrapper } from './style';

interface IProps {
    className: string;
    text: string;
    onClick?: () => void
}

const MobileMenuItem = (props: IProps) => (
    <MobileMenuItemWrapper onClick={props.onClick}>
        <div className={`${props.className} icon`}/>
        <div className='text'>{props.text}</div>
    </MobileMenuItemWrapper>
);

export default MobileMenuItem;