import * as React from 'react';
import { isEmpty } from 'lodash';

import { ICameraDevice } from "../../../redux/store";
import CameraOption from "../CameraOption/CameraOption";
import { FrontBackCamerasWrapper, RegularCamerasWrapper, Wrapper } from './style';
import { ICamerasSplit } from "./types";
import { extractFrontAndBackCameras } from "./utils";

interface IProps {
    cameras: ICameraDevice[];
    selectedCamera?: ICameraDevice;

    onSelectCamera: (id: string) => (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => void;
}

const getCameraOptions = (props: IProps, cameras: ICameraDevice[]) => (
    cameras.map((camera: ICameraDevice) =>
        <CameraOption
            key={camera.deviceId}
            camera={camera}
            isSelected={camera === props.selectedCamera}
            onClick={props.onSelectCamera(camera.deviceId)}
        />
    )
);

const CamerasSection = (props: IProps) => {
    const camerasSplit: ICamerasSplit = extractFrontAndBackCameras(props.cameras);

    return (
        <Wrapper>
        {!isEmpty(camerasSplit.frontAndBack) &&
            <FrontBackCamerasWrapper>
                {getCameraOptions(props, camerasSplit.frontAndBack)}
            </FrontBackCamerasWrapper>
        }

        <RegularCamerasWrapper>
            {getCameraOptions(props, camerasSplit.others)}
        </RegularCamerasWrapper>
    </Wrapper>
    );
};

export default CamerasSection;