import * as React from 'react';
import { CSSTransition, TransitionGroup as ReactTransitionGroup } from 'react-transition-group';

interface ITimeout {
    enter: number;
    exit: number;
}

interface IClassNames {
    appear?: string;
    appearActive?: string;

    enter: string;
    enterActive: string;
    enterDone: string;

    exit: string;
    exitActive: string;
    exitDone: string;
}

export interface ITransitionGroupProps<T> {
    timeout: ITimeout;
    classNames: IClassNames;
    transitionedItems: T[];

    itemKey: (item: T) => any;
    children: (item: T) => JSX.Element;
}

class TransitionGroup<T> extends React.Component<ITransitionGroupProps<T>> {

    render() {
        const items: T[] = this.props.transitionedItems || [];

        return(
            <ReactTransitionGroup>
                {
                    items.map((item: T) => (
                        <CSSTransition
                            key={this.props.itemKey(item)}
                            in={true}
                            timeout={this.props.timeout}
                            classNames={this.props.classNames}
                            unmountOnExit={true}
                        >
                            {this.props.children(item)}
                        </CSSTransition>
                    ))
                }
            </ReactTransitionGroup>
        );
    }
}

export default TransitionGroup;