import * as React from 'react';
import { ITranslation } from '../../../redux/store';
import ActionSectionContainer from '../ActionSection';
import {
    ActionSectionWrapper,
    FeatureWrapper,
    Motto,
    ProfileInfoInnerWrapper,
    ProfileName,
    ProfileNameWrapper,
    IProfileNameProps,
    ProfileOnlineBullet
} from './style';
import FavoriteContainer from '../Favorite';
import ContactNoteContainer from '../ContactNote';
import { isMobile } from 'react-device-detect';

interface IProps {
    children?: any;
    hasFavorite: boolean;
    hasContactNote: boolean;
    hasModelName: boolean;
    hasModelProfilePicture: boolean;
    modelName: string;
    tooltipText: string;
    shouldRenderMotto: boolean;
    hasActionSectionInProfile: boolean;
    hasProfileClickHover: boolean;
    isModelOnline: boolean;
    translation: ITranslation;

    onClickTitle: () => void;
}

const ProfileInfo = (props: IProps) => {

    const profileNameProps: IProfileNameProps = {
        onClick: props.onClickTitle
    };

    if (props.hasProfileClickHover) {
        profileNameProps.title = props.translation.user.visitProfile;
    }

    return (
        <ProfileInfoInnerWrapper>
            <ProfileNameWrapper hasModelProfilePicture={props.hasModelProfilePicture}>
                {isMobile && <ProfileOnlineBullet isModelOnline={props.isModelOnline} />}

                {props.hasModelName &&
                    <ProfileName {...profileNameProps}>
                        {props.modelName}
                    </ProfileName>
                }
    
                {props.hasFavorite &&
                    <FeatureWrapper>
                        <FavoriteContainer />
                    </FeatureWrapper>
                }
    
                {props.hasContactNote &&
                    <FeatureWrapper>
                        <ContactNoteContainer />
                    </FeatureWrapper>
                }
            </ProfileNameWrapper>
    
            <ActionSectionWrapper>
                {props.shouldRenderMotto && props.children &&
                    <Motto>{props.children}</Motto>
                }
    
                {props.hasActionSectionInProfile && <ActionSectionContainer />}
            </ActionSectionWrapper>
        </ProfileInfoInnerWrapper>
    );
};

export default ProfileInfo;
