import { isEmpty, get } from "lodash";
import { createSelector } from "reselect";

import IStore, { IModel } from "../store";
import { getInitialInstanceData } from "../initialStore";

export const getModel = (store: IStore, instanceId: string | undefined) => {
    const id = instanceId as string;
    const data = get(store, id) || getInitialInstanceData();
    return data.model;
};

/**
 * @return {string} a string containing the motto of the model
 */
export const getModelMotto = createSelector(getModel, selection => selection.motto);

/**
 * @return {string} - model's name
 */
export const getModelName = createSelector(getModel, selection => selection.name);

/**
 * @return {string} - link to model's picture
 */
export const getModelProfilePicture = createSelector(getModel, selection => selection.profilePicture);

/**
 * @return {boolean} - true if current model was marked as favorite
 */
export const isModelFavorite = createSelector(getModel, selection => selection.isFavorite);

/**
 * @return {Partner} a structure identifying the chat partner (aka. model)
 */
export const getPartner = createSelector(getModel, selection => selection.partner);

/**
 * @return {IOnlineState} a structure with all online state flags;
 * useful especially when wants to see if it was set yet or not
 */
export const getOnlineState = createSelector(getModel, model => model.onlineState);

/**
 * @return {boolean} - true if the model is online
 */
export const isModelOnline = createSelector(getModel, selection => selection.isOnline);

/**
 * @return {boolean} - true if the model is allowing a regular video chat session
 */
export const isModelSupportingChat = createSelector(getModel,
    selection => (selection.onlineState && selection.onlineState.supportsChat) as boolean
);

/**
 * @return {boolean} true if the model is currently in a private chat session
 */
export const isModelInPrivateChat = createSelector(getModel,
    model => (
        model.isOnline &&
        model.onlineState &&
        !model.onlineState.supportsChat) as boolean
);

/**
 * @return {boolean} true if the model is allowing a private video chat session
 */
export const isModelSupportingPrivateChat = createSelector(getModel,
    selection => (selection.onlineState && selection.onlineState.supportsPrivateChat) as boolean
);

/**
 * @return {boolean} true if the model is allowing a soft chat session
 * (model is currently doing a soft show, appropriate for people under 18
 */
export const isModelSupportingSoftChat = createSelector(getModel,
    selection => (selection.onlineState && selection.onlineState.supportsSoftChat) as boolean
);

/**
 * @return {boolean} true if the model is allowing a short preview video session
 */
export const isModelSupportingPreviewChat = createSelector(getModel,
    selection => (selection.onlineState && selection.onlineState.supportsPreviewChat) as boolean
);

/**
 * @return {boolean} true if the model is allowing a voyeur video chat session
 * (user can watch, but cannot interact with the model)
 */
export const isModelSupportingVoyeurChat = createSelector(getModel,
    selection => (selection.onlineState && selection.onlineState.supportsVoyeurChat) as boolean
);

/**
 * @return {boolean} true if the model has toy control support activated
 */
export const isModelSupportingToyControl = createSelector(getModel,
    model => (model.onlineState && model.onlineState.supportsToyControl) as boolean
);

/**
 * @return {boolean} true if the model is allowing a voyeur video chat session
 * even if she is in a private chat with someone else
 */
export const isModelSupportingVoyeurToPrivateChat = createSelector(getModel,
    selection => (selection.onlineState && selection.onlineState.supportsVoyeurToPrivateChat) as boolean
);

/**
 * @return {boolean} true if the model can receive tips
 */
export const isModelSupportingTip = createSelector(getModel,
    model => (model.onlineState && model.onlineState.supportsTip) as boolean
);

/**
 * @return {number} - price and currency per minute of regular video chat
 */
export const getLiveChatPrice = createSelector(getModel, model => model.prices.videoChatPrice);

/**
 * @return {number} - price and currency per minute of voyeur video chat
 */
export const getVoyeurCamPrice = createSelector(getModel, model => model.prices.voyeurChatPrice);

/**
 * @return {boolean} - if we receive any price from the back-end
 */
export const hasPrices = createSelector([getModel],
    (model: IModel) => !isEmpty(model.prices)
);

/**
 * @return {number} - price and currency per message sent (when not in a video session)
 */
export const getMessagePrice = createSelector(getModel, model => model.prices.messagePrice);

/**
 * @return {number} representing the free messages between the client and this specific model
 */
export const hasFreeModelMessages = createSelector(getModel, model => model.freeMessages);

/**
 * @return {boolean} true if the client has free stream time for this specific model
 */
export const hasFreeModelChatDuration = createSelector(getModel, model => model.freeChatDuration > 0);

/**
 * @return {number} returns the number of free seconds between the client and this specific model
 */
export const getFreeModelChatDuration = createSelector(getModel, model => model.freeChatDuration);