import * as React from 'react';
import Tooltip from '../../../features/common/Tooltip';
import { ITranslation } from '../../../redux/store';
import utils from '../../../utils';
import { SingleRowText } from '../../common/BaseButton/style';
import { LiveChatPreviewButtonContent, LiveChatPreviewButtonWrapper } from './style';
import BaseButtonContainer from '../../common/BaseButton';

interface IProps {
    seconds: number;
    shouldDisplayIcon?: boolean;
    isForActionSection?: boolean;
    translation: ITranslation;

    onClick: () => void;
}

const LiveChatPreviewButton = (props: IProps) => {
    const { livePreview, tooltipPreview } = props.translation.chat;
    const seconds = {
        seconds: props.seconds
    };

    const previewButton = (
        <LiveChatPreviewButtonWrapper >
			{utils.getShouldDisplayIcon(props.shouldDisplayIcon) &&
				<i className={"-icon-eyes-line"} />
			}

			<LiveChatPreviewButtonContent shouldDisplayIcon={props.shouldDisplayIcon}>
				<SingleRowText>{livePreview}</SingleRowText>
			</LiveChatPreviewButtonContent>
        </LiveChatPreviewButtonWrapper>
    );

    const previewButtonWithTooltip = (
        <Tooltip
            id={"live-chat-preview"}
            text={utils.translationWithValues(tooltipPreview, seconds)}
        >
            {previewButton}
        </Tooltip>
    );

    return (
        <BaseButtonContainer
            onClick={props.onClick}
            isForActionSection={props.isForActionSection}
        >
            {tooltipPreview
                ? previewButtonWithTooltip
                : previewButton
            }
        </BaseButtonContainer>
    );
};

export default LiveChatPreviewButton;
