import styled from "styled-components";
import config from '../../../static/config';
import withIdentifier, { DomIdentifier } from "../../hoc/withIdentifier";

interface IChatBoxWrapperProps {
    shouldLiftChatbox: boolean;
    shouldTrimChatbox: boolean;
	shouldApplyOverflow: boolean;
}

interface IMessageListingProps {
    opacity: number;
    isGroupMessage?: boolean;
}

interface IMessageListProps {
    isMobileLandscape?: boolean;
}

export const ChatBoxWrapper = withIdentifier<IChatBoxWrapperProps>(styled.div.attrs({ className: 'chatbox-wrapper' })<IChatBoxWrapperProps>`
    ${props => props.shouldLiftChatbox && 'margin-bottom: 82px;'}
    ${props => props.shouldTrimChatbox && `max-height: calc(100% - ${config.viewSizes.countdownWarning.height}px) !important;`}
	${props => props.shouldApplyOverflow && 'overflow: hidden;'}
    clear: both;
    flex: 1 0 auto;
	position: relative;
	box-sizing: content-box;
`, DomIdentifier.ChatBox);

export const ChatBoxBlanket = styled.div.attrs({className: 'chatbox-blanket'})`
	${(props) => `background-color: ${props.theme.colors.chatBoxContainer};`}
	
	position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 4;
`;

export const MessageItem = styled.li<IMessageListingProps>`
    opacity: ${(props: IMessageListingProps) => props.opacity};
    ${(props: IMessageListingProps) => !props.isGroupMessage && 'margin: 0 15px;'}
`;

export const MessageList = styled.ol<IMessageListProps>`
    display: block;
    list-style: none;
    height: 100%;
    margin: 0px;
    padding: 0px;
    clear: both;
    ${props => props.isMobileLandscape && 'margin-bottom: 15px;'}
`;

export const ImageUrlContentWrapper = styled.a`
    display: block;
    cursor: pointer;
    text-decoration: none;
    color: #111111;
`;

export const ImageUrlTextWrapper = styled.div`
    margin-top: 5px;
`;

export const GroupChatParticipantsWrapper = styled.div`
    position: absolute;
    left: 5px;
    top: 5px;
    z-index: 1;
`;

