import * as React from 'react';
import { IActionResponse, ActionResponseType } from '../../../redux/store';
import { TextWrapper, ActionResponseWrapper } from './style';
import CloseButtonContainer, { CloseButtonType } from '../CloseButton/CloseButtonContainer';
import { ITheme } from '../../../themes';
import { withTheme } from 'styled-components';
import { isUndefined } from 'lodash';
import defaultTheme from '../../../themes/defaultTheme';

export interface IProps {
    actionResponse: IActionResponse;
    theme?: ITheme;

    closeText: string;
    onCloseClick: () => void;
}

interface IWrapperProps {
    color: string;
    iconClassName: string;
    className: string;
}

const ActionResponse = (props: IProps) => {
    const { type, text } = props.actionResponse;
    const currentTheme = isUndefined(props.theme) ? defaultTheme : props.theme;
    const { warningColor, errorColor, successfulColor } = currentTheme.colors.actionResponse;

    const actionResponseWrapperProps: {[key: string]: IWrapperProps} = {};

    actionResponseWrapperProps[ActionResponseType.SUCCESSFUL] = {
        iconClassName: '-icon-round-success-line',
        className: 'successful-action-response-wrapper',
        color: successfulColor
    };
    actionResponseWrapperProps[ActionResponseType.ERROR] = {
        iconClassName: '-icon-round-info-line',
        className: 'error-action-response-wrapper',
        color: errorColor
    };
    actionResponseWrapperProps[ActionResponseType.WARNING] = {
        iconClassName: '-icon-round-info-line',
        className: 'warning-action-response-wrapper',
        color: warningColor
    };

    const defaultWrapperProps = actionResponseWrapperProps[ActionResponseType.SUCCESSFUL];
    const wrapperProps: IWrapperProps = actionResponseWrapperProps[type] || defaultWrapperProps;

    return (
        <ActionResponseWrapper
            color={wrapperProps.color}
            className={wrapperProps.className}
        >
            <TextWrapper>
                <i className={wrapperProps.iconClassName} /> {text}
            </TextWrapper>

            <CloseButtonContainer
                onClick={props.onCloseClick}
                hoverText={props.closeText}
                closeButtonType={CloseButtonType.ACTION_RESPONSE}
            />
        </ActionResponseWrapper>
    );
};

export default withTheme<IProps, ITheme>(ActionResponse);
