import * as React from 'react';
import ProfilePictureContainer from '../../specific/ProfilePicture/ProfilePictureContainer';
import {
	ButtonSection,
	CancelButton,
	ContentElement,
	ContentWrapper,
	ContinueButtonWrapper,
	InformationDialogWrapper,
	OCRInformationDialogWrapper,
	ProfilePictureWrapper,
	TextElement,
	TitleElement
} from './style';
import CloseButtonContainer, { CloseButtonType } from '../CloseButton/CloseButtonContainer';
import PerfectScrollbarContainer from "../PerfectScrollbar";
import { VideochatInformationType } from "../../../redux/store";

export interface IInformationDialogProps {
	title: string;
	text?: string;
	button?: JSX.Element;
	buttonText?: string;
	cancelText?: string;
	closeTooltip?: string;
	informationType?: VideochatInformationType;
	children?: JSX.Element;
	hasModelProfilePicture: boolean;
	hasCloseButton: boolean;
	isVideoChatOpen: boolean;
	isMobileLandscape: boolean;

	onButtonClick?: () => void;
	onCloseClick?: () => void;
}

const InformationDialog = (props: IInformationDialogProps) => {
	const InformationDialogWrapperComponent = props.informationType === VideochatInformationType.OUT_OF_MONEY_ONE_CLICK_RECHARGE
			? OCRInformationDialogWrapper
			: InformationDialogWrapper;

	return (<InformationDialogWrapperComponent
				isVideoChatOpen={props.isVideoChatOpen}
				isMobileLandscape={props.isMobileLandscape}
				informationType={props.informationType}
		>
			<CloseButtonContainer
				onClick={props.onCloseClick}
				hoverText={props.closeTooltip}
				closeButtonType={CloseButtonType.INFORMATION_DIALOG}
				hasCloseButton={props.hasCloseButton}
			/>

			<PerfectScrollbarContainer>
				<ContentWrapper>
					{props.hasModelProfilePicture && !props.isMobileLandscape &&
						<ProfilePictureWrapper>
							<ProfilePictureContainer/>
						</ProfilePictureWrapper>
					}

					{props.title && <TitleElement>{props.title.toUpperCase()}</TitleElement>}
					{props.text && <TextElement>{props.text}</TextElement>}

					{props.children}

					<ButtonSection>
						{props.button
							? (
								<div onClick={props.onButtonClick}>
									{props.button}
								</div>
							)
							: (props.buttonText &&
								<ContinueButtonWrapper onClick={props.onButtonClick}>
									<ContentElement>{props.buttonText}</ContentElement>
								</ContinueButtonWrapper>
							)
						}

						{props.cancelText && !props.isMobileLandscape &&
							<CancelButton onClick={props.onCloseClick}>
								{props.cancelText}
							</CancelButton>
						}
					</ButtonSection>
				</ContentWrapper>
			</PerfectScrollbarContainer>
		</InformationDialogWrapperComponent>
	);
}

export default InformationDialog;
