import * as React from 'react';

import { IContainerProps as IProps } from "./types";
import { IMessengerBaseButtonProps, MessengerBaseButton, MessengerBaseButtonIcon } from '../MessageSection/style';
import { MessengerBaseButtonIconGroupWrapper } from './style';
import withIdentifier, { DomIdentifier } from '../../hoc/withIdentifier';

export const expandDropdown = (props: IProps) => {
	if (!props.isInteractive) {
		return;
	}

	if (props.isResponsive) {
		props.toggleDropDown(props.instanceId);
	}

	if (props.isEmoticonSectionOpen) {
		props.closeEmoticonSection(props.instanceId);
	}

	if (props.isGiftSectionOpen) {
		props.closeGiftSection(props.instanceId);
	}
};

export const toggleEmoticonSection = (props: IProps) => () => {
	if (!props.isInteractive) {
		return;
	}

	if (props.isEmoticonSectionOpen) {
		props.closeEmoticonSection(props.instanceId);
	} else {
		props.openEmoticonSection(props.instanceId);
	}

	if (!props.isResponsive && !props.isDropDownOpen) {
		expandDropdown(props);
	}
};

export const toggleGiftSection = (props: IProps) => () => {
	if (!props.isInteractive) {
		return;
	}

	props.isGiftSectionOpen
		? props.closeGiftSection(props.instanceId)
		: props.openGiftSection(props.instanceId);

	if (!props.isResponsive && !props.isDropDownOpen) {
		expandDropdown(props);
	}
};

export const toggleToySection = (props: IProps) => () => {
	if (!props.isInteractive) {
		return;
	}

	props.toggleToySectionAction(props.instanceId);

	if (!props.isResponsive && !props.isDropDownOpen) {
		expandDropdown(props);
	}
};

export const getBaseButtonContent = (icon: string, text: string, isResponsive?: boolean) => {
	const baseButtonIcon = <MessengerBaseButtonIcon className={icon} />;

	const responsiveBaseButtonIcon = (
		<MessengerBaseButtonIconGroupWrapper>
			{baseButtonIcon}
			{text}
		</MessengerBaseButtonIconGroupWrapper>
	);

	return isResponsive ? responsiveBaseButtonIcon : baseButtonIcon;
};

export const getButtonWithIdentifier = (identifier: DomIdentifier) =>
	withIdentifier<IMessengerBaseButtonProps>(MessengerBaseButton, identifier);

