import { handleActions, Action } from "redux-actions";

import { createAction, createActionType } from "../utils";
import { IPlatformProps } from "../store";
import { getInitialInstanceData } from "../initialStore";
import { ActionIds } from "../interfaces";
import { RESET_STORE } from "./app";
import {AnyAction} from "redux";

const createPlatformPropsActionType = createActionType(ActionIds.PLATFORM_PROPS, { isReduxAction: true });

// Actions
const SET_PLATFORM_PROPS = createPlatformPropsActionType("SET_PLATFORM_PROPS");

// Action Creator
export const setPlatformProps = createAction<IPlatformProps>(SET_PLATFORM_PROPS);

// Reducer
const platformProps = handleActions({
    [RESET_STORE]: (state: IPlatformProps, action: AnyAction) => getInitialInstanceData().platformProps,

    [SET_PLATFORM_PROPS]: (state: IPlatformProps, { payload }: Action<IPlatformProps>) => payload
}, getInitialInstanceData().platformProps);

export default platformProps;
