import screenfull from "screenfull";

/** Check if the window is in fullscreen mode */
export const isFullscreen = () => screenfull.isEnabled && screenfull.isFullscreen;

/** Toggle the fullscreen status for a certain HTML element */
export const toggle = (element: Element | undefined) => {
	if (screenfull.isEnabled) {
		screenfull.toggle(element);
	}
};

/** Exit window's fullscreen state */
export const exit = () => {
	if (screenfull.isEnabled && screenfull.isFullscreen) {
		screenfull.exit();
	}
};

/** You can configurate a hook that will be called when the fullscreen state changes */
export const onChange = (func: (args: any) => void) => {
    if (screenfull && screenfull.isEnabled && screenfull.onchange) {
        screenfull.onchange((args: any) => {
        	func(args);
        });
    }
};
