import * as React from 'react';
import { FavoriteIcon } from './style';

interface IProps {
    className: string;
    title: string;
    onClick: () => void;
}

const Favorite = (props: IProps) => (
    <FavoriteIcon 
        className={props.className}
        title={props.title}
        onClick={props.onClick}
    />
);

export default Favorite;