import * as React from 'react';

import { exitFullscreen } from './utils';
import selectors from '../../../redux/selectors';
import FullscreenHeader from './FullscreenHeader';
import IStore from '../../../redux/store';
import { clickCloseMessengerButton } from "../../../sagas/common/ui";
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import { IContainerProps as IProps } from "./types";

const FullscreenHeaderContainer = (props: IProps) => (
    <FullscreenHeader
        closeText={props.translation.common.close}
        exitFullscreen={exitFullscreen(props)}
    />
);

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
    translation: selectors.getTranslation(store, instanceId)
});

const mapDispatchToProps = {
    clickCloseMessengerButton
};

export default withRedux(FullscreenHeaderContainer, mapStateToProps, mapDispatchToProps);
