import { Action } from 'redux-actions';
import { filter, find, isEmpty } from 'lodash';
import { put, select, take, takeEvery } from 'redux-saga/effects';
import {
    ACTION,
    CMDC_CHANNEL,
    CMDP_CHANNELSEEN,
    CMDP_GETCHANNELS,
    CMDP_GETCHANNELS_RESPONSE,
	EnumSortOrder
} from 'vxcontrol-client-lib';

import { requestHistory } from './history';
import { createAction, createActionType } from '../../redux/utils';
import { IPayload } from '../../redux/interfaces';
import selectors from '../../redux/selectors';
import { requestNewChannel } from './newChannel';
import { requestOnlineState } from "./onlineState";
import ApiService from '../../services/ApiService';
import { requestGetChannelInfo } from './channelInfo';
import { MessageDirection, IMessage, IPartner } from '../../redux/store';
import { setChannel, SET_CHANNEL } from '../../redux/modules/session';
import { ADD_MESSAGE, INSERT_ALL_MESSAGES } from '../../redux/modules/messenger';
import { ActionIds } from '../../redux/interfaces';
import { takePattern } from '../utils';

const createChannelActionType = createActionType(ActionIds.CHANNEL, { isSagaAction: true });

// Actions
const REQUEST_CHANNEL = createChannelActionType("REQUEST_CHANNEL");
export const RECEIVE_CHANNELS = createChannelActionType("RECEIVE_CHANNELS");

const RECEIVE_CHANNEL_UPDATE = createChannelActionType("RECEIVE_CHANNEL_UPDATE");
const REQUEST_CHANNEL_SEEN = createChannelActionType("REQUEST_CHANNEL_SEEN");

// Action Creators
export const requestChannel = createAction(REQUEST_CHANNEL);
export const onReceiveChannels = createAction<CMDP_GETCHANNELS_RESPONSE>(RECEIVE_CHANNELS);
export const receiveChannelUpdate = createAction<CMDC_CHANNEL>(RECEIVE_CHANNEL_UPDATE);
export const requestChannelSeen = createAction<string>(REQUEST_CHANNEL_SEEN);

/**
 * It takes the sessionID from the store and sends the data
 * for receiving a certain partner's channel of communication.
 */
export const requestChannelSaga = function* ({ payload: { instanceId }}: Action<IPayload>) {
    const sessionID = yield select(selectors.getSessionId, instanceId);
    const partner: IPartner = yield select(selectors.getPartner, instanceId);

    const command: CMDP_GETCHANNELS = {
        action: ACTION.CMDP_GETCHANNELS,
        params: {
            sessionID,
            partnerId: partner.id,
            partnerKey: partner.key,
            sort: EnumSortOrder.ASC
        }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

/**
 * When starting the conversation with the model, if there is no channel configured for her,
 * we should add a new one by calling the newChannelSaga, which will save the
 * new channelId in the store. If there is already a channel for that partner,
 * just save the channelId in the store.
 */
const receiveChannelSaga = function* ({ payload }: Action<IPayload<CMDP_GETCHANNELS_RESPONSE>>) {
	const { instanceId, value: response } = payload;
	const currentChannelId = yield select(selectors.getChannelId, instanceId);

    if (currentChannelId) {
        return;
	}

	const channelCommand = find(response.commands, command => command.action === ACTION.CMDC_CHANNEL);
	
    /** Set the channelId in the Redux store */
    if (channelCommand && channelCommand.params) {
        const { channelId } = channelCommand.params;

        if (!isEmpty(channelId)) {
            yield put(setChannel(instanceId, channelId));
        }

        /** Get the history messages for the obtained channel */
        const isFullscreen = yield select(selectors.isFullScreen, instanceId);
        if (!isFullscreen) {
            yield put(requestHistory(instanceId));
        }
    } else {
        yield put(requestNewChannel(instanceId));

        /** We block our saga until we have the channelId in our Redux store */
        yield take(takePattern(SET_CHANNEL, instanceId));
    }

    /** Get the channel info */
    yield put(requestGetChannelInfo(instanceId));

    /** Get the online state for the model */
    yield put(requestOnlineState(instanceId));
}

/**
 * Set the model's message as seen (read) by the client
 * @param action
 */
const requestChannelSeenSaga = function* ({ payload }: Action<IPayload<string>>) {
    const { instanceId, value } = payload;
    const channelId: string = yield select(selectors.getChannelId, instanceId);

    const command: CMDP_CHANNELSEEN = {
        action: ACTION.CMDP_CHANNELSEEN,
        params: {
            channelId,
            messageId: value
        }
    };

    ApiService.getService(instanceId).sendCommand(command);
};

/**
 * Set all model's messages as seen (read) by the client when opening the messenger
 */
const readModelMessagesSaga = function* ({ payload, type }: Action<IPayload<IMessage | IMessage[]>>) {
    const { instanceId, value } = payload;
    const isOpen = yield select(selectors.isOpen, instanceId);
    const isChatExternallyProvided = yield select(selectors.isChatExternallyProvided, instanceId);

    // Do not send message seen request if we don't have the vxcontrol api
    if (isChatExternallyProvided) {
        return;
    }

    if (isOpen) {
        switch (type) {
            // If the messenger is open and we receive a message from the model,
            // we consider it to be seen by the client.
            case ADD_MESSAGE:
                const message = value as IMessage;

                if (message && message.id && message.direction === MessageDirection.in) {
                    yield put(requestChannelSeen(instanceId, message.id));
                }
                break;

            // If the messenger is open and we insert all the history messages
            // in our store, we consider all model's messages as seen by our client.
            case INSERT_ALL_MESSAGES:
                const messages = value as IMessage[];
                const messagesFromModel = filter(messages, (m) => m.direction === MessageDirection.in);

                let messageFromModel: IMessage;
                for (messageFromModel of messagesFromModel) {
                    yield put(requestChannelSeen(instanceId, messageFromModel.id));
                }
                break;
        }
    }
};

const watchSagas = function* () {
	yield takeEvery(REQUEST_CHANNEL, requestChannelSaga);
	yield takeEvery(REQUEST_CHANNEL_SEEN, requestChannelSeenSaga);
	yield takeEvery(RECEIVE_CHANNELS, receiveChannelSaga);

    yield takeEvery(ADD_MESSAGE, readModelMessagesSaga);
    yield takeEvery(INSERT_ALL_MESSAGES, readModelMessagesSaga);

};

export default watchSagas;
