import * as React from 'react';
import { differenceWith, some, first, isEqual, isUndefined } from 'lodash';
import { withTheme } from 'styled-components';
import { withResizeDetector } from 'react-resize-detector';
import { isMobile } from "react-device-detect";

import utils from '../../../utils';
import { ChatBoxBlanket, ChatBoxWrapper, GroupChatParticipantsWrapper } from './style';
import { ITheme } from '../../../themes';
import config from '../../../static/config';
import selectors from '../../../redux/selectors';
import { ActionDispatcher } from '../../../redux';
import { requestHistory } from '../../../sagas/websockets/history';
import { setModalContent } from '../../../redux/modules/messenger';
import { IInstanceProps } from '../../hoc/withInstanceInfo';
import withRedux from '../../hoc/withRedux';
import ChatBox from './ChatBox';
import PerfectScrollbarContainer, { ScrollPosition } from '../../../features/common/PerfectScrollbar';
import IStore, {
	IChat,
	IEmoticon,
	IGift,
	IMessage,
	ITranslation,
	IModalContent
} from '../../../redux/store';
import GroupChatParticipantsContainer from "../GroupChatParticipants";
import FontSizeSwitcherContainer from "../FontSizeSwitcher";
import { IChatBoxProps, CONTENT_UPDATE_TYPE, IOrganizedMessage } from './types';
import MessageSectionPopupsContainer from '../../common/MessageSectionPopups';

interface IStoreProps {
	chat?: IChat;
	gifts: IGift[];
	emoticons: IEmoticon[];
	isFullscreen: boolean;
	width: number;
	height: number;
	messages: IMessage[];
	translation: ITranslation;
	isChatExternallyProvided: boolean;
	shouldLiftChatbox: boolean;
	isCountdownWarningOpen: boolean;
	isMobileLandscape: boolean;
	hasHistorySectionsOpened: boolean;
	hasMessagesGrouped: boolean;
	isPlaying: boolean;
	isResponsive: boolean;
	shouldIncreaseMessageFontSize: boolean;
	mentionOptions: string[];
	theme?: ITheme;

	requestHistory: ActionDispatcher;
	setModalContent: ActionDispatcher<IModalContent>;
}

type IProps = IInstanceProps & IStoreProps;

interface IState {
	organizedMessages: IOrganizedMessage;
	isScrollInitialized: boolean;
}

class ChatBoxContainer extends React.Component<IProps, IState> {
	constructor(props: IProps) {
		super(props);

		this.scrollbar = React.createRef();
		this.state = {
			organizedMessages: [],
			isScrollInitialized: false
		};
	}

	// We need a ref to our PerfectScrollbar component in order to
	// call scroll functions on it
	scrollbar: any;

	scrollPosition?: ScrollPosition = ScrollPosition.BOTTOM;

	firstMessageId?: string;

	wasMounted: boolean;

	componentDidMount() {
		// Set the initial store for the organizedMessages
		const organizedMessages = this.getMessages();

		this.setState({ organizedMessages });

		this.wasMounted = true;
	}

	componentDidUpdate(prevProps: IProps) {
		if (this.props.shouldIncreaseMessageFontSize !== prevProps.shouldIncreaseMessageFontSize
			&& this.scrollPosition === ScrollPosition.BOTTOM
		) {
			this.scrollDown()
		}

		const { isPlaying, messages } = this.props;
		// since messenger resizes when player starts to play we need to re-scroll the messages
		if (isPlaying && !prevProps.isPlaying) {
			this.scrollDown();
		}

		if (!isEqual(messages, prevProps.messages)) {
			// If the number of messages has changed, set the new store of the organizedMessages
			const organizedMessages = this.getMessages();
			this.setState({ organizedMessages });

			const isHistoryMessagesUpdate = this.isHistoryMessagesUpdate(
				prevProps.messages,
				messages
			);

			if (isHistoryMessagesUpdate && this.scrollPosition === ScrollPosition.TOP) {
				this.scrollTo(this.firstMessageId);
			} else {
				// it's a new message: scroll down
				this.scrollDown();
			}
		}

		if (!isEqual(prevProps.width, this.props.width)
			&& isUndefined(prevProps.width)) {
			// scroll down, since we now have width and messages were rendered
			this.scrollDown().then(() => {
				if (this.wasMounted) {
					this.setState({ isScrollInitialized: true })
				}
			});
		}
	}

	componentWillUnmount() {
		this.wasMounted = false;
	}

	render() {
		const {
			isCountdownWarningOpen,
			shouldLiftChatbox,
			theme,
			gifts,
			emoticons,
			isFullscreen,
			width
		} = this.props;

		// Set chat box component's properties
		const chatBoxProps: IChatBoxProps = {
			gifts,
			emoticons,
			setModalContent: this.setModalContent,
			isFullscreen,
			isResponsive: width < config.viewSizes.chatboxSectionMaxWidth,
			organizedMessages: this.state.organizedMessages,
			isMobileLandscape: this.props.isMobileLandscape,
			mentionOptions: this.props.mentionOptions,
			hasHistorySectionsOpened: this.props.hasHistorySectionsOpened,
			hasMessagesGrouped: this.props.hasMessagesGrouped,
			width: this.props.width,

			onContentUpdate: this.handleContentUpdate
		};
		const shouldTrimChatbox = isCountdownWarningOpen && this.props.isMobileLandscape;

		const isMobilePortrait = isMobile && !this.props.isMobileLandscape;
		const shouldDisplayParticipants = !isMobilePortrait && !this.props.isFullscreen;

		return (
			<ChatBoxWrapper
				shouldLiftChatbox={shouldLiftChatbox}
				shouldTrimChatbox={shouldTrimChatbox}
				shouldApplyOverflow={this.props.isResponsive && !this.props.isFullscreen}
			>
				{!this.state.isScrollInitialized && <ChatBoxBlanket />}

				{shouldDisplayParticipants &&
				<GroupChatParticipantsWrapper>
					<GroupChatParticipantsContainer />
				</GroupChatParticipantsWrapper>
				}

				<FontSizeSwitcherContainer />

				{!this.props.isMobileLandscape && <MessageSectionPopupsContainer />}

				{!isUndefined(this.props.width) &&
					<PerfectScrollbarContainer
						className="chatbox-scrollbar-container"
						ref={this.scrollbar}
						hasFullHeight={true}
						hasFullWidth={true}
						onScroll={this.onScroll}
						isFullscreen={isFullscreen}
						theme={theme}
						config={{
							options: {
								suppressScrollX: true
							}
						}}
					>
						<ChatBox {...chatBoxProps} />
					</PerfectScrollbarContainer>
				}

			</ChatBoxWrapper>
		);
	}

	private handleContentUpdate = (content: CONTENT_UPDATE_TYPE, message?: IMessage) => () => {
		if (content === CONTENT_UPDATE_TYPE.GROUP) {
			this.updateScroll();
		}
	};

	private onScroll = (scrollPosition: ScrollPosition) => {
		this.scrollPosition = scrollPosition;

		if (scrollPosition === ScrollPosition.TOP) {
			this.firstMessageId = this.getFirstMessageId(this.props.messages);
			this.props.requestHistory(this.props.instanceId);
		}
	};

	private getMessages = () => this.props.hasMessagesGrouped && !this.props.isFullscreen
		? utils.getOrganizedMessages(this.props.messages, this.props.chat?.chatId)
		: this.props.messages;

	private setModalContent = (modalContent?: IModalContent) => () => {
		if (modalContent) {
			this.props.setModalContent(this.props.instanceId, modalContent)
		}
	};

	private getFirstMessageId = (messages: IMessage[]) => {
		const firstMessage = first(messages);
		return firstMessage?.id;
	};

	private updateScroll = () => {
		const scrollbar = this.scrollbar.current;
		if (scrollbar) {
			scrollbar.updateScroll();
		}
	};

	private scrollDown = async () => {
		const scrollbar = this.scrollbar.current;
		if (scrollbar) {
			return scrollbar.scrollDown();
		}
	};

	private scrollTo = (anchorId?: string) => {
		const scrollbar = this.scrollbar.current;
		if (scrollbar) {
			scrollbar.scrollYTo(anchorId);
		}
	};

	private isHistoryMessagesUpdate = (
		prevMessages: IMessage[],
		currentMessages: IMessage[]
	): boolean => {
		const newMessages = differenceWith(currentMessages, prevMessages, isEqual);
		return !some(newMessages, newMessage => !newMessage.isFromHistory);
	};
}

const mapStateToProps = (store: IStore, { instanceId }: IInstanceProps) => ({
	chat: selectors.getChat(store, instanceId),
	gifts: selectors.getTranslatedGifts(store, instanceId),
	emoticons: selectors.getEmoticons(store, instanceId),
	isFullscreen: selectors.isFullScreen(store, instanceId),
	isChatExternallyProvided: selectors.isChatExternallyProvided(store, instanceId),
	messages: selectors.getActiveMessages(store, instanceId),
	translation: selectors.getTranslation(store, instanceId),
	shouldLiftChatbox: selectors.shouldLiftChatbox(store, instanceId),
	isCountdownWarningOpen: selectors.isCountdownWarningOpen(store, instanceId),
	isMobileLandscape: selectors.isMobileLandscape(store, instanceId),
	hasHistorySectionsOpened: selectors.hasHistorySectionsOpened(store, instanceId),
	hasMessagesGrouped: selectors.hasMessagesGrouped(store, instanceId),
	mentionOptions: selectors.getMentionOptions(store, instanceId),
	isPlaying: selectors.isPlaying(store, instanceId),
	isResponsive: selectors.isResponsive(store, instanceId),
	shouldIncreaseMessageFontSize: selectors.getShouldIncreaseMessageFontSize(store, instanceId)
});

const mapDispatchToProps = {
	requestHistory,
	setModalContent
};

export default withRedux(ChatBoxContainer, mapStateToProps, mapDispatchToProps, {
	withResizeDetector,
	withTheme
});
