import { isUndefined } from 'lodash';
import { Action } from 'redux-actions';
import { eventChannel } from "redux-saga";
import { call, put, select, spawn, take, takeEvery } from "redux-saga/effects";

import utils from "../../utils";
import loggers from "../../loggers";
import { createAction, createActionType } from "../../redux/utils";
import { ActionIds, IPayload } from "../../redux/interfaces";
import { pauseChat } from "../specific/chat";
import selectors from '../../redux/selectors';
import { openPrivateChatPrompt } from "./userPrompt";
import { setTranslations } from "../../redux/modules/user";
import { receiveMessageFromChat } from "../websockets/message";
import { QUERYSINGLE } from "../../static/constants/constants";
import { IHost, IOnMessagePayload } from '../../services/types';
import { resetChat, setChatId, setOneClickRecharge } from "../../redux/modules/session";
import { closeMessenger, openMessenger, setEmoticons } from "../../redux/modules/messenger";
import ChatService from "../../services/ChatService";
import {
	Abilities,
	ChargeInfo,
	CoreChat,
	ExitCode,
	Limits,
	Query,
	SourceSet,
	User,
	UserEvent,
	UserEventType
} from "../../services/ChatService/types";
import { onTextMutedUpdated, resumePreviousVideochat, stopVideoChat, switchVoyeurPrivate2Voyeur } from "./videochat";
import PlayerService from '../../services/PlayerService';

import {
	setChatAbility,
	setLivePreviewAbility,
	setModelName,
	setPrivateChatAbility,
	setProfilePicture,
	setTipAbility
} from "../../redux/modules/model";
import {
	addGroupChatParticipant,
	closeVideochat,
	removeGroupChatParticipant,
	resetType,
	setCam2CamStatus,
	setExitEvent,
	setGroupChatClientName,
	setGroupChatParticipants,
	setHasAudio,
	setInformation,
	setIsGroupChat,
	setIsInVoyeurMode,
	setLivePreviewTextTimeLimit,
	setLivePreviewTimeLimit,
	setResumeChatTimestamp,
	setType,
	setVideoChatTimeLimit,
	setVideoChatTimeLimitBelowThreshold
} from "../../redux/modules/videochat";
import {
	CurrencyName,
	ICurrency,
	IEmoticon,
	Translations,
	VideochatInformationType,
	VideoChatType
} from "../../redux/store";

interface IChatStoppedPayload {
	exitCode: ExitCode;
	exitMessage?: string;
}

const createChatEventsActionType = createActionType(ActionIds.CHAT_EVENTS, { isSagaAction: true });

// Actions
const CHAT_STOPPED = createChatEventsActionType("CHAT_STOPPED");
const CHARGE_INFO_RETRIEVED_FROM_CHAT = createChatEventsActionType("CHARGE_INFO_RETRIEVED_FROM_CHAT");

// Action Creators
const chatStopped = createAction<IChatStoppedPayload>(CHAT_STOPPED);
const chargeInfoRetrievedFromChat = createAction<ChargeInfo>(CHARGE_INFO_RETRIEVED_FROM_CHAT);

// Sagas

const subscribeToChatInitListener = emitter => {
	ChatService.registerChatInitListener((instanceId: string, chat: CoreChat) => {
		loggers.saga.debug('Chat initialized; setting stuff in store', { details: { chat }});

		const chatID = chat.id.toString();
		loggers.saga.debug('taking chatId from vchat-core instance', { details: { chatID }});
		emitter(setChatId(instanceId, chatID));

		const isMuted = chat.audioMuted;
		loggers.saga.debug('taking audioMuted from vchat-core instance', { details: { isMuted }});
		emitter(setHasAudio(instanceId, !isMuted));

		const translations: Translations = chat.translations as unknown as Translations;
		loggers.saga.debug('Taking the newest translations from vchat-core instance');
		emitter(setTranslations(instanceId, translations));

		const host: IHost = chat.host;
		loggers.saga.debug('Taking the model name from vchat-core instance', { details: host });
		emitter(setModelName(instanceId, host.name));
		emitter(setProfilePicture(instanceId, host.imageSrc));

		const emoticons: IEmoticon[] = chat.emojiList;
		loggers.saga.debug('Taking emoticons from vchat-core instance', { details: emoticons });
		emitter(setEmoticons(instanceId, emoticons));

		const users: User[] = chat.currentUsers || [];
		loggers.saga.debug('Taking group chat participants info from vchat-core instance',
			{ details: users }
		);
		emitter(setGroupChatParticipants(instanceId, users.map(user => user.userName)));

		const username = chat.username || "";
		loggers.saga.debug("Client's username during the group chat from vchat-core instnace",
			{ details: username }
		);
		emitter(setGroupChatClientName(instanceId, username));

		const abilities: Abilities = chat.abilities;
		loggers.saga.debug('Taking abilities from vchat-core instance', { details: abilities });
		if (abilities) {
			emitter(setCam2CamStatus(instanceId, abilities.cam2cam));
			emitter(setChatAbility(instanceId, abilities.video));
			emitter(setLivePreviewAbility(instanceId, abilities.preview));
			emitter(setPrivateChatAbility(instanceId, abilities.single));
			emitter(setTipAbility(instanceId, abilities.tip));
		}

		const isGroupChat = chat.isGroupChat;
		loggers.saga.debug('Taking isGroupChat from vchat-core instance', { details: isGroupChat });
		emitter(setIsGroupChat(instanceId, isGroupChat));

		// @ts-ignore
		const isInVoyeurMode = chat.isInVoyeurMode;
		loggers.saga.debug('Taking isInVoyeurMode from vchat-core instance', { details: isInVoyeurMode });
		emitter(setIsInVoyeurMode(instanceId, isInVoyeurMode));
	});
};

const subscribeToLimitsChange = emitter => {
	ChatService.registerLimitsChangeListener((instanceId: string, limits: Partial<Limits>) => {
		if (!isUndefined(limits.video)) {
			emitter(setVideoChatTimeLimit(instanceId, limits.video / 1000));
		}

		if (!isUndefined(limits.preview)) {
			emitter(setLivePreviewTimeLimit(instanceId, limits.preview / 1000));

			if (!isUndefined(limits.text)) {
				emitter(setLivePreviewTextTimeLimit(instanceId, (limits.text - limits.preview) / 1000));
			}
		}
	});
};

const subscribeToVideoLimitWarningUpdate = emitter => {
	ChatService.registerVideoLimitWarningListener((instanceId, isBelowThreshold, value) => {
		emitter(setVideoChatTimeLimitBelowThreshold(instanceId, isBelowThreshold));
		emitter(setVideoChatTimeLimit(instanceId, value / 1000));
	});
};

const subscribeToOnMessageListener = emitter => {
	ChatService.registerOnMessageListener((instanceId: string, payload: IOnMessagePayload) => {
		emitter(receiveMessageFromChat(instanceId, payload));
	});
};

const subscribeToAbilityUpdate = emitter => {
	ChatService.registerAbilityUpdateListener((instanceId: string, name: keyof Abilities, value: boolean) => {
		loggers.saga.debug('Ability update ', { details: { name, value } });
		switch (name) {
			case 'video':
				emitter(setChatAbility(instanceId, value));
				break;
			case 'preview':
				emitter(setLivePreviewAbility(instanceId, value));
				break;
			case 'single':
				emitter(setPrivateChatAbility(instanceId, value));
				break;
			case 'tip':
				emitter(setTipAbility(instanceId, value));
				break;
			case 'text':
				if (value) {
					emitter(switchVoyeurPrivate2Voyeur(instanceId));
				}
				break;
		}
	});
};

const subscribeToQuery = emitter => {
	ChatService.registerQueryListener((instanceId: string, query: Query) => {
		if (query.key === QUERYSINGLE && query.text) {
			emitter(openPrivateChatPrompt(instanceId, query.text));
		}
	});
};

const subscribeToSingleModeUpdate = emitter => {
	ChatService.registerSingleModeUpdateListener((instanceId: string, value: boolean) => {
		if (value) {
			emitter(setType(instanceId, VideoChatType.PRIVATE_CHAT));
		}
	});
};

const subscribeToChatStop = emitter => {
	ChatService.registerChatStopListener((instanceId: string, exitCode: ExitCode, exitMessage?: string) => {
		loggers.saga.debug('chat stopped', { details: { exitCode, exitMessage } });
		emitter(chatStopped(instanceId, { exitCode, exitMessage }));
	});
};

const subscribeToChatPause = emitter => {
	ChatService.registerChatPauseListener((instanceId: string) => {
		emitter(pauseChat(instanceId));
	});
};

const subscribeToChatResume = emitter => {
	ChatService.registerChatResumeListener((instanceId: string) => {
		loggers.saga.debug('chat resumed');
	});
};

const subscribeToTextMuteUpdate = emitter => {
	ChatService.registerTextMuteUpdateListener((instanceId: string, value) => {
		loggers.saga.debug('text mute updated to', { details: { value } });
		emitter(onTextMutedUpdated(instanceId, !value));
	});
};

const subscribeToAudioMuteUpdate = emitter => {
	ChatService.registerAudioMuteUpdateListener((instanceId: string, isMuted) => {
		loggers.saga.debug('audio mute updated to', { details: { isMuted } });
		const modelHasAudio = !isMuted;
		emitter(setHasAudio(instanceId, modelHasAudio));
	});
};

const subscribeToChargeInfo = emitter => {
	ChatService.registerChargeInfoListener((instanceId: string, chargeInfo: ChargeInfo) => {
		loggers.saga.debug('Taking charge info from vchat-core instance', { details: chargeInfo });
		emitter(chargeInfoRetrievedFromChat(instanceId, chargeInfo));
	});
};

const subscribeToOnStreamSourceUpdateListener = emitter => {
	ChatService.registerStreamSourceUpdateListener((instanceId: string, updatedSourceSet: SourceSet) => {
		PlayerService.getService(instanceId).play(updatedSourceSet);
	});
};

const subscribeToUserEventListener = emitter => {
	ChatService.registerUserEventListener((instanceId: string, event: UserEvent) => {
		loggers.saga.debug(`Group chat participant ${event.type} the chat`);
		emitter(
			event.type === UserEventType.Entered
				? addGroupChatParticipant(instanceId, event.user.userName)
				: removeGroupChatParticipant(instanceId, event.user.userName)
		);
	});
};

const chatEventsChannel = () => {
	return eventChannel(emitter => {
		subscribeToChatInitListener(emitter);

		subscribeToLimitsChange(emitter);
		subscribeToVideoLimitWarningUpdate(emitter);
		subscribeToAbilityUpdate(emitter);
		subscribeToQuery(emitter);
		subscribeToSingleModeUpdate(emitter);
		subscribeToChatStop(emitter);
		subscribeToChatPause(emitter);
		subscribeToChatResume(emitter);
		subscribeToOnMessageListener(emitter);
		subscribeToTextMuteUpdate(emitter);
		subscribeToAudioMuteUpdate(emitter);
		subscribeToChargeInfo(emitter);
		subscribeToOnStreamSourceUpdateListener(emitter);
		subscribeToUserEventListener(emitter);

		return () => {
			// no need for un-subscribing; saga's live exceeds the app live
		}
	});
};

/** Sagas */


/** The chat information types to be displayed for each chat exit code */
const chatStopInformations: { [exitCode: number]: any } = {};
chatStopInformations[ExitCode.HOST] = VideochatInformationType.HOST_OFFLINE;
chatStopInformations[ExitCode.HOSTKICK] = VideochatInformationType.EJECTED;
chatStopInformations[ExitCode.FSKDENIED] = VideochatInformationType.VERIFY_AGE;
chatStopInformations[ExitCode.VOICECALLHANGUP] = VideochatInformationType.VOICECALL_ENDED;
chatStopInformations[ExitCode.HOSTSINGLE] = VideochatInformationType.PRIVATE_CHAT;
chatStopInformations[ExitCode.HOSTSINGLE] = VideochatInformationType.PRIVATE_CHAT;
chatStopInformations[ExitCode.VIDEOTIMELIMIT] = VideochatInformationType.OUT_OF_MONEY;
chatStopInformations[ExitCode.TEXTTIMELIMIT] = VideochatInformationType.TEXTTIMELIMIT;

const isErrorCode = (exitCode: ExitCode) =>
	exitCode !== ExitCode.USER &&
	exitCode !== ExitCode.CLIENTDISCONNECT &&
	exitCode !== ExitCode.CLIENTNEWCHAT;

const isGenericErrorCode = (exitCode: ExitCode) => isErrorCode(exitCode)
	&& !chatStopInformations[exitCode];

// Sagas
const chatStoppedSaga = function* ({ payload }: Action<IPayload<IChatStoppedPayload>>) {
	const { instanceId, value } = payload;

	if (value.exitCode === ExitCode.VIDEOFAILGUEST) {
		const resumeChatTimestamp = yield select(selectors.resumeChatTimestamp, instanceId);

		if (utils.shouldResetChat(resumeChatTimestamp)) {
			yield put(setResumeChatTimestamp(instanceId, new Date()));

			// cannot use the existing chat instance anymore; force getting a new one
			yield put(resetChat(instanceId));
			yield put(resetType(instanceId));

			yield put(resumePreviousVideochat(instanceId));

			return;
		}
	}

	if (value.exitCode === ExitCode.TEXTTIMELIMIT) {
		yield put(closeVideochat(instanceId));
		yield put(openMessenger(instanceId));
		return;
	}

	const isPlaying = yield select(selectors.isPlaying, instanceId);
	if (isPlaying) {
		yield put(stopVideoChat(instanceId));
	}

	const { exitCode, exitMessage } = value;

	// save the last error
	yield put(setExitEvent(instanceId, { exitCode, exitMessage }));

	// check if the exit popups are supposed to be handled externally or not
	const hasExitPopups = yield select(selectors.hasExitPopups, instanceId);

	// show our exit pop-ups
	if (hasExitPopups) {
		const chatInformation = chatStopInformations[exitCode];

		if (chatInformation) {
			yield put(setInformation(instanceId, chatInformation));
		} else if (isGenericErrorCode(exitCode)) {
			yield put(setInformation(instanceId, VideochatInformationType.ERROR_OCCURRED));
		}
	} else {
		// displaying exit pop-ups is on the platform to be created/ displayed
		if (isErrorCode(exitCode)) {
			yield put(closeMessenger(instanceId));
		}
	}
};

const chargeInfoRetrievedSaga = function* ({ payload }: Action<IPayload<ChargeInfo>>) {
	const { instanceId, value: chargeInfo } = payload;

	if (!chargeInfo) {
		yield put(setOneClickRecharge(instanceId, {
			available: false,
			amounts: [],
			defaultAmount: null
		}));

		return;
	}

	const currency: ICurrency = yield select(selectors.getCurrency, instanceId);

	const { available } = chargeInfo;
	let amounts = chargeInfo.amounts;
	let defaultAmount = utils.getPreselectedOneClickAmount(amounts);

	// for other currencies than Credits, the amounts from the BE are expressed as cents
	if (currency.name !== CurrencyName.Credits) {
		defaultAmount /= 100;
		amounts = amounts.map((amount: number) => amount / 100);
	}

	yield put(setOneClickRecharge(instanceId, {
		available,
		amounts,
		defaultAmount
	}));
};

const listenForChatEventsSaga = function* () {
	const chatEvents = yield call(chatEventsChannel);

	while (true) {
		const action = yield take(chatEvents);
		yield put(action);
	}
};

const watchSaga = function* () {
	yield spawn(listenForChatEventsSaga);
	yield takeEvery(CHAT_STOPPED, chatStoppedSaga);
	yield takeEvery(CHARGE_INFO_RETRIEVED_FROM_CHAT, chargeInfoRetrievedSaga);
};

export default watchSaga;
