import * as React from 'react';
import { ProgressBar, ProgressBarKnob } from './style';

interface IProps {
    currentPercentage: number;
    audioPlayerBarRef: any;
    onMouseDown: (e: React.MouseEvent) => void;
}

const AudioPlayerBar = (props: IProps) => (
    <ProgressBar
        innerRef={props.audioPlayerBarRef}
        onMouseDown={props.onMouseDown}
    >
        <ProgressBarKnob currentPercentage={props.currentPercentage} />
    </ProgressBar>
);

export default AudioPlayerBar;
