import { isMobile } from 'react-device-detect';
import { get, isEmpty, isUndefined, filter, find } from 'lodash';
import {
    Abilities,
    ChargeInfo,
    Chat as CoreChat,
    ChatConfig,
    ChatHandler,
    ExitCode,
    Intent,
    Limits,
    MessageKey,
    Query,
    SourceSet,
    TargetSet,
    User,
    UserEvent,
    UserEventType
} from 'vchat-core';
import request from 'superagent';

import loggers from '../../loggers';
import {
    IOnMessagePayload,
    OK_RESULT,
    OK_RESULT_VALUE,
    PrivateChatInvitationAnswer,
    PromiseResult
} from "../types";
import { MessageContentType, MessageDirection, MsgType } from "../../redux/store";
import { GET_CHAT_ERROR } from "./types";

enum ChatStatus {
    /** Initial state */
    NONE,

    /** existing chat instance is not reusable, a new one (based on a new clientId) should be created */
    NOT_REUSABLE,

    /** chat instance was successfully initialized */
    INITIALIZED,

    /** chat instance is about to be closed */
    CLOSING,

    /** chat instance was closed */
    CLOSED
};

interface IListeners {
    limitsChangeListener: ((instanceId: string, limits: Partial<Limits>) => any),
    videoLimitWarningListener: ((instanceId: string, isBelowThreshold: boolean, value: number) => any),
    abilityUpdateListener: ((instanceId: string, name: keyof Abilities, value: boolean) => any),
    singleModeUpdateListener: ((instanceId: string, value: boolean) => any),
    queryListener: ((instanceId: string, query: Query) => any),
    chatPauseListener: ((instanceId: string) => any),
    chatResumeListener: ((instanceId: string) => any),
    chatStopListener: (instanceId: string, exitCode: ExitCode, exitMessage?: string) => any,
    textMuteUpdateListener: ((instanceId: string, value: boolean) => any),
    onMessageListener: (instanceId: string, payload: IOnMessagePayload) => any,
    onAudioMuteUpdateListener: (instanceId: string, isMuted: boolean) => any,
    onStreamSourceUpdateListener: (instanceId: string, updatedSourceSet: SourceSet) => any,
    chargeInfoListener: (instanceId: string, chargeInfo: ChargeInfo) => any,
    userEventListener: (instanceId: string, event: UserEvent) => any,

    chatInitListener: (instanceId: string, chat: CoreChat) => any
};

class ChatService {

    private static services: ChatService[] = [];
    private static listeners: Partial<IListeners> = {};

    constructor(instanceId: string) {
        this.instanceId = instanceId;
    }

    private instanceId: string;

    private chat: CoreChat | null;
    private chatConfig: ChatConfig | null;

    private sources: SourceSet | null;
    private targets: TargetSet | null;

    private chatStatus: ChatStatus = ChatStatus.NONE;

    private exitCode: ExitCode;

    private initializing: Promise<PromiseResult<CoreChat, any>> | null;

    private audioState: boolean;

    static registerLimitsChangeListener(listener: (instanceId: string, limits: Partial<Limits>) => any) {
        ChatService.listeners.limitsChangeListener = listener;
    }

    static registerVideoLimitWarningListener(listener: (instanceId: string, isBelowThreshold: boolean, value: number) => any) {
        ChatService.listeners.videoLimitWarningListener = listener;
    }

    static registerAbilityUpdateListener(listener: (instanceId: string, name: keyof Abilities, value: boolean) => any) {
        ChatService.listeners.abilityUpdateListener = listener;
    }

    static registerSingleModeUpdateListener(listener: (instanceId: string, value: boolean) => any) {
        ChatService.listeners.singleModeUpdateListener = listener;
    }

    static registerQueryListener(listener: (instanceId: string, query: Query) => any) {
        ChatService.listeners.queryListener = listener;
    }

    static registerChatStopListener(listener: (instanceId: string, code: ExitCode) => any) {
        ChatService.listeners.chatStopListener = listener;
    }

    static registerOnMessageListener(handler: (instanceId: string, payload: IOnMessagePayload) => any) {
        ChatService.listeners.onMessageListener = handler;
    }

    static registerChatPauseListener(listener: (instanceId: string) => any) {
        ChatService.listeners.chatPauseListener = listener;
    }

    static registerChatResumeListener(listener: (instanceId: string) => any) {
        ChatService.listeners.chatResumeListener = listener;
    }

    static registerTextMuteUpdateListener(listener: (instanceId: string, isMuted: boolean) => any) {
        ChatService.listeners.textMuteUpdateListener = listener;
    }

    static registerAudioMuteUpdateListener(listener: (instanceId: string, isMuted: boolean) => any) {
        ChatService.listeners.onAudioMuteUpdateListener = listener;
    }

    static registerStreamSourceUpdateListener(listener: (instanceId: string, updatedSourceSet: SourceSet) => any) {
        ChatService.listeners.onStreamSourceUpdateListener = listener;
    }

    static registerChargeInfoListener(listener: (instanceId: string, chargeInfo: ChargeInfo) => any) {
        ChatService.listeners.chargeInfoListener = listener;
    }

    static registerUserEventListener(listener: (instanceId: string, event: UserEvent) => any) {
        ChatService.listeners.userEventListener = listener;
    }

    static registerChatInitListener(listener: (instanceId: string, chat: CoreChat | User[]) => any) {
        ChatService.listeners.chatInitListener = listener;
    }

    static getService(instanceId: string) {
        let chatService =
            find(ChatService.services, service => service.getInstanceId() === instanceId);

        if (!chatService) {
            chatService = new ChatService(instanceId);
            ChatService.services.push(chatService);
        }

        return chatService;
    }

    static destroyService(instanceId: string) {
        const chatService = find(ChatService.services, service => service.getInstanceId() === instanceId);

        if (chatService) {
            chatService.closeChat();
            ChatService.services =
                filter(ChatService.services,service => service.getInstanceId() !== instanceId);
        }
    }

    private getChatHandler: () => ChatHandler = () => {
        const { instanceId } = this;

        return {
            onChatStop: (exitCode: ExitCode, exitMessage?: string) => {
                loggers.chat.debug('onChatStop called with', { details: { exitCode } });

                // Some codes will not come as numbers and we should cast them
				if (typeof exitCode === "string") {
					exitCode = parseInt(exitCode, 10);
				}
				this.exitCode = exitCode;

                this.chatStatus = ChatStatus.NOT_REUSABLE;
                if (this.chat && ChatService.listeners.chatStopListener) {

                    ChatService.listeners.chatStopListener(instanceId, exitCode, exitMessage);

                    // Time limiting exit codes
                    if (ChatService.listeners.limitsChangeListener) {
                        if (exitCode === ExitCode.VIDEOTIMELIMIT) {
                            ChatService.listeners.limitsChangeListener(instanceId, { video: 0 });
                        }
                    }
                }
            },

            onChatPause: () => {
                loggers.chat.debug('onChatPause');
                if (this.chat && ChatService.listeners.chatPauseListener) {
                    ChatService.listeners.chatPauseListener(instanceId);
                }
            },

            onChatResume: (sourceSet: SourceSet) => {
                loggers.chat.debug('onChatResume');

                if (isEmpty(sourceSet)) {
                    return;
                }

                this.sources = sourceSet;
                if (this.chat && ChatService.listeners.chatResumeListener) {
                    ChatService.listeners.chatResumeListener(instanceId);
                }
            },

            onMessage: (text: string, from?: string, key?: keyof MessageKey, params?: any) => {
                loggers.chat.debug('onMessage called with', { details: { text, from, key, params } });

                const imgSrc = get(params, 'imgSrc');
                const imgSrcSet = get(params, 'imgSrcSet');
                const imgWidth = get(params, 'imgWidth');
                const imgHeight = get(params, 'imgHeight');
                const mediaType = get(params, 'mediaType');
                const audioMp3 = get(params, 'audioMp3');

                const payload: IOnMessagePayload = {
                    text,
					msgType: params?.msgType || MsgType.CHAT,
					username: from,
                };

                if (!isUndefined(imgSrc)) {
                    payload.imgSrc = imgSrc;
                    payload.imgSrcSet = imgSrcSet;
                    payload.imgWidth = imgWidth;
                    payload.imgHeight = imgHeight;
                }

                if (!isUndefined(mediaType) && mediaType === 'Audio') {
                    // Ignoring the audio messages with no audioSrc
                    // TODO: Implement loading feature
                    if (isUndefined(audioMp3)) {
                        return;
                    }

                    payload.audioSrc = audioMp3;
                    payload.contentType = MessageContentType.AUDIO;
                }

                if (!isUndefined(key)) {
                    payload.key = key;
                }

                if (this.chat && ChatService.listeners.onMessageListener) {
                    ChatService.listeners.onMessageListener(instanceId, payload);
                }
            },

            onAbilityUpdate: (name: keyof Abilities, value: boolean) => {
                loggers.chat.debug('onAbilityUpdate called with', { details: { name, value } });

                if (isEmpty(name)) {
                    return;
                }

                if (this.chat && ChatService.listeners.abilityUpdateListener) {
                    ChatService.listeners.abilityUpdateListener(instanceId, name, value);
                }
            },

            onQuery: (query: Query) => {
                loggers.chat.debug('onQuery called with', { details: { query } });

                if (isEmpty(query)) {
                    return;
                }

                if (this.chat && ChatService.listeners.queryListener) {
                    ChatService.listeners.queryListener(instanceId, query);
                }
            },

            onSingleModeUpdate: (value: boolean) => {
                loggers.chat.debug('onSingleModeUpdate called with', { details: { value } });

                if (this.chat && ChatService.listeners.singleModeUpdateListener) {
                    ChatService.listeners.singleModeUpdateListener(instanceId, value);
                }
            },

            onTextMuteUpdate: (value: boolean) => {
                loggers.chat.debug('onTextMuteUpdate called with', { details: { value } });

                if (this.chat && ChatService.listeners.textMuteUpdateListener) {
                    ChatService.listeners.textMuteUpdateListener(instanceId, value);
                }
            },

            onStreamSourceUpdate: (updatedSourceSet: SourceSet) => {
                loggers.chat.debug('onStreamSourceUpdate called with', { updatedSourceSet });

                if (this.chat && ChatService.listeners.onStreamSourceUpdateListener) {
                    ChatService.listeners.onStreamSourceUpdateListener(instanceId, updatedSourceSet);
                }
            },

            onAudioMuteUpdate: (isMuted: boolean) => {
                loggers.chat.debug('onAudioMuteUpdate called with', { details: { isMuted } });

                if (this.chat && ChatService.listeners.onAudioMuteUpdateListener) {
                    ChatService.listeners.onAudioMuteUpdateListener(instanceId, isMuted);
                }
            },

            onLimitUpdate: (param: keyof Limits, value: number) => {
                loggers.chat.debug('onLimitUpdate called with', { details: { param, value } });
                if (isEmpty(param)) {
                    return;
                }

                if (this.chat && ChatService.listeners.limitsChangeListener) {
                    const limits: Partial<Limits> = {};
                    limits[param] = value;
                    ChatService.listeners.limitsChangeListener(instanceId, limits);
                }
            },

            onVideoLimitWarningUpdate: (isBelowThreshold: boolean, value: number) => {
                loggers.chat.debug('onVideoLimitWarningUpdate called with',
                    { details: { isBelowThreshold, value } });

                /*
                   this is to overcome a very strange API behavior, undocumented but confirmed verbally by Marco Spatz:
                   unless charge info is queried, we cannot benefit from those 2 minutes "grace period"
                   of paused chat, if run out of money;
                   also, the query should be done EACH TIME we're about to run out of money
                */
                if (isBelowThreshold && this.chat) {
                    this.chat.getChargeInfo().then((chargeInfo: ChargeInfo) => {
                        if (ChatService.listeners.chargeInfoListener) {
                            ChatService.listeners.chargeInfoListener(instanceId, chargeInfo);
                        }
                    });
                }

                if (this.chat && ChatService.listeners.videoLimitWarningListener) {
                    ChatService.listeners.videoLimitWarningListener(instanceId, isBelowThreshold, value);
                }
            },

            onUserEvent: (event: UserEvent) => {
                loggers.chat.debug('onUserEvent called with', { details: { event } });
                if (this.chat && ChatService.listeners.userEventListener) {
                    ChatService.listeners.userEventListener(instanceId, event);
                }
            }
        };
    };

    private initializeChat = (chat: CoreChat): Promise<PromiseResult<CoreChat, any>> => {
        const { instanceId } = this;

        return chat.init()
            .then(async (result: { intent: Intent; limits: Limits; }) => {
                if (ChatService.listeners.chatInitListener) {
                    ChatService.listeners.chatInitListener(instanceId, chat);
                }

                if (ChatService.listeners.limitsChangeListener) {
                    ChatService.listeners.limitsChangeListener(instanceId, result.limits);
                }

                if (ChatService.listeners.chargeInfoListener) {
                    const chargeInfo = await chat.getChargeInfo();
                    ChatService.listeners.chargeInfoListener(instanceId, chargeInfo);
                }

                this.chatStatus = ChatStatus.INITIALIZED;
                loggers.chat.debug('chat instance initiated successfully');
                return ({ result: chat });
            })
            .catch(error => {
                this.chatStatus = ChatStatus.NONE;
                loggers.chat.debug('chat instance failed to initialize', { details: { error } });
                return ({ error });
            });
    }

    private destroyChat = () => {
        this.sources = null;
        this.targets = null;

        this.chatStatus = ChatStatus.NONE;

        this.initializing = null;

        this.chat = null;
        this.chatConfig = null;
    }

    private isNewChatConfig = (config: ChatConfig): boolean  => {
        if (isEmpty(config)) {
            return false;
        }

        const existingConfig = this.chatConfig;
        const isNewConfig = (config.clientId !== (existingConfig && existingConfig.clientId));
        return isNewConfig;
    }

    private getNewChat = (chatConfig: ChatConfig): Promise<PromiseResult<CoreChat, any>> => {
        return new Promise<PromiseResult<CoreChat, any>>((resolve) => {
            this.closeChat().then(() => {
                this.getExistingOrBuildAChat(chatConfig)
                    .then((chatOrError: PromiseResult<CoreChat, any>) => {
                        resolve(chatOrError);
                    });
            }).catch(error => {
                resolve({ error });
            });
        });
    }

    private getExistingOrBuildAChat = (chatConfig: ChatConfig): Promise<PromiseResult<CoreChat, any>> => {
        return new Promise<PromiseResult<CoreChat, any>>(async (resolve) => {
            let chat: CoreChat;

            if (!this.chat) {
                loggers.chat.debug('creating a new chat instance');

                chat = new CoreChat(chatConfig, this.getChatHandler());
                this.chatConfig = chatConfig;
                this.chat = chat;
                this.chatStatus = ChatStatus.NONE;
                this.initializing = null;
                this.sources = null;
                this.targets = null;
            } else {
                loggers.chat.debug('using an existing chat instance');
                chat = this.chat;
            }

            if (this.chatStatus !== ChatStatus.INITIALIZED) {
                if (!this.initializing) {
                    this.initializing = this.initializeChat(chat);
                }
                const { result, error } = await this.initializing;
                resolve(error ? { error } : { result });
            } else {
                resolve({ result: chat });
            }
        });
    }

    getInstanceId = () => this.instanceId;

    getChat = (chatConfig: ChatConfig): Promise<PromiseResult<CoreChat, GET_CHAT_ERROR | any>> => {
        /** If new chat config was provided, then discard the old chat and recreate */
        const isConfigNew = this.isNewChatConfig(chatConfig);

        /**
         * If the current chat cannot be reused, but we got the same chat config,
         * raise an exception signalling that chat config is outdated
         */
        const isExistingChatNotReusable = this.chatStatus === ChatStatus.NOT_REUSABLE
            && !this.isNewChatConfig(chatConfig);

        if (isConfigNew) {
            return this.getNewChat(chatConfig);
        } else {
            return isExistingChatNotReusable
                ? Promise.resolve({ error: GET_CHAT_ERROR.BAD_CHAT_CONFIG })
                : this.getExistingOrBuildAChat(chatConfig);
        }
    }

    sendQueryResponse = (chat: CoreChat, key: string, response: PrivateChatInvitationAnswer): Promise<PromiseResult<any, any>> =>
        // TODO remove chat from params list, use the instance one
        new Promise<PromiseResult<any, any>>(resolve => {
            chat.sendQueryResponse(key, response)
                .then((result) => resolve({ result }))
                .catch(error => resolve({ error }));
        });

    /**
     * It starts the message communication between the client and the model through
     * the chat instance. If the the ability canText is true => enable the text first
     * and then use the sendMessage function.
     */
    startText = (chat: CoreChat): Promise<boolean> => {
        // TODO remove chat from params list, use the instance one
		loggers.chat.debug('Starting text ability...');
        return new Promise<boolean>(resolve => {
            chat.startText()
                .then(result => resolve(true))
                .catch(error => resolve(false));
        });
    };

    sendMessage = (text: string): Promise<any> => {
        return new Promise<any>(resolve => {
            if (this.chat) {
				loggers.chat.debug('Sending a message', { text });

                this.chat.sendMessage(text)
                    .then(result => {
                        loggers.chat.debug('Message successfully sent');

                        if (ChatService.listeners.onMessageListener) {
                            ChatService.listeners.onMessageListener(this.instanceId, {
								text,
								msgType: MsgType.CHAT,
                                direction: MessageDirection.out
                            });
                        }

                        resolve({ result, isError: false });
                    })
                    .catch(error => {
                        loggers.chat.debug('Message sending has failed', { details: { error } });
	                    resolve({ error, isError: true });
                    });
            } else {
                resolve({ error: true });
            }
        });
    };

    sendMediaFile = (file: File, messageKey?: string): Promise<PromiseResult<any, any>> => (
        new Promise<any>(resolve => {
            if (this.chat) {
                loggers.chat.debug('Sending media file', { file });
                this.chat.sendMediaFile(file, messageKey || '')
                    .then(result => {
                            loggers.chat.debug('Media file successfully sent');
                            resolve({ result });
                        }
                    )
                    .catch(error => {
                        loggers.chat.debug('Media file sending has failed', { details: { error } });
                        resolve({ error });
                    })
            } else {
                resolve({ error: true });
            }
        })
    );

    getStream = (chat: CoreChat, previewMode?: boolean): Promise<PromiseResult<SourceSet, any>>  => {
        // TODO remove chat from params list, use the instance one
        return new Promise<PromiseResult<SourceSet, any>>(resolve => {
            const isOurChat = this.chat === chat;
            const streamConfig: any = { mobile: isMobile };

            if (previewMode) {
                streamConfig.type = 'preview';
            }

            if (isOurChat && this.sources) {
                loggers.chat.debug('using an existing video stream');
                resolve({ result: this.sources });
            } else {
                chat.startStream(streamConfig).then((sources: SourceSet) => {
                    if (isOurChat) {
                        this.sources = sources;
                    }

                    loggers.chat.debug('starting a new video stream');
                    resolve({ result: sources });
                }).catch(error => {
                    loggers.chat.debug(
                        'starting stream failed with the following reason', { details: { error } }
                    );
                    resolve({ error });
                });
            }
        });
    };

    startPrivateChat = (chat: CoreChat): Promise<PromiseResult<any, any>> => {
        // TODO remove chat from params list, use the instance one
        return new Promise<PromiseResult<any, any>>(resolve => {
            chat.startSingle().then((result) => {
                loggers.chat.debug('started private chat', { details: { result } });
                resolve({ result });
            })
                .catch(error => {
                    loggers.chat.debug('failed to start private chat', { details: { error } });
                    resolve({ error })
                });
        });
    };

    stopUpstream = (): Promise<PromiseResult<OK_RESULT, any>> => {
        return new Promise<PromiseResult<OK_RESULT, any>>((resolve) => {
            if (this.chat) {
                this.chat.stopUpstream()
                    .then((result) => {
                        this.targets = null;
                        loggers.chat.debug('upstream stopped successfully');
                        resolve({ result: OK_RESULT_VALUE });
                    })
                    .catch(error => {
                        loggers.chat.debug('stopping upstream raised this error:', { details: { error } });
                        resolve({ error });
                    });
            } else {
                loggers.chat.debug('no chat to stop upstream for');
                resolve({ result: OK_RESULT_VALUE });
            }
        });

    };

    getUpstream = (chat: CoreChat): Promise<PromiseResult<TargetSet, any>> => {
        // TODO remove chat from params list, use the instance one
        return new Promise<PromiseResult<TargetSet, any>>((resolve) => {
            const isOurChat = this.chat === chat;

            if (isOurChat && this.targets) {
                loggers.chat.debug('using an existing up stream');
                resolve({ result: this.targets });
            } else {
                chat.startUpstream().then((targets: TargetSet) => {
                    loggers.chat.debug('upstream (re)started successfully');
                    if (isOurChat) {
                        this.targets = targets;
                    }
                    resolve({ result: targets });
                }).catch(error => {
                    loggers.chat.debug('upstream (re)starting failed with the following reason',
                        { details: { error } });
                    resolve({ error });
                });
            }
        });
    };

    closeChat = (isUserIntended?: boolean): Promise<PromiseResult<OK_RESULT, any>> => {
        const userIntendedExitCode = ExitCode.USER;
        return new Promise<PromiseResult<OK_RESULT, any>>((resolve) => {
            if (this.chat) {
                if (this.chatStatus !== ChatStatus.CLOSING) {
                    this.chatStatus = ChatStatus.CLOSING;

                    let closeArgs: ExitCode[] = isUserIntended ? [userIntendedExitCode] : [];
                    if (this.exitCode) {
						closeArgs = [this.exitCode];
					}

                    this.chat
                        .close.apply(this.chat, closeArgs)
                        .then((result) => {
                            loggers.
							chat.debug(`chat instance closed successfully with arguments ${closeArgs.join()}`);
                            this.chatStatus = ChatStatus.CLOSED;
                            this.destroyChat();
                            resolve({ result: OK_RESULT_VALUE });
                        })
                        .catch(error => {
                            loggers.chat.debug('closing chat raised this error:', { details: { error } });
                            this.chatStatus = ChatStatus.CLOSED;
                            if (isUserIntended && ChatService.listeners.chatStopListener) {
                                ChatService.listeners.chatStopListener(this.instanceId, userIntendedExitCode);
                            }
                            this.destroyChat();
                            resolve({ error });
                        });
                }
            } else {
                loggers.chat.debug('no chat to close');
                resolve({ result: OK_RESULT_VALUE });
            }
        });
    };

    sendTip = (chat: CoreChat, amount: number): Promise<PromiseResult<any, any>> => {
        // TODO remove chat from params list, use the instance one
        return chat && chat.sendTip(amount, "")
            .then(result => {
                loggers.chat.debug('tip sent successfully');
                return { result };
            })
            .catch(error => {
                loggers.chat.debug('sending tip raised this error', { details: { error } });
                return { error };
            });
    };

    sendMetrics = (info) => {
        if (this.chat) {
            this.chat.sendMetrics(info);
        }
    };

    sendCharge = (chat: CoreChat, amount: number): Promise<PromiseResult<any, any>> => {
        // TODO remove chat from params list, use the instance one
        loggers.chat.debug('recharging with...', { details: { amount } });

        return chat && chat.sendCharge(amount)
            .then(result => {
                loggers.chat.debug('charge sent successfully');
                return { result };
            })
            .catch(error => {
                loggers.chat.debug('sending charge raised this error', { details: { error } });
                return { error };
            });
    };

    getChargeInfo = (chat: CoreChat): Promise<PromiseResult<ChargeInfo, any>> => {
        // TODO remove chat from params list, use the instance one
        return chat && chat.getChargeInfo()
            .then(result => {
                loggers.chat.debug('charge info successfully retrieved', { details: { result } });
                return { result };
            })
            .catch(error => {
                loggers.chat.debug('getting charge info raised this error', { details: { error } });
                return { error };
            });
    };

    sendAudioState = (enabled: boolean): Promise<any> => {
        return new Promise<any>(resolve => {
            if (this.chat) {
            	if (this.audioState === enabled) {
            		// don't call the API with the same value repeatedly
					resolve({ result: true });
					return;
				}

				this.chat.sendAudioState(enabled)
                    .then(result => {
                        loggers.chat.debug('audio state change successfully sent', { details: { result } })

						this.audioState = enabled;
                        resolve({ result })
                    })
                    .catch(error => {
                        loggers.chat.debug('audio state change has failed', { details: { error } });

                        resolve({ error })
                    });
            } else {
                resolve({ error: true })
            }
        });
    };

    uploadImage = (chat: CoreChat, data: FormData): Promise<PromiseResult<any, any>> => {
        return new Promise<PromiseResult<any, any>>((resolve) => {
            if (chat) {
                const imageUploadUrl = chat.uploadUrl;
                request.post(imageUploadUrl)
                    .send(data)
                    .set("Accept", "application/json")
                    .end((error, result) => {
                        if (error) {
                            resolve({ error });
                        } else {
                            resolve({ result });
                        }
                    });
            } else {
                resolve({ error: true })
            }
        });
    }
}

export default ChatService;

// re-export vchat-core things and use them from here, to avoid testing problems
export {
    Abilities, Limits, Query, CoreChat, SourceSet, TargetSet, ChatConfig,
    ChargeInfo, ExitCode, User, UserEvent, UserEventType };

