import * as React from 'react';
import EmoticonSectionContainer from '../EmoticonSection';
import { ITranslation } from '../../../redux/store';
import GiftSectionContainer from '../GiftSection/GiftSectionContainer';
import ToySectionContainer from '../ToySection';

interface IProps {
    isResponsive: boolean
    isEmoticonSectionOpen: boolean;
    isGiftSectionOpen: boolean;
    isToySectionOpen: boolean;
    translation: ITranslation;

    emoticonClick: (emoticon: string) => () => void;
    giftClick: (gift: string) => () => void;
}

const SymbolSection = (props: IProps) => (
    <>
        <EmoticonSectionContainer
            isOpen={props.isEmoticonSectionOpen}
            translation={props.translation}
            emoticonClick={props.emoticonClick}
            isResponsive={props.isResponsive}
        />

        <GiftSectionContainer
            isOpen={props.isGiftSectionOpen}
            onGiftClick={props.giftClick}
            isResponsive={props.isResponsive}
        />

        <ToySectionContainer isOpen={props.isToySectionOpen} />
    </>
);

export default SymbolSection;
