import * as React from 'react';
import { ITranslation } from '../../../redux/store';
import { GiftDetail, GiftItem, GiftName, GiftWrapper, Inline, InlineInitialPrice, InlineReducedPrice } from './style';

interface IProps {
    icon: string
    url: string;
    title: string;
    price: number;
    initialPrice?: number;
    currency: string;
    quantity?: number;
    isBundleGiftPage: boolean;
    translation: ITranslation;

    onGiftClick: (icon: string) => () => void;
}

const Gift = (props: IProps) => (
    <GiftWrapper onClick={props.onGiftClick(props.icon)}>
        <GiftItem 
            url={props.url} 
            title={props.title}
        />
 
        <GiftName isBundleGiftPage={props.isBundleGiftPage}>
            {props.title}
        </GiftName>

        {props.quantity && props.quantity > 0
            ? <GiftDetail>({props.translation.user.stock}: {props.quantity})</GiftDetail>
            : <GiftDetail>
                <Inline>
                    <Inline>(</Inline>
                    {props.initialPrice
                        ? (
                            <>
                                <InlineInitialPrice>{props.initialPrice} {props.currency}</InlineInitialPrice>
                                <InlineReducedPrice>{props.price} {props.currency}</InlineReducedPrice>
                            </>
                        ) : (
                            <Inline>{props.price} {props.currency}</Inline>
                        )
                        
                    }
                    <Inline>)</Inline>
                </Inline>
            </GiftDetail>
        }
    </GiftWrapper>
);

export default Gift;
