import * as React from 'react';
import { ITranslation } from "../../../redux/store";
import {
    Wrapper,
    Content,
    Arrow,
    Title,
    Text
} from "./style";
import CloseIcon from "../CloseIcon/CloseIcon";

interface IProps {
    translation: ITranslation;
    children: any;

    onClose?: () => void;
}

const content = (props: IProps) => (
    <div>
        <Title>{props.translation.chat.hint}:</Title>
        <CloseIcon onClick={props.onClose} />
        <Text>{props.translation.chat.tooltipNoVolume1}</Text>
        <Text>{props.translation.chat.tooltipNoVolume2}</Text>
    </div>
);

const InitiallyMutedTooltip = (props: IProps) => (
    <>
        <Wrapper>
            <Content>
                {content(props)}
            </Content>
            <Arrow />
        </Wrapper>
        
        {props.children}
    </>
);

export default InitiallyMutedTooltip;