import {produce} from "immer";
import {AnyAction} from "redux";
import { handleActions, Action } from "redux-actions";

import { createAction, createActionType } from "../utils";
import {RESET_STORE} from "./app";
import { IHooks, HooksArguments } from "../store";
import { getInitialInstanceData } from '../initialStore';
import { ActionIds } from "../interfaces";

export interface IHookPayload {
    name: keyof IHooks;
    args: any[];
}

const createHooksActionType = createActionType(ActionIds.HOOKS, { isReduxAction: true });

// Actions
export const SET_HOOKS = createHooksActionType("SET_HOOKS");

// Action Creators
export const setHooks = createAction<IHookPayload>(SET_HOOKS);

/** Reducers */
const hooks = handleActions({
    [RESET_STORE]: (state: IHooks, action: AnyAction) => getInitialInstanceData().hooks,

    [SET_HOOKS]: produce((draft: HooksArguments, { payload: { args, name }}: Action<IHookPayload>) => {
        draft[name] = args;
    })
}, getInitialInstanceData().hooks);

export default hooks;
