import styled from 'styled-components';
import withCustomClasses, { DomCustomClass } from "../../hoc/withCustomClasses/";

const TooltipWrapper = withCustomClasses(styled.div`
    font-family: ${(props) => props.theme.typography.fontFamily};
    font-weight: ${(props) => props.theme.typography.weight.regular};
    
    & .hover-tooltip {
        pointer-events: auto;
        
        &:hover {
            visibility: visible;
            opacity: 1;  
        }
    }
    
    & .update-browser {
        color: grey;
        font-style: italic;
        font-size: 0.9em;
    }
`, [DomCustomClass.Tooltip]);

export default TooltipWrapper;